import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import * as constants from "../../../../redux/constants";
import { Redirect } from "react-router-dom/cjs/react-router-dom.min";
import { Card, Col, Row, Form, Select, Table, Button, Checkbox } from "antd";
import { fetchAllProjects } from "../../../../redux/actions/Project/ProjectsAction";
import { validateModule } from "../../../handle/PermissionMethods/PermissionMethods";
import { fetchAllAfiliatedCompany } from "../../../../redux/actions/Configuration/AfiliatedCompanyAction";

const columns = [
	{
		title: "Total pagado",
		width: 50,
		dataIndex: "TotalPagado",
		key: "TotalPagado",
	},
	{
		title: "Total comisionado",
		width: 50,
		dataIndex: "Totalcomisionado",
		key: "TotalComisionado",
	},
	{
		title: "Comisión por pagar",
		width: 50,
		dataIndex: "ComisionPagar",
		key: "ComisionPagar",
	},
	{
		title: "Incluir en acta",
		width: 50,
		dataIndex: "IncluirActa",
		key: "IncluirActa",
	},
];

const mainTitles = ["Nombre empresa", "Comisiones pagadas", "desistimientos"];

const request = [
	{
		type: "Valor Total",
		percent: 20,
	},
	{
		type: "Escritutacion",
		percent: 2,
	},
];

const constructionTitles = [
	{
		title: "#",
		dataIndex: "id",
	},
	{
		title: "Fecha venta",
		dataIndex: "sale_date",
	},
	{
		title: "cliente",
		dataIndex: "name",
	},
	{
		title: "Valor del negocio",
		dataIndex: "business_value",
	},
	{
		title: "Documento legalizados al 100%",
		dataIndex: "percentage_legalized_document",
	},
	{
		title: "Recaudos abonos",
		dataIndex: "fertilizer_collections",
	},
];

const commissionsPaidTitles = [
	{
		title: "Total pagado comisiones",
		dataIndex: "total_paid_commissions",
	},
	{
		title: "Recálculo comisiones pagadas",
		dataIndex: "recalculation_commission_paid",
	},
	{
		title: "Ajustes aplicados",
		dataIndex: "applied_settings",
	},
	{
		title: "Saldo por cambio valor venta",
		dataIndex: "balance_for_change_sale_value",
	},
	{
		title: "Valor a cancelar acta actual",
		dataIndex: "amount_cancel_current_act",
	},
	{
		title: "Comisión total por multa",
		dataIndex: "total_commission_per_fine",
	},
];

const withdrawalsTitles = [
	{
		title: "Total pagado comisiones",
		dataIndex: "total_paid_commissions_withdrawal",
	},
	{
		title: "Total deuda",
		dataIndex: "total_debt",
	},
	{
		title: "Total deuda",
		dataIndex: "total_debt",
	},
	{
		title: "Incluir desistimiento",
		dataIndex: "include_withdrawal",
	},
];

const mainSubtitles = [
	constructionTitles,
	commissionsPaidTitles,
	withdrawalsTitles,
];
let commissionVerificationTitle = [];
let minutesStatesValues = [];

const validateMessages = {
	// eslint-disable-next-line no-template-curly-in-string
	required: "${placeholder} es requerido",
};

const CommissionVerification = () => {
	const dispatch = useDispatch();

	useEffect(() => {
		dispatch(fetchAllProjects());
	}, []);

	useEffect(() => {
		dispatch(fetchAllAfiliatedCompany());
	}, []);

	createMainTitles();
	createOptionsCheckBox();

	let projects = useSelector((state) => state?.project);
	let afiliatedsCompanys = useSelector((state) => state?.afiliatedCompany);
	// eslint-disable-next-line no-unused-vars
	let requestComision;
	const [formVisible, setFormVisible] = useState(true);

	const visibleForm = () => {
		setFormVisible(false);
	};

	const onFinish = (values) => {
		requestComision = values;
	};

	function createOptionsCheckBox() {
		minutesStatesValues = [];
		request.forEach((p) => {
			minutesStatesValues.push({
				label: p.type,
				value: p.type,
			});
		});
	}

	function createMainTitles() {
		let positionForAddPoliticComision = 1;
		let title = "";
		commissionVerificationTitle = [];
		for (let i = 0; i < mainTitles.length; i++) {
			title = createTitle(positionForAddPoliticComision, i, title);
			title = mainTitles[i];
			commissionVerificationTitle.push({
				title: title,
				key: "index",
				children: mainSubtitles[i],
			});
		}
	}

	function createTitle(positionForAddPoliticComision, i, title) {
		if (positionForAddPoliticComision === i) {
			request.forEach((p) => {
				let subTitle = [];
				title = p.percent + " %" + " de " + p.type;
				subTitle.push({
					title: title,
					dataIndex: "b",
				});
				subTitle.push({
					title: "Fecha pago",
					dataIndex: "a",
				});
				commissionVerificationTitle.push({
					title: "Incluir en el acta" + <div>hola</div>,
					key: i.toString(),
					filters: [{ text: "Si", value: "SI" }],
					children: subTitle,
				});
			});
		}
		return title;
	}

	function onChange(checkedValues) {
	}
	if (!validateModule(constants.COMISSION_VERIFICATION_SUBMODULE)) {
		return <Redirect to={constants.DEFAULT_ROUTE} />;
	}
	return (
		<>
			<Card title="Verificación comisiones">
				<Form
					layout="vertical"
					onFinish={onFinish}
					validateMessages={validateMessages}
				>
					<Row gutter={16}>
						<Col xs={24} sm={24} md={12} lg={12} xl={12}>
							<Form.Item
								label="Proyecto:"
								name="project"
								rules={[
									{
										required: true,
										message: 'proyecto requerido'
									}]}
							>
								<Select
									showSearch
									style={{ width: "100%" }}
									optionFilterProp="children"
									defaultValue=""
									filterOption={(input, option) =>
										option.children
											.toLowerCase()
											.indexOf(input.toLowerCase()) >= 0
									}
								>
									<Select.Option value="">Seleccione...</Select.Option>
									{!Array.isArray(projects) &&	projects._payload?.map((item) => (
											<Select.Option key={item.id} value={item.id}>{item.name}</Select.Option>
										))}
								</Select>
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={12} lg={12} xl={12}>
							<Form.Item label="Empresa:" name="afiliated_company">
								<Select
									showSearch
									style={{ width: "100%" }}
									optionFilterProp="children"
									defaultValue=""
									filterOption={(input, option) =>
										option.children
											.toLowerCase()
											.indexOf(input.toLowerCase()) >= 0
									}
								>
									<Select.Option value="">Seleccione...</Select.Option>
									{!Array.isArray(afiliatedsCompanys) &&
										afiliatedsCompanys._payload.map((item) => (
											<Select.Option key={item.id} value={item.id}>{item.name}</Select.Option>
										))}
								</Select>
							</Form.Item>
						</Col>
					</Row>
					<Row>
						<Col offset="22" xs={24} sm={24} md={2} lg={2} xl={2}>
							<Form.Item>
								<Button type="primary" htmlType="submit" onClick={visibleForm}>
									Filtrar
								</Button>
							</Form.Item>
						</Col>
					</Row>
				</Form>
			</Card>
			<Form.Item hidden={formVisible}>
				<Card>
					<Row>
						<Col>
							<h5>Comisión administradora:</h5>
						</Col>
					</Row>
					<Row>
						<Col xs={24} sm={24} md={14} lg={14} xl={14}>
							<Table columns={columns} dataSource={null} pagination={false} />
						</Col>
					</Row>
				</Card>
				<Card style={{ height: 400 }}>
					<div style={{ display: "inline-flex", marginBottom: 15 }}>
						<h5 style={{ marginRight: 50 }}>Informe Cumplimiento Comisiones</h5>
						<div style={{ display: "inline-flex" }}>
							<div
	style={{
		marginRight: 5,
		backgroundColor: "#D9EDF7",
		width: 23,
		height: 23,
		borderStyle: "solid",
		borderWidth: 1,
	}}
	/>
							<p style={{ marginRight: 5 }}>Comisión Cumplida</p>
						</div>
						<div style={{ display: "inline-flex" }}>
							<div
	style={{
		marginRight: 5,
		backgroundColor: "#DFF0D8",
		width: 23,
		height: 23,
		borderStyle: "solid",
		borderWidth: 1,
	}}
	/>
							<p style={{ marginRight: 5 }}>Comisión con acta</p>
						</div>
						<div style={{ display: "inline-flex" }}>
							<div
	style={{
		marginRight: 5,
		backgroundColor: "#FFFFFF",
		width: 23,
		height: 23,
		borderStyle: "solid",
		borderWidth: 1,
	}}
	/>
							<p style={{ marginRight: 5 }}>Comisión sin cumplir</p>
						</div>
						<div style={{ display: "inline-flex" }}>
							<div
	style={{
		marginRight: 5,
		backgroundColor: "#F2DEDE",
		width: 23,
		height: 23,
		borderStyle: "solid",
		borderWidth: 1,
	}}
	/>
							<p style={{ marginRight: 5 }}>Venta Desistida</p>
						</div>
					</div>
					<br />
					<br />
					<h6>Incluir en el Acta</h6>
					<br />
					<Checkbox.Group options={minutesStatesValues} onChange={onChange} />
					<br />
					<br />
					<Table
						rowKey="uid"
						size={"small"}
						columns={commissionVerificationTitle}
						scroll={{ x: 2800 }}
						dataSource={null}
						bordered={true}
					/>
					<Row>
						<Col offset="22" xs={24} sm={24} md={2} lg={2} xl={2}>
							<Form.Item>
								<Button type="primary" htmlType="submit">
									Generar acta
								</Button>
							</Form.Item>
						</Col>
					</Row>
				</Card>
			</Form.Item>
		</>
	);
};

export default CommissionVerification;
