import React, {useEffect, useState} from "react";
import { Table, Card } from "antd";
import {convertAllMapDataState} from "../../../../../handle/HandleFilterTable/handleFilterTable";

const formatterMoney = new Intl.NumberFormat("es-CO", {
	style: "currency",
	currency: "COP",
	minimumFractionDigits: 2,
});

const DescriptionIncluded = React.memo(({included}) => {
	const [dataSource,setDataSource] = useState([]);
	
	useEffect(()=>{
		data();
	},[included]);
	
	const columns = [
		{
			dataIndex: "descripcion",
			width: 80,
			height: 40,
			padding: "10px",
		},
		{
			dataIndex: "value",
			width: 80,
			height: 40,
			padding: "10px",
		},
	];
	const data = () => {
		setDataSource( [
			{
				descripcion: "Valor Parqueadero",
				value: formatterMoney.format(included?.valueParking)
			},
			{
				descripcion : "Valor Kit de Acabados",
				value: formatterMoney.format(included?.valueKit)
			},
			{
				descripcion : "Valor Terraza/ Cuarto útil/ otros",
				value: formatterMoney.format(included?.valueOthers)
			},
			{
				descripcion : "Prima Vista",
				value: formatterMoney.format(included?.heightPremium)
			},
			{
				descripcion : "Prima Altura por m2",
				value: formatterMoney.format(included?.sightCousin)
			}
		]);
	}
	
	return (
		<>
			<Card
				headStyle={{
					color: "#31708f",
					backgroundColor: "#d9edf7",
					borderColor: "#bce8f1",
				}}
				title="Incluidos"
				style={{ marginRight: 20 }}
			>
				<Table
					style={{ width: "100%" }}
					pagination={false}
					columns={columns}
					dataSource={convertAllMapDataState(dataSource)}
					bordered
				/>
			</Card>
		</>
	);
});
export default DescriptionIncluded;
