import React, { useState, useEffect } from "react";
import { Form, Input, Button, DatePicker, Select, Modal, Tooltip } from "antd";
import { useDispatch, useSelector } from "react-redux";
import { UserSwitchOutlined } from "@ant-design/icons";
import { fetchAllAssessor } from "../../../../../redux/actions/Configuration/AssesorAction";
import { reassignmentAssessorCustomerById } from "../../../../../redux/actions/Customer/CustomerAction";
import {
	errorGlobal,
	success,
} from "../../../../handle/Notification/Notification";
import { fetchAllSaleMade } from "../../../../../redux/actions/Price/SaleMade/SalesMadeAction";

const ReassignAdvisorSales = ({ data }) => {
	const dispatch = useDispatch();
	const [isModalVisible, setIsModalVisible] = useState();
	const assesorSelector = useSelector((state) => state?.assessor);

	useEffect(() => {
		if (isModalVisible) {
			dispatch(fetchAllAssessor());
			if (data?.assessor_name) {
				fillFields();
			}
		}
	}, [isModalVisible]);

	const [form] = Form.useForm();

	const handleSubmit = async () => {
		try {
			const values = await form.validateFields();
			const dto = {
				assessor: values["advisers"],
				reassignment_date: values["reassignmentDate"].format("YYYY-MM-DD"),
			};
			dispatch(
				reassignmentAssessorCustomerById(
					data.customer.id,
					dto,
					async (response) => {
						const res = await response;
						if (res.error) {
 
							errorGlobal("¡UPS! Ha ocurrido un error.");
						} else {
							success("Ha sido actualizado con éxito.");
							dispatch(fetchAllSaleMade());
							handleReset();
							onClose();
						}
					}
				)
			);
		} catch (errorInfo) {
			console.error("Failed:", errorInfo);
		}
	};

	const fillFields = () => {
		form.setFieldsValue({
			previousAdvisor: data.assessor_name,
		});
	};

	const handleReset = () => {
		form.resetFields();
	};

	const showModal = () => {
		setIsModalVisible(true);
	};
	const onClose = () => {
		setIsModalVisible(false);
	};

	const textAssignResponsible = (
		<span style={{ color: "#6A6963" }}>Reasignar Asesor</span>
	);

	return (
		<>
			<Tooltip placement="left" color="#ffffff" title={textAssignResponsible}>
				<Button
					type="primary"
					onClick={showModal}
					style={{ background: "#40a9ff", color: "#fff" }}
					size={"small"}
					icon={<UserSwitchOutlined />}
					disabled={data.is_desisted}
				/>
			</Tooltip>

			<Modal
				title="Reasignar Asesor"
				name="FormCuopons"
				layout="vertical"
				visible={isModalVisible}
				onCancel={onClose}
				footer={false}
				width={800}
				centered
			>
				<Form
					form={form}
					onFinish={handleSubmit}
					labelCol={{ span: 8 }}
					wrapperCol={{ span: 12 }}
				>
					<Form.Item
						label="Asesor Anterior"
						name="previousAdvisor"
						disabled={true}
					>
						<Input
							value={form.getFieldValue("previousAdvisor")}
							disabled={true}
							style={{ width: "100%" }}
						/>
					</Form.Item>
					<Form.Item
						label="Usuario"
						name="advisers"
						rules={[{ required: true, message: "El campo es requerido." }]}
					>
						<Select
							defaultValue={""}
							showSearch
							style={{ width: "100%" }}
							optionFilterProp="children"
							filterOption={(input, option) =>
								option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
							}
						>
							<Select.Option value="">Seleccione...</Select.Option>
							{assesorSelector.hasOwnProperty("_payload") &&
								assesorSelector._payload.map((item) => (
									<Select.Option
										value={item.id}
									>{` ${item.name} - CC: ${item.identification}`}</Select.Option>
								))}
						</Select>
					</Form.Item>
					<Form.Item
						name="reassignmentDate"
						label="Fecha reasignación"
						rules={[
							{
								required: true,
								message: "El campo es requerido.",
							},
						]}
					>
						<DatePicker
							placeholder="Selecione la fecha..."
							style={{ width: "100%" }}
						/>
					</Form.Item>
					<Form.Item>
						<Button
							type="primary"
							style={{ marginLeft: "100%" }}
							htmlType="submit"
						>
							Guardar
						</Button>
					</Form.Item>
				</Form>
			</Modal>
		</>
	);
};

export default ReassignAdvisorSales;
