import * as constants from './../../constants';

export const fetchAllBuilder = () => ({
    type: constants.API,
    payload: {
      method: 'GET',
      url: `${constants.RUTA}/constructionCompany`,
      success: (response) => (setAllBuilder(response))
    }
});

export const createBuilder = (data, onSuccess, onError) => ({
    type: constants.API,
    payload: {
        method: 'POST',
        url: `${constants.RUTA}/constructionCompany`,
        data,
        success: (builder) => (addBuilder(builder)),
        postProcessSuccess: onSuccess,
        postProcessError: onError
    }
});

const addBuilder = (builder) => ({
    type: constants.ADD_BUILDER ,
    payload: builder
});

const setAllBuilder = (data) => ({
		type: constants.SET_ALL_BUILDER,
  	payload: data
});
