import React from "react";
import { Table} from 'antd';
import {convertAllMapDataState} from "../../../../handle/HandleFilterTable/handleFilterTable";

const NewReformRequest = ({columns,dataSource})=>{
 
    return (

    
        <Table bordered  pagination={{ position: ['none', 'none'] }} dataSource={convertAllMapDataState(dataSource)} columns={columns}  />
        
    )


}


export default NewReformRequest;