import React from "react";
import {useDispatch} from "react-redux";
import {Button, Card, Col, Form, Row} from "antd";
import CustomerRequests from "./CustomerRequests";
import {errorGlobal, success} from "../../../../../../../../handle/Notification/Notification";
import {requestPostSale} from "../../../../../../../../../redux/actions/Customer/AfterSalesCustomer";



const PostSaleAddForm = ({buttons,sale_id,searchForInformation,NuevoSet}) => {
	const dispatch = useDispatch()
	const [form] = Form.useForm()
	
	const onFinish = async () => {
		const value = await form.validateFields()
		const formData = new FormData()
		formData.append('comment', value['comment0'])
		formData.append('assessor_id',(JSON.parse(localStorage.getItem('USER_INFO'))).id)
		if (typeof value["application_document0"] !== "undefined"){
			for (let i = 0; i < value["application_document0"].length; i++) {
				formData.append(
					`application_document`,
					value["application_document0"][i].originFileObj,
					value["application_document0"][i].name
				)
			}
		}
		dispatch(
			requestPostSale(sale_id,formData,async (response)=>{
				const res = await response
				success(res.message)
				searchForInformation()
			},async (error)=>{
				const res = await error
				errorGlobal(res.message)
			})
		)
		NuevoSet()
	}
	
	return (
		<Form
			layout="vertical"
			form={form}
			name={"PostSaleSave"}
		>
			<Row>
				<Col span={8}>
					<Card
						title={"Solicitudes del Cliente"}
					>
						<Row>
							<Col>
								<CustomerRequests/>
							</Col>
						</Row>
						<Row>
							<Col span={12}>
								{buttons("Cancelar",true)}
							</Col>
							<Col span={12}>
								<Button
									size={"large"}
									type="primary"
									htmlType="submit"
									onClick={()=>onFinish()}
								>
									Guardar
								</Button>
							</Col>
						</Row>
					</Card>
				</Col>
			</Row>
		</Form>
	)
}

export default PostSaleAddForm