import * as constants from "../../constants";


export const getAllDirectCollections = (onSuccess) => ({
	type: constants.API,
	payload: {
		method: "GET",
		url: `${constants.RUTA}/direct-collection`,
		headers: constants.TOKEN,
		success: (reseponse) => AllDirectCollections(reseponse),
		postProcessSuccess: onSuccess,
	},
});

export const updateDirectCollection = (id, data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		url: `${constants.RUTA}/direct-collection/${id}`,
		data,
		success: (id, data) => (updateDirect(id, data)),
		postProcessSuccess: onSuccess,
		postProcessError: onError
	}
});

const updateDirect = (directId, data) => ({
	type: constants.UPDATE_DIRECT_COLLECTION,
	payload: { directId, data }
});

export const createDirectCollection = (data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		url: `${constants.RUTA}/direct-collection`,
		data,
		success: (directColletion) => (addDirectCollection(directColletion)),
		postProcessSuccess: onSuccess,
		postProcessError: onError
	}
});

export const sendDirectCollection = (id, onSuccess, onError)=>({
	type: constants.API,
	payload: {
		method: 'POST',
		url: `${constants.RUTA}/direct-collection/send-mail/${id}`,
		success: (id, data) => (sendCollection(id, data)),
		postProcessSuccess: onSuccess,
		postProcessError: onError
	}

})

export const updateCheckDirectCollection = (id, data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		url: `${constants.RUTA}/direct-collection/verify/${id}`,
		data,
		success: (id, data) => (updateCheckDirect(id, data)),
		postProcessSuccess: onSuccess,
		postProcessError: onError
	}
});

const updateCheckDirect = (directId, data) => ({
	type: constants.UPDATE_CHECK_DIRECT_COLLECTION,
	payload: { directId, data }
});

const sendCollection = (directId, data) => ({
	type: constants.SEND_DIRECT_COLLECTION,
	payload: { directId, data }
});

const addDirectCollection = (directColletion) => ({
	type: constants.ADD_DIRECT_COLLECTION,
	payload: directColletion
});

const AllDirectCollections = (directCollections) => ({
	type: constants.GET_ALL_DIRECT_COLLECTION,
	payload: directCollections,
});
