import  React,{ createContext, useState } from "react";
import {errorGlobal} from "../../../../../handle/Notification/Notification";

const SelectedUnitsContext = createContext();

const initalSelectUnits = [];

const SelectedDataProvider = ({ children }) => {
	const [selectedUnits, setSelectedUnits] = useState(initalSelectUnits);

	const handleSelectedUnit = (data) => {
		if (selectedUnits.length > 2) {
			errorGlobal("Solo se pueden generar tres cotizacones");
		} else {
			const arrayToWork = [...selectedUnits, data];
			setSelectedUnits(arrayToWork);
		}
	};

	const handleDelete = (id) => {
		const arrayToWork = [...selectedUnits];

		setSelectedUnits(arrayToWork.filter((item) => item.id !== id));
	};

	const data = { selectedUnits, handleSelectedUnit, handleDelete };
	return (
		<SelectedUnitsContext.Provider value={data}>
			{children}
		</SelectedUnitsContext.Provider>
	);
};

export { SelectedDataProvider };
export default SelectedUnitsContext;
