import * as constants from "../../constants";

export const getUnitByIdProject = (id, onSuccess) => ({
		type: constants.API,
		payload: {
			method: 'GET',
			url: `${constants.RUTA}/unit/${id}`,
			postProcessSuccess: onSuccess,
			success: (response) => (getUnit(response))
	}
});

export const updateUnitsAction = (data , onSuccess, onError) => ({
    type: constants.API,
    payload: {
        method: 'PUT',
        url: `${constants.RUTA}/unit`,
        data,

		postProcessSuccess: onSuccess, 
		postProcessError: onError
       
    }
});

export const resetUnitsAction = () => ({
    type: constants.API,
    payload: {
		method: null,
		success: () => (resetUnits())
	}
})

const resetUnits = () =>({
	type:constants.RESET_UNIT_BY_PROJECT_ID,
	payload: []
});
/*
const updateUnits = (data) => ({
    type: constants.UPDATE_UNIT_BY_PROJECT_ID,
    payload: { data }
});

*/
const getUnit = (response) => ({
	type: constants.GET_UNIT_BY_PROJECT_ID,
	payload: response
})
