import React from "react";
import { List, Row, Col } from "antd";
import Text from "antd/lib/typography/Text";
import {convertAllMapDataState} from "../../../../../../handle/HandleFilterTable/handleFilterTable";

const ListTitleData = (props) => {
	return (
		<div style={{ display: "block", width: 200, minWidth: 200 }}>
			
			<List
				style={{
					display: "block",
					background: "#d9edf7",
					border: "1px solid #bce8f1",
					borderBottom: "none"
					
				}}
				size="small"
				bordered
				dataSource={convertAllMapDataState(props.data)}
				renderItem={(item) => (
					<Text strong>
						<List.Item
							style={{
								borderBottom: "1px solid #bce8f1",
                                display: 'block',
                                textAlign: 'end'
							}}
						>
							<Row>
								{" "}
								<Col>{item}</Col>{" "}
							</Row>
						</List.Item>
					</Text>
				)}
			/>
		</div>
	);
};

export default ListTitleData;
