import * as constants from '../../constants';

export default function controlPower(state = [], action) {
    switch (action.type) {
        case constants.SET_ALL_CONTROL_POWERS:
            return action.payload;
        case constants.ADD_CONTROL_POWER:
            return state;
        case constants.REMOVE_CONTROL_POWER:
            return state._payload.filter(item => item._id !== action.payload);
        case constants.UPDATE_CONTROL_POWER:
            return state;
        case constants.RESET_CONTROL_POWER:
            return [];
			case constants.GET_COMPETING_PROJECTS:
				   return action.payload;
        default:
            return state;
    }
}