import React, {useEffect, useState} from "react";
import { fetchAllProjects } from "../../../../redux/actions/Project/ProjectsAction";
import { fetchAllAssessor } from "../../../../redux/actions/Configuration/AssesorAction";
import { fetchAllAfiliatedCompany } from "../../../../redux/actions/Configuration/AfiliatedCompanyAction";
import {  useDispatch } from "react-redux";
import { Card, Col, Row, Form, Button } from "antd";
import { validateModule } from "../../../handle/PermissionMethods/PermissionMethods";
import * as constants from "../../../../redux/constants";
import { Redirect } from "react-router-dom/cjs/react-router-dom.min";
import ApplicationForm from "./Components/ApplicationForm";
import {postAdvertisingEffectiveness} from "../../../../redux/actions/Report/AdvertisingEffectiveness";
import {errorGlobal, success} from "../../../handle/Notification/Notification";
import {DownloadDocument} from "../../../handle/Multimedia/DownloadDocument";

const AdvertisingEffectivenessReport = () => {
	
	const [form] = Form.useForm()
	const dispatch = useDispatch()
	const [consultind, setConsulting] = useState( false )
	
	useEffect(()=>{
		dispatch(fetchAllProjects())
		dispatch(fetchAllAfiliatedCompany())
		dispatch(fetchAllAssessor())
	},[]);

	const onFinish = async () => {
		setConsulting(true);
		const values = await form.validateFields()
		dispatch(postAdvertisingEffectiveness(values,async (response)=>{
			const res = await response
			DownloadDocument(res._payload);
			setConsulting(false);
		},async (error)=>{
			const err = error
			errorGlobal(err.message)
			setConsulting(false);
		}))
		
	};
	
	const validateMessages = {
		// eslint-disable-next-line no-template-curly-in-string
		required: "${label} es requerido",
		types: {
			// eslint-disable-next-line no-template-curly-in-string
			email: "${label} no es un correo válido",
			// eslint-disable-next-line no-template-curly-in-string
			number: "${label} debe ser un número",
		},
	};
	
	if (!validateModule(constants.ADVERTISING_EFFECTIVENESS_REPORT_SUBMODULE)) {
		return <Redirect to={constants.DEFAULT_ROUTE} />;
	}
	
	return (
		<Card 
			title="Informe Efectividad en Publicidad"
		>
			<Form
				layout="vertical"
				form={form}
				onFinish={onFinish}
				validateMessages={validateMessages}
			>
				<ApplicationForm/>
				<Row>
					<Col xs={18} sm={20} md={21} lg={22} xl={22} />

					<Col xs={2} sm={2} md={2} lg={2} xl={2}>
						<Form.Item>
							<Button type="primary" htmlType="submit" loading={consultind}>
								Exportar
							</Button>
						</Form.Item>
					</Col>
				</Row>
			</Form>
		</Card>
	);
};
export default AdvertisingEffectivenessReport;
