import { useDispatch } from "react-redux";
import React, { useEffect, useState } from "react";
import { UngroupOutlined } from "@ant-design/icons";
import { Form, Button, Row, Col, InputNumber, Tooltip, Modal } from "antd";
import { errorGlobal, success } from "../../../../handle/Notification/Notification";
import { updateQuoteSeparationById, getAllQuotation } from "../../../../../redux/actions/Price/Quotation/QuotesAction";

const layout = {
	labelCol: {
		span: 24,
	},
	wrapperCol: {
		span: 24,
	},
};

const validateMessages = {
	// eslint-disable-next-line no-template-curly-in-string
	required: "${label} es requerido",
	// eslint-disable-next-line no-template-curly-in-string
	number: "El ${label} no puede ser menor a 0",
	types: {
		number: "No es un numero válido!",
	},
};
const QuoteSeparation = ({ props }) => {
	useEffect(() => {
		fillFields();
	}, []);
	const fechaLocal = new Date().toISOString().slice(0, 10);
//	let FechaCotizacion = new Date(props.final_date).toISOString().slice(0, 10)

	const [visible, setVisible] = useState(false);
	const dispatch = useDispatch();

	const onShow = () => setVisible(true);
	const onCancel = () => setVisible(false);

	const [form] = Form.useForm();

	const fillFields = () => {
		form.setFieldsValue({
			maximumDaysSeparation: props.maximum_separation_days,
		});
	};

	const handleSumit = async () => {
		try {
			const values = await form.validateFields();
			const dto = {
				number_days_separated: values.splitDays,
				quotation_id: props.id
			};
			dispatch(updateQuoteSeparationById(props.unit_id, dto , async (response) => {
				const res = await response;
				if(!res.error){
					success("Ha sido actualizada con éxito");
					dispatch(
						getAllQuotation()
					);
				}
				else{
					errorGlobal('¡UPS! Ocurió un error mientras se actualizaba');
				}
			}
			));
			handleReset();
			onCancel();
		} catch (errorInfo) {
			console.error("Failed:", errorInfo);
		}
	};

	const handleReset = () => {
		form.setFieldsValue({
			splitDays: null,
		});
	};

	const textSepararUnidad = (
		<span style={{ color: "#6A6963" }}> Separar Unidad </span>
	);

	return (
		<>
			<Tooltip placement="left" color="#ffffff" title={textSepararUnidad}>
				<Button
					style={{ background: "#2827CC", borderColor: "#2827CC" }}
					size="small"
					icon={<UngroupOutlined style={{ color: "#fff" }} />}
					disabled={props?.unit_state === "Comprada" || props?.unit_state === "Separada" || props.final_date < fechaLocal}
					onClick={onShow}
				/>
			</Tooltip>
			<Modal
				visible={visible}
				onCancel={onCancel}
				title={textSepararUnidad}
				size="small"
				footer={false}
				style={{ width: 350 }}
			>
				<Row>
					<Col offset={4} span={18}>
						<Form
							validateMessages={validateMessages}
							form={form}
							{...layout}
							onFinish={handleSumit}
							name="FormPresupuesto"
						>
							<Form.Item
								label="Máximo Días de separación"
								name="maximumDaysSeparation"
								rules={[
									{
										required: false,
										message: "El campo es requerido",
									},
								]}
							>
								<InputNumber
									value={form.getFieldValue("maximumDaysSeparation")}
									style={{ width: "90%" }}
									disabled
								/>
							</Form.Item>
							<Form.Item
								label="Dias de separación"
								name="splitDays"
								rules={[
									{
										required: true,
										validator: async (_, value) => {
											if (!value) {
												{
													return Promise.reject(
														new Error("El campo es requerido")
													);
												}
											}
											if (value > form.getFieldValue("maximumDaysSeparation")) {
												return Promise.reject(
													new Error("No se puede superar el máximo de días")
												);
											}
										},
									},
								]}
							>
								<InputNumber
									value={form.getFieldValue("splitDays")}
									style={{ width: "90%" }}
								/>
							</Form.Item>
							<Form.Item>
								<Button
									style={{
										backgroundColor: "#52c41a",
										color: "#fff",
										marginLeft: "25%",
									}}
									htmlType="submit"
								>
									Separar unidad
								</Button>
							</Form.Item>
						</Form>
					</Col>
				</Row>
			</Modal>
		</>
	);
};

export default QuoteSeparation;
