import * as constants from '../../../constants';
import LocalStorage from '../../../../views/config/LocalStorage'

let Storage = new LocalStorage();


export const fetchAllAssignmentSiteDirectors = (id) => ({
    type: constants.API,
    payload: {
        method: 'GET',
	    headers: { 'access-token':Storage.getToken() },
        url: `${constants.RUTA}/projectConstructionDirectors/${id}`,
        success: (response) => (setAllAssignmentSiteDirectors(response))
    }
});

export const updateOrCreateAssignmentSiteDirectorsById = (data, onSuccess, onError) => ({
    type: constants.API,
    payload: {
        method: 'PUT',
        url: `${constants.RUTA}/projectConstructionDirectors/assignProject`,
        data,
        success: (data) => (updateOrCreateAssignmentSiteDirectors(data)),  
        postProcessSuccess: onSuccess,
        postProcessError: onError           
    }
});

export const deleteAssignmentSiteDirectorsById = ( id, onSuccess, onError) => ({
    type: constants.API,
    payload: {
        method: 'DELETE',
        url: `${constants.RUTA}/projectConstructionDirectors/${id}`,
        success: () => (removeAssignmentSiteDirectors(id)),
        postProcessSuccess: onSuccess,
        postProcessError: onError
    }
});

export const resetAssignmentSiteDirectorsAction = () => ({
    type: constants.API,
    payload: {
		method: null,
		success: () => (resetAssignmentSiteDirectors())
	}
})

const resetAssignmentSiteDirectors = () =>({
	type:constants.RESET_ASSIGNED_SITE_DIRECTORS,
	payload: []
});

const setAllAssignmentSiteDirectors = (data) => ({
    type: constants.SET_ALL_ASSIGNED_SITE_DIRECTORS,
    payload: data
});

const updateOrCreateAssignmentSiteDirectors = ( id, data) => ({
    type: constants.UPDATE_OR_CREATE_ASSIGNED_SITE_DIRECTORS,
    payload: { id, data }
});

const removeAssignmentSiteDirectors = ( id) => ({
    type: constants.REMOVE_ASSIGNED_SITE_DIRECTORS,
    payload:  id
});