import * as constants from "../../../constants";

export default function legalizeDocumentReducer(state = [], action) {
	switch (action.type) {
		case constants.SET_ALL_LEGALIZE_DOCUMENTS:
			return action.payload;
		case constants.UPLOAD_LEGALIZE_DOCUMENT:
			return state;
		case constants.RESET_LEGALIZE_DOCUMENTS:
			return action.payload;
		case constants.UPDATE_PERCENTAGE_LEGALIZED_DOCUMENTS:
			return state;
		default:
			return state;
	}
}
