import * as constants from "../../../constants";

export const fecthDesistSaleAction = (id, onSuccess) => ({
	type: constants.API,
	payload: {
		method: "GET",
		headers: constants.TOKEN,
		url: `${constants.RUTA}/sale-desisted/${id}`,
		success: (response) => fecthDesistSale(response),
		postProcessSuccess: onSuccess,
	},
});

export const createDesistSaleAction = (data , onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: "POST",
		headers: constants.TOKEN,
		url: `${constants.RUTA}/sale-desisted`,
		data,
		success: (response) => createDesistSale(response),
        postProcessSuccess: onSuccess,
		postProcessError: onError
	},
});

export const updateDesistSaleAction = (data , onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: "POST",
		headers: constants.TOKEN,
		url: `${constants.RUTA}/sale-desisted/update`,
		data,
		success: (response) => updateDesistSale(response),
        postProcessSuccess: onSuccess,
		postProcessError: onError
	},
});

export const resetDesistSaleAction = () => ({
    type: constants.API,
    payload: {
		method: null,
		success: () => (resetDesistSale())
	}
})

const resetDesistSale = () => ({
	type: constants.RESET_SALE_WITHDRAWAL,
	payload: []
}) 

const  createDesistSale = (data) => ({
	type: constants.CREATE_SALE_WITHDRAWAL,
	payload: data
});

const updateDesistSale = (data) => ({
	type: constants.UPDATE_SALE_WITHDRAWAL,
	payload: data
});

const fecthDesistSale = (data) => ({
	type: constants.FETCH_BY_ID_SALE_WITHDRAWAL,
	payload: data
});
