import * as constants from "../../constants";

export default function increasePolicy(state = [], action) {
	switch (action.type) {
		case constants.SET_ALL_INCREASE_POLICY:
			return action.payload;
		case constants.UPDATE_OR_CREATE_INCREASE_POLICY:
			return state;
		case constants.REMOVE_INCREASE_POLICY:
			return state;
		case constants.RESET_INCREASE_POLICY:
			return action.payload;
		default:
			return state;
	}
}
