import React, { useEffect, useState } from "react";
import { fetchAllProjects } from "../../../../redux/actions/Project/ProjectsAction";
import { fetchAllAssessor } from "../../../../redux/actions/Configuration/AssesorAction";
import { fetchAllAfiliatedCompany } from "../../../../redux/actions/Configuration/AfiliatedCompanyAction";
import { fetchAllCustomer } from "../../../../redux/actions/Customer/CustomerAction";
import { captureDate } from "../../../handle/HandleDatePicker/HandleDate";
import { connect, useDispatch, useSelector } from "react-redux";
import { validateModule } from "../../../handle/PermissionMethods/PermissionMethods";
import * as constants from "../../../../redux/constants";
import { Redirect } from "react-router-dom/cjs/react-router-dom.min";
import { Card, DatePicker, Col, Row, Form, Select, Button } from "antd";
import { fetchExcelPortfolioMonitoringReport } from "../../../../redux/actions/Report/PortfolioMonitoringReport/portfolioMonitoringReportAction";

const PortfolioMonitoringReport = ({ dispatchFetchAllCustomerAction, dispatchFetchAllAfiliatedCompanyAction, dispatchFetchAllProjectAction, dispatchFetchAllAssessorAction }) => {
	const dispatch = useDispatch();
	//!Array.isArray(selectProject) && selectProject._payload.map
	const selectProject = useSelector((state) => state?.project);
	const selectAssessor = useSelector((state) => state?.assessor);
	const selectAfiliatedCompany = useSelector((state) => state?.afiliatedCompany);
	const selectCustomer = useSelector((state) => state?.customer);

	const [idProject, setIdProject] = useState(null);
	const [idAssessor, setIdAssessor] = useState(null);
	const [idAfiliatedCompany, setIdAfiliatedCompany] = useState(null);
	const [idCustomer, setIdCustomer] = useState(null);
	const [endDate, setEndDate] = useState(null);
	const [idPortfolioStatus, setPortfolioStatus] = useState(null);
	const [consultind, setConsulting] = useState( false );
	
	const dataPortfolioMonitoringReport = {
		"id_project": idProject,
		"id_assessor": idAssessor,
		"id_company": idAfiliatedCompany,
		"id_customer": idCustomer,
		"client_portfolio_status": idPortfolioStatus,
		"final_date": endDate,
	};


	useEffect(
		() => dispatchFetchAllCustomerAction(),
		[dispatchFetchAllCustomerAction]
	);
	useEffect(
		() => dispatchFetchAllProjectAction(),
		[dispatchFetchAllProjectAction]
	);
	useEffect(
		() => dispatchFetchAllAssessorAction(),
		[dispatchFetchAllAssessorAction]
	);
	useEffect(
		() => dispatchFetchAllAfiliatedCompanyAction(),
		[dispatchFetchAllAfiliatedCompanyAction]
	);

	const portfolioStatus = [
		{ id: "CRITICO", name: "Crítico" },
		{ id: "REGULAR", name: "Regular" },
		{ id: "NORMAL", name: "Normal" },
	];
	
	const onFinish = () => {
		setConsulting(true);
		dispatch(fetchExcelPortfolioMonitoringReport(dataPortfolioMonitoringReport, async (response) => {
			let res = await response;
			if (res.code === 200) {
				const tagA = document.createElement("a");
				tagA.href = res._payload;
				tagA.target = "_blank";
				tagA.click();
			}setConsulting(false);
		}));
		
	};

	const validateMessages = {
		required: "${label} es requerido",
		types: {
			email: "${label} no es un correo válido",
			number: "${label} debe ser un número",
		},
	};
	
	if (!validateModule(constants.PORTFOLIO_MONITORING_REPORT_SUBMODULE)) {
		return <Redirect to={constants.DEFAULT_ROUTE} />;
	}
	
	return (
		<Card title="Informe de Seguimiento de Cartera">
			<Form
				layout="vertical"
				onFinish={onFinish}
				validateMessages={validateMessages}
			>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item label="Proyecto:" name="Proyecto">
							<Select
								defaultValue={""}
								showSearch
								style={{ width: "100%" }}
								optionFilterProp="children"
								onChange={setIdProject}
								filterOption={(input, option) =>
									option.children.toLowerCase().indexOf(input.toLowerCase()) >=	0
								}
							>
								<Select.Option value="">Todos...</Select.Option>
								{
									!Array.isArray(selectProject) && selectProject._payload?.map(item => (
										<Select.Option key={item.id} value={item.id}>{` ${item.name} `}</Select.Option>
									))}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item
							label="Asesor:"
							name="Asesor"

						>
							<Select
								defaultValue={""}
								showSearch
								style={{ width: "100%" }}
								optionFilterProp="children"
								onChange={setIdAssessor}
								filterOption={(input, option) =>
									option.children.toLowerCase().indexOf(input.toLowerCase()) >=
									0
								}
							>
								<Select.Option value={""}>Todos...</Select.Option>
								{!Array.isArray(selectAssessor) &&
									selectAssessor._payload.map((item) => (
										<Select.Option key={item.id} value={item.id}>{` ${item.name} `}</Select.Option>
									))}
							</Select>
						</Form.Item>
					</Col>
				</Row>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item
							label="Empresa:"
							name="Empresa"
						>
							<Select
								defaultValue={""}
								showSearch
								style={{ width: "100%" }}
								optionFilterProp="children"
								onChange={setIdAfiliatedCompany}
								filterOption={(input, option) =>
									option.children.toLowerCase().indexOf(input.toLowerCase()) >=
									0
								}
							>
								<Select.Option value="">Todos...</Select.Option>
								{!Array.isArray(selectAfiliatedCompany) &&
									selectAfiliatedCompany._payload.map((item) => (
										<Select.Option key={item.id} value={item.id}>{` ${item.name} `}</Select.Option>
									))}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item
							label="Cliente:"
							name="Cliente"
						>
							<Select
								defaultValue={""}
								showSearch
								style={{ width: "100%" }}
								optionFilterProp="children"
								onChange={setIdCustomer}
								filterOption={(input, option) =>
									option.children.toLowerCase().indexOf(input.toLowerCase()) >=
									0
								}
							>
								<Select.Option value="">Todos...</Select.Option>
								{
									!Array.isArray(selectCustomer) && selectCustomer._payload?.map(item => (
										<Select.Option key={item.id} value={item.id}>{` ${item.name} - CC: ${item.identification}`}</Select.Option>

									))

								}
							</Select>
						</Form.Item>
					</Col>
				</Row>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item label="Estado de Cartera:" name="Estado de Cartera"
						
						>
							<Select
								defaultValue={""}
								showSearch
								style={{ width: "100%" }}
								optionFilterProp="children"
								onChange={setPortfolioStatus}
								filterOption={(input, option) =>
									option.children.toLowerCase().indexOf(input.toLowerCase()) >=
									0
								}
							>
								<Select.Option value="">Seleccione...</Select.Option>
								{portfolioStatus.map((item) => (
									<Select.Option key={item.id} value={item.id}>{` ${item.name} `}</Select.Option>
								))}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item
							label="Fecha Fin:"
							name="Fecha Fin:"
							rules={[
								{
									required: true,
								},
							]}
						>
							<DatePicker
								value={endDate}
								onChange={(value) => captureDate(value, setEndDate)}
								style={{ width: "100%" }}
							/>
						</Form.Item>
					</Col>
				</Row>
				<Row>
					<Col xs={17} sm={20} md={21} lg={22} xl={22} />

					<Col xs={2} sm={2} md={2} lg={2} xl={2}>
						<Form.Item>
							<Button type="primary" htmlType="submit" loading={consultind}>
								Exportar
							</Button>
						</Form.Item>
					</Col>
				</Row>
			</Form>
		</Card>
	);
};

const mapDispatchToProps = (dispatch) => ({
	dispatchFetchAllCustomerAction: () => dispatch(fetchAllCustomer()),
	dispatchFetchAllProjectAction: () => dispatch(fetchAllProjects()),
	dispatchFetchAllAssessorAction: () => dispatch(fetchAllAssessor()),
	dispatchFetchAllAfiliatedCompanyAction: () =>
		dispatch(fetchAllAfiliatedCompany()),
});
export default connect(null, mapDispatchToProps)(PortfolioMonitoringReport);
