import React, {useEffect} from "react";
import {Form, Button, InputNumber, Card, Select} from "antd";
import {DeleteOutlined} from "@ant-design/icons";
import {FORMATTER_INPUT_NUMBER} from "../../../../../../../../redux/constants";

const formItemStyle = {
	width: "100%",
	margin: 0,
	paddingTop: 4,
};

const IncreasePolicyComponent = ({item, handleDeleteIncrementPolicy, form}) => {

	useEffect(() => {
		fillFields();
	}, [item]);

	const fillFields = () => {
		form.setFieldsValue({
			[`lower_limit;${item.id}`]: item.lower_limit,
			[`upper_limit;${item.id}`]: item.upper_limit,
			[`increase_type;${item.id}`]: item.increase_type
				? String(item.increase_type)
				: "",
			[`amount;${item.id}`]: item.amount,
		});
	};

	const handleDelete = () => {
		handleDeleteIncrementPolicy(item.id);
	};

	return (
		<>
			<Card
				className={"formStyle"}
				style={
					({minWidth: "200px", padding: "10% 0"},
						{display: "block"},
						{borderColor: "black"})
				}
				title="Politica"
				extra={
					<>
						<Button
							onClick={handleDelete}
							icon={<DeleteOutlined/>}
							type="primary"
							danger
						/>
					</>
				}
				bodyStyle={{padding: 5}}
			>
				<Form.Item
					style={formItemStyle}
					name={`lower_limit;${item.id}`}
				>
					<InputNumber
						style={{width: "100%"}}
						placeholder={"Límite Inferior"}
						value={form.getFieldValue(`lower_limit;${item.id}`)}
					/>
				</Form.Item>
				<Form.Item
					style={formItemStyle}
					name={`upper_limit;${item.id}`}
				>
					<InputNumber
						style={{width: "100%"}}
						placeholder={"Límite Superior"}
						value={form.getFieldValue(`upper_limit;${item.id}`)}
					/>
				</Form.Item>
				<Form.Item
					style={formItemStyle}
					name={`increase_type;${item.id}`}
					value={form.getFieldValue(`increase_type;${item.id}`)}
					showSearch
					optionFilterProp="children"
					filterOption={(input, option) =>
						option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
					}
					rules={[
						{
							required: true,
							message: "El campo es requerido.",
						},
					]}
				>
					<Select defaultValue={""}>
						<Select.Option value="">Seleccionar</Select.Option>
						<Select.Option value="1">Valor m2</Select.Option>
						<Select.Option value="2">Valor Total</Select.Option>
					</Select>
				</Form.Item>
				<Form.Item
					style={formItemStyle}
					name={`amount;${item.id}`}
					rules={[
						{
							required: true,
							message: "El campo es requerido.",
						},
					]}
				>
					<InputNumber
						{...FORMATTER_INPUT_NUMBER}
						parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
						style={{width: "100%"}}
						placeholder={"Valor"}
						value={form.getFieldValue(`amount;${item.id}`)}
					/>
				</Form.Item>
			</Card>
		</>
	);
};

export default IncreasePolicyComponent;
