import React, { useState, useEffect } from "react";
import { DeleteOutlined } from "@ant-design/icons";
import { FormatterPeso} from "../Utilitis/Utilitis";
import {useEditQuotes} from "../../Hooks/useEditQuotes";
import { Table, Button, Tooltip, InputNumber } from "antd";
import {FORMATTER_INPUT_NUMBER} from "../../../../../../redux/constants";
import {convertAllMapDataState} from "../../../../../handle/HandleFilterTable/handleFilterTable";

const TablePagoCliente = (dataTable) => {
	
	const [dataSource, setDataSource] = useState([]);
	const [modify, setModify] = useState([]);
	const [deliveryDate, setDelivery] = useState(0);
	const { modified_values, calculatePor,calculatePresentValueByTheClient } = dataTable;
	const { CalculateDelivery } = useEditQuotes()

	useEffect(() => {
		let formOfPayments = dataTable.dataTable;
		let modi = [];
		if (dataSource !== []){
			for (let index =0;index < formOfPayments?.length;index++){
				if (dataSource[index]?.value !== formOfPayments[index]?.value){
					modi.push(formOfPayments[index])
				}
			}
		}
		if (modi.length > 0) {
			modified_values(modi);
			setModify(modi);
		}
		setDataSource(formOfPayments);
	}, [dataTable]);

	useEffect(() => {
		calculateDelivery();
	}, [dataSource]);
	
	useEffect(()=>{
		calculatePresentValueByTheClient(dataSource,deliveryDate);
	},[deliveryDate]);

	const clearAll =  (
		<>
			<lebel>Valor&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</lebel>
			<Tooltip placement="left" color="#ffffff">
				<Button
					size="small"
					onClick={() => {ClearAll()}}
					type="danger"
					icon={<DeleteOutlined />}
				/>
			</Tooltip>
		</>
		)

	const columns = [
		{
			title: "Número de cuota",
			dataIndex: "month",
			width: 80,
			render: (_, record) => split(record),
			align: 'center'
		},
		{
			title: clearAll,
			dataIndex: "value",
			width: 80,
			render: (_, record) => actions(record),
			align: 'center'
		},

		{
			title: "Porcentaje",
			dataIndex: "porcentaje",
			width: 80,
			render: (_, record) => valuates(record),
			align: 'center'
		},
	];
	
	const calculateDelivery = () => {
		let valor;
		if(dataTable.trueUnitValue==0){
		 valor=	dataTable.valueUnit
		}else{
			valor=dataTable.trueUnitValue
		}
		let total = CalculateDelivery(dataSource,valor);
		setDelivery(total);
		return total;
	};
	
	const ClearAll = () => {
		
		const arrayToWork = [...dataSource]

		arrayToWork.forEach((element,key) =>{
			if (element.month !== "Entrega"){
				element.porcentaje = calculatePor(0, dataTable.trueUnitValue)
				element.value = 0
				setData(key,0)
			}else {
				element.value = calculateDelivery()
				element.porcentaje = calculatePor(calculateDelivery(), dataTable.trueUnitValue)
			}
		})
		setDataSource(arrayToWork)
	}

	const split = (record) => {
		return (
			<>
				{record?.month === "C.I. No Financiada" ? (
					record.month
				) : record?.month === "Entrega" ? (
					record.month
				) : (
					<div style={{ width: "100%", display: "flex", textAlign: "center" }}>
						<div style={{ width: "30%", borderRight: "1px solid #f0f0f0" }}>
							{record?.key}
						</div>
						<div style={{ width: "70%" }}>{record?.month}</div>
					</div>
				)}
			</>
		);
	};

	const clean = (key) => {
		const arrayToWork = [...dataSource];
		arrayToWork.forEach((item) => {
			if (item.key === key) {
				calculateDelivery();
				item.porcentaje = calculatePor(0, dataTable.trueUnitValue);
				item.value = 0;
			}
		});
		arrayToWork.forEach((item) => {
			if (item.month === "Entrega") {
				item.porcentaje = calculatePor(calculateDelivery(), dataTable.trueUnitValue);
			}
		});
		setData(key,0)
		setDataSource(arrayToWork);
	};
	
	const setData = (key,value) => {
		const arrayToWork = [...dataSource];
		if (modify.length === 0) {
			let tem = [
				{
					month: arrayToWork[key].month,
					value: parseFloat(value),
				},
			];
			modified_values(tem)
			setModify(tem);
		} else {
			let tem = modify;
			let found = false;
			tem.forEach((item) => {
				if (item.month === arrayToWork[key].month) {
					item.value = parseFloat(parseFloat(value).toFixed(2));
					found = true;
				}
			});
			if (!found) {
				let array = {
					month: arrayToWork[key].month,
					value: value,
				};
				tem.push(array);
			}
			modified_values(tem);
			setModify(tem);
		}
	}
	
	const Modify = (event, record) => {
		const arrayToWork = [...dataSource];
		arrayToWork.forEach((item) => {
			if (item.key === record.key) {
				calculateDelivery();
				if (item.month === "C.I. No Financiada") {
					item.porcentaje = calculatePor(event, dataTable.trueUnitValue);
				} else {
					item.porcentaje = calculatePor(event, dataTable.trueUnitValue);
				}
				item.value = event;
			}
		});
		arrayToWork.forEach((item) => {
			if (item.month === "Entrega") {
				item.porcentaje = calculatePor(calculateDelivery(), dataTable.trueUnitValue);
			}
		});
		setData(record.key,record.value)
		setDataSource(arrayToWork);
	};

	const actions = (record) => {
		const text = <span style={{ color: "#6A6963" }}> Limpiar </span>;
		return (
			<>
				{record?.month === "Entrega" ? (
					FormatterPeso.format(deliveryDate) 
				) : (
					<>
						{() => {}}
						<InputNumber
							{...FORMATTER_INPUT_NUMBER}
							parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
							onChange={(value) => Modify(value, record)}
							style={{ width: "80%" }}
							name={record?.key}
							value={parseFloat(dataSource[record?.key]?.value)}
							size="small"
						/>
						<Tooltip placement="left" color="#ffffff" title={text}>
							<Button
								size="small"
								onClick={() => {
									clean(record?.key);
								}}
								type="danger"
								icon={<DeleteOutlined />}
							/>
						</Tooltip>
					</>
				)}
			</>
		);
	};
	
	const valuates = (record) => {
		return <>{parseFloat(record?.porcentaje).toFixed(2) + " %"}</>;
	};
	if (dataSource.length === 0) return null;
	return (
		<>
			<Table
				pagination={false}
				title={() => ""}
				columns={columns}
				bordered
				dataSource={convertAllMapDataState(dataSource)}
				size="small"
			/>
		</>
	);
};

export default TablePagoCliente;
