import * as constants from "../../../constants";

export const exportExcelPotentialClientsDatabase = (data, postProcessSuccess) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		data: data,
		url: `${constants.RUTA}/potential_clients_database/getPathExcelReport`,
		success: (response) => (getExcelPotentialClientsDatabase(response)),
		postProcessSuccess: postProcessSuccess
	}
});

const getExcelPotentialClientsDatabase = (data) => ({
	type: constants.GET_EXCEL_POTENTIAL_CLIENTS_DATABASE,
	payload: data
});

