import "./style.scss";
import _ from "lodash";
import React, { useContext, useEffect, useState } from "react";
import DiscountsCoupons from "./../editQuote/DiscountsCoupons";
import PropertyDataContext from "../context/PropertyDataContext";
import SelectedUnitsContext from "../context/SelectedUnitsContext";
import CommercialDiscounts from "./../editQuote/CommercialDiscounts";
import { Card, Popconfirm, Button, Tag, Checkbox, Modal } from "antd";
import {CalculateValueMeter, formatDate, TypeOfParking} from "../Utilitis/Utilitis";
import {DeleteOutlined, ArrowRightOutlined, CheckOutlined,} from "@ant-design/icons";

const tagStyle = {
	borderRadius: "10px",
	width: "90%",
	fontSize: "12px",
	margin: "-5px 0px 0px 9px",
};

const SelectedItemComponent = ({ dataSource }) => {
	const { handlePropertyData } = useContext(PropertyDataContext);
	const { handleDelete } = useContext(SelectedUnitsContext);
	const [check, setCheckSource] = useState(dataSource.check);
	const [unitValue,setUnitValue] = useState(dataSource?.unity_value);
	const [isModalVisible, setIsModalVisible] = useState(false);
	const [isModalVisible2, setIsModalVisible2] = useState(false);
	const [descuentos, setDescuentos] = useState(!dataSource.check);
	const [valueMeter2, setValueMeter2] = useState(dataSource.squareMeterValue);
	const [valueMeters, setValueMeters] = useState(0);
	const [valorDescuento, setValorDescuento] = useState(0);
	const [valorDescuentoTotal, setvalorDescuentoTotal] = useState(0);
	const [valorCupon, setValorCupon] = useState(0);
	const [response, setResponse] = useState(dataSource);

	const deleteItem = () => {
		handleDelete(dataSource.id);
	};

	useEffect(() => {
		setValueMeters(
			CalculateValueMeter(dataSource.bonus_height,dataSource.squareMeterValue,dataSource.squareMeter)
		);
	}, []);
	const handleOnChange = () => {
		const arrayToWork = _.cloneDeep(response);
		arrayToWork.ValorM2 = valueMeter2;
		arrayToWork.SubTotal = valueMeters;
		arrayToWork.check = check;
		arrayToWork.ValorInicial = dataSource?.unity_value;
		arrayToWork.valorDescuento = valorDescuento;
		arrayToWork.valorCupon = valorCupon;
		arrayToWork.valorTotal = unitValue;
		handlePropertyData(arrayToWork);
	};
	useEffect(() => {}, [check, dataSource]);
	useEffect(() => {}, [valueMeters]);

	const showModal = () => {
		setIsModalVisible(true);
	};
	const showModal2 = () => {
		setIsModalVisible2(true);
	};
	const Discount = (value, discount, checked, discountType) => {
		const arrayToWork = response;
		let calculator = parseFloat(value) * parseFloat(dataSource.squareMeter);
		let discounter = parseFloat(valorDescuento) - calculator;
		if (discountType === "Valor m2") {
			let cost = [];
			if (arrayToWork.discounts) {
				cost = arrayToWork.discounts;
			}
			cost.push(discount);
			arrayToWork.discounts = cost;
			if (checked) {
				let cut = valueMeter2;
				cut = cut - value;
				let recall = valorDescuento + calculator;

				setValueMeter2(cut);
				setValorDescuento(recall);
				setUnitValue(unitValue - recall);

				if (!descuentos) setDescuentos(true);
				setValueMeters(
					CalculateValueMeter(dataSource.bonus_height,dataSource.squareMeterValue,dataSource.squareMeter,value)
				);
			} else {
				let cut = valueMeter2;
				cut = cut + parseFloat(value);
				setUnitValue(unitValue + calculator);
				setValorDescuento(discounter);
				setValueMeter2(cut);
				if (
					check &&
					discounter === 0 &&
					valorCupon === 0 &&
					valorDescuentoTotal === 0
				) {
					setDescuentos(false);
				}
			}
		}
		if (discountType === "Valor total") {
			let cost = [];
			if (arrayToWork.discounts) {
				cost = arrayToWork.discounts;
			}
			cost.push(discount);
			arrayToWork.discounts = cost;
			if (checked) {
				let hh = parseInt(valorDescuento) + parseInt(value);
				setvalorDescuentoTotal(parseInt(value));
				setValorDescuento(parseInt(hh));
				setUnitValue(unitValue - parseInt(value));
				if (!descuentos) setDescuentos(true);
			} else {
				let hh = valorDescuento - value;
				let ds = valorDescuentoTotal - value;
				if (Number.isNaN(ds)) {
					ds = 0;
				}
				if (check && hh === 0 && ds === 0 && valorCupon === 0) {
					setDescuentos(false);
				}
				setvalorDescuentoTotal(ds);
				setUnitValue(unitValue + parseInt(value));
				setValorDescuento(hh);
			}
		}
		if (discountType === "Cupon") {
			let cost = [];
			if (arrayToWork.cupones) {
				cost = arrayToWork.cupones;
			}
			cost.push(discount);
			arrayToWork.cupones = cost;
			if (checked) {
				let des = valorCupon;
				des += parseInt(value);
				setUnitValue(unitValue - parseInt(value));
				setValorCupon(parseInt(des));
				if (!descuentos) setDescuentos(true);
			} else {
				let des = valorCupon;
				des = des - parseInt(value);
				setUnitValue(unitValue + parseInt(value));
				setValorCupon(parseInt(des));
				if (
					check &&
					valorDescuento === 0 &&
					des === 0 &&
					valorDescuentoTotal === 0
				) {
					setDescuentos(false);
				}
			}
		}
		setResponse(arrayToWork);
	};


	const handleCancel = () => {
		setIsModalVisible(false);
	};
	const handleCancel2 = () => {
		setIsModalVisible2(false);
	};

	const onChangeKit = (evenet) => {
		let finishKitValue = parseFloat(dataSource.finishKitValue);
		let UnityValue = parseFloat(unitValue);
		let totalDiscountable;
		if (evenet.target.checked) {
			setCheckSource(true);
			totalDiscountable = UnityValue + finishKitValue;
			if (valorDescuento === 0) {
				setDescuentos(false);
			}
		} else {
			totalDiscountable = UnityValue - finishKitValue;
			setDescuentos(true);
			setCheckSource(false);
		}
		setUnitValue(totalDiscountable);
	};
	const formatterPeso = new Intl.NumberFormat("es-CO", {
		style: "currency",
		currency: "COP",
		minimumFractionDigits: 2,
	});
	return (
		<>
			<Card
				headStyle={{
					color: "#31708f",
					backgroundColor: "#d9edf7",
					borderColor: "#bce8f1",
				}}
				title={dataSource.nameProject}
				extra={
					<Popconfirm title="¿Estas seguro?" onConfirm={deleteItem}>
						<Button icon={<DeleteOutlined />} type="primary" danger />
					</Popconfirm>
				}
				actions={[<Button onClick={handleOnChange} icon={<CheckOutlined />} />]}
				className="container card-selected"
			>
				<section className="grid-selected-item">
					<div className="item">
						{" "}
						<h6><b>Tipo de unidad</b></h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6>
							{dataSource.squareMeter}
							{" m²"}
						</h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6><b>Etiqueta</b></h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6>{dataSource.label}</h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6><b>Fecha estimada de entrega</b></h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6>{formatDate(dataSource.estimatedDeliveryDate)}</h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6><b>Habitaciones</b></h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6>{dataSource.numberRooms}</h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6><b>Baños</b></h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6>{dataSource.bathroomNumber}</h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6><b>Parqueadero</b></h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6>{dataSource.parking_number} { TypeOfParking(dataSource.parking) } </h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6><b>Valor m²</b></h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6>{formatterPeso.format(dataSource.squareMeterValue)}</h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6><b>Valor parqueadero</b></h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6>{formatterPeso.format(dataSource.parkingValue)}</h6>{" "}
					</div>
					{
						dataSource.finishKitValue !== "0.00"
							?(
								<>
									<div className="item">
										{" "}
										<h6><b>Valor kit de acabados</b></h6>{" "}
									</div>
									<div className="item">
										{" "}
										<h6>{formatterPeso.format(dataSource.finishKitValue)}</h6>{" "}
									</div>
								</>
							)
							:null
					}
					<div className="item">
						{" "}
						<h6><b>Valor Terraza/ cuarto útil/ otros</b></h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6>
							{formatterPeso.format(dataSource.valueOtherAdditional)}
						</h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6><b>Prima Vista</b></h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6>{formatterPeso.format(dataSource.bonus_view)}</h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6><b>Prima Altura por m2</b></h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6>{formatterPeso.format(dataSource.bonus_height)}</h6>{" "}
					</div>
					{
						dataSource.finishKitValue !== "0.00" 
							?(<>
								<div className="item">
									{" "}
									<h6><b>Incluye acabados</b></h6>{" "}
								</div>
								<div className="item">
									{" "}
									<h6>
										<Checkbox
											style={{ paddingLeft: "50%" }}
											onChange={(evenet) => onChangeKit(evenet)}
											checked={check}
										/>
									</h6>{" "}
								</div>
							</>)
							:null
					}
					<div className="item">
						<Tag style={tagStyle} color="black">
							<span>
								Haga clic aquí para ver las <br />
								políticas comerciales <ArrowRightOutlined />
							</span>
						</Tag>
					</div>
					<div className="item">
						<Button type="primary" onClick={showModal}>
							Politicas comerciales
						</Button>
					</div>
					<div className="item">
						<Tag style={tagStyle} color="black">
							<span>
								Haga click aquí para <br /> aplicar cupón <ArrowRightOutlined />
							</span>
						</Tag>
					</div>
					<div className="item">
						<Button type="primary" onClick={showModal2}>
							Cupones
						</Button>
					</div>
					{descuentos ? (
						<>
							<div className="item">
								{" "}
								<h6>Valor inicial</h6>{" "}
							</div>
							<div className="item">
								{" "}
								<h6>
									{formatterPeso.format(dataSource.unity_value)}
								</h6>{" "}
							</div>
						</>
					) : null}
					{!check && dataSource.finishKitValue !== "0.00" ? (
						<>
							<div className="item">
								{" "}
								<h6><b>Descuento acabados</b></h6>{" "}
							</div>
							<div className="item">
								{" "}
								<h6>
									{formatterPeso.format(dataSource.finishKitValue)}
								</h6>{" "}
							</div>
						</>
					) : null}

					{valorDescuento !== 0 ? (
						<>
							<div className="item">
								{" "}
								<h6><b>Descuento comerciales</b></h6>{" "}
							</div>
							<div className="item">
								{" "}
								<h6>{formatterPeso.format(valorDescuento)}</h6>{" "}
							</div>
						</>
					) : null}

					{valorCupon !== 0 ? (
						<>
							<div className="item">
								{" "}
								<h6><b>Descuentos cupones</b></h6>{" "}
							</div>
							<div className="item">
								{" "}
								<h6>{formatterPeso.format(valorCupon)}</h6>{" "}
							</div>
						</>
					) : null}

					<div className="item">
						{" "}
						<h6><b>Valor de la unidad</b></h6>{" "}
					</div>
					<div className="item">
						{" "}
						<h6><b>{formatterPeso.format(unitValue)}</b></h6>{" "}
					</div>
				</section>
			</Card>
			<Modal
				title="Descuentos Comerciales"
				visible={isModalVisible}
				onCancel={handleCancel}
				width={1000}
				footer={[
					<Button key="back" onClick={handleCancel}>
						Cerrar
					</Button>,
				]}
			>
				<CommercialDiscounts
					Discoun={Discount}
					Project={dataSource}
				/>
			</Modal>
			<Modal
				title="Descuentos Cupones"
				visible={isModalVisible2}
				onCancel={handleCancel2}
				width={1000}
				footer={[
					<Button key="back" onClick={handleCancel2}>
						Cerrar
					</Button>,
				]}
			>
				<DiscountsCoupons
					ProjectId={dataSource.projectId}
					CustomerId={dataSource.customerId}
					Precie={dataSource.unity_value}
					Discoun={Discount}
				/>
			</Modal>
		</>
	);
};

export default SelectedItemComponent;
