import * as constants from '../../constants';
import LocalStorage from '../../../views/config/LocalStorage'

let Storage = new LocalStorage();

export const fetchAllUnitType = (id) => ({
	type: constants.API,
	payload: {
		method: 'GET',
		headers: { 'access-token':Storage.getToken() },
		url: `${constants.RUTA}/unit-type/${id}`,
		success: (response) => (setAllUnitType(response))
	}
});

export const creatUnitType = (data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		url: `${constants.RUTA}/unit-type`,
		data,
		success: (unitType) => (addUnitType(unitType)),
		postProcessSuccess: onSuccess,
		postProcessError: onError
	}
});
export const unitTypeDuplicate = (id, data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		url: `${constants.RUTA}/unit-type/duplicate/${id}`,
		data,
		success: (id, dataDuplicateUnitType) => (duplicateUnitType(id, dataDuplicateUnitType)),
		postProcessSuccess: onSuccess,
		postProcessError: onError
	}
});

export const updateFilesUnitType = (id, data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		url: `${constants.RUTA}/unit-type/addAttachment/${id}`,
		data,
		postProcessSuccess: onSuccess,
		postProcessError: onError
	}
});

export const updateUnitTypeById = (id, data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: 'PUT',
		url: `${constants.RUTA}/unit-type/${id}`,
		data,
		success: (id, data) => (updateUnitType(id, data)),
		postProcessSuccess: onSuccess,
		postProcessError: onError
	}
});

export const deleteUnitTypeById = (id, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: 'DELETE',
		url: `${constants.RUTA}/unit-type/${id}`,
		success: () => (removeUnitType(id)),
		postProcessSuccess: onSuccess,
		postProcessError: onError
	}
});

export const creatUnitTypeAttachment  = (id, data, onSuccess, onError) => ({
	type: constants.API, 
	payload: {
		method: 'POST',
		url: `${constants.RUTA}/unit-type/addAttachment/${id}`,
		data,
		success: (id, attachment) => (addUnitTypeAttachment(id, attachment)),
		postProcessSuccess: onSuccess,
		postProcessError: onError
	}
});

export const deleteUnitTypeByIdAttachment = (id, unitTypeId , onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: 'DELETE',
		url: `${constants.RUTA}/unit-type/deleteAttachment/${id}/${unitTypeId}`,
		//success: () => (removeAttachment(id)),
		postProcessSuccess: onSuccess,
		postProcessError: onError
	}
});


const setAllUnitType = (data) => ({
	type: constants.SET_ALL_UNIT_TYPE,
	payload: data
});

export const resetUnitTypeByAction = () => ({
    type: constants.API,
    payload: {
		method: null,
		success: () => (resetUnitType())
	}
})

const resetUnitType = () =>({
	type:constants.RESET_UNIT_TYPE,
	payload: []
});



const addUnitType = (unitType) => ({
	type: constants.ADD_UNIT_TYPE,
	payload: unitType
});

const addUnitTypeAttachment = (attachment) => ({
	type: constants.ADD_ATTACHMENT,
	payload: attachment
});

const updateUnitType = (id, data) => ({
	type: constants.UPDATE_UNIT_TYPE,
	payload: {  id, data }
});

const duplicateUnitType = (id, data) => ({
	type: constants.DUPLICATE_UNIT_TYPE,
	payload: {  id, data }
});

const removeUnitType = (id) => ({
	type: constants.REMOVE_UNIT_TYPE,
	payload: {id}
});

const removeAttachment = (id) => ({
	type: constants.REMOVE_ATTACHMENT,
	payload: {id}
});


