import React, { useState, createContext } from "react";

const PropertyDataContext = createContext();

const initalPropertyData = {};

const PropertyDataProvider = ({ children }) => {
	const [propertyData, setPropertyData] = useState(initalPropertyData);

	const handlePropertyData = (data) => {
		setPropertyData(data);
	};
	const handlePropertyDelete = () => {
		setPropertyData({});
	};

	const data = { propertyData, handlePropertyData, handlePropertyDelete };

	return (
		<PropertyDataContext.Provider value={data}>
			{children}
		</PropertyDataContext.Provider>
	);
};

export { PropertyDataProvider };
export default PropertyDataContext;
