import React, {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {Button, Card, Col, Form, Input, InputNumber, Row, Select, Table, Tag, Upload} from "antd";
import {UploadOutlined} from "@ant-design/icons";
import {
	error,
	errorGlobal,
	errorPolicy,
	openNotificationWithIcon,
	warning
} from "../../../../../../handle/Notification/Notification";
import {ExcelRenderer} from "react-excel-renderer";
import {useForm} from "antd/lib/form/Form";
import {updateUnitsAction} from "../../../../../../../redux/actions/Project/UnitsAction";
import {FORMATTER_INPUT_NUMBER, RUTA_API_MULTIMEDIA, RUTA_FORMAT_FIDUCIARY} from "../../../../../../../redux/constants";
import ParkingSelect from "./ParkingSelect";


const UnitsFloor = ({unitsFloorDataBase}) => {
	const dispatch = useDispatch();
	
	
	

	useEffect(() => {
		let array = unitsFloorDataBase._payload
		array.map((item) => {
			item.state = item?.name === "Comprada" || item?.name === "Separada";
			if (item?.name_project_types === "Casa") {
				if (item.label === "") {
					item.label = "C-" + item.id
				}
			}
			if (item?.name_project_types === "Casa campestre") {
				if (item.label === "") {
					item.label = "CC-" + item.id
				}
			}
			if (item?.name_project_types === "Bodega") {
				if (item.label === "") {
					item.label = "B-" + item.id
				}
			}
			if (item?.name_project_types === "Lotes") {
				if (item.label === "") {
					item.label = "L-" + item.id
				}
			}
			return item
		})
		setStateUnit(array);
	}, [unitsFloorDataBase]);

	const project = useSelector((state) => state?.project);


	const [form] = useForm();

	const [stateUnit, setStateUnit] = useState([]);
	const [floorsNumber, setFloorsNumber] = useState(0);
	const [counterFloors, setCounterFloors] = useState([]);
	const [disabledSimpleParking, setDisabledSimpleParking] = useState(false);
	const [disabledDoubleParking, setDisabledDoubleParking] = useState(false);
	
	const [codeFiduciary, setCodeFiduciary] = useState([]);
	const [cargarVentasFiduciarias, setCargarVentasFiduciarias] = useState([]);
	
	useEffect(() => {
		if (codeFiduciary.length > 0) {
			if (codeFiduciary[0].hasOwnProperty("code")) {
				const arrayToWorkStateUnit = [...stateUnit];
				const arrayToWorkCodeFiduciary = [...codeFiduciary];

				arrayToWorkStateUnit.map((item, index) => {
					if (index < arrayToWorkCodeFiduciary.length) {
						item.fiduciary_code = arrayToWorkCodeFiduciary[index].code;
					}
					if (!item.square_meter_value_old) {
						item.square_meter_value_old = parseFloat(item?.square_meter_value ?? 0);
					}
					if (!item.parking_number_old) {
						item.parking_number_old = parseInt(item.parking_number ?? 0);
					}
					item.square_meter_value += parseFloat(item.bonus_height);
					item.parking = item?.parking === "null" || item?.parking === "" ? 0 : item.parking;
					return item
				});

				setStateUnit(arrayToWorkStateUnit);
			}
		}
	}, [codeFiduciary]);
	
	useEffect(() => {
		if (Array.isArray(stateUnit)) {


			for (let index = 0; index < stateUnit.length; index++) {
				setFloorsNumber(stateUnit[index]?.floors_number);
			}

			const arrayToWorkStateUnit = [...stateUnit];
			const arrayToWorkCounterFloors = [];
			
			arrayToWorkStateUnit.forEach(x => {
				if(arrayToWorkCounterFloors.findIndex(y => y.floorLabel === x.label) === -1 && x.label !== ""){
					arrayToWorkCounterFloors.push({
						floorLabel: x.label
					})
				}
			});
			setCounterFloors(arrayToWorkCounterFloors);
			stateUnit.map((item) => {
				item.area = item?.area == null ? 0.00 : parseFloat(item?.area);
				if (!item.square_meter_value_old) {
					item.square_meter_value_old = parseFloat(item.square_meter_value ?? 0);
				}
				//item.label  = item.label == "" ? ValidFloorLabel(parseInt(item?.number_floors ?? 0) * 100 + 1,arrayToWorkCounterFloors) : item.label;
				//ValidAddExistLabel(item.label,arrayToWorkCounterFloors)
				item.square_meter_value = parseFloat(item?.square_meter_value_old ?? 0) + parseFloat(item?.bonus_height ?? 0);
				item.bonus_view = parseFloat(item?.bonus_view ?? 0);
				return item
			});
		}
	}, [stateUnit]);
	
	const updateLabel = (value,record) => {
		const arrayToWorkStateUnit = [...stateUnit];
		let index = arrayToWorkStateUnit.findIndex(x => x.id === record.id);
		let state = arrayToWorkStateUnit[index];
		arrayToWorkStateUnit[index]  = {
			...state,
			label_old : state.label,
			label : ValidFloorLabel(parseInt(value))
		}
		setStateUnit(arrayToWorkStateUnit);
	}

	if (unitsFloorDataBase?.length === 0)
		return (
			<>
				<Card>
					<h3>No hay unidades registradas</h3>
				</Card>
			</>
		);
	const UploadFilesCodeFiduciary = {
		name: "file",
		action: RUTA_API_MULTIMEDIA,
		headers: {
			authorization: "authorization-text",
		},
		onChange(info) {		console.log(info);
			if (info.file.status !== "uploading") {
				for (let index = 0; index < info.fileList.length; index++) {
					const element = info.fileList[index].originFileObj;
					ExcelRenderer(element, (err, resp) => {
						if (err) {
						} else {
							let newRows = [];
							resp.rows.slice(0).map((row, index) => {
								if (row) {
									newRows.push({
										key: index,
										code: row[0],
									});
								}
								return row
							});
							if (newRows.length === 0) {
								return false;
							} else {
								setCodeFiduciary(newRows);
								return true;
							}
						}
					});
				}
			}

			if (info.file.status === "done") {
				success(`${info.file.name} se ha subido correctamente`);
			} else if (info.file.status === "error") {
				error(`${info.file.name} ha fallado.`);
			}
		},
	};
	
	const UploadFilesVentasFiduciarias = {
		name: "file",
		action: {RUTA_API_MULTIMEDIA},
		headers: {
			authorization: "authorization-text",
		},
		onChange(info) {
			
			if (info.file.status !== "uploading") {
				for (let index = 0; index < info.fileList.length; index++) {
					const element = info.fileList[index].originFileObj;
					ExcelRenderer(element, (err, resp) => {
						if (err) {
						}
						else {
							let newRows=[];
							let anotherNewRows=[];
							let totalDatos=0;
							let numero=0;
							let noNumero=0;
							if(resp.cols.length===3){
								for (let x=0;x<resp.rows.length;x++) {
									for (let y=0;y<resp.rows[x].length;y++) {
										totalDatos++;
									}
								}
								for (let x=0;x<resp.rows.length;x++) {
									for (let y=0;y<resp.rows[x].length;y++) {
										if (Number.isInteger(resp.rows[x][y])){
											numero++;
										} else {
											noNumero++;
											if(noNumero===1){
												info.file.status = "error";
												noNumero=0;
												setCargarVentasFiduciarias([]);
											return 	error(`${info.file.name} ha fallado.`);
											}
										}
									}
								}
							
								if(numero===totalDatos){
								
									setCargarVentasFiduciarias(resp);
									resp.rows.slice(0).map((row, index) => {
										if (row) {
											newRows.push({
												key: index,
												code: row[0],
											});
										}
										return row
									});
									
									if(stateUnit?.length===0){
										setCargarVentasFiduciarias(newRows);
									}else {
										for(var fi of newRows){
											if(stateUnit.findIndex(x => parseInt(x.fiduciary_code) == parseInt(fi.code)) == -1){
												anotherNewRows.push(fi);
											}
									}
										info.file.status = "done";
										setCargarVentasFiduciarias(anotherNewRows);
										return success(`${info.file.name} archivo correcto.`);
									}
								}
							} else {
								info.file.status = "error";
								warning(`${info.file.name} ha fallado.`)
							}
						}
					});
				}
			}
			if (info.file.status === "removed") {
				setCargarVentasFiduciarias([]);
			}
			/*
			if (info.file.status === "done") {
				return success(`${info.file.name} archivo correcto.`);
			} else if (info.file.status === "error") {
				error(`${info.file.name} ha fallado.`);
			}*/
		},

	};

	const selectFloorsNumber = [];

	for (let index = 0; index < floorsNumber; index++) {
		let option = {
			id: index + 1,
			name: index + 1,
		};
		selectFloorsNumber.push(option);
	}

	const formatterPeso = new Intl.NumberFormat("es-CO", {
		style: "currency",
		currency: "COP",
		minimumFractionDigits: 0,
	});

	
	const ValidFloorLabel = (value, arrayToWorkCounterFloors = null) => {
		value = parseInt(value);
		arrayToWorkCounterFloors = arrayToWorkCounterFloors ??  [...counterFloors];
		if(arrayToWorkCounterFloors.findIndex(x => x.floorLabel ===  value)>-1){
			value = value+1;
			return ValidFloorLabel(value,arrayToWorkCounterFloors);
		}
		return value;
	};
	
/*	const ValidAddExistLabel = (value, arrayToWorkCounterFloors = null) => {
		arrayToWorkCounterFloors = arrayToWorkCounterFloors ??  [...counterFloors];
		if(arrayToWorkCounterFloors.findIndex(y => y.floorLabel === value) === -1 && value !== ""){
			arrayToWorkCounterFloors.push({
				floorLabel: value
			})
		}
		setCounterFloors(arrayToWorkCounterFloors);
	};
*/	
	
	const assignFloorNumber = (value, record) => {
		if (value !== "") {
			const arrayToWorkStateUnit = [...stateUnit];
			const arrayToWorkCounterFloors = [...counterFloors];
			let index = arrayToWorkStateUnit.findIndex(x => x.id === record.id);
			let state = arrayToWorkStateUnit[index];
			let indexCounterFloors = arrayToWorkCounterFloors.findIndex(y => y.floorLabel === record.label);
			arrayToWorkStateUnit[index]  = {
				...state,
				label : ValidFloorLabel(value * 100 + 1),
				number_floors : value
			}

			if(indexCounterFloors>-1){
				arrayToWorkCounterFloors[indexCounterFloors] ={
					floorLabel: record.label
				};
			}
			setCounterFloors(arrayToWorkCounterFloors);
			setStateUnit(arrayToWorkStateUnit);
		}
	};

	const updateBonusHeight = (value, record) => {
		const arrayToWorkStateUnit = [...stateUnit];
		let index = arrayToWorkStateUnit.findIndex(x => x.id === record.id);
		let state = arrayToWorkStateUnit[index];
		arrayToWorkStateUnit[index]  = {
			...state,
			bonus_height : value,
			square_meter_value : state.square_meter_value_old + state.bonus_height
		}
		setStateUnit(arrayToWorkStateUnit);
	};

	const updateParkingLocation = (value, record) => {
		const arrayToWorkStateUnit = [...stateUnit];
		let index = arrayToWorkStateUnit.findIndex(x => x.id === record.id);
		let state = arrayToWorkStateUnit[index];
		arrayToWorkStateUnit[index]  = {
			...state,
			parking_location : value,
		}
		setStateUnit(arrayToWorkStateUnit);
	};

	const updateParking = (value, record) => {
		const arrayToWorkStateUnit = [...stateUnit];
		let index = arrayToWorkStateUnit.findIndex(x => x.id === record.id);
		let state = arrayToWorkStateUnit[index];
		arrayToWorkStateUnit[index]  = {
			...state,
			parking : parseInt(value != "" ?  value : 0),
		}
		record.parking = parseInt(value != "" ?  value : 0)
		setStateUnit(arrayToWorkStateUnit);
		updateParkingNumber(record.parking_number, record);
	};

	const calculateParkingValue = (item) => {
		let simple_parking_value = item?.simple_parking_value ?? 0;
		let double_parking_value = item?.double_parking_value ?? 0;
		let parking_number = parseInt(item?.parking_number ?? 0);
		return parseInt(item?.parking) === 1
			? parseFloat(simple_parking_value) * parking_number
			: parseFloat(double_parking_value) * parking_number;
	}

	const calculateTotalValueUnit = (item) => {
		let square_meter_value = parseFloat(item?.square_meter_value ?? 0)
		let area = parseFloat(item?.area ?? 0)
		let value_other_additional = parseFloat(item?.value_other_additional ?? 0)
		let finish_kit_value = parseFloat(item?.finish_kit_value ?? 0);
		item.Valor = (square_meter_value * area) + calculateParkingValue(item) + finish_kit_value + value_other_additional
		return item.Valor
		
	};

	const calculateTotalValue = () => {
		let value = 0;
		stateUnit.forEach((item) => {
			value += calculateTotalValueUnit(item);
		});
		return value;
	};

	const calculateCurrentNumberDoubleParking = (arrayToWorkStateUnit) => {
		let currentNumberDoubleParking = 0;
		arrayToWorkStateUnit.forEach((item) => {
			if (parseInt(item.parking) === 2) {
				currentNumberDoubleParking += item.parking_number;
			}
		});
		return currentNumberDoubleParking;
	};

	const calculateCurrentNumberSimpleParking = (arrayToWorkStateUnit) => {
		let currentNumberSimpleParking = 0;
		arrayToWorkStateUnit.forEach((item) => {
			if (parseInt(item.parking) === 1) {
				currentNumberSimpleParking += item.parking_number;
			}
		});
		return currentNumberSimpleParking;
	};
	
	const updateParkingNumber = (value, record) => {
		const arrayToWorkStateUnit = [...stateUnit];
		let index = arrayToWorkStateUnit.findIndex(x => x.id === record.id);
		let state = arrayToWorkStateUnit[index];
		let resetNumber = 0;
		if (parseInt(record.parking) === 2) {
			arrayToWorkStateUnit[index]  = {
				...state,
				parking_number : value ?? 1,
				valor : calculateTotalValueUnit(state)
			}
			if (calculateCurrentNumberDoubleParking(arrayToWorkStateUnit) >record.double_parking_number) {
				arrayToWorkStateUnit[index]  = {
					...state,
					parking_number : resetNumber,
					valor : calculateTotalValueUnit(state)
				}
				setDisabledDoubleParking(true);
				errorPolicy("Límite de parqueadores dobles alcanzado");
			}
			if (calculateCurrentNumberDoubleParking(arrayToWorkStateUnit) < record.double_parking_number) {
				setDisabledDoubleParking(false);
			}
		} else {
			arrayToWorkStateUnit[index]  = {
				...state,
				parking_number : value ? value : 1,
				valor : calculateTotalValueUnit(state)
			}
			if (calculateCurrentNumberSimpleParking(arrayToWorkStateUnit) >record.simple_parking_number) {
				arrayToWorkStateUnit[index]  = {
					...state,
					parking_number : resetNumber,
					valor : calculateTotalValueUnit(state)
				}
				setDisabledSimpleParking(true);
				errorPolicy("Límite de parqueadores simples alcanzado");
			}
			if (calculateCurrentNumberSimpleParking(arrayToWorkStateUnit) <record.simple_parking_number) {
				setDisabledSimpleParking(false);
			}
		}
		setStateUnit(arrayToWorkStateUnit);
	};
	const columns2 = [
		{
			title: "Códigos no Aceptados",
			dataIndex: "code",
			key: 'code',
			width: 10,
			align: "center",
			fixed: 'left',
		}];
	
	const columns = [
		{
			title: "#",
			dataIndex: "id",
			key: 'id',
			width: 50,
			align: "center",
			fixed: 'left',
			sorter: (a, b) => a.id - b.id,
			render: (text, record) => <span>{stateUnit.indexOf(record) + 1}</span>,
		},
		{
			title: "Tipo unidad",
			dataIndex: "type_unit",
			Key: 'type_unit',
			align: "center",
			width: 50,
			defaultSortOrder: 'asc',
			sorter: (a, b) => a.type_unit - b.type_unit,
		},
		{
			title: "Tipo",
			dataIndex: "area",
			Key: 'area',
			align: "center",
			width: 70,
			defaultSortOrder: 'descend',
			sorter: (a, b) => a.area - b.area,
		},
		{
			title: "Piso",
			align: "center",
			dataIndex: "number_floors",
			Key: 'number_floors',
			defaultSortOrder: 'asc',
			sorter: (a, b) => a.number_floors - b.number_floors,
			render: (_, record) => (
				<Select
					name="number_floors"
					defaultValue={parseInt(record.number_floors)}
					value={parseInt(record.number_floors)}
					disabled={record?.name_project_types !== "Edificio" || record?.state}
					onChange={(value) => {
						assignFloorNumber(value, record);
					}}
				>
					<Select.Option value={""}>Seleccione</Select.Option>
					{selectFloorsNumber.length !== 0 &&
					selectFloorsNumber.map((item) => (
						<Select.Option key={item.id} value={item.id}>
							{item.name}
						</Select.Option>
					))}
				</Select>
			),
			width: 70,
		},
		{
			title: "Etiqueta",
			dataIndex: "label",
			align: "center",
			Key: 'label',
			render: (_, record) => (
				<InputNumber
					
					name="label"
					value={record?.label === "none" ? "Etiqueta" : record?.label}
					disabled={record?.state}
					style={{width: "100%"}}
					min={101}
					max={record.floors_number * 100  +1}
					onChange={(value) => {
						updateLabel(value, record);
					}}
				/>
			),
			width: 70,
		},
		{
			title: "Código Fiducia",
			align: "center",
			dataIndex: "fiduciary_code",
			Key: 'fiduciary_code',
			width: 80,
		},
		{
			title: "Valor m2",
			align: "center",
			dataIndex: "square_meter_value",
			Key: 'square_meter_value',
			width: 100,
			render: (_, record) => (
				<span style={{fontSize: 13}}>
					{" "}
					{formatterPeso.format(record?.square_meter_value)}{" "}
				</span>
			),
		},
		{
			title: "Prima de Vista",
			align: "center",
			dataIndex: "bonus_view",
			key: 'bonus_view',
			render: (_, record) => (
				<InputNumber
					disabled={true}
					min={0}
					name="primaVista"
					value={record?.bonus_view}
					style={{width: "100%"}}
				/>
			),
			width: 100,
		},
		{
			title: "Prima de Altura por m2",
			dataIndex: "bonus_height",
			Key: 'bonus_height',
			align: "center",
			render: (_, record) => (
				<InputNumber
					{...FORMATTER_INPUT_NUMBER}
					parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
					name="primaAlturam2"
					value={record?.bonus_height ? record?.bonus_height : 0}
					disabled={record?.state}
					style={{width: "100%"}}
					onChange={(value) => {
						updateBonusHeight(value, record);
					}}
				/>
			),
			width: 110,
		},
		{
			title: "Parqueadero",
			dataIndex: "parking",
			Key: 'parking',
			align: "center",
			render: (text, record) => (
				<ParkingSelect
					key={record?.id}
					disabledSimpleParking={disabledSimpleParking}
					disabledDoubleParking={disabledDoubleParking}
					updateParking={updateParking}
					record={record}
				/>
			),
			width: 90,
		},
		{
			title: "Cantidad",
			dataIndex: "parking_number",
			Key: 'parking_number',
			align: "center",
			render: (_, record) => (
				<InputNumber
					onChange={(value) => {
						updateParkingNumber(value, record);
					}}
					min={0}
					disabled={record?.state}
					name="parking_number"
					value={record?.parking_number ?? 0}
					style={{width: "100%"}}
				/>
			),
			width: 75,
		},
		{
			title: "Ubicación Parqueadero",
			dataIndex: "parking_location",
			Key: 'parking_location',
			align: "center",
			render: (_, record) => (
				<Select
					onChange={(value) => {
						updateParkingLocation(value, record);
					}}
					disabled={record?.state}
					defaultValue={record?.parking_location ? record?.parking_location : ""}
					name="parking_location"
				>
					<Select.Option value="">Seleccion</Select.Option>
					<Select.Option value={1}>Sótano</Select.Option>
					<Select.Option value={2}>Lobby</Select.Option>
				</Select>
			),
			width: 90,
		},
		{
			title: "Estado",
			dataIndex: "name",
			Key: 'name',
			align: "center",
			width: 90,
			render: (_, record) => {
				switch (record?.name) {
					case "Separada":
						return <Tag color={"yellow"}> {record?.name} </Tag>;
					case "Comprada":
						return <Tag color={"red"}> {record?.name} </Tag>;
					default:
						return <Tag color={"green"}> {record?.name} </Tag>;
				}
			},
		},
		{
			title: "Valor",
			dataIndex: "valor",
			Key: 'valor',
			align: "center",
			fixed: "right",
			render: (_, record) => {
				return (
					<span style={{fontSize: 13}}>
						{formatterPeso.format(calculateTotalValueUnit(record))}
					</span>
				);
			},
			width: 150,
		},
		
	];

	const success = (response) => {
		openNotificationWithIcon("success", response);
	};

	const cleanDtoToSend = (array) => {
		let arrayWork = JSON.parse(JSON.stringify(array));

		arrayWork.map((item) => {
			delete item.area;
			delete item.area_value;
			delete item.double_parking_number;
			delete item.floors_number;
			delete item.name;
			delete item.number_days_separated;
			delete item.project_id;
			delete item.simple_parking_number;
			item.label = String(item.label);
			item.fiduciary_code = String(item.fiduciary_code);
			item.parking = String(item.parking)
			item.bonus_height = item.bonus_height ?? 0
			item.quote_meter_value = item.square_meter_value
			item.square_meter_value = item.square_meter_value_old
			return item
		});

		return arrayWork;
	};

	const downloadFormat = () => {
		let tagA = document.createElement("a");
		tagA.href = RUTA_FORMAT_FIDUCIARY
		tagA.download = "Formato_codigos_Fiduciarios.xls";
		tagA.dispatchEvent(new MouseEvent("click"));
	};

	const onFinish = () => {
		const dto = cleanDtoToSend(stateUnit)
		dispatch(
			updateUnitsAction(dto, (response) => {
				if (response.error) {
					errorGlobal("Hay campos vacios");
				} else {
					success("Ha sido actualizado correctamente");
				}
			})
		);
	};

	return (
		<>
			<Card
				title="Especificación de unidades por piso"
				style={{borderColor: "black"}}
			>
				<Form
					layout="vertical"
					form={form}
					onFinish={onFinish}
					style={{marginLeft: "25px", marginRight: "25px"}}
				>
					<Row gutter={16}>
						<Col span={10}>
							<Input.Group compact>
								<Form.Item label="Cargar Códigos Fiduciarios">
									<Upload
										name="cargarCodigosFiduciarios"
										accept=".xls, .xlsx"
										value={codeFiduciary}
										{...UploadFilesCodeFiduciary}
									>
										<Button icon={<UploadOutlined/>}>Subir Archivo</Button>
									</Upload>
								</Form.Item>
							</Input.Group>
						</Col>
						<Col span={10}>
							<Input.Group compact>
								<Form.Item label="Cargar Ventas Fiduciarias">
									<Upload
										name="cargarVentasFiduciarias"
										//value={cargarVentasFiduciarias}

										{...UploadFilesVentasFiduciarias}
									>
										<Button icon={<UploadOutlined/>}>
											Subir Archivo
										</Button>
									</Upload>
								</Form.Item>
							</Input.Group>
						</Col>
						<Col span={4}>
							<Form.Item>
								<Button
									style={{
										backgroundColor: "#5cb85c",
										borderColor: "#5cb85c",
									}}
									type="primary"
									htmlType="button"
									onClick={downloadFormat}
								>
									Descargar Formato
								</Button>
							</Form.Item>
						</Col>
					</Row>
					<Row gutter={16}>
						<Col span={5}>
							{
								cargarVentasFiduciarias?.length>0 && (
									<Table
										columns={columns2}
										dataSource={cargarVentasFiduciarias}
										size="small"
										pagination={{  defaultPageSize: 1000, showSizeChanger: true}}
									/>
								)
							}
						</Col>
					</Row>
					<br></br>
					<Form.Item>
						{stateUnit?.length && (
							<Table
							columns={columns}
							dataSource={stateUnit}
							size="small"
							scroll={{x: 1200}}
							pagination={{  defaultPageSize: 1000, showSizeChanger: true}}
						/>)}
					</Form.Item>
					<Col>
						<Row style={{marginTop: "0%", marginBottom: 10}}>
							<Col span={4}>
								<h4>Valor Total:</h4>
							</Col>
							<Col span={4}>
								<h4>{formatterPeso.format(calculateTotalValue())}</h4>
							</Col>
						</Row>
						<Row style={{marginTop: "0%", marginBottom: 10}}>
							<Col span={6}>
								<h4>Número de registros:</h4>
							</Col>
							<Col span={2}>
								<h4>{stateUnit?.length}</h4>
							</Col>
						</Row>
					</Col>

					<Row>
						<Col offset={20} span={4}>
							<Form.Item>
								<Button
									disabled={JSON.parse(localStorage.stateDisable)}
									htmlType="submit"
									type="primary"
								>
									Guardar
								</Button>
							</Form.Item>
						</Col>
					</Row>
				</Form>
			</Card>
		</>
	);
};

export default UnitsFloor;
