export let Months;
Months = {
	0: "Enero",
	1: "Febrero",
	2: "Marzo",
	3: "Abril",
	4: "Mayo",
	5: "Junio",
	6: "Julio",
	7: "Agosto",
	8: "Septiembre",
	9: "Octubre",
	10: "Noviembre",
	11: "Diciembre",
};
export let FormatterPeso;
FormatterPeso = new Intl.NumberFormat("es-CO", {
	style: "currency",
	currency: "COP",
	minimumFractionDigits: 2,
});
export function ParkingValue( parking, sale_parameters ) {
	return parking === 1
		? parseInt(sale_parameters.simple_parking_value)
		: parseInt(sale_parameters.double_parking_value);
}

export function formatDate(date) {
	let fetch = new Date(date);
	let mes = fetch.getMonth();
	let dia = fetch.getDay();
	let ano = fetch.getFullYear();
	return dia +" de "+Months[mes]+" de "+ano;
}

export function TypeOfParking(parking) {
	return parking === "1"
		? "Sencillo"
		: "Doble";
}
export function CalculateValueMeter(bonusHeight,squareMeterValue,meter2,discount) {
	let meterValue;
	if (discount !== undefined) {
		meterValue = (parseInt(squareMeterValue) - parseInt(discount)) * parseFloat(meter2);
	} else {
		meterValue = parseInt(squareMeterValue) * parseFloat(meter2);
	}
//	console.log('bonusHeight ',bonusHeight,'squareMeterValue ',squareMeterValue,'meter2 ',meter2,'discount ',discount)
	return meterValue;
}

export function CreateBudget(parkingValue,finishValue,valueAdditional,check,totalDiscountable,valueMeters,couponValue) {
	let totalValue = valueMeters + parkingValue + valueAdditional;
	if (check) {
		totalValue = totalValue + finishValue;
	}
	if (totalDiscountable !== 0) {
		totalValue = totalValue - totalDiscountable;
	}
	if (couponValue !== 0) {
		totalValue = totalValue - couponValue;
	}
	return totalValue;
}

export function CalculateDate(estimatedDeliveryDate,maximumNumberMonthsFinanceInitialFee,constructionStartDate) {
	let date = new Date(estimatedDeliveryDate);
	let constructionStartDates = new Date(constructionStartDate);
	let local = new Date();
	let month = local.getMonth();
	let year = local.getFullYear();
	let difference = date.getFullYear() * 12 + date.getMonth() - (local.getFullYear() * 12 + local.getMonth());
	if (difference <= 0) {
		difference = 1;
	}
	if (difference > maximumNumberMonthsFinanceInitialFee) {
		difference = maximumNumberMonthsFinanceInitialFee;
	}
	if (constructionStartDates)
	return {
		diferencia: difference,
		month: month,
		year: year,
	}
}

export function CalculatePor(valor, valorTotal){
	let percentage = (parseInt(valor) * 100) / parseInt(valorTotal);
	if (valorTotal === 0) return 0;
	if (valor === 0 || valorTotal === 0) return 0;
	return percentage;
}
export function Calculate(valor,valorMes,cantidadActual){
	let valorNuevo = valor - valorMes
	valorNuevo = Math.abs(valorNuevo)
	if (valor < valorMes){
		return cantidadActual + valorNuevo
	}
	if (valor > valorMes){
		if (valorMes === 0){
			return cantidadActual + valorNuevo
		}else {
			return cantidadActual - valorNuevo
		}
	}
	return cantidadActual
}