import moment from 'moment';
import {useDispatch} from 'react-redux';
import React, {useState, useEffect} from 'react';
import Spinner from '../../../../handle/Spinner/Spinner';
import {dateFormat} from "../../../../handle/HandleDatePicker/HandleDate";
import {errorGlobal, success } from "../../../../handle/Notification/Notification";
import {Button, Select, Form, Row, Col, InputNumber, Divider, Input, DatePicker} from "antd";
import { getAllTrustCollections, updateTrustCollection } from '../../../../../redux/actions/Collection/TrustCollectionAction';

const CompostAdjustment = ({data, onClose}) => {
	const [stateLoading, setStateLoading] = useState(false);
	const [pressBottonState, setPressBottonState] = useState(false);

	const dispatch = useDispatch();
	const [form] = Form.useForm();

	useEffect(() => {
		fillFields();
	}, [])

	function bottonActive() {
		setPressBottonState(false)
	}

	const fillFields = () => {
		form.setFieldsValue({
			consignmentDate: moment(data.consignment_date),
			consignment_amount: data.consignment_amount,
			consignmentConcept: data.concept,
			AdjustmentConcept: data?.adjustment_concept
		})
	};

	const validateMessages = {
		// eslint-disable-next-line no-template-curly-in-string
		required: '${label} es requerido',
		types: {
			// eslint-disable-next-line no-template-curly-in-string
			email: '${label} no es un correo válido',
			// eslint-disable-next-line no-template-curly-in-string
			number: '${label} debe ser un número'

		}
	};

	const onFinish = async () => {
		setPressBottonState(true);
		setStateLoading(true);
		const values = await form.validateFields();
		const dto = {
			...values,
			consignment_date: values["consignmentDate"].format(dateFormat),
			consignment_amount: parseFloat(values["consignment_amount"])
		}
		delete dto.consignmentDate;

		let formData = new FormData();

		formData.append('date_consignment', dto.consignment_date);
		formData.append('concept', dto.consignmentConcept);
		formData.append('value', parseFloat(dto.consignment_amount));
		formData.append('adjustment_concept', dto.AdjustmentConcept);

		dispatch(updateTrustCollection(data.id, dto, (response) => {
			if (!response.error) {
				dispatch(getAllTrustCollections())
				setStateLoading(false);
				success("¡Fiducia actualizada!");
				setPressBottonState(false);
				onReset();
				onClose(false);
			} else {
				errorGlobal("¡UPS! Ocurrió un Error en verificación de Abono");
				setPressBottonState(false);
			}
		}));


	};
	const onReset = () => {
		form.resetFields();
	}


	return (
		<>

			<Form
				layout="vertical"
				form={form}
				size='middle'
				onFinish={onFinish}
				validateMessages={validateMessages}
			>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item
							name='consignmentDate'
							label="Fecha de consignación"
							rules={[{required: true}]}
						>
							<DatePicker
								onChange={bottonActive}
								value={form.getFieldValue("consignmentDate")}
								style={{width: '100%'}}
							/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item
							name="consignment_amount"
							label="Valor de consignación"
							rules={[{required: true}]}
						>
							<InputNumber
								onChange={bottonActive}
								value={form.getFieldValue("consignment_amount")}
								style={{width: '100%'}}/>
						</Form.Item>
					</Col>
				</Row>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item
							name="consignmentConcept"
							label="Concepto de consignación"
							rules={[{required: true}]}
						>
							<Select
								onChange={bottonActive}
								placeholder="Escoja el concepto"
								value={form.getFieldValue("consignmentConcept")}
							>
								<Select.Option value=" ">Seleccione...</Select.Option>
								<Select.Option value={"Abono"}>Abono</Select.Option>
								<Select.Option value={"Reformas"}>Reformas</Select.Option>
								<Select.Option value={"Canje"}>Canje</Select.Option>
								<Select.Option value={"Otros"}>Otros</Select.Option>
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item name='AdjustmentConcept' label="Concepto ajuste" rules={[{required: true}]}>
							<Input.TextArea
								onChange={bottonActive}
								value={form.getFieldValue("AdjustmentConcept")}
							/>
						</Form.Item>
					</Col>
				</Row>
				<Divider/>
				<Row>
					<Col offset={20} xs={24} sm={24} md={4} lg={4} xl={2}>
						<Form.Item style={{margin: '0'}}>
							<Button disabled={pressBottonState} type="primary" htmlType="submit">
								Guardar
							</Button>
						</Form.Item>
					</Col>
					{stateLoading ? <Col xs={2} sm={2} md={2} lg={2} xl={2}><Spinner/></Col> : null}
				</Row>
			</Form>
		</>
	);
}

export default CompostAdjustment;