import React from 'react';
import { Modal } from 'antd';
import CompostAdjustment from './CompostAdjustment';

const ModalAdjustmentCollection = ({ data, onClose, onCancel}) => {
    return(
        <>
        <Modal
            centered
            visible={true}
            width={900}
            footer={false}
            destroyOnClose={true}
            onCancel={onCancel}
        >
            <CompostAdjustment data={data} onClose={onClose}/>
        </Modal>
        </>
    );
}

export default ModalAdjustmentCollection;