import {dateFormat} from "../../../../../handle/HandleDatePicker/HandleDate";

let fData;

export async function getRequest(form,document,sale_id) {
	fData = new FormData();
	let values = await form.validateFields();
	values.is_deed_sale = (values.is_deed_sale === undefined) ? false : values.is_deed_sale;
	delete values["customer"];
	delete values["project"];
	delete values["unit"];
	
	fData.append('sale_id', sale_id);
	fData.append(`document`, document);
	appendDateFormData(values["date_outlay"],'date_outlay');
	appendDateFormData(values["date_deed"],'date_deed');
	appendDateFormData(values["date_peace_and_safety"],'date_peace_and_safety');
	appendDateFormData(values["date_delivery_property"],'date_delivery_property');
	appendDateFormData(values["date_legalization"],'date_legalization');
	appendDateFormData(values["date_log"],'date_log');
	fData.append("builder_credit_payment", values["builder_credit_payment"] || 0);
	fData.append("deed_value", values["deed_value"] || 0);
	fData.append("owner", values["owner"] || '');
	fData.append("comments", values["comments"] || '');
	fData.append("is_deed_sale", values["is_deed_sale"] ? 1 : 0);
	return fData;
}

function appendDateFormData(value,name) {
	if(value){
		fData.append(name, value.format(dateFormat));
	}
}