import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { SendOutlined } from "@ant-design/icons";
import { Form, Input, Button, Row, Col, Modal, Tooltip } from 'antd';
import { errorGlobal, success } from "../../../../handle/Notification/Notification";
import { sendDirectCollection } from '../../../../../redux/actions/Collection/DirectCollectionAction';

const layout = {
	labelCol: {
		span: 11,
	},
	wrapperCol: {
		span: 20,
	},
};


const SendEmail = (data) => {

	const dispatch = useDispatch();
	const [isModalVisible, setIsModalVisible] = useState(false);
	const [pressBottonState, setPressBottonState] = useState(false);
	const showModal = () => {
		setIsModalVisible(true);
	};

	const [form] = Form.useForm();

	const onFinish = (fieldsValue) => {
		return  {
			...fieldsValue,
			'date-picker': fieldsValue['date-picker'].format('YYYY-MM-DD'),
		};
	};
	

	const sendEmail  = async () => {
		setPressBottonState(true);
		dispatch(
			sendDirectCollection(data.data.id, async(response) => {
				const res = await response;
				res.error ? errorGlobal("¡UPS! Ocurrió Un error al momento de enviar el correo") : success("Correo enviado")
				setPressBottonState(false);
				setIsModalVisible(false)
			})
		)
	};

	const fillFields = () => {
		showModal();
		form.setFieldsValue({
			customer_name: data.data.name,
			project_name: data.data.project_name,
			unit_label: data.data.unit_label
		})
	}

	const textEmail = <span style={{ color: '#6A6963' }}> Enviar a Contabilidad </span>

	return (
		<>
			<Tooltip placement="left" color='#ffffff' title={textEmail}>
				<Button style={{background: '#ff4d4f', color: '#fff'}} size={'small'} onClick={fillFields} icon={<SendOutlined/>}/>
			</Tooltip>
			<Modal
				title={'Enviar email'}
				centered
				visible={isModalVisible}
				footer={false}
				onCancel={() => setIsModalVisible(false)}
				width={900}
			>
				<Form form={form} layout="vertical"  {...layout} name="nest-messages" onFinish={onFinish}>
					<Row gutter={16}>
						<Col xs={24} sm={24} md={8} lg={8} xl={8}>
							<Form.Item name="customer_name" label="Nombre del cliente">
								<Input placeholder='Nombre cliente' style={{width: '100%'}} disabled={true}/>
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={8} lg={8} xl={8}>
							<Form.Item name="project_name" label="Proyecto">
								<Input placeholder='Nombre proyecto' style={{width: '100%'}} disabled={true}/>
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={8} lg={8} xl={8}>
							<Form.Item name="unit_label" label="Unidad">
								<Input placeholder='Unidad' style={{width: '100%'}} disabled={true} />
							</Form.Item>
						</Col>
					</Row>
					<Row gutter={16}>
						<Col xs={24} sm={24} md={24} lg={24} xl={24}>
							<Form.Item name='data' label=" ">
								<Input.TextArea disabled={true} />
							</Form.Item>
						</Col>
					</Row>
					<Row>
						<Col offset={18} xs={24} sm={24} md={4} lg={4} xl={4}>
							<Form.Item >
								<Button loading={pressBottonState} type="primary" htmlType="submit" onClick={sendEmail}>
									Enviar email
								</Button>
							</Form.Item>
						</Col>
					</Row>
				</Form>
			</Modal>
		</>
	);
}

export default SendEmail;