import * as constants from "../../constants";

export const createTrustCollection = (data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		url: `${constants.RUTA}/trust-funds`,
		data,
		success: (trustCollection) => (addTrustCollection(trustCollection)),
		postProcessSuccess: onSuccess,
		postProcessError: onError
	}
});

export const getAllTrustCollections = (onSuccess) => ({
	type: constants.API,
	payload: {
		method: "GET",
		url: `${constants.RUTA}/trust-funds`,
		headers: constants.TOKEN,
		success: (reseponse) => allTrustCollections(reseponse),
		postProcessSuccess: onSuccess,
	},
});
export const getAllLastPaymentFiduciaryCollections = (onSuccess) => ({
	type: constants.API,
	payload: {
		method: "GET",
		url: `${constants.RUTA}/trust-funds/last-payment-fiduciary`,
		headers: constants.TOKEN,
		success: (reseponse) => allLastPaymentFiduciaryCollections(reseponse),
		postProcessSuccess: onSuccess,
	},
});
export const updateTrustCollection = (id, data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: 'PUT',
		url: `${constants.RUTA}/trust-funds/${id}`,
		data,
		success: (id, data) => (updateTrust(id, data)),
		postProcessSuccess: onSuccess,
		postProcessError: onError
	}
});
const addTrustCollection = (trustCollection) => ({
	type: constants.ADD_TRUST_COLLECTION,
	payload: trustCollection
});
const updateTrust = (directId, data) => ({
	type: constants.UPDATE_TRUST_COLLECTION,
	payload: { directId, data }
});
const allTrustCollections = (trustCollection) => ({
	type: constants.GET_ALL_TRUST_COLLECTION,
	payload: trustCollection,
});
const allLastPaymentFiduciaryCollections = (something) => ({
	type: constants.LAST_PAYMENT_FIDUCIARY,
	payload: something,
});