import { useState} from 'react';

export const useCoupons=()=> {
    const [code, setCode] = useState("");
    const [customer_id, setCustomer] = useState("");
    const [creator_user_id, setCreatorUser] = useState("");
    const [project_id, setProject] = useState("");
    const [expiration_code, setExpirationCode] = useState("");
    const [coupon_type, setCouponType] = useState("");
    const [discount_type , setDiscountType]=useState("");
    const [discount_percentage , setDiscountPercentage]=useState(0);
    const [discount_value , setDiscountValue]=useState(0);


    return {
        code, setCode,
        customer_id, setCustomer,
        project_id, setProject,
        creator_user_id,  setCreatorUser,
        expiration_code, setExpirationCode, 
        coupon_type, setCouponType, 
        discount_type, setDiscountType,
        discount_percentage, setDiscountPercentage,
        discount_value, setDiscountValue
    };
}