import * as constants from "../../constants";

export default function saleLegalization(state = [], action) {
	switch (action.type) {
		case constants.SET_ALL_SALE_LEGALIZATION:
			return action.payload;
		case constants.UPDATE_OR_CREATE_SALE_LEGALIZATION:
			return state._payload.map((item) => {
				if (item._id === action.payload.saleLegalizationId)
					return { ...item, ...action.payload.data };
				else return item;
			});
		case constants.REMOVE_SALE_LEGALIZATION:
			return state._payload.filter((item) => item._id !== action.payload);
		case constants.RESET_SALE_LEGALIZATION:
			return action.payload;
		default:
			return state;
	}
}