import { Modal,  Spin } from 'antd';
import SaleDetail from './SaleDetail';
import React, { useEffect } from 'react';
import ListOfRequest from './ListOfRequest';
import { shallowEqual, useDispatch, useSelector } from 'react-redux';
import { errorGlobal } from '../../../../handle/Notification/Notification';
import { startFetchingAllReformRequest } from '../../../../../redux/actions/Price/ReformRequest/ReformRequestBySaleAction';
import { reformsRequestBySaleSel } from '../../../../../redux/reducers/Price/ReformRequest/Selectors/ReformRequestSelector';
import { clearStateReformRequest, fetchAllReformRequest } from '../../../../../redux/actions/Price/ReformRequest/ReformRequestBySaleAction';

const ModalDetailReform = ({  visibleModal, handleCancel, onClose, idSale }) => {
	const dispatch = useDispatch();

	const reformsRequestBySale = useSelector(reformsRequestBySaleSel, shallowEqual)

	const updateListReformRequest = () => {
		dispatch(startFetchingAllReformRequest())
		dispatch(fetchAllReformRequest(idSale))
	}
	
	useEffect(() => {
		updateListReformRequest()
		return () => {
			dispatch(clearStateReformRequest())
		}
	}, []);

	const showErrorMessage = (text)=>{
		errorGlobal(text)
	}
	
	useEffect(()=>{
		if(reformsRequestBySale?.errorAddReformRequest){
			(showErrorMessage('No fue posible agregar la solicitud'))
		}
		if(reformsRequestBySale?.errorUpdatingReformRequest){
			(showErrorMessage('No fue posible actualizar los datos de la solicitud'))
		}
		
		if(reformsRequestBySale?.errorDeletingReformRequest){
			(showErrorMessage('No fue posible eliminar la solicitud'))
		}

		if(reformsRequestBySale?.errorAllReformRequest){
			(showErrorMessage('No fue posible consultar las solicitudes'))
		}
		
	},[reformsRequestBySale])



	const style = {
		content: {
			border: '1px solid #ccc', background: '#fff',
			overflow: 'auto', WebkitOverflowScrolling: 'touch',
			borderRadius: '4px', outline: 'none', padding: '20px',
			top: '50%', left: '50%', right: 'auto', bottom: 'auto',
			marginRight: '-50%', transform: 'translate(-50%, -50%)',
		}
	};

	let conditionLoading = (reformsRequestBySale.isFetchingAllReformRequest || reformsRequestBySale.isAddingReformRequest || reformsRequestBySale.isUpdatingReformRequest || reformsRequestBySale.isDeletingReformRequest)
	
	let bodyModal = (<>
		<SaleDetail {...reformsRequestBySale} />
		<ListOfRequest onClose={onClose} {...reformsRequestBySale} idSale={idSale} updateListReformRequest={updateListReformRequest} />

	</>)
	return (
		<>
			<Modal
				title={'Solicitud de reforma'}
				visible={visibleModal}
				onCancel={handleCancel}
				footer={false}
				width={"80%"}
				destroyOnClose={true}
			>
				 <Spin spinning={conditionLoading} tip="Cargando..." >{bodyModal}</Spin>
			</Modal>
		</>

	);
}
export default ModalDetailReform;