import {Form, InputNumber, Select} from "antd";
import React, {useEffect, useState} from "react";
import UploadOrDownloadFile from "./UploadOrDownloadFile";
import {FORMATTER_INPUT_NUMBER} from "../../../../../../../../../redux/constants";

const TechnicalManagement = ({record}) => {
	const [state,setState] = useState(false)
	let id = record?.id !== undefined ? record?.id : 0
	let name = "budget"+id
	let nameUp = "technicalDocument"+id
	let nameSel = "approvalOfWorksDirector"+id
	let link = record?.technicalManagement.link
	let nameFile = record?.technicalManagement.name
	useEffect(() => {
		if (record.state === 'APROBADA' || record.state === 'TRAMITADA' || record.state === 'DECLINADA'){
			setState(true)
		}else {
			setState(false)
		}
		
	},[record])
	
	return <>
		{}
		<Form.Item
			label={"Presupuesto"}
			name={name}
			initialValue={record.budget}
		>
			<InputNumber
				{...FORMATTER_INPUT_NUMBER}
				style={{ width: "100%" }}
				size={"small"}
				disabled={state}
			/>
		</Form.Item>
		<UploadOrDownloadFile
			label={"Documento Técnico"}
			name={nameUp}
			state={state}
			id={id}
			link={link}
			nameFile={nameFile}
		/>
		<Form.Item
			label={"Aprobación Director de la Obra"}
			style={{ fontSize:10 }}
			name={nameSel}
			initialValue={record.approvalOfWorksDirector}
		>
			<Select
				size={"small"}
				disabled={state}
				defaultValue={record.approvalOfWorksDirector}
			>
				<Select.Option value={1}>Si</Select.Option>
				<Select.Option value={0}>No</Select.Option>
			</Select>
		</Form.Item>
	</>
}

export default TechnicalManagement