import * as constants from "../../constants";

export default function unitType(state = [], action) {
	switch (action.type) {
		case constants.SET_ALL_UNIT_TYPE:
			return action.payload;
		case constants.ADD_UNIT_TYPE:
			return state._payload.concat(action.payload);
		case constants.DUPLICATE_UNIT_TYPE:
			return state._payload.concat(action.payload);
		case constants.ADD_ATTACHMENT:
			return { ...state };
		case constants.REMOVE_UNIT_TYPE:
			return state._payload.filter((item) => item._id !== action.payload);
		case constants.REMOVE_ATTACHMENT:
			return state._payload.filter((item) => item._id !== action.payload);
		case constants.UPDATE_UNIT_TYPE:
			return state._payload.map((item) => {
				if (item._id === action.payload.assessorId)
					return { ...item, ...action.payload.data };
				else return item;
			});
		case constants.RESET_UNIT_TYPE:
			return action.payload;
		default:
			return state;
	}
}
