import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { fetchAllSaleLegalization } from "../../../../../../../redux/actions/Project/SaleLegalizationAction";
import Spinner from "../../../../../../handle/Spinner/Spinner";
import FormCardDocument from "./Components/FormCardDocument";
import {Card} from "antd";

const SaleLegalization = ({id}) => {
	
	const dispatch = useDispatch();

	useEffect(() => {
		dispatch(fetchAllSaleLegalization(id));
	}, []);

	const saleLegalizationDatabase = useSelector((state) => state?.saleLegalization);

	return !Array.isArray(saleLegalizationDatabase) ? (
		<FormCardDocument saleLegalizationDatabase={saleLegalizationDatabase} id={id}/>
	) : (
		<Card title="Listado de documentos" style={{ borderColor: "black" }}>
			<Spinner/>
		</Card>
	);
};

export default SaleLegalization;
