import moment from "moment";
import React, { useEffect } from "react";
import { useDispatch } from "react-redux";
import { Form,InputNumber,Input,DatePicker,Col,Row,Button,Upload } from "antd";
import { UploadOutlined,DeleteOutlined,ArrowDownOutlined } from "@ant-design/icons";
import { FORMATTER_INPUT_NUMBER,VALID_DOCUMENT_UPLOAD } from "../../../../../../redux/constants";
import { resetDesistSaleAction } from "../../../../../../redux/actions/Price/SaleMade/desistSaleAction";

const { TextArea } = Input;

const itemStyle = {
	width: "100%",
};

const FormDesistSale = ({
	form,
	data,
	setDocumentNameField,
	desistSaleDatabase,
	setDocumentToSend,
}) => {
	const dispatch = useDispatch();
	useEffect(() => {
		fillFields();
		return () => {
			dispatch(resetDesistSaleAction());
		};
	}, []);

	const normFile = (e) => {
		if (Array.isArray(e)) {
			return e;
		}
		return e && e.fileList;
	};

	const propsUpload = {
		showUploadList: false,
		beforeUpload: (file) => {
			return false;
		},
		onChange: (info) => {
			if (info.status !== "uploading") {
				setDocumentNameField(info);
				setDocumentToSend(info.file);
			}
		},
	};

	const fillFields = () => {
		form.setFieldsValue({
			customer: data.customer.person.name,
			phoneNumber: data.customer.person.phone_number,
			unitLabel: data.unit_name,
			totalValue: data.value_total,
			clientDocument: data.customer.person.identification,
			project: data.project_name,
			user: data.assessor_name,
			unitFiduciaryCode: 0,
			fineValue: 0,
			date: moment(data.sale_date),
		});

		if (!Array.isArray(desistSaleDatabase)) {
			fillFieldsToDatabaseData();
		}
	};

	const fillFieldsToDatabaseData = () => {
		form.setFieldsValue({
			valuePaidProject: desistSaleDatabase.value_to_pay_to_project,
			amountPayAssociatedCompany:
				desistSaleDatabase.amount_paid_associated_company,
			withdrawalDate: desistSaleDatabase.withdrawal_date
				? moment(desistSaleDatabase.withdrawal_date)
				: null,
			reason: desistSaleDatabase.motive,
			fineValue: desistSaleDatabase.fine_value,
			documentDesist: buildDocument(desistSaleDatabase),
			documentName: desistSaleDatabase.document
				? desistSaleDatabase.document.name
				: null,
		});
	};

	const buildDocument = (desistSaleDatabase) => {
		let file = [];
		setDocumentToSend(desistSaleDatabase.document);
		if (desistSaleDatabase.document) {
			file.push(desistSaleDatabase.document);
		} else {
			file.push({});
		}

		return file;
	};

	const download = () => {
		const tagA = document.createElement("a");
		tagA.href = desistSaleDatabase.document.path;
		tagA.target = "_blank";
		document.body.appendChild(tagA);
		tagA.click();
	};

	const deleteDocument = () => {
		form.setFieldsValue({
			documentDesist: [{}],
			documentName: null,
		});
		setDocumentToSend(null);
	};

	return (
		<Form layout="vertical" form={form} name="formDesist">
			<Row gutter={16}>
				<Col xs={24} sm={24} md={12} lg={12} xl={12}>
					<Form.Item label="Cliente" name="customer">
						<Input disabled />
					</Form.Item>
					<Form.Item label="Teléfono" name="phoneNumber">
						<Input disabled />
					</Form.Item>
					<Form.Item label="Unidad" name="unitLabel">
						<Input disabled />
					</Form.Item>
					<Form.Item label="Valor total" name="totalValue">
						<InputNumber
							min={0}
							{...FORMATTER_INPUT_NUMBER}
							disabled
							style={itemStyle}
						/>
					</Form.Item>
					<Form.Item label="Total abonos al día" name="totalBonusesPerDay">
						<Input disabled />
					</Form.Item>
					<Form.Item label="Valor devolución" name="returnValue">
						<InputNumber
							min={0}
							{...FORMATTER_INPUT_NUMBER}
							disabled
							style={itemStyle}
						/>
					</Form.Item>
					<Form.Item
						label="Codigo fiduciario de la unidad"
						name="unitFiduciaryCode"
						rules={[
							{
								required: true,
								message: "El campo es requerido.",
							},
						]}
					>
						<InputNumber style={itemStyle} />
					</Form.Item>
					<Form.Item
						rules={[
							{
								required: true,
								message: "El campo es requerido.",
							},
						]}
						label="Valor a pagar a proyecto"
						name="valuePaidProject"
					>
						<InputNumber
							min={0}
							{...FORMATTER_INPUT_NUMBER}
							style={itemStyle}
							placeholder="Valor a pagar a proyecto"
						/>
					</Form.Item>
					<Form.Item
						rules={[
							{
								required: true,
								message: "El campo es requerido.",
							},
						]}
						label="Motivo"
						name="reason"
					>
						<TextArea rows={4} style={itemStyle} placeholder="Motivo" />
					</Form.Item>
				</Col>
				<Col xs={24} sm={24} md={12} lg={12} xl={12}>
					<Form.Item label="Documento cliente" name="clientDocument">
						<Input disabled />
					</Form.Item>
					<Form.Item label="Proyecto" name="project">
						<Input disabled />
					</Form.Item>
					<Form.Item label="Fecha" name="date">
						<DatePicker disabled style={itemStyle} />
					</Form.Item>
					<Form.Item label="Usuario" name="user">
						<Input disabled />
					</Form.Item>
					<Form.Item
						rules={[
							{
								required: true,
								message: "El campo es requerido.",
							},
						]}
						label="Valor multa"
						name="fineValue"
					>
						<InputNumber
							min={0}
							{...FORMATTER_INPUT_NUMBER}
							style={itemStyle}
						/>
					</Form.Item>
					<Form.Item
						rules={[
							{
								required: true,
								message: "El campo es requerido.",
							},
						]}
						label="Fecha de desistimiento"
						name="withdrawalDate"
					>
						<DatePicker
							style={itemStyle}
							placeholder="Fecha de desistimiento"
						/>
					</Form.Item>
					<Form.Item
						label="Valor a pagar a empresa asociada"
						name="amountPayAssociatedCompany"
						rules={[
							{
								required: true,
								message: "El campo es requerido.",
							},
						]}
					>
						<InputNumber
							min={0}
							{...FORMATTER_INPUT_NUMBER}
							style={itemStyle}
							placeholder="Valor a pagar a empresa asociada"
						/>
					</Form.Item>
					<Input.Group compact>
						<Form.Item label="Documento" name="documentName">
							<Input style={{ width: "100%" }} disabled />
						</Form.Item>
						<Form.Item
							noStyle
							shouldUpdate={(prevValues, currentValues) =>
								prevValues.documentName !== currentValues.documentName
							}
						>
							{({ getFieldValue }) =>
								getFieldValue("documentName") === undefined ||
								getFieldValue("documentName") === null ? (
									<Form.Item
										valuePropName="fileList"
										label="."
										name="documentDesist"
										getValueFromEvent={normFile}
									>
										<Upload
											style={{ width: "50%" }}
											accept={VALID_DOCUMENT_UPLOAD}
											{...propsUpload}
											name="documentDesist"
											maxCount={1}
										>
											<Button
												size="middle"
												type="primary"
												icon={<UploadOutlined />}
											/>
										</Upload>
									</Form.Item>
								) : (
									<>
										<Form.Item label=" ">
											<Button
												size="middle"
												type="primary"
												icon={<DeleteOutlined />}
												danger
												onClick={deleteDocument}
											/>
										</Form.Item>
										<Form.Item
											noStyle
											shouldUpdate={(prevValues, currentValues) =>
												prevValues.documentDesist !==
												currentValues.documentDesist
											}
										>
											{({ getFieldValue }) =>
												getFieldValue("documentDesist") ? (
													getFieldValue("documentDesist")[0].hasOwnProperty(
														"path"
													) ? (
														<Form.Item label=" ">
															<Button
																size="middle"
																style={{ background: "#389e0d", color: "#fff" }}
																type="primary"
																icon={<ArrowDownOutlined />}
																onClick={download}
															/>
														</Form.Item>
													) : null
												) : null
											}
										</Form.Item>
									</>
								)
							}
						</Form.Item>
					</Input.Group>
				</Col>
			</Row>
		</Form>
	);
};
export default FormDesistSale;
