import {Col, Row, Table} from "antd";
import {useDispatch} from "react-redux";
import SeePrint from "../ActionsSales/SeePrint";
import React, {useEffect, useState} from "react";
import {useCustomer} from "../../../Hooks/useCustomer";
import Observations from "../ActionsSales/Observations";
import ReformRequests from "../ActionsSales/ReformRequests";
import AfterSales from "../ActionsSales/AfterSales/AfterSales";
import {errorGlobal} from "../../../../../../handle/Notification/Notification";
import {getSalesCustomer} from "../../../../../../../redux/actions/Customer/AfterSalesCustomer";
import {getColumnSearchProps, noWhiteWindow} from "../../../../../../handle/HandleFilterTable/handleFilterTable";
import LegalizeDocuments from "../../../../../Price/SalesMade/Componets/legalizeDocumentsComponents/LegalizeDocuments";

const ListSales = ({query}) => {
	const [sales,setSales] = useState([])
	const dispatch = useDispatch()
	const {SaleSet} = useCustomer()
	useEffect(()=>{
		let sale = []
		query.customer.sale.forEach((elem)=>{
			sale.push(SaleSet(elem))
		})
		setSales(sale)
	},[query])
	
	const GetSales = () => {
		let sale = []
		dispatch(
			getSalesCustomer(query.customer.id,async (response)=>{
				const res = await response
				res._payload.forEach((elem)=>{
					sale.push(SaleSet(elem))
				})
				setSales(sale)
			},async (error)=>{
				const err = await error
				errorGlobal(err.message)
			})
		)
	}
	
	const getActionsSales = (record) => {
		return <>
			<SeePrint record={record} />
			<LegalizeDocuments data={record} />
			<ReformRequests record={record} />
			<AfterSales basicInformation={record} />
			<Observations record={record} setSalesUpdate={GetSales} />
		</>
	}
	const trackingClients =[
		{
			title: 'Código',
			width: 50,
			dataIndex: 'id',
			align: "center",
			key: 'id',
			sorter: (a, b) => a.id - b.id
		}, {
			title: 'Fecha',
			width: 70,
			dataIndex: 'saleDate',
			align: "center",
			key: 'saleDate',
			sorter: (a, b) => a.saleDate - b.saleDate
		}, {
			title: 'Proyecto',
			dataIndex: 'projectName',
			align: "center",
			key: 'projectName',
			width: 100,
			...getColumnSearchProps('project'),
			...noWhiteWindow
		}, {
			title: 'Unidad',
			dataIndex: 'labelUnit',
			align: "center",
			key: 'labelUnit',
			width: 70,
			sorter: (a, b) => a.unit - b.unit
		}, {
			title: 'Cód. Fiduciario',
			dataIndex: 'fiduciaryCode',
			align: "center",
			key: 'fiduciaryCode',
			width: 70,
			...getColumnSearchProps('trustCode'),
			...noWhiteWindow
		}, {
			title: 'Valor Unidad',
			dataIndex: 'unitValue',
			align: "center",
			key: 'unitValue',
			width: 70,
			sorter: (a, b) => a.unitValue - b.unitValue
		}, {
			title: 'Valor Reformas',
			dataIndex: 'reformsValue',
			key: 'reformsValue',
			align: "center",
			width: 70,
			sorter: (a, b) => a.reformsValue - b.reformsValue
		}, {
			title: 'Valor Total',
			dataIndex: 'totalValue',
			key: 'totalValue',
			align: "center",
			width: 70,
			sorter: (a, b) => a.totalValue - b.totalValue
		}, {
			title: '# CI Recaudo',
			dataIndex: 'percentageCollected',
			key: 'percentageCollected',
			align: "center",
			width: 70,
			...getColumnSearchProps('ciCollection')
			,...noWhiteWindow
		}, {
			title: 'Legalizacion',
			dataIndex: 'percentageLegalization',
			key: 'percentageLegalization',
			align: "center",
			width: 70,
			...getColumnSearchProps('legalization'),...noWhiteWindow
		}, {
			title: 'Observaciones',
			dataIndex: 'observation',
			key: 'observation',
			align: "center",
			width: 100,
			...getColumnSearchProps('observations'),...noWhiteWindow
		}, {
			title: 'Acciones',
			key: 'acciones',
			fixed: 'right',
			width: 80,
			render: (_, record) => getActionsSales(record)
		}
	]
	return (
		<Row>
			<Col span={24}>
				<Table
					scroll={{ x: 1800, y: 300 }}
					size='small'
					bordered={true}
					columns={trackingClients}
					dataSource={sales}
				/>
			</Col>
		</Row>
	)
}
export default ListSales