import { Button, Card, Table, Col, Row, Popover } from "antd";
import React from "react";
import { useDispatch } from "react-redux";
import {getExcelActivitiesByAssessor,getPdfActivitiesByAssessor,} from "../../../../redux/actions/Report/MonitoringActivities/MonitoringActivitiesActions";
import {errorGlobal,warning} from "../../../handle/Notification/Notification";
import { convertAllMapDataState } from "../../../handle/HandleFilterTable/handleFilterTable";

const TableMonitoringActivities = ({ dataSource, selectedAssesor }) => {
	const dispatch = useDispatch();
	const download = (path) => {
		const tagA = document.createElement("a");
		tagA.href = path;
		tagA.target = "_blank";
		document.body.appendChild(tagA);
		tagA.click();
	};
	const handlePdf = () => {
		warning("Generando pdf...");
		dispatch(
			getPdfActivitiesByAssessor(
				selectedAssesor.value,
				async (response) => {
					const res = await response;
					if (res.error) {
						return errorGlobal("¡UPS, Ha ocurrido un error.");
					} else {
						download(res._payload);
					}
				}
			)
		);
	};
	const handleExcel = () => {
		warning("Generando Excel...");
		dispatch(
			getExcelActivitiesByAssessor(
				selectedAssesor.value,
				async (response) => {
					const res = await response;
					if (res.error) {
						return errorGlobal("¡UPS, Ha ocurrido un error.");
					} else {
						download(res._payload);
					}
				}
			)
		);
	};

	const content = (
		<div>
		<a style={{ marginRight: 21 }} onClick={handleExcel}>
			Excel
		</a>
		<a onClick={handlePdf}>PDF</a>
	</div>
	);
	const columns = [
		{
			title: "",
			width: 20,
			dataIndex: "period",
			align: "center",
		},
		{
			title: "#Actividades Programadas",
			width: 20,
			dataIndex: "scheduled",
			align: "center",
		},
		{
			title: "#Actividades Realizadas",
			width: 20,
			dataIndex: "performed",
			align: "center",
		},
		{
			title: "#Actividades Pendientes",
			width: 20,
			dataIndex: "pending",
			align: "center",
		},
		{
			title: "#Actividades Vencidas",
			width: 20,
			dataIndex: "expired",
			align: "center",
		},
		{
			title: "#Visitas Clientes",
			width: 20,
			dataIndex: "visits",
			align: "center",
		},
	];

	return (
		<Card>
			<Row>
				<Col xs={18} sm={20} md={21} lg={22} xl={22} />
				<Col xs={2} sm={2} md={2} lg={2} xl={2}>
				<Popover content={content} trigger="click">
						<Button
							style={{
								backgroundColor: "#5cb85c",
								borderColor: "#5cb85c",
							}}
							type="primary"
						
						>
							Exportar
						</Button>
				</Popover>
				</Col>
			</Row>

			<h4 style={{ margin: "5px" }}>
				{selectedAssesor.children === "Seleccionar"
					? null
					: selectedAssesor.children}
			</h4>
			<Table
				columns={columns}
				dataSource={convertAllMapDataState(dataSource)}
				scroll={{ x: 1000, y: 300 }}
				size="small"
				bordered={true}
			/>
		</Card>
	);
};

export default TableMonitoringActivities;
