import React, { useState, useEffect } from "react";
import {Table, Input, Button, InputNumber, Popconfirm, Form, Typography, Space, Row, Col, Card} from "antd";
import { useDispatch, useSelector } from "react-redux";
import {SaveOutlined, DeleteOutlined, FormOutlined, CloseOutlined, CopyOutlined} from "@ant-design/icons";
import ModalUnitType from "./ModalUnitType";
import RedistribuirUnidades from "./ModalRedistribuirUnidades";
import ModalImagenes from "./ModalImagenes";
import ModalPlans from "./ModalPlans";
import {updateUnitTypeById, deleteUnitTypeById, fetchAllUnitType, unitTypeDuplicate} from "../../../../../../../redux/actions/Project/UnitType";
import {success} from "../../../../../../handle/Notification/Notification";

const EditableCell = ({editing, dataIndex, title, inputType, record, index, children, ...restProps}) => {
	const inputNode = inputType === "number" ? <InputNumber /> : <Input />;
	return (
		<td {...restProps}>
			{editing ? (
				<Form.Item
					name={dataIndex}
					style={{
						margin: 0,
					}}
					rules={[
						{
							required: true,
							message: `requerido ${title}!`,
						},
					]}
				>
					{inputNode}
				</Form.Item>
			) : (
				children
			)}
		</td>
	);
};

const TypeUnit = ({ unitTypeDatabase, id }) => {
	const dispatch = useDispatch();

	const loaderUnitType = () => {
		dispatch(fetchAllUnitType(id));
	};
	const [form] = Form.useForm();
	const [data, setData] = useState([]);
	const [editingKey, setEditingKey] = useState("");

	useEffect(() => {
		if (unitTypeDatabase._payload)
			unitTypeDatabase._payload.map(x => {x.type_unit = x.id ; return x});
			setData(unitTypeDatabase._payload);
	}, [unitTypeDatabase]);

	const isEditing = (record) => record.id === editingKey;

	const edit = (record) => {
		form.setFieldsValue({
			id: "",
			area: "",
			finish_kit_value: "",
			area_value: "",
			units_number: "",
			rooms_number: "",
			value_other_additional: "",
			number_levels: "",
			income_level: "",
			bathroom_number: "",
			type_unit : "",
			...record,
		});
		setEditingKey(record.id);
	};

	const cancel = () => {
		setEditingKey("");
	};

	const updateUnitType = (id, row) => {
		dispatch(updateUnitTypeById(id, row, function () {
			loaderUnitType();
		}));
	};

	const save = async (id) => {
		try {
			const row = await form.validateFields();
			const newData = [...data];
			const index = newData.findIndex((item) => id === item.id);
			updateUnitType(id, row);

			if (index > -1) {
				const item = newData[index];
				newData.splice(index, 1, { ...item, ...row });
				setData(newData);
				setEditingKey("");
			} else {
				newData.push(row);
				setData(newData);
				setEditingKey("");
			}
		} catch (errInfo) {
			console.error("Validate Failed:", errInfo);
		}
	};

	const searchUnitType = (record) => {
		for (let index = 0; index < data.length; index++) {
			if (data[index].id === record.id) {
				return data[index];
			}
		}
	};

	const duplicateUnitType = (record) => {
		const dataDuplicateUnitType = {
			project_id: id,
			area: record.area,
			finish_kit_value: record.finish_kit_value,
			area_value: record.area_value,
			units_number: record.number_levels,
			rooms_number: record.rooms_number,
			value_other_additional: record.value_other_additional,
			number_levels: record.number_levels,
			income_level: record.income_level,
			bathroom_number: record.bathroom_number,
			blueprints: record.blueprints,
			images: record.images,
		};
		dispatch(unitTypeDuplicate(record.id, dataDuplicateUnitType, function () {
			loaderUnitType();
		}));
	};

	const formatterPeso = new Intl.NumberFormat("es-CO", {
		style: "currency",
		currency: "COP",
		minimumFractionDigits: 0,
	});

	const columns = [
		{
			title: "#",
			dataIndex: "id",
			key: "id",
			width: 50,
			fixed: "left",
			render: (text, record, index) => <span>{index + 1}</span>,
		},
		{
			title: "Tipo de unidad Id",
			dataIndex: "type_unit",
			Key: 'type_unit',
			align: "center",
			width: 140,
			defaultSortOrder: 'asc',
			sorter: (a, b) => a.type_unit - b.type_unit,
		},
		{
			title: "m2",
			dataIndex: "area",
			width: 100,
			editable: true,
			disabled: true,
		},
		{
			title: "Cantidad",
			dataIndex: "units_number",
			width: 100,
			editable: true,
			disabled: true,
		},
		{
			title: "No. Habitaciones",
			dataIndex: "number_rooms",
			editable: true,
			disabled: true,
			width: 120,
		},
		{
			title: "No. Baños",
			dataIndex: "bathroom_number",
			editable: true,
			width: 100,
		},
		{
			title: "No. Niveles",
			dataIndex: "number_levels",
			editable: true,
			width: 120,
		},
		{
			title: "Valor Kit de Acabados",
			dataIndex: "finish_kit_value",
			editable: true,
			width: 160,
			render: (_, record) =>
				formatterPeso.format(record.finish_kit_value),
		},
		{
			title: "Valor terraza/ Cuarto útil/ otros",
			dataIndex: "value_other_additional",
			editable: true,
			width: 160,
			render: (_, record) =>
				formatterPeso.format(record.value_other_additional),
		},
		{
			title: "Nivel de Ingresos",
			dataIndex: "income_level",
			editable: true,
			width: 120,
			render: (_, record) => formatterPeso.format(record.income_level),
		},
		{
			title: "Valor m2 (Prioritario)",
			dataIndex: "area_value",
			editable: true,
			width: 120,
			render: (_, record) => formatterPeso.format(record.area_value),
		},
		{
			title: "Imágenes",
			dataIndex: "images",
			width: 100,
			render: (_, record) => (
				<ModalImagenes record={searchUnitType(record)} />
			),
		},
		{
			title: "Planos",
			dataIndex: "blueprints",
			width: 100,
			render: (_, record) => (
				<ModalPlans record={searchUnitType(record)} />
			),
		},
		{
			title: "Acciones",
			width: 150,
			dataIndex: "operation",
			fixed: "right",
			render: (_, record) => {
				const editable = isEditing(record);
				const disabled = editingKey !== "";
				const recordAea = record.area;
				return editable ? (
					<span>
						<a
							title={"Guardar cambios"}
							href="javascript:;"
							onClick={() => save(record.id)}
							style={{
								marginRight: 8,
							}}
						>
							<SaveOutlined />
						</a>
						<Popconfirm title="Cancelar" onConfirm={cancel}>
							<a title={"Cancelar"}>
								<CloseOutlined />
							</a>
						</Popconfirm>
					</span>
				) : (
					<>
						<Space>
							<Typography.Link
								title={"Modificar"}
								disabled={editingKey !== ""}
								onClick={() => edit(record)}
							>
								<Button
									style={{
										backgroundColor: "#ffa247",
										color: "#ffffff",
									}}
									size="small"
									disabled={editingKey !== ""}
									icon={<FormOutlined />}
								/>
							</Typography.Link>

							<Typography.Link  title={"Eliminar"} disabled={editingKey !== ""}>
								<Popconfirm
									title="¿Desea eliminar este registro?"
									onConfirm={() => handleDelete(record.id)}
								>
									<Button
										style={{
											backgroundColor: "red",
											color: "#ffffff",
										}}
										size="small"
										disabled={editingKey !== ""}
										icon={<DeleteOutlined />}
									/>
								</Popconfirm>
							</Typography.Link>
						</Space>
						<Space>
							<Typography.Link title={"expandir"}>
								<RedistribuirUnidades
									disabled={disabled}
									recordAea={recordAea}
								/>
							</Typography.Link>
							<Button
								title={"Duplicar"}
								disabled={editingKey !== ""||!statusBotton}
								size="small"
								type="primary"
								style={{ marginBottom: "20px" }}
								icon={<CopyOutlined />}
								onClick={() => duplicateUnitType(record)}
							/>
						</Space>
					</>
				);
			},
		},
	];

	const handleDelete = (id) => {
		const dataSource = [...data];
		setData(dataSource.filter((item) => item.id !== id));
		dispatch(deleteUnitTypeById(id, function (response) {
			loaderUnitType();
			success("tipo de unidad elimina")
		}));
		
	};
	const mergedColumns = columns.map((col) => {
		if (!col.editable) {
			return col;
		}
		return {
			...col,
			onCell: (record) => ({
				record,
				inputType: col.dataIndex === "age" ? "number" : "text",
				dataIndex: col.dataIndex,
				title: col.title,
				editing: isEditing(record),
			}),
		};
	});
	function theTrust(a,b){
		return a>b?true:false
	}
	const arraya=[];
	data?.forEach(x=>arraya.push(x.units_number));
	const d =  arraya?.reduce((actual,anterios)=> actual + anterios,0);
	const TotalUnits = useSelector((state) => state?.EditProject._payload[0].total_units);
	const statusBotton=theTrust(TotalUnits,d);
	return (
		unitTypeDatabase &&
		<>
			<Card title="Tipo de unidades" style={{ borderColor: "black" }}>
				<Form form={form} component={false}>
					<Row>
						<Col span={24} offset={19}>
							<ModalUnitType bottonstate={statusBotton} myFunction={loaderUnitType} />
						</Col>
					</Row>
					<Row>
						<Col span={24}>
							<Table
								components={{
									body: {
										cell: EditableCell,
									},
								}}
								style={{
									marginLeft: "25px",
									marginRight: "25px",
								}}
								bordered
								dataSource={data}
								columns={mergedColumns}
								rowClassName="editable-row"
								pagination={{
									onChange: cancel,
								}}
								scroll={{ x: 1000, y: 300 }}
							/>
						</Col>
					</Row>
				</Form>
			</Card>
		</>
	);
};

export default TypeUnit;
