import * as constants from '../../../constants';

export const filterUnits = (data, onSuccess) => ({
    type: constants.API,
    payload: {
			method: 'POST',
			url: `${constants.RUTA}/sales/get-by-customer-and-project`,
			data,
			postProcessSuccess: onSuccess
    }
});

export const StatusOfLegalizationPetition = (onSuccess,onError) => ({
	type: constants.API,
	payload:{
		method: 'GET',
		url: `${constants.RUTA}/dashboard/legalization`,
		postProcessSuccess: onSuccess,
		postProcessError: onError
	}
})