import * as constants from '../../constants';
import LocalStorage from '../../../views/config/LocalStorage'

let Storage = new LocalStorage();


export const fetchAllSaleLegalization = (id) => ({
    type: constants.API,
    payload: {
        method: 'GET',
	    headers: { 'access-token':Storage.getToken() },
        url: `${constants.RUTA}/saleLegalizationDocument/${id}`,
        success: (response) => (setAllSaleLegalization(response))
    }
});

export const updateOrCreateSaleLegalizationById = (data, onSuccess, onError) => ({
    type: constants.API,
    payload: {
        method: 'PUT',
        url: `${constants.RUTA}/saleLegalizationDocument`,
        data,
        success: (data) => (updateOrCreateSaleLegalization(data)),  
        postProcessSuccess: onSuccess,
        postProcessError: onError           
    }
});

export const deleteSaleLegalizationById = ( id, onSuccess, onError) => ({
    type: constants.API,
    payload: {
        method: 'DELETE',
        url: `${constants.RUTA}/saleLegalizationDocument/${id}`,
        success: () => (removeSaleLegalization(id)),
        postProcessSuccess: onSuccess,
        postProcessError: onError
    }
});

export const resetSaleLegalizationAction = () => ({
    type: constants.API,
    payload: {
		method: null,
		success: () => (resetSaleLegalization())
	}
})

const resetSaleLegalization = () =>({
	type:constants.RESET_SALE_LEGALIZATION,
	payload: []
});

const setAllSaleLegalization = (data) => ({
    type: constants.SET_ALL_SALE_LEGALIZATION,
    payload: data
});

const updateOrCreateSaleLegalization = ( data) => ({
    type: constants.UPDATE_OR_CREATE_SALE_LEGALIZATION,
    payload: { data }
});

const removeSaleLegalization = ( id) => ({
    type: constants.REMOVE_SALE_LEGALIZATION,
    payload:  id
});