import React, {useState} from "react";
import {useSelector} from "react-redux";
import {Col, Row, Form, Select, DatePicker, Button} from "antd";
import {validateMessages} from "../../../handle/ValidateMessages/validateMenssages";
import {dateFormat} from "../../../handle/HandleDatePicker/HandleDate";

const FormPotentialClientsDatabase = ({exportExcel,consultindExport}) => {

	const [form] = Form.useForm();
	const selectAssessor = useSelector((state) => state?.assessor);
	const selectAfiliatedCompany = useSelector((state) => state?.afiliatedCompany);
	const selectCustomer = useSelector((state) => state?.customer);

	
	const submitData = async () => {
		let values = await getDataForm();
		await exportExcel(values);
	}

	const getDataForm = async () => {
		const values = await form.getFieldsValue();
		
		for (const property in values) {
			values[property] = values[property] === undefined || values[property] === '' ? 'all' : values[property];
		}
		return {
			...values,
			startDate : values['startDate'].format(dateFormat),
			endDate : values['endDate'] !== 'all' && values['endDate'] != null ? values['endDate'].format(dateFormat) : 'all'
		}
	}
	return (
		<Form
			layout="vertical"
			onFinish={submitData}
			validateMessages={validateMessages}
			form={form}
		>
			<Row gutter={16}>
				<Col xs={24} sm={24} md={8} lg={8} xl={8}>
					<Form.Item label="Asesor:" name="adviserId" >
						<Select
							defaultValue={""}
							showSearch
							style={{ width: "100%" }}
							optionFilterProp="children"
							filterOption={(input, option) =>
								option.children.toLowerCase().indexOf(input.toLowerCase()) >=
								0
							}
						>
							<Select.Option value="">Todos...</Select.Option>
							{!Array.isArray(selectAssessor) &&
							selectAssessor._payload.map((item) => (
								<Select.Option key={item.id} value={item.id}>{` ${item.name} `}</Select.Option>
							))}
						</Select>
					</Form.Item>
				</Col>
				<Col xs={24} sm={24} md={8} lg={8} xl={8}>
					<Form.Item label="Empresa:" name="companyId">
						<Select
							defaultValue={""}
							showSearch
							style={{ width: "100%" }}
							optionFilterProp="children"
							filterOption={(input, option) =>
								option.children.toLowerCase().indexOf(input.toLowerCase()) >=
								0
							}
						>
							<Select.Option value="">Todos...</Select.Option>
							{!Array.isArray(selectAfiliatedCompany) &&
							selectAfiliatedCompany._payload.filter(x => x.state !== false).map((item) => (
								<Select.Option key={item.id} value={item.id}>{` ${item.name} `}</Select.Option>
							))}
						</Select>
					</Form.Item>
				</Col>
				<Col xs={24} sm={24} md={8} lg={8} xl={8}>
					<Form.Item label="Cliente:" name="customerId">
						<Select
							defaultValue={""}
							showSearch
							style={{ width: "100%" }}
							optionFilterProp="children"
							filterOption={(input, option) =>
								option.children.toLowerCase().indexOf(input.toLowerCase()) >=
								0
							}
						>
							<Select.Option value="">Todos...</Select.Option>
							{
								!Array.isArray(selectCustomer) && selectCustomer._payload?.map(item => (
									<Select.Option key={item.id} value={item.id}>{` ${item.name} - CC: ${item.identification}`}</Select.Option>
								))

							}
						</Select>
					</Form.Item>
				</Col>
			</Row>
			<Row gutter={16}>
				<Col xs={24} sm={24} md={12} lg={12} xl={12}>
					<Form.Item
						label="Fecha Inicio:"
						name="startDate"
						rules={[
							{
								required: true,
							},
						]}
					>
						<DatePicker style={{ width: "100%" }}/>
					</Form.Item>
				</Col>
				<Col xs={24} sm={24} md={12} lg={12} xl={12}>
					<Form.Item 
						label="Fecha Fin:"
						name="endDate"
						rules={[
							{
								required: true,
							},
						]}
					>
						<DatePicker style={{ width: "100%" }} />
					</Form.Item>
				</Col>
			</Row>
			<Row>
				<Col xs={17} sm={20} md={21} lg={22} xl={22} />
				<Col xs={2} sm={2} md={2} lg={2} xl={2}>
					<Form.Item>
						<Button type="primary" htmlType="submit" loading={consultindExport}> Exportar </Button>
					</Form.Item>
				</Col>
			</Row>
		</Form>
	);
};

export default FormPotentialClientsDatabase;
