import * as constants from "./../../../constants";
import LocalStorage from '../../../../views/config/LocalStorage'

let Storage = new LocalStorage();


export const fetchAllCommissonParameterAction = (id) => ({
    type: constants.API,
    payload: {
        method: 'GET',
	    headers: { 'access-token':Storage.getToken() },
        url: `${constants.RUTA}/commissionParameter/${id}`,
        success: (response) => (fetchAllCommissonParamete(response))
    }
});

export const updateOrCreateCommissonParameterActionById = (id, data, onSuccess, onError) => ({
    type: constants.API,
    payload: {
        method: 'PUT',
        url: `${constants.RUTA}/commissionParameter/${id}`,
        data,
        success: (id,data) => (updateOrCreateCommissonParameter(id,data)),  
        postProcessSuccess: onSuccess,
        postProcessError: onError           
    }
});

export const deleteSaleLegalizationById = ( id, onSuccess, onError) => ({
    type: constants.API,
    payload: {
        method: 'DELETE',
        url: `${constants.RUTA}/saleLegalizationDocument/${id}`,
        success: () => (removeCommissonParameter(id)),
        postProcessSuccess: onSuccess,
        postProcessError: onError
    }
});

export const resetCommissonParameterAction = () => ({
    type: constants.API,
    payload: {
		method: null,
		success: () => (resetCommissonParameter())
	}
})

const resetCommissonParameter = () =>({
	type:constants.RESET_COMMISSION_PARAMETER,
	payload: []
});

const fetchAllCommissonParamete = (data) => ({
    type: constants.FETCH_ALL_COMMISSION_PARAMETER,
    payload: data
});

const updateOrCreateCommissonParameter = ( id, data) => ({
    type: constants.UPDATE_OR_CREATE_COMMISSION_PARAMETER,
    payload: {id, data }
});

const removeCommissonParameter = ( id) => ({
    type: constants.REMOVE_COMMISSION_PARAMETER,
    payload: id
});