import { createSelector } from "@reduxjs/toolkit";

export const saleDate_reform_requestSel = state => state.reformRequestBySale?.date_reform_request;
export const saleReformsRequestsSel = state => state.reformRequestBySale?.reforms_requests;

export const isFetchingAllReformRequestSel = state => state.reformRequestBySale?.isFetchingAllReformRequest;
export const errorAllReformRequestSel = state => state.reformRequestBySale?.errorAllReformRequest;

export const isAddingReformRequestSel = state => state.reformRequestBySale?.isAddingReformRequest;
export const errorAddReformRequestSel = state => state.reformRequestBySale?.errorAddReformRequest;

export const isUpdatingReformRequestSel = state => state.reformRequestBySale?.isUpdatingReformRequest;
export const errorUpdatingReformRequestSel = state => state.reformRequestBySale?.errorUpdatingReformRequest;

export const isDeletingReformRequestSel = state => state.reformRequestBySale?.isDeletingReformRequest;
export const errorDeletingReformRequestSel = state => state.reformRequestBySale?.errorDeletingReformRequest;




export const saleProjectIdSel = state => state.reformRequestBySale?.project?.id;
export const saleProjectNameSel = state => state.reformRequestBySale?.project?.name;
export const saleProjectMaximumReformsDateSel = state => state.reformRequestBySale?.project?.maximum_reforms_date;

export const saleCustomerIdSel = state => state.reformRequestBySale?.customer?.id;
export const saleCustomerNameSel = state => state.reformRequestBySale?.customer?.name;

export const saleUnitIdSel = state => state.reformRequestBySale?.unit?.id;
export const saleUnitLabelSel = state => state.reformRequestBySale?.unit?.label;

export const saleCreatorUserIdSel = state => state.reformRequestBySale?.creator_user?.id;
export const saleCreatorUserNameSel = state => state.reformRequestBySale?.creator_user?.name;



export const salesWhitReformRequestSel = state => state.salesWhitReformRequest;

export const reformsRequestBySaleSel = createSelector(
    saleDate_reform_requestSel,
    saleReformsRequestsSel,
    saleProjectIdSel,
    saleProjectNameSel,
    saleProjectMaximumReformsDateSel,
    saleCustomerIdSel,
    saleCustomerNameSel,
    saleUnitIdSel,
    saleUnitLabelSel,
    saleCreatorUserIdSel,
    saleCreatorUserNameSel,
    isFetchingAllReformRequestSel,
    errorAllReformRequestSel,
    isAddingReformRequestSel,
    errorAddReformRequestSel,
    isUpdatingReformRequestSel,
    errorUpdatingReformRequestSel,
    isDeletingReformRequestSel,
    errorDeletingReformRequestSel,
    (date_reform_request,
        reforms_requests,
        project_id,
        project_name,
        project_maximun_reform_date,
        customer_id,
        customer_name,
        unit_id,
        unit_label,
        creator_user_id,
        creator_user_name,
        isFetchingAllReformRequest,
        errorAllReformRequest,
        isAddingReformRequest,
        errorAddReformRequest,
        isUpdatingReformRequest,
        errorUpdatingReformRequest,
        isDeletingReformRequest,
        errorDeletingReformRequest
    ) => ({
        date_reform_request,
        reforms_requests,

        isFetchingAllReformRequest,
        errorAllReformRequest,
        isAddingReformRequest,
        errorAddReformRequest,
        isUpdatingReformRequest,
        errorUpdatingReformRequest,
        isDeletingReformRequest,
        errorDeletingReformRequest,
        project: {
            id: project_id,
            name: project_name,
            maximun_reform_date: project_maximun_reform_date,
        },
        customer: {
            id: customer_id,
            name: customer_name,
        },
        unit: {
            id: unit_id,
            label: unit_label,
        },
        creator_user: {
            id: creator_user_id,
            name: creator_user_name,
        }
    })
);

