import React from 'react';
import { Modal } from 'antd';
import AdjustTrustCollection from './AdjustTrustCollection';

const ModalAdjustTrustCollection = ({ handleCancel, onClose, data }) => {
    return (
        <>
            <Modal
                centered
                visible={true}
                onCancel={handleCancel}
                width={900}
                footer={false}
                destroyOnClose={true}
                title="Nuevo recaudo fiducia"
            >
                <AdjustTrustCollection data={data} onClose={onClose} />
            </Modal>
        </>
    )
}

export default ModalAdjustTrustCollection


