import React from "react"
import {Button, Col, Form, Input, Row, Upload} from "antd";
import {RUTA_API_MULTIMEDIA} from "../../../../../../../../../redux/constants";
import {DeleteOutlined, DownloadOutlined, UploadOutlined} from "@ant-design/icons";


const UploadOrDownloadFile = ({name,nameFile,state,link,id,label}) => {
	const downloadFile = (link) => {
		const tagA = document.createElement("a")
		tagA.href = `${link}`;
		tagA.target = "_blank";
		document.body.appendChild(tagA);
		tagA.click();
	}
	const deleteFile = (id) => {}
	const normFile = (e) => {
		if (Array.isArray(e)) {
			return e;
		}

		return e && e.fileList;
	};
	if (link){
		return (
			<Form.Item
				label={label}
			>
				<Row>
					<Col span={16}>
						<Input disabled defaultValue={nameFile} size="small"/>
					</Col>
					<Col>
						<Button onClick={()=>downloadFile(link)}  size="small" type="primary" icon={<DownloadOutlined />}/>
						<Button disabled={state} onClick={()=>deleteFile(id)} size="small" type="primary" danger icon={<DeleteOutlined />}/>
					</Col>
				</Row>
			</Form.Item>
		)
	}else {
		return (
			<Form.Item
				label={label}
				name={name}
				getValueFromEvent={normFile}
			>
				<Upload
					name={name}
					action={RUTA_API_MULTIMEDIA}
					valuePropName="fileList"
					maxCount={1}
					accept={'.pdf'}
				>
					<Button disabled={state} icon={<UploadOutlined />}>Documento Solicitud</Button>
				</Upload>
			</Form.Item>
		)
	}
}

export default UploadOrDownloadFile