
import { useDispatch, useSelector } from "react-redux";
import { fetchAllUnitType } from "../../../../../../../redux/actions/Project/UnitType";
import TypeUnit from "./TypeUnit";
import { Card } from "antd";
import Spinner from "../../../../../../handle/Spinner/Spinner";
import {useEffect} from "react";
import {warning} from "../../../../../../handle/Notification/Notification";

const MainUnitType = ({id}) => {
	const dispatch = useDispatch();

	
	useEffect(() => {
		warning("Para un mejor funcionamiento, [Configuraciones para Pagos y de Relación, Parámetros de Ventas]  deben estar gestionados")
		dispatch(fetchAllUnitType(id))
	}, [dispatch]);

	var unitTypeDatabase = useSelector((state) => state?.unitType);
	if (Array.isArray(unitTypeDatabase))
		return (
			<Card title="Tipo de unidades" style={{ borderColor: "black" }}>
                <Spinner/>
            </Card>
		);
	return <TypeUnit unitTypeDatabase={unitTypeDatabase} id={id} />;
};

export default MainUnitType;
