import React, { useEffect} from "react";
import { Divider, Card } from "antd";
import { useDispatch, useSelector } from "react-redux";
import {fetchAllIncreasePolicy} from "../../../../../../../redux/actions/Project/IncreasePolicyAction";
import {fetchAllDiscountPolicy} from "../../../../../../../redux/actions/Project/DiscountPolicyAction";
import "./styles.scss";
import IncreasePolicyContainer from "./containerComponents/IncreasePolicyContainer";
import DiscountPolicyContainer from "./containerComponents/DiscountPolicyContainer";

const MainPolicyIncreaseDiscountComponent = ({id}) => {

	const dispatch = useDispatch();
	

	useEffect(() => {
		dispatch(fetchAllIncreasePolicy(id));
		dispatch(fetchAllDiscountPolicy(id));
	}, [dispatch]);

	const increasePoliciesDatabase = useSelector((state) => state?.increasePolicy);
	const discountPoliciesDatabase = useSelector((state) => state?.discountPolicy);

	return (
		<>
			<Card title="Políticas de incremento y descuento" style={{ borderColor: "black" }}>
				<IncreasePolicyContainer
					increasePoliciesDatabase={increasePoliciesDatabase}
				/>
				<Divider />
				<DiscountPolicyContainer
					discountPoliciesDatabase={discountPoliciesDatabase}
				/>
			</Card>
		</>
	);
};

export default MainPolicyIncreaseDiscountComponent;
