import {Form, Input} from "antd";
import React, {useEffect, useState} from "react";
import UploadOrDownloadFile from "./UploadOrDownloadFile";

const CustomerRequests = ({record}) => {
	const [state,setState] = useState(false)
	let id = record?.id !== undefined ? record?.id : 0
	let name = "comment"+id
	let nameUp = "application_document"+id
	let link = record?.customerRequests.link
	let nameFile = record?.customerRequests.name
	useEffect(()=>{
		if (record?.state === 'APROBADA' || record?.state === 'TRAMITADA' || record?.state === 'DECLINADA'){
			setState(true)
		}else {
			setState(false)
		}
	},[record])
	
	return (<>
		<Form.Item
			name={name}
			initialValue={record?.name}
		>
			<Input.TextArea disabled={state} showCount maxLength={200}/>
		</Form.Item>
		<UploadOrDownloadFile
			label={"Documento Solicitud"}
			name={nameUp}
			state={state}
			id={id}
			link={link}
			nameFile={nameFile}
		/>
	</>)
}

export default CustomerRequests