import * as constants from "../../../constants";

export const fetchAllReformRequest = (saleId) => ({
    type: constants.API,
    payload: {
        method: 'GET',
        headers: constants.TOKEN,
        url: `${constants.RUTA}/reformRequest/reform_by_sale/${saleId}`,
        success: (response) => (DispatchPosErrorOrSucces(response, 'fetchAllReformRequest')),
        // postProcessError: (error) => DispatchPosError(error)
    }
});

export const createReformRequest = (idSale, data, onSuccess, onError) => ({
    type: constants.API,
    payload: {
        method: 'POST',
        url: `${constants.RUTA}/reformRequest/${idSale}`,
        data,
        success: (ReformRequest) => (DispatchPosErrorOrSucces(ReformRequest,'createReformRequest')),
        postProcessSuccess: onSuccess,
        postProcessError: onError
    }
});

export const getReformRequestById = (reformRequestId, onSuccess) => ({
    type: constants.API,
    payload: {
        method: 'GET',
        url: `${constants.RUTA}/reform-request/${reformRequestId}`,
        postProcessSuccess: onSuccess
    }
});

export const updateReformRequestById = (reformRequestId, data, onSuccess, onError) => ({
    type: constants.API,
    payload: {
        method: 'POST',
        url: `${constants.RUTA}/reformRequest/update/${reformRequestId}`,
        data,
        success: (response) => (DispatchPosErrorOrSucces(response,'updateReformRequest')),
        postProcessSuccess: onSuccess,
        postProcessError: onError
    }
});

export const deleteReformRequestrById = (reformRequestId, onSuccess, onError) => ({
    type: constants.API,
    payload: {
        method: 'DELETE',
        url: `${constants.RUTA}/reformRequest/${reformRequestId}`,
        success: (response) => (DispatchPosErrorOrSucces(response,'deleteReformRequest')),
        postProcessSuccess: onSuccess,
        postProcessError: onError
    }
});

const getAllReformRequest = (data) => ({
    type: constants.GET_ALL_REFORM_REQUEST,
    payload: data
});

const addReformRequest = (reformRequestAdded) => ({
    type: constants.ADD_REFORM_REQUEST,
    payload: reformRequestAdded
});

const updateReformRequest = (reformRequestUpdated) => ({
    type: constants.UPDATE_REFORM_REQUEST,
    payload: reformRequestUpdated
});

const removeReformRequest = (reformRequestRemovedId) => ({
    type: constants.REMOVE_REFORM_REQUEST,
    payload: reformRequestRemovedId
});

const DispatchPosErrorOrSucces = (response, actionOrigin)=>{
    switch(actionOrigin){
        case 'fetchAllReformRequest':
            if(response.error){
                return errorFetchingAllReformRequest()
            }else{
                return getAllReformRequest(response)
            }
        case 'createReformRequest':
            if(response.error){
                return errorAddReformRequest()
            }else{
                return addReformRequest(response)
            }
        case 'updateReformRequest':
            if(response.error){
                return errorUpdateReformRequest()
            }else{
                return updateReformRequest(response)
            }
        case 'deleteReformRequest':
            if(response.error){
                return errorDeleteReformRequest()
            }else{
                return removeReformRequest(response)
            }
        default:
            break;
    }
}

export const startFetchingAllReformRequest = () => ({
    type: constants.START_FETCHING_ALL_REFORM_REQUEST,
    payload: null
});


export const errorFetchingAllReformRequest = () => ({
    type: constants.ERROR_FETCHING_ALL_REFORM_REQUEST,
    payload: null
});


export const startAddReformRequest = () => ({
    type: constants.START_ADD_REFORM_REQUEST,
    payload: null
});

export const errorAddReformRequest = () => ({
    type: constants.ERROR_ADD_REFORM_REQUEST,
    payload: null
});

export const startUpdateReformRequest = () => ({
    type: constants.START_UPDATE_REFORM_REQUEST,
    payload: null
});


export const errorUpdateReformRequest = () => ({
    type: constants.ERROR_UPDATE_REFORM_REQUEST,
    payload: null
});

export const startDeleteReformRequest = () => ({
    type: constants.START_REMOVE_REFORM_REQUEST,
    payload: null
});


export const errorDeleteReformRequest = () => ({
    type: constants.ERROR_REMOVE_REFORM_REQUEST,
    payload: null
});

export const clearStateReformRequest = () => ({
    type: constants.CLEAR_STATE_REFORM_REQUEST,
    payload: {}
});