import {useDispatch} from "react-redux";
import React, {useEffect, useState} from "react";
import {Redirect, useParams} from "react-router-dom";
import {useEditQuotes} from "../Hooks/useEditQuotes";
import PropertyValue from "./editQuote/PropertyValue";
import DiscountsCoupons from "./editQuote/DiscountsCoupons";
import BasicInformation from "./editQuote/BasicInformation";
import TablePagoCliente from "./propertyData/TablePagoCliente";
import DescriptionProject from "./editQuote/DescriptionProject";
import TableMetodosPagos from "./propertyData/TableMetodosPagos";
import DescriptionIncluded from "./editQuote/DescriptionIncluded";
import CommercialDiscounts from "./editQuote/CommercialDiscounts";
import {PaymentForTheProject} from "./editQuote/PaymentForTheProject";
import {Button, Card, Col, Divider, Form, PageHeader, Row} from "antd";
import DescriptionOfProjectValues from "./editQuote/DescriptionOfProjectValues";
import {errorGlobal, success} from "../../../../handle/Notification/Notification";
import {fetchAllChannel} from "../../../../../redux/actions/Configuration/ChannelAction";
import {getQuoteById, updateQuoteById} from "../../../../../redux/actions/Price/Quotation/QuotesAction";

const UpdateQuotes = () => {
	const { id } = useParams();
	const [form] = Form.useForm(); 
	const dispatch = useDispatch();
	const [discountPolicy,setDiscountPolicy] = useState({});
	const [acavado,setAcavado] = useState(false);
	const [coupon,setCoupon] = useState([]);
	const {
			//states
		 	basicInformation,project,included,propertyData,paymentMethodProposedByTheProject,paymentMethodProposedByTheClient
			,response,finalValueProposedByTheClient,
		  	////functions,
			projectSet,includedSet,propertyDataSet,basicInformationSet,changeStateTableIncluded,discountsSet,onChangeKit,
			changeStateTableCoupon,	paymentMethodProposedByTheProjectSet,paymentMethodProposedByTheClientSet,
			modified_values,calculatePor,calculatePresentValueByTheClient,recalculatePaymentPlan,
	} = useEditQuotes();
	const titleProject = "Forma de pago propuesta por el proyecto";
	const titleCustomer = "Forma de Pago propuesta por el cliente";

	useEffect(() => {
		dispatch(
			getQuoteById(id,async (response)=>{
				const res = await response;
				basicInformationSet(res._payload);
				projectSet(res._payload);
				includedSet(res._payload);
				propertyDataSet(res._payload);
				paymentMethodProposedByTheProjectSet(res._payload);
				paymentMethodProposedByTheClientSet(res._payload);
				setDiscountPolicy(res._payload.project);
				setCoupon(res._payload.coupons);
				
			})
		)
		dispatch(fetchAllChannel())
	}, []);
	useEffect(()=>{
		establishBasicData();
	},[basicInformation]);
	const Cancelar = () => {
		setAcavado(true)
	}
	const establishBasicData = () => {
		form.setFieldsValue({
			nivelInteres: basicInformation.nivelInteres,
			comoEntero: basicInformation.comoEntero,
			fechaQuote: basicInformation.fechaQuote
		});
	}
	const onFinish = async () => {
		const values = await form.validateFields()
		 let repuesta = {
		 	interest_level: values["nivelInteres"],
		 	channel_id: values["comoEntero"],
		 	amounts: response.modified_values,
		 	discounts: response.discount,
		 	cupones: response.coupons,
		 	finishes_included: propertyData.includesFinishes ? 1 : 0,
		 	value_m2: propertyData.valueM,
		 	value_total_unit: paymentMethodProposedByTheProject.unitValue,
		 	value_discounts: paymentMethodProposedByTheClient.commercialDiscounts,
		 	value_coupons: paymentMethodProposedByTheClient.discountCoupons,
		 }
		 dispatch(
		 	updateQuoteById(id,repuesta,async (response)=>{
		 		const res = await response
		 		if (res.error){
		 			errorGlobal(res.message)
		 		}else{
		 			success(res.message)
		 			setAcavado(true)
		 		}
		 	})
		 )
	}
	if (acavado) return <Redirect exact to="/sales/quotes" />
	return (
		<Card>
			<Form
				name="FormQuoteUpdate"
				layout="vertical"
				form={form}
				onFinish={onFinish}
			>
				<Form.Item>
					<BasicInformation />
				</Form.Item>
				<Form.Item>
					<Row>
						<Col xs={24} sm={24} md={8} lg={8} xl={8}>
							<DescriptionProject project={project} />
						</Col>
						<Col xs={24} sm={24} md={8} lg={8} xl={8}>
							<DescriptionIncluded included={included} />
						</Col>
						<Col xs={24} sm={24} md={8} lg={8} xl={8}>
							<PropertyValue 
								propertyData={propertyData}
								changeStateTableIncluded={changeStateTableIncluded}
								onChangeKit={onChangeKit}
								changeStateTableCoupon={changeStateTableCoupon}
								recalculatePaymentPlan={recalculatePaymentPlan}
							/>
						</Col>
					</Row>
				</Form.Item>
				<Form.Item>
					<Row gutter={2}>
						<Col span={12}>
							{
								propertyData.tradePolicies && (<>
									<PageHeader
										style={{ border: " solid rgb(235, 237, 240);" }}
										title={"Descuentos comerciales"}
									/>
									<CommercialDiscounts
										Project={discountPolicy} 
										Discoun={discountsSet}
									/>
								</>)
							}
						</Col>
						<Col span={12}>
							{
								propertyData.Coupon && (<>
									<PageHeader
										style={{ border: " solid rgb(235, 237, 240);" }}
										title={"Descuentos cupones"}
									/>
									<DiscountsCoupons 
										Discoun={discountsSet}
										Precie={propertyData.unitValue}
										dataSource={coupon}
									/>
								</>)
							}
						</Col>
					</Row>
				</Form.Item>
				<Form.Item>
					<Row gutter={2}>
						<Col span={11}>
							<PaymentForTheProject title={titleProject} quote={paymentMethodProposedByTheProject}/>
						</Col>
						<Col span={1}/>
						<Col span={11}>
							<PaymentForTheProject quote={paymentMethodProposedByTheClient} title={titleCustomer} />
						</Col>
					</Row>
				</Form.Item>
				<Form.Item>
					<Row gutter={2}>
						<Col span={11}>
							<TableMetodosPagos amountOfFees={paymentMethodProposedByTheProject.formOfPayments} />
						</Col>
						<Col span={1}/>
						<Col span={11}>
							<TablePagoCliente 
								valueMonth={paymentMethodProposedByTheProject?.formOfPayments[1]?.value}
								calculatePor={calculatePor} 
								modified_values={modified_values} 
								dataTable={paymentMethodProposedByTheClient.formOfPayments} 
								valueUnit={paymentMethodProposedByTheClient.unitValue}
								calculatePresentValueByTheClient={calculatePresentValueByTheClient}
								trueUnitValue={finalValueProposedByTheClient.valueOfTheUnitProposed}
							/>
						</Col>
					</Row>
				</Form.Item>
				<Form.Item>
					<Row gutter={2}>
						<Col span={11}>
							<DescriptionOfProjectValues
								paymentMethodProposedByTheProject={paymentMethodProposedByTheProject}
							/>
						</Col>
						<Col span={1}/>
						<Col span={11}>
							<DescriptionOfProjectValues
								paymentMethodProposedByTheProject={paymentMethodProposedByTheClient}
							/>
						</Col>
					</Row>
				</Form.Item>
				<Divider />
				<Row>
					<Col span={20}/>
					<Col span={4}>
						<Row>
							<Col>
								<Form.Item>
									<Button type="primary" danger onClick={()=>Cancelar()}>
										Cancelar
									</Button>
								</Form.Item>
							</Col>
							<Col>
								<Form.Item>
									<Button
										type="primary"
										htmlType="submit"
										style={{
											marginLeft: "5%",
											backgroundColor: "#5cb85c",
											borderColor: "#5cb85c",
										}}
									>
										Guardar
									</Button>
								</Form.Item>
							</Col>
						</Row>
					</Col>
				</Row>
			</Form>
		</Card>
	)
}

export default UpdateQuotes