import React, {useEffect, useState} from "react";
import {Button, Card, Col, Divider, Form, PageHeader, Row} from "antd";
import {Link, Redirect, useParams} from "react-router-dom";
import { ArrowRightOutlined} from "@ant-design/icons";
import {useDispatch} from "react-redux";
import {fetchSaleById, updateDataSale} from "../../../../../../redux/actions/Price/SaleMade/SalesMadeAction";
import {useSaleMade} from "../../Hooks/useSaleMade";
import {SalesMadeContext} from "./context/SalesMadeContext";
import {TotalInformationToPay} from "./Components/TotalInformationToPay";
import {GeneralInformation} from "./Components/GeneralInformation";
import {TablesForDiscounts} from "./Components/TablesForDiscounts";
import {WayToPay} from "./Components/WayToPay";
import {errorGlobal, success} from "../../../../../handle/Notification/Notification";

const EditSalesMade = () => {
	const { id } = useParams();
	const [form] = Form.useForm();
	const dispatch = useDispatch();
	const [finish, setFinish] = useState(false);
	const [info, setInfo] = useState({
		ProjectId:0,CustomerId:0
	});

	const [coupon,setCoupon] = useState([]);
	const [Project,setProject] = useState([]);
	const [numberOfMonthsP,setNumberOfMonthsP] = useState(0);
	const [cutoffDate,setCutoffDate] = useState(0);
	const [payment_relationship_setting,setPayment_relationship_setting] = useState([]);
	const {
		//states
		project,included,propertyData,response,paymentMethodProposedByTheProject,paymentMethodProposedByTheClient,
		setPaymentMethodProposedByTheClient,
		////functions
		initialization,changeStateTableIncluded,onChangeKit,changeStateTableCoupon,recalculatePaymentPlan,discountsSet,
		calculatePor,calculatePresentValueByTheClient
	} = useSaleMade();

	useEffect(()=>{
		dispatch(
			fetchSaleById(id,async (response)=>{
				const res = await response._payload;
				// console.log(res);
				initialization(res);
				setInfo({
					CustomerId: res.customer_id,
					ProjectId: res.quotation.unit["unit_type"].project_id
				});
				setProject(res.quotation.unit["unit_type"].project);
				setCoupon(res.coupons);
				setPayment_relationship_setting(res.quotation.unit["unit_type"].project.payment_relationship_setting);
				setNumberOfMonthsP(parseInt(res.amount_dues));//setNumberOfMonthsP(parseInt(data.amount_dues));
				setCutoffDate(parseInt(res.cutoff_date));
			}))
	},[]);
	
	const onFinish = async () => {
		
		let answer = {
		 	'valueTotalUnit': paymentMethodProposedByTheProject.unitValue,
		 	'valueM2': propertyData.valueM,
		 	'valueCoupons': paymentMethodProposedByTheProject.discountCoupons,
		 	'coupons': response.coupons,
		 	'valueDiscounts': paymentMethodProposedByTheProject.commercialDiscounts,
		 	'discounts': response.discounts,
		 	'amounts': response.modified_values,
		 	'valuePresented': paymentMethodProposedByTheProject.presentValue,
		 	'amountDues': paymentMethodProposedByTheClient.formOfPayments.length,
		 	'amountDuesValue': paymentMethodProposedByTheProject.formOfPayments[1].value,
		};
		dispatch(
			updateDataSale(id,answer,async (response)=>{
				const res = await response
				if (res.error){
					errorGlobal(res.message)
				}else{
					success(res.message)
					setFinish(true)
				}
			})
		)
	}
	
	if (finish) return <Redirect exact to="/sales/sales-made" />
	return (
		<>
			<PageHeader
				title={"Editar Venta"}
				extra={[
					<Link key="1" to="/sales/sales-made"
						  style={{
							  backgroundColor: '#1890ff',
							  padding: 8,
							  borderRadius: 2,
							  color: '#ffffff'
						  }}
					>
						Ventas <ArrowRightOutlined style={{ fontSize: 20 }} />
					</Link>
				]}
			>
				<Card style={{ marginRight: 20 }}>
					<Form
						name="FormQuoteUpdate"
						layout="vertical"
						form={form}
						onFinish={onFinish}
					>
						<SalesMadeContext.Provider value={{
							paymentMethodProposedByTheProject,paymentMethodProposedByTheClient,project,included,
							propertyData,changeStateTableIncluded,changeStateTableCoupon,onChangeKit,recalculatePaymentPlan,
							discountsSet,info,coupon,setPaymentMethodProposedByTheClient,calculatePor,numberOfMonthsP,
							calculatePresentValueByTheClient,cutoffDate,payment_relationship_setting,Project
						}}>
							<GeneralInformation/>
							<TablesForDiscounts/>
							<Divider/>
							<WayToPay/>
							<TotalInformationToPay/>
							<Divider/>
						</SalesMadeContext.Provider>
						<Row>
							<Col span={20}/>
							<Col span={4}>
								<Form.Item>
									<Button
										type="primary"
										htmlType="submit"
										style={{
											marginLeft: "5%",
											backgroundColor: "#5cb85c",
											borderColor: "#5cb85c",
										}}
									>
										Guardar
									</Button>
								</Form.Item>
							</Col>
						</Row>
					</Form>
				</Card>
			</PageHeader>
		</>
	)
}

export default EditSalesMade