import React, {useState, useEffect} from "react";
import {useDispatch, useSelector} from "react-redux";
import {Button, Tooltip, Radio, Table} from "antd";
import {EditOutlined, CheckSquareFilled} from "@ant-design/icons";
import {Link} from "react-router-dom";
import {validatePermission} from "../../../../handle/PermissionMethods/PermissionMethods";
import * as constants from "../../../../../redux/constants";
import {fetchAllProjects, updateStateProjectById} from "../../../../../redux/actions/Project/ProjectsAction";
import Spinner from "../../../../handle/Spinner/Spinner";
import {convertAllMapDataState, getColumnSearchProps} from "../../../../handle/HandleFilterTable/handleFilterTable";
import {TootipInfo} from "../../../TooltipInfoComponent/TootipInfo";
import {success, errorGlobal} from "../../../../handle/Notification/Notification";
import {getFormatDate} from "../../../../handle/HandleDatePicker/HandleDate";

const TableProjects = () => {

	
	const dispatch = useDispatch();

	const [loading, setLoading] = useState(true);


	useEffect(function () {
		dispatch(fetchAllProjects());
	}, []);

	
	
	
	const redCell = {
		background: "#f2dede",
		borderColor: "#f2dede"
	};

	const getRedStyle = (value) => {
		return (value !== 1) ? redCell : null;
	};


	let reponseFetchAllProjects = useSelector(state => state?.project._payload);
	let [elementProject, setElementProject] = useState([]);

	useEffect(function () {
		setLoading(false);
	}, [reponseFetchAllProjects]);
	
	const onChangeChecked = (id) => {
		dispatch(
			updateStateProjectById(id, async (response) => {
				const res = await response;
				if (!res.error) {
					success("Actualizado exitosamente");
					setElementProject(res._payload);
				} else errorGlobal("¡UPS! ha ocurrido un error mientras se actualizaba");
			})
		);

	}

	useEffect(() => {
		setElementProject(reponseFetchAllProjects);
	}, [reponseFetchAllProjects]);

	const getActions = (record) => {
		const textEditar = <span style={{color: "#6A6963"}}> Editar </span>;
		const textChangeState = <span style={{color: "#6A6963"}}>Activar/Desactivar</span>
		return (
			<>
				<TootipInfo created_at={record.created_at} name={record.name} updated_at={record.updated_at}/>
				{"  "}
				{validatePermission(constants.UPDATE_PROJECT_PERMISSION) ? (
					<Link to={`/projects/edit-project/${record.id}`}>
						<Tooltip placement="left" color="#ffffff" title={textEditar}>
							<Button
								type="primary"
								style={{backgroundColor: "#ffa247", borderColor: "#ffa247"}}
								size="small"
								icon={<EditOutlined/>}
							/>
						</Tooltip>
					</Link>
				) : null}

				{"  "}
				<Tooltip placement="left" color="#ffffff" title={textChangeState}>
					<Button
						size="small"
						onClick={() => {
							onChangeChecked(record.id)
						}}
						icon={
							<CheckSquareFilled
								style={{color: '#df534f', display: 'inline-block', verticalAlign: 'middle'}}/>
						}
					>
					</Button>
				</Tooltip>
			</>
		);
	};

	const columns = [
		{
			title: "id",
			width: 15,
			dataIndex: "id",
			fixed: "left",
			key: "id",
			render(text, record) {
				return {
					props: {
						style: getRedStyle(record?.is_active),
					},
					children: <h6 style={{fontSize: "14px"}}>{text}</h6>,
				};
			},
		},
		{
			title: "Nombre Proyecto",
			width: 70,
			dataIndex: "name",
			key: "name",
			...getColumnSearchProps("name"),
			render(text, record) {
				return {
					props: {
						style: getRedStyle(record?.is_active),
					},
					children: <h6 style={{fontSize: "14px"}}>{text}</h6>,
				};
			},
		},
		{
			title: "Forma de Venta",
			width: 70,
			dataIndex: "sale_type",
			key: "sale_type",
			...getColumnSearchProps("sale_type"),
			render(text, record) {
				return {
					props: {
						style: getRedStyle(record.is_active),
					},
					children: <h6 style={{fontSize: "14px"}}>{text}</h6>,
				};
			},
		},
		{
			title: "Estado",
			dataIndex: "project_state",
			key: "project_state",
			width: 50,
			...getColumnSearchProps("project_state"),
			render(text, record) {
				return {
					props: {
						style: getRedStyle(record.is_active),
					},
					children: <h6 style={{fontSize: "14px"}}>{text}</h6>,
				};
			},
		},
		{
			title: "Tipo de Proyecto",
			dataIndex: "project_type",
			key: "project_type",
			width: 50,
			...getColumnSearchProps("project_type"),
			render(text, record) {
				return {
					props: {
						style: getRedStyle(record.is_active),
					},
					children: <h6 style={{fontSize: "14px"}}>{text}</h6>,
				};
			},
		},
		{
			title: "Total Unidades",
			dataIndex: "total_units",
			key: "total_units",
			width: 35,
			...getColumnSearchProps("total_units"),
			render(text, record) {
				return {
					props: {
						style: getRedStyle(record.is_active),
					},
					children: <h6 style={{fontSize: "14px"}}>{text}</h6>,
				};
			},
		},
		{
			title: "Fecha de Inicio de Venta",
			dataIndex: "sale_start_date",
			key: "sale_start_date",
			width: 60,
			render(sale_start_date, record,) {
				return {
					props: {
						style: getRedStyle(record.is_active),
					},
					children: <h6 style={{fontSize: "14px"}}>{getFormatDate(sale_start_date)}</h6>,
				};
			},
		},
		{
			title: "Fecha estimada de inicio de Construcción",
			dataIndex: "start_build_date",
			key: "start_build_date",
			width: 60,
			render(start_build_date, record,) {
				return {
					props: {
						style: getRedStyle(record.is_active),
					},
					children: <h6 style={{fontSize: "14px"}}>{getFormatDate(start_build_date)}</h6>,
				};
			},
		},
		{
			title: "Fecha estimada de Entrega",
			dataIndex: "estimated_delivery_date",
			key: "estimated_delivery_date",
			width: 60,
			render(estimated_delivery_date, record,) {
				return {
					props: {
						style: getRedStyle(record.is_active),
					},
					children: <h6 style={{fontSize: "14px"}}>{getFormatDate(estimated_delivery_date)}</h6>,
				};
			},
		},
		{
			title: "Acciones",
			key: "acciones",
			fixed: "right",
			width: 40,
			render(_, record,) {
				return {
					props: {
						style: getRedStyle(record.is_active),
					},
					children: getActions(record),
				};
			},
		},
	];

	return (
		<>
			<Radio.Group>
				<Radio disabled value="Todos">
					Todos
				</Radio>
				<Radio disabled value="Activos">
					Activos
				</Radio>
				<Radio disabled value="Inactivos">
					Inactivos
				</Radio>
			</Radio.Group>
			<br/>
			<br/>
			<Table
				loading={loading && <Spinner/>}
				columns={columns}
				dataSource={convertAllMapDataState(elementProject)}
				scroll={{x: 1800, y: 300}}
				size="small"
				bordered={true}
			/>
		</>
	);
};

export default TableProjects;
