import * as constants from '../../../constants';
import LocalStorage from '../../../../views/config/LocalStorage'

let Storage = new LocalStorage();


export const fetchAllAssignmentTechnicians = (id) => ({
    type: constants.API,
    payload: {
        method: 'GET',
	    headers: { 'access-token':Storage.getToken() },
        url: `${constants.RUTA}/projectTechnicians/${id}`,
        success: (response) => (setAllAssignmentTechnicians(response))
    }
});

export const updateOrCreateAssignmentTechniciansById = (data, onSuccess, onError) => ({
    type: constants.API,
    payload: {
        method: 'PUT',
        url: `${constants.RUTA}/projectTechnicians/assignProject`,
        data,
        success: (data) => (updateOrCreateAssignmentTechnicians(data)),  
        postProcessSuccess: onSuccess,
        postProcessError: onError           
    }
});

export const deleteAssignmentTechniciansById = ( id, onSuccess, onError) => ({
    type: constants.API,
    payload: {
        method: 'DELETE',
        url: `${constants.RUTA}/projectTechnicians/${id}`,
        success: () => (removeAssignmentTechnicians(id)),
        postProcessSuccess: onSuccess,
        postProcessError: onError
    }
});

export const resetAssignmentTechniciansAction = () => ({
    type: constants.API,
    payload: {
		method: null,
		success: () => (resetAssignmentTechnicians())
	}
})

const resetAssignmentTechnicians = () =>({
	type:constants.RESET_ASSIGNED_TECHNICIAN,
	payload: []
});

const setAllAssignmentTechnicians = (data) => ({
    type: constants.SET_ALL_ASSIGNED_TECHNICIAN,
    payload: data
});

const updateOrCreateAssignmentTechnicians = (  data) => ({
    type: constants.UPDATE_OR_CREATE_ASSIGNED_TECHNICIAN,
    payload: { data }
});

const removeAssignmentTechnicians = ( id) => ({
    type: constants.REMOVE_ASSIGNED_TECHNICIAN,
    payload:  id
});