import "./tableLegalize.scss";
import InfoQuote from "./InfoQuote";
import React, { useEffect, useState } from "react";

const QuoteDetails = ({ data, onClose }) => {
	const [dataSource, setDataSource] = useState([]);
	useEffect(() => {
		setDataSource(data);
	}, [data]);

	return (
		<>
			{dataSource && (
				<>
					<h6 style={{ textAlign: "center", marginBottom: "16px" }}>
						Legalización de venta
					</h6>
					<InfoQuote onClose={onClose} info={dataSource.record} />{" "}
				</>
			)}
		</>
	);
};

export default QuoteDetails;
