
import React from 'react';
import { Modal } from 'antd';
import UpdateCuopons from "./UpdateCuopons";

const ModalUpdateCoupons =({handleCancel, onClose, data}) => {
    return(
        <>
            <Modal
                centered
                visible={true}
                width={900}
                footer={false}
                onCancel={handleCancel}
                destroyOnClose={true}
                title="Actualizar cupón"
            >
                <UpdateCuopons data={data} onClose={onClose}/>
            </Modal>
        </>

    )
}

export default ModalUpdateCoupons;