import React from 'react';
import {Col, Row} from 'antd';
import {FORMATTER_PESO} from "../../../../../redux/constants";


const CustomerAndUnitSummary = ({infoCustomer,InfoUnit,agreedValue}) => {
  return (
		<Row>
			<Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 24 }}>
				<table className="table-report">
					<tr className="th-report">
						<td colSpan="3" className="title head_color th-report"><b>RESUMEN CLIENTE</b></td>
						<td colSpan="3" className="title head_color th-report"><b>RESUMEN UNIDAD</b></td>
					</tr>
					<tr className="cell_not_center th-report">
						<td className="th-report">NOMBRE DEL CLIENTE</td>
						<td className="th-report" colSpan="2">{infoCustomer.name}</td>
						<td className="th-report">FECHA DE COMPRA</td>
						<td className="th-report" colSpan="2">{InfoUnit.sale_date}</td>
					</tr>
					<tr className="cell_not_center th-report">
						<td className="th-report">IDENTIFICACION</td>
						<td className="th-report" colSpan="2">{infoCustomer.identification}</td>
						<td className="th-report">N° DE APTO</td>
						<td className="th-report" colSpan="2">{InfoUnit?.label_unit}</td>
					</tr>
					<tr className="cell_not_center th-report">
						<td className="th-report">DIRECCION</td>
						<td className="th-report" colSpan="2">{infoCustomer.address}</td>
						<td className="th-report">N° DE CUOTAS</td>
						<td className="th-report" colSpan="2">{InfoUnit?.number_dues}</td>
					</tr>
					<tr className="cell_not_center th-report">
						<td className="th-report">CORREO</td>
						<td  className="th-report" colSpan="2">{infoCustomer.email}</td>
						<td className="th-report">FECHA DE CORTE</td>
						<td className="th-report" colSpan="2">{InfoUnit.cutoff_date}</td>
					</tr>
					<tr className="cell_not_center th-report">
						<td className="th-report">CELULAR</td>
						<td className="th-report" colSpan="2">{infoCustomer.phone_number}</td>
						<td className="th-report" >VR. TOTAL INMUEBLE</td>
						<td className="th-report" colSpan="2">{FORMATTER_PESO.format(agreedValue)}</td>
					</tr>

				</table>
			</Col>
		</Row>
  );
}

export default CustomerAndUnitSummary;