import React, {useEffect, useState} from "react";
import {Checkbox, Col, DatePicker, Divider, Form, Input, InputNumber, Row, Select,} from "antd";
import RegisterBuilderControlPower from "../BuilderControlPower/RegisterBuilderControlPower";
import {useDispatch, useSelector} from "react-redux";
import {FORMATTER_INPUT_NUMBER} from "../../../../../../redux/constants";
import {getOptionsSelect} from "../../../../../../redux/actions/Project/ProjectsAction";
import moment from "moment";
import {monthDiff} from "../../../../../handle/Utilitis";

const prefixSelector = (
	<Form.Item name="prefix" noStyle>
		<Select
			style={{
				width: 70,
			}}
		>
			<Select.Option value="86">+57</Select.Option>
			<Select.Option value="87">+87</Select.Option>
		</Select>
	</Form.Item>
);

const ProjectData = ({itemStyle, calculatePercentageUnitsSold, controlCompetencesDatabase, projectStates}) => {
	const priceRange = useSelector((state) => state?.pricesRange);
	const builders = useSelector((state) => state?.builder);

	const [projectTypes, setProjectTypes] = useState([]);
	const [stateProject, setStateProject] = useState("");

	const [unitPercentage, setUnitPercentage] = useState(0);
	const [unitDate, setUnitDate] = useState(0);
	const [totalUnits, setTotalUnits] = useState(0);
	const [salesSpeedMonth, setSalesSpeedMonth] = useState(0);
	const [stateDateDeliver, setStateDateDeliver] = useState();
	const [stateDateStart, setStateDateStart] = useState();
	const [stateDateConstruction, setStateDateConstruction] = useState();

	const dispatch = useDispatch();	

	useEffect(() => {
		dispatch(getOptionsSelect((response) => {
			let project = []
			response._payload.project_types.forEach((value) => {
				project.push({
					name: value.name,
					id: value.id,
				});
			});
			setProjectTypes(project);
		}));


	}, []);


	useEffect(() => {
		if (controlCompetencesDatabase && controlCompetencesDatabase?._payload && projectStates) {
			let c = controlCompetencesDatabase?._payload;
			setStateDateStart(moment(c?.release_date));
			setTotalUnits(parseFloat(c?.total_units ?? 0))
			setUnitDate(parseFloat(c?.sold_units ?? 0));
			setUnitPercentage(((c?.sold_units ?? 0) / (c?.total_units ?? 0)) * 100)
			setStateProject(projectStates.find(x => x.id === parseInt(c?.project_state_id)).name)
			setStateDateDeliver(moment(c?.delivery_date))
			setStateDateConstruction(moment(c?.building_date));
			getSpeedMonth(moment(c?.release_date), c?.sold_units)
			onChangeDate(moment(c?.delivery_date), moment(c?.building_date))
		}
	}, [controlCompetencesDatabase,projectStates])

	function calculateLengthNumber(number) {
		return number.toString().split("").length;
	}

	function getTelephoneValidationPromise(phoneNumber) {
		let spaces = /^(?!\s)/;
		if (phoneNumber != null) {
			if (calculateLengthNumber(phoneNumber) < 7) {
				return Promise.reject(
					new Error("El teléfono debe tener al menos 7 dígitos")
				);
			} else if (calculateLengthNumber(phoneNumber) >= 15) {
				if (!spaces.test(phoneNumber)) {
					return Promise.reject(new Error("Teléfono no válido"));
				}
				return Promise.reject(
					new Error("El teléfono debe tener maxímo 15 dígitos")
				);
			}
		}
		return Promise.resolve();
	}

	
	
	const getSpeedMonth = function (stateDateSt, unitD) {
		let today = new Date();
		let d;
		if (typeof (stateDateSt) != 'undefined' && typeof (unitD) != 'undefined') {
			let lanzamiento = new Date(stateDateSt._d);
			let mesDiff = monthDiff(lanzamiento, today);
			if (mesDiff == 0) {
				 d = parseFloat(unitD);
				 setSalesSpeedMonth(d);
			} else {
				 d = (parseFloat(unitD) / monthDiff(lanzamiento, today)) ?? 0;
				setSalesSpeedMonth(d);
			}
		}
		return d;
	};
	

	const onChangeDate = (DateDeliver = null, dateConstruction = null) => {
		let fecha_construccion = '';
		let fecha_fin = '';
		let hoy = new Date();

		fecha_construccion = new Date(dateConstruction);
		fecha_fin = new Date(DateDeliver);
		if (projectStates){
			if (hoy < fecha_construccion) {
				setStateProject(projectStates.find(x => x.id == 1).name)
			}

			if (hoy > fecha_fin) {
				setStateProject(projectStates.find(x => x.id == 3).name)
			}

			if (hoy >= fecha_construccion && hoy <= fecha_fin) {
				setStateProject(projectStates.find(x => x.id == 2).name)
			}
		}
		
	}


	return (
		<>
			<Divider orientation="left">Datos del proyecto</Divider>
			<Row gutter={16}>
				<Col xs={24} sm={24} md={12} lg={12} xl={12}>
					<Form.Item
						label="Nombre"
						name="name"
						rules={[
							{
								required: true,
								message: "El campo es requerido.",
							},
						]}
					>
						<Input placeholder="Nombre"/>
					</Form.Item>
					<Form.Item label="Comercializadora" name="marketer" style={itemStyle}>
						<Input placeholder="Comercializadora"/>
					</Form.Item>
					<Form.Item
						label="Tipo de proyecto"
						name="projectType"
						showSearch
						optionFilterProp="children"
						filterOption={(input, option) =>
							option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
						}
						style={itemStyle}
					>
						<Select defaultValue="">
							<Select.Option value="">Seleccionar</Select.Option>
							{projectTypes.map((value) => {
								return(
									<Select.Option key={value.id} value={value.id}>
										{value.name}
									</Select.Option>
								)
							})}
						</Select>
					</Form.Item>

					<Form.Item valuePropName={stateDateStart} name="releaseDate" label="Fecha de lanzamiento">
						<DatePicker value={stateDateStart} onChange={(value) => {getSpeedMonth(value, unitDate); setStateDateStart(value);}} style={itemStyle} placeholder="Fecha de lanzamiento"/>
					</Form.Item>
					<Form.Item valuePropName={stateDateDeliver} name="deliveryDate" label="Fecha de entrega">
						<DatePicker value={stateDateDeliver} style={itemStyle} placeholder="Fecha de Entrega"
									onChange={(value) => {
										onChangeDate(value, stateDateConstruction)
										setStateDateDeliver(value);
									}}
						/>
					</Form.Item>
					<Form.Item valuePropName={totalUnits} name="totalUnits" label="Total unidades">
						<InputNumber value={totalUnits} style={itemStyle} placeholder="Total unidades"
									 onChange={(value) => {
										 setUnitPercentage((unitDate / value) * 100)
										 setTotalUnits(value);
									 }}
						/>
					</Form.Item>
					<Form.Item valuePropName={unitPercentage.toFixed(2)} name="percentageUnitsSold"
							   label="Unidades vendidas (%)">
						<Input
							value={unitPercentage.toFixed(2)}
							disabled
							style={itemStyle}
							placeholder={parseFloat(calculatePercentageUnitsSold()).toFixed(2)}
						/>
					</Form.Item>
					<Form.Item name="launchPriceM2" label="Precio lanzamiento del m2">
						<InputNumber
							style={itemStyle}
							{...FORMATTER_INPUT_NUMBER}
							placeholder="Precio lanzamiento del m2"
						/>
					</Form.Item>
					<Form.Item label="Nombre de la fuente" name="sourceName">
						<Input placeholder="Nombre de la fuente" style={itemStyle}/>
					</Form.Item>
					<Form.Item name="observations" label="Observaciones">
						<Input.TextArea placeholder="Observaciones" Rows={5}/>
					</Form.Item>
				</Col>
				<Col xs={24} sm={24} md={12} lg={12} xl={12}>
					<Input.Group compact>
						<Form.Item
							label="Constructor"
							name="builder"
							style={{width: "90%"}}
							rules={[
								{
									required: true,
									message: "El campo es requerido.",
								},
							]}
						>
							<Select
								showSearch
								optionFilterProp="children"
								filterOption={(input, option) =>
									option.children.toLowerCase().indexOf(input.toLowerCase()) >=
									0
								}
								defaultValue=""
								loading={Array.isArray(builders)}
							>
								<Select.Option value="">Seleccionar</Select.Option>
								{!Array.isArray(builders) &&
								builders._payload.map((item) => (
									<Select.Option value={item.id}>{item.name}</Select.Option>
								))}
							</Select>
						</Form.Item>
						<Form.Item style={{marginLeft: 3}} label=" ">
							<RegisterBuilderControlPower/>
						</Form.Item>
					</Input.Group>
					<Form.Item label="Dirección" name="address">
						<Input placeholder="Dirección" style={itemStyle}/>
					</Form.Item>
					<Form.Item
						label="Rango de precios"
						name="priceRange"
						style={itemStyle}
					>
						<Select
							showSearch
							optionFilterProp="children"
							filterOption={(input, option) =>
								option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
							}
							loading={Array.isArray(priceRange)}
							defaultValue=""
						>
							<Select.Option value="">Seleccionar</Select.Option>
							{!Array.isArray(priceRange) &&
							priceRange._payload.map((item) => (
								<Select.Option value={item.id}>{item.name}</Select.Option>
							))}
						</Select>
					</Form.Item>

					<Form.Item valuePropName={stateDateConstruction} name={"buildingDate"} label="Fecha de construcción">
						<DatePicker value={stateDateConstruction} style={itemStyle} placeholder="Fecha de construcción"
									onChange={(value) => {
										onChangeDate(stateDateDeliver, value);
										setStateDateConstruction(value)
									}}
						/>
					</Form.Item>
					<Form.Item valuePropName={stateProject} label="Estado del proyecto" name="projectStatus">
						<Input
							placeholder="Estado del proyecto"
							defaultValue={stateProject}
							disabled
							value={stateProject}
							style={itemStyle}
						/>
					</Form.Item>
					<Form.Item
						name="unitsSoldToDate"
						label="Unidades vendidas a la fecha"
						valuePropName={unitDate}
					>
						<InputNumber
							value={unitDate}
							placeholder="Unidades vendidas a la fecha"
							onChange={(value) => {
								setUnitDate(value);
								setUnitPercentage((value / totalUnits) * 100)
								getSpeedMonth(stateDateStart, value);
							}}
							style={itemStyle}
						/>
					</Form.Item>
					<Form.Item valuePropName={salesSpeedMonth?.toFixed(2) ?? 0} name="salesSpeedMonth"
							   label="Velocidad de ventas mes">
						<Input
							value={salesSpeedMonth?.toFixed(2) ?? 0}
							disabled
							style={itemStyle}
							placeholder="Velocidad de ventas mes"
						/>
					</Form.Item>
					<Form.Item label="Precio actual del m2" name="currentPriceOfM2">
						<InputNumber
							style={itemStyle}
							placeholder="Precio actual del m2"
							{...FORMATTER_INPUT_NUMBER}
						/>
					</Form.Item>
					<Form.Item
						name="sourcePhone"
						label="Teléfono de la fuente"
						rules={[
							{required: true, message: "El Teléfono es requerido."},
							{
								validator: async (_, value) => {
									return getTelephoneValidationPromise(value);
								},
							},
						]}
					>
						<Input
							addonBefore={prefixSelector}
							style={{
								width: "100%",
							}}
							placeholder="Teléfono de la fuente"
						/>
					</Form.Item>
					<Form.Item valuePropName="checked" label="Activo" name="state">
						<Checkbox/>
					</Form.Item>
				</Col>
			</Row>
		</>
	);
};

export default ProjectData;
