import { useDispatch } from "react-redux";
import React, { useEffect, useState } from "react";
import Spinner from "../../../../handle/Spinner/Spinner";
import { Button, Card, Checkbox, Col, notification, Row, Spin } from "antd";
import { fetchAllProfile } from "../../../../../redux/actions/Configuration/ProfileAction";
import { fetchAllPermission, fetchProfilePermission, fetchSavePermissions } from "../../../../../redux/actions/Configuration/PermissionAction";

function PermissionList(props) {
	const [ profilePermissions, setProfilePermissions ] = useState([]);
	const [ permissions, setPermissions ] = useState([]);
	const [ checkedValues, setCheckedValues ] = useState([]);
	const dispatch = useDispatch();
	const [ loading, setLoading ] = useState(false);

	useEffect(() => {
		dispatch(
			fetchProfilePermission(props.profileId, async (response) => {
				let _response = await response;
				if (!_response.error) {
					setProfilePermissions(response._payload);
					setCheckedValues(
						response._payload.map((item) => {
							return item.id;
						})
					);
				}
			})
		);
		dispatch(
			fetchAllPermission(async (response) => {
				let _response = await response;
				if (!_response.error) {
					setPermissions(response._payload);
				}
			})
		);
	}, []);
	
	if (permissions.length === 0) {
		return <Spinner />;
	}

	function onChange(checkedValue,event) {
		let orderedList=[];
		let exist = false;
		let list = [];
		let indes = 0;
		
		checkedValues.forEach((item,index) => {
			if (item !== checkedValue) list.push(item);
			else {
				exist = true
				indes = index
			}
		});
		if (event.target.checked){
			if (!exist) list.push(parseInt(checkedValue));
		}else {
			if (!exist){
				for (let i = 0; i < list.length; i++){
					if(parseInt(checkedValue)!=list[i]){
						orderedList.push(list[i]);
					}
				}
				list=orderedList;
			}
		}
		list=list.sort(function(a, b) {return a - b})
		setCheckedValues(list);
	}

	function onSubmit() {
		setLoading(true);
		let data = {
			permissions: checkedValues,
		};
		dispatch(
			fetchSavePermissions(props.profileId, data, async (response) => {
				let _response = await response;
				setLoading(false);
				if (!_response.error) {
					notification["success"]({
						message: "Proceso exitoso",
						description: "Permisos actualizados con éxito",
					});
					if (
						JSON.parse(localStorage.getItem("USER_INFO")).idProfile ===
						props.profileId
					) {
						localStorage.removeItem("USER_PERMISSIONS");
						localStorage.setItem(
							"USER_PERMISSIONS",
							JSON.stringify(data.permissions)
						);
						dispatch(fetchAllProfile());
					}
					props.closed();
				}
			})
		);
	}
	
	const validateChecked = (permissionId) => {
		let response = false
		profilePermissions.forEach((item)=>{
			if (parseInt(item.id) === parseInt(permissionId)){
				response = true
			}
		})
		return response
	};

	return (
		<>
			<div>
				<Row gutter={[24, 24]}>
					{permissions.map((item) => (
						<Col span={6}>
							<Card
								hoverable
								type="inner"
								title={item.module_name}
								style={{ width: "100%" }}
							>
								<Row style={{ display: "block" }}>
									{item.permissions
										? item.permissions.map((subItem) => (
												<Col>
													<Checkbox
														key={subItem.id}
														onChange={(e) => {
															onChange(subItem.id,e);
														}}
														defaultChecked={validateChecked(subItem.id)}
													>
														{subItem.name}
													</Checkbox>
												</Col>
										  ))
										: null}
								</Row>
							</Card>
						</Col>
					))}
				</Row>
				<br />
				<Row justify="end">
					<Col>
						{loading ? (
							<Spin
								style={{ paddingLeft: "5%" }}
								spinning={true}
								size="large"
							/>
						) : (
							<Button onClick={onSubmit}>Guardar cambios</Button>
						)}
					</Col>
				</Row>
			</div>
		</>
	);
}

export default PermissionList;
