import React from "react";
import { Link } from "react-router-dom";
import { PageHeader, Button } from "antd";
import TableQuotes from "./Components/Table";
import { FileTextOutlined } from "@ant-design/icons";
import * as constants from "../../../../redux/constants";
import { Redirect } from "react-router-dom/cjs/react-router-dom.min";
import { validateModule, validatePermission } from "../../../handle/PermissionMethods/PermissionMethods";

const Quotes = () => {
	if (!validateModule(constants.QUOTATION_SUBMODULE)) {
		return <Redirect to={constants.DEFAULT_ROUTE} />;
	}
	return (
		<React.Fragment>
			<PageHeader
				style={{ border: "1px solid rgb(235, 237, 240);" }}
				title={"Lista de cotizaciones"}
				extra={[
					
					validatePermission(constants.CREATE_QUOTATION_PERMISSION) ? (
						<Link to="/sales/new-quotes">
						<Button
							style={{ background: "#1890ff", color: "#fff" }}
							icon={<FileTextOutlined />}
						>
							Nueva cotización
						</Button>
					</Link>
					) : null
				]}
			/>
			<TableQuotes />
		</React.Fragment>
	);
};

export default Quotes;
