import { Modal, Button } from "antd";
import React, { useState } from "react";
import FormPresupuesto from "./FormPresupuesto";
import { PlusOutlined } from "@ant-design/icons";

const ModalQuotes = (prods) => {
	const [isModalVisible, setIsModalVisible] = useState(false);
    const {createArrangement} = prods;
	const showModal = () => {
		setIsModalVisible(true);
	};
    const onClose = () => {
		setIsModalVisible(false);
	};
    const setPriceRange = (priceRange) =>{
        createArrangement(priceRange);
    }
	return (
		<>
			<Button type="primary" onClick={showModal}>
				<PlusOutlined />
			</Button>
			<Modal
				width={500}
				title="Nuevo rango de precios"
				visible={isModalVisible}
                onCancel={onClose}
                footer={false}
			>
				<FormPresupuesto onClose={onClose} setPriceRange={setPriceRange} />
			</Modal>
		</>
	);
};

export default ModalQuotes;
