import * as constants from "../../../constants";

export const fetchExcelBuyerDatabase= (data, postProcessSuccess, onError) => ({
    type: constants.API,
	payload: {
		method: 'POST',
		data: data,
		url: `${constants.RUTA}/report/get_path_excel_report_buyer_database`,
		postProcessSuccess: postProcessSuccess,
		postProcessError: onError
	}
})





