import React, {useState, useEffect, useContext} from "react";
import { Table, Button, Tooltip, InputNumber } from "antd";
import {warning} from "../../../../../../handle/Notification/Notification";
import {FORMATTER_INPUT_NUMBER} from "../../../../../../../redux/constants";
import {FormatterPeso} from "../../../../Quotes/Components/Utilitis/Utilitis";
import { DeleteOutlined, PlusOutlined, MinusOutlined } from "@ant-design/icons";
import {convertAllMapDataState} from "../../../../../../handle/HandleFilterTable/handleFilterTable";
import {useSaleMade} from "../../../Hooks/useSaleMade";
import {SalesMadeContext} from "../context/SalesMadeContext";

let dates = ["Ene-", "Feb-", "Mar-", "Abr-", "May-", "Jun-", "Jul-", "Ago-", "Sep-", "Oct-", "Nov-", "Dic-"];

const TablePagoCliente = (dataTable) => {
	
	const { paymentMethodProposedByTheClient,paymentMethodProposedByTheProject,setPaymentMethodProposedByTheClient,
		calculatePor,numberOfMonthsP,calculatePresentValueByTheClient,cutoffDate,payment_relationship_setting
	} = useContext(SalesMadeContext);
	
    const [modify, setModify] = useState([])
    const [deliveryDate, setDelivery] = useState(0)
    // const { modified_values, calculatePor,payment_relationship_setting,valueUnit,numberOfMonthsP,cutoff,calculatePresentValueByTheClient } = dataTable
    const [contQuotes, setContQuotes] = useState(0)
    const [numberElements, setNumberElements] = useState(0);
	const {CalculateDelivery,data} = useSaleMade();

    useEffect(() => {
		if (paymentMethodProposedByTheClient.formOfPayments.length !== 0) {  
            setNumberElements(numberOfMonthsP);
        }
    }, [paymentMethodProposedByTheClient]);
	//
    useEffect(() => {
        calculateDelivery()
    }, [paymentMethodProposedByTheClient.formOfPayments]);

	useEffect(()=>{
		calculatePresentValueByTheClient(paymentMethodProposedByTheClient.formOfPayments,deliveryDate);
	},[deliveryDate]);
	
    const calculateDateAdd = () => {
		
        let lastDate = paymentMethodProposedByTheClient.formOfPayments[paymentMethodProposedByTheClient.formOfPayments.length - 2]?.month?.split('-');
		if (Array.isArray(lastDate)){
			if (lastDate.length === 3) {
				let monthDate = lastDate[0];
				let posMonth = 0;
				let year = parseInt(lastDate[2]);
				let cutoff_date = cutoffDate;
				dates.forEach((d) => {
					if (d.includes(monthDate)) {
						posMonth = dates.indexOf(d) + 1;
					}
				});
				return {
					month: posMonth,
					cutoff_date: cutoff_date,
					year: year,
				}
			} else {
				let monthDate = lastDate[0];
				let posMonth = 0;
				let year = parseInt(lastDate[1]);
				dates.forEach((d) => {
					if (d.includes(monthDate)) {
						posMonth = dates.indexOf(d) + 1;
					}
				});
				return {
					month: posMonth,
					year: year,
				}
			}
		}


    };

	

    const removeQuote = () => {
        let arrayToWork = [...paymentMethodProposedByTheClient.formOfPayments]
        let contMounts = contQuotes - 1
        setContQuotes(contMounts)
        let lastData = arrayToWork.pop()
        arrayToWork.pop()
        if (arrayToWork[arrayToWork.length - 1].key !== numberElements) {
            arrayToWork[arrayToWork.length - 1].new = 1
        }
        arrayToWork.push(lastData)
		setPaymentMethodProposedByTheClient({
			...paymentMethodProposedByTheClient,
			formOfPayments:arrayToWork
		});
    }
   
    const addQuote = () => {
        let arrayToWork = [...paymentMethodProposedByTheClient.formOfPayments]
		let maximumNumberOfFees = parseInt(payment_relationship_setting?.maximum_number_months_finance_initial_fee)
        if (arrayToWork.length <= maximumNumberOfFees + 1) {
            let contMounts = 0;
            let lastData = arrayToWork.pop();
            let lastKey = (arrayToWork[arrayToWork.length - 1].key) + 1;
            let date = calculateDateAdd();
            if (date.month === 12) {
                date.month = 0;
                date.year++;
            } else if (date.month > 12 && date.month <= 23) {
                contMounts = date.month - 12;
                date.month = contMounts;
                date.year++;
            }
            else if (date.month >= 24) {
                contMounts = date.month - 24;
                date.month = contMounts;
                date.year = date.year + 2;
            }
            arrayToWork.forEach((a) => {
                a.new = 0
            });
            if(date.cutoff_date){
            arrayToWork.push({
                key: lastKey,
                month: data[date.month]+ date.cutoff_date+"-"+ date.year,
                value: 0,
                porcentaje: 0,
                new: 1,
                idMonth: date.month
            })
            }else{
                arrayToWork.push({
                    key: lastKey,
                    month: data[date.month]+ date.year,
                    value: 0,
                    porcentaje: 0,
                    new: 1,
                    idMonth: date.month
                })
            }

            lastKey++;
            lastData.key = lastKey;
            arrayToWork.push(lastData);
			setPaymentMethodProposedByTheClient({
				...paymentMethodProposedByTheClient,
				formOfPayments:arrayToWork
			});
        }else {
			warning('Número máximo de cuotas alcanzado');
		}

    }
	
    const columns = [
        {
            title: "Número de Cuota",
            dataIndex: "month",
            width: 80,
            render: (_, record) => split(record),
        },
        {
            title: "Valor",
            dataIndex: "value",
            width: 80,
            render: (_, record) => actions(record),
        },
        {
            title: "Porcentaje",
            dataIndex: "porcentaje",
            width: 80,
            render: (_, record) => valuates(record),
        },
    ];

    const calculateDelivery = () => {
		let total = CalculateDelivery(paymentMethodProposedByTheClient.formOfPayments,paymentMethodProposedByTheProject.unitValue);
		setDelivery(total)
		return total
    }

    const split = (record) => {
       
        const text = <span style={{ color: "#6A6963" }}> Agregar </span>;
        return (
            <>
                {record.month === "C.I. No Financiada" ? (
                    record.month
                ) : record.month === "Entrega" ? (
                    <div style={{ width: "100%", display: "flex", textAlign: "center" }}>
                        <div style={{ width: "30%", borderRight: "1px solid #f0f0f0" }}>
                            <Tooltip placement="left" color="#ffffff" title={text}>
                                <Button
                                    size="small"
                                    type="primary"
									disabled={paymentMethodProposedByTheClient.formOfPayments.length === payment_relationship_setting?.maximum_number_months_finance_initial_fee}
                                    onClick={addQuote}
                                    icon={<PlusOutlined />}
                                />
                            </Tooltip>
                        </div>
                        <div style={{ width: "70%" }}>{record.month}</div>
                    </div>
                ) :
                    (<div style={{ width: "100%", display: "flex", textAlign: "center" }} >
                        <div style={{ width: "30%", borderRight: "1px solid #f0f0f0" }}>
                            {record.key}
                        </div>
                        <div style={{ width: "70%" }}>{record.month}</div>
                    </div>)
                }
            </>
        );
    };

    const clean = (key) => {
        const arrayToWork = [...paymentMethodProposedByTheClient.formOfPayments]
        arrayToWork.forEach((item) => {
            if (item.key === key) {
                calculateDelivery(item.value)
                item.porcentaje = calculatePor(0, paymentMethodProposedByTheProject.unitValue)
                item.value = 0
            }
        })
        arrayToWork.forEach((item) => {
            if (item.month === "Entrega") {
                item.porcentaje = calculatePor(calculateDelivery(), paymentMethodProposedByTheProject.unitValue)
            }
        })
		setData(key,0)
		setPaymentMethodProposedByTheClient({
			...paymentMethodProposedByTheClient,
			formOfPayments:arrayToWork
		})
    }

	const setData = (key,value) => {
		const arrayToWork = [...paymentMethodProposedByTheClient.formOfPayments];
		if (modify.length === 0) {
			let tem = [
				{
					id: arrayToWork[key].id,
					month: arrayToWork[key].month,
					value: parseFloat(value),
				},
			];
			// modified_values(tem,tem.length - 2)
			setModify(tem);
		} else {
			let tem = modify;
			let found = false;
			tem.forEach((item) => {
				if (item.month === arrayToWork[key].month) {
					item.value = parseFloat(parseFloat(value).toFixed(2));
					found = true;
				}
			});
			if (!found) {
				let array = {
					id: arrayToWork[key].id,
					month: arrayToWork[key].month,
					value: value,
				};
				tem.push(array);
			}
			// modified_values(tem,tem.length - 2);
			setModify(tem);
		}
	}
	
    const Modify = (event, record) => {
        const arrayToWork = [...paymentMethodProposedByTheClient.formOfPayments];
        arrayToWork.forEach((item) => {
            if (item.key === record.key) {
				item.porcentaje = calculatePor(parseFloat(event), paymentMethodProposedByTheProject.unitValue);
                item.value = parseFloat(event);
                calculateDelivery()
            }
        })
        arrayToWork.forEach((item) => {
            if (item.month === "Entrega") {
                item.porcentaje = calculatePor(calculateDelivery(), paymentMethodProposedByTheProject.unitValue)
            }
        })
		setData(record.key,record.value)
		setPaymentMethodProposedByTheClient({
			...paymentMethodProposedByTheClient,
			formOfPayments: arrayToWork
		})
    };

    const actions = (record) => {
        const text = <span style={{ color: "#6A6963" }}> Limpiar </span>;
        return (
            <>
                {paymentMethodProposedByTheClient.formOfPayments.length === 0 ? (<></>) : record.month === "Entrega" ? (
					FormatterPeso.format(deliveryDate)
                ) : (
                    <>
                        <InputNumber
							{...FORMATTER_INPUT_NUMBER}
                            onChange={(event) => Modify(event, record)}
                            style={{ width: "80%" }}
                            name={record.key}
                            value={paymentMethodProposedByTheClient.formOfPayments[paymentMethodProposedByTheClient.formOfPayments.indexOf(record)].value}
                            size="small"
                        />
                        <Tooltip placement="left" color="#ffffff" title={text}>
                            <Button
                                size="small"
                                onClick={() => {
                                    clean(record.key);
                                }}
                                type="danger"
                                icon={<DeleteOutlined />}
                            />
                        </Tooltip>
                    </>
                )}
            </>
        );
    };
    const valuates = (record) => {
        const text = <span style={{ color: "#6A6963" }}> Remover </span>;
        return (<>{
            record.new === 1 ? (<div style={{ width: "100%", display: "flex", textAlign: "center" }} >
                <div style={{ width: "70%" }}>{parseFloat(record.porcentaje).toFixed(2) + " %"}</div>
                <div style={{ width: "30%", borderRight: "1px solid #f0f0f0" }}>
                    <Tooltip placement="left" color="#ffffff" title={text}>
                        <Button
                            size="small"
                            type="primary"
                            onClick={removeQuote}
                            icon={<MinusOutlined />}
                        />
                    </Tooltip>
                </div>
            </div>) : (<div style={{ width: "100%", display: "flex", textAlign: "center" }} >
                <div style={{ width: "70%" }}>{parseFloat(record.porcentaje).toFixed(2) + " %"}</div>
            </div>)
        }</>);
    };
    if (paymentMethodProposedByTheClient.formOfPayments.length === 0) return null;
    return (
        <>
            <Table
				bordered
				title={() => ''}
				columns={columns}
                dataSource={convertAllMapDataState(paymentMethodProposedByTheClient.formOfPayments)}
				pagination={false}
				size="small"
			/>
        </>
    );
};

export default TablePagoCliente;
