import React, {useEffect} from "react";
import Card from "../../../components/Card/Card";
import * as constants from "../../../../redux/constants";
import AssignResponsible from "./Componets/AssignResponsibleComponents/AssignResponsible";
import ReassignAdvisorSales from "./Componets/ReassignAssesor";
import LegalizeDocuments from "./Componets/legalizeDocumentsComponents/LegalizeDocuments";
import RemainingPaymentMethod from "./Componets/RemaniningPaymentMethodComponent/RemainingPaymentMethod";
import ReassignCustomer from "./Componets/ReassignCustomer";
import ManageExchanges from "./Componets/ManageExchanges/ManageExchanges";
import RegisterDeed from "./Componets/Deed/RegisterDeed";
import {validateModule, validatePermission} from "../../../handle/PermissionMethods/PermissionMethods";
import {Redirect} from "react-router-dom/cjs/react-router-dom.min";
import UpdateDeed from "./Componets/Deed/UpdateDeed.jsx";
import {Button, Table, Tooltip} from "antd";
import {convertAllMapDataState, getColumnSearchProps} from "../../../handle/HandleFilterTable/handleFilterTable";
import {
	PrinterOutlined,
	CheckOutlined,
	CloseOutlined,
	EditOutlined,
} from "@ant-design/icons";
import {useDispatch, useSelector} from "react-redux";
import {fetchAllSaleMade} from "../../../../redux/actions/Price/SaleMade/SalesMadeAction";
import {
	downloadQuotes,
	printView,
} from "../../../../redux/actions/Price/Quotation/QuotesAction";
import ModalDesistSale from "./Componets/DesistSale/ModalDesistSale";
import {Link} from "react-router-dom";

const redCell = {
	background: "#f2dede",
};

const SalesMade = () => {
	const dispatch = useDispatch();
	const salesMades = useSelector((state) => state?.salesMade._payload);

	let customerDatabase = useSelector((state) => state?.customer);

	const getRedStyle = (value) => {
		return value ? redCell : null;
	};
	useEffect(() => {
		dispatch(fetchAllSaleMade());
	}, []);

	const getRenderCustomerPersonaldata = (record) => {
		let person = record.customer.person;
		let customer = record.customer;
		let fullName = getFullName(person);

		return {
			props: {
				style: getRedStyle(record.is_desisted),
			},
			children: (
				<div style={record.is_desisted ? redCell : null}>
					<strong>Nombre:</strong> <span>{fullName}</span> <br/>
					<strong>Teléfono:</strong> <span>{person.phone_number}</span> <br/>
					<strong>Correo:</strong> <span>{customer.email}</span>
				</div>
			),
		};
	};

	function getFullName(person) {
		let names = person.name.split("_");
		return names[0] + " " + names[1];
	}

	const columns = [
		{
			title: "#",
			width: 50,
			dataIndex: "id",
			key: "id",
			align: "center",
			fixed: "left",
			render(text, record) {
				return {
					props: {
						style: getRedStyle(record.is_desisted),
					},
					children: <h6 style={{fontSize: "14px"}}>{text}</h6>,
				};
			},
		},
		{
			title: "Codigo",
			width: 90,
			dataIndex: "unit_code",
			key: "code",
			align: "center",
			fixed: "left",
			...getColumnSearchProps("code"),
			render(text, record) {
				return {
					props: {
						style: getRedStyle(record.is_desisted),
					},
					children: <h6 style={{fontSize: "14px"}}>{text}</h6>,
				};
			},
		},
		{
			title: "Cliente",
			width: 250,
			dataIndex: "customer.email",
			key: "customer.email",
			render: (_, record) => getRenderCustomerPersonaldata(record),
		},
		{
			title: "Proyecto",
			width: 150,
			dataIndex: "project_name",
			key: "project_name",
			align: "center",
			...getColumnSearchProps("project_name"),
			render(text, record) {
				return {
					props: {
						style: getRedStyle(record.is_desisted),
					},
					children: <h6 style={{fontSize: "14px"}}>{text}</h6>,
				};
			},
		},
		{
			title: "Unidad",
			width: 90,
			dataIndex: "unit_name",
			key: "unit_name",
			align: "center",
			...getColumnSearchProps("unit_name"),
			render(text, record) {
				return {
					props: {
						style: getRedStyle(record.is_desisted),
					},
					children: <h6 style={{fontSize: "14px"}}>{text}</h6>,
				};
			},
		},
		{
			title: "Valor Unidad",
			dataIndex: "value_unit",
			width: 120,
			align: "center",
			key: "value_unit",
			render(text, record) {
				return {
					props: {
						style: getRedStyle(record.is_desisted),
					},
					children: constants.FORMATTER_PESO.format(text),
				};
			},
		},
		{
			title: "Valor Reformas",
			dataIndex: "value_reform",
			width: 120,
			align: "center",
			key: "value_reform",
			render(text, record) {
				return {
					props: {
						style: getRedStyle(record.is_desisted),
					},
					children: constants.FORMATTER_PESO.format(text),
				};
			},
		},
		{
			title: "Valor Total",
			dataIndex: "value_total",
			width: 120,
			align: "center",
			key: "value_total",
			render(text, record) {
				return {
					props: {
						style: getRedStyle(record.is_desisted),
					},
					children: constants.FORMATTER_PESO.format(text),
				};
			},
		},
		{
			title: "% CI Recaudado ",
			dataIndex: "ciRecaude",
			width: 100,
			align: "center",
			key: "ciRecaude",
			render(text, record) {
				return {
					props: {
						style: getRedStyle(record.is_desisted),
					},
					children: <h6 style={{fontSize: "14px"}}>{text}</h6>,
				};
			},
		},
		{
			title: "Asesor",
			dataIndex: "assessor_name",
			width: 140,
			key: "assessor_name",
			align: "center",
			...getColumnSearchProps("assessor_name"),
			render(text, record) {
				return {
					props: {
						style: getRedStyle(record.is_desisted),
					},
					children: <h6 style={{fontSize: "14px"}}>{text}</h6>,
				};
			},
		},
		{
			title: "Escrituración",
			dataIndex: "deed",
			width: 110,
			key: "deed",
			align: "center",
			render(text, record) {
				return {
					props: {
						style: getRedStyle(record.is_desisted),
					},
					children: record.deed_id ? (
						<CheckOutlined style={{fontSize: "18px", color: "#0D6B04"}}/>
					) : (
						<CloseOutlined style={{fontSize: "18px", color: "#f5222d"}}/>
					),
				};
			},
		},
		{
			title: "Legalización Documentos",
			dataIndex: "percentage_legalized_documents",
			width: 120,
			key: "percentage_legalized_documents",
			align: "center",
			render(text, record) {
				return {
					props: {
						style: getRedStyle(record.is_desisted),
					},
					children: <h6>{`${parseFloat(text).toFixed(2)}%`}</h6>,
				};
			},
		},
		{
			title: "Acciones",
			dataIndex: "actions",
			width: 180,
			fixed: "right",
			render: (_, record) => getActions(record),
		},
	];

	const getActions = (record) => {

		const textPrint = <span style={{color: "#6A6963"}}>Ver / Imprimir</span>;
		let fullName = getFullName(record.customer.person);

		const viewPrint = () => {
			dispatch(
				printView(record.id, async (response) => {
					const res = await response;
					if (!response.error) {
					const tagA = document.createElement("a");
					tagA.href = `${res._payload.path}`;
					tagA.target = "_blank";
					document.body.appendChild(tagA);
					tagA.click();
					}
				})
			);
		};

		const getDeed = (record) => {
			return record["deed_id"] == null ? (
				<RegisterDeed salesMade={record}/>
			) : (
				<UpdateDeed salesMade={record}/>
			);
		};

		const textEditar = <span style={{color: "#6A6963"}}> Editar </span>;
		return {
			
			props: {
				style: getRedStyle(record.is_desisted),
			},
			children: (
				<div>
					{validatePermission(constants.PRINT_SALE_MADE_PERMISSION) ? (
						<Tooltip placement="left" color="#ffffff" title={textPrint}>
							<Button
								onClick={viewPrint}
								style={{background: "#ffc53d", color: "#fff"}}
								size={"small"}
								icon={<PrinterOutlined/>}
							/>
						</Tooltip>
					) : null}
					{"  "}
					{validatePermission(constants.REASSIGN_ADVISOR_SALE_MADE_PERMISSION) ? (
						<ReassignAdvisorSales fullName={fullName} data={record}/>
					) : null}
					{"  "}
					{validatePermission(constants.ASSIGN_RESPONSIBLE_SALE_MADE_PERMISSION) ? (
						<AssignResponsible data={record}/>
					) : null}
					{"  "}
					{validatePermission(constants.LEGALIZE_DOCUMENTS_SALE_MADE_PERMISSION) ? (
						<LegalizeDocuments data={record}/>
					) : null}
					{"  "}
					{validatePermission(constants.REMAINING_WAY_TO_PAY_SALE_MADE_PERMISSION) ? (
						<RemainingPaymentMethod salesMade={record}/>
					) : null}
					{"  "}
					{validatePermission(constants.DEED_SALE_MADE_PERMISSION) ? getDeed(record) : null}
					{"  "}
					{validatePermission(constants.CHANGE_CUSTOMER_PERMISSION) ? (
						<ReassignCustomer data={record} customerDatabase={customerDatabase}/>
					) : null}
					{"  "}
					{validatePermission(constants.EXCHANGE_MANAGEMENT_PERMISSION) ? (
						<ManageExchanges data={record}/>
					) : null}
					{"  "}
					{validatePermission(constants.UPDATE_SALE_MADE_PERMISSION) ? (
						<Link to={`/sales/update-sales-made/${record.id}`}>
							<Tooltip placement="left" color="#ffffff" title={textEditar}>
								<Button
									type="primary"
									style={{
										backgroundColor: "#ffa247",
										borderColor: "#ffa247",
										color: "#fff",
									}}
									size="small"
									icon={<EditOutlined/>}
									disabled={record.is_desisted}
								/>
							</Tooltip>
						</Link>
					) : null}
					{"  "}
					{validatePermission(constants.DESIST_SALE_MADE_PERMISSION) ? (
						<ModalDesistSale data={record}/>
					) : null}
				</div>
			),
		};
	};
	if (!validateModule(constants.SALES_MADE_SUBMODULE)) {
		return <Redirect to={constants.DEFAULT_ROUTE}/>;
	}
	return (
		<Card
			title="Ventas Realizadas"
			table={
				<Table
					columns={columns}
					dataSource={convertAllMapDataState(salesMades)}
					scroll={{x: 1500, y: 300}}
					bordered
				/>
			}
		/>
	);
};

export default SalesMade;
