import React from "react";
import { Select, Form, InputNumber, Col, Row, Divider } from "antd";

const SpecificationsArea = ({itemStyle}) => {
	return (
		<>
			<Divider orientation="left">Especificaciones por área</Divider>
			<Row gutter={16}>
				<Col xs={24} sm={24} md={12} lg={12} xl={12}>
					<Form.Item name="numberBedrooms" label="Número de habitaciones">
						<InputNumber placeholder="Número de habitaciones" style={itemStyle} />
					</Form.Item>
					<Form.Item name="numberBalconies" label="Número de balcones">
						<InputNumber placeholder="Número de balcones" style={itemStyle} />
					</Form.Item>
					<Form.Item label="Cocina integral" name="integralKitchen">
						<Select defaultValue="2">
							<Select.Option value="">Seleccione...</Select.Option>
							<Select.Option value="1">SI</Select.Option>
							<Select.Option value="2">NO</Select.Option>
						</Select>
					</Form.Item>
				</Col>
				<Col xs={24} sm={24} md={12} lg={12} xl={12}>
					<Form.Item name="numberBathrooms" label="Número de baños">
						<InputNumber style={itemStyle} placeholder="Número de baños" />
					</Form.Item>
					<Form.Item label="Alcoba de servicio" name="serviceBedroom">
						<Select defaultValue="2">
							<Select.Option value="">Seleccione...</Select.Option>
							<Select.Option value="1">SI</Select.Option>
							<Select.Option value="2">NO</Select.Option>
						</Select>
					</Form.Item>

					<Form.Item label="Estudio" name="study">
						<Select defaultValue="2">
							<Select.Option value="">Seleccione...</Select.Option>
							<Select.Option value="1">SI</Select.Option>
							<Select.Option value="2">NO</Select.Option>
						</Select>
					</Form.Item>
				</Col>
			</Row>
		</>
	);
};

export default SpecificationsArea;
