import _ from "lodash";
import * as constants from "../../../constants";


const initialValueflags = {
    isFetchingAllReformRequest: false,
    errorAllReformRequest: false,

    isAddingReformRequest: false,
    errorAddReformRequest: false,

    isUpdatingReformRequest: false,
    errorUpdatingReformRequest: false,

    isDeletingReformRequest: false,
    errorDeletingReformRequest: false,
}

const initialState = {
    project: {
        id: undefined,
        name: undefined,
        maximum_reforms_date: undefined
    },
    reforms_requests: [],
    customer: {
        name: undefined,
        id: undefined
    },
    unit: {
        label: undefined,
        id: undefined
    },
    creator_user: {
        id: undefined,
        name: undefined
    },
    date_reform_request: null,
    ...initialValueflags
    
}
export default function reformRequestBySaleReducer(state = initialState, action) {
    switch (action.type) {
        case constants.START_FETCHING_ALL_REFORM_REQUEST:
            return {
                ...state,
                ...initialValueflags,
                isFetchingAllReformRequest: true,
                
            };
        case constants.GET_ALL_REFORM_REQUEST:
            return { 
                ...action.payload._payload,
                ...initialValueflags,
                isFetchingAllReformRequest: false,
                errorAllReformRequest:      false,   
            };
        case constants.ERROR_FETCHING_ALL_REFORM_REQUEST:
            return { 
                ...state,
                isFetchingAllReformRequest: false,
                errorAllReformRequest:      true,
                };

        case constants.START_ADD_REFORM_REQUEST:
            return { 
                ...state,
                ...initialValueflags,
                isAddingReformRequest:      true,
                
             }
        case constants.ADD_REFORM_REQUEST:
            return { 
                ...state,
                isAddingReformRequest:      false,
            }
            
            
        case constants.ERROR_ADD_REFORM_REQUEST:
            return { 
                ...state,
                isAddingReformRequest:      false,
                errorAddReformRequest:      true,

            }

        case constants.START_REMOVE_REFORM_REQUEST:
            return { 
                ...state,
                ...initialValueflags,
                isDeletingReformRequest:    true,

            };
        case constants.REMOVE_REFORM_REQUEST:
            // return { ...state, reforms_requests: state.reforms_requests.filter((item, index) => item.id !== action.payload._payload.id),isDeletingReformRequest: false, errorDeletingReformRequest: false  };
            return { ...state,
                isDeletingReformRequest:    false,
                errorDeletingReformRequest: false
            
            };
        case constants.ERROR_REMOVE_REFORM_REQUEST:
            return { 
                ...state,
                isDeletingReformRequest:    false,
                errorDeletingReformRequest: true
            };

        case constants.START_UPDATE_REFORM_REQUEST:
            return { 
                ...state,
                ...initialValueflags,
                isUpdatingReformRequest:    true,

            }
        case constants.UPDATE_REFORM_REQUEST:
            return { 
                ...state,
                isUpdatingReformRequest:    false,

            }
        case constants.ERROR_UPDATE_REFORM_REQUEST:
            return { ...state,
                isUpdatingReformRequest:    false,
                errorUpdatingReformRequest: true,

            }
        case constants.CLEAR_STATE_REFORM_REQUEST:
            return _.cloneDeep(initialState)
        default:
            return state;
    }
}