import React, { useState } from "react";
import { Button, Modal, Tooltip } from "antd";
import { DollarCircleOutlined } from "@ant-design/icons";
import TableManageExchanges from "./TableManageExchanges";
import { useDispatch } from "react-redux";
import {
	errorGlobal,
	success,
	warning,
} from "../../../../../handle/Notification/Notification";
import { applyExchangeAction } from "../../../../../../redux/actions/Price/SaleMade/SalesMadeAction";

const ManageExchanges = ({ data }) => {
	const dispatch = useDispatch();
	const [isModalVisible, setIsModalVisible] = useState(false);
	const [selectedExchanges, setSelectedExchanges] = useState([]);
	const [requestExchanges, setRequestExchanges] = useState([]);

	const showModal = () => {
		setIsModalVisible(true);
	};

	const showModalClose = () => {
		setIsModalVisible(false);
	};

	const addExchangeToApply = (exchange) => {
		let exchangeExist = selectedExchanges.find(
			(element) => element.id === exchange.id
		);
		if (!exchangeExist) {
			setSelectedExchanges([...selectedExchanges, exchange]);
		} else {
			setSelectedExchanges(
				selectedExchanges.filter((item) => item.id !== exchange.id)
			);
		}
	};

	const textManageExchanges = (
		<span style={{ color: "#6A6963" }}>Administrar canjes</span>
	);

	const buildDto = () => {
		let dto = [];

		selectedExchanges.forEach((item) => {
			let dtoExchange = {
				coupon_id: item.id,
			};
			dto.push(dtoExchange);
		});

		return dto;
	};

	const handleSubmit = () => {
		let exchanges = buildDto();
		if (exchanges.length === 0)
			return warning("No ha sido selecionado ningun canje.");
		let dto = {
			customer_id: data.customer.id,
			sale_id: data.id,
			coupons: exchanges,
		};

		dispatch(
			applyExchangeAction(dto, async (response) => {
				let res = await response;
				if (res.error) {
					errorGlobal("¡UPS, Ha ocurrido un problema.");
				} else {
					showModalClose()
					success("Canjes aplicados exitosamente.");
					setRequestExchanges(res);
				}
			})
		);
	};

	return (
		<>
			<Tooltip placement="left" color="#ffffff" title={textManageExchanges}>
				<Button
					style={{ background: "#ffc53d", color: "#fff" }}
					size={"small"}
					onClick={showModal}
					icon={<DollarCircleOutlined />}
					disabled={data.is_desisted}
				/>
			</Tooltip>

			<Modal
				title={"Administrar Canjes"}
				centered
				visible={isModalVisible}
				footer={
					<Tooltip>
						<Button
							type="primary"
							style={{ background: "rgb(56, 158, 13)", color: "#fff" }}
							onClick={handleSubmit}
						>
							Aplicar Canje
						</Button>
						<Button type="primary" onClick={showModalClose}>
							Finalizar
						</Button>
					</Tooltip>
				}
				onCancel={() => setIsModalVisible(false)}
				width={900}
			>
				{isModalVisible && (
					<TableManageExchanges
						isModalVisible={isModalVisible}
						data={data}
						addExchangeToApply={addExchangeToApply}
						setRequestExchanges={setRequestExchanges}
						requestExchanges={requestExchanges}
					/>
				)}
			</Modal>
		</>
	);
};

export default ManageExchanges;
