import * as constants from "../../../constants";

export const setAllAssignResponsibleAction = (id, onSuccess) => ({
	type: constants.API,
	payload: {
		method: "GET",
		headers: constants.TOKEN,
		url: `${constants.RUTA}/sales/get-responsible/${id}`,
		success: (response) => setAllAssignResponsible(response),
		postProcessSuccess: onSuccess,
	},
});

export const updateOrCreateAssignResponsibleAction = (id, data , onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: "POST",
		headers: constants.TOKEN,
		url: `${constants.RUTA}/sales/assign-responsible/${id}`,
		data,
		success: (response) => updateOrCreateAssignResponsible(response),
        postProcessSuccess: onSuccess,
		postProcessError: onError
	},
});

export const resetAssignResponsibleAction = () => ({
    type: constants.API,
    payload: {
		method: null,
		success: () => (resetAssignResponsible())
	}
})

const  resetAssignResponsible = () => ({
	type: constants.RESET_ASSIGN_RESPONSIBLE,
	payload: []
});

const updateOrCreateAssignResponsible = (data) => ({
	type: constants.UPDATE_OR_CREATE_ASSIGN_RESPOSIBLE,
	payload: data
});

const setAllAssignResponsible = (data) => ({
	type: constants.SET_ALL_ASSIGN_RESPONSIBLE,
	payload: data
});
