import { PlusOutlined } from '@ant-design/icons';
import { UploadOutlined } from "@ant-design/icons";
import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { filterUnits } from "../../../../../redux/actions/Price/Sales/SalesAction";
import { fetchAllProjects } from "../../../../../redux/actions/Project/ProjectsAction";
import { errorGlobal, success, warning } from "../../../../handle/Notification/Notification";
import { Form, Input, Button, Row, Col, DatePicker, Modal, Select, Upload, Divider, InputNumber } from 'antd';
import { getAllDirectCollections, createDirectCollection } from '../../../../../redux/actions/Collection/DirectCollectionAction';

const FormDirectCollection = () => {
    const [visible, setVisible] = useState(false);
    const [selectCustomer, setSelectCustomer] = useState(0);
    const [selectProject, setSelectProject] = useState(0);
    const [visibleSelect, setVisibleSelect] = useState(false);
    const [dataUnits, setDataUnits] = useState([])
    const [form] = Form.useForm();
    const [fileUpload, setFileUpload] = useState(null);
    const dispatch = useDispatch();
    const dateFormat = "YYYY/MM/DD";

    const setStateData = (value) => {
        setVisibleSelect(value);
    }

    const showModal = () => {
        setVisible(true);
    }
    const onClose = () => {
        setVisible(false);
        onReset();
    }

    useEffect(() => {
        if (visible) {
            dispatch(fetchAllProjects());
        }
    }, [visible]);


    let userStorage = JSON.parse(localStorage.getItem('USER_INFO'));

    useEffect(() => {
        setStateData(false);
        form.setFieldsValue({
            sale_id: ""
        })
        if (selectProject !== 0 && selectCustomer !== 0) {
            const dto = {
                customer_id: selectCustomer,
                project_id: selectProject
            }
            dispatch(filterUnits(dto, (response) => {
                if (response._payload.length === 0) {
                    warning("No hay unidades disponibles");
                    setStateData(false);
                } else {
                    setDataUnits(response._payload);
                    setStateData(true);
                }
            }));
        }
    }, [selectProject, selectCustomer]);

    const handleSubmit = async () => {
        try {
            const values = await form.validateFields();
            const dto = {
                ...values,
                user_id: userStorage.id,
                consignment_date: values["consignmentDate"].format("YYYY-MM-DD"),
                consignment_amount: parseFloat(values["consignment_amount"])
            }
            let formData = new FormData();
            formData.append('consignment_support', fileUpload, fileUpload.name);
            formData.append('consignment_date', dto.consignment_date);
            formData.append('consignment_concept', dto.consignment_concept);
            formData.append('consignment_amount', dto.consignment_amount);
            formData.append('user_id', dto.user_id);
            formData.append('sale_id', dto.sale_id);

            delete dto.voucher_Support;
            delete dto.consignmentDate;
            delete dto.customer_id;
            delete dto.project_id;


            dispatch(createDirectCollection(formData, (response) => {
                if (!response.error) {
                    success("Recaudo directo creado");
                    dispatch(getAllDirectCollections());
                    closeAndResetForm();
                } else {
                    errorGlobal("¡UPS! Ocurrió un error mientras se registraba el recaudo");
                }
            }));


        } catch (errorInfo) {
            console.error(errorInfo);
        }
    }

    const uploadProps = {
        name: 'file',
        beforeUpload: () => {
            return false;
        },
        onChange: info => {
            form.setFieldsValue({
                voucher_Support: info.file !== undefined ? info.file.name : ' '
            });
            setFileUpload(info.file);
        }, showUploadList: false,

    };

    let selectCustomerElement = useSelector(state => state?.customer);
    let elementProject = useSelector(state => state?.project);

    const onReset = () => {
        setStateData(false);
        form.resetFields();

    }

    const closeAndResetForm = () => {
        onClose();
        onReset();
    }


    return (
        <>
            <Button
                type="primary" onClick={showModal} icon={<PlusOutlined />}
            >
                Nuevo
            </Button>
            {visible === true ? (<Modal
                visible={visible}
                footer={false}
                onCancel={onClose}
                width={900}
            >
                <Form
                    layout="vertical"
                    size='middle'
                    form={form}
                    onFinish={handleSubmit}
                >
                    <Row gutter={16}>
                        <Col xs={24} sm={24} md={8} lg={8} xl={8}>
                            <Form.Item
                                label="Cliente"
                                name="customer_id"
                                labelCol={{ span: 24 }}
                                wrapperCol={{ span: 24 }}
                                rules={[
                                    {
                                        required: true,
                                        message: 'Campo requerido',
                                    },
                                ]}
                            >
                                <Select
                                    showSearch
                                    style={{ width: '100%' }}
                                    optionFilterProp="children"
                                    onChange={setSelectCustomer}
                                    filterOption={(input, option) =>
                                        option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                    }
                                    rules={[{ required: true }]}
                                    defaultValue={""}
                                >
                                    <Select.Option key={"0"} value="" >Seleccione...</Select.Option>
                                    {
                                        selectCustomerElement.hasOwnProperty('_payload') && selectCustomerElement._payload?.map(item => (
                                            <Select.Option key={item.id} value={item.id}>{item.name}</Select.Option>

                                        ))
                                    }
                                </Select>
                            </Form.Item>
                        </Col>
                        <Col xs={24} sm={24} md={8} lg={8} xl={8}>
                            <Form.Item
                                label="Proyecto"
                                name="project_id"
                                labelCol={{ span: 24 }}
                                wrapperCol={{ span: 24 }}
                                rules={[
                                    {
                                        required: true,
                                        message: 'Campo requerido',
                                    },
                                ]}
                            >
                                <Select
                                    showSearch
                                    style={{ width: '100%' }}
                                    optionFilterProp="children"
                                    onChange={setSelectProject}
                                    filterOption={(input, option) =>
                                        option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                    }
                                    rules={[{ required: true }]}
                                    defaultValue={""}
                                >
                                    <Select.Option value="">Seleccionar...</Select.Option>
                                    {
                                        elementProject.hasOwnProperty('_payload') && elementProject._payload.map(item => (
                                            <Select.Option key={item.id} value={item.id}>{item.name}</Select.Option>
                                        ))
                                    }
                                </Select>
                            </Form.Item>
                        </Col>
                        <Col xs={24} sm={24} md={8} lg={8} xl={8}>
                            <Form.Item
                                label="Unidad"
                                name="sale_id"
                                labelCol={{ span: 24 }}
                                wrapperCol={{ span: 24 }}
                                rules={[
                                    {
                                        required: true,
                                        message: 'Campo requerido',
                                    },
                                ]}
                            >
                                <Select
                                    showSearch
                                    style={{ width: '100%' }}
                                    optionFilterProp="children"
                                    disabled={!visibleSelect}
                                    filterOption={(input, option) =>
                                        option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                    }
                                    rules={[{ required: true }]}
                                    defaultValue={""}
                                    value={form.getFieldValue("unit_id")}
                                >
                                    <Select.Option value="">Seleccionar...</Select.Option>
                                    {
                                        dataUnits.map(item => (
                                            <Select.Option key={item.id} value={item.sale_id}>{item.unit_label}</Select.Option>
                                        ))
                                    }
                                </Select>
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={16}>
                        <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                            <Form.Item
                                name='consignmentDate'
                                label="Fecha de consignación"
                                rules={[{ required: true, message: 'Campo requerido' }]}
                            >
                                <DatePicker
                                    style={{ width: '100%' }}
                                    format={dateFormat}
                                    placeholder="Ingrese una fecha"
                                />
                            </Form.Item>
                        </Col>
                        <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                            <Form.Item
                                name="consignment_amount"
                                label="Valor de consignación"
                                rules={[{ type: "number", required: true, message: 'Campo requerido', }]}
                            >
                                <InputNumber
	formatter={value => `$ ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
	parser={value => value.replace(/\$\s?|(,*)/g, '')}
	style={{width: '100%'}} onChange={form.getFieldValue("consignment_Value")}/>
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={16}>
                        <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                            <Form.Item
                                name="consignment_concept"
                                label="Concepto de consignación"
                                rules={[{ required: true, message: 'Campo requerido' }]}
                            >
                                <Select
                                    placeholder="Escoja el concepto"
                                    onChange={form.getFieldValue("consignment_Concept")}
                                >
                                    <Select.Option value={""}>Seleccione...</Select.Option>
                                    <Select.Option value={1}>Abono</Select.Option>
                                    <Select.Option value={2}>Reformas</Select.Option>
                                    <Select.Option value={3}>Otros</Select.Option>
                                </Select>
                            </Form.Item>
                        </Col>
                        <Col xs={24} sm={24} md={5} lg={5} xl={6}>
                            <Form.Item name="voucher_Support" label="Soporte del comprobante" rules={[{ required: true, message: 'Campo requerido' }]} >
                                <Input style={{width: '100%'}} disabled={true}/>
                            </Form.Item>
                        </Col>
                        <Col xs={24} sm={24} md={5} lg={5} xl={6}>
                            <Form.Item name='SelectFile' label=' '>
                                <Upload {...uploadProps}>
                                    <Button type='primary' icon={<UploadOutlined />}>Elegir archivo</Button>
                                </Upload>
                            </Form.Item>
                        </Col>
                    </Row>
                    <Divider/>
                    <Row>
                        <Col offset={18} xs={24} sm={24} md={4} lg={4} xl={4}>
                            <Form.Item style={{ margin: '0' }}>
                                <Button type="primary" htmlType="submit">
                                    Guardar
                                </Button>
                            </Form.Item>
                        </Col>
                    </Row>
                </Form>
            </Modal>
            ) : (<Modal
	visible={false}
	footer={false}
	width={900}
	/>)}
        </>
    )
}

export default FormDirectCollection