import { Modal, Table, Button, } from "antd";
import React, { useState } from "react";
import { getFormatDate } from "../../../../handle/HandleDatePicker/HandleDate";


const ModalListPlanSales = ({planSalesList}) => {
    const [isModalVisible, setIsModalVisible] = useState(false);
  
    const showModal = () => {
        setIsModalVisible(true);
    };
    const handleCancel = () => {
        setIsModalVisible(false);
    };
    const downloadDocument=(path)=>{
        const tagA = document.createElement("a");
        tagA.href = path;
        tagA.target = "_blank";
        tagA.click();
    }
    const colums =  [
        {
          title: "Descripción del Documento",
          width: 30,
          align: "center",
          dataIndex: "description",
          key: "description",
        },
        {
          title: "Fecha de Creación",
          width: 10,
          align: "center",
          key:"created_at",
          render: (_, record) => {
            return `${getFormatDate(record.created_at)}`;
          }
        },
        {
          title: "Acción",
          width: 10,
          align: "center",
          key:"path",
          render: (_, record) => {
            return <Button onClick={()=>{downloadDocument(record.path)}} type="primary" >Descargar Plan de Venta</Button>;
          }
        },
	];
	
    return (
        <>
            <Button onClick={showModal} style={{ backgroundColor: "#64CDA5", borderColor: "#64CDA5" }} type="primary">Lista de Planes</Button>
            <Modal
                title="Lista Planes de Ventas"
                visible={isModalVisible}
                onCancel={handleCancel}
                width={900}
                footer={false}
            >
                <Table
                    columns={colums}
                    dataSource={planSalesList}
                    size="small"
                    bordered={true}
                />
            </Modal>
        </>
    );
};

export default ModalListPlanSales;
