import * as constants from "../../../constants";

export const fetchExcelPortfolioMonitoringReport= (data, postProcessSuccess, onError ) => ({
    type: constants.API,
	payload: {
		method: 'POST',
		data: data,
		url: `${constants.RUTA}/report2/get_path_excel_portfolio_monitoring_report`,
		postProcessSuccess: postProcessSuccess,
		postProcessError: onError
	}
})





