import React, {useContext} from "react";
import {Col, Form, Row} from "antd";
import DescriptionProject from "../../../../Quotes/Components/editQuote/DescriptionProject";
import DescriptionIncluded from "../../../../Quotes/Components/editQuote/DescriptionIncluded";
import PropertyValue from "../../../../Quotes/Components/editQuote/PropertyValue";
import {SalesMadeContext} from "../context/SalesMadeContext";

export const GeneralInformation = () =>{
	
	const { project,included,propertyData,changeStateTableIncluded,changeStateTableCoupon,onChangeKit,recalculatePaymentPlan } = useContext(SalesMadeContext);
	
	return (
		<Form.Item>
			<Row>
				<Col xs={24} sm={24} md={8} lg={8} xl={8}>
					<DescriptionProject project={project} />
				</Col>
				<Col xs={24} sm={24} md={8} lg={8} xl={8}>
					<DescriptionIncluded included={included} />
				</Col>
				<Col xs={24} sm={24} md={8} lg={8} xl={8}>
					<PropertyValue
						propertyData={propertyData}
						changeStateTableIncluded={changeStateTableIncluded}
						changeStateTableCoupon={changeStateTableCoupon}
						onChangeKit={onChangeKit}
						recalculatePaymentPlan={recalculatePaymentPlan}
					/>
				</Col>
			</Row>
		</Form.Item>
	)
}