import _ from "lodash";
import moment from 'moment';
import {useDispatch} from 'react-redux';
import Text from 'antd/lib/typography/Text';
import NewReformRequest from './NewReformRequest';
import React, {useEffect, useRef, useState} from 'react';
import * as constants from "../../../../../redux/constants";
import {success} from '../../../../handle/Notification/Notification';
import {dateFormat} from '../../../../handle/HandleDatePicker/HandleDate';
import {validatePermission} from "../../../../handle/PermissionMethods/PermissionMethods";
import {convertAllMapDataState} from "../../../../handle/HandleFilterTable/handleFilterTable";
import {DeleteFilled, EyeOutlined, QuestionCircleOutlined, SyncOutlined, UploadOutlined} from '@ant-design/icons';
import {Button,Checkbox,Col,DatePicker,Input,InputNumber,Popconfirm,Row,Select,Space,Table,Tooltip,Upload} from 'antd';
import {
	createReformRequest,
	deleteReformRequestrById,
	startAddReformRequest,
	startDeleteReformRequest,
	startUpdateReformRequest,
	updateReformRequestById
} from '../../../../../redux/actions/Price/ReformRequest/ReformRequestBySaleAction';

const {TextArea} = Input;

function usePrevious(value) {
	const ref = useRef();
	useEffect(() => {
		ref.current = value;
	});
	return ref.current;
}


const ListOfRequest = ({maximum_reforms_date, reforms_requests, idSale, updateListReformRequest}) => {

	let newReformStructure = {
		"isNew": true,
		"body_description": null,
		"request_document": null,
		"technical_document": null,
		"customer_document": null,
		"customer_management": {
			"approved": null,
			"is_paid": null
		},
		"technical_management": {
			"approved": null,
			"buget": null,
			"date_pay": null
		}
	};

	const [dataSourceNewRequest, setDataSourceNewRequest] = useState([_.cloneDeep(newReformStructure)]);
	const [tableVisible, setTableVisible] = useState(false);
	const [arrayRequest, setArrayRequest] = useState([]);
	const [arrayRequestLocal, setArrayRequestLocal] = useState([]);

	const [, refreshPage] = useState({});

	const dispatch = useDispatch();
	const prevArrayRequest = usePrevious(arrayRequest);
	const prevArrayRequestLocal = usePrevious(arrayRequestLocal);
	const prevData = usePrevious(reforms_requests);


	useEffect(() => {}, []);

	useEffect(() => {
		setArrayRequest(_.cloneDeep(reforms_requests));
		let loadCondition = ((prevArrayRequest?.length === 0 && arrayRequest?.length === 0) && _.isEqual(prevArrayRequest, arrayRequest));
		let createCondition = ((prevData?.length === reforms_requests.length - 1) && _.isEqual(prevArrayRequest, arrayRequest));
		let deleteCondition = ((prevData?.length === reforms_requests.length + 1) && _.isEqual(prevArrayRequest, arrayRequest));
		let updateCondition = ((prevData?.length === reforms_requests.length) && !(_.isEqual(prevData, reforms_requests)));
		let sameStateCondition = (_.isEqual(prevData, reforms_requests));


		if (loadCondition || sameStateCondition) {
			setArrayRequestLocal(_.cloneDeep(reforms_requests))
		}

		if (createCondition) {
			let itemCreated = _.differenceWith([..._.cloneDeep(reforms_requests)], [..._.cloneDeep(arrayRequestLocal)], _.isEqual)
			setArrayRequestLocal([...arrayRequestLocal, ...itemCreated])
		}

		if (updateCondition) {
			let copyData = _.cloneDeep(reforms_requests)
			let itemUpdated = _.differenceWith(copyData, _.cloneDeep(arrayRequest), _.isEqual)[0];
			let index = copyData.indexOf(itemUpdated)
			let arrayToWork = arrayRequestLocal.map((item) => {
				if (item.id === itemUpdated.id) {
					return reforms_requests[index]
				}
				return item
			});
			setArrayRequestLocal(_.cloneDeep(arrayToWork))
		}

		if (deleteCondition) {
			let itemDeleted = _.difference(_.cloneDeep(arrayRequestLocal), _.cloneDeep(reforms_requests));
			setArrayRequestLocal([...arrayRequestLocal.filter((item) => item.id !== itemDeleted.id)])
		}
	}, [reforms_requests]);


	//metodos asociados a los campos
	// -- columna solicitud del cliente

	const bodyDescriptionHandler = (e, record, index) => {

		record?.isNew ? dataSourceNewRequest[0]['body_description'] = e.target.value : arrayRequestLocal[index]['body_description'] = e.target.value
		refreshPage({})

	}

	const requestDocumentHandler = (file, record, index) => {

		record?.isNew ? dataSourceNewRequest[0]['request_document'] = file : arrayRequestLocal[index]['request_document'] = file
		refreshPage({})

	}
	// --- culumna aprobacion tecnica
	const technicalApprovalHandler = (value, record, index) => {
		record?.isNew ? (dataSourceNewRequest[0]['technical_management'] ? dataSourceNewRequest[0]['technical_management']['approved'] = value : dataSourceNewRequest[0]['technical_management'] = {approved: value}) : (arrayRequestLocal[index]['technical_management'] ? arrayRequestLocal[index]['technical_management']['approved'] = value : arrayRequestLocal[index]['technical_management'] = {approved: value})
		refreshPage({})

	}

	const budgetHandler = (value, record, index) => {
		let item;
		record?.isNew ? (item = dataSourceNewRequest[0]['technical_management']) : (item = arrayRequestLocal[index]['technical_management']);
		item ? item['buget'] = value : item = {buget: value}
		refreshPage({})
	}
	const payDayHandler = (date, dateString, record, index) => {


		if (record?.isNew) {

			let item = dataSourceNewRequest[0]['technical_management'];
			item ? item['date_pay'] = moment(date).format(dateFormat) : item = {date_pay: moment(date).format(dateFormat)}

		} else {

			arrayRequestLocal[index]['technical_management'] ? arrayRequestLocal[index]['technical_management']['date_pay'] = moment(date).format(dateFormat) : arrayRequestLocal[index]['technical_management'] = {date_pay: moment(date).format(dateFormat)}

		}

		refreshPage({})

	}
	const technicalDocumentHandler = (file, record, index) => {
		record?.isNew ? dataSourceNewRequest[0]['technical_document'] = file : arrayRequestLocal[index]['technical_document'] = file
		refreshPage({})
	}

	// --- culumna aprobacion cliente
	const customerApprovalHandler = (value, record, index) => {
	
		record?.isNew ? (dataSourceNewRequest[0]['customer_management'] ? dataSourceNewRequest[0]['customer_management']['approved'] = value : dataSourceNewRequest[0]['customer_management'] = {approved: value}) : (arrayRequestLocal[index]['customer_management'] ? arrayRequestLocal[index]['customer_management']['approved'] = value : arrayRequestLocal[index]['customer_management'] = {approved: value})
		refreshPage({})
	}

	const customerDocumentHandler = (file, record, index) => {
		record?.isNew ? dataSourceNewRequest[0]['customer_document'] = file : arrayRequestLocal[index]['customer_document'] = file;
		refreshPage({})
	}

	const isPaidHandler = (value, record, index) => {

		if (record?.isNew) {

			let item = dataSourceNewRequest[0]['customer_management'];
			item ? item['is_paid'] = changeBooleanToNumber(value.target.checked) : item = {is_paid: changeBooleanToNumber(value.target.checked)}

		} else {

			if (arrayRequestLocal[index]['customer_management']) {

				arrayRequestLocal[index]['customer_management']['is_paid'] = changeBooleanToNumber(value.target.checked)
			} else {
				arrayRequestLocal[index]['customer_management'] = {is_paid: changeBooleanToNumber(value.target.checked)}

			}
		}
		refreshPage({})

	}
	// ---------------------------- fin metodos asociados a campos

	const buildFormData = (record) => {

		let formData = new FormData();
		let recordCopy = _.cloneDeep(record);

		let copyStructureNewReform = _.cloneDeep(newReformStructure)
		delete copyStructureNewReform.isNew

		if (record.isNew) {
			delete recordCopy.isNew
		}

		let keysAll = Object.keys(copyStructureNewReform);
		let preBuildFormData = {};

		const buildSecondaryData = (nameItem, recordCopy) => {
			
			let keys = Object.keys(newReformStructure[nameItem]);
			let ob = {};
			if (
				recordCopy[nameItem]?.approved !== null &&
				recordCopy[nameItem]?.approved === 1
			) {

				for (let key of keys) {
					if (key === "is_paid") {
						ob[key] = _.cloneDeep(record[nameItem][key] || 0)
					} else {
						ob[key] = _.cloneDeep(record[nameItem][key] || '')

					}
				}
				preBuildFormData[nameItem] = JSON.stringify(ob);
			} else {

				if(recordCopy[nameItem]?.approved == 1){
					for (let key of keys) {
						if (key === "is_paid") {
							ob[key] = _.cloneDeep(record[nameItem][key] || 0)
						} else {
							ob[key] = _.cloneDeep(record[nameItem][key] || '')
	
						}
					}
					preBuildFormData[nameItem] = JSON.stringify(ob);	
				}else{
					for (let key of keys) {
						ob[key] = '';
						if (key === "is_paid") {
							ob[key] = 0;
	
						}
					}
					ob['approved'] = 0;
					preBuildFormData[nameItem] = JSON.stringify(ob);
				}
				

			}
		};


		const removeItemToPrebuild = (
			recordCopy,
			preBuildFormData,
			item,
			nameApproval
		) => {
			if (
				(recordCopy[nameApproval]?.approved !== undefined) &&
				(recordCopy[nameApproval]?.approved !== null)
			) {

				if (recordCopy[nameApproval]?.approved === 1) {

					preBuildFormData[item] = recordCopy[item];
				} else if (!recordCopy[nameApproval]?.approved === 0) {

					preBuildFormData[item] = '';
				}
			} else {

				delete preBuildFormData[item];
				delete preBuildFormData[nameApproval];
			}

		};

		for (let item of keysAll) {
			switch (item) {
				case 'technical_management':
					buildSecondaryData(item, recordCopy);
					break;
				case 'customer_management':
					buildSecondaryData(item, recordCopy);
					break;
				default:
					preBuildFormData[item] = recordCopy[item];
			}
		}

		if (
			recordCopy['technical_management']?.approved !== undefined ||
			recordCopy['technical_management']?.approved !== null
		) {
			removeItemToPrebuild(
				recordCopy,
				preBuildFormData,
				'technical_document',
				'technical_management'
			);
		}

		if (
			recordCopy['customer_management']?.approved !== undefined ||
			recordCopy['customer_management']?.approved !== null
		) {
			removeItemToPrebuild(
				recordCopy,
				preBuildFormData,
				'customer_document',
				'customer_management'
			);
		}

		if (
			recordCopy['request_document'] === undefined ||
			recordCopy['request_document'] === null
		) {
			preBuildFormData['request_document'] = '';
		}

		for (let item in preBuildFormData) {
			formData.append(item, preBuildFormData[item])
		}

		if (record.isNew) {


			sendDataToBackCreate(formData, record)

		} else {
			/// editar


			sendDataToBackUpdate(formData, record)

		}
	}

	const sendDataToBackCreate = (formData) => {
		formData.append('creator_user_id', (JSON.parse(localStorage.getItem('USER_INFO'))).id)

		dispatch(startAddReformRequest())
		dispatch(createReformRequest(idSale, formData, (response) => {
			if (!response.error) {
				success('Solicitud creada')
			}
			tableVisibleHandler()

			updateListReformRequest()
		}))

	}

	const sendDataToBackUpdate = (formData, record) => {

		dispatch(startUpdateReformRequest())
		dispatch(updateReformRequestById(record.id, formData, (response) => {
			if (!response.error) {
				success('Solicitud modificada')
			}


			updateListReformRequest()
		}))

	}
	const sendDataToBackRemove = (record) => {

		dispatch(startDeleteReformRequest())
		dispatch(deleteReformRequestrById(record.id, (response) => {
			// success('Solicitud eliminada')
			// 
			if (!response.error) {
				success('Solicitud eliminada')
			}
			updateListReformRequest()
		}))

	}


	const tableVisibleHandler = () => {
		setDataSourceNewRequest([{isNew: true}]);
		setTableVisible(!tableVisible)
	}

	const changeNumberToBoolean = (number) => {
		if (number === undefined || number === null) {
			return null
		} else if (number === 1) {
			return true
		} else {
			return false
		}
	}

	const changeBooleanToNumber = (bool) => {
		if (bool === undefined || bool === null || (!bool)) {
			return 0
		} else {
			return 1
		}
	}

	const enableColumsFromState = (nameColum, record, index, isSelect = false) => {
		let disable = false;
		let technical_approvalOrigin = arrayRequest[index]?.technical_management?.approved
		let technical_approvalLocal = arrayRequestLocal[index]?.technical_management?.approved
		let customer_approvalOrigin = arrayRequest[index]?.customer_management?.approved
		let customer_approvalLocal = arrayRequestLocal[index]?.customer_management?.approved

		let technical_approvalNewRequest = dataSourceNewRequest[0]?.technical_management?.approved


		if (record?.isNew) {

			disable = false;
			switch (nameColum) {
				case "customer_request":

					if ((dataSourceNewRequest[index]?.technical_management?.approved === undefined ? null : dataSourceNewRequest[index]?.technical_management?.approved) === null) {
						disable = false;
					}
					if (technical_approvalNewRequest === 0 || technical_approvalNewRequest === 1) {
						disable = true;
					}


					break;
				case "technical_management":

					disable = true;
					// if ((dataSourceNewRequest[index]?.customer_management?.approved === undefined ? null : dataSourceNewRequest[index]?.customer_management?.approved) === null) { disable = false; }
					// if ((customer_approvalNewRequest === undefined ? null : customer_approvalNewRequest) == 0 || customer_approvalNewRequest == 1) { disable = true; }
					// if (technical_approvalNewRequest == 0 && !isSelect) { disable = true; }

					break;
				case "customer_management":

					// if ((dataSourceNewRequest[index]?.technical_management?.approved === undefined ? null : dataSourceNewRequest[index]?.technical_management?.approved) === null) { disable = false; }
					// if (technical_approvalNewRequest == 0) { disable = true; }
					// if (technical_approvalNewRequest == 1) { disable = false; }
					// if (customer_approvalNewRequest == 0 && !isSelect) { disable = true; }
					disable = true;

					break;

				default:
			}
		} else {
			disable = false;

			switch (nameColum) {
				case "customer_request":

					if (arrayRequest[index]?.state?.id >= 2) {
						disable = true;
					}
					if ((technical_approvalOrigin === undefined ? null : technical_approvalOrigin) === null) {
						disable = false;
					}
					if ((technical_approvalOrigin === 1) || (technical_approvalOrigin === 0)) {
						disable = true;
					}
					if ((arrayRequestLocal[index]?.technical_management?.approved === undefined ? null : arrayRequestLocal[index]?.technical_management?.approved) === null) {
						disable = false;
					}
					if (technical_approvalLocal === 0 || technical_approvalLocal === 1) {
						disable = true;
					}

					if (arrayRequest[index]?.state?.id >= 3) {
						disable = true;
					}
					if (!validatePermission(constants.CUSTOMER_REQUEST_REFORM_REQUEST_PERMISSION)) {
						disable = true;
					}
					
					break;
				case "technical_management":

					if (arrayRequest[index]?.state?.id >= 3) {
						disable = true;
					}
					if ((customer_approvalOrigin === undefined ? null : customer_approvalOrigin) === null) {
						disable = false;
					}
					if ((customer_approvalOrigin === 1) || (customer_approvalOrigin === 0)) {
						disable = true;
					}
					if ((arrayRequestLocal[index]?.customer_management?.approved === undefined ? null : arrayRequestLocal[index]?.customer_management?.approved) === null) {
						disable = false;
					}
					if (customer_approvalLocal === 0 || customer_approvalLocal === 1) {
						disable = true;
					}
					if (technical_approvalOrigin === 0 && !isSelect) {
						disable = true;
					}
					if (technical_approvalLocal === 0 && !isSelect) {
						disable = true;
					}

					if (arrayRequest[index]?.state?.id >= 4) {
						disable = true;
					}
					if (!validatePermission(constants.TECHNICAL_MANAGEMENT_REFORM_REQUEST_PERMISSION)) {
						disable = true;
					}

					break;
				case "customer_management":
					// if (arrayRequest[index]?.state?.id >= 4) {disable = true;}
					if ((technical_approvalOrigin === undefined ? null : technical_approvalOrigin) === null) {
						disable = false;
					}
					if ((technical_approvalOrigin === 0)) {
						disable = true;
					}
					if ((technical_approvalOrigin === 1)) {
						disable = false;
					}
					if ((arrayRequestLocal[index]?.technical_management?.approved === undefined ? null : arrayRequestLocal[index]?.technical_management?.approved) === null) {
						disable = false;
					}
					if (technical_approvalLocal === 0) {
						disable = true;
					}
					if (technical_approvalLocal === 1) {
						disable = false;
					}
					if (customer_approvalOrigin === 0 && !isSelect) {
						disable = true;
					}
					if (customer_approvalLocal === 0 && !isSelect) {
						disable = true;
					}
					if (arrayRequest[index]?.state?.id >= 4) {
						disable = true;
					}
					//dataSourceNewRequest[0]['technical_management']['approved']
					if(record.technical_management?.approved==1 || record.state_id==2){
						disable = false;
					}else{
						disable = true;
					}
					
					if(record.state.name_state=="TRAMITADA" ||record.state.name_state=="DECLINADA" ){
						disable = true;
					}
					if (!validatePermission(constants.CUSTOMER_MANAGEMENT_REFORM_REQUEST_PERMISSION)) {
						disable = true;
					}

					break;

				default:
			}
		}


		return disable
	}


	const styleList = () => {

		return {
			backgroundBody: {
				background: '#d9edf7'
			},
			back_danger: {
				background: '#d9534f'
			},
			back_white: {
				background: '#d9534f'
			},
			color_white: {
				color: '#ffffff'
			},
			colors: {
				white: '#ffffff',
				success: '#5cb85c'
			}
		}
	};

	const renderNode = ({props, children = (<></>)}) => {

		props = props || {style: styleList().backgroundBody}

		return {
			props: props,
			children: children
		}
	}

	const columns = [
		{
			title: '#',
			dataIndex: 'index',
			key: 'index',
			render: (text, record) => {
				return renderNode({children: (<label>{1 + (arrayRequest.indexOf(record))}</label>)})
			}
		},
		{
			title: 'Fecha',
			dataIndex: 'date',
			key: 'date',
			render: (text, record) => {

				return renderNode({children: (<Text>{moment(record?.created_at).format(dateFormat)} </Text>)})
			}
		},
		{
			title: 'Solicitudes del cliente',
			dataIndex: 'customerRequests',
			key: 'customerRequests',
			render: (text, record) => {

				let index = arrayRequest.indexOf(record);
				const propsUpload = {
					onChange(info) {
						if (info.fileList.length > 0) {
							info.fileList[0].name = "Archivo cargado"
						}

						if (info.file.status === 'removed') {
							requestDocumentHandler(null, record, index);
						}

					},

					beforeUpload: file => {
						requestDocumentHandler(file, record, index)

						return false;
					},
					maxCount: 1,

				}

				
				const showFileLink = (<a href={record?.multimedia?.path} target="_blank"><Button type="primary"
																								 icon={<EyeOutlined/>}/></a>)
				return renderNode({
					children: (
						<>
							<Row justify="center" align="top">
								<Space direction="vertical" size="middle">
									<Col span={24}>
										<Row justify="start" align="top">
											<label>Descripción</label>
										</Row>
										{record?.isNew ? <TextArea onBlur={(e) => {
												bodyDescriptionHandler(e, record, index)
											}} disabled={enableColumsFromState('customer_request', record, 0)} rows={4}/> :
											<TextArea defaultValue={record?.body_description}
													  disabled={enableColumsFromState('customer_request', record, index)}
													  onBlur={(e) => {
														  bodyDescriptionHandler(e, record, index)
													  }} rows={4}/>}

									</Col>
									<Col span={24}>

										<Row justify="start" align="top">
											<label>Documento solicitud</label>
										</Row>
										{record?.multimedia && showFileLink}
										{record?.isNew ? <Upload {...propsUpload}>
											<Button disabled={enableColumsFromState('customer_request', record, 0)}
													icon={<UploadOutlined/>}/>
										</Upload> : <Upload {...propsUpload}>
											<Button style={{
												background: styleList().colors.success,
												color: styleList().colors.white
											}}
													disabled={enableColumsFromState('customer_request', record, index)}
													icon={<UploadOutlined/>}/>

										</Upload>}
									</Col>
								</Space>
							</Row>

						</>
					)
				})


			}
		},
		{
			title: 'Gestión técnica',
			dataIndex: 'technicalManagement',
			key: 'technicalManagement',
			render: (text, record) => {
				let index = arrayRequest.indexOf(record);
				const propsUpload = {
					onChange(info) {
						if (info.fileList.length > 0) {
							info.fileList[0].name = "Archivo cargado"
						}

						if (info.file.status === 'removed') {
							technicalDocumentHandler(null, record, index);
						}

					},

					beforeUpload: file => {
						technicalDocumentHandler(file, record, index);
						return false;
					},
					maxCount: 1,
				};

				const propsInputNumber = {

					min: 0,
					size: "small",
					formatter: value => `$ ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ','),
					parser: value => value.replace(/\$\s?|(,*)/g, ''),
					onBlur: (value) => {
						budgetHandler(value.target.ariaValueNow, record, index)
					}
				};

				const showFileLink = (
					<a href={record?.technical_management?.multimedia?.path} target="_blank"><Button type="primary"
																									 icon={
																										 <EyeOutlined/>}/></a>)


				return renderNode({
					children: (<>
						<Row justify="center" align="top">
							<Space direction="vertical">
								<Col span={24}>
									<Row justify="start" align="top">
										<label>Aprobación técnica</label>
									</Row>
									{record?.isNew ? <Select
										disabled={enableColumsFromState('technical_management', record, 0, true)}
										defaultValue={null} style={{width: 120}} onChange={(value) => {
										technicalApprovalHandler(value, record, index)
									}} size="small">

										<Select.Option value={2}>Seleccione</Select.Option>
										<Select.Option value={1}>Si</Select.Option>
										<Select.Option value={0}>No</Select.Option>
									</Select> : <Select
										disabled={enableColumsFromState('technical_management', record, index, true)}
										defaultValue={record?.technical_management?.approved === undefined ? null : (parseInt(record?.technical_management?.approved) || null)}
										style={{width: 120}} onChange={(value) => {
										technicalApprovalHandler(value, record, index)
									}} size="small">

										<Select.Option value={null}>Seleccione</Select.Option>
										<Select.Option value={1}>Si</Select.Option>
										<Select.Option value={0}>No</Select.Option>
									</Select>}

								</Col>

								<Col span={24}>
									<Row justify="start" align="top">
										<label>Presupuesto</label>
									</Row>
									{record?.isNew ? (
										<InputNumber disabled={enableColumsFromState('technical_management', record, 0)}
													 {...propsInputNumber}
										/>) : (<InputNumber
										disabled={enableColumsFromState('technical_management', record, index)}
										defaultValue={(record?.technical_management?.buget) || 0}
										{...propsInputNumber}
									/>)}


								</Col>

								<Col span={24}>
									<Row justify="start" align="top">
										<label>Fecha de pago</label>
									</Row>
									{record?.isNew ? (
										<DatePicker disabled={enableColumsFromState('technical_management', record, 0)}
													format={dateFormat}
													onChange={(date, dateString) => {
														payDayHandler(date, dateString, record, index)
													}} size="small"/>) : (
										<DatePicker
											disabled={enableColumsFromState('technical_management', record, index)}
											format={dateFormat}
											defaultValue={record?.technical_management?.date_pay === undefined ? null : (record?.technical_management?.date_pay === null ? null : moment(record?.technical_management?.date_pay, dateFormat))}
											onChange={(date, dateString) => {
												payDayHandler(date, dateString, record, index)
											}} size="small"/>)}
								</Col>
								<Col span={24}>
									<Row justify="start" align="top">
										<label>Documento técnico</label>
									</Row>
									{record?.technical_management?.multimedia && showFileLink}
									{record?.isNew ? <Upload {...propsUpload}>
										<Button disabled={enableColumsFromState('technical_management', record, 0)}
												icon={<UploadOutlined/>}/>
									</Upload> : <Upload {...propsUpload}>
										<Button style={{
											background: styleList().colors.success,
											color: styleList().colors.white
										}}
												disabled={enableColumsFromState('technical_management', record, index)}
												icon={<UploadOutlined/>}/>
									</Upload>}

								</Col>
							</Space>

						</Row>
					</>)
				})
			}
		},
		{
			title: 'Gestión clientee',
			dataIndex: 'clientManagement',
			key: 'clientManagement',
			render: (text, record) => {
				let index = arrayRequest.indexOf(record);
				const propsUpload = {
					onChange(info) {
						if (info.fileList.length > 0) {
							info.fileList[0].name = "Archivo cargado"
						}

						if (info.file.status === 'removed') {
							customerDocumentHandler(null, record, index);
						}

					},

					beforeUpload: file => {
						customerDocumentHandler(file, record, index)
						return false;
					},
					maxCount: 1
				}
				const showFileLink = (
					<a href={record?.customer_management?.multimedia?.path} target="_blank"><Button type="primary"
																									icon={
																										<EyeOutlined/>}/></a>)
				return renderNode({
					//acá
					children: (<>
						<Row justify="center" align="top">
							<Space direction="vertical">
								<Col span={24}>
									<Row justify="start" align="top">
										<label>Aprobación del cliente</label>
									</Row>
									{record?.isNew ?
										<Select disabled={enableColumsFromState('customer_management', record, 0, true)}
												defaultValue={record?.customer_management?.approved}
												style={{width: 120}}
												onChange={(value) => {
													customerApprovalHandler(value, record, index)
												}} size="small">

											<Select.Option value={null}>Seleccione</Select.Option>
											<Select.Option value={1}>Si</Select.Option>
											<Select.Option value={0}>No</Select.Option>
										</Select> : <Select
											disabled={enableColumsFromState('customer_management', record, index, true)}
											defaultValue={(record?.customer_management?.approved === undefined || record?.customer_management?.approved === null) ? null : parseInt(record?.customer_management?.approved)}
											style={{width: 120}} onChange={(value) => {
											customerApprovalHandler(value, record, index)
										}} size="small">

											<Select.Option value={null}>Seleccione</Select.Option>
											<Select.Option value={1}>Si</Select.Option>
											<Select.Option value={0}>No</Select.Option>
										</Select>}
								</Col>
								<Col span={24}>
									<Row justify="start" align="top">
										<label>Aprobación cliente</label>
									</Row>
									{record?.customer_management?.multimedia && showFileLink}
									{record?.isNew ? <Upload {...propsUpload}>
										<Button disabled={enableColumsFromState('customer_management', record, 0)}
												icon={<UploadOutlined/>}/>
									</Upload> : <Upload {...propsUpload} >
										<Button style={{
											background: styleList().colors.success,
											color: styleList().colors.white
										}}
												disabled={enableColumsFromState('customer_management', record, index)}
												icon={<UploadOutlined/>}/>

									</Upload>}

								</Col>
								<Col span={24}>
									<Row justify="start" align="top">
										<label>Pago realizado?</label>
									</Row>
									{record?.isNew ?
										<Checkbox disabled={enableColumsFromState('customer_management', record, 0)}
												  onChange={(value) => {
													  isPaidHandler(value, record, index)
												  }} size="small">Si</Checkbox> :
										<Checkbox disabled={enableColumsFromState('customer_management', record, index)}
												  defaultChecked={changeNumberToBoolean(record?.customer_management?.is_paid)}
												  onChange={(value) => {
													  isPaidHandler(value, record, index)
												  }} size="small">Si</Checkbox>}

								</Col>
							</Space>

						</Row>
					</>)
				})

			}
		},
		{
			title: 'Estado',
			dataIndex: 'state',
			key: 'state',
			render: (text, record) => (renderNode({children: (<Text>{record?.state?.name_state}</Text>)}))
		},
		{
			title: 'Acciones',
			dataIndex: 'actions',
			key: 'actions',
			render: (text, record) => {

				return renderNode({
					children: (<>
						{showEditButton(record)}
						{
							validatePermission(constants.DELETE_REFORM_REQUEST_PERMISSION) ?
								<Tooltip title="Eliminar">
									<Popconfirm
										title="¿Desea eliminar esta solicitud?"
										onConfirm={() => {
											sendDataToBackRemove(record)
										}}
										onCancel={() => {
										}}
										okText="Si"
										cancelText="No"
										icon={<QuestionCircleOutlined style={{color: 'red'}}/>}
									>
										<Button style={styleList().back_danger} type="default" onClick={() => {
										}} icon={<DeleteFilled style={styleList().color_white}/>} size="small"/>

									</Popconfirm>
								</Tooltip> : null
						}
					</>)
				})
			}
		},
	];

	const showEditButton = (record) => {

		let index = arrayRequest.indexOf(record)

		let comparation = _.isEqual(_.cloneDeep(arrayRequest[index]), _.cloneDeep(arrayRequestLocal[index]))
		if (arrayRequest.length === index - 1) {


		}
		if (!validatePermission(constants.UPDATE_REFORM_REQUEST_PERMISSION)) {
			return null;
		}
		if (comparation) {

			return (<Tooltip title="Actualizar">
				<Button disabled type="default" icon={<SyncOutlined/>} size="small" onClick={() => {
					buildFormData(arrayRequest.indexOf(record))
				}}/>
			</Tooltip>)
		}

		return (<Tooltip title="Actualizar">
			<Button type="default" icon={<SyncOutlined/>} size="small" onClick={() => {
				buildFormData(arrayRequestLocal[index])
			}}/>
		</Tooltip>)


	}


	const finalFront = (<>
		<Row justify="center" align="top" gutter={[16, 44]}>

			<Col span={24}>
				<Table dataSource={convertAllMapDataState(arrayRequest)} columns={columns} rowKey="id" bordered/>

			</Col>

			{tableVisible && <Col span={24}>
				<NewReformRequest columns={columns.slice(2, columns.length - 2)}
								  dataSource={convertAllMapDataState(dataSourceNewRequest)}/>
			</Col>}

		</Row>

		<Row justify="end" align="top" gutter={16} className="margin-top-10">

			{!tableVisible && <Col>
				{
					validatePermission(constants.CREATE_REFORM_REQUEST_PERMISSION) ?
						<Button type="primary" onClick={tableVisibleHandler}
								disabled={(moment(maximum_reforms_date).isBefore(moment()))}>
							Nueva petición
						</Button>
						: null
				}
			</Col>}
			{tableVisible && <Col>
				<Button type="primary" onClick={tableVisibleHandler}>Cancelar</Button>
			</Col>}
			{tableVisible && <Col>
				{
					validatePermission(constants.CREATE_REFORM_REQUEST_PERMISSION) ?
						<Button type="primary" onClick={() => {
							buildFormData(dataSourceNewRequest[0])
						}}>Registrar</Button>
						: null
				}
			</Col>}

		</Row></>)
	return (
		<>

			{finalFront}
		</>)
}


export default ListOfRequest;