import React, {useState} from "react";
import {useDispatch} from "react-redux";
import {SearchOutlined} from "@ant-design/icons";
import {Button, Form, Input, Modal, Tooltip} from "antd";
import {setObservation} from "../../../../../../../redux/actions/Customer/CustomerAction";
import {errorGlobal, success} from "../../../../../../handle/Notification/Notification";

const Observations = ({record, setSalesUpdate}) => {
	
	const textObservations = <span style={{ color: '#6A6963' }}> Observaciones </span>
	const [show,setShow] = useState(false)
	const [form] = Form.useForm()
	const dispatch = useDispatch()
	
	const showModal = () => {setShow(true)}
	const closeModal = () => {reset();setShow(false)}
	const reset = () => {form.resetFields()}
	
	const onFinis = async () => {
		const values = await form.validateFields()
		dispatch(
			setObservation(record.id,values,async (response)=>{
				const res = await response
				if (res.error){
					errorGlobal(res.message)
				}else {
					setSalesUpdate()
					closeModal()
					success(res.message)
				}
			},async (error) => {
				const e = await error
				errorGlobal(e.message)
			})
		)
	}
	
	return <>
		<Tooltip placement="left" color='#ffffff' title={textObservations}>
			<Button
				size="small"
				onClick={()=>showModal()}
				style={{ backgroundColor: '#002140', marginTop: 3 }}
				icon={<SearchOutlined style={{ color: '#fff' }} />}
			/>
		</Tooltip>
		<Modal
			visible={show}
			title={"Observación"}
			onCancel={()=>closeModal()}
			onOk={()=>onFinis()}
			okText={"Guardar"}
			cancelText={"Finalizar"}
		>
			<Form
				name="registerObservations"
				layout="vertical"
				form={form}
			>
				<Form.Item 
					label="Observaciónes" 
					name="observation"
					rules={[{ required: true, message: 'Please input your phone number!' }]}
				>
					<Input.TextArea showCount maxLength={100}/>
				</Form.Item>
			</Form>
		</Modal>
	</>
}

export default Observations