import React, {useState, useEffect} from 'react';
import {Modal, Button, Upload, Row, Space} from 'antd';
import {useDispatch} from 'react-redux'
import {EyeOutlined} from '@ant-design/icons';
import {PlusOutlined} from '@ant-design/icons';
import {errorGlobal} from "../../../../../../handle/Notification/Notification";
import {deleteUnitTypeByIdAttachment, creatUnitTypeAttachment} from '../../../../../../../redux/actions/Project/UnitType';
import {RUTA_API_MULTIMEDIA} from '../../../../../../../redux/constants';
import {onPreviewHelper} from "../../../../../../handle/Multimedia/Multimedia";

const ModalPlans = (props) => {

	const dispatch = useDispatch()
	const [isModalVisible, setIsModalVisible] = useState(false);
	const req = props.record.blueprints;
	useEffect(() => {
		if (req != null) setData(req)
	}, [req]);
	const [data, setData] = useState([])
	const [ setIsModalPrevVisible] = useState(false);
	const [images, setImages] = useState([]);
	useEffect(() => {
		if (req != null) {
			let tempImages = [];
			req.forEach((r, index) => {
				tempImages.push({
					uid: r.id,
					percent: 50,
					name: 'Multimedia ' + (index + 1),
					status: 'done',
					url: r.path,
				});
			})
			setImages(tempImages);
		}
	}, [])

	const showModal = () => {
		setIsModalVisible(true);
	};

	const handleOk = () => {
		setIsModalVisible(false);
	};

	const handleCancel = () => {
		setIsModalVisible(false);
	};

	const handleDelete = (record) => {
		const dataSource = [...data];
		setData(dataSource.filter((item) => item.id !== record.uid));
		dispatch(deleteUnitTypeByIdAttachment(record.uid,props.record.id))
	};

	const visibleModals = () => {
		if (isModalVisible) {
			setIsModalPrevVisible(true);
			setIsModalVisible(false);

		} else {
			setIsModalVisible(true);
			setIsModalPrevVisible(false);
		}
	}


	const handlePreview = async file => {
		await onPreviewHelper(file);
		visibleModals();
	};

	const uploadProps = {

		beforeUpload: file => {
			if (file.type !== 'image/png' && file.type !== 'image/jpeg' && file.type !== 'application/pdf') {
				errorGlobal("Archivo no válido");
				return file.type === ('image/png' || 'image/jpeg') ? true : Upload.LIST_IGNORE;
			}
		}, customRequest({onSuccess, onError, file}) {
			let formData = new FormData();
			formData.append('file', file, file.name);
			formData.append('type', 2);
			dispatch(creatUnitTypeAttachment(props.record.id, formData, onSuccess, onError));
		},
		onChange: () => {
		},
		
		
	};

	const uploadImage = async (file) => {
		setImages(file.fileList)
	};


	const uploadButton = (
		<div>
			<PlusOutlined/>
			<div style={{marginTop: 8}}>Upload</div>
		</div>
	);

	return (
		<>
			<Button type="primary" onClick={showModal}>
				<EyeOutlined/>
			</Button>
			<Modal
				title="Planos"
				visible={isModalVisible}
				onOk={handleOk}
				onCancel={handleCancel}
				footer={false}

			>
				<Row>
					<Space>

						<Upload {...uploadProps}
								action={RUTA_API_MULTIMEDIA}
								listType="picture-card"
								fileList={images}
								accept="image/*,.pdf"
								onPreview={handlePreview}
								onChange={uploadImage}
								onRemove={handleDelete}
						>
							{(images.length < 100) ? '+ Upload' : uploadButton}
						</Upload>

					</Space>
				</Row>
				<br/> <br/>

				<br/>
				<br/>

			</Modal>
		</>
	);
};

export default ModalPlans