import * as constants from "../../../constants";

export const fetchExcelAccountState = (data, postProcessSuccess) => ({
    type: constants.API,
	payload: {
		method: 'POST',
		data: data,
		url: `${constants.RUTA}/customer/report/get-excel-account-status`,
		success: (response) => (fethAccountStateReport(response)),
		postProcessSuccess: postProcessSuccess
	}
})

