import * as constants from "../../../constants";

export const createDeed = (data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		headers: constants.TOKEN,
		url: `${constants.RUTA}/deed`,
		data,
		success: (response) => (addDeed(response)),
		postProcessSuccess: onSuccess,
		postProcessError: onError
	}
});

export const updateDeed = (deedId, data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		headers: constants.TOKEN,
		url: `${constants.RUTA}/deed/${deedId}`,
		data,
		success: (deed_id, data) => (updateDeedById(deed_id, data)),
		postProcessSuccess: onSuccess,
		postProcessError: onError
	}
});

export const getDeedById = (deedId, onSuccess) => ({
	type: constants.API,
	payload: {
		method: 'GET',
		url: `${constants.RUTA}/deed/${deedId}`,
		postProcessSuccess: onSuccess,
		success: (response) => (getById(response))
	}
});

const addDeed = (data) => ({
	type: constants.ADD_DEED,
	payload: data
});

const getById = (deed_id) => ({
	type: constants.GET_DEED_BY_ID,
	payload: {deed_id}
});

const updateDeedById = (deed_id, data) => ({
	type: constants.UPDATE_STATE_CHANNEL,
	payload: { deed_id, data }
});