import { Table } from "antd";
import { Checkbox, } from 'antd';
import { useDispatch } from "react-redux";
import React, { useState, useEffect } from "react";
import Spinner from "../../../../../handle/Spinner/Spinner";
import { filterCoupons } from "../../../../../../redux/actions/Configuration/CuoponsActions";

const formatterPeso = new Intl.NumberFormat("es-CO", {
	style: "currency",
	currency: "COP",
	minimumFractionDigits: 0,
});

const DiscountsCoupons = ( props ) => {
	const {ProjectId, CustomerId,Precie,Discoun , dataSource} = props;
	const dispatch = useDispatch();
	const [consul,setConsul] = useState(false)
	const [coupons, setCoupons] = useState([])
	let Discount = [];
	useEffect(() => searchCoupons(), []);
	const columns = [
		{
			title: "Código",
			width: 20,
			dataIndex: "code",
			key: "code",
		},
		{
			title: "Fecha cencimiento",
			width: 30,
			dataIndex: "expiration_code",
			key: "expiration_code",
		},
		{
			title: "Tipo descuento",
			width: 20,
			dataIndex: "discount_type",
			key: "discount_type",
			render:(_, record)=> get_discount_type(record),
		},
		{
			title: "Total a descontar",
			width: 20,
			dataIndex: "valueDiscount",
			key: "valueDiscount",
			render: (_, record) => Descontar(record),
		},
		{
			title: "Acciones",
			width: 20,
			dataIndex: "",
			key: "",
			render: (_, record) => Acciones(record),
		},
	];

	const get_discount_type=(value)=>{
		switch(value.discount_type){
			case 1:
				return 'Valor total';
			case 2:
				return 'Porcentaje';
			default:
				return 'Valor total';
		}
	}

	const Descontar = (record) => {
		return (
			formatterPeso.format(record.valueDiscount)
		);
	};
	const Acciones = (record) => {
		let date_expiration = new Date(record.expiration_code);
		let date_now = new Date();
		return (
			<Checkbox
				style={{ paddingLeft: "50%" }}
				onChange={(event) => onChangeKit(record,event)}
				disabled={date_now>date_expiration}
			/>
		);
	};
	
	const onChangeKit = (value,event) => {
		let ubi;
		let valor = value.valueDiscount;
		if(event.target.checked){
			Discount.push(value.key);
		}else{
			Discount.forEach((element,index) => {
				if(element.id === value.key){
					ubi = index;
				}
			});
			Discount.splice(ubi,1);
		}
		Discoun(valor,value.key,event.target.checked,"Cupon");
	}
	const searchCoupons = () => {
		let fData = new FormData();
		let couponsTable = [];
		fData.append("project_id", ProjectId);
		fData.append("customer_id", CustomerId);
		if(dataSource !== undefined){
			dataSource.forEach((a)=>{
				couponsTable.push({
					key:a.id,
					code: a.code,
					expiration_code: a.expiration_code,
					discount_type: a.discount_type,
					valueDiscount:
						parseInt(a.discount_type) === 1
							? a.discount_value
							: ( Precie * (a.discount_percentage/100))
				});
			})
			setCoupons(couponsTable)
			setConsul(true)
		}else{
			dispatch(filterCoupons(fData , async (response) => {
				const res = await response
				res._payload.forEach((p) => {
					couponsTable.push({
						key: p.id,
						code: p.code,
						expiration_code: p.expiration_code,
						discount_type: p.discount_type,
						valueDiscount:
							p.discount_type === 1
								? p.discount_value
								: ( Precie * (p.discount_percentage/100))
					});
				})
				setCoupons(couponsTable)
				setConsul(true)
			}))
		}
	}
	if (!consul) return <Spinner />;
	return (
		<>
            <Table
                style={{ width: "100%" }}
                pagination={false}
                columns={columns}
				dataSource={coupons}
                bordered
            />
		</>
	);
};
export default DiscountsCoupons;
