import React from "react";
import {Col, Form, Input, Row} from "antd";

const BasicInformation = () => {
	return (<>
		<Row>
			<Col span={11}>
				<Form.Item
					label="Proyecto"
					name="project"
				>
					<Input disabled={true}/>
				</Form.Item>
			</Col>
			<Col span={2}/>
			<Col span={11}>
				<Form.Item
					label="Empresa Comercializadora"
					name="tradingCompany"
				>
					<Input disabled={true}/>
				</Form.Item>
			</Col>
		</Row>
		<Row>
			<Col span={11}>
				<Form.Item
					label="Usuario Creador"
					name="creatorUser"
				>
					<Input disabled={true}/>
				</Form.Item>
			</Col>
			<Col span={2}/>
			<Col span={11}>
				<Form.Item
					label="Cliente"
					name="customer"
				>
					<Input disabled={true}/>
				</Form.Item>
			</Col>
		</Row>
		<Row>
			<Col span={11}>
				<Form.Item
					label="Unidad"
					name="unit"
				>
					<Input disabled={true}/>
				</Form.Item>
			</Col>
			<Col span={2}/>
			<Col span={11}>
				<Form.Item
					label="Fecha de Solicitud"
					name="applicationDate"
				>
					<Input disabled={true}/>
				</Form.Item>
			</Col>
		</Row>
	</>)
}

export default BasicInformation