import moment from 'moment';
import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { VALID_DOCUMENT_UPLOAD } from '../../../../../redux/constants';
import { formatterMoney } from '../../../../handle/FormatterMoney/FormatterMoney';
import { errorGlobal, success } from "../../../../handle/Notification/Notification";
import { UploadOutlined, CheckSquareOutlined, ArrowDownOutlined } from "@ant-design/icons";
import {convertAllMapDataState} from "../../../../handle/HandleFilterTable/handleFilterTable";
import { updateCheckDirectCollection } from "../../../../../redux/actions/Collection/DirectCollectionAction";
import { Button, Form, Row, Col, InputNumber, Upload, Divider, Descriptions, Input, Modal, Table, Tooltip,} from "antd";
import { DownloadDocument } from '../../../../handle/Multimedia/DownloadDocument';

const PaymentVerification = ({data, onSave}) => {

	const [form] = Form.useForm();

	const dispatch = useDispatch();

	const [isModalVisible, setIsModalVisible] = useState(false);

	const [fileUpload, setFileUpload] = useState(null);
	const [pressBottonState, setPressBottonState] = useState(false);

	const showModal = () => {
		setIsModalVisible(true);
	};

	const download = () => {
		
		if (fileUpload === null) {
			DownloadDocument(data?.reification_document_path);
		}
	}

	const normFile = (e) => {
		if (Array.isArray(e)) {
			return e;
		}
		return e && e.fileList;
	};

	const closeAndResetForm = () => {
		onClose();
		onReset();
	}
	const onReset = () => {
		form.resetFields();
	}
	const onClose = () => {
		setIsModalVisible(false);

	}
	const fillFields = () => {
		form.setFieldsValue({
			voucher_Support: data?.reification_document_name,
			voucher_number : data?.voucher_number
		})
		showModal();
	}

	const handleSubmit = async () => {
		setPressBottonState(true);
		const values = await form.validateFields();
		let formData = new FormData();

		formData.append('voucher_number', values["voucher_number"]);
		if (fileUpload !== null) {
			formData.append('voucher_support_document', fileUpload, fileUpload.name);
		}
		dispatch(updateCheckDirectCollection(data.id, formData, (response) => {
			if (!response.error) {
				success("¡Recaudo verificado correctamente!");
				onSave();
				setFileUpload(null);
				closeAndResetForm();
				setPressBottonState(false);
			} else {
				errorGlobal("¡UPS! Ocurrió un error al momento de verificar el recaudo");
				setPressBottonState(false);
			}
		}));
	}

	const propsUpload = {
		name: 'file',
		beforeUpload: () => {
			return false;
		},
		onChange: info => {
			form.setFieldsValue({
				voucher_Support: info.file !== undefined ? info.file.name : ' '
			});
			setFileUpload(info.file);
		}, showUploadList: false,
	}

	const propertyValueColumns = [
		{
			dataIndex: "descripcion",
			width: 40,

			padding: "10px",
		},
		{
			dataIndex: "value",
			width: 80,

			padding: "10px",
		},
	];
	function nameCustomer() {
		let newName = data.name.split("_");
		return newName[0] + " " + newName[1];
	}
	const propertyValue = [
		{
			descripcion: "Cliente",
			value: nameCustomer(),
		},

		{
			descripcion: "Proyecto",
			value: data.project_name,
		},
		{
			descripcion: "Unidad",
			value: data.unit_label,
		},
		{
			descripcion: "Fecha de consignación",
			value: moment(data.consignment_date).format('DD/MM/YYYY'),
		},
		{
			descripcion: "Valor de consignación",
			value: formatterMoney.format(data.consignment_amount) ,
		},
		{
			descripcion: "Concepto de consignación",
			value:
				data?.consignment_concept === 1
					? "recaudo"
					: data?.consignment_concept === 2
						? "Abono"
						: "otros",
		},
		{
			descripcion: "Soporte  de consignación",
			value: data?.name_multimedia,
		},
	];
	const validateMessages = {
		// eslint-disable-next-line no-template-curly-in-string
		required: '${label} es requerido',
		types: {
			// eslint-disable-next-line no-template-curly-in-string
			email: '${label} no es un correo válido',
			// eslint-disable-next-line no-template-curly-in-string
			number: '${label} debe ser un número'

		}
	};
	const textCheck = (
		<span style={{ color: "#6A6963" }}> Verificar </span>
	);
	return (
		<>
			<Tooltip placement="left" color="#ffffff" title={textCheck}>
				<Button
    style={{backgroundColor: '#4BAFF3', color: "#ffffff"}}
    size={"small"}
    onClick={fillFields}
    icon={<CheckSquareOutlined/>}
    />
			</Tooltip>

			<Modal
				title={"Verificación abono"}
				visible={isModalVisible}
				footer={false}
				onCancel={() => setIsModalVisible(false)}
				width="70%"
			>

				<Form
					layout="vertical"
					size="middle"
					validateMessages={validateMessages}
					form={form}
					onFinish={handleSubmit}
				>
					<Table
						showHeader={false}
						style={{ width: "60%" }}
						pagination={false}
						columns={propertyValueColumns}
						dataSource={convertAllMapDataState(propertyValue)}
						bordered

					/>

					<Divider />
					<Descriptions title="Verificado por contabilidad"/>
					<Row gutter={16}>
						<Col xs={24} sm={24} md={6} lg={6} xl={6}>
							<Form.Item
								name="voucher_number"
								label="No. de comprobante"
								rules={[{ required: true }]}
							>
								<InputNumber style={{ width: "100%" }} />
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={8} lg={8} xl={8}>
							<Form.Item
								name="voucher_Support"
								label="Soporte del comprobante"
								rules={[{ required: true }]}
							>
								<Input style={{ width: "100%" }} disabled={true} />
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={8} lg={8} xl={9}>
							<Form.Item
								valuePropName="fileList"
								label={" "}
								name="voucher_support_document"
							>
								<Upload
									accept={VALID_DOCUMENT_UPLOAD}
									name={"voucher_support_document"}
									{...propsUpload}
									getValueFromEvent={normFile}
								>

									<Button
										style={{ width: "100%" }}
										type="primary"
										icon={<UploadOutlined />}
									>
										Elegir archivo
									</Button>
								</Upload>
								<Button onClick={download} style={{ background: '#389e0d', color: '#fff' }} icon={<ArrowDownOutlined />}>Descargar</Button>
							</Form.Item>
						</Col>
					</Row>
					<Divider />
					<Row>
						<Col xs={2} sm={2} md={2} lg={2} xl={22} />
						<Col xs={2} sm={2} md={2} lg={2} xl={2}>
							<Form.Item style={{ margin: '0' }}>
								<Button disabled={pressBottonState} type="primary" htmlType="submit">
									Guardar
								</Button>
							</Form.Item>
						</Col>
					</Row>
				</Form>
			</Modal>
		</>
	);
};

export default PaymentVerification;
