import React, {useEffect} from "react";
import {Form, Button, InputNumber, Card, Select, DatePicker} from "antd";
import {DeleteOutlined} from "@ant-design/icons";
import moment from "moment";
import {FORMATTER_INPUT_NUMBER} from "../../../../../../../../redux/constants";

const formItemStyle = {
	width: "100%",
	margin: 0,
	paddingTop: 4,
};

const DiscountPolicyComponent = ({item, handleDeleteDiscountPolicy, form}) => {
	
	useEffect(() => {
		fillFields();
	}, [item]);

	const fillFields = () => {
		form.setFieldsValue({
			[`lower_limit;${item.id}`]: item.lower_limit,
			[`upper_limit;${item.id}`]: item.upper_limit,
			[`discount_type;${item.id}`]: item.discount_type
				? String(item.discount_type)
				: "",
			[`amount;${item.id}`]: item.amount,
			[`initial_date;${item.id}`]: moment(item.initial_date),
			[`final_date;${item.id}`]: moment(item.final_date),
		});
	};

	const handleDelete = () => {
		handleDeleteDiscountPolicy(item.id);
	};

	return (
		<>
			<Card
				className={"formStyle"}
				style={
					({minWidth: "200px", padding: "10% 0"},
						{display: "block"},
						{borderColor: "black"})
				}
				title="Politica"
				extra={
					<>
						<Button
							onClick={handleDelete}
							icon={<DeleteOutlined/>}
							type="primary"
							danger
						/>
					</>
				}
				bodyStyle={{padding: 5}}
			>
				<Form.Item
					name={`initial_date;${item.id}`}
					rules={[
						{
							required: true,
							message: "El campo es requerido.",
						},
					]}
					style={formItemStyle}
				>
					<DatePicker
						style={{width: "100%"}}
						value={form.getFieldValue(`initial_date;${item.id}`)}
						placeholder="Ingrese la fecha"
					/>
				</Form.Item>
				<Form.Item
					style={formItemStyle}
					name={`final_date;${item.id}`}
					rules={[
						{
							required: true,
							message: "El campo es requerido",
						},
					]}
				>
					<DatePicker
						style={{width: "100%"}}
						value={form.getFieldValue(`final_date;${item.id}`)}
						placeholder="Ingrese la fecha"
					/>
				</Form.Item>
				<Form.Item
					style={formItemStyle}
					name={`lower_limit;${item.id}`}
				>
					<InputNumber
						style={{width: "100%"}}
						value={form.getFieldValue(`lower_limit;${item.id}`)}
						placeholder={"Límite Inferior"}
					/>
				</Form.Item>
				<Form.Item
					style={formItemStyle}
					name={`upper_limit;${item.id}`}
				>
					<InputNumber
						style={{width: "100%"}}
						value={form.getFieldValue(`upper_limit;${item.id}`)}
						placeholder={"Límite Superior"}
					/>
				</Form.Item>
				<Form.Item
					style={formItemStyle}
					name={`discount_type;${item.id}`}
					value={form.getFieldValue(`discount_type;${item.id}`)}
					showSearch
					optionFilterProp="children"
					filterOption={(input, option) =>
						option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
					}
					rules={[
						{
							required: true,
							message: "El campo es requerido",
						},
					]}
				>
					<Select defaultValue="">
						<Select.Option value="">Seleccionar</Select.Option>
						<Select.Option value="1">Valor m2</Select.Option>
						<Select.Option value="2">Valor Total</Select.Option>
					</Select>
				</Form.Item>
				<Form.Item
					rules={[
						{
							required: true,
							message: "El campo es requerido",
						},
					]}
					style={formItemStyle}
					name={`amount;${item.id}`}
				>
					<InputNumber
						{...FORMATTER_INPUT_NUMBER}
						parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
						style={{width: "100%"}}
						value={form.getFieldValue(`amount;${item.id}`)}
						placeholder={"Valor"}
					/>
				</Form.Item>
			</Card>
		</>
	);
};

export default DiscountPolicyComponent;
