import { useCoupons } from "./UseCoupons";
import { PlusOutlined } from "@ant-design/icons";
import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import Spinner from "../../../../handle/Spinner/Spinner";
import {FORMATTER_INPUT_NUMBER} from "../../../../../redux/constants";
import { errorGlobal, success } from "../../../../handle/Notification/Notification";
import { fetchAllProjects } from "../../../../../redux/actions/Project/ProjectsAction";
import { fetchAllChannel } from "../../../../../redux/actions/Configuration/ChannelAction";
import { fetchAllCoupons } from "../../../../../redux/actions/Configuration/CuoponsActions";
import { Form, Input, InputNumber, Button, Row, Col, DatePicker, Modal, Select } from "antd";
import { createCoupon, getCodeCoupon } from "../../../../../redux/actions/Configuration/CuoponsActions";

const FormCuopons = () => {
	const dispatch = useDispatch();
	const [visible, setVisible] = useState(false);
	const dateFormat = "YYYY/MM/DD";
	const [loading, setLoading] = useState(false);
	const {
		code,
		setCode,
		discount_type,
		setDiscountType,
		discount_percentage,
		setDiscountPercentage,
		discount_value,
		setDiscountValue,
	} = useCoupons();

	const handleSubmit = async () => {
		try {
			setLoading(true)
			const values = await form.validateFields();
			const dto = {
				...values,
				code: code,
				expiration_code: values["expiration_code"].format("YYYY-MM-DD"),
				project_id:
					values["project_id"] === "0" ? null : parseInt(values["project_id"]),
				creator_user_id: userStorage.id,
			};
			dispatch(
				createCoupon(dto, (response) => {
					if (!response.error) {
						success("Ha sido agregado con éxito");
						dispatch(fetchAllCoupons());
						setLoading(false)
						closeAndResetForm();
					} else {
						errorGlobal(
							"¡UPS! Ocurió un error mientras se registraba el cupón"
						);
					}
				})
			);
		} catch (errorInfo) {
			console.error(errorInfo);
		}
	};

	useEffect(() => {
		if (discount_type === "Valor Total" || discount_type === 1) {
			setDiscountPercentage(0);
		} else if (discount_type === "Porcentaje" || discount_type === 2) {
			setDiscountValue(0);
		}
	}, [discount_type]);

	useEffect(() => {
		if (discount_type === "Valor Total" || discount_type === 1) {
			setDiscountPercentage(0);
		} else if (discount_type === "Porcentaje" || discount_type === 2) {
			setDiscountValue(0);
		}
	}, []);

	let userStorage = JSON.parse(localStorage.getItem("USER_INFO"));

	useEffect(() => {
		if (visible) {
			dispatch(fetchAllProjects());
			dispatch(fetchAllChannel());
			dispatch(
				getCodeCoupon((response) => {
					if (!response.error) {
						setCode(response._payload);
					}
				})
			);
		}
	}, [visible]);

	const showModal = () => {
		setVisible(true);
	};

	const [form] = Form.useForm();
	const onClose = () => {
		setVisible(false);
		setDiscountType(0);
		setDiscountPercentage(0);
		setDiscountValue(0);
	};

	const closeAndResetForm = () => {
		onClose();
		onReset();
	};

	const onReset = () => {
		form.resetFields();
	};

	let elementProject = useSelector((state) => state?.project);
	let selectCustomerElement = useSelector((state) => state?.customer);

	return (
		<>
			<Button type="primary" onClick={showModal} icon={<PlusOutlined />}>
				Nuevo Cupón
			</Button>
			{visible && (
				<Modal
					width={1000}
					icon={<PlusOutlined />}
					title="Nuevo cupón"
					text="Nuevo cupón"
					backgroundColor="#1890ff"
					color="#ffffff"
					visible={visible}
					footer={false}
					onCancel={onClose}
				>
					<Form
						name="FormCuopons"
						layout="vertical"
						form={form}
						onFinish={handleSubmit}
					>
						<Row gutter={16}>
							<Col xs={24} sm={24} md={8} lg={8} xl={8}>
								<Form.Item
									label="Código"
									name="code"
									rules={[
										{
											required: false,
											message: "Campo requerido",
										},
									]}
								>
									{() => {}}
									<Input value={code} disabled={true}/>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={8} lg={8} xl={8}>
								<Form.Item
									label="Cliente"
									name="customer_id"
									rules={[
										{
											required: true,
											message: "Campo requerido",
										},
									]}
								>
									<Select
										showSearch
										style={{ width: "100%" }}
										optionFilterProp="children"
										value={form.getFieldValue("customer_id")}
										filterOption={(input, option) =>
											option.children
												.toLowerCase()
												.indexOf(input.toLowerCase()) >= 0
										}
										rules={[{ required: true }]}
										defaultValue={""}
									>
										<Select.Option value="">Seleccione...</Select.Option>
										{selectCustomerElement.hasOwnProperty("_payload") &&
											selectCustomerElement._payload.map((item) => (
												<Select.Option value={item.id}>{item.name}</Select.Option>
											))}
									</Select>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={8} lg={8} xl={8}>
								<Form.Item
									label="Proyecto"
									name="project_id"
									rules={[
										{
											required: true,
											message: "Campo requerido",
										},
									]}
								>
									<Select
										style={{ width: "100%" }}
										showSearch
										optionFilterProp="children"
										value={form.getFieldValue("project_id")}
										rules={[{ required: true }]}
										placeholder="Seleccionar..."
									>
										<Select.Option value="">Seleccionar...</Select.Option>
										<Select.Option value="0">TODOS</Select.Option>
										{elementProject.hasOwnProperty("_payload") &&
											elementProject._payload.map((item) => (
												<Select.Option value={item.id}>{item.name}</Select.Option>
											))}
									</Select>
								</Form.Item>
							</Col>
						</Row>
						<Row gutter={16}>
							<Col xs={24} sm={24} md={8} lg={8} xl={8}>
								<Form.Item
									label="Fecha de vencimiento"
									name="expiration_code"
									rules={[
										{
											required: true,
											message: "Campo requerido",
										},
									]}
								>
									<DatePicker
										style={{ width: "100%" }}
										format={dateFormat}
										onChange={form.getFieldValue("expiration_code")}
										placeholder="Ingrese una fecha"
									/>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={8} lg={8} xl={8}>
								<Form.Item
									label="Tipo de Cupón"
									name="coupon_type"
									rules={[
										{
											required: true,
											message: "Campo requerido",
										},
									]}
								>
									<Select
										style={{ width: "100%" }}
										value={form.getFieldValue("coupon_type")}
										placeholder="Seleccionar..."
									>
										<Select.Option value="">Seleccionar...</Select.Option>
										<Select.Option value="1">Descuento</Select.Option>
										<Select.Option value="2">Pronto pago</Select.Option>
										<Select.Option value="3">Canje</Select.Option>
									</Select>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={8} lg={8} xl={8}>
								<Form.Item
									label="Tipo de descuento"
									name="discount_type"
									rules={[
										{
											required: true,
											message: "Campo requerido",
										},
									]}
								>
									<Select
										style={{ width: "100%" }}
										placeholder="Seleccionar..."
										value={form.getFieldValue("discount_type")}
									>
										<Select.Option value="">Seleccionar...</Select.Option>
										<Select.Option value={1}>Valor total</Select.Option>
										<Select.Option value={2}>Porcentaje</Select.Option>
									</Select>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={8} lg={8} xl={8}>
								<Form.Item
									noStyle
									shouldUpdate={(prevValues, currentValues) =>
										prevValues.discount_type !== currentValues.discount_type
									}
								>
									{({ getFieldValue }) =>
										getFieldValue("discount_type") === 2 ? (
											<Form.Item
												label="Descuento (%)"
												name="discount_percentage"
												rules={[
													{
														type: 'number',
														required: true,
														message: "Campo requerido y debe ser mayor a 0",
														min: 1
													},
												]}
											>
												<InputNumber 
													style={{ width: '100%' }}
													placeholder="Valor"
													defaultValue={discount_percentage}
													onChange={form.getFieldValue("discount_percentage")}
												/>
											</Form.Item>
										) : getFieldValue("discount_type") === 1 ? (
											<Form.Item
												label="Descuento ($)"
												name="discount_value"
												rules={[
													{
														type: 'number',
														required: true,
														message: "Campo requerido y debe ser mayor a 0",
														min: 1
													},
												]}
											>
												<InputNumber
													{...FORMATTER_INPUT_NUMBER}
													style={{ width: '100%' }}
													placeholder="Valor"
													defaultValue={discount_value}
													onChange={form.getFieldValue("discount_value")}
												/>
											</Form.Item>
										) : null
									}
								</Form.Item>
							</Col>
						</Row>
						<Row>
						<Col offset={21} xs={24} sm={24} md={4} lg={4} xl={4}>
							<Form.Item>
								{ !loading ? (<Button type="primary" htmlType="submit">
										Guardar
									</Button>) : <Spinner />
								}
							</Form.Item>
							
						</Col>
						</Row>
					</Form>
				</Modal>
			)}
		</>
	);
};

export default FormCuopons;
