import {useDispatch} from "react-redux";
import {Button, Form, Table} from "antd";
import PostSaleAddForm from "./PostSaleAddForm";
import React, {useEffect, useState} from "react";
import {useCustomer} from "../../../../../Hooks/useCustomer";
import AfterSalesAccomplished from "./AfterSalesAccomplished";
import {DeleteOutlined, PlusOutlined, SaveOutlined} from "@ant-design/icons";
import {errorGlobal, success} from "../../../../../../../../handle/Notification/Notification";
import {deletePostSale, fulfilled, technicalDocument} from "../../../../../../../../../redux/actions/Customer/AfterSalesCustomer";

const InteractiveTable = ({sale_id,query,searchForInformation}) => {
	const [data,setData] = useState([])
	const [nuevo,setNuevo] = useState(false)
	const dispatch = useDispatch()
	const [forms] = Form.useForm()
	const [chek,setChek] = useState([])
	const {DataSet,columns} = useCustomer()
	
	useEffect(()=>{
		dataSet(query)
	},[query])
	
	const dataSet = (data) => {
		let d = []
		data?.forEach((element)=>{
			d.push(DataSet(element))
		})
		setData(d)
	}
	
	const onDelete = (id) => {
		dispatch(deletePostSale(id,async (response)=>{
			const res = await response
			success('Eliminado con exito : '+res._payload.comment)
			searchForInformation()
		},async (error)=>{
			const err = await error
			errorGlobal(err.message)
		}))
	}
	
	const onEdit = async (id,data) => {
		const value = await forms.validateFields()
		const formData = new FormData()
		let name = 'finish'+id
		let inds = findIndex(name)
		let fulfillment
		if (inds !== null){
			fulfillment = chek[inds].state === true ? 1 : 0 
		}else {
			fulfillment = 0
		}
		formData.append('valor', value['budget'+id])
		formData.append('comment', value['comment'+id])
		formData.append('fulfilled', fulfillment)
		formData.append('approval_of_works_director', value['approvalOfWorksDirector'+id])
		if (typeof value["complianceDocument"+id] !== "undefined"){
			for (let i = 0; i < value["complianceDocument"+id].length; i++){
				formData.append(
					`fulfilled_document`,
					value["complianceDocument"+id][i].originFileObj,
					value["complianceDocument"+id][i].name
				)
			}
		}
		if (typeof value["technicalDocument"+id] !== "undefined"){
			for (let i = 0; i < value["technicalDocument"+id].length; i++){
				formData.append(
					`technicalDocument`,
					value["technicalDocument"+id][i].originFileObj,
					value["technicalDocument"+id][i].name
				)
			}
		}
		if (typeof value["application_document"+id] !== "undefined"){
			for (let i = 0; i < value["application_document"+id].length; i++) {
				formData.append(
					`application_document`,
					value["application_document"+id][i].originFileObj,
					value["application_document"+id][i].name
				)
			}
		}
		if (data.state === 'EN PROCESO'){
			dispatch(technicalDocument(id,formData,async (response)=>{
				const res = await response
				success(res.message)
				searchForInformation()
			},async (error)=>{
				const res = await error
				errorGlobal(res.message)
			}))
		}else if (data.state === 'APROBADA'){
			dispatch(fulfilled(id,formData,async (response)=>{
				const res = await response
				success(res.message)
				searchForInformation()
			},async (error)=>{
				const res = await error
				errorGlobal(res.message)
			}))
		}
	}
	
	const Actions = (record) => {
		let state = record?.state === 'TRAMITADA'
		let statute = false
		if (record?.state === 'TRAMITADA' || record?.state === 'DECLINADA')
		{
			statute = true
		}
		return (
			<>
				<Button disabled={statute} onClick={()=> onEdit(record.id,record)}  size="small" type="primary" icon={<SaveOutlined />}/>
				<Button disabled={state} onClick={()=> onDelete(record.id)}  size="small" type="primary" danger icon={<DeleteOutlined />}/>
			</>
		)
	}
	const buttons = (name,state) => {
		return (
			<Button
				type="primary"
				icon={<PlusOutlined />}
				size={"large"}
				onClick={()=> setNuevo(!nuevo)}
				danger={state}
			>
				{name}
			</Button>
		)
	}
	const NuevoSet = () => {
		setNuevo(false)
	}
	const findIndex = (name) => {
		let indies = null
		if (chek.length > 0){
			chek.find((element,index) => element.name === name ? indies = index : null)
		}
		return indies
	}
	const setCheckes = (id,estate) => {
		let checklist = []
		let indes = null
		let name = 'finish'+id
		if (chek.length > 0){
			checklist = chek
			indes = findIndex(name)
		}
		if (indes !== null ){
			checklist[indes] = estate
		}else {
			checklist.push({
				name: name,
				state: estate
			})
		}
		setChek(checklist)
	}
	columns.push(
		{
			title: 'Postventa Cumplida',
			dataIndex: 'afterSalesAccomplished',
			align: "center",
			key: 'afterSalesAccomplished',
			render: (_, record) => <AfterSalesAccomplished setCheckes={setCheckes} record={record} />
		}, {
			title: 'Estado',
			width: 15,
			dataIndex: 'state',
			align: "center",
			key: 'state',
		}, {
			title: '',
			width: 50,
			dataIndex: 'action',
			align: "center",
			key: 'action',
			render: (_, record) => Actions(record)
		}
	)
	return<>
		{
			nuevo ? (
				<>
					<PostSaleAddForm 
						searchForInformation={searchForInformation}
						buttons={buttons}
						sale_id={sale_id}
						NuevoSet={NuevoSet}
					/>
				</>
			) : buttons("Solicitudes",false)
		}
		<Form
			form={forms}
			name={"EditarPostVentas"}
		>
			<Table
				columns={columns}
				dataSource={data}
				width={"100%"}
			/>
		</Form>
	</>
}
export default InteractiveTable