import React, { useState} from "react";
import "./assets/style.css";
import {useDispatch} from "react-redux";
import {useParams} from "react-router-dom";
import {UploadOutlined} from "@ant-design/icons";
import {Modal, Form, InputNumber, Button, Upload, Col, Row} from "antd";
import {creatUnitType, fetchAllUnitType} from "../../../../../../../redux/actions/Project/UnitType";
import {errorGlobal, success} from "../../../../../../handle/Notification/Notification";
import {getUnitByIdProject} from "../../../../../../../redux/actions/Project/UnitsAction";
import {FORMATTER_INPUT_NUMBER, RUTA_API_MULTIMEDIA} from "../../../../../../../redux/constants";
import FormFileLabel from "react-bootstrap/FormFileLabel";
import {onPreviewHelper} from "../../../../../../handle/Multimedia/Multimedia";

const ModalUnitType = ({bottonstate}) => {
	
	const onChange = async (value) => {
		let imageListArray = [];
		let imageList = value.fileList;
		for (let index = 0; index < imageList.length; index++) {
			let base64 = imageList[index].originFileObj;
			imageListArray.push(base64);
		}
		setImages(imageListArray);
	};
	const onChangeBlueprints = async (value) => {
		let blueprintsListArray = [];
		let blueprintsList = value.fileList;
		for (let index = 0; index < blueprintsList.length; index++) {
			let base64BlueprintsList = blueprintsList[index].originFileObj;
			blueprintsListArray.push(base64BlueprintsList);
		}
		setBlueprints(blueprintsListArray);
	};

	const {id} = useParams();
	const dispatch = useDispatch();

	const [form] = Form.useForm();
	const [area, setM2] = useState(null);
	const [finish_kit_value, setFinishKitValue] = useState(null);
	const [area_value, setValorM2] = useState(null);
	const [units_number, setQuantity] = useState(null);
	const [number_rooms, setRoomsNumber] = useState(null);
	const [value_other_additional, setTerraceValueUsefulRoomOthers] = useState(null);
	const [number_levels, setNumberLevels] = useState(null);
	const [income_level, setIncomeLevels] = useState(null);
	const [bathroom_number, setBathroomNumber] = useState(null);
	const [images, setImages] = useState([]);
	const [blueprints, setBlueprints] = useState([]);

	const [isModalVisible, setIsModalVisible] = useState(false);

	const showModal = () => {
		setIsModalVisible(true);
	};
	const onReset = () => {
		form.resetFields();
	};
	const handleCancel = () => {
		setIsModalVisible(false);
	};
	const onFinish = () => {
		const data = {
			project_id: id,
			area,
			finish_kit_value,
			area_value,
			units_number,
			number_rooms,
			value_other_additional,
			number_levels,
			income_level,
			bathroom_number,
			images,
			blueprints,
		};

		let fData = new FormData();

		fData.append("project_id", id);
		fData.append("area", area);
		fData.append("finish_kit_value", !finish_kit_value ? 0 : finish_kit_value);
		fData.append("area_value", !area_value ? 0 : area_value);
		fData.append("units_number", !units_number ? 0 : units_number);
		fData.append("number_rooms", !number_rooms ? 0 : number_rooms);
		fData.append(
			"value_other_additional",
			!value_other_additional ? 0 : value_other_additional
		);
		fData.append("number_levels", !number_levels ? 0 : number_levels);
		fData.append("income_level", !income_level ? 0 : income_level);
		fData.append("bathroom_number", !bathroom_number ? 0 : bathroom_number);

		for (let j = 0; j < images.length; j++) {
			fData.append(`images[]`, data.images[j], data.images[j].name);
		}

		for (let i = 0; i < blueprints.length; i++) {
			fData.append(`blueprints[]`, blueprints[i], blueprints[i].name);
		}

		dispatch(
			creatUnitType(fData, async (response) => {
				const res = await response;
				if (res.error) {
					if (res.message === "Error number of units exceeded") {
						errorGlobal("¡UPS! Número de unidades excedido.");
					} else {
						errorGlobal("¡UPS! Ha ocurrido un error");
					}
				} else {
					success("Ha sido agregado con éxito");
					handleCancel();
					onReset();
					
				}
				finish();
			})
		);
	};
	
	const finish = () =>{
		dispatch(fetchAllUnitType(id));
		dispatch(getUnitByIdProject(id));
	}
	
	const onFinishFailed = (errorInfo) => {
		console.error("Failed:", errorInfo);
	};

	const [fileList] = useState([]);

	return (
		<>

			<Button
				type="default"
				onClick={finish}
				style={{marginBottom: "20px", marginTop: "10px", right : "6%"}}
			>
				Actualizar
			</Button>
				
			<Button
				type="primary"
				style={{marginBottom: "20px", marginTop: "10px",right : "5%"}}
				onClick={showModal}
				disabled={!bottonstate}
			>
				Agregar Tipo Unidad
			</Button>

			
			
			<Modal
				title="Agregar Tipo de Unidad"
				visible={isModalVisible}
				onCancel={handleCancel}
				width={700}
				footer={false}

			>
				<Form
					layout="vertical"
					name="basic"
					onFinish={onFinish}
					onFinishFailed={onFinishFailed}
					form={form}
				>
					<Row>
						<Col span={8}>
							<Form.Item
								label="M2"
								name="area"
								rules={[
									{
										required: true,
										message: "Campo requerido",
									},
								]}
							>
								<InputNumber
									min={0}
									onChange={(value) => setM2(value)}
									value={area}
									style={{width: "85%"}}
								/>
							</Form.Item>

							<Form.Item label="No. Baños " name="bathroom_number">
								<InputNumber
									min={0}
									onChange={(value) => setBathroomNumber(value)}
									value={bathroom_number}
									style={{width: "85%"}}
								/>
							</Form.Item>

							<Form.Item
								label="Valor terraza/ cuarto útil/ otros"
								name="value_other_additional"
							>
								<InputNumber
									min={0}
									{...FORMATTER_INPUT_NUMBER}
									onChange={(value) => setTerraceValueUsefulRoomOthers(value)}
									value={value_other_additional}
									style={{width: "85%"}}
								/>
							</Form.Item>


						</Col>
						<Col span={8}>

							<Form.Item
								label="Cantidad"
								name="units_number"
								rules={[
									{
										required: true,
										message: "Campo requerido",
									},
								]}
							>
								<InputNumber
									min={0}
									onChange={(value) => setQuantity(value)}
									value={units_number}
									style={{width: "85%"}}
								/>
							</Form.Item>

							<Form.Item label="No. Niveles" name="income_level">
								<InputNumber
									min={0}
									onChange={(value) => setNumberLevels(value)}
									value={income_level}
									style={{width: "85%"}}
								/>
							</Form.Item>

							<Form.Item label="Nivel de Ingresos" name="number_levels">
								<InputNumber
									min={0}
									{...FORMATTER_INPUT_NUMBER}
									onChange={(value) => setIncomeLevels(value)}
									value={number_levels}
									style={{width: "85%"}}
								/>
							</Form.Item>

						</Col>
						<Col span={8}>


							<Form.Item
								label="No. Habitaciones"
								name="number_rooms"
							>
								<InputNumber
									min={0}
									onChange={(value) => setRoomsNumber(value)}
									value={number_rooms}
									style={{width: "85%"}}
								/>
							</Form.Item>

							<Form.Item label="Valor Kit de Acabados" name="finish_kit_value">
								<InputNumber
									min={0}
									{...FORMATTER_INPUT_NUMBER}
									onChange={(value) => setFinishKitValue(value)}
									value={finish_kit_value}
									style={{width: "85%"}}
								/>
							</Form.Item>


							<Form.Item label="Valor m2 (Prioritario)" name="area_value">
								<InputNumber
									min={0}
									{...FORMATTER_INPUT_NUMBER}
									onChange={(value) => setValorM2(value)}
									value={area_value}
									style={{width: "85%"}}
								/>
							</Form.Item>


						</Col>
					</Row>
					<Row>
						<Col span={11}>
							<Form.Item
								label="Planos"
								name="blueprints"
							>
								<Upload
									action={RUTA_API_MULTIMEDIA}
									listType="picture"
									defaultFileList={[...fileList]}
									onChange={onChangeBlueprints}
									onPreview={onPreviewHelper}
									value={blueprints}
									className="upload-list-inline"
								>
									<Button icon={<UploadOutlined/>}>Upload</Button>
								</Upload>
							</Form.Item>
						</Col>
						<Col span={2}/>
						<Col span={11}>
							<Form.Item
								name="images"
								style={{width: "90%"}}
							>
								<Upload
									action={RUTA_API_MULTIMEDIA}
									listType="picture"
									defaultFileList={[...fileList]}
									onChange={onChange}
									onPreview={onPreviewHelper}
									accept="image/*"
									value={images}
									multiple={true}
								>
									<FormFileLabel style={{marginLeft: "100%"}}>Imágenes</FormFileLabel>
									<Button style={{marginLeft: "78%"}} icon={<UploadOutlined/>}>Upload</Button>
								</Upload>
							</Form.Item>
						</Col>
					</Row>
					<Row>
						<Col span={6}/>
						<Col span={6}/>
						<Col span={6}/>
						<Col span={6}>
							<Form.Item label=" ">
								<Button
									type="primary"
									htmlType="submit"
								>
									Guardar
								</Button>
							</Form.Item>
						</Col>
					</Row>
				</Form>
			</Modal>
		</>
	);
};

export default ModalUnitType;
