import React, { useEffect, useState } from "react";
import { fetchAllProjects } from "../../../../redux/actions/Project/ProjectsAction";
import {  useDispatch, useSelector } from "react-redux";
import { Card, Col, Row, Form, Select, Button, Table, Popover } from "antd";
import { validateModule } from "../../../handle/PermissionMethods/PermissionMethods";
import * as constants from "../../../../redux/constants";
import { Redirect } from "react-router-dom/cjs/react-router-dom.min";
import { convertAllMapDataState } from "../../../handle/HandleFilterTable/handleFilterTable";
import { fetchInfoReportSalesPlan, fetchPathExcelReportSaleplan, fetchPathPdfReportSaleplan,fetchListPLanSales } from "../../../../redux/actions/Report/Report_sales_plan/reportSalesPlanAction";
import { getColumnsTable } from "./Components/columSalesPlan";
import { CreateSummary } from "./Components/summaryCellSalesPlan"
import { errorGlobal } from "../../../handle/Notification/Notification";
import ModalSavePlanReport from "./Components/ModalSavePlanReport";
import ModalListPlanSales from "./Components/modalListPlanSales";
import { DownloadDocument } from "../../../handle/Multimedia/DownloadDocument";
import Spinner from "../../../handle/Spinner/Spinner";


const SalesPlan = () => {
	const dispatch = useDispatch();

	const selectProject = useSelector((state) => state?.project);

	const [idProject, setIdProject] = useState(null);
	const [generateTo, setGenerateTo] = useState(null);
	const [infoSalePlan, setInfoSalePlan] = useState(null);
	const [planSalesList, setPlanSalesList] = useState([]);
	const [consultind, setConsulting] = useState( false )
	
	let data = {
		"project_id": idProject,
		"generate_to": generateTo,
	}

	useEffect(() => { dispatch(fetchAllProjects()) }, []);

	let filterPlan = [
		{ id: 1, name: "Administrador" },
		{ id: 2, name: "Asesor" },
	];
	
	const exportExcel= () =>{
		dispatch(fetchPathExcelReportSaleplan(data, async (response) => {
			let res = await response;
			if (res.code === 200) {
				DownloadDocument(res._payload.path);
			}else {
				errorGlobal(res.message);
			}
		}));
	}

	const exportPdf = () => {
		
		dispatch(fetchPathPdfReportSaleplan(data, async (response) => {
			const res = await response;
			if (res.code === 200) {
				const tagA = document.createElement("a");
				tagA.href = res._payload.path;
				tagA.target = "_blank";
				tagA.click();
			}
			else {
				errorGlobal(res.message);
			}
		}));	
	}
	const onFinish = () => {
		setConsulting(true);
		setInfoSalePlan(null)
		dispatch(fetchListPLanSales(data,(resp)=>{
            if(!resp.error){
                setPlanSalesList(resp._payload);
            }
        }));
		dispatch(fetchInfoReportSalesPlan(data, (resp) => {
			if (!resp.error) {
				setInfoSalePlan(resp._payload);	
			}else {
				errorGlobal(resp.message);
			}setConsulting(false)
		}));
	};
	const validateMessages = {
		required: "${label} es requerido",
		types: {
			email: "${label} no es un correo válido",
			number: "${label} debe ser un número",
		},
	};
	const content = (
		<div>
			<a style={{ marginRight: 21 }} onClick={exportExcel}>
				Excel
			</a>
			<a  onClick={exportPdf}>PDF</a>
		</div>
	);
	const elementProject = [];

	if (!validateModule(constants.SALE_PLAN_SUBMODULE)) {
		return <Redirect to={constants.DEFAULT_ROUTE} />;
	}
	return (
		<Card title="Plan de Ventas">
			<Form
				layout="vertical"
				onFinish={onFinish}
				validateMessages={validateMessages}
			>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item
							label="Proyecto:"
							name="Proyecto"
							rules={[
								{
									required: true,
								},
							]}
						>
							<Select
								defaultValue={""}
								showSearch
								style={{ width: "100%" }}
								optionFilterProp="children"
								onChange={setIdProject}
								filterOption={(input, option) =>
									option.children.toLowerCase().indexOf(input.toLowerCase()) >=
									0
								}
							>
								<Select.Option value="">Seleccione...</Select.Option>
								{
									!Array.isArray(selectProject) && selectProject._payload?.map(item => (
										<Select.Option key={item?.id + item?.name + item?.id} value={item.id}>{` ${item.name} `}</Select.Option>

									))

								}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item label="Generar para:" name="Generar para">
							<Select onChange={setGenerateTo} defaultValue="Seleccionar">
								<Select.Option value="">Seleccione...</Select.Option>
								{filterPlan.map((item) => (
									<Select.Option key={item?.id + item?.name + item?.id} value={item.id}>{` ${item.name} `}</Select.Option>
								))}
							</Select>
						</Form.Item>
					</Col>
				</Row>
				<Row>
					<Col xs={18} sm={20} md={21} lg={22} xl={22} />
					<Col xs={2} sm={2} md={2} lg={2} xl={2}>
						<Form.Item>
							<Button type="primary" htmlType="submit">
								Buscar
							</Button>
						</Form.Item>
					</Col>
				</Row>
			</Form>
			{infoSalePlan !== null ? (
				<Row>
					<Form.Item>
						<Popover content={content} trigger="click">
							<Button
								style={{ backgroundColor: "#5cb85c", borderColor: "#5cb85c" }}
								type="primary"
							>
								Exportar
							</Button>
						</Popover>
						{" "}
						<ModalSavePlanReport data={data} />{" "}
						<ModalListPlanSales	 planSalesList={planSalesList}/>

					</Form.Item>
					<Table
						columns={getColumnsTable(infoSalePlan)}
						dataSource={convertAllMapDataState(infoSalePlan?.cell_information || elementProject)}
						scroll={{ x: 2500, y: 300 }}
						size="small"
						bordered={true}
						summary={pageData => CreateSummary(infoSalePlan?.sale_information)}
					/>
				</Row>
			) : (
				consultind?<Spinner/>:null
				
			)}
		</Card>
	);
};

export default SalesPlan;	
