import * as constants from '../../constants';

export default function paymentRelationshipSetting(state = [], action) {
    switch (action.type) {
        case constants.SET_ALL_PAYMENT_RELATIONSHIP_SETTING:
            return action.payload;
        case constants.ADD_PAYMENT_RELATIONSHIP_SETTING:
            return state;
        case constants.RESET_PAYMENT_RELATIONSHIP_SETTING:
            return action.payload;
        default:
            return state;
    }
}