import * as constants from '../../constants';
import LocalStorage from '../../../views/config/LocalStorage'

let Storage = new LocalStorage();


export const fetchAllDiscountPolicy = (id) => ({
    type: constants.API,
    payload: {
        method: 'GET',
	    headers: { 'access-token':Storage.getToken() },
        url: `${constants.RUTA}/discountPolicy/${id}`,
        success: (response) => (setAllDiscountPolicy(response))
    }
});

export const updateOrCreateDiscountPolicyById = (data, onSuccess, onError) => ({
    type: constants.API,
    payload: {
        method: 'PUT',
        url: `${constants.RUTA}/discountPolicy`,
        data,
        success: (data) => (updateOrCreateDiscountPolicy(data)),  
        postProcessSuccess: onSuccess,
        postProcessError: onError           
    }
});

export const deleteDiscountPolicyById = ( id, onSuccess, onError) => ({
    type: constants.API,
    payload: {
        method: 'DELETE',
        url: `${constants.RUTA}/discountPolicy/${id}`,
        success: () => (removeDiscountPolicy(id)),
        postProcessSuccess: onSuccess,
        postProcessError: onError
    }
});

export const resetDiscountPolicyAction = () => ({
    type: constants.API,
    payload: {
		method: null,
		success: () => (resetDiscountPolicy())
	}
})

const resetDiscountPolicy = () =>({
	type:constants.RESET_DISCOUNT_POLICY,
	payload: []
});

const setAllDiscountPolicy = (data) => ({
    type: constants.SET_ALL_DISCOUNT_POLICY,
    payload: data
});

const updateOrCreateDiscountPolicy = ( id, data) => ({
    type: constants.UPDATE_OR_CREATE_DISCOUNT_POLICY,
    payload: { id, data }
});

const removeDiscountPolicy = ( id) => ({
    type: constants.REMOVE_DISCOUNT_POLICY,
    payload:  id
});