import React from "react"
import {Button, Col, Descriptions, Form, Input, Row, Select} from "antd";

const CustomerInterest = () => {
	return(
		<>
			<Row gutter={16}>
				<Col xs={24} sm={24} md={24} lg={24} xl={24}>
					<Descriptions title={"Interes del Cliente"} />
					<Form.Item
						name="interestLevel"
						label="Nivel de Interes"
						rules={[{required: true}]}
					>
						<Select
							placeholder="Nivel de Interés"
						>
							<Select.Option value="">Seleccione...</Select.Option>
							<Select.Option value="1">Bajo</Select.Option>
							<Select.Option value="2">Medio</Select.Option>
							<Select.Option value="3">Alto</Select.Option>
							<Select.Option value="4">Cliente Actual</Select.Option>
						</Select>
					</Form.Item>
				</Col>
			</Row>
			<Row>
				<Col span={24}>
					<Form.Item
						name="interests"
						label="Qué estas buscando?"
						rules={[{required: true}]}
					>
						<Input.TextArea
							placeholder="Ingrese que esta Buscando"
						/>
					</Form.Item>
				</Col>
			</Row>
			<Row span={24}>
				<Button
					style={{marginLeft: "85%"}}
					type="primary"
					htmlType="submit"
				>
					Guardar
				</Button>
			</Row>
		</>
	)
}

export default CustomerInterest