import * as constants from "../../constants";

export default function unit(state = [], action) {
	switch (action.type) {
		case constants.GET_UNIT_BY_PROJECT_ID:
			return action.payload;
		case constants.UPDATE_UNIT_BY_PROJECT_ID:
			return state._payload.map((item) => {
				if (item._id === action._payload.id)
					return { ...item, ...action.payload.data };
				else return item;
			});
			case constants.RESET_UNIT_BY_PROJECT_ID:
				return action.payload;
		default:
			return state;
	}
}
