import React from 'react'
import { Layout } from "antd";

const { Footer } = Layout;

const FooterComponent = () => {
    let today= new Date(Date.now());

    return (
        <Footer style={
        { 
            textAlign: "center", 
            color: '#fff', 
            backgroundColor: '#212529',
            width: "100%",
            height: 48,
            position: 'fixed',
            bottom: 0,
            zIndex:99,
        }
        }>
           © 2014-{today.getFullYear().toString()} GPI Constructores. Todos los derechos reservados.
        </Footer>
    );
    
}
export default FooterComponent;