import FormDesistSale from "./FormDesistSale";
import { CloseOutlined } from "@ant-design/icons";
import React, { useEffect, useState } from "react";
import { Button, Tooltip, Modal, Form } from "antd";
import { useDispatch, useSelector } from "react-redux";
import Spinner from "../../../../../handle/Spinner/Spinner";
import { fetchAllSaleMade } from "../../../../../../redux/actions/Price/SaleMade/SalesMadeAction";
import { errorGlobal,success } from "../../../../../handle/Notification/Notification";
import { createDesistSaleAction,fecthDesistSaleAction,updateDesistSaleAction } from "../../../../../../redux/actions/Price/SaleMade/desistSaleAction";

const ModalDesistSale = ({ data }) => {
	const dispatch = useDispatch();
	const [fetching, setFetching] = useState(false);
	const [isModalVisible, setIsModalVisible] = useState(false);
	const [documentToSend, setDocumentToSend] = useState([]);
	const [form] = Form.useForm();

	useEffect(() => {
		if (isModalVisible) {
			setFetching(false);
			dispatch(
				fecthDesistSaleAction(data.id, async (response) => {
					let res = await response;
					if (res.error) {
						errorGlobal("¡UPS! Ha ocurrido un problema.");
					} else {
						setFetching(true);
					}
				})
			);
		}
	}, [isModalVisible]);

	const desistSaleDatabase = useSelector((state) => state.desistSale);

	const showModal = () => setIsModalVisible(true);

	const onCancel = () => setIsModalVisible(false);

	const onReset = () => {
		form.resetFields();
	};

	const handleSubmit = async () => {
		let values = await form.validateFields();
		let fDto = new FormData();

		fDto.append("sale_id", data.id);
		fDto.append(
			"withdrawal_date",
			values["withdrawalDate"].format("YYYY-MM-DD")
		);
		fDto.append("value_to_pay_to_project", values["valuePaidProject"]);
		fDto.append(
			"amount_paid_associated_company",
			values["amountPayAssociatedCompany"]
		);
		fDto.append("motive", values["reason"]);
		fDto.append("fine_value", values["fineValue"]);

		updateOrCreateDesistSale(fDto);
	};

	const updateOrCreateDesistSale = (fDto) => {
		if (!Array.isArray(desistSaleDatabase)) {
			if (documentToSend) {
				if (documentToSend.hasOwnProperty("path")) {
					fDto.append("document", documentToSend.id);
				} else {
					fDto.append("document", documentToSend);
				}
			}

			dispatch(
				updateDesistSaleAction(fDto, async (response) => {
					let res = await response;
					if (res.error) {
						errorGlobal("¡UPS! Ha ocurrido un error.");
					} else {
						success("Ha sido actualizado con éxito");
						onCancel();
						onReset();
					}
				})
			);
		} else {
			fDto.append("document", documentToSend ? documentToSend : null);
			dispatch(
				createDesistSaleAction(fDto, async (response) => {
					let res = await response;

					if (res.error) {
						errorGlobal("¡UPS! Ha ocurrido un error.");
					} else {
						success("Ha sido actualizado con éxito");
						dispatch(fetchAllSaleMade());
						onCancel();
						onReset();
					}
				})
			);
		}
	};

	const setDocumentNameField = (info) => {
		form.setFieldsValue({
			documentName: info.file.name,
		});
	};

	const textDesist = <span style={{ color: "#6A6963" }}>Desistir</span>;

	return (
		<>
			<Tooltip placement="left" color="#ffffff" title={textDesist}>
				<Button
					onClick={showModal}
					icon={<CloseOutlined />}
					type="primary"
					size="small"
					danger
				/>
			</Tooltip>
			{isModalVisible && (
				<Modal
					title={`Desistimiento - Venta No. ${data.unit_code}`}
					centered
					visible={isModalVisible}
					footer={
						<Tooltip>
							<Button type="primary" onClick={handleSubmit}>
								Guardar
							</Button>
						</Tooltip>
					}
					onCancel={onCancel}
					width={1000}
				>
					{fetching ? (
						<FormDesistSale
							form={form}
							handleSubmit={handleSubmit}
							setDocumentNameField={setDocumentNameField}
							data={data}
							desistSaleDatabase={desistSaleDatabase}
							setDocumentToSend={setDocumentToSend}
						/>
					) : (
						<Spinner />
					)}
				</Modal>
			)}
		</>
	);
};
export default ModalDesistSale;
