import React, {useEffect, useState} from "react"
import {Card, Form} from "antd";
import SearchForm from "./Components/SearchForm";
import {useDispatch} from "react-redux";
import {fetchAllAssessor} from "../../../../redux/actions/Configuration/AssesorAction";
import {errorGlobal, success, warning} from "../../../handle/Notification/Notification";
import Content from "./Components/Content";
import {GenerateEXCEL, GeneratePDF, getAllPotentialClientsReport} from "../../../../redux/actions/Report/potentialClientsReportAction";
import {DownloadDocument} from "../../../handle/Multimedia/DownloadDocument";
import Spinner from "../../../handle/Spinner/Spinner";
const validateMessages = {
	required: '${label} es requerido',
	types: {
		email: '${label} no es un correo válido',
		number: '${label} debe ser un número'

	}
}
const PotentialClientsReport = () => {
	const dispatch = useDispatch();
	const [form] = Form.useForm();
	const [tablaVisibility, setTablaVisibility] = useState(null);
	const [data,setData]= useState([])
	
	useEffect(() => {
		dispatch(fetchAllAssessor(async (response)=>{
			const res = await response
			if (res) {
				success("Carga Completa");
			}else {
				warning('No hay Datos en la tabla')
			}
		},async (error)=>{
			const err = await error
			errorGlobal(err.message);
		}))
	}, []);
	
	const excelGenerator = () => {
		dispatch(GenerateEXCEL(data,async (response)=>{
			await Success(response)
		},async (error)=>{
			await Error(error)
		}))
	}
	
	const Success = async (response) => {
		const res = await response
		DownloadDocument(res._payload)
	}
	
	const Error = async (error) => {
		const err = await error
		errorGlobal(err.message)
	}
	
	const PDFGenerator = () => {
		dispatch(GeneratePDF(data,async (response) => {
			await Success(response)
		},async (error)=>{
			await Error(error)
		}))
	}
	
	const onFinish = async () => {
		setTablaVisibility(false)
		const values = await form.validateFields();
		dispatch(getAllPotentialClientsReport(values,async (response)=>{
			const res = await response;
			success(res.message)
			setTablaVisibility(true)
			setData(values)
		},async (error) =>{
			await Error(error)
			setData([])
			setTablaVisibility(null)
		}))
	}
	
	return <>
		<Card title={"Informe de Clientes Potenciales"}>
			<Form 
				layout="vertical" 
				form={form} 
				onFinish={onFinish} 
				validateMessages={validateMessages}>
				<SearchForm/>
			</Form>
			{
				tablaVisibility ? <Content PDFGenerator={PDFGenerator} excelGenerator={excelGenerator} /> : tablaVisibility==null? null:<Spinner/>
			}
		</Card>
	</>
}

export default PotentialClientsReport