import React, { useEffect } from "react";
import { Button, Select, Form } from "antd";
import { DeleteOutlined } from "@ant-design/icons";
import {  useSelector } from "react-redux";

const ClientItem = ({ data, index, handleDelete, form, getDataToChild }) => {
	useEffect(() => {
		fillFields();
	}, []);

	const selectCustomer = useSelector((state) => state.customer);

	const fillFields = () => {
		form.setFieldsValue({
			[`responsible${index + 1}`]: data.customer_id,
		});
	};

	const sendDataToFather = (value) => {
		data.customer_id = value;
		getDataToChild(data);
	};

	const itemDelete = () => {
		handleDelete(data.id);
	};


	return (
		<>
			<h6 style={{ fontSize: 14, marginBottom: "3px", marginLeft: "20px" }}>
				Cliente Responsable
			</h6>
			<div style={{ display: "flex", flexFlow: "row" }}>
				<article
					style={{
						width: "10%",
						textAlign: "center",
						border: "1px solid #d9d9d9",
						height: "32px",
					}}
				>
					{index + 1}
				</article>
				<Form.Item
					name={`responsible${index + 1}`}
					rules={[{ required: true, message: "El campo es requerido" }]}
					style={{ width: "90%" }}
				>
					<Select
						value={data.customer_id}
						defaultValue={""}
						showSearch
						style={{ width: "90%" }}
						optionFilterProp="children"
						onChange={sendDataToFather}
						filterOption={(input, option) =>
							option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
						}
					>
						<Select.Option value="">Seleccione...</Select.Option>
						{selectCustomer._payload !== undefined
							? !Array.isArray(selectCustomer) &&
							  selectCustomer._payload.map((item) => (
									<Select.Option
										key={item.id}
										value={item.id}
									>{` ${item.name} - CC: ${item.identification}`}</Select.Option>
							  ))
							: null}
					</Select>
				</Form.Item>

				<Button
					icon={<DeleteOutlined />}
					type="primary"
					danger
					onClick={itemDelete}
				/>
			</div>
		</>
	);
};

export default ClientItem;
