import { Button, message, Table, Upload } from "antd";
import React, { useEffect, useState } from "react";
import { ArrowDownOutlined, UploadOutlined } from "@ant-design/icons";
import { useDispatch } from "react-redux";
import {
	setAllLegalizeDocumentAction,
	uploadLegalizeDocumentAction,
} from "../../../../../../redux/actions/Price/SaleMade/legalizeDocumentAction";
import {
	errorGlobal,
	success,
} from "../../../../../handle/Notification/Notification";
import {convertAllMapDataState} from "../../../../../handle/HandleFilterTable/handleFilterTable";

const TableLegalizeDocuments = ({ data, idSale, setDataSource }) => {
	const [file, setFile] = useState({});
	const dispatch = useDispatch();

	useEffect(() => {
		if (!isObjEmpty(file) && file != undefined) {
			const arrayToWork = [...data];
			const { record } = file;

			arrayToWork.forEach((item) => {
				if (item.id === record.id) {
					file.name = record.FileName;
					item.file = file.originFileObj;
					let fDto = new FormData();
					fDto.append("sales_legalization_documents_id", record.id);
					fDto.append("user_id", (JSON.parse(localStorage.getItem('USER_INFO'))).id);
					fDto.append("document", item.file);
					dispatch(
						uploadLegalizeDocumentAction(idSale, fDto, async (response) => {
							let res = await response;
							if (res.error) {
								errorGlobal("¡UPS, Ha ocurrido un error.");
							} else {
								success("Documento subido con éxito.");
								dispatch(
									setAllLegalizeDocumentAction(idSale, async (response) => {
										let res = await response;
										if (res.error) {
											errorGlobal("¡UPS, Ha ocurrido un error.");
										} else {
											setDataSource(res);
										}
									})
								);
							}
						})
					);
				}
			});
			setFile({});
		}
	}, [file]);

	const isObjEmpty = (obj) => {
		return Object.keys(obj).length === 0;
	};
	const props = {
		name: "file",
		headers: {
			authorization: "authorization-text",
		},
		beforeUpload: (file) => {
			return false;
		},
		showUploadList: false,
		progress: {
			strokeColor: {
				"0%": "#108ee9",
				"100%": "#87d068",
			},
			strokeWidth: 3,
			format: (percent) => `${parseFloat(percent.toFixed(2))}%`,
			showInfo: true,
		},
	};

	const onChange = (info, record) => {
		if (info.file.status !== "uploading") {
			for (let index = 0; index < info.fileList.length; index++) {
				var element = info.fileList[index];
				element.record = record;
			}

			var newElement = element;

			setFile(newElement);
		}
	};

	const download = (record) => {
		const tagA = document.createElement("a");
		tagA.href = `${record.file.path}`;
		tagA.target = "_blank";
		document.body.appendChild(tagA);
		tagA.click();
	};

	const columns = [
		{
			title: "#",
			width: 50,
			dataIndex: "id",
			key: "id",
			render: (_, record, index) => (
				<>
					{" "}
					<h6>{index + 1}</h6>{" "}
				</>
			),
		},
		{
			title: <strong>{"Etiqueta"}</strong>,
			width: 200,
			dataIndex: "label",
			key: "label",
		},
		{
			title: <strong>{"Nombre del  archivo"}</strong>,
			width: 220,
			dataIndex: "fileName",
			key: "fileName",
			render: (_, record) => (
				<>
					{record.file ? (
						<h6 style={{ fontSize: "14px" }}>{record.fileName}</h6>
					) : (
						<h6 style={{ fontSize: "14px" }}>No se ha adjuntado archivo..</h6>
					)}
				</>
			),
		},
		{
			dataIndex: "ActionsSelectFile",
			width: 160,
			render: (_, record) => (
				<Upload
					name="cargarCodigosFiduciarios"
					onChange={(info) => {
						onChange(info, record);
					}}
					{...props}
				>
					<Button
						type="primary"
						disabled={record.file}
						icon={<UploadOutlined />}
					>
						Elegir Archivo
					</Button>
				</Upload>
			),
			fixed: "right",
		},
		{
			dataIndex: "ActionsDownload",
			width: 160,
			fixed: "right",
			render: (_, record) => (
				<>
					<Button
						disabled={!record.file}
						style={{ background: "#389e0d", color: "#fff" }}
						icon={<ArrowDownOutlined />}
						onClick={() => download(record)}
					>
						Descargar
					</Button>
				</>
			),
		},
	];

	return (
		<Table
			size={"small"}
			scroll={{ x: 900, y: 400 }}
			bordered={true}
			columns={columns}
			dataSource={convertAllMapDataState(data) || []}
			pagination={false}
		/>
	);
};

export default TableLegalizeDocuments;
