import * as constants from './../../constants';
import LocalStorage from '../../../views/config/LocalStorage';

let Storage = new LocalStorage();

export const getAllProjectStates = (onSuccess) => ({
	type: constants.API,
	payload: {
		method: 'GET',
		headers: {'access-token': Storage.getToken()},
		url: `${constants.RUTA}/projectStates`,
		success: (response) => (setAllProjectStates(response)),
		postProcessSuccess: onSuccess,
	}
});

const setAllProjectStates = (data) => ({
	type: constants.SET_STATES_PROJECTS,
	payload: data
});
