import * as constants from "../../../constants";

export const fetchGeneralCommercialManagement = (data, postProcessSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		url: `${constants.RUTA}/report/commercial_management/${data}`,
		success: (response) => (getGeneralCommercialManagementReport(response)),
		postProcessSuccess: postProcessSuccess,
		postProcessError: onError
	}
});

const getGeneralCommercialManagementReport = (data) => ({
	type: constants.GET_GENERAL_COMMERCIAL_MANAGEMENT_REPORT,
	payload: data
});

