import { getStateUnit } from "../../../../handle/StateUnit/StateUnit";
import { formatterMoney } from "../../../../handle/FormatterMoney/FormatterMoney";

export const getColumnsTable = (infoSalePlan) => {
  return [
    {
      title: "Unidad",
      width: 20,
      align: "center",
      dataIndex: "area",
      key: "area",
    },
    {
      title: "N° de No. Piso",
      width: 20,
      align: "center",
      dataIndex: "number_floors",
      key: "number_floors",
    },
    {
      title: "No. de Unidad",
      width: 20,
      align: "center",
      dataIndex: "label",
      key: "label",
    },
    {
      title: "# Encargo Fiduciario",
      width: 40,
      align: "center",
      dataIndex: "fiduciary_code",
      key: "",
    },
    {
      title: "Área Unidad",
      width: 20,
      align: "center",
      dataIndex: "area",
      key: "area",
    },

    {
      title: "Vr. / mt2",
      width: 20,
      align: "center",
      render: (_, record) => {
        return `${formatterMoney.format(record.square_meter_value)} `;
      }
    },

    {
      title: "Vr.",
      width: 20,
      align: "center",
      render: (_, record) => {
        return `${formatterMoney.format(record.unit_net_value)} `;
      }

    },

    {
      title: "Cantidad y Parqueadero",
      width: 20,
      align: "center",
      render: (_, record) => {
        return `(${record.parking_number}) ${record.parking}\n ${record.parking_location}`;
      }
    },

    {
      title: "Vr. Parqueadero + Valor terraza / Cuarto útil / otros",
      width: 40,
      align: "center",
      render: (_, record) => {
        return `${formatterMoney.format(record.value_parking)} + \n ${formatterMoney.format(record.value_other_additional)}`;
      }
    },

    {
      title: "Kit de acabados + Prima Vista",
      width: 30,
      align: "center",
      render: (_, record) => {
        return `${formatterMoney.format(record.finish_kit_value)} + \n ${formatterMoney.format(record.bonus_view)}`;
      }
    },

    {
      title: "Vr. Total Unidad",
      width: 40,
      align: "center",
      render: (_, record) => {
        return `${formatterMoney.format(record.unit_value)}`;
      }
    },

    {
      title: "SEPARACIÓN",
      width: 25,
      align: "center",
      key: "separation_value",
      render: (_, record) => {
        return `${formatterMoney.format(record.separation_value)}`;
      }
    },

    {
      title: `C.I. no Financiada (${infoSalePlan?.sale_information.unfunded_percentage_initial_fee})`,
      width: 30,
      align: "center",
      render: (_, record) => {
        return ` ${formatterMoney.format(record.unfunded_percentage_initial_fee)}`;
      }
    },

    {
      title: `C.I. a Financiar (${infoSalePlan?.sale_information.funded_percentage_initial_fee})`,
      width: 30,
      align: "center",
      render: (_, record) => {
        return `${formatterMoney.format(record.funded_percentage_initial_fee)}`;
      }
    },
    {
      title: `Financiación ${infoSalePlan?.sale_information.funded_percentage_initial_fee} en ${infoSalePlan?.sale_information.value_of_dues} cuotas`,
      width: 30,
      align: "center",
      render: (_, record) => {
        return `${formatterMoney.format(record.value_of_dues)}`;
      }
    },
    {
      title: "Nivel de Ingresos",
      width: 20,
      align: "center",
      render: (_, record) => {
        return ` ${formatterMoney.format(record.income_level)}`;
      }
    },
    {
      title: `Financiación Banco / Recursos propios (${infoSalePlan?.sale_information.bank_financing_percentage})`,
      width: 40,
      align: "center",
      render: (_, record) => {
        return `${formatterMoney.format(record.bank_funded_percentage)}`;
      }
    },
    {
      title: "ESTADO",
      width: 20,
      align: "center",
      render: (_, record) => {
        return getStateUnit(record.unit_state);
      }
    },
  ];
}