import { Table } from "antd";
import { getFormatDate } from "../../../../handle/HandleDatePicker/HandleDate";
import { formatterMoney } from "../../../../handle/FormatterMoney/FormatterMoney";

export const CreateSummary = (sale_information) => {


    return (
      <>
        <Table.Summary.Row>
          <Table.Summary.Cell colSpan={4}  >Fecha de entrega estimada:</Table.Summary.Cell>
          <Table.Summary.Cell align={"right"} colSpan={2}>{getFormatDate(sale_information?.estimated_delivery_date)}</Table.Summary.Cell>
          <Table.Summary.Cell rowSpan={6}  ></Table.Summary.Cell>
          <Table.Summary.Cell align={"center"} rowSpan={3} colSpan={11}  >{sale_information?.income_information}</Table.Summary.Cell>
        </Table.Summary.Row>
        <Table.Summary.Row>
          <Table.Summary.Cell colSpan={4}>Separación</Table.Summary.Cell>
          <Table.Summary.Cell align={"right"} colSpan={2} >{formatterMoney.format(sale_information?.separation_value)}</Table.Summary.Cell>
        </Table.Summary.Row>
        <Table.Summary.Row>
          <Table.Summary.Cell colSpan={4}>% Cuota inicial - Firma promesa o encargo fiduciario</Table.Summary.Cell>
          <Table.Summary.Cell align={"right"} colSpan={2}>{sale_information?.unfunded_percentage_initial_fee}</Table.Summary.Cell>
        </Table.Summary.Row>
        <Table.Summary.Row>
          <Table.Summary.Cell colSpan={4}>% Cuota inicial - A financiar durante la construcción</Table.Summary.Cell>
          <Table.Summary.Cell align={"right"} colSpan={2}>{sale_information?.funded_percentage_initial_fee}</Table.Summary.Cell>
        </Table.Summary.Row>
        <Table.Summary.Row>
          <Table.Summary.Cell colSpan={4}>% Financiación Banco / Recursos propios</Table.Summary.Cell>
          <Table.Summary.Cell align={"right"} colSpan={2}>{sale_information?.bank_financing_percentage}</Table.Summary.Cell>
        </Table.Summary.Row>
        <Table.Summary.Row>
          <Table.Summary.Cell colSpan={4}>Plazo de financiación durante la construcción</Table.Summary.Cell>
          <Table.Summary.Cell align={"right"} colSpan={2}>{sale_information?.value_of_dues}</Table.Summary.Cell>
          <Table.Summary.Cell align={"center"} rowSpan={3} colSpan={11}  >{sale_information?.income_data}</Table.Summary.Cell>
        </Table.Summary.Row>
      </>
    );
  }