import React, { useState, useEffect } from "react";
import { useDispatch } from "react-redux";
import {Form, InputNumber, Row, Col, Divider, Button, Card} from "antd";
import {success, errorGlobal} from "../../../../../../handle/Notification/Notification";
import {getSaleParameterById, updateSalesParametersById} from "../../../../../../../redux/actions/Project/Sales_ParameterAction";
import { FORMATTER_INPUT_NUMBER } from "../../../../../../../redux/constants";

const FormSellingParameter = ({ salesParemeterDatabase, id }) => {
	
	const dispatch = useDispatch();

	useEffect(() => {
		if (
			!Array.isArray(salesParemeterDatabase) &&
			salesParemeterDatabase._payload.length !== 0
		) {
			setSquareMeterValue(
				salesParemeterDatabase._payload[0].square_meter_value
			);
			setSimpleParkingValue(
				salesParemeterDatabase._payload[0].simple_parking_value
			);
			setDoubleParkingValue(
				salesParemeterDatabase._payload[0].double_parking_value
			);
			setInteresOnPlansPercentage(
				salesParemeterDatabase._payload[0].interest_on_plans_percentage
			);
			setInterestInConstructionPercentage(
				salesParemeterDatabase._payload[0].interest_in_construction_percentage
			);
		} else {
			setSquareMeterValue(salesParemeterDatabase.square_meter_value);
			setSimpleParkingValue(salesParemeterDatabase.simple_parking_value);
			setDoubleParkingValue(salesParemeterDatabase.double_parking_value);
			setInteresOnPlansPercentage(
				salesParemeterDatabase.interest_on_plans_percentage
			);
			setInterestInConstructionPercentage(
				salesParemeterDatabase.interest_in_construction_percentage
			);
		}
	}, [salesParemeterDatabase]);

	const [form] = Form.useForm();

	const [squareMeterValue, setSquareMeterValue] = useState(null);
	const [simpleParkingValue, setSimpleParkingValue] = useState(null);
	const [doubleParkingValue, setDoubleParkingValue] = useState(null);
	const [interesOnPlansPercentage, setInteresOnPlansPercentage] =
		useState(null);
	const [
		interestInConstructionPercentage,
		setInterestInConstructionPercentage,
	] = useState("");

	const dataSalesParameter = {
		square_meter_value: squareMeterValue,
		simple_parking_value: simpleParkingValue,
		double_parking_value: doubleParkingValue,
		interest_on_plans_percentage: interesOnPlansPercentage,
		interest_in_construction_percentage: interestInConstructionPercentage,
	};

	const fillFields = () => {
		form.setFieldsValue({
			squareMeterValue,
			simpleParkingValue,
			doubleParkingValue,
			interesOnPlansPercentage,
			interestInConstructionPercentage,
		});
	};
	//Form enviar datos actualizados al servidor
	const onFinish = () => {
		dispatch(
			updateSalesParametersById(id, dataSalesParameter, async (res) => {
				const response = await res;
				if (response.error) {
					errorGlobal("¡UPS! ha ocurrido un error mientras se actualizaba");
				} else {
					success("Actualizado exitosamente");
					dispatch(getSaleParameterById(id));
				}
			})
		);
		dispatch(getSaleParameterById(id));
	};
	useEffect(() => {
		fillFields();
	}, [salesParemeterDatabase]);

	return (
		<>
			<Card title="Parámetros de ventas" style={{ borderColor: "black" }}>
				<Form
					layout="vertical"
					name="PagosRelacion"
					onFinish={onFinish}
					form={form}
					style={{ marginLeft: "27px", marginRight: "27px", marginTop: "27px" }}
				>
					<Row gutter={16}>
						<Col span={12}>
							{
								<Form.Item label="Valor m2" name="squareMeterValue">
									<p style={{ visibility: "hidden" }}> ''</p>
									<InputNumber
										{...FORMATTER_INPUT_NUMBER}
										parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
										defaultValue={squareMeterValue}
										value={squareMeterValue}
										onChange={(value) => setSquareMeterValue(value)}
										style={{ width: "100%" }}
									/>
								</Form.Item>
							}
						</Col>
					</Row>
					<Row gutter={16}>
						<Divider orientation="left">Parqueadero</Divider>
						<Col span={12}>
							{
								<Form.Item
									label="Valor Parqueadero Sencillo"
									name="simpleParkingValue"
								>
									<p style={{ visibility: "hidden" }}> ''</p>
									<InputNumber
										min={0}
										{...FORMATTER_INPUT_NUMBER}
										parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
										defaultValue={simpleParkingValue}
										value={simpleParkingValue}
										onChange={(value) => setSimpleParkingValue(value)}
										style={{ width: "100%" }}
									/>
								</Form.Item>
							}
						</Col>
						<Col span={12}>
							{
								<Form.Item
									label="Valor Parqueadero Doble Lineal"
									name="doubleParkingValue"
								>
									<p style={{ visibility: "hidden" }}> ''</p>
									<InputNumber
										min={0}
										{...FORMATTER_INPUT_NUMBER}
										parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
										defaultValue={doubleParkingValue}
										value={doubleParkingValue}
										onChange={(value) => setDoubleParkingValue(value)}
										style={{ width: "100%" }}
									/>
								</Form.Item>
							}
						</Col>
					</Row>
					<Row gutter={16}>
						<Divider orientation="left">Porcentaje Valor Presente</Divider>
						<Col span={12}>
							{
								<Form.Item
									label="Interés Sobre Planos (%)"
									name="interesOnPlansPercentage"
								>
									<p style={{ visibility: "hidden" }}> ''</p>
									<InputNumber
										min={0}
										max={100}
										formatter={(value) => `${value}%`}
										parser={(value) => value.replace("%", "")}
										defaultValue={interesOnPlansPercentage}
										value={interesOnPlansPercentage}
										onChange={(value) => setInteresOnPlansPercentage(value)}
										style={{ width: "100%" }}
									/>
								</Form.Item>
							}
						</Col>
						<Col span={12}>
							{
								<Form.Item
									label="Interés en Construcción (%)"
									name="interestInConstructionPercentage"
								>
									<p style={{ visibility: "hidden" }}> ''</p>
									<InputNumber
										min={0}
										max={100}
										formatter={(value) => `${value}%`}
										parser={(value) => value.replace("%", "")}
										defaultValue={interestInConstructionPercentage}
										value={interestInConstructionPercentage}
										onChange={(value) =>
											setInterestInConstructionPercentage(value)
										}
										style={{ width: "100%" }}
									/>
								</Form.Item>
							}
						</Col>
					</Row>
					<Row>
						<Col span={2} offset={22}>
							<Form.Item>
								<Button disabled={JSON.parse(localStorage.stateDisable)} type="primary" htmlType="submit">
									Guardar
								</Button>
							</Form.Item>
						</Col>
					</Row>
				</Form>
			</Card>
		</>
	);
};

export default FormSellingParameter;
