import * as constants from "./../../../constants";

export const fetchAllPolicyToComplyAction = () => ({
	type: constants.API,
	payload: {
		method: "GET",
		url: `${constants.RUTA}/polityToComply`,
		success: (response) => fetchAllPolicyToComply(response),
	},
});
export const resetPolicyToComplyAction = () => ({
    type: constants.API,
    payload: {
		method: null,
		success: () => (resetPolicyToComply())
	}
})

const resetPolicyToComply = () =>({
	type:constants.RESET_ALL_POLICY_TO_COMPLY,
	payload: []
});

const fetchAllPolicyToComply = (data) => ({
	type: constants.FETCH_ALL_POLICY_TO_COMPLY,
	payload: data,
});
