import {useContext, useEffect, useState} from "react";
import PropertyDataContext from "../Components/context/PropertyDataContext";
import {
	calularDescuentoOIncremto,
	findTheCurrentMonthAndYear,
	CalcularEntregaGeneralCliente,
	Sumatory, SumatoryValueCoutes,
	valorPresenteCuotas
} from "../../../../handle/Formulas/Quote";

const month = {
	0: "Ene-", 1: "Feb-", 2: "Mar-",
	3: "Abr-", 4: "May-", 5: "Jun-",
	6: "Jul-", 7: "Ago-", 8: "Sep-",
	9: "Oct-", 10: "Nov-", 11: "Dic-",
};
export const useQuotes = () => {
	//estados de la aplicación
	const { propertyData, handlePropertyData } = useContext(PropertyDataContext);
	const [ project,setProject ] = useState({
		project:'',squareMeter:0.00,label:'',squareMeterValue:0.00,sightPremium:0,heightPremiumPerMeter:0,
		partialValueUnit:0,parkingValue:0,valueOthers:0,FinishedKitValue:0,totalUnitValue:0
	});
	const [finalValuesProposedByTheProject,setFinalValuesProposedByTheProject] = useState({
		presentValueGivenByTheProject:0,valueOfTheUnitProposedByTheProject:0
	});
	const [finalValueProposedByTheClient,setFinalValueProposedByTheClient] = useState({
		presentValueGiven:0,valueOfTheUnitProposed:0,financialDiscountOrIncrease:0,
		discountFinishes:0
	});
	const [ initialFeeState, setInitialFee ] = useState({
		initialFee:0,initialFeePercentage:0,separation:0,initialFeeNotFunded:0,initialFeeNotFundedPercentage:0,
		initialFeeFinanced:0,initialFeeFinancedPercentage:0
	});
	const [dataDates,setDataDates] = useState({
		mes: 0, year: 0, mesConstruccion: 0, mesPlano: 0,mesTotal:0
	});
	const [formOfPaymentProposedByTheProject,setFormOfPaymentProposedByTheProject] = useState([{
		key: 0,month:'',value:0,porcentaje:0,tipo:''
	}]);
	const [paymentMethodProposedByTheClient,setPaymentMethodProposedByTheClient] = useState([{
		key: 0,month:'',value:0,porcentaje:0,tipo:''
	}]);
	const [taxStatements,setTaxStatements] = useState({
		flatTax:0,constructionTax:0
	});
	//cambios cada vez que cambie el estado 
	useEffect(()=>{
		projectSet();
		initialFeeSet();
		dataDatesSet();
		taxStatementsSet();
	},[propertyData]);
	
	useEffect(()=>{
		formOfPaymentProposedByTheProjectSet();
		paymentMethodProposedByTheClientSet();
	},[initialFeeState,dataDates]);
	
	useEffect(()=>{
		calculatePresentValue();
	},[initialFeeState,formOfPaymentProposedByTheProject,taxStatements]);
	
	const taxStatementsSet = () => {
		setTaxStatements({
			flatTax: parseFloat(propertyData.interestOnPlansPercentage)/100,
			constructionTax: parseFloat(propertyData.interestInConstructionPercentage)/100
		});
	}
	
	const projectSet = () => {
		setProject({
			project:propertyData.nameProject,
			squareMeter:propertyData.squareMeter,
			label:propertyData.label,
			squareMeterValue:propertyData.squareMeterValue,
			sightPremium:propertyData.bonus_view,
			heightPremiumPerMeter:propertyData.bonus_height,
			partialValueUnit:propertyData.SubTotal,
			parkingValue:propertyData.parkingValue,
			valueOthers:propertyData.valueOtherAdditional,
			FinishedKitValue:propertyData.finishKitValue,
			totalUnitValue:propertyData.valorTotal
		});
	}
	
	const dataDatesSet = () => {
		let datosFechas = findTheCurrentMonthAndYear(propertyData.fechaDeConstruccion,propertyData.estimatedDeliveryDate,propertyData.maximumNumberMonthsFinanceInitialFee);
		setDataDates(datosFechas);
	}
	
	const initialFeeSet = () => {
		let initialFee = propertyData.valorTotal * (parseInt(propertyData.initialFeePercentage)/100);
		let initialFeeNotFunded = propertyData.valorTotal * (parseInt(propertyData.initialFeePromiseSignatureTrustPercentage)/100);
		let initialFeeFinanced =  propertyData.valorTotal * (parseInt(propertyData.initialFeeFinanceInConstructionPercentage)/100);
		setInitialFee({
			initialFee,
			initialFeePercentage: parseInt(propertyData.initialFeePercentage),
			separation: propertyData.separationValue,
			initialFeeNotFunded,
			initialFeeNotFundedPercentage: parseInt(propertyData.initialFeePromiseSignatureTrustPercentage),
			initialFeeFinanced,
			initialFeeFinancedPercentage: parseInt(propertyData.initialFeeFinanceInConstructionPercentage)
		});
	}
	
	const calculatePor = (valor, valorTotal) => {
		let percentage = (parseInt(valor) * 100) / parseInt(valorTotal);
		if (valorTotal === 0) return 0;
		if (valor === 0 || valorTotal === 0) return 0;
		return percentage;
	};

	const formOfPaymentProposedByTheProjectSet = () => {
		let date = {...dataDates};
		const velorMes = initialFeeState.initialFeeFinanced / date.mesTotal;
		let percentage = calculatePor(velorMes, propertyData.valorTotal);
		let element = [
			{
				key: 0,
				month: "C.I. No Financiada",
				value: initialFeeState.initialFeeNotFunded,
				porcentaje: propertyData.initialFeePromiseSignatureTrustPercentage,
				tipo: date.mesPlano > 0 ? 'Presente' : 'Construccion'
			},
		];
		for (let index = 1; index <= date.mesTotal;index++){
			if (date.mes > 11) {
				date.mes = 0;
				date.year++;
			}
			element = element.concat({
				key: index,
				month: month[date.mes] + date.year,
				value: parseFloat(velorMes),
				porcentaje: parseFloat(percentage).toFixed(2),
				tipo: index > date.mesPlano ? 'Construccion' : 'Presente'
			});
			date.mes++;
		}
		element = element.concat({ 
		
			key: date.mesTotal + 1,
			month: "Entrega",
			value: propertyData.valorTotal - initialFeeState.initialFee,
			porcentaje: propertyData.bankFinancingPercentage,
			tipo: element[element.length-1].tipo
		});
		setFormOfPaymentProposedByTheProject(JSON.parse(JSON.stringify(element)));
		
	}
	
	const paymentMethodProposedByTheClientSet = () => {
		let date = {...dataDates};
		const velorMes = initialFeeState.initialFeeFinanced / date.mesTotal;
		let percentage = calculatePor(velorMes, propertyData.valorTotal);
		let element = [
			{
				key: 0,
				month: "C.I. No Financiada",
				value: initialFeeState.initialFeeNotFunded,
				porcentaje: propertyData.initialFeePromiseSignatureTrustPercentage,
				tipo: date.mesPlano > 0 ? 'Presente' : 'Construccion'
			},
		];
		for (let index = 1; index <= date.mesTotal;index++){
			if (date.mes > 11) {
				date.mes = 0;
				date.year++;
			}
			element = element.concat({
				key: index,
				month: month[date.mes] + date.year,
				value: parseFloat(parseFloat(velorMes).toFixed(2)),
				porcentaje: parseFloat(percentage).toFixed(2),
				tipo: index > date.mesPlano ? 'Construccion' : 'Presente'
			});
			date.mes++;
		}
		element = element.concat({
			key: date.mesTotal + 1,
			month: "Entrega",
			value: CalcularEntregaGeneralCliente(finalValuesProposedByTheProject.presentValueGivenByTheProject,element,undefined,taxStatements.flatTax,taxStatements.constructionTax),
			porcentaje: propertyData.bankFinancingPercentage,
			tipo:element[element.length-1].tipo
		});
		setPaymentMethodProposedByTheClient(JSON.parse(JSON.stringify(element)));
	}
	
	const calculatePresentValue = () => {
		let valorCuotas = valorPresenteCuotas(formOfPaymentProposedByTheProject,undefined,taxStatements.flatTax,taxStatements.constructionTax);
		setFinalValuesProposedByTheProject({
			presentValueGivenByTheProject: valorCuotas,
			valueOfTheUnitProposedByTheProject: propertyData.valorTotal
		});
	};
	
	const calculatePresentValueByTheClient = (formOfPayments) => {
		const entregaGeneral=CalcularEntregaGeneralCliente(finalValuesProposedByTheProject.presentValueGivenByTheProject,formOfPayments,undefined,taxStatements.flatTax,taxStatements.constructionTax);
		const presentValueGiven = valorPresenteCuotas(formOfPayments,entregaGeneral,taxStatements.flatTax,taxStatements.constructionTax);
		const filterEntrega =formOfPayments?.filter(x=>x.month!== 'Entrega');
		const sumatoryValueCoutes=parseFloat(SumatoryValueCoutes(filterEntrega)+entregaGeneral);
		const financialDiscountOrIncrease = calularDescuentoOIncremto (finalValuesProposedByTheProject.valueOfTheUnitProposedByTheProject,sumatoryValueCoutes);
		setFinalValueProposedByTheClient({
			presentValueGiven,
			valueOfTheUnitProposed: sumatoryValueCoutes,
			financialDiscountOrIncrease,
			discountFinishes: propertyData.valorCupon
		});
	
	}
	
	return {
		//exportar los estados
		project,initialFeeState,formOfPaymentProposedByTheProject,finalValuesProposedByTheProject,
		paymentMethodProposedByTheClient,finalValueProposedByTheClient,dataDates,
		//exportar las funciones
		calculatePresentValueByTheClient
	}
}