import {SearchOutlined} from "@ant-design/icons";
import React, {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {Col, Divider, Row, Input, Select, Form, Button} from "antd";
import RegisterForm from "../../../../Configuration/Channel/components/ChannelRegister";
import {fetchAllCustomer} from "../../../../../../redux/actions/Customer/CustomerAction";
import CustomersRegistration from "../../../../Customers/Componets/CustomersRegistration";
import {fetchAllChannel} from "../../../../../../redux/actions/Configuration/ChannelAction";

const StepClienteQuotes = ({selectCustomerSet,idChannelSet}) => {
	const dispatch = useDispatch()
	const [idChannel,setIdChannel] = useState()
	const [selectCustomer,setSelectCustomer] = useState()
	
	useEffect(() =>{
		dispatch(fetchAllCustomer())
		dispatch(fetchAllChannel())
	},[])
	useEffect( () => {
		setInfoCustomer()
		getCustomer()
	},[selectCustomer])
	useEffect( () => {
		setInfoChannel()
	},[idChannel])
	
	const getCustomer = () => {
		if (selectCustomer !== ""){
			
		}
	}
	const setInfoCustomer = () => {
		selectCustomerSet(selectCustomer)
	}
	
	const setInfoChannel = () => {
		idChannelSet(idChannel)
	}
	
	const selectCustomerElement = useSelector((state) => state?.customer)
	const selectChannelElement = useSelector((state) => state?.channel)
	
	return (
		<>
			<Row gutter={16}>
				<Col span={16}>
					<Divider className="divDatosCliente">
						Datos del Cliente
					</Divider>
				</Col>
				<Col span={8}>
					<Divider>Asesor</Divider>
				</Col>
			</Row>
			<Row gutter={16}>
				<Col span={16}>
					<Row gutter={16}>
						<Col of xs={24} sm={24} md={24} lg={15} xl={16}>
							<Input.Group compact>
								<Form.Item
									label="Buscar cliente (identificación o nombre)"
									name="customerItemForm"
									rules={[{required: true, message: "Campo requerido"}]}
									style={{width: "100%"}}
								>
									<Select
										defaultValue={""}
										showSearch
										style={{width: "100%"}}
										optionFilterProp="children"
										onChange={setSelectCustomer}
										filterOption={(input, option) =>
											option.children
												.toLowerCase()
												.indexOf(input.toLowerCase()) >= 0
										}
										rules={[{required: true}]}
									>
										<Select.Option value="">Seleccione...</Select.Option>
										{selectCustomerElement.hasOwnProperty("_payload") &&
										selectCustomerElement._payload?.map((item) => (
											(item.state === 1)? (
												<Select.Option
													key={item?.id+item?.name+item?.id}
													value={item.id}
												>{` ${item.name.replace('_', ' ')} - CC: ${item.identification}`}</Select.Option>
											) : ("")
										))}
									</Select>
								</Form.Item>
							</Input.Group>
						</Col>
						<Col of xs={24} sm={24} md={24} lg={8} xl={7}>
							<Form.Item label=" ">
								<CustomersRegistration/>
							</Form.Item>
						</Col>
					</Row>
					<Row>
						<Col xs={24} sm={24} md={12} lg={12} xl={12}>
							<Form.Item label="Nombre" name="nombre">
								<Input placeholder="Cliente" disabled/>
							</Form.Item>
							<Form.Item
								label="Apellido"
								name="apellido"
								rules={[
									{
										required: false,
									},
								]}
							>
								<Input placeholder="Apellido" disabled/>
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={12} lg={12} xl={12}>
							<Form.Item label="Email" name="email" type="email">
								<Input placeholder="E-mail" disabled/>
							</Form.Item>
							<Form.Item
								label="Teléfono"
								name="télefono"
								rules={[
									{
										required: false,
									},
								]}
							>
								<Input placeholder="Telefono" disabled/>
							</Form.Item>
						</Col>
					</Row>
					<Row>
						<Input.Group compact>
							<Form.Item
								name="channelFormItem"
								label="¿Como se enteró?"
								rules={[{required: true, message: "Campo requerido"}]}
								style={{width: "50%"}}
							>
								<Select
									defaultValue={""}
									showSearch
									style={{width: "100%"}}
									optionFilterProp="children"
									onChange={setIdChannel}
									filterOption={(input, option) =>
										option.children
											.toLowerCase()
											.indexOf(input.toLowerCase()) >= 0
									}
								>
									<Select.Option value="">Seleccione...</Select.Option>
									{selectChannelElement.hasOwnProperty("_payload") &&
									selectChannelElement._payload?.map((item) => (
										<Select.Option key={item?.id+item?.name+item?.id} value={item.id}>{` ${item.name}`}</Select.Option>
									))}
								</Select>
							</Form.Item>

							<Form.Item label=" ">
								<RegisterForm/>
							</Form.Item>
						</Input.Group>
					</Row>
				</Col>
				<Col span={8}>
					<Form.Item
						label="Nombre asesor"
						name="nombreAsesor"
						rules={[
							{
								required: false,
								message: "",
							},
						]}
					>
						<Input
							style={{width: "100%"}}
							placeholder="Asesor"
							disabled
						/>
					</Form.Item>
					<Form.Item
						label="Empresa"
						name="empresaAssessor"
						rules={[
							{
								required: false,
								message: "",
							},
						]}
					>
						<Input placeholder="Empresa" disabled/>
					</Form.Item>
					<Form.Item
						label="Email"
						name="emailAssessor"
						rules={[
							{
								required: false,
								message: "",
							},
						]}
					>
						<Input disabled placeholder="E-mail"/>
					</Form.Item>
					<Form.Item
						label="Teléfono"
						name="télefonoAssessor"
						rules={[
							{
								required: false,
								message: "",
							},
						]}
					>
						<Input placeholder="Telefono" disabled/>
					</Form.Item>
					<Form.Item
						label="Último contacto"
						name="ultimoContacto"
						rules={[
							{
								required: false,
								message: "",
							},
						]}
					>
						<Input placeholder="Ultimo contacto" disabled/>
					</Form.Item>
				</Col>
			</Row>
			<Row>
				<Col xs={24} sm={24} md={8} lg={8} xl={10}/>
				<Col>
					<Form.Item>
						<Button
							type="primary"
							icon={<SearchOutlined/>}
							// disabled={!buttonState}
							htmlType="submit"
							activateKey={"2"}
						>
							Buscar Proyecto
						</Button>
					</Form.Item>
				</Col>
			</Row>
		</>
	)
}

export default StepClienteQuotes