import React from "react"
import {Button, Col, Form, Row, Select} from "antd";
import {useSelector} from "react-redux";

const SearchForm = () => {
	const selectAssessor = useSelector((state) => state?.assessor._payload);
	return <>
		<Row gutter={16}>
			<Col xs={24} sm={24} md={12} lg={12} xl={12}>
				<Form.Item 
					label="Asesores:" 
					name="assessor_id"
				>
					<Select
						showSearch
						defaultValue={""}
						optionFilterProp="children"
						filterOption={
							(input, option) =>
								option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
						}
					>
						<Select.Option value="">Todos...</Select.Option>
						{
							selectAssessor?.map( item => (
								<Select.Option key={item.id+item.name+item.id} value={item.id}>{item.name}</Select.Option>
							))
						}
					</Select>
				</Form.Item>
			</Col>
			<Col offset={10} xs={24} sm={24} md={12} lg={12} xl={1}/>
		</Row>
		<Row>
			<Col xs={18} sm={20} md={21} lg={22} xl={22} />
			<Col xs={2} sm={2} md={2} lg={2} xl={2}>
				<Form.Item>
					<Button type="primary" htmlType="submit">
						Buscar
					</Button>
				</Form.Item>
			</Col>
		</Row>
	</>
}

export default SearchForm