import "./tableLegalize.scss";
import React, { useState } from "react";
import QuoteDetails from "./quoteDetails";
import { Tooltip, Button, Modal } from "antd";
import { DollarCircleOutlined } from "@ant-design/icons";

const FormLegalizeSale = (props) => {
	const [visible, setVisible] = useState(false);

	const onShow = () => setVisible(true);

	const onClose = () => setVisible(false);

	const textComprarUnidad = (
		<span style={{ color: "#6A6963" }}> Comprar Unidad </span>
	);

	return (
		<>
			<Tooltip placement="left" color="#ffffff" title={textComprarUnidad}>
				<Button
					style={{ background: "#5cb85c", borderColor: "#4cae4c" }}
					icon={<DollarCircleOutlined style={{ color: "#fff" }} />}
					disabled={props.record.unit_state === "Comprada"}
					onClick={onShow}
					size="small"
				/>
			</Tooltip>
			<Modal
				size="small"
				footer={false}
				visible={visible}
				backgroundColor="#40CD0A"
				title="Legalizar venta"
				width={700}
				onCancel={onClose}
			>
				<QuoteDetails onClose={onClose} data={props} />
			</Modal>
		</>
	);
};

export default FormLegalizeSale;
