import React, {useState} from 'react';
import {Form, Input, Button, Modal} from 'antd';
import {PlusOutlined} from '@ant-design/icons';
import {useDispatch} from 'react-redux';
import {createBuilder, fetchAllBuilder} from '../../../../../../redux/actions/Project/BuilderAction';
import {success} from '../../../../../handle/Notification/Notification';

const RegisterBuilderControlPower = () => {

	const [isModalVisible, setIsModalVisible] = useState(false);
	const [name, setName] = useState('');
	const dispatch = useDispatch();

	const validateMessages = {
		// eslint-disable-next-line no-template-curly-in-string
		required: '${label} es requerido',
		types: {
			// eslint-disable-next-line no-template-curly-in-string
			email: '${label} no es un correo válido',
			// eslint-disable-next-line no-template-curly-in-string
			number: '${label} de	be ser un número'
		}
	};

	const [form] = Form.useForm();

	const builderData = {
		name
	};

	const showModal = () => setIsModalVisible(true);
	const onClose = () => setIsModalVisible(false);
	const resetFields = () => {
		form.resetFields();
	}
	const onFinish = () => {
		dispatch(createBuilder(builderData,async (response)=>{
			const res = await response
			if (!res.error){
				resetFields();
				onClose();
				success('Ha sido agregado con éxito');
				dispatch(fetchAllBuilder());
			}
		}));
		
	}

	return (
		<>
			<Button type="primary" onClick={showModal} icon={<PlusOutlined/>}/>
			<Modal
				title="Nueva Constructora"
				centered
				visible={isModalVisible}
				footer={false}
				onCancel={onClose}
				width={700}
			>
				<Form
					layout="vertical"

					size='middle'
					form={form}
					validateMessages={validateMessages}
					onFinish={onFinish}

				>
					<Form.Item
						label="Nombre"
						name="name"
						rules={[
							{
								required: true,
								message: 'El campo es requerido',
							},
						]}
					>
						<Input
							value={name}
							onChange={e => setName(e.target.value)}
						/>
					</Form.Item>

					<Form.Item>
						<Button type="primary" htmlType="submit" style={{position: 'absolute', right: 1, top: 20}}>
							Guardar
						</Button>
					</Form.Item>
				</Form>

			</Modal>
		</>
	);
}

export default RegisterBuilderControlPower;