import {Table} from "antd";
import {useDispatch} from "react-redux";
import React, {useEffect, useState} from "react";
import {errorGlobal} from "../../../../handle/Notification/Notification";
import {getColumnSearchProps} from "../../../../handle/HandleFilterTable/handleFilterTable";
import {StatusOfLegalizationPetition} from "../../../../../redux/actions/Price/Sales/SalesAction";

const StatusOfLegalization = () => {
	const dispatch = useDispatch()
	const [legalization,setLegalization] = useState([])
	useEffect(()=>{
		dispatch(StatusOfLegalizationPetition(async (response)=>{
			const res = await response
			setLegalization(res._payload)
		},async (error)=>{
			const err = await error
			errorGlobal(err.message)
		}))
	},[])
	
	const EstadoDeLegalizacion = [
		{
			title: '#',
			width: 100,
			dataIndex: 'id',
			align: "center",
			key: 'id',
			defaultSortOrder: 'descend',
			sorter: (a, b) => a.id - b.id,
		},
		{
			title: 'Asesor',
			width: 100,
			dataIndex: 'asesor',
			align: "center",
			key: 'asesor',
			...getColumnSearchProps('asesor'),
		},
		{
			title: 'Proyecto',
			dataIndex: 'proyecto',
			key: 'proyecto',
			align: "center",
			width: 100,
			...getColumnSearchProps('proyecto'),
		},
		{
			title: 'Cliente',
			dataIndex: 'cliente',
			align: "center",
			key: 'clinte',
			width: 140,
			...getColumnSearchProps('cliente'),
		},
		{
			title: '# Días sin Legalizar',
			dataIndex: 'diasSinLegalizar',
			key: 'diasSinLegalizar',
			align: "center",
			width: 100,
			...getColumnSearchProps('diasSinLegalizar'),
		},
		{
			title: '% Legalización',
			dataIndex: 'legalizacion',
			key: 'legalizacion',
			align: "center",
			width: 120,
			...getColumnSearchProps('legalizacion'),
		},
		{
			title: '# Documento Faltante',
			dataIndex: 'documentoFaltante',
			key: 'documentoFaltante',
			align: "center",
			width: 100,
			...getColumnSearchProps('docummentoFaltante'),
		}
	];
	return <Table
		columns={EstadoDeLegalizacion}
		dataSource={legalization}
		y={400}
	/>
}

export default StatusOfLegalization