import React, { useEffect,useState } from "react";
import { fetchAllProjects } from "../../../../redux/actions/Project/ProjectsAction";
import { connect, useDispatch, useSelector } from "react-redux";
import { Card, Col, Row, Form, Select, Button } from "antd";
import { validateModule } from "../../../handle/PermissionMethods/PermissionMethods";
import * as constants from "../../../../redux/constants";
import { Redirect } from "react-router-dom/cjs/react-router-dom.min";
import { fetchExcelLegalizationAndDeed } from "../../../../redux/actions/Report/ReportLegalizationAndDeed/reportLegalizationAndDeed";
import { warning } from "../../../handle/Notification/Notification";

const LegalizationandDeedReport = ({dispatchFetchAllProjectAction}) => {
	useEffect(
		() => dispatchFetchAllProjectAction(),
		[dispatchFetchAllProjectAction]
	);

	const [form] = Form.useForm();
	const [idProject, setIdProject] = useState(null);
	const dispatch = useDispatch();
	const [consultind, setConsulting] = useState( false )

	const selectProject = useSelector((state) => state?.project);



	const onFinish = async () => {	setConsulting(true);
		if(idProject!=null){
			dispatch(fetchExcelLegalizationAndDeed(idProject, async (response) => {
				let res = await response;
				if (res.code === 200) {
					const tagA = document.createElement("a");
					tagA.href = res._payload;
					tagA.target = "_blank";
					tagA.click();
				}
				else{
					warning(res.message);
				}setConsulting(false);
			}));
		}
	};
	const validateMessages = {
		required: "${label} es requerido",
		types: {
			email: "${label} no es un correo válido",
			number: "${label} debe ser un número",
		},
	};
	if (!validateModule(constants.LEGALIZATION_DEED_REPORT_SUBMODULE)) {
		return <Redirect to={constants.DEFAULT_ROUTE} />;
	}
	return (
		<Card title="Informe de Legalización y Escrituración">
			<Form
				layout="vertical"
				onFinish={onFinish}
				validateMessages={validateMessages}
				form={form}
			>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item
							label="Proyecto:"
							name="project_id"
							rules={[
								{
									required: true,
								},
							]}
						>
							<Select
								defaultValue={""}
								showSearch
								style={{ width: "100%" }}
								optionFilterProp="children"
								onChange={setIdProject}
								value={form.getFieldValue("project_id")}
								filterOption={(input, option) =>
									option.children.toLowerCase().indexOf(input.toLowerCase()) >=
									0
								}
							>
								<Select.Option value="">Seleccione...</Select.Option>
								{
									!Array.isArray(selectProject) && selectProject._payload?.map(item => (
										<Select.Option key={item.id} value={item.id}>{` ${item.name} `}</Select.Option>
									))}
							</Select>
						</Form.Item>
					</Col>
				</Row>
				<Row>
					<Col xs={18} sm={20} md={21} lg={22} xl={22} />

					<Col xs={2} sm={2} md={2} lg={2} xl={2}>
						<Form.Item>
							<Button type="primary" htmlType="submit" loading={consultind}>
								Exportar
							</Button>
						</Form.Item>
					</Col>
				</Row>
			</Form>
		</Card>
	);
};

const mapDispatchToProps = (dispatch) => ({
	dispatchFetchAllProjectAction: () => dispatch(fetchAllProjects()),
});
export default connect(null, mapDispatchToProps)(LegalizationandDeedReport);
