import React, {useState, useEffect} from "react";
import {Redirect} from "react-router-dom";
import {UploadOutlined} from "@ant-design/icons";
import MapQuest from "../../../../handle/MapComponent/MapQuest";
import ActionsMap from "../../../../handle/MapComponent/ActionsMap";
import NameActions from "../../../../handle/MapComponent/NameActions";
import colombiaJSON from "../../../../handle/MapComponent/ColombiaJSON";
import {success, errorGlobal} from "../../../../handle/Notification/Notification";
import {getOptionsSelect, createProject} from "../../../../../redux/actions/Project/ProjectsAction";
import {useDispatch} from "react-redux";
import {Form, Input, InputNumber, DatePicker, Upload, Row, Col, Select, Divider, Checkbox, Button, Card, PageHeader} from "antd";
import Spinner from "../../../../handle/Spinner/Spinner";
import {RUTA_API_MULTIMEDIA, VALID_DOCUMENT_UPLOAD} from "../../../../../redux/constants";

const NewProject = () => {
	const dispatch = useDispatch();
	const [form] = Form.useForm();

	const [limitImagesLogo, setLimitImagesLogo] = useState([]);
	const [projectTypes] = useState([]);
	const [projectStatuses] = useState([]);
	const [projectClassifications] = useState([]);
	const [typesSale, setTypesSale] = useState([]);
	const [municipalities, setMunicipalities] = useState([]);
	const [quantityDisabled, setQuantityDisabled] = useState(2);
	const [formSale, setFormSale] = useState("");
	const [project_id, setProjectId] = useState(null);
	const [lat] = useState("3.998749");
	const [lng] = useState("-75.2545871");
	const [loading, setLoading] = useState(false);
	const [country,setCountry] = useState("-");

	let departments = [];
	let land = ["Colombia"];
	const [location,setLocation] = useState();
	for (let i = 0; i < colombiaJSON.length; i++) {
		departments.push(colombiaJSON[i].departamento);
	}

	useEffect(() => {
		dispatch(
			getOptionsSelect((response) => {
				let typesSale = [];
				if (!response.error) {
					response._payload.project_classification.forEach((value) => {
						projectClassifications.push({
							name: value.name,
							id: value.id,
						});
					});
					response._payload.project_state.forEach((value) => {
						projectStatuses.push({
							name: value.name,
							id: value.id,
						});
					});
					response._payload.project_types.forEach((value) => {
						projectTypes.push({
							name: value.name,
							id: value.id,
						});
					});
					response._payload.sales_types.forEach((value) => {
						typesSale.push({
							name: value.name,
							id: value.id,
						});
					});
					setTypesSale(typesSale);
				}
			})
		);
	}, []);

	useEffect(() => {
		if (quantityDisabled === "2") {
			backToNull("quantity");
		}
		if (formSale === 1) {
			backToNull("fiduciaryCode");
		}
	}, [quantityDisabled, formSale]);

	const backToNull = (value) => {
		form.setFieldsValue({
			[value]: null,
		});
	};

	const handleDisableFiduciaryCode = () => {
		switch (formSale) {
			case 1:
				return true;
			case "":
				return true;
			default:
				return false;
		}
	};

	const onChangeCountry = (value) => {
		if (value === "-") {
			form.setFieldsValue({department: "-"});
			form.setFieldsValue({city: "-"});
		}
		form.setFieldsValue({country: value});
		ActionsMap(land[value], null, NameActions.Find_Location, 4.5);
		setCountry(value)
	};

	const onChangeDepartment = (value) => {
		if (value !== "-") {
			let departmentInJson = colombiaJSON.find(
				(element) => element.departamento === departments[value]
			);
			form.setFieldsValue({
				department: value,
			});
			ActionsMap(
				null,
				departmentInJson.latLng,
				NameActions.Find_Location_by_latlng,
				7
			);
			setMunicipalities(departmentInJson.ciudades);
		}
	};
	const onChangeMunicipality = (value) => {
		if (value !== "-") {
			let auxiliary = municipalities[value];
			let varMunicipalities =
				auxiliary.name +
				" - " +
				departments[form.getFieldValue("department")] +
				" - " +
				land[form.getFieldValue("country")];
			setLocation(auxiliary["id"]);
			form.setFieldsValue({
				city: value,
			});
			ActionsMap(varMunicipalities, null, NameActions.Maker_Location, 14);
		}
	};
	const captureStartDateSale = (date) => {
		if (date !== null) {
			if (projectStatuses.length !== 0) {
				form.setFieldsValue({
					stateProject: getProjectState(projectStatuses[0].id),
				});
			}
		}
	}
	const captureEstimatedDateStartConstruction = (date) => {
		if (date !== null) {
			let now = new Date().toJSON().slice(0, 10).replace(/-/g, "/");
			if (form.getFieldsValue("startDateSale") > now) {
				if (projectStatuses.length !== 0) {
					form.setFieldsValue({
						stateProject: getProjectState(projectStatuses[1].id),
					});
				}
			}
		}
	};
	const captureEstimatedDeliveryDate = (date) => {
		if (date !== null) {
			let now = new Date().toJSON().slice(0, 10).replace(/-/g, "/");
			if (form.getFieldsValue("estimatedDateStartConstruction") > now) {
				if (projectStatuses.length !== 0) {
					form.setFieldsValue({
						stateProject: getProjectState(projectStatuses[2].id),
					});
				}
			}
		}
	};

	const getProjectState = (state) => {
		switch (state) {
			case 3:
				return "Finalizado";
			case 2:
				return "En construcción";
			case 1:
				return "En planos";
			default:
				return "";
		}
	};

	const onChange = ({fileList: newFileList}) => {
		setLimitImagesLogo(newFileList);
	};

	const handleSubmit = async () => {
		setLoading(true);
		let values = await form.validateFields();
		let fData = new FormData();
		
		fData.append("name", values["projectName"]);
		fData.append("sale_type", values["saleForm"]);
		fData.append("fiduciary_code", values["fiduciaryCode"]);
		fData.append("state", values["state"] ? 1 : 0);
		fData.append("project_state", values["stateProject"] === "Finalizado" ? 3 : values["stateProject"] === "En construcción" ? 2 : 1);
		fData.append("location", location ? location : 0);
		fData.append("area", values["zone"] || null);
		fData.append("description", values["descriptionProject"] || null);
		fData.append("description_point", values["descriptionPoint"] || null);
		fData.append("project_type", values["projectType"]);
		fData.append("classification", values["classification"]);
		fData.append("total_units", values["totalUnits"] || null);
		fData.append("floors_number", values.numberFloors ?? 0);
		fData.append("simple_parking_number", values?.simpleParkingLots ?? 0);
		fData.append("double_parking_number", values?.doubleLineaParkingLot ?? 0);
		fData.append(
			"visitor_parking_number",
			values["quantity"] ? values["quantity"] : 0
		);
		fData.append(
			"sale_start_date",
			values["startDateSale"].format("YYYY-MM-DD")
		);
		fData.append(
			"start_build_date",
			values["estimatedDateStartConstruction"].format("YYYY-MM-DD")
		);
		fData.append(
			"estimated_delivery_date",
			values["estimatedDeliveryDate"].format("YYYY-MM-DD")
		);
		fData.append(
			"maximum_reforms_date",
			values["dateMaximumReforms"].format("YYYY-MM-DD")
		);

		if (typeof values["uploadImages"] !== "undefined") {
			for (let i = 0; i < values["uploadImages"].length; i++) {
				fData.append(
					`images[]`,
					values["uploadImages"][i].originFileObj,
					values["uploadImages"][i].name
				);
			}
		}
		if (typeof values["promoterImages"] !== "undefined") {
			for (let i = 0; i < values["promoterImages"].length; i++) {
				fData.append(
					`promoters[]`,
					values["promoterImages"][i].originFileObj,
					values["promoterImages"][i].name
				);
			}
		}

		if (typeof values["uploadDocuments"] !== "undefined") {
			for (let i = 0; i < values["uploadDocuments"].length; i++) {
				fData.append(
					`documents[]`,
					values["uploadDocuments"][i].originFileObj,
					values["uploadDocuments"][i].name
				);
			}
		}

		if (typeof values["logo"] !== "undefined") {
			for (let i = 0; i < values["logo"].length; i++) {
				fData.append(
					`logo`,
					values["logo"][i].originFileObj,
					values["logo"][i].name
				);
			}
		}

		dispatch(
			createProject(fData, (response) => {
				if (!response.error) {
					success("Se registro con éxito el nuevo proyecto!!!");
					setProjectId(response._payload);
					setLoading(false);
				} else {

					errorGlobal("¡UPS! Ha ocurrido un error");
					setLoading(false);
				}
			})
		);
	};

	const normFile = (e) => {
		if (Array.isArray(e)) {
			return e;
		}

		return e && e.fileList;
	};

	return (
		<>
			{project_id ? (
				<Redirect to={`/projects/edit-project/${project_id}`}/>
			) : (
				<>
					<div className="site-page-header-ghost-wrapper">
						<PageHeader
							className="site-page-header"
							onBack={() => window.history.back()}
							title="Nuevo Proyecto"
							style={{marginLeft: "-25px"}}
						/>
						<Card style={{borderColor: "black"}}>
							<Form
								layout="vertical"
								form={form}
								onFinish={handleSubmit}
								initialValues={{state: true}}
								name="formNewProject"
								style={{marginLeft: "27px", marginRight: "27px"}}
							>
								<Row gutter={16}>
									<Col xs={5} sm={24} md={24} lg={24} xl={24}>
										<Divider orientation="left">Información General</Divider>
										<Row gutter={16}>
											<Col xs={5} sm={24} md={8} lg={8} xl={6}>
												<Row>
													<Col span={24} offset={1}>
														<Form.Item
															style={{
																paddingTop: "60px",
																paddingLeft: "50px",
															}}
															name="logo"
															valuePropName="fileList"
															getValueFromEvent={normFile}
														>
															<Upload
																action={RUTA_API_MULTIMEDIA}
																listType="picture-card"
																accept="image/*"
																maxCount={1}
																name={"logo"}
																onChange={onChange}
															>
																{limitImagesLogo.length > 0 ? null : "+ Subir"}
															</Upload>
														</Form.Item>
													</Col>
												</Row>
											</Col>
											<Col xs={5} sm={24} md={8} lg={8} xl={8}>
												<Form.Item
													label="Nombre del proyecto"
													name="projectName"
													rules={[
														{
															min: 3,
															message:
																"El nombre del proyecto debe tener al menos 3 caracteres.",
														},
														{
															required: true,
															message: "El nombre del proyecto es requerido.",
														},
													]}
												>
													<Input placeholder="Nombre del proyecto"/>
												</Form.Item>
												<Form.Item
													label="Descripción"
													name={"descriptionProject"}
												>
													<Input.TextArea placeholder="Descripción" rows={5}/>
												</Form.Item>
											</Col>
											<Col xs={5} sm={24} md={9} lg={9} xl={9}>
												<Form.Item
													label="Forma de venta"
													name="saleForm"
													rules={[
														{
															required: true,
															message: "La forma de venta es requerida.",
														},
													]}
												>
													<Select
														loading={typesSale.length === 0}
														onChange={setFormSale}
														defaultValue=""
													>
														<Select.Option value="">
															Seleccione...
														</Select.Option>
														{typesSale?.map((value) => {
															return (
																<Select.Option key={value.id} value={value.id}>
																	{value.name}
																</Select.Option>
															);
														})}
													</Select>
												</Form.Item>
												<Form.Item
													label="Código fiduciario"
													name="fiduciaryCode"
													rules={[
														{
															type: "number",
															min: 99,
															max: 999999999999999,
															message:
																"Debe tener al menos 3 números y no más de 15.",
														},
													]}
												>
													<InputNumber
														min={0}
														placeholder="Código fiduciario"
														disabled={handleDisableFiduciaryCode()}
														style={{width: "100%"}}
													/>
												</Form.Item>
												<Form.Item
													label="Estado del proyecto"
													name="stateProject"
												>
													<Input disabled/>
												</Form.Item>
											</Col>
										</Row>
										<Row gutter={16}>
											<Col xs={5} sm={24} md={12} lg={12} xl={12}>
												<Row gutter={16}>
													<Col xs={5} sm={24} md={12} lg={12} xl={12}>
														<Form.Item label="País" name="country">
															<Select
																defaultValue="-"
																style={{width: "100%"}}
																onChange={onChangeCountry}
															>
																<Select.Option value="-">
																	Seleccionar...
																</Select.Option>
																{land.map((item, index) => {
																	return (
																		<Select.Option key={index} value={index}>
																			{item}
																		</Select.Option>
																	);
																})}
															</Select>
														</Form.Item>
													</Col>
													<Col xs={5} sm={24} md={12} lg={12} xl={12}>
														<Form.Item label="Departamento" name="department">
															<Select
																defaultValue="-"
																onChange={onChangeDepartment}
																style={{width: "100%"}}
																disabled={
																	form.getFieldValue("country") === undefined ||
																	form.getFieldValue("country") === "-"
																}
															>
																<Select.Option value="-">
																	Seleccionar...
																</Select.Option>
																{departments.map((item, index) => {
																	return (
																		<Select.Option key={index} value={index}>
																			{item}
																		</Select.Option>
																	);
																})}
															</Select>
														</Form.Item>
													</Col>
												</Row>
												<Row gutter={16}>
													<Col xs={5} sm={24} md={12} lg={12} xl={12}>
														<Form.Item label="Ciudad" name="city"

																   rules={[
																	   {
																		   required: true,
																		   message:
																			   "La ciudad es obligatoria.",
																	   },
																   ]
																   }

														>
															<Select
																defaultValue="-"
																onChange={onChangeMunicipality}
																disabled={
																	form.getFieldValue("department") === "" ||
																	form.getFieldValue("department") ===
																	undefined ||
																	form.getFieldValue("department") === "-"
																}
																style={{width: "100%"}}
															>
																<Select.Option value="-">
																	Seleccionar...
																</Select.Option>
																{municipalities.map((item, index) => {
																	return (
																		<Select.Option key={index} value={index}>
																			{item.name}
																		</Select.Option>
																	);
																})}
															</Select>
														</Form.Item>
													</Col>
													<Col xs={5} sm={24} md={12} lg={12} xl={12}>
														<Form.Item name="zone" label="Zona">
															<Input placeholder="Zona"/>
														</Form.Item>
													</Col>
												</Row>
												<Row gutter={16}>
													<Col xs={5} sm={24} md={24} lg={24} xl={24}>
														<Form.Item
															name="descriptionPoint"
															label="Descripción de punto"
														>
															<Input.TextArea
																placeholder="Descripción de punto"
																rows={3}
															/>
														</Form.Item>
													</Col>
												</Row>
											</Col>
											<Col xs={5} sm={24} md={12} lg={12} xl={12}>
												<MapQuest
													height="43.5vh"
													width="76vh"
													center={[lat, lng]}
													tileLayer={"map"}
													zoom={4.7}
													apiKey={"muDSpMXsg0x5Pjt4cyhAkuk0O4wjV8hR"}
												/>
											</Col>
										</Row>
										<br/>
										<Row gutter={22}>
											<Col xs={5} sm={24} md={5} lg={5} xl={8}>
												<Form.Item
													name="projectType"
													label="Tipos de proyectos"
													rules={[
														{
															required: true,
															message: "El tipo de proyecto es requerido.",
														},
													]}
												>
													<Select
														loading={projectTypes.length === 0}
														defaultValue=""
													>
														<Select.Option value="">
															Seleccionar...
														</Select.Option>
														{projectTypes.map((value) => {
															return (
																<Select.Option key={value.id} value={value.id}>
																	{value.name}
																</Select.Option>
															);
														})}
													</Select>
												</Form.Item>
											</Col>
											<Col xs={5} sm={24} md={10} lg={10} xl={8}>
												<Form.Item
													label="Clasificación"
													name="classification"
													rules={[
														{
															required: true,
															message: "La clasificación es requerida.",
														},
													]}
												>
													<Select
														loading={projectClassifications.length === 0}
														defaultValue="-"
													>
														<Select.Option value="-">
															Seleccionar...
														</Select.Option>
														{projectClassifications.map((value) => {
															return (
																<Select.Option key={value.id} value={value.id}>
																	{value.name}
																</Select.Option>
															);
														})}
													</Select>
												</Form.Item>
											</Col>
											<Row gutter={16}/>
											<Col xs={5} sm={24} md={3} lg={3} xl={7}>
												<Form.Item label="Total de unidades" name="totalUnits">
													<InputNumber
														placeholder="Total de unidades"
														min={0}
														style={{width: "100% "}}
													/>
												</Form.Item>
											</Col>
										</Row>
										<Row gutter={16}>
											<Col xs={5} sm={24} md={3} lg={3} xl={8}>
												<Form.Item valuePropName={0} label="Número de pisos" name="numberFloors">
													<InputNumber
														defaultValue={0}
														placeholder="Número de pisos"
														min={0}
														style={{width: "100%"}}
													/>
												</Form.Item>
											</Col>
											<Col xs={5} sm={24} md={4} lg={4} xl={8}>
												<Form.Item valuePropName={0}
													label="Parqueaderos sencillos"
													name="simpleParkingLots"
												>
													<InputNumber
														type="number"
														defaultValue={0}
														placeholder="Parqueaderos sencillos"
														min={0}
														style={{width: "100%"}}
													/>
												</Form.Item>
											</Col>
											<Col xs={5} sm={24} md={5} lg={5} xl={7}>
												<Form.Item
													label="Parqueaderos doble lineal"
													name="doubleLineaParkingLot"
												>
													<InputNumber
													   type="number"	
														defaultValue={0}
														placeholder="Parqueaderos doble lineal"
														min={0}
														style={{width: "100%"}}
													/>
												</Form.Item>
											</Col>
										</Row>
										<Row gutter={16}>
											<Col xs={5} sm={24} md={6} lg={6} xl={8}>
												<Form.Item
													label="Parqueadero de visitantes"
													name="parkingLotVisitors"
												>
													<Select
														defaultValue="2"
														onChange={setQuantityDisabled}
													>
														<Select.Option value="1">SI</Select.Option>
														<Select.Option value="2">NO</Select.Option>
													</Select>
												</Form.Item>
											</Col>
											<Col xs={5} sm={24} md={4} lg={4} xl={7}>
												<Form.Item
													label="Cantidad"
													name="quantity"
													rules={[
														{
															type: "number",
															min: 0,
															max: 999,
															message:
																"Cantidad maxima de parqueaderos excedida.",
														},
													]}
												>
													<InputNumber
														defaultValue={0}
														disabled={quantityDisabled !== "1"}
														style={{width: "100%"}}
													/>
												</Form.Item>
											</Col>
											<Col xs={5} sm={24} md={2} lg={2} xl={4}>
												<Form.Item
													valuePropName="checked"
													name="state"
													label="Estado"
												>
													<Checkbox checked={true}>Activo</Checkbox>
												</Form.Item>
											</Col>
										</Row>
										<Divider orientation="left">
											Línea de tiempo del proyecto
										</Divider>
										<Row gutter={16}>
											<Col xs={5} sm={24} md={5} lg={5} xl={11}>
												<Form.Item
													name="startDateSale"
													label="Fecha de inicio de la venta"
													rules={[
														{required: true, message: "Fecha requerida."},
													]}
												>
													<DatePicker
														placeholder="Fecha de inicio de la venta"
														onChange={captureStartDateSale}
														style={{width: "100%"}}
													/>
												</Form.Item>
											</Col>
											<Col xs={5} sm={24} md={7} lg={7} xl={11}>
												<Form.Item
													label="Fecha estimada de inicio de construcción"
													name="estimatedDateStartConstruction"
													rules={[
														{required: true, message: "Fecha Requerida"},
													]}
												>
													<DatePicker
														placeholder="Fecha estimada de inicio de construcción"
														onChange={captureEstimatedDateStartConstruction}
														style={{width: "100%"}}
													/>
												</Form.Item>
											</Col>
										</Row>
										<Row gutter={16}>
											<Col xs={5} sm={24} md={6} lg={6} xl={11}>
												<Form.Item
													label="Fecha estimada de entrega"
													name="estimatedDeliveryDate"
													rules={[
														{required: true, message: "Fecha Requerida"},
													]}
												>
													<DatePicker
														placeholder="Fecha estimada de entrega"
														onChange={captureEstimatedDeliveryDate}
														style={{width: "100%"}}
													/>
												</Form.Item>
											</Col>
											<Col xs={5} sm={24} md={6} lg={6} xl={11}>
												<Form.Item
													label="Fecha máxima para reformas"
													name="dateMaximumReforms"
													rules={[
														{required: true, message: "Fecha Requerida"},
													]}
												>
													<DatePicker
														placeholder="Fecha máxima para reformas"
														style={{width: "100%"}}
													/>
												</Form.Item>
											</Col>
										</Row>
										<Row gutter={16}>
											<Col xs={5} sm={24} md={8} lg={8} xl={20}>
												<Divider orientation="left">Documentos</Divider>
												<Form.Item
													valuePropName="fileList"
													name="uploadDocuments"
													getValueFromEvent={normFile}
												>
													<Upload
														action={RUTA_API_MULTIMEDIA}
														accept={VALID_DOCUMENT_UPLOAD}
														multiple
														name="uploadDocuments"
														maxCount={100}
													>
														<Button icon={<UploadOutlined/>}>
															Subir documentos
														</Button>
													</Upload>
												</Form.Item>
											</Col>
										</Row>
										<Row gutter={16}>
											<Col xs={24} sm={24} md={8} lg={8} xl={12}>
												<Divider orientation="left">Imágenes</Divider>
												<Form.Item
													name="uploadImages"
													valuePropName="fileList"
													getValueFromEvent={normFile}
												>
													<Upload
														action={RUTA_API_MULTIMEDIA}
														listType="picture-card"
														accept="image/*"
														multiple
														name="uploadImages"
														maxCount={100}
													>
														+ Subir
													</Upload>
												</Form.Item>
											</Col>
											<Col xs={24} sm={24} md={8} lg={8} xl={12}>
												<Divider orientation="left">
													Imágenes de promotores
												</Divider>
												<Form.Item
													valuePropName="fileList"
													name="promoterImages"
													getValueFromEvent={normFile}
												>
													<Upload
														action={RUTA_API_MULTIMEDIA}
														listType="picture-card"
														accept="image/*"
														multiple
														name="promoterImages"
														maxCount={100}
													>
														+ subir
													</Upload>
												</Form.Item>
											</Col>
										</Row>
										<Divider/>
										<Form.Item
											wrapperCol={{
												span: 12,
												offset: 21,
											}}
										>
											<Button size="long" type="primary" htmlType="submit">
												Guardar
											</Button>
										</Form.Item>
									</Col>
								</Row>
							</Form>
							{loading && <Spinner/>}
						</Card>
					</div>
				</>
			)}
		</>
	);
};
export default NewProject;
