import React, { useEffect, useState } from "react";
import { fetchAllProjects } from "../../../../redux/actions/Project/ProjectsAction";
import { fetchAllAssessor } from "../../../../redux/actions/Configuration/AssesorAction";
import { fetchAllAfiliatedCompany } from "../../../../redux/actions/Configuration/AfiliatedCompanyAction";
import { captureDate } from "../../../handle/HandleDatePicker/HandleDate";
import { useDispatch, useSelector } from 'react-redux';
import {Card, Col, Row, Form, Select, DatePicker, Button, Table, Popover} from 'antd';
import {success, warning} from "../../../handle/Notification/Notification";
import {fetchAllSalesReport} from "../../../../redux/actions/Report/Report_Sales/reportSalesAction";
import Spinner from "../../../handle/Spinner/Spinner";
import {FORMATTER_PESO} from "../../../../redux/constants";
import {convertAllMapDataState} from "../../../handle/HandleFilterTable/handleFilterTable";

const SalesPlan = ({ assessor}) => {

	const [idProject, setIdProject] = useState(null);
	const [idAssessor, setIdAssessor] = useState(null);
	const [idAfiliatedCompany, setIdAfiliatedCompany] = useState(null);
	const [startDate, setStartDate] = useState(null);
	const [endDate, setEndDate] = useState(null);

	const ListSales = useSelector(state => state?.reportSalesReducer);
	
	const [total, setTotal] = useState(null);
	

	const [visiblePopover, setVisiblePopover] = useState( false )
	const [show, setShow] = useState( false )
	const [consultind, setConsulting] = useState( false )
	
	const dispatch = useDispatch();
	
	useEffect(() => {
		dispatch(fetchAllProjects());
		dispatch(fetchAllAssessor());
		dispatch(fetchAllAfiliatedCompany());
	}, []);
	

	const selectProject = useSelector((state) => state.project);
	const selectAssessor = useSelector((state) => state.assessor);
	const selectAfiliatedCompany = useSelector((state) => state.afiliatedCompany);

	//const dataSalePlan = { idProject, idAssessor, idAfiliatedCompany, startDate, endDate }
	
	
	
	const elementAssessor = [];
	for (const key in assessor) {
		if (key === "_payload") {
			const assessors = assessor[key];
			for (let index = 0; index < assessors.length; index++) {
				elementAssessor.push(assessors[index]);
			}
		}
	}

	const style = {
		visibility: 'hidden'
	};
	
	const onFinish = () => {
		let data = {
			"business":  (idAfiliatedCompany && idAfiliatedCompany !== "") ? idAfiliatedCompany  : 0,
			"start_date":  startDate,
			"end_date": endDate,
			"project": (idProject && idProject !== "") ? idProject  : 0,
			"assessor": (idAssessor && idAssessor !== "") ? idAssessor  : 0
		}
		if(startDate > endDate){
			warning("Fecha incial mayor que la fecha final");
		}else{
			setConsulting(true);
			setShow(false);
			dispatch(fetchAllSalesReport(data, (response) => {
				if(!response.error){
					setShow(true);
					setTotal(response._payload.sales);
					success("Datos recuperados");
				}else{
					warning("No hay datos");
				}
				setConsulting(false);
			}, () =>{
				warning("No hay datos");
			}));	
		}
	};
	const columns = [
		{
			title: "mes*",
			width: 15,
			dataIndex: "mes",
			key: "mes",
			render: (text, record) => {
				return record.name_month
			}
		},
		{
			title: "Ventas Brutas (Und)",
			width: 20,
			dataIndex: "sales_quantity",
			key: "sales_quantity",
			render: (text, record) => {
				return record.sales_quantity
			}
		},
		{
			title: "Ventas Brutas (Pesos)",
			width: 20,
			dataIndex: "value_total_unit",
			key: "value_total_unit",
			render: (text, record) => {
				return FORMATTER_PESO.format(record.value_total_unit)
			}
		},
		{
			title: "Desistimientos (Und)",
			width: 20,
			dataIndex: "discontinued_sales",
			key: "discontinued_sales",
			render: (text, record) => {
				return record.discontinued_sales
			}
		},
		{
			title: "Desistimientos (Pesos)",
			width: 20,
			dataIndex: "value_total_unit_by_discontinued_sales",
			key: "value_total_unit_by_discontinued_sales",
			render: (text, record) => {
				return FORMATTER_PESO.format(record.value_total_unit_by_discontinued_sales)
			}
		},

		{
			title: "Ventas Netas (Pesos)",
			width: 20,
			dataIndex: "nate_sales",
			key: "nate_sales",
			render: (text, record) => {
				return FORMATTER_PESO.format(record.nate_sales)
			}
		},
	];
	const validateMessages = {
		required: "${label} es requerido",
		types: {
			email: "${label} no es un correo válido",
			number: "${label} debe ser un número",
		},
	};

	const tableDate = (<Table
		columns={columns}

		dataSource={convertAllMapDataState(ListSales?.sales?.sales)}
		scroll={{ x: 1900, y: 300 }}
		size='small'
		bordered={true}
	/>);
	
	return (
		<Card title="Informes de Ventas">
			<Form
				layout="vertical"
				onFinish={onFinish}
				validateMessages={validateMessages}
			>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={8} lg={8} xl={8}>
						<Form.Item label="Proyecto:" name="Proyecto:">
							<Select
								defaultValue={""}
								showSearch
								style={{ width: "100%" }}
								optionFilterProp="children"
								onChange={setIdProject}
								filterOption={(input, option) =>
									option.children.toLowerCase().indexOf(input.toLowerCase()) >=
									0
								}
							>
								<Select.Option value="">Seleccione...</Select.Option>
								{
									!Array.isArray(selectProject) && selectProject._payload?.map(item => (
										<Select.Option key={item?.id+item?.name+item?.id} value={item.id}>{` ${item.name} `}</Select.Option>
									))}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={8} lg={8} xl={8}>
						<Form.Item label="Empresa:" name="Empresa:">
							<Select
								defaultValue={""}
								showSearch
								style={{ width: "100%" }}
								optionFilterProp="children"
								onChange={setIdAfiliatedCompany}
								filterOption={(input, option) =>
									option.children.toLowerCase().indexOf(input.toLowerCase()) >=
									0
								}
							>
								<Select.Option value="">Seleccione...</Select.Option>
								{!Array.isArray(selectAfiliatedCompany) &&
									selectAfiliatedCompany._payload.map((item) => (
										<Select.Option key={item?.id+item?.name+item?.id} value={item.id}>{` ${item.name} `}</Select.Option>
									))}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={8} lg={8} xl={8}>
						<Form.Item label="Asesor:" name="Asesor:">
							<Select
								defaultValue={""}
								showSearch
								style={{ width: "100%" }}
								optionFilterProp="children"
								onChange={setIdAssessor}
								filterOption={(input, option) =>
									option.children.toLowerCase().indexOf(input.toLowerCase()) >=
									0
								}
							>
								<Select.Option value="">Seleccione...</Select.Option>
								{!Array.isArray(selectAssessor) &&
									selectAssessor._payload.map((item) => (
										<Select.Option key={item?.id+item?.name+item?.id} value={item.id}>{` ${item.name} `}</Select.Option>
									))}
							</Select>
						</Form.Item>
					</Col>
				</Row>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item
							label="Fecha Inicio:"
							name="Fecha Inicio:"
							rules={[
								{
									required: true,
								},
							]}
						>
							<DatePicker
								value={startDate}
								onChange={(value) => captureDate(value, setStartDate)}
								style={{ width: "100%" }}
							/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item
							label="Fecha Fin:"
							name="Fecha Fin:"
							rules={[
								{
									required: true,
								},
							]}
						>
							<DatePicker
								value={endDate}
								onChange={(value) => captureDate(value, setEndDate)}
								style={{ width: "100%" }}
							/>
						</Form.Item>
					</Col>
				</Row>
				<Row>
					<Col xs={18} sm={20} md={21} lg={22} xl={22} />
					<Col xs={2} sm={2} md={2} lg={2} xl={2}>
						<Form.Item>
							<Button type="primary" htmlType="submit">
								Buscar
							</Button>
						</Form.Item>
					</Col>
				</Row>
			</Form>
			<Row justify="end" aria-disabled={ListSales.path_excel != null}>
				<Col>
					{
						show ? (<Popover
							content={ <> <a href={ListSales.path_excel} target="_blank">Excel</a> </>}
							trigger="click"
							visible={visiblePopover}
							onVisibleChange={(state) => {setVisiblePopover(state)}}
						>
							<Button type="primary" style={{ background: "green"}} >Exportar</Button>
						</Popover>) : null
					}
				</Col>
			</Row>
			<br/>
			{
				show ? tableDate : consultind ? <Spinner/> : null
			}
			{ 
				show ? (<><h5>Totales:</h5> 
					<b>Ventas brutas (und)</b>:<spam style={style}>........</spam> {total?.sales_quantity}<br/>
						<b>Ventas brutas (pesos)</b>:<spam style={style}>....</spam> {FORMATTER_PESO.format(total?.value_total_unit)}<br/>
						<b>Desistimientos (und)</b>:<spam style={style}>......</spam> {total?.discontinued_sales}<br/>
						<b>Desistimientos (pesos)</b>:<spam style={style}>..</spam> {FORMATTER_PESO.format(total?.value_total_unit_by_discontinued_sales)}<br/>
					<b>Ventas Netas (Pesos)</b>:<spam style={style}>.....</spam> {FORMATTER_PESO.format(total?.nate_sales)}<br/></>) : null
			} 
			
		</Card>
	);
	
}




export default SalesPlan;
