import * as constants from '../../constants';

export const fetchAllPaymentRelationshipSetting = (id) => ({
    type: constants.API,
    payload: {
      method: 'GET',
      url: `${constants.RUTA}/paymentRelationshipSetting/${id}`,
      success: (response) => (setAllPaymentRelationshipSetting(response))
    }
});


export const creatPaymentRelationshipSetting = (id, data, onSuccess, onError) => ({
    type: constants.API,
    payload: {
        method: 'PUT',
        url: `${constants.RUTA}/paymentRelationshipSetting/${id}`,
        data,
        success: (data) => (addPaymentRelationshipSetting(data)),
        postProcessSuccess: onSuccess,
        postProcessError: onError
    }
});

export const resetPaymentRelationshipSettingAction = () => ({
    type: constants.API,
    payload: {
		method: null,
		success: () => (resetPaymentRelationshipSetting())
	}
})

const resetPaymentRelationshipSetting = () =>({
	type:constants.RESET_PAYMENT_RELATIONSHIP_SETTING,
	payload: []
});


const setAllPaymentRelationshipSetting = (data) => ({
    type: constants.SET_ALL_PAYMENT_RELATIONSHIP_SETTING,
    payload: data
});


const addPaymentRelationshipSetting = (data) => ({
    type: constants.ADD_PAYMENT_RELATIONSHIP_SETTING,
    payload: { data }
});