import React, { useEffect, useState } from "react";
import { Button, Card, Divider } from "antd";
import AssignedTechniciansComponent from "./AssignmentTechniciansAccountants/AssignedTechniciansComponent";
import AssignedCounterComponent from "./AssignmentTechniciansAccountants/AssignedCountersComponent";
import AssignedSiteDirectorsComponent from "./AssignmentTechniciansAccountants/AssignedSiteDirectorsComponent";
import { useDispatch, useSelector } from "react-redux";
import {fetchAllAssignmentTechnicians, updateOrCreateAssignmentTechniciansById} from "../../../../../../../redux/actions/Project/AssignmentTechniciansAccountantsAction/AssignmentTechniciansAction";
import {error, errorAssigment, success} from "../../../../../../handle/Notification/Notification";
import {fetchAllAssignmentSiteDirectors, updateOrCreateAssignmentSiteDirectorsById} from "../../../../../../../redux/actions/Project/AssignmentTechniciansAccountantsAction/AssignmentSiteDirectosAction";
import {fetchAllAssignmentAccountants, updateOrCreateAssignmentAccountantsById} from "../../../../../../../redux/actions/Project/AssignmentTechniciansAccountantsAction/AssigmentCountersAction";
import Spinner from "../../../../../../handle/Spinner/Spinner";

const MainAssignmentTechniciansAccountants = ({id}) => {
	

	useEffect(() => {
		dispatch(fetchAllAssignmentTechnicians(id));
		dispatch(fetchAllAssignmentAccountants(id));
		dispatch(fetchAllAssignmentSiteDirectors(id));
	}, []);

	const dispatch = useDispatch();

	const dataAssignedTechnician = useSelector((state) => state?.assignedTechnician);
	const dataAssignedAccountans = useSelector((state) => state?.assignedCounters);
	const dataAssignedSiteDirectors = useSelector((state) => state?.assignedSiteDirectos);

	useEffect(() => {
		if (dataAssignedTechnician) {
			setListAssignedTechnician(dataAssignedTechnician);
		}
	}, []);
	useEffect(() => {
		if (dataAssignedAccountans) {
			setListAssignedCounters(dataAssignedAccountans);
		}
	}, []);
	useEffect(() => {
		if (dataAssignedSiteDirectors) {
			setListAssignedSiteDirectors(dataAssignedSiteDirectors);
		}
	}, []);

	const dataSelectUsers = useSelector((state) => state?.users._payload);

	const [disabled, setDisabled] = useState(false);

	const [listAssignedTechnician, setListAssignedTechnician] = useState([]);
	const [listAssignedCounters, setListAssignedCounters] = useState([]);
	const [listAssignedSiteDirectors, setListAssignedSiteDirectors] = useState(
		[]
	);

	const validateNullOfArray = (listAssignedTechnician) => {
		return !(Array.isArray(listAssignedTechnician) &&  listAssignedTechnician.length>0);
	};
	
	const validateRepeatAssignedTechnician = (array) => {
		let found = 0;
		const validateRepeat = [...array];
		validateRepeat.forEach((item) => {
			let repeat = validateRepeat.filter(
				(itemFilter) => itemFilter.technician_id === item.technician_id
			);
			if (repeat.length > 1) {
				found = 1;
			}
		});

		return found === 1;

		
	};

	const validateRepeatAssignedAccountants = (array) => {
		let found = 0;
		const validateRepeat = [...array];
		validateRepeat.forEach((item) => {
			let repeat = validateRepeat.filter(
				(itemFilter) => itemFilter.accountant_id === item.accountant_id
			);
			if (repeat.length > 1) {
				found = 1;
			}
		});

		return found === 1;

		
	};
	const validateRepeatAssignedSiteDirectors = (array) => {
		let found = 0;
		const validateRepeat = [...array];
		validateRepeat.forEach((item) => {
			let repeat = validateRepeat.filter(
				(itemFilter) =>
					itemFilter.construction_director_id === item.construction_director_id
			);
			if (repeat.length > 1) {
				found = 1;
			}
		});

		return found === 1;

		
	};

	const cleanDtoAssigned = (array) => {
		let arrayWork = JSON.parse(JSON.stringify(array));
		arrayWork?.map((item) => {
			if (item.hasOwnProperty("created_at")) {
				delete item.created_at;
				delete item.updated_at;
				delete item.deleted_at;
				delete item.key;
			} else {
				delete item.key;
				item.id = null;
			}
			return item;
		});
		return arrayWork;
	};

	const handleClick = () => {
		if (
			!validateNullOfArray(listAssignedTechnician) ||
			!validateNullOfArray(listAssignedCounters) ||
			!validateNullOfArray(listAssignedSiteDirectors)
		){
			const dataToSendTechnician = cleanDtoAssigned(listAssignedTechnician);
			const dataToSendAccountant = cleanDtoAssigned(listAssignedCounters);
			const dataToSendSiteDirector = cleanDtoAssigned(listAssignedSiteDirectors);

			if (validateRepeatAssignedTechnician(dataToSendTechnician))
				return errorAssigment("El tecnico ya ha sido asignado!");
			if (validateRepeatAssignedAccountants(dataToSendAccountant))
				return errorAssigment("El contador ya ha sido asginado!");
			if (validateRepeatAssignedSiteDirectors(dataToSendSiteDirector))
				return errorAssigment("El director de obra ya ha sido asginado!");

			dispatch(
				updateOrCreateAssignmentTechniciansById(
					dataToSendTechnician,
					async (response) => {
						const res = await response;
						res.error ? error(res.message) : success("Tecnicos guardados");
						dispatch(fetchAllAssignmentTechnicians(id));
					}
				)
			);
			dispatch(
				updateOrCreateAssignmentAccountantsById(
					dataToSendAccountant,
					async (response) => {
						const res = await response;
						res.error ? error(res.message) : success("Contadores guardados");
						dispatch(fetchAllAssignmentAccountants(id));
					}
				)
			);
			dispatch(
				updateOrCreateAssignmentSiteDirectorsById(
					dataToSendSiteDirector,
					async (response) => {
						const res = await response;
						res.error
							? error(res.message)
							: success("Directores de Obra guardados");
						dispatch(fetchAllAssignmentSiteDirectors(id));
					}
				)
			);
		}else{
			return errorAssigment("No hay nada para guardar!");
		}
		
	};

	if (
		Array.isArray(dataAssignedTechnician) ||
		Array.isArray(dataAssignedSiteDirectors) ||
		Array.isArray(dataAssignedAccountans)
	)
		return (
			<Card
				title="Asignación de técnicos y contadores"
				style={{ borderColor: "blue" }}
			>
				<Spinner />
			</Card>
		);

	return (
		<>
			<Card
				title="Asignación de técnicos y contadores"
				style={{ borderColor: "blue" }}
			>
				{
					<>
						<AssignedTechniciansComponent
							setDisabled={setDisabled}
							setListAssignedTechnician={setListAssignedTechnician}
//							dataSelectUsers={dataSelectUsers.filter(x => x.name == process.env.REACT_APP_PROFILE_TECHNICAL)}
							dataSelectUsers={dataSelectUsers.filter(x => x.profile_id === 6)}
							dataAssignedTechnician={dataAssignedTechnician}
							projectId={id}
						/>
						<Divider/>
						<AssignedCounterComponent
//							dataSelectUsers={dataSelectUsers.filter(x => x.name == process.env.REACT_APP_PROFILE_ACCOUNTING)}
							dataSelectUsers={dataSelectUsers.filter(x => x.profile_id === 3)}
							setListAssignedCounters={setListAssignedCounters}
							dataAssignedAccountans={dataAssignedAccountans}
							projectId={id}
						/>
						<Divider/>
						<AssignedSiteDirectorsComponent
//							dataSelectUsers={dataSelectUsers.filter(x => x.name == process.env.REACT_APP_PROFILE_DIRECTORWORK)}
							dataSelectUsers={dataSelectUsers.filter(x => x.profile_id === 5)}
							setListAssignedSiteDirectors={setListAssignedSiteDirectors}
							dataAssignedSiteDirectors={dataAssignedSiteDirectors}
							projectId={id}
						/>
						<Divider/>
						<Button
							onClick={handleClick}
							disabled={disabled || JSON.parse(localStorage.stateDisable)}
							style={{ marginLeft: "80%" }}
							type="primary"
						>
							Guardar
						</Button>
					</>
				}
			</Card>
		</>
	);
};

export default MainAssignmentTechniciansAccountants;
