import React, {useEffect, useState} from "react";
import { Table, Card } from "antd";
import {convertAllMapDataState} from "../../../../../handle/HandleFilterTable/handleFilterTable";

const DescriptionProject = React.memo(({project}) => {
	const [dataSource, setDataSource] = useState([]);
	
	useEffect(()=>{
		data();
	},[project]);
	
	const columns = [
		{
			dataIndex: "descripcion",
			width: 80,
			height: 40,
		},
		{
			dataIndex: "value",
			width: 80,
			height: 40,
		},
	];
	
	const data = () => {
		setDataSource( [
			{
				descripcion: "Proyecto",
				value: project?.name
			},
			{
				descripcion : "Tipo de Unidad",
				value: project?.area + " m2"
			},
			{
				descripcion : "Etiqueta",
				value: project?.label
			},
			{
				descripcion : "Fecha Estimada de Entrega",
				value: project?.estimatedDeliveryDate
			},
			{
				descripcion : "Habitaciones",
				value: project?.bedrooms
			},
			{
				descripcion : "Baños",
				value: project?.toilets
			},
			{
				descripcion : "Parqueadero",
				value: project?.parking
			}
		]);
	}
	
	return (
		<>
			<Card
				headStyle={{
					color: "#31708f",
					backgroundColor: "#d9edf7",
					borderColor: "#bce8f1",
				}}
				title="Datos del Inmueble"
				style={{ marginRight: 20 }}
			>
				<Table
					style={{ width: "100%" }}
					pagination={false}
					columns={columns}
					dataSource={convertAllMapDataState(dataSource)}
					bordered
				/>
			</Card>
		</>
	);
});
export default DescriptionProject;
