import React from "react"
import {Button, Col, Form, Popover, Row, Table} from "antd";
import {getColumnSearchProps} from "../../../../handle/HandleFilterTable/handleFilterTable";
import {useSelector} from "react-redux";

const Content = ({excelGenerator,PDFGenerator}) => {
	const selectPotentialClientsReport = useSelector((state) => state?.potentialClientsReport?._payload);
	const column = [
		{
			title: 'Asesor',
			align: 'center',
			width: 80,
			dataIndex: 'adviser',
			key: 'adviser',
			...getColumnSearchProps('adviser')
		},
		{
			title: 'Proyecto',
			align: 'center',
			dataIndex: 'project',
			key: 'project',
			width: 90,
			...getColumnSearchProps('project')
		},
		{
			title: 'Cliente',
			align: 'center',
			dataIndex: 'customer',
			key: 'customer',
			width: 80,
			...getColumnSearchProps('customer')
		},
		{
			title: 'Fecha Próxima Actividad',
			align: 'center',
			dataIndex: 'nextActivityDate',
			key: 'nextActivityDate',
			width: 90,
			...getColumnSearchProps('nextActivityDate')
		},
		{
			title: 'Nombre Próxima Actividad',
			align: 'center',
			dataIndex: 'nameNextActivity',
			key: 'nameNextActivity',
			width: 100,
			...getColumnSearchProps('nameNextActivity')
		},
		{
			title: 'Último Contacto',
			align: 'center',
			dataIndex: 'lastContact',
			key: 'lastContact',
			width: 90,
			...getColumnSearchProps('lastContact')
		},
		{
			title: 'Última Actividad Realizada',
			align: 'center',
			dataIndex: 'nameLastContact',
			key: 'nameLastContact',
			width: 100,
			...getColumnSearchProps('nameLastContact')
		},
		{
			title: 'Nivel de Interés',
			align: 'center',
			dataIndex: 'interestLevel',
			key: 'interestLevel',
			width: 90,
			...getColumnSearchProps('interestLevel')
		},
		{
			title: 'Última Cotización', 
			align: 'center',
			dataIndex: 'LastQuote',
			key: 'LastQuote',
			width: 90,
		},
		{
			title: '¿Qué Busca?',
			align: 'center',
			dataIndex: 'WhatAreYouLookingFor',
			key: 'WhatAreYouLookingFor',
			width: 100,
			...getColumnSearchProps('WhatAreYouLookingFor'),
		}
	];
	return <Form.Item>
		<Row>
			<Col xs={17} sm={20} md={21} lg={22} xl={22}/>
			<Col xs={2} sm={2} md={2} lg={2} xl={2}>
				<Popover
					content={
						<div>
							<a style={{ marginRight: 21 }} onClick={excelGenerator}>Excel</a>
							<a onClick={PDFGenerator}>PDF</a>
						</div>
					}
					trigger="click"
				>
					<Button style={{ backgroundColor: '#5cb85c', borderColor: '#5cb85c' }} type="primary">Exportar</Button>
				</Popover>
			</Col>
		</Row>
		<br/>
		<Row>
			<Table 
				columns={column}
				dataSource={selectPotentialClientsReport}
				scroll={{ x: 1500, y: 800 }}
			/>
		</Row>
	</Form.Item>
}

export default Content