/* eslint-disable no-template-curly-in-string */
import React, { useEffect, useState } from "react";
import { Form, Input, Button, Select, Modal, Tooltip } from "antd";
import { SwapOutlined } from "@ant-design/icons";
import { useDispatch } from "react-redux";
import {
	errorGlobal,
	success,
} from "../../../../handle/Notification/Notification";
import {
	fetchAllSaleMade,
	updateReassignCustomerAction,
} from "../../../../../redux/actions/Price/SaleMade/SalesMadeAction";


const ReassignCustomer = ({ customerDatabase, data }) => {
	const [form] = Form.useForm();
	const dispatch = useDispatch();
	const [isModalVisible, setIsModalVisible] = useState(false);

	useEffect(() => {
		if (isModalVisible) {
			fillFields();
		}
	}, [isModalVisible]);

	const handleSubmit = async () => {
		const values = await form.validateFields();
		let dto = {
			id_customer: values["newCustomer"],
			code: values["newTrustUnit"] || null,
		};

		dispatch(
			updateReassignCustomerAction(data.id, dto, async (response) => {
				let res = await response;
				if (res.error) {
					errorGlobal("¡UPS, Ha corrido un error.");
				} else {
					success("Ha sido actualizado con éxito.");
					resetFields();
					onCancel();
				}
			})
		);

		dispatch(fetchAllSaleMade());
	};

	const fillFields = () => {
		form.setFieldsValue({
			actuallyCustomer: data.customer.person.name,
		});
	};

	const resetFields = () => {
		form.resetFields();
	};
	const showModal = () => {
		setIsModalVisible(true);
	};

	const onCancel = () => {
		setIsModalVisible(false);
	};

	const textReassignCustomer = (
		<span style={{ color: "#6A6963" }}>Cambiar cliente </span>
	);

	return (
		<>
			<Tooltip placement="left" color="#ffffff" title={textReassignCustomer}>
				<Button
					style={{ background: "#ffc53d", color: "#fff" }}
					size={"small"}
					onClick={showModal}
					icon={<SwapOutlined />}
					disabled = {data.is_desisted}
				/>
			</Tooltip>
			<Modal
				title="Cambiar cliente"
				centered
				visible={isModalVisible}
				footer={false}
				onCancel={() => setIsModalVisible(false)}
				width={800}
			>
				<Form
					form={form}
					name="nest-messages"
					layout="vertical"
					onFinish={handleSubmit}
				>
					<Form.Item label="Cliente actual" name="actuallyCustomer">
						<Input disabled={true} />
					</Form.Item>
					<Form.Item
						name="newCustomer"
						label="Nuevo cliente"
						rules={[{ required: true, message: "El campo es requerido." }]}
					>
						<Select defaultValue="">
							<Select.Option value="">Seleccionar...</Select.Option>
							{!Array.isArray(customerDatabase) &&
								customerDatabase._payload.map((item) => (
									<Select.Option key={item.id} value={item.id}>
										{item.name}
									</Select.Option>
								))}
						</Select>
					</Form.Item>
					<Form.Item name="newTrustUnit" label="Nueva fiducia unidad">
						<Input placeholder="Nueva fiducia unidad" />
					</Form.Item>
					<Form.Item>
						<div style={{ position: "absolute", right: 1, display: "flex" }}>
							<Button
								type="primary"
								htmlType="submit"
								style={{ marginRight: "10px" }}
							>
								Guardar
							</Button>
							<Button type="primary" onClick={onCancel}>
								Finalizar
							</Button>
						</div>
					</Form.Item>
				</Form>
			</Modal>
		</>
	);
};

export default ReassignCustomer;
