import React from 'react';
import { Tabs } from 'antd';
import { Divider } from "antd";
import CustomerAndUnitSummary from "./CustomerAndUnitSummary";
import GeneralAccountStatement from "./GeneralAccountStatement";
import DetailToDate from "./DetailToDate";
import PaymentPlan from "./PaymentPlan";

const { TabPane } = Tabs;


const PaginationTables = ({data}) => {
  return (
    <Divider orientation="left">Tablas</Divider>,
    <Tabs defaultActiveKey="1">
      <TabPane
        tab={
          <span>
            Resumen de Usuario
          </span>
        }
        key="1"
      >
        <CustomerAndUnitSummary 
					infoCustomer={data?.info_customer} InfoUnit={data?.info_unit} 
					agreedValue={data?.general_account_statement_info?.value_total_unit?.agreed_value}
				/>
      </TabPane>
      <TabPane
        tab={
          <span>
            Estado de Cuenta Generales
          </span>
        }
        key="2"
      >
        <GeneralAccountStatement generalAccountStatementInfo={data?.general_account_statement_info} />
      </TabPane>
      <TabPane
        tab={
          <span>
            Detalle a la Fecha
          </span>
        }
        key="3"
      >
        <DetailToDate detailToDate={data?.detail_to_date}/>
      </TabPane>
      <TabPane
        tab={
          <span>
            Plan de Pagos
          </span>
        }
        key="4"
      >
        <PaymentPlan paymentPlan={data?.payment_plan}/>
      </TabPane>
    </Tabs>
  );
}

export default PaginationTables;