import React from "react";
import { Modal } from "antd";

const ImageUnit = ({ visible, handleOk, handleCancel, data }) => {
	return (
		<Modal
			okText={"Cerrar"}
			visible={visible}
			onOk={handleOk}
			onCancel={handleCancel}
			title={data.name}
			width={1100}
		>
			<h4>Imágenes</h4>
			<div style={{ display: "flex", flexFlow: "wrap" }}>
				{data.images?.length > 0 ? (
					data.images?.map((item) => (
						<div
							style={{
								width: "320px",
								margin: 10,
								padding: 5,
								border: "1px solid navy",
								borderColor: "navy",
							}}
						>
							<a href={item.path} target={"_blank"} rel="noreferrer">
								<img
									style={{ backgroundSize: "cover" }}
									width="290px"
									src={item.path}
								 alt={""}/>
							</a>
						</div>
					))
				) : (
					<h6>No hay imágenes cargadas.</h6>
				)}
			</div>
			<hr />
			<h4>Planos</h4>
			<div style={{ display: "flex", overflow: "auto " }}>
				{data.blueprints?.length > 0 ? (
					data.blueprints?.map((item) => (
						<a href={item.path} target="_blank" rel="noreferrer">
							<img width="300" height="300" src={item.path}  alt={""}/>
						</a>
					))
				) : (
					<h6>No hay planos cargados.</h6>
				)}
			</div>
		</Modal>
	);
};

export default ImageUnit;
