import {
	CalcularEntregaGeneralCliente, calularDescuentoOIncremto,
	findTheCurrentMonthAndYearCreate, limpiarNULLs, SumatoryValueCoutes,
	valorPresenteCuotas
} from "../../../../handle/Formulas/Quote";
import {useEffect, useState} from "react";

const months = {
	0: "Enero", 1: "Febrero", 2: "Marzo", 3: "Abril",
	4: "Mayo", 5: "Junio", 6: "Julio", 7: "Agosto",
	8: "Septiembre", 9: "Octubre", 10: "Noviembre", 11: "Diciembre"
}

export const useSaleMade = () => {
	const data = {
		0: "Ene-", 1: "Feb-", 2: "Mar-", 3: "Abr-",
		4: "May-", 5: "Jun-", 6: "Jul-", 7: "Ago-",
		8: "Sep-", 9: "Oct-", 10: "Nov-", 11: "Dic-",
	}
	const valueParking = (parking,saleParameter,parkingNumber) => {
		if (parseInt(parkingNumber) !== 0){
			if (parking === "1"){
				return parseFloat(saleParameter.simple_parking_value)
			}else {
				return parseFloat(saleParameter.double_parking_value)
			}
		}
		return 0;
	}
	const formatDate = (date) => {
		let fetch = new Date(date);
		let month = fetch.getMonth();
		let day = fetch.getDay();
		let year = fetch.getFullYear();
		return day +' de '+ months[month]+' de '+year;
	}
	const selectParking = (parking) => {
		switch (parking) {
			case "0":
				return ""
			case "1":
				return " Sencillo"
			default:
				return " Doble"
		}
	}
	const removeItemFromArr = (arr,item) => {
		return arr.filter((e)=>{
			return e !== item;
		});
	}
	const [response,setResponse] = useState({
		discount: [], coupons: [], modified_values: []
	});
	const [projectSetting,setProjectSetting] = useState({
		valueKit:0,unitValue:0,initialFeeNotFundedPercentage:0,initialFeeFinancedPercentage:0,flatTax:0,
		constructionTax:0,finishesIncluded:false
	});
	const [project,setProject] = useState({
		name: '', area: 0, label: '', estimatedDeliveryDate:'', bedrooms: 0,
		toilets: 0, parking: ''
	});
	const [included,setIncluded] = useState({
		valueParking: 0, valueKit: 0, valueOthers: 0, heightPremium: 0, sightCousin: 0
	});
	const [propertyData,setPropertyData] = useState({
		valueM:0,tradePolicies:false,Coupon:false,initialValue:0,discountFinishes:0,discountCoupons:0,
		discountCouponsPrevious:0,commercialDiscounts:0,previousCommercialDiscount:0,unitValue:0,includesFinishes:false
	});
	const [paymentMethodProposedByTheProject,setPaymentMethodProposedByTheProject] = useState({
		initialFeeNotFunded:0,initialFeeNotFundedPercentage:0,initialFeeFinanced:0,initialFeeFinancedPercentage:0,
		formOfPayments: [{key: 0,month:'',value:0,porcentaje:0,tipo:''}],unitValue:0,presentValue:0,commercialDiscounts:0,
		discountCoupons:0,discountFinishes:0,finishesIncluded:false
	});
	const [paymentMethodProposedByTheClient,setPaymentMethodProposedByTheClient] = useState({
		initialFeeNotFunded:0,initialFeeNotFundedPercentage:0,initialFeeFinanced:0,initialFeeFinancedPercentage:0,
		formOfPayments: [{key: 0,month:'',value:0,porcentaje:0,tipo:''}],unitValue:0,presentValue:0,commercialDiscounts:0,
		discountCoupons:0,discountFinishes:0,finishesIncluded:false,financialDiscountOrIncrease:0
	});
	const [dataDates,setDataDates] = useState({
		mes: 0, year: 0, mesConstruccion: 0, mesPlano: 0,mesTotal:0
	});
	const [taxStatements,setTaxStatements] = useState({
		flatTax:0,constructionTax:0
	});
	
	useEffect(() => {
		modifyFormsOfPayment();
	}, [projectSetting]);
	
	const initialization = (data) => {
		let quotation = data.quotation;
		let unit = quotation.unit;
		let unitType = unit['unit_type'];
		let project = unitType.project;
		const date = findTheCurrentMonthAndYearCreate(
			quotation['way_to_pay'].created_at,
			project.start_build_date,
			project.estimated_delivery_date,
			project.payment_relationship_setting.maximum_number_months_finance_initial_fee
		);
		const formOfPaymentsTheClient = createPaymentsImposedByTheProject(
			parseFloat(data.total_unfunded_initial_fee_value),
			data.amount_dues,
			parseFloat(data['amount_dues_value']),
			parseFloat(data.value_total_unit),
			(parseFloat(data.total_funded_initial_fee_value) + parseFloat(data.total_unfunded_initial_fee_value)),
			date
		);
		const formOfPayments = createPaymentsImposedByTheProject(
			parseFloat(data.total_unfunded_initial_fee_value),
			quotation['way_to_pay'].amount_dues,
			parseFloat(quotation['way_to_pay'].value),
			parseFloat(data.value_total_unit),
			(parseFloat(data.total_funded_initial_fee_value) + parseFloat(data.total_unfunded_initial_fee_value)),
			date
		);
		setDataDates(date);
		setTaxStatements({
			flatTax: parseFloat(project['sale_parameter'].interest_on_plans_percentage)/100,
			constructionTax: parseFloat(project['sale_parameter'].interest_in_construction_percentage)/100
		})
		setProject({
			name: project.name, area: parseFloat(unitType.area),
			label: unit.label, estimatedDeliveryDate: formatDate(project.estimated_delivery_date),
			bedrooms: parseInt(unitType.number_rooms), toilets: parseInt(unitType.bathroom_number),
			parking: selectParking(unit.parking_number)
		});
		setProjectSetting({
			valueKit: parseFloat(unitType.finish_kit_value),unitValue: parseFloat(data.value_total_unit),
			initialFeeNotFundedPercentage: parseInt(project.payment_relationship_setting.initial_fee_promise_signature_trust_percentage),
			initialFeeFinancedPercentage: parseInt(project.payment_relationship_setting.initial_fee_finance_in_construction_percentage),
			flatTax: parseFloat(project['sale_parameter'].interest_on_plans_percentage)/100,
			constructionTax: parseFloat(project['sale_parameter'].interest_in_construction_percentage)/100,
			finishesIncluded: (data.finishes_included === '1')
		})
		setIncluded({
			valueParking: valueParking(unit.parking,project['sale_parameter'],unit.parking_number),
			valueKit: parseFloat(unitType.finish_kit_value), valueOthers: parseFloat(unitType.value_other_additional),
			heightPremium: parseFloat(unit.bonus_view), sightCousin: parseFloat(unit.bonus_height)
		});
		setPropertyData({
			valueM: parseFloat(data.value_m2), 
			tradePolicies: false, 
			Coupon: false,
			initialValue: parseFloat(quotation.value_initial_unit),
			discountFinishes:parseFloat(unitType.finish_kit_value),
			discountCoupons: parseFloat(data.value_coupons),
			discountCouponsPrevious:0,
			commercialDiscounts: parseFloat(data.value_discounts),
			previousCommercialDiscount:0,
			unitValue: parseFloat(data.value_total_unit), 
			includesFinishes: (data.finishes_included !== '0')

		});
		const valorCuotas = valorPresenteCuotas(
			formOfPayments,
			undefined,
			parseFloat(project['sale_parameter'].interest_on_plans_percentage)/100,
			parseFloat(project['sale_parameter'].interest_in_construction_percentage)/100
		);
		setPaymentMethodProposedByTheProject({
			...paymentMethodProposedByTheProject,
			initialFeeNotFunded: parseFloat(data.total_unfunded_initial_fee_value),
			initialFeeNotFundedPercentage: parseInt(project.payment_relationship_setting.initial_fee_promise_signature_trust_percentage),
			initialFeeFinanced: parseFloat(data.total_funded_initial_fee_value),
			initialFeeFinancedPercentage: parseInt(project.payment_relationship_setting.initial_fee_finance_in_construction_percentage),
			unitValue: parseFloat(data.value_total_unit),
			presentValue:valorCuotas,
			commercialDiscounts: parseFloat(data.value_discounts),
			discountCoupons: parseFloat(data.value_coupons),
			discountFinishes: unitType.finish_kit_value,
			finishesIncluded: (data.finishes_included === '1'),
			financialDiscountOrIncrease: 0,
			formOfPayments
		});
		const modifyQuota = modifyQuotas(
			JSON.parse(JSON.stringify(formOfPaymentsTheClient)),
			quotation['way_to_pay']['way_to_pay_amounts'],
			parseFloat(data.value_total_unit)
		);
		/*
		const valorPresente = valorPresenteCuotas(
			modifyQuota,
			undefined,
			parseFloat(project['sale_parameter'].interest_on_plans_percentage)/100,
			parseFloat(project['sale_parameter'].interest_in_construction_percentage)/100
		);
	 */
		const entregaGeneralCliente=CalcularEntregaGeneralCliente(paymentMethodProposedByTheProject.presentValue,formOfPayments,undefined,taxStatements.flatTax,taxStatements.constructionTax);
		const presentValueGivenCliente = valorPresenteCuotas(formOfPayments,entregaGeneralCliente,taxStatements.flatTax,taxStatements.constructionTax);
		const filterEntregaCliente =formOfPayments?.filter(x=>x.month!== 'Entrega');
		const sumatoryValueCoutesCliente=parseFloat(SumatoryValueCoutes(filterEntregaCliente)+entregaGeneralCliente);
		const financialDiscountOrIncreaseCliente = (-1)*(calularDescuentoOIncremto (paymentMethodProposedByTheProject.unitValue,sumatoryValueCoutesCliente));
		const initialFeeFinanced = calculateInitialFeeFinanced(modifyQuota);
		
		setPaymentMethodProposedByTheClient({
			...paymentMethodProposedByTheClient,
			initialFeeNotFunded: parseFloat(data.total_unfunded_initial_fee_value),
			initialFeeNotFundedPercentage: parseFloat(project.payment_relationship_setting.initial_fee_promise_signature_trust_percentage),
			initialFeeFinanced: Math.round(initialFeeFinanced),
			initialFeeFinancedPercentage: calculatePor(Math.round(initialFeeFinanced), parseFloat(data.value_total_unit)),
			unitValue: parseFloat(data.value_total_unit),
			presentValue: parseFloat(presentValueGivenCliente),
			commercialDiscounts: parseFloat(data.value_discounts),
			discountCoupons: parseFloat(data.value_coupons),
			discountFinishes: unitType.finish_kit_value,
			finishesIncluded: (data.finishes_included === '1'),
			financialDiscountOrIncrease:financialDiscountOrIncreaseCliente,
			formOfPayments: JSON.parse(JSON.stringify(modifyQuota))
		});
	}

	const modifyFormsOfPayment = () => {
		let date = {...dataDates};
		const initialFeeNotFunded = (projectSetting.initialFeeNotFundedPercentage / 100) * projectSetting.unitValue;
		const initialFeeFinanced = (projectSetting.initialFeeFinancedPercentage / 100) * projectSetting.unitValue;
		const formOfPayments = createPaymentsImposedByTheProject(
			initialFeeNotFunded,dataDates.mesTotal,(initialFeeFinanced / dataDates.mesTotal),projectSetting.unitValue,
			(initialFeeFinanced + initialFeeNotFunded),date
		);
		const presentValue = valorPresenteCuotas(formOfPayments,undefined,	projectSetting.flatTax,	projectSetting.constructionTax);
		
		const entregaGeneral2=CalcularEntregaGeneralCliente(paymentMethodProposedByTheProject.presentValue,formOfPayments,undefined,taxStatements.flatTax,taxStatements.constructionTax);
		const presentValueGiven2 = valorPresenteCuotas(formOfPayments,entregaGeneral2,taxStatements.flatTax,taxStatements.constructionTax);
		const filterEntrega2 =formOfPayments?.filter(x=>x.month!== 'Entrega');
		const sumatoryValueCoutes2=parseFloat(SumatoryValueCoutes(filterEntrega2)+entregaGeneral2);
		const financialDiscountOrIncrease2 = (-1)*(calularDescuentoOIncremto (paymentMethodProposedByTheProject.unitValue,sumatoryValueCoutes2));
		
		setPaymentMethodProposedByTheProject({
			...paymentMethodProposedByTheProject,
			unitValue: projectSetting.unitValue,
			initialFeeNotFunded,
			initialFeeFinanced, 
			formOfPayments: JSON.parse(JSON.stringify(formOfPayments)),
			presentValue: presentValue,
			finishesIncluded: projectSetting.finishesIncluded
		});
		setPaymentMethodProposedByTheClient({
			...paymentMethodProposedByTheClient,
			unitValue: projectSetting.unitValue,
			formOfPayments: JSON.parse(JSON.stringify(formOfPayments)),
			presentValue:presentValueGiven2,
			initialFeeNotFunded: parseFloat(JSON.parse(JSON.stringify(initialFeeNotFunded))),
			initialFeeFinanced: parseFloat(JSON.parse(JSON.stringify(initialFeeFinanced))),
			initialFeeNotFundedPercentage: projectSetting.initialFeeNotFundedPercentage,
			financialDiscountOrIncrease: financialDiscountOrIncrease2
		});
	}
	let i=0;
	const createPaymentsImposedByTheProject = (initialFeeNotFunded,amountDues,value,valueUnit,delivery,date) => {
		if(i==0){
			date.mesPlano=date.mesPlano-1;
			i++;
		}
		const percentage = calculatePor(value,valueUnit);
		let element = [
			{
				key: 0,
				month: "C.I. No Financiada",
				value: parseFloat(initialFeeNotFunded),
				porcentaje: parseFloat(calculatePor(initialFeeNotFunded,valueUnit)),
				tipo: date.mesPlano > 0 ? 'Presente' : 'Construccion'
			},
		];
		for (let i = 1;i <= parseInt(amountDues);i++ ){
			if (date.mes > 11) {
				date.mes = 0;
				date.year++;
			}
			element = element.concat({
				key: i,
				month: data[date.mes] + date.year,
				value: parseFloat(parseFloat(value).toFixed(2)),
				porcentaje: parseFloat(percentage),
				tipo: i >= date.mesPlano ? 'Construccion' : 'Presente'
			});
			date.mes++;
		}
		element = element.concat({
			key: parseInt(amountDues) + 1,
			month: "Entrega",
			value: parseFloat(valueUnit) - parseFloat(delivery),
			porcentaje: parseFloat(calculatePor((parseFloat(valueUnit) - parseFloat(delivery)),parseFloat(valueUnit))),
			tipo: element[element.length-1].tipo
		});
		return element;
	}
	
	const modifyQuotas = (formOfPayments,wayToPayAmounts,valueUnit) => {
		let formOfPayment =  formOfPayments.map((payment) => {
			wayToPayAmounts.forEach((amount) => {
				if (payment.month === amount.month) {
					payment.value = parseFloat(amount.value);
					payment.porcentaje = calculatePor(payment.value,valueUnit)
				}
			});
			return payment;
		});
		formOfPayment[formOfPayment.length - 1].value = CalculateDelivery(formOfPayment,valueUnit);
		formOfPayment[formOfPayment.length - 1].porcentaje = calculatePor(formOfPayment[formOfPayment.length - 1].value,valueUnit);
		return formOfPayment;
	}
	
	const CalculateDelivery = (formOfPayments,valueUnit) => {
		limpiarNULLs(formOfPayments);
		let contador = 0;
		formOfPayments.forEach((payment)=>{
			if (payment?.month !== 'Entrega') contador += parseFloat(payment?.value);
		});
		return valueUnit - contador;
	}
	
	//cambios de estados para la tablas de cupones y descuentos
	const changeStateTableIncluded = () => {
		setPropertyData({
			...propertyData,
			tradePolicies: !propertyData.tradePolicies
		});
	}
	
	const changeStateTableCoupon = () => {
		setPropertyData({
			...propertyData,
			Coupon: !propertyData.Coupon
		});
	}
	
	//descuentos del estado de kit de acabados
	const onChangeKit = (event) => {
		let data = {...propertyData}
		if (event.target.checked){
			data.unitValue = data.unitValue + included.valueKit;
		}else {
			data.unitValue = data.unitValue - included.valueKit;
		}
		data.includesFinishes = event.target.checked;
		setPropertyData(data);
		setProjectSetting({
			...projectSetting,
			unitValue: parseFloat(JSON.parse(JSON.stringify(data.unitValue))),
			finishesIncluded: event.target.checked
		});
	}
	
	const recalculatePaymentPlan = () => {
		modifyFormsOfPayment();
	}
	
	const calculatePor = (valor, valorTotal) => {
		let percentage = (parseFloat(valor) * 100) / parseFloat(valorTotal);
		if (valorTotal === 0) return 0;
		if (valor === 0 || valorTotal === 0) return 0;
		return percentage;
	};
	
	const discountsSet = (value,key,checked,discountType) => {
		let discount = [];
		let coupons = [];
		if (response.coupons !== []) coupons = response.coupons;
		if (response.discount !== []) discount = response.discount;
		let respuesta = {...response};
		const data = {...propertyData};
		const valor = parseFloat(value) * parseFloat(project.area);
		if (discountType === 'Valor m2'){
			if (checked){
				discount.push(key);
				data.valueM = data.valueM - parseFloat(value);
				data.unitValue = data.unitValue - valor;
				data.commercialDiscounts += parseFloat(valor);
			}else {
				discount = removeItemFromArr(respuesta.discount,key);
				data.valueM = data.valueM + parseFloat(value);
				data.unitValue = data.unitValue + valor;
				data.commercialDiscounts -= parseFloat(valor);
			}
		}
		if (discountType === 'Valor total'){
			if (checked){
				discount.push(key);
				data.unitValue = data.unitValue - parseFloat(value);
				data.commercialDiscounts += parseFloat(value);
			}else {
				discount = removeItemFromArr(respuesta.discount,key);
				data.unitValue = data.unitValue + parseFloat(value);
				data.commercialDiscounts -= parseFloat(value);
			}
		}
		if (discountType === 'Cupon'){
			if (checked){
				coupons.push(key);
				data.discountCoupons += parseFloat(value);
				data.unitValue = data.unitValue - parseFloat(value);
			}else {
				coupons = removeItemFromArr(respuesta.coupons,key);
				data.discountCoupons -= parseFloat(value);
				data.unitValue = data.unitValue + parseFloat(value);
			}
		}
	
		respuesta.discount = discount;
		respuesta.coupons = coupons;
		setResponse(respuesta);
		setPropertyData(data);
		setProjectSetting({
			...projectSetting,
			unitValue: parseFloat(JSON.parse(JSON.stringify(data.unitValue)))
		});
	}
	
	const calculateInitialFeeFinanced = (formOfPayments) => {
		let valor = 0;
		formOfPayments.map((payment)=>{
			if (payment.month !== 'C.I. No Financiada' && payment.month !== 'Entrega'){
				valor += parseFloat(payment.value)
			}
		});
		return valor;
	}
	
	const calculatePresentValueByTheClient = (formOfPayments,deliveryDate) => {
		const entregaGeneral=CalcularEntregaGeneralCliente(paymentMethodProposedByTheProject.presentValue,formOfPayments,undefined,taxStatements.flatTax,taxStatements.constructionTax);
		const presentValueGiven = valorPresenteCuotas(formOfPayments,entregaGeneral,taxStatements.flatTax,taxStatements.constructionTax);
		const filterEntrega =formOfPayments?.filter(x=>x.month!== 'Entrega');
		const sumatoryValueCoutes=parseFloat(SumatoryValueCoutes(filterEntrega)+entregaGeneral);
		const financialDiscountOrIncrease = (-1)*(calularDescuentoOIncremto (paymentMethodProposedByTheProject.unitValue,sumatoryValueCoutes));
		const initialFeeFinanced = Math.round(calculateInitialFeeFinanced(formOfPayments));
		const initialFeeFinancedPercentage = 
			calculatePor(initialFeeFinanced,(parseFloat(paymentMethodProposedByTheProject.unitValue)));
		setPaymentMethodProposedByTheClient({
			...paymentMethodProposedByTheClient,
			presentValue: presentValueGiven,
			financialDiscountOrIncrease: financialDiscountOrIncrease,
			initialFeeFinanced,
			unitValue: Math.round(sumatoryValueCoutes),
			initialFeeFinancedPercentage,
			formOfPayments: formOfPayments,
			finishesIncluded: projectSetting.finishesIncluded
		});
	}
	
	const modified_values = (record) => {
		const arrayToWork = {...response};
		arrayToWork.modified_values = record;
		setResponse(arrayToWork);
	};
	
	return {
		//states
		project,included,propertyData,response,paymentMethodProposedByTheProject,paymentMethodProposedByTheClient,
		data,setPaymentMethodProposedByTheClient,
		//functions
		initialization,changeStateTableIncluded,onChangeKit,changeStateTableCoupon,recalculatePaymentPlan,discountsSet,
		selectParking,CalculateDelivery,modified_values,calculatePor,calculatePresentValueByTheClient
	}
}