import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { ClockCircleOutlined } from "@ant-design/icons";
import Spinner from '../../../../handle/Spinner/Spinner';
import {Form, Button, Row, Col, Modal, Card, Divider, Table} from 'antd';
import {errorGlobal,success, warning} from "../../../../handle/Notification/Notification";
import { getColumnSearchProps } from '../../../../handle/HandleFilterTable/handleFilterTable';
import { getAllLastPaymentFiduciaryCollections } from '../../../../../redux/actions/Collection/TrustCollectionAction';



const LastPayment = () => {
    const [visible, setVisible] = useState(false);
    const [stateLoading, setStateLoading] = useState(true);
    const [form] = Form.useForm();
    const dispatch = useDispatch();
	
	

	const lastPaymentfiduciarytrustColletio = useSelector((state) => state?.lastPaymentfiduciarytrustColletion._payload);
    
    const columns = [
        {
            title: " #",
            dataIndex: "num",
            key: "num",
            width: 10,
            align: "center",
            fixed: "left",
            render: (_,record)=>{
               return lastPaymentfiduciarytrustColletio.indexOf(record)+1;
            }
        },
        {
            title: 'Proyecto',
            width: 40,
            dataIndex: 'project_name',
            key: 'project_name',
            ...getColumnSearchProps('project_name'),
        },
        {
            title: 'Fecha de Último Abono',
            width: 50,
            dataIndex: 'last_payment_fiduciary',
            key: 'last_payment_fiduciary',
            ...getColumnSearchProps('last_payment_fiduciary'),
        }
    ];

    const showModal = () => {
        setVisible(true)
		dispatch(
			getAllLastPaymentFiduciaryCollections(async (response) => {
				const res = await response;
				if(!res){
					warning("No hay Datos en la tabla")
				}else if( res.error === true){
					errorGlobal("No hay Datos en la tabla")
				}else{
					success("Carga Completa")
				}
				setStateLoading(false);
			})
		)
    }

    const onClose = () => {
        setVisible(false);
    }

    return (
        <>
            <Button
                type="primary" onClick={showModal} icon={<ClockCircleOutlined />}
            >
                Últimos Abonos
            </Button>
            <Modal
                visible={visible}
                footer={false}
                width={900}
                onCancel={onClose}
                title="Últimos Abonos"
            >
                <Form
                    layout="vertical"
                    size="middle"
                    form={form}
                >
                    <Card style={{ marginTop: 20 }} >
                    {
                        stateLoading?
                        <Spinner/>
                        :
                        <Table columns={columns} dataSource={lastPaymentfiduciarytrustColletio} size={"small"}/>                    
                    }
                        
                        <Divider />
                        <Row justify="end">
                            <Col>
                                <Button
                                    type="primary"
                                    onClick={onClose}
                                >
                                    Finalizar
                                </Button>
                            </Col>
                        </Row>
                    </Card>
                </Form>
            </Modal>
        </>
    )
}

export default LastPayment;
