import { Divider } from "antd";
import React, { useContext } from "react";
import PropertyDataContext from "./context/PropertyDataContext";
import SelectedUnitsContext from "./context/SelectedUnitsContext";
import AvailableProjects from "./availableProjects/AvailableProjects";
import DescriptionCotizacion from "./propertyData/DescriptionCotizacion";
import SelectedUnitComponent from "./selectedUnit/SelectedUnitComponent";

const TabsProyects = ({dataPro,customerId}) => {

	const { selectedUnits } =
		useContext(SelectedUnitsContext);

	const { propertyData } =
		useContext(PropertyDataContext);

	const isObjEmpty = (obj) => {
		return Object.keys(obj).length === 0;
	}
	
	return (
		<div >
			<AvailableProjects projects = {dataPro} customerId={customerId} />
			<Divider />
			{selectedUnits.length > 0 && <SelectedUnitComponent />}
			<Divider />
			{!isObjEmpty(propertyData) && <DescriptionCotizacion />}
		</div>
	);
};

export default TabsProyects;
