import React from 'react'
import {Switch, Route, Redirect} from "react-router-dom";
import Dashboard from "./../../pages/dashboard/index";
// settings
import Profiles from "./../../pages/settings/profiles/index";
import Users from "./../../pages/settings/users/index";
import Advisers from "./../../pages/settings/advisers/index";
import GeneralActivities from "./../../pages/settings/generalActivities/index";
import Affiliates from "./../../pages/settings/affiliates/index";
import PlacesOfCare from "./../../pages/settings/placesOfCare/index";
import Channels from "./../../pages/settings/channels/index";
import RecordType from "./../../pages/settings/recordType/index";
import TypeActivities from "./../../pages/settings/typeActivities/index";
import Occupation from "../../pages/settings/occupation/index";
import PriceRange from "./../../pages/settings/priceRange/index";
import AdvertisingImages from "./../../pages/settings/advertisingImages/index";
import Modules from "./../../pages/settings/modules/index";
import SystemAudit from "./../../pages/settings/systemAudit/index";
import Mistakes from "./../../pages/settings/mistakes/index";
import GeneralSettings from "./../../pages/settings/generalSettings/index";
//projects
import Projects from "./../../pages/projects/listProjects/index";
import NewProject from "../../components/Projects/Projects/components/NewProject";

import ControlPowers from "./../../pages/projects/controlPowers/index";
import NewControlCompetences from "../../pages/projects/controlPowers/NewControlCompetences";
//customer
import Client from "./../../pages/clients/listClient/index";
import CustomerTracking from "./../../pages/clients/customerTracking/index";
//sales
import QuotesPage from "../../pages/sales/quotes/Quotes";
import NewQuotesPage from '../../pages/sales/quotes/NewQuotes'
import SalesMade from "./../../pages/sales/salesMade/index";
import ReformRequests from "./../../pages/sales/reformRequests/index";
import AfterSales from "./../../pages/sales/afterSales/index";
import CouponsPage from "./../../pages/sales/coupons/index";
import CommissionVerification from "./../../pages/sales/commissionVerification/index";
import UpdateQuote from "../../pages/sales/quotes/UpdateQuote";
//collections
import DirectCollections from "./../../pages/collections/directCollections/index";
import TrustFunds from "./../../pages/collections/trustFunds/index";
//report
import SalesReport from "./../../pages/reports/salesReport/index";
import SalesPlan from "./../../pages/reports/salesPlan/index";
import CompetitionReport from "./../../pages/reports/competitionReport/index";
import ReformReport from "./../../pages/reports/reformReport/index";
import ActivityTracking from "./../../pages/reports/activityTracking/index";
import PotentialCustomers from "./../../pages/reports/potentialCustomers/index";
import AccountStatusReport from "./../../pages/reports/accountStatusReport/index";
import LegalizationAndDeedReport from "./../../pages/reports/legalizationAndDeedReport/index";
import CollectionFlowReport from "./../../pages/reports/collectionFlowReport/index";
import PortfolioMonitoringReport from "./../../pages/reports/portfolioMonitoringReport/index";
import CommitteeReport from "./../../pages/reports/committeeReport/index";
import GeneralCommercialManagementReport from "./../../pages/reports/generalCommercialManagementReport/index";
import AdvisoryManagementReport from "./../../pages/reports/advisoryManagementReport/index";
import AdvertisingEffectivenessReport from "./../../pages/reports/advertisingEffectivenessReport/index";
import BuyersDatabase from "./../../pages/reports/buyersDatabase/index";
import PotentialClientsDatabase from "./../../pages/reports/potentialClientsDatabase/index";
import ProjectManagementReport from "./../../pages/reports/projectManagementReport/index";
import AuditReport from "./../../pages/reports/auditReport/index";
import CommissionsPaymentMinutes from "./../../pages/reports/commissionsPaymentMinutes/index";
import Tabs from "../../components/Projects/Projects/components/UpdatedProject/Tabs"
import UpdateControlCompetences from '../../pages/projects/controlPowers/UpdateControlCompetences';
import EditSalesMadePage from '../../pages/sales/salesMade/EditSalesMadePage';

const Routes = () => {
	
	return (
		<Switch>
			{/* dashboard */}
			<Route exact path="/dashboard" component={Dashboard}/>
			{/* settings */}
			<Route exact path="/settings/profiles" component={Profiles}/>
			<Route exact path="/settings/users" component={Users}/>
			<Route exact path="/settings/advisers" component={Advisers}/>
			<Route
				exact
				path="/settings/general-activities"
				component={GeneralActivities}
			/>
			<Route exact path="/settings/affiliates" component={Affiliates}/>
			<Route
				exact
				path="/settings/places-of-care"
				component={PlacesOfCare}
			/>
			<Route exact path="/settings/channels" component={Channels}/>
			<Route
				exact
				path="/settings/record-type"
				component={RecordType}
			/>
			<Route
				exact
				path="/settings/type-activities"
				component={TypeActivities}
			/>
			<Route exact
			       path="/settings/occupation"
			       component={Occupation}
			/>
			<Route
				exact
				path="/settings/price-range"
				component={PriceRange}
			/>
			<Route
				exact
				path="/settings/advertising-images"
				component={AdvertisingImages}
			/>
			<Route exact path="/settings/modules" component={Modules}/>
			<Route
				exact
				path="/settings/system-audit"
				component={SystemAudit}
			/>
			<Route exact path="/settings/mistakes" component={Mistakes}/>
			<Route
				exact
				path="/settings/general-settings"
				component={GeneralSettings}
			/>
			{/* projects */}
			<Route
				exact
				path="/projects"
				component={Projects}
				children={null}
				
			/>
			<Route
				exact
				path="/projects/new-project"
				component={NewProject}
			/>
			<Route
				exact
				path="/projects/edit-project/:id"
				component={Tabs}
			/>
			<Route
				exact
				path="/projects/control-powers"
				component={ControlPowers}
			/>
			<Route
				exact
				path="/projects/new-control-competences"
				component={NewControlCompetences}
			/>
			<Route
				exact
				path="/projects/edit-control-competences/:id"
				component={UpdateControlCompetences}
			/>
			{/* customer */}
			<Route
				exact
				path="/customer/client"
				component={Client}
			/>
			<Route
				exact
				path="/customer/customer-tracking/:id"
				component={CustomerTracking}
			/>
			<Route
				exact
				path="/customer/customer-tracking"
				component={CustomerTracking}
			/>
			{/* sales */}
			<Route exact path="/sales/quotes" component={QuotesPage}/>
			<Route exact path="/sales/new-quotes" component={NewQuotesPage}/>
			<Route exact path="/sales/sales-made" component={SalesMade}/>
			<Route exact path="/sales/update-sales-made/:id" component={EditSalesMadePage}/>
			<Route
				exact
				path="/sales/reform-requests"
				component={ReformRequests}
			/>
			<Route exact path="/sales/after-sales" component={AfterSales}/>
			<Route exact path="/sales/coupons" component={CouponsPage}/>
			<Route
				exact
				path="/sales/commission-verification"
				component={CommissionVerification}
			/>
			<Route exact path="/sales/update-quote/:id" component={UpdateQuote}/>
			{/* collections */}
			<Route
				exact
				path="/collections/direct-collections"
				component={DirectCollections}
			/>
			<Route
				exact
				path="/collections/trust-funds"
				component={TrustFunds}
			/>
			{/* report */}
			<Route
				exact
				path="/report/sales-report"
				component={SalesReport}
			/>
			<Route exact path="/report/sales-plan" component={SalesPlan}/>
			<Route
				exact
				path="/report/competition-report"
				component={CompetitionReport}
			/>
			<Route
				exact
				path="/report/reform-report"
				component={ReformReport}
			/>
			<Route
				exact
				path="/report/activity-tracking"
				component={ActivityTracking}
			/>
			<Route
				exact
				path="/report/potential-customers"
				component={PotentialCustomers}
			/>
			<Route
				exact
				path="/report/account-status-report"
				component={AccountStatusReport}
			/>
			<Route
				exact
				path="/report/legalization-and-deed-report"
				component={LegalizationAndDeedReport}
			/>
			<Route
				exact
				path="/report/collection-flow-report"
				component={CollectionFlowReport}
			/>
			<Route
				exact
				path="/report/portfolio-monitoring-report"
				component={PortfolioMonitoringReport}
			/>
			<Route
				exact
				path="/report/committee-report"
				component={CommitteeReport}
			/>
			<Route
				exact
				path="/report/general-commercial-management-report"
				component={GeneralCommercialManagementReport}
			/>
			<Route
				exact
				path="/report/advisory-management-report"
				component={AdvisoryManagementReport}
			/>
			<Route
				exact
				path="/report/advertising-effectiveness-report"
				component={AdvertisingEffectivenessReport}
			/>
			<Route
				exact
				path="/report/buyers-database"
				component={BuyersDatabase}
			/>
			<Route
				exact
				path="/report/potential-clients-database"
				component={PotentialClientsDatabase}
			/>
			<Route
				exact
				path="/report/project-management-report"
				component={ProjectManagementReport}
			/>
			<Route
				exact
				path="/report/audit-report"
				component={AuditReport}
			/>
			<Route
				exact
				path="/report/commissions-payment-minutes"
				component={CommissionsPaymentMinutes}
			/>
			{/* Redirect */}
			<Redirect to="/dashboard"/>
		</Switch>
	)
}

export default Routes