import * as constants from '../../../constants';

export default function deedReducer(state = [], action) {
    switch (action.type) {
        case constants.ADD_DEED:
            return action.payload;
        case constants.UPDATE_DEED:
            return action.state;
        case constants.GET_DEED_BY_ID:
            return action.payload
        default:
			      return state;
    }
}