import moment from 'moment';
import SendEmail from './SendEmail';
import Card from '../../../Card/Card';
import {Button, Spin, Table, Tooltip} from "antd";
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import PaymentVerification from './PaymentVerification';
import * as constants from "../../../../../redux/constants";
import ModalAdjustmentCollection from './ModalAdjustmentCollection';
import { errorGlobal } from "../../../../handle/Notification/Notification";
import { formatterMoney } from '../../../../handle/FormatterMoney/FormatterMoney';
import { validatePermission } from "../../../../handle/PermissionMethods/PermissionMethods";
import { getColumnSearchProps, noWhiteWindow } from '../../../../handle/HandleFilterTable/handleFilterTable';
import { InfoOutlined, CheckOutlined, CloseOutlined, SettingOutlined } from "@ant-design/icons";
import { getAllDirectCollections } from '../../../../../redux/actions/Collection/DirectCollectionAction';

const TableDirectCollection = () => {
	const [loading, setLoading] = useState(false);
	const [visibleAdjustment, setVisibleAdjustment] = useState(false);
	const [dataRecord, setDataRecord] = useState(null)

	const dispatch = useDispatch();
	const handleGetAllDirectCollections = () =>{
		setLoading(true);
		dispatch(getAllDirectCollections(async (response) => {
			const res = await response;
			if (res.error) {
				errorGlobal("¡UPS! Ocurrió un error mientras se registraba el recaudo");
			}
			setLoading(false);
		}))
	}

	useEffect(
		() => handleGetAllDirectCollections(),
		[]
	);

	const directColletions = useSelector((state) => state?.directColletion._payload);

	const handleCancel = () => {
		setVisibleAdjustment(false);
		setLoading(true);
		dispatch(
			getAllDirectCollections(async (response) => {
				const res = await response
				// eslint-disable-next-line no-unused-expressions
				res.error ? errorGlobal("¡UPS! Ocurrió un error mientras se registraba el recaudo") : null
				setLoading(false);
			})
		)
	}

	const showAdjustmetModal = (record) => {
		setVisibleAdjustment(true);
		setDataRecord(record);
	}

	const columns = [
		{
			title: 'Cliente',
			width: 100,
			dataIndex: 'name',
			key: 'name',
			align: "center",
			render: (name) => {
				let newName = name.split("_");
				return newName[0] + " " + newName[1]
			}
		},
		{
			title: 'Proyecto',
			width: 50,
			dataIndex: 'project_name',
			key: 'project_name',
			align: "center",
			...getColumnSearchProps('project_name'),...noWhiteWindow
		},
		{
			title: 'Unidad',
			dataIndex: 'unit_label',
			key: 'unit_label',
			width: 20,
			align: "center",
			...getColumnSearchProps('unit_label'),...noWhiteWindow
		},
		{
			title: 'Fecha consignación',
			dataIndex: 'consignment_date',
			key: 'consignment_date',
			width: 40,
			align: "center",
			render: (consignment_date) => {	return moment(consignment_date).format('DD/MM/YYYY');}

		},
		{
			title: 'Valor consignación',
			dataIndex: 'consignment_amount',
			key: 'consignment_amount',
			width: 40,
			align: "center",
			// eslint-disable-next-line jsx-a11y/anchor-is-valid
			render: (_, record) => {
				return <a href={record?.path_multimedia} style={{  borderBottom: "1px solid"}} target="_blank" rel="noreferrer">{formatterMoney.format(record.consignment_amount)}</a>	
			}
		},
		{
			title: 'Concepto',
			dataIndex: 'consignment_concept',
			key: 'consignment_concept',
			align: "center",
			width: 40,
			render: (consignment_concept) => (consignment_concept === 1) ? "Abono" : (consignment_concept === 2) ? 'Recaudo' : "Otros"
		},
		{
			title: 'Verificado',
			width: 20,
			align: 'center',
			dataIndex: 'verified',
			render: (_, record) => getState(record?.verified),
		},
		{
			title: 'Enviado a cliente',
			width: 30,
			align: 'center',
			dataIndex: 'SentToCustomer',
			render: (_, record) => getState(record?.sent_to_customer),
		},
		{
			title: 'Acciones',
			key: 'acciones',
			fixed: 'right',
			align: "center",
			width: 120,
			render: (_, record) => getActions(record),
		},
	];

	const getState = (something) => {
		if (parseInt(something) === 1) {
			return <CheckOutlined style={{ fontSize: '18px', color: '#0D6B04' }} />
		} else {
			return <CloseOutlined style={{ fontSize: '18px', color: '#f5222d' }} />
		}
	}
	const getActions = (record) => {
		const text = <span
			style={{ color: '#6A6963' }}> {(record?.consignment_concept === 1) ? "Reformas" : (record?.consignment_concept === 2) ? 'Abono' : "Otros"}
			<hr />
			Creado: {record.created_at} <br />  Modificado: {record.updated_at} </span>

		const textAdjustment = <span style={{ color: '#6A6963' }}> Ajustes </span>
		return (
			<>
				{validatePermission(constants.VERIFY_DIRECT_COLLECTION_PERMISSION) ? (
					<PaymentVerification data={record} onSave={handleGetAllDirectCollections}/>
				) : null}
				{"  "}
				{validatePermission(constants.UPDATE_DIRECT_COLLECTION_PERMISSION) ? (
					<Tooltip placement="left" color='#ffffff' title={textAdjustment}>
						<Button
							onClick={() => showAdjustmetModal(record)}
							style={{ background: '#52c41a', color: '#fff' }}
							size={'small'}
							icon={<SettingOutlined />}
						/>
					</Tooltip>
				) : null}

				{"  "}
				{validatePermission(constants.SEND_2_ACCOUNTANCY_COLLECTION_PERMISSION) ? (
					<SendEmail data={record} />
				) : null}
				{"  "}
				<Tooltip placement="left" color='#ffffff' title={text}>
					<Button size="small" type="primary" icon={<InfoOutlined />}/>
				</Tooltip>
			</>
		);
	};


	return (
		<>
			<Card
				title="Recaudos directos"
				table={
						<Spin style={{ paddingLeft: "46%" }} spinning={loading} size="large" >
							<Table
								columns={columns}
								dataSource={directColletions}
							/>
						</Spin>
				}
			/>
			{visibleAdjustment &&
				<ModalAdjustmentCollection onCancel={handleCancel} onClose={setVisibleAdjustment} data={dataRecord} onSave={handleGetAllDirectCollections} />}
		</>

	);
}

export default TableDirectCollection