import React from "react";
import {Button, Tooltip} from "antd";
import {useDispatch} from "react-redux";
import {PrinterOutlined} from "@ant-design/icons";
import {printView} from "../../../../../../../redux/actions/Price/Quotation/QuotesAction";


const SeePrint = ({record}) => {

	const dispatch = useDispatch()
	const textSeePrint = <span style={{ color: '#6A6963' }}> Ver / Imprimir </span>

	const viewPrint = () => {
		dispatch(
			printView(record.id, async (response) => {
				const res = await response;
				const tagA = document.createElement("a");
				tagA.href = `${res._payload.path}`;
				tagA.target = "_blank";
				document.body.appendChild(tagA);
				tagA.click();
			})
		);
	}
	
	return(
		<Tooltip placement="left" color='#ffffff' title={textSeePrint}>
			<Button
				size="small"
				onClick={()=>viewPrint()}
				style={{ backgroundColor: '#2cc02c', marginTop: 3 }}
				icon={<PrinterOutlined style={{ color: '#fff' }}/>}
			/>
		</Tooltip>
	)
}
export default SeePrint