import * as constants from "./../../constants";

export const fetchAllControlPowers = () => ({
	type: constants.API,
	payload: {
		method: "GET",
		url: `${constants.RUTA}/competingProject`,
		success: (response) => setAllControlPowers(response),
	},
});

export const createControlPower = (data, onSuccess) => ({
	type: constants.API,
	payload: {
		method: "POST",
		url: `${constants.RUTA}/competingProject`,
		data,
		success: (controlPower) => addControlPower(controlPower),
		postProcessSuccess: onSuccess,
	},
});

export const getControlPowerById = (id, onSuccess) => ({
	type: constants.API,
	payload: {
		method: "GET",
		url: `${constants.RUTA}/competingProject/${id}`,
		postProcessSuccess: onSuccess,
	},
});

export const updateControlPowerById = (id, data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: "PUT",
		url: `${constants.RUTA}/competingProject/${id}`,
		data,
		success: (id, data) => updateControlPower(id, data),
		postProcessSuccess: onSuccess,
		postProcessError: onError,
	},
});

export const deleteControlPowerById = (id, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: "DELETE",
		url: `${constants.RUTA}/competingProject/${id}`,
		success: () => removeControlPower(id),
		postProcessSuccess: onSuccess,
		postProcessError: onError,
	},
});

export const getCompetingReportFilter = (data, onSuccess,onError) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		url: `${constants.RUTA}/competingProjectSearchs`,
		data,
		success: (response) => (getDataReportCompeting(response)),
		postProcessSuccess: onSuccess
		,postProcessError: onError
	}
});


export const getCompetingReportExcel = (data, onSuccess) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		url: `${constants.RUTA}/competingProjectSearchs/EXCEL`,
		data,
		postProcessSuccess: onSuccess
	}
});

export const getCompetingReportPDF = (data, onSuccess) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		url: `${constants.RUTA}/competingProjectSearchs/PDF`,
		data,
		postProcessSuccess: onSuccess
	}
});

const addControlPower = (data) => ({
	type: constants.ADD_CONTROL_POWER,
	payload: data,
});

const getDataReportCompeting = (data) => ({
	type: constants.GET_REPORT_COMPETING,
	payload: data,
});

const setAllControlPowers = (data) => ({
	type: constants.SET_ALL_CONTROL_POWERS,
	payload: data,
});

const updateControlPower = (id, data) => ({
	type: constants.UPDATE_CONTROL_POWER,
	payload: { id, data },
});

const removeControlPower = (id) => ({
	type: constants.REMOVE_CONTROL_POWER,
	payload: id,
});
