import "./../css/quotes.scss";
import "./../css/propertyData.scss";
import {Step} from "@material-ui/core";
import {useDispatch} from "react-redux";
import {Redirect} from "react-router-dom";
import TabsProyects from "./ProjectAvailability";
import React, {useContext, useEffect, useState} from "react";
import StepClienteQuotes from "./createQuote/StepClienteQuotes";
import StepSearchProject from "./createQuote/StepSearchProject";
import PropertyDataContext from "./context/PropertyDataContext";
import {SelectedDataProvider} from "./context/SelectedUnitsContext";
import {Card, Col, Divider, Form, Select, Steps, Row, Button} from "antd";
import {quotesSaveData} from "../../../../../redux/actions/Price/Quotation/QuotesAction";
import {errorGlobal, success, warning} from "../../../../handle/Notification/Notification";
import {getCustomerByIdWithAssesor} from "../../../../../redux/actions/Customer/CustomerAction";

const layout = {
	labelCol: {
		span: 32,
	},
	wrapperCol: {
		span: 23,
	},
}

const StepsForm = () => {
	const dispatch = useDispatch()
	const [form] = Form.useForm()
	const [idChannel,setIdChannel] = useState(0)
	const [selectCustomer, setSelectCustomer] = useState("")
	const [current, setCurrent] = useState(0)
	const [projects, setProjects] = useState([])
	const [show, setShow] = useState(true)
	const [interestLevel, setInterestLevel] = useState("")
	const [acavado, setAcavado] = useState(false)
	const {propertyData} = useContext(PropertyDataContext)

	useEffect( () => {
		getCustomer()
	},[selectCustomer])

	const getCustomer = () => {
		if (selectCustomer !== "") {
			dispatch(
				getCustomerByIdWithAssesor(selectCustomer, async (response) => {
					const res = await response
					if (!res.error) {
						fillFields(res._payload)
					}
				})
			);
		}
	}
	
	const projectsSet = (value) => {
		setProjects(value)
	}
	
	const showSet = (value) => {
		setShow(value)
	}

	const fillFields = (customer) => {
		let names = customer.name.split("_")
		form.setFieldsValue({
			nombre: names[0],
			apellido: names[1],
			télefono: customer.phone_number,
			email: customer.email,
			ultimoContacto:
				customer.assessor !== null ? customer.assessor.updated_at : "",
			nombreAsesor: customer.assessor != null ? customer.assessor.name : "",
			empresaAssessor:
				customer.assessor !== null ? customer.assessor.affiliated_company : "",
			télefonoAssessor:
				customer.assessor !== null ? customer.assessor.phone_number : "",
			emailAssessor: customer.assessor != null ? customer.assessor.email : "",
		})
	}
	
	const selectCustomerSet = (value) => {
		setSelectCustomer(value)
	}
	const idChannelSet = (value) => {
		setIdChannel(value)
	}
	const prev = () => {
		setCurrent(current - 1);
	}
	
	const intersNiel = (
		<Row>
			<Col xs={24} sm={24} md={10} lg={10} xl={17}/>
			<Col xs={24} sm={24} md={10} lg={10} xl={7}>
				<Form.Item
					name="nivelIneres"
					label="Nivel de Interés"
					rules={[{required: true, message: "Campo requerido"}]}
				>
					<Select
						placeholder="Seleccione un nivel de interés"
						onChange={setInterestLevel}
						defaultValue={""}
						showSearch
						style={{width: "100%"}}
						optionFilterProp="children"
					>
						<Select.Option value="">Seleccione</Select.Option>
						<Select.Option  value="1">Bajo</Select.Option>
						<Select.Option value="2">Medio</Select.Option>
						<Select.Option value="3">Alto</Select.Option>
					</Select>
				</Form.Item>
			</Col>
		</Row>
	)
	const saveData = () => {
		let data = propertyData.response
		if (!data){
			warning("Debe salecionar una unidad")
		}else {
			if (selectCustomer == null) {
				errorGlobal("debe de selecionar un cliente");
				return null;
			}
			if (interestLevel == null) {
				errorGlobal("debe de selecionar un cliente");
				return null;
			}
			Object.assign(data, {
				channel_id: idChannel,
				customer_id: selectCustomer,
				interest_level: parseInt(interestLevel),
				email_sent: 0,
				assesor_id: (JSON.parse(localStorage.getItem('USER_INFO'))).id
			});
			dispatch(
				quotesSaveData(data, (response) => {
					if (response.error) {
						if (response.code === 199) {
							warning(response.message);
							setAcavado(true);
						} else {
							errorGlobal("¡UPS! Ha ocurrido un error");
						}
					} else {
						success("Cotización creada");
						setAcavado(true);
					}
				})
			);
		}
		
	}
	
	const isDisable = () => {
		if (idChannel !== 0 && selectCustomer !== ""){
			return false
		}
	}
	
	const Botton = (
		<Row>
			<Col xs={14} sm={24} md={10} lg={10} xl={22}/>
			<Col xs={2} sm={24} md={10} lg={10} xl={1}>
				<Form.Item>
					<Button
						onClick={()=>{saveData();}}
						type="danger"
						disabled={
							isDisable()
						}
					>
						Guardar
					</Button>
				</Form.Item>
			</Col>
		</Row>
	)
	
	const formStep = (current) => {
		switch (current) {
			case 0:
				return (
					<StepClienteQuotes selectCustomerSet={selectCustomerSet} idChannelSet={idChannelSet}/>
				)
			case 1:
				return (
					<>
						<StepSearchProject prev={prev} Customer={selectCustomer} projectsSet={projectsSet} showSet={showSet}/>
						<br/>
						<Divider/>
						{
							show ? (<span/>) : (
								<SelectedDataProvider>
									<TabsProyects dataPro={projects} customerId={selectCustomer} />
								</SelectedDataProvider>
							)
						}
						
						{intersNiel}
						<Divider/>
						{Botton}
					</>
				)
		}
	}

	const steps = [
		{
			title: "Paso N1",
			content: formStep(0),
			subTitle: "firstStep",
			status: "first",
			description: "stepNumberone",
		},
		{
			title: "Paso N2",
			content: formStep(1),
			subTitle: "secondStep",
			status: "second",
			description: "stepNumberTwo",
		},
	]

	const next = async () => {
		const values = await form.getFieldValue()
		if (values.nombreAsesor) {
			if (values["channelFormItem"] && values["customerItemForm"]) {
				setCurrent(current + 1)
			}
		} else {
			errorGlobal("El cliente no tiene un asesor asignado")
		}
	}

	if (acavado) return <Redirect exact to="/sales/quotes"/>;
	
	return (
		<>
			<Form {...layout} name="formSelectCliente" form={form} onFinish={next} layout="vertical">
				<Card>
					<Steps
						current={current}
						type="navigation"
						className="site-navigation-steps"
					>
						{
							steps.map((item) =>(
								<Step
									className="stepQuotes"
									status={item.status}
									key={item.title}
									title={item.title}
								/>
							))
						}
						<div className="steps-content2">{steps[current].content}</div>
					</Steps>
				</Card>
			</Form>
		</>
	)
  
}

export default StepsForm