import { useDispatch } from "react-redux";
import React, { useEffect, useState } from "react";
import Spinner from "../../../../../handle/Spinner/Spinner";
import TableAssignResponsible from "./TableAssignResponsible";
import { fetchAllCustomer } from "../../../../../../redux/actions/Customer/CustomerAction";
import { setAllAssignResponsibleAction } from "../../../../../../redux/actions/Price/SaleMade/assignResponsibleAction";

const AssignResponsibleContainer = ({ isModalVisible, data, handleCancel }) => {
	const dispatch = useDispatch();

	const [assignResponsibleDatabase, setAssignResponsibleDatabase] = useState(
		[]
	);

	useEffect(() => {
		if (isModalVisible) {
			dispatch(
				setAllAssignResponsibleAction(data.id, async (response) => {
					const res = await response;
					setAssignResponsibleDatabase(res);
				})
			);
			dispatch(fetchAllCustomer());
		}
	}, [isModalVisible]);

	return (
		<>
			{Array.isArray(assignResponsibleDatabase) && isModalVisible ? (
				<Spinner />
			) : (
				<TableAssignResponsible
					id={data.id}
					handleCancel={handleCancel}
					assignResponsibleDatabase={assignResponsibleDatabase}
					isModalVisible={isModalVisible}
				/>
			)}
		</>
	);
};

export default AssignResponsibleContainer;
