import * as constants from "../../../constants";

export const fetchAllSaleMade = () => ({
	type: constants.API,
	payload: {
		method: "GET",
		headers: constants.TOKEN,
		url: `${constants.RUTA}/salesmade`,
		success: (response) => setAllSaleMade(response),
	},
});

export const fetchSaleById = (id, onSuccess,onError) => ({
	type: constants.API,
	payload: {
		method: "GET",
		headers: constants.TOKEN,
		url: `${constants.RUTA}/sales2/${id}`,
		postProcessSuccess: onSuccess,
		postProcessError: onError
	},
});

export const fetchAllExchangesAction = (data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: "POST",
		headers: constants.TOKEN,
		url: `${constants.RUTA}/salesmade/coupons`,
		data,
		success: (response) => fetchAllExchange(response),
		postProcessSuccess: onSuccess,
		postProcessError: onError,
	},
});

export const updateReassignCustomerAction = (id, data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: "PUT",
		headers: constants.TOKEN,
		url: `${constants.RUTA}/salesmade/switch-customer-in-sale/${id}`,
		data,
		success: (response) => updateReassignCustomer(response),
		postProcessSuccess: onSuccess,
		postProcessError: onError,
	},
});



export const updateSaleById=(id, data, onSuccess)=> ({
	type: constants.API,
	payload: {
		method: "PUT",
		headers: constants.TOKEN,
		url: `${constants.RUTA}/quotation/update_sale/${id}`,
		data,
		success: (response) => updateSale(response),
		postProcessSuccess: onSuccess,
	},
})


export const applyExchangeAction = (data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: "POST",
		headers: constants.TOKEN,
		url: `${constants.RUTA}/salesmade/exchanges-coupons`,
		data,
		success: (response) => applyExchange(response),
		postProcessSuccess: onSuccess,
		postProcessError: onError,
	},
});

export const updateDataSale= (id, data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: "PUT",
		headers: constants.TOKEN,
		url: `${constants.RUTA}/sales2/${id}`,
		data,
		success: (response) => dateSaleUpdate(response),
		postProcessSuccess: onSuccess,
		postProcessError: onError,
	},
});



const dateSaleUpdate=(data)=> ({
	type: constants.DATA_SALE_UPDATE,
	payload: data,
})

const applyExchange = (data) => ({
	type: constants.APPLY_EXCHANGE,
	payload: data,
});

const setAllSaleMade = (data) => ({
	type: constants.SET_ALL_SALE_MADE,
	payload: data,
});

const updateReassignCustomer = (data) => ({
	type: constants.CHANGE_CUSTOMER,
	payload: data,
});

const updateSale = (data) => ({
	type: constants.UPDATE_SALE,
	payload: data,
});

const fetchAllExchange = (data) => ({
	type: constants.SET_ALL_EXCHANGES,
	payload: data,
});
