import React from "react";
import { PageHeader } from "antd";
import FormCuopons from "./Components/FormCuopons";
import TableCoupons from "./Components/TableCoupons";
import * as constants from "../../../../redux/constants";
import { Redirect } from "react-router-dom/cjs/react-router-dom.min";
import { validateModule, validatePermission } from "../../../handle/PermissionMethods/PermissionMethods";

const Quotes = () => {
	if (!validateModule(constants.COUPONS_SUBMODULE)) {
		return <Redirect to={constants.DEFAULT_ROUTE} />;
	}
	return (
		<React.Fragment>
			<PageHeader
				style={{ border: "1px solid rgb(235, 237, 240);" }}
				title={"Cupones"}
				extra={[
					validatePermission(constants.CREATE_COUPON_PERMISSION) ? (
						<FormCuopons />
					) : null
				]}
			/>
			<TableCoupons />
		</React.Fragment>
	);
};

export default Quotes;
