import { QuestionCircleOutlined } from "@ant-design/icons";
import {Button, Popconfirm, Popover, Table, Tag, Tooltip} from "antd";
import React, {useEffect} from "react"
import {useDispatch, useSelector} from "react-redux";
import {getQuotationSeparated, updateQuotationSeparated} from "../../../../../redux/actions/Price/Quotation/QuotesAction";
import {convertAllMapDataState, getColumnSearchProps} from "../../../../handle/HandleFilterTable/handleFilterTable";
import { errorGlobal, success } from "../../../../handle/Notification/Notification";

const SeparateUnits = () => {
	const quotations = useSelector(state => state['quotation']._payload);
	const dispatch = useDispatch();
	
	useEffect(()=>{
		dispatch(getQuotationSeparated(async (response) => {
			await response;
		}));
	},[])
	
	function getTagDaysRemaining(days_remaining) {
		if (days_remaining >= 0) {
			return <Tag color={"green"}> {days_remaining} </Tag>;
		} else {
			return <Tag color={"red"}> {days_remaining} </Tag>;
		}
	}

	function uploadSepareitor  (id)  {
	
		let data = {"id": id}
		
		dispatch(
			updateQuotationSeparated(data, async (response) => { 
		
				await response;
				if(response.code===200){success("la cotizacion esta disponible");}
				dispatch(getQuotationSeparated(async (response) => {
					await response;
				}));
		}));
				
	}
	
	const separateUnits = [
		{
			title: '#',
			width: 100,
			dataIndex: 'id',
			key: 'id',
			defaultSortOrder: 'asc',
			fixed: 'left',
			render: (_,record)=>{
				return convertAllMapDataState(quotations)?.indexOf(record)+1;
			 }
		},
		{
			title: 'Cotización#',
			width: 140,
			dataIndex: 'code',
			key: 'cotizacion',
			...getColumnSearchProps('code'),
		},
		{
			title: 'Cliente',
			dataIndex: 'customer_name',
			key: 'cliente',
			width: 150,
			...getColumnSearchProps('customer_name'),
		},
		{
			title: 'Asesor',
			dataIndex: 'advisor_name',
			key: 'asesor',
			width: 150,
			...getColumnSearchProps('advisor_name'),

		},
		{
			title: 'Proyecto',
			dataIndex: 'project_name',
			key: 'proyecto',
			width: 150,
			...getColumnSearchProps('project_name'),

		},
		{
			title: 'Unidad',
			dataIndex: 'unit_label',
			key: 'unidad',
			width: 150,
			...getColumnSearchProps('unit_label'),

		},
		{
			title: 'Separada Hasta',
			dataIndex: 'separation_date_up_to',
			key: 'separadaHasta',
			width: 150,
			...getColumnSearchProps('separation_date_up_to'),

		},
		{
			title: 'Días Faltantes',
			dataIndex: 'days_remaining',
			key: 'diasFaltantes',
			align: "center",
			width: 150,
			fixed: 'right',
			...getColumnSearchProps('days_remaining'),
			render: (_, record) => {
				return getTagDaysRemaining(record['days_remaining']);
			},

		},
		{
			title: 'Estado Unidad',
			dataIndex: 'state_quotation',
			fixed: 'right',
			key: 'estadoUnidad',
			align: "center",
			width: 150,
			render: (_, record) => {
				return <>
				<Popover content={
					<Button onClick={()=> uploadSepareitor(record.id) }>
						Cambiar a disponible
					</Button>
						
				} trigger="click">
						<Tag color={"lime"}> {record['state_quotation']} </Tag>
						</Popover>
			
			</> 
			},
		},
	];
	return <Table 
		size='small'
		columns={separateUnits}
		dataSource={convertAllMapDataState(quotations)}
		scroll={{x: 600}}
	/>
}

export default SeparateUnits