import React, {useEffect, useState} from "react";
import {fetchAllProjects} from "../../../../redux/actions/Project/ProjectsAction";
import {useDispatch, useSelector} from "react-redux";
import {Button, Card, Col, DatePicker, Form, Row, Select} from "antd";
import {validateModule} from "../../../handle/PermissionMethods/PermissionMethods";
import * as constants from "../../../../redux/constants";
import {Redirect} from "react-router-dom/cjs/react-router-dom.min";
import {CollectionFlowReportGet} from "../../../../redux/actions/Report/CollectionFlowAction";
import {DownloadDocument} from "../../../handle/Multimedia/DownloadDocument";
import {errorGlobal} from "../../../handle/Notification/Notification";

const CollectionFlowReport = () => {
	const dispatch = useDispatch();
	
	const selectProject = useSelector((state) => state?.project);
	const [form] = Form.useForm();
	const [endDate] = useState(null);
	const [consulting, setConsulting] = useState( false )
	useEffect(() =>{
		dispatch(fetchAllProjects())
	}, [dispatch])
	
	const validateMessages = {
		// eslint-disable-next-line no-template-curly-in-string
		required: "${label} es requerido",
		types: {
			// eslint-disable-next-line no-template-curly-in-string
			email: "${label} no es un correo válido",
			// eslint-disable-next-line no-template-curly-in-string
			number: "${label} debe ser un número",
		},
	};
	const onFinish = async () => {
		setConsulting(true);
		const values = await form.validateFields();
		dispatch(CollectionFlowReportGet(values,async (response) =>{
			const res = await response
			DownloadDocument(res._payload)
			setConsulting(false);
		},async (error)=> {
			const err = await error
			errorGlobal(err.message)
			setConsulting(false);
		}))

	}


	if (!validateModule(constants.COLLECTION_FLOW_REPORT_SUBMODULE)) {
		return <Redirect to={constants.DEFAULT_ROUTE} />;
	}
	return (
		<Card title="Informe de Flujo de Recaudo">
			<Form
				layout="vertical"
				onFinish={onFinish}
				form={form}
				validateMessages={validateMessages}
			>
				<Row gutter={16}>
					<Col span="12">
						<Form.Item 
							label="Proyecto:" 
							name="project"
							rules={[
								{required: true}
							]}
						>
							<Select
								showSearch
								placeholder="Seleccionar..."
								style={{ width: '100%' }}
								optionFilterProp="children"
								onChange={form.getFieldValue("project_id")}
								filterOption={(input, option) =>
									option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
								}
							>
								{
									!Array.isArray(selectProject) && selectProject._payload?.map(item => (
										<Select.Option value={item.id}>{ item.name }</Select.Option>
									))
								}
							</Select>
						</Form.Item>
					</Col>
					<Col span="12">
						<Form.Item
							label="Fecha Fin:"
							name="end_date"
							rules={[
								{
									required: true,
								},
							]}
						>
							<DatePicker
								value={endDate}
								onChange={form.getFieldValue("final_date")}
								style={{ width: "100%" }}
								placeholder="Ingrese una fecha"
							/>
						</Form.Item>
					</Col>
				</Row>
				<Row>
					<Col xs={18} sm={20} md={21} lg={21} xl={22} />
					<Col xs={6} sm={4} md={3} lg={3} xl={2}>
						<Form.Item>
							<Button type="primary" htmlType="submit" loading={consulting}>
								Exportar
							</Button>
						</Form.Item>
					</Col>
				</Row>
			</Form>
		</Card>
	);
};
export default CollectionFlowReport
