import ClientItem from "./ClientItem";
import { useDispatch } from "react-redux";
import React, { useEffect, useState } from "react";
import { Table, Card, Row, Col, Divider, Form, Button } from "antd";
import {errorGlobal,success} from "../../../../../handle/Notification/Notification";
import {convertAllMapDataState} from "../../../../../handle/HandleFilterTable/handleFilterTable";
import {
	resetAssignResponsibleAction,
	setAllAssignResponsibleAction,
	updateOrCreateAssignResponsibleAction,
} from "../../../../../../redux/actions/Price/SaleMade/assignResponsibleAction";

const TableAssignResponsible = ({
	assignResponsibleDatabase,
	handleCancel,
	id,
}) => {
	const dispatch = useDispatch();

	const [form] = Form.useForm();
	const [key, setKey] = useState(1);
	const [dataSource, setDataSource] = useState(
		assignResponsibleDatabase._payload
	);

	const addResponsibleCliente = () => {
		const arrayToWork = [...dataSource];

		const client = {
			customer_id: null,
			id: key * -1,
			key,
		};

		setDataSource([...arrayToWork, client]);
		setKey(key + 1);
	};

	const handleDelete = (idItem) => {
		const arrayToWork = [...dataSource];

		setDataSource(arrayToWork.filter((item) => item.id !== idItem));
	};

	const handleSubmit = async () => {
		const values = await form.validateFields();
		const dto = [];
		const arrayToWork = [...dataSource];
		arrayToWork.forEach((item) => {
			const newResponsible = {
				customer_id: item.hasOwnProperty(`responsible${item.key}`)
					? values[`responsible${item.key}`]
					: item.customer_id,
				id: item.id < 0 ? null : item.id,
			};
			dto.push(newResponsible);
		});
		dispatch(
			updateOrCreateAssignResponsibleAction(id, dto, async (response) => {
				const res = await response;
				if (res.error) {
					errorGlobal("¡UPS! Ha ocurrido un error");
				} else {
					success("Actualizado correctamente");
					handleCancel();
					dispatch(setAllAssignResponsibleAction(id));
				}
			})
		);
	};

	useEffect(() => {
		return () => {
			dispatch(resetAssignResponsibleAction());
		};
	}, []);

	const getDataToChild = (value) => {
		const arrayToWork = [...dataSource];

		arrayToWork.map((item) => {
			if (item.id === value.id) {
				item = value;
			}
			return item;
		});
		setDataSource(arrayToWork);
	};

	const columns = [
		{
			title: null,
			dataIndex: "customer_id",
			key: "customer_id",
			render: (_, record, index) => (
				<ClientItem
					key={record.customer_id}
					getDataToChild={getDataToChild}
					handleDelete={handleDelete}
					form={form}
					data={record}
					index={index}
				/>
			),
		},
	];

	return (
		<Form form={form} onFinish={handleSubmit}>
			<Card
				style={{ width: "100%" }}
				title="Listado de clientes"
				actions={[
					<>
						<Row>
							<Col xs={16} sm={0} md={0} lg={0} xl={20} />
							<Col xs={8} sm={8} md={8} lg={8} xl={3}>
								<Button
									style={{ textAlign: "right" }}
									type="primary"
									onClick={addResponsibleCliente}
								>
									{" "}
									Agregar{" "}
								</Button>
							</Col>
						</Row>
					</>,
				]}
			>
				{dataSource.length > 0 ? (
					<Table
						size={"small"}
						pagination={false}
						columns={columns}
						dataSource={convertAllMapDataState(dataSource)}
					/>
				) : null}
			</Card>
			<Divider />
			<Row>
				<Col xs={16} sm={0} md={0} lg={0} xl={18} />
				<Col xs={8} sm={8} md={8} lg={8} xl={6}>
					<Button
						htmlType="submit"
						type={"primary"}
						style={{
							background: "#389e0d",
							borderColor: "#389e0d",
							color: "#fff",
						}}
						key="finish"
					>
						Guardar
					</Button>
					,
					<Button key="close" type={"primary"} onClick={handleCancel}>
						Finalizar
					</Button>
				</Col>
			</Row>
		</Form>
	);
};

export default TableAssignResponsible;
