import React, {useState} from "react";
import {useEditQuotes} from "../../../Price/Quotes/Hooks/useEditQuotes";
import {formatterMoney} from "../../../../handle/FormatterMoney/FormatterMoney";
import CustomerRequests from "../Components/MovementData/ActionsSales/AfterSales/Components/CustomerRequests";
import TechnicalManagement from "../Components/MovementData/ActionsSales/AfterSales/Components/TechnicalManagement";

export const useCustomer = () => {
	const {formatDate} = useEditQuotes()

	const columns = [
		{
			title: 'Fecha de Solicitud',
			width: 15,
			dataIndex: 'applicationDate',
			align: "center",
			key: 'applicationDate',
		},
		{
			title: 'Solicitudes del Cliente',
			dataIndex: 'customerRequests',
			align: "center",
			key: 'customerRequests',
			render: (_,record) => <CustomerRequests record={record}/>
		},
		{
			title: 'Gestión Técnica',
			dataIndex: 'technicalManagement',
			align: "center",
			key: 'technicalManagement',
			render: (_,record) => <TechnicalManagement record={record} />
		}
	]
	
	const [customer,setCustomer]  = useState({
		email: '',
		id: 0,
		identification: 0,
		name: '',
		occupation: '',
		phoneNumber: '',
		registerDate: ''
	});
	const [interest,setInterest] = useState({
		id: 0,
		interests: ''
	})
	const [quotations,setQuotations] = useState([{
		code: '',
		project: '',
		unit: '',
		trustCode: '',
		numberRooms: 0,
		projectUnitStatus: ''
	}])
	
	const CustomerSet = (customer) => {
		setCustomer({
			email: customer.email,
			id: customer.id,
			identification: customer.identification,
			name: customer.name,
			occupation: customer.occupation,
			phoneNumber: customer.phone_number,
			registerDate: formatDate(customer.register_date)
		})
	}
	
	const DataSet = (data) => {
		return  {
			id: data.id,
			name: data.comment,
			applicationDate: data.application_date,
			customerRequests: Documents(data.application_document),
			budget: parseFloat(data.budget).toFixed(2),
			approvalOfWorksDirector: parseInt(data.approval_of_works_director),
			technicalManagement: Documents(data.technical_document),
			finish: data.fulfilled === "1",
			afterSalesAccomplished: Documents(data.fulfilled_document),
			state: data.state.name_state,
		}
	}
	
	const Documents = (documents) => {
		if (documents === null){
			return {}
		}else {
			return {
				id: documents.id,
				name: documents.name,
				link: documents.path,
			}
		}
	}
	
	const SaleSet = (data) => {
		return {
			id: parseInt(data.id),
			saleDate: formatDate(data.sale_date),
			projectName: data.project_name,
			labelUnit: data.label_unit,
			fiduciaryCode: data.fiduciary_code,
			unitValue:  formatterMoney.format(data.unit_value) ,
			reformsValue: formatterMoney.format(data.reform_value),
			totalValue: formatterMoney.format(data.total_value),
			percentageCollected: data.percentage_collected,
			percentageLegalization: formatPercentage(data.percentage_of_legalization),
			observation: data.observation
		}
	}

	const formatPercentage = (percentage) => {
		let percentageSet
		if (percentage === '0.000000' || percentage === '100.000000'){
			percentageSet = parseInt(percentage) + ' %'
		}else {
			percentageSet = parseFloat(percentage).toFixed(2) + ' %'
		}
		return percentageSet
	}
	
	return {
		customer,setCustomer,interest,setInterest,quotations,setQuotations,columns,
		CustomerSet,DataSet,SaleSet
	}
}