import React from "react";
import { Table, Row, Col} from "antd";

import './css/index.scss'
import {convertAllMapDataState, getColumnSearchProps} from "../../handle/HandleFilterTable/handleFilterTable";
import CustomerUpdateActvities from "../Customers/Componets/Activity/CustomerUpdateActivities";

const TableComponent = (props) => {

	const CustomerActivitiesColums = [
		{
			title: '#',
			width: 40,
			dataIndex: 'id',
			key: 'id',
			sorter: (a, b) => a.id - b.id,
		},
		{
			title: 'Usuario',
			width: 100,
			dataIndex: 'creatorUser',
			key: 'creatorUser',
			...getColumnSearchProps('creatorUser'),
		},
		{
			title: 'Fecha Programada',
			dataIndex: 'scheduleDate',
			key: 'scheduleDate',
			width: 150,
			sorter: (a, b) => a.scheduleDate - b.scheduleDate,
		},
		{
			title: 'Tipo de Actividad',
			dataIndex: 'typeActivity',
			key: 'typeActivity',
			width: 150,
			...getColumnSearchProps('typeActivity'),
		},
		{
			title: 'Tipo de Gestion',
			dataIndex: 'managementType',
			key: 'managementType',
			width: 150,
			...getColumnSearchProps('managementType'),
		},
		{
			title: 'Actividad',
			dataIndex: 'activity',
			key: 'activity',
			width: 150,
			...getColumnSearchProps('activity'),
		},
		{
			title: 'Realizada',
			dataIndex: 'activityPerformed',
			key: 'activityPerformed',
			width: 150,
			render: (_, record) => getActions(record.id , record.activityPerformed)
		},
		{
			title: 'Comentario',
			dataIndex: 'commentary',
			key: 'commentary',
			width: 150,
			...getColumnSearchProps('commentary'),
			render : (_, record) => (record.commentary != null) ? record.commentary : null
		}
	];
	const getActions = (id,activityPerformed) => {
		return (
			<>
				{ activityPerformed == null ? <CustomerUpdateActvities myClick={props.myOnClick} id={id} activityPerformed = {activityPerformed}/> : activityPerformed}
			</>
		);

	}

  return <Row>
		<Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 24 }}>
			<Table	
				columns={CustomerActivitiesColums} 
				dataSource={convertAllMapDataState(props?.data)} 
				size='small' 
				scroll={{ x: props.x, y: props.y }}
				bordered ={true}
			/>
		</Col>
  </Row>
  
}

export default TableComponent;
