import * as constants from '../../constants';

export default function project(state = [], action) {
		
    switch (action.type) {
        case constants.SET_ALL_PROJECTS:
            return action.payload;
        case constants.ADD_PROJECT:
            return state._payload.concat(action.payload);
        case constants.REMOVE_PROJECT:
            return state._payload.filter(item => item._id !== action.payload);
        case constants.UPDATE_PROJECT:
            return state._payload.map(item => {
                if (item._id === action.payload.assessorId)
                    return { ...item, ...action.payload.data };
                else
                    return item;
            });
        case constants.GET_OPTION_SELECT:
            return state;
        case constants.RESET_PROJECT:
            return action.payload;
        default:
            return state;
    }
}