import * as constants from './../../constants';

export const updateSalesParametersById = ( id, data, onSuccess) => ({
    type: constants.API,
    payload: {
        method: 'PUT',
        url: `${constants.RUTA}/salesParameters/${id}`,
        data,
        postProcessSuccess: onSuccess,
        success: (id, data) => (updateSalesParametersProject( id, data)),      
    }
});


export const getSaleParameterById = ( id, onSuccess) => ({
	type: constants.API,
	payload: {
		method: 'GET',
		url: `${constants.RUTA}/salesParameters/${id}`,
		postProcessSuccess: onSuccess,
		success: (response) => (getSaleParameter(response))
	}
});

export const resetSaleParameterAction = () => ({
    type: constants.API,
    payload: {
		method: null,
		success: () => (resetSaleParameter())
	}
})


const updateSalesParametersProject = ( id, data) => ({
    type: constants.UPDATE_OR_CREATE_SALES_PARAMETERS,
    payload: { id, data }
});

const getSaleParameter = (response) =>({
	type:constants.GET_SALE_PARAMETER_BY_ID,
	payload: response
});

const resetSaleParameter = () =>({
	type:constants.RESET_SALE_PARAMETER,
	payload: []
});