import {Descriptions} from "antd";
import React, {useEffect} from "react";
import {FormatterPeso} from "../Utilitis/Utilitis";

const PropertyDetails = ({project,initialFeeState}) => {
	const descriptionItemStyle = {
		minWidth: 105,
		padding: 0,
	};
	return <>
		<Descriptions
			size="small"
			title="Datos del inmueble"
			bordered
			column={{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }}
		>
			<Descriptions.Item label="Proyecto">{project.project}</Descriptions.Item>
			<Descriptions.Item label="m²">{project.squareMeter}</Descriptions.Item>
			<Descriptions.Item label="Etiqueta">{project.label}</Descriptions.Item>
		</Descriptions>
		<br/>
		<Descriptions
			size="small"
			title="Valor inmueble"
			bordered
			column={{ xxl: 3, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }}
		>
			<Descriptions.Item contentStyle={descriptionItemStyle} label="Valor m²">
				{FormatterPeso.format(project.squareMeterValue)}
			</Descriptions.Item>
			<Descriptions.Item
				contentStyle={descriptionItemStyle}
				label="Prima de vista"
			>
				{FormatterPeso.format(project.sightPremium)}
			</Descriptions.Item>
			<Descriptions.Item
				contentStyle={descriptionItemStyle}
				label="Prima de altura por m²"
			>
				{FormatterPeso.format(project.heightPremiumPerMeter)}
			</Descriptions.Item>
			<Descriptions.Item
				contentStyle={descriptionItemStyle}
				className="valor"
				label="Valor parcial unidad"
			>
				{FormatterPeso.format(project.partialValueUnit)}
			</Descriptions.Item>

			<Descriptions.Item
				contentStyle={descriptionItemStyle}
				label="Valor parqueadero"
			>
				{FormatterPeso.format(project.parkingValue)}
			</Descriptions.Item>
			<Descriptions.Item
				contentStyle={descriptionItemStyle}
				label="Valor terraza/ cuarto útil/ otros"
			>
				{FormatterPeso.format(project.valueOthers)}
			</Descriptions.Item>
			<Descriptions.Item
				contentStyle={descriptionItemStyle}
				label="Valor kit acabados"
			>
				{FormatterPeso.format(project.FinishedKitValue)}
			</Descriptions.Item>
			<Descriptions.Item
				contentStyle={descriptionItemStyle}
				className="valor"
				label="Valor total unidad"
			>
				{FormatterPeso.format(project.totalUnitValue)}
			</Descriptions.Item>
		</Descriptions>
		<br/>
		<Descriptions
			title="Cuota inicial"
			size="small"
			bordered
			column={{ xxl: 2, xl: 2, lg: 2, md: 2, sm: 2, xs: 2 }}
		>
			<Descriptions.Item
				contentStyle={descriptionItemStyle}
				label={`C.I. (${initialFeeState.initialFeePercentage}%)`}
			>
				{FormatterPeso.format(initialFeeState.initialFee)}
			</Descriptions.Item>
			<Descriptions.Item
				contentStyle={descriptionItemStyle}
				label="Separación"
			>
				{FormatterPeso.format(initialFeeState.separation)}
			</Descriptions.Item>
			<Descriptions.Item
				contentStyle={descriptionItemStyle}
				label={`C.I. No financiada (${initialFeeState.initialFeeNotFundedPercentage}%)`}
			>
				{FormatterPeso.format(initialFeeState.initialFeeNotFunded)}
			</Descriptions.Item>
			<Descriptions.Item
				contentStyle={descriptionItemStyle}
				label={`C.I. financiada (${initialFeeState.initialFeeFinancedPercentage}%)`}
			>
				{FormatterPeso.format(initialFeeState.initialFeeFinanced)}
			</Descriptions.Item>
		</Descriptions>
		<br/>
	</>
}

export default PropertyDetails