import _ from "lodash";
import "./availableProjects.scss";
import { Card, Checkbox } from "antd";
import React, { useState, useContext, useEffect } from "react";
import SelectedUnitsContext from "../context/SelectedUnitsContext";
import {errorGlobal} from "../../../../../handle/Notification/Notification";

const styleItem = {
	border: "1px solid #bce8f1",
	borderTop: 'none',
	borderLeft: 'none',
	padding: "9px 0px",
};
const borderTop = {
	border: "1px solid #bce8f1",
	borderLeft: 'none',
	padding: "9px 0px",
};

const item = {
	textAlign: "center",
};

const InfoUnit = ({ info }) => {
	const { selectedUnits, handleSelectedUnit, handleDelete } =
		useContext(SelectedUnitsContext);

	const [dataSource, setDataSource] = useState({});
	const [check, setCheck] = useState(false);

	useEffect(() => {
		if (!isObjEmpty(info)) {
			setDataSource(info);
		}
	}, [info]);
	useEffect(()=> {
			if (selectedUnits.length !== 0){
				if (_.some(selectedUnits,info)){
					setCheck(true);
				}else {
					setCheck(false);
				}
			}
	},[selectedUnits]);
	const onChange = (event) => {
		if (event.target.checked){
			if (selectedUnits.length > 2) {
				errorGlobal("Solo se pueden generar tres cotizacones");
			}else {
				handleSelectedUnit(dataSource);
				setCheck(true)
			}
		}else {
			handleDelete(info.id);
			setCheck(false);
		}
	};
	const formatterPeso = new Intl.NumberFormat("es-CO", {
		style: "currency",
		currency: "COP",
		minimumFractionDigits: 2,
	});

	const isObjEmpty = (obj) => {
		return Object.keys(obj).length === 0;
	};

	if (isObjEmpty(dataSource)) return null;
	return (
		<Card
			style={{
				display: "block",
				width: 200,
				minWidth: 200,
			}}
			bordered={false}
			className={"card-unit"}
		>
			<div style={borderTop}>
				{" "}
				<h6 style={item}> {dataSource.squareMeter} </h6>
			</div>
			<div style={styleItem}>
				{" "}
				<h6 style={item}> {dataSource.label}</h6>
			</div>
			<div style={styleItem}>
				{" "}
				<h6 style={item}>{dataSource.unit_state_id}</h6>
			</div>
			<div style={styleItem}>
				{" "}
				<h6 style={item}>{dataSource.numberRooms}</h6>
			</div>
			<div style={styleItem}>
				{" "}
				<h6 style={item}>{dataSource.bathroomNumber}</h6>
			</div>
			<div style={styleItem}>
				{" "}
				<h6 style={item}>{dataSource.numberLevels}</h6>
			</div>
			<div style={styleItem}>
				{" "}
				<h6 style={item}>{dataSource.number_floors}</h6>
			</div>
			<div style={styleItem}>
				{" "}
				<h6 style={item}>{dataSource.parking_number}</h6>
			</div>
			<div style={styleItem}>
				{" "}
				<h6 style={item}>{formatterPeso.format(dataSource.unity_value)}</h6>
			</div>
			<div style={styleItem}>
				<h6>
					<Checkbox style={{ paddingLeft: "50%" }} onChange={(event) => onChange(event)} checked={check} />
				</h6>
			</div>
		</Card>
	);
};

export default InfoUnit;
