import moment from 'moment';
import { useDispatch } from "react-redux";
import React, { useState, useEffect } from 'react';
import { UploadOutlined, ArrowDownOutlined } from "@ant-design/icons";
import { DownloadDocument } from "../../../../handle/Multimedia/DownloadDocument";
import { errorGlobal, success } from "../../../../handle/Notification/Notification";
import { Button, Select, Form, Row, Col, InputNumber, Upload, Divider, Input, DatePicker } from "antd";
import { updateDirectCollection } from "../../../../../redux/actions/Collection/DirectCollectionAction";
import { getAllDirectCollections } from '../../../../../redux/actions/Collection/DirectCollectionAction'
;
const CompostAdjustment = ({ data, onClose }) => {
	const [form] = Form.useForm();
	const [pressBottonState, setPressBottonState] = useState(false);
	const [fileUpload, setFileUpload] = useState(null);
	const dispatch = useDispatch();

	const onReset = () => {
		form.resetFields();
	}

	useEffect(() => {
		fillFields();
	}, [])

	const fillFields = () => {
		form.setFieldsValue({
			consignmentDate: moment(data?.consignment_date),
			consignment_amount: data?.consignment_amount,
			customer_name: data?.name,
			project_name: data?.project_name,
			unit: data?.unit_label,
			AdjustmentConcept: data?.adjustment_concept,
			consignmentConcept: data?.consignment_concept,
			voucher_Support: data?.name_multimedia
		});
	}

	const handleSubmit = async () => {
		setPressBottonState(true);
		try {
			const values = await form.validateFields();
			const dto = {
				...values,
				id: data.id,
				consignment_date: values["consignmentDate"].format("YYYY-MM-DD"),
				consignment_amount: parseFloat(values["consignment_amount"])
			}

			let formData = new FormData();

			if (fileUpload !== null) {
				formData.append('consignment_support', fileUpload, fileUpload.name);
			}
			formData.append('adjustment_concept', dto.AdjustmentConcept);
			formData.append('consignment_date', dto.consignment_date);
			formData.append('consignment_concept', dto.consignmentConcept);
			formData.append('consignment_amount', dto.consignment_amount);
			var id = parseInt(data.id);
			dispatch(updateDirectCollection(id, formData, (response) => {
				if (!response.error) {
					success("Recaudo directo creado");
					dispatch(getAllDirectCollections());
					onReset()
					onClose(false);
				} else {
					errorGlobal("¡UPS! Ocurrió un error mientras se registraba el recaudo");
				}
				setPressBottonState(false);
			}));

		} catch (errorInfo) {
			console.error(errorInfo);
		}
	}

	const download = () => {
		if (fileUpload === null) {
			DownloadDocument(data.path_multimedia);
		}
	}

	const uploadProps = {
		name: 'file',
		beforeUpload: () => {
			return false;
		},
		onChange: info => {
			form.setFieldsValue({
				voucher_Support: info.file !== undefined ? info.file.name : ' '
			});
			setFileUpload(info.file);
		}, showUploadList: false,

	}

	const validateMessages = {
		// eslint-disable-next-line no-template-curly-in-string
		required: '${label} es requerido',
		types: {
			// eslint-disable-next-line no-template-curly-in-string
			email: '${label} no es un correo válido',
			// eslint-disable-next-line no-template-curly-in-string
			number: '${label} debe ser un número'

		}
	};

	return (
		<>
			<Form
				layout="vertical"
				form={form}
				size='middle'
				validateMessages={validateMessages}
				onFinish={handleSubmit}
			>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={8} lg={8} xl={8}>
						<Form.Item name="customer_name" label="Cliente">
							<Input
								style={{ width: '100%' }}
								disabled={true}
							/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={8} lg={8} xl={8}>
						<Form.Item name="project_name" label="Proyecto">
							<Input
								style={{ width: '100%' }}
								disabled={true}
							/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={8} lg={8} xl={8}>
						<Form.Item name="unit" label="Unidad">
							<Input
								style={{ width: '100%' }}
								disabled={true}
							/>
						</Form.Item>
					</Col>
				</Row>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item
							name='consignmentDate'
							label="Fecha de consignación"
							rules={[{ required: true }]}
						>
							<DatePicker
								value={form.getFieldValue("consignmentDate")}
								// onChange={(value) => captureDate(value, setConsignmentDate)}
								style={{ width: '100%' }}
							/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item

							name="consignment_amount"
							label="Valor de consignación"
							rules={[{ required: true, message: "Campo requerido" }]}
						>
							<InputNumber
	value={form.getFieldValue("consignment_amount")}
	formatter={value => `$ ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
	parser={value => value.replace(/\$\s?|(,*)/g, '')}
	style={{width: '100%'}}/>
						</Form.Item>
					</Col>
				</Row>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item
							name="consignmentConcept"
							label="Concepto de consignación"
							rules={[{ required: true, message: 'Campo requerido' }]}
						>
							<Select
								placeholder="Escoja el concepto"
								value={form.getFieldValue("consignmentConcept")}
							>
								<Select.Option value={""}>Seleccione...</Select.Option>
								<Select.Option value={1}>Abono</Select.Option>
								<Select.Option value={2}>Reformas</Select.Option>
								<Select.Option value={3}>Otros</Select.Option>
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item
							name='AdjustmentConcept'
							label="Concepto ajuste"
							rules={[{ required: true }]}
						>
							<Input.TextArea
								value={form.getFieldValue("AdjustmentConcept")}
							/>
						</Form.Item>
					</Col>
				</Row>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={16} lg={16} xl={16}>
						<Form.Item name="voucher_Support" label="Soporte del comprobante" rules={[{ required: true, message: 'Campo requerido' }]}>
							<Input style={{width: '100%'}} disabled={true}/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={4} lg={4} xl={4}>
						<Form.Item name='SelectFile' label=' '>
							<Upload {...uploadProps}

							>
								<Button type='primary' icon={<UploadOutlined />}>Elegir archivo</Button>
							</Upload>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={4} lg={4} xl={4}>
						<Form.Item name='download' label=' '>
							<Button onClick={download} style={{ background: '#389e0d', color: '#fff' }} icon={<ArrowDownOutlined />}>Descargar</Button>
						</Form.Item>
					</Col>
				</Row>
				<Divider/>
				<Row>
					<Col offset={18} xs={24} sm={24} md={4} lg={4} xl={4}>
						<Form.Item style={{ margin: '0' }}>
							<Button loading={pressBottonState} type="primary" htmlType="submit">
								Guardar
							</Button>
						</Form.Item>
					</Col>
				</Row>
			</Form>
		</>
	);
}

export default CompostAdjustment;