import * as constants from "../../../constants";

export const fetchAllReformRequest = (data, postProcessSuccess) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		data: data,
		url: `${constants.RUTA}/reformRequest/report/reform_request_report`,
		success: (response) => (getReformRequestReport(response)),
		postProcessSuccess: postProcessSuccess
	}
});

export const fetchPathPdfForReformReport = (data, postProcessSuccess) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		data: data,
		url: `${constants.RUTA}/reformRequest/report/get_path_pdf`,
		postProcessSuccess: postProcessSuccess
	}
});

export const fetchPathExcelForReformReport = (data, postProcessSuccess) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		data: data,
		url: `${constants.RUTA}/reformRequest/report/get_path_excel`,
		postProcessSuccess: postProcessSuccess
	}
});

export const fetchAllStatusReformRequest = () => ({
	type: constants.API,
	payload: {
		method: 'GET',
		url: `${constants.RUTA}/reform_state/states`,
		success: (response) => (getStatusReformRequestReport(response))
	}
});

export const fetchExcelReformRequest = (data, postProcessSuccess) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		data: data,
		url: `${constants.RUTA}/report/reforms`,
		success: (response) => (getExcelReformRequestReport(response)),
		postProcessSuccess: postProcessSuccess
	}
});

const getExcelReformRequestReport = (data) => ({
	type: constants.GET_EXCEL_REQUEST_REFORMS_REPORT,
	payload: data
});

const getStatusReformRequestReport = (data) => ({
	type: constants.GET_STATUS_REQUEST_REFORMS,
	payload: data
});

const getReformRequestReport = (data) => ({
	type: constants.GET_REQUEST_REFORMS_REPORT,
	payload: data
});

