import {useDispatch} from "react-redux";
import ModalQuotes from "../ModalQuotes";
import React, {useEffect, useState} from "react";
import {DoubleLeftOutlined} from "@ant-design/icons";
import {Button, Col, Divider, Form, Row, Select} from "antd";
import {errorGlobal,warning} from "../../../../../handle/Notification/Notification";
import {quotesProjectFilter} from "../../../../../../redux/actions/Price/Quotation/QuotesAction";
import {fetchAllPricesRange} from "../../../../../../redux/actions/Configuration/PricesRangeAction";
import {getOptionsSelect, getZoneSelect} from "../../../../../../redux/actions/Project/ProjectsAction";

const StepSearchProject = ({prev,Customer,projectsSet,showSet}) => {
	const dispatch = useDispatch()
	const [project_type, setProjectType] = useState(0)
	const [area, setArea] = useState("Todo")
	const [state, setState] = useState(0)
	const [number_rooms, setNumber_rooms] = useState(0)
	const [price_range, setPriceRange] = useState("0")
	const [arrayStateProject, setArrayStateProject] = useState([])
	const [arrayProjectType, setArrayProjectType] = useState([])
	const [arrayZone, setArrayZone] = useState([])
	const [arrayPrice_range, setArrayPrice_range] = useState([])
	const [selectCustomer] = useState(Customer)


	const createArrangement = (response) => {
		let arrPrices = arrayPrice_range
		response._payload.forEach((item) => {
			arrPrices.push({
				id: item.id,
				name: item.name,
			})
		})
		setArrayPrice_range(arrPrices)
	}
	
	useEffect(()=>{
		dispatch(getOptionsSelect((res)=>{
			if (!res.error){
				let varArrayStateProject = []
				let varArrayProjectType = []
				res._payload.project_state.forEach((value) => {
					varArrayStateProject.push({
						name: value.name,
						id: value.id,
					})
				})
				res._payload?.project_types.forEach((value) => {
					varArrayProjectType.push({
						name: value.name,
						id: value.id,
					})
				})
				setArrayStateProject(varArrayStateProject)
				setArrayProjectType(varArrayProjectType)
			}
		}))
		dispatch(getZoneSelect((res)=>{
			if (!res.error){
				let arrZone = []
				res._payload.forEach((i)=>{
					arrZone.push(i.area)
				})
				setArrayZone(arrZone)
			}
		}))
		dispatch(fetchAllPricesRange((res)=>{
			if (!res.error){
				createArrangement(res)
			}
		}))
	},[])

	const dataStepsForm = {
		project_type,
		number_rooms,
		state,
		area,
		price_range,
		selectCustomer,
	}

	const filterProject = () => {
		dispatch(quotesProjectFilter(dataStepsForm,(res)=>{
			if (res.error){
				errorGlobal("¡UPS! Ha ocurrido un error")
			}else {
				if (res._payload.length !== 0){
					showSet(false)
					projectsSet(res._payload)
				}else {
					warning("Lo sentimos no hay proyectos disponibles con los filtros actuales ")
					projectsSet([])
					showSet(true)
				}
			}
		}))
	};
	
	return (
		<>
			<Row gutter={12}>
				<Col span={4}>
					<Button
						size="large"
						icon={<DoubleLeftOutlined/>}
						onClick={() => prev()}
					>
						Previous
					</Button>
				</Col>
				<Col span={20}>
					<Divider>Buscar Proyecto</Divider>
				</Col>
			</Row>
			<Row gutter={12}>
				<Col span={6}>
					<Form.Item name="projectType" label="Tipo de proyecto">
						<Select onChange={setProjectType} defaultValue={"Todo"}>
							<Select.Option value="Todo">Todos</Select.Option>
							{arrayProjectType.map((item) => (
								<Select.Option key={item?.id+item?.name+item?.id} value={item.id}>
									{item.name}
								</Select.Option>
							))}
						</Select>
					</Form.Item>
				</Col>
				<Col span={6}>
					<Form.Item name="numberRooms" label="No. Habitaciones">
						<Select
							placeholder="Seleccione un No. Habitaciones"
							onChange={setNumber_rooms}
							defaultValue={"Todos"}
						>
							<Select.Option value="0">Todos</Select.Option>
							<Select.Option value="1">1</Select.Option>
							<Select.Option value="2">2</Select.Option>
							<Select.Option value="3">3</Select.Option>
							<Select.Option value="4">4</Select.Option>
							<Select.Option value="5">5</Select.Option>
							<Select.Option value="6">6</Select.Option>
						</Select>
					</Form.Item>
				</Col>
				<Col span={6}>
					<Form.Item name="state" label="Estado">
						<Select onChange={setState} defaultValue={"Todos"}>
							<Select.Option value="0">Todos</Select.Option>
							{arrayStateProject.map((item) => (
								<Select.Option key={item?.id+item?.name+item?.id} value={item.id}>
									{item.name}
								</Select.Option>
							))}
						</Select>
					</Form.Item>
				</Col>
				<Col span={6}>
					<Form.Item name="zone" label="Zona">
						<Select
							placeholder="Seleccione una Zona"
							onChange={setArea}
							defaultValue={"Todo"}
						>
							<Select.Option value="Todo">Todos</Select.Option>
							{arrayZone.map((item) => (
								<Select.Option  key={item?.id+item?.name+item?.id} value={item}>{item}</Select.Option>
							))}
						</Select>
					</Form.Item>
				</Col>
			</Row>
			<Row gutter={12}>
				<Col span={8}>
					<Form.Item
						name="presupuestoDisponible"
						label="Presupuesto disponible (Valores en Millones)"
					>
						<Select
							style={{width: "100%"}}
							placeholder="Seleccione un presupuesto disponible"
							onChange={setPriceRange}
							defaultValue={"Todos"}
						>
							<Select.Option value="0">Todos</Select.Option>
							{arrayPrice_range.map((item) => (
								<Select.Option key={item?.id+item?.name+item?.id} value={item.id}>
									{item.name}
								</Select.Option>
							))}
						</Select>
					</Form.Item>
				</Col>
				<Col span={4}>
					<Form.Item label=" ">
						<ModalQuotes createArrangement={createArrangement}/>
					</Form.Item>
				</Col>
				<Col span={6}>
					<Form.Item label=" ">
						<Button type="primary" onClick={filterProject}>
							Buscar Disponibilidad de Proyectos
						</Button>
					</Form.Item>
				</Col>
			</Row>
		</>
	)
}

export default StepSearchProject