import React, { useState, useEffect } from "react";
import IncreasePolicyComponent from "../PoliciesProject/IncreasePolicyComponent";

import { Button, Form } from "antd";
import ListTitleData from "../ListTitleDataComponent";
import { useParams } from "react-router-dom";
import { useDispatch } from "react-redux";
import {
	errorGlobal,
	success,
} from "../../../../../../../handle/Notification/Notification";
import {
	deleteIncreasePolicyById, fetchAllIncreasePolicy,
	updateOrCreateIncreasePolicyById,
} from "../../../../../../../../redux/actions/Project/IncreasePolicyAction";

const dataTitlePolicyIncrement = [
	"Límite Inferior",
	"Límite Superior",
	"Tipo de Incremento",
	"Valor",
];

const IncreasePolicyContainer = ({ increasePoliciesDatabase }) => {
	const { id } = useParams();
	const idParams = id;
	const [form] = Form.useForm();
	const dispatch = useDispatch();
	const [count, setCount] = useState(1);
	const [increasePolicies, setIncreasePolicies] = useState([]);

	useEffect(() => {
		setIncreasePolicies(increasePoliciesDatabase);
	}, [increasePoliciesDatabase]);

	const handleAddIncrementPolicy = () => {
		let increasePolicy = increasePolicies;
		let arrayToWork = [...increasePolicy._payload];
		const newItem = {
			id: count * -1,
			lower_limit: null,
			upper_limit: null,
			increase_type: "",
			amount: null,
			project_id: parseInt(idParams),
		};
		increasePolicy._payload = [...arrayToWork, newItem];
		setIncreasePolicies(increasePolicy);
		setCount(count + 1);
	};

	const handleDeleteIncrementPolicy = (id) => {
		if (id && id >= 0) {
			dispatch(
				deleteIncreasePolicyById(id, async (response) => {
					const res = await response;
					if (res.error) {
 
						errorGlobal("¡UPS! Ocurió un error mientras se actualizaba.");
					} else {
						success("La política ha sido eliminada con éxito.");
					}
				})
			);
		}
		let increasePolicy = increasePolicies;
		let arrayToWork = [...increasePolicy._payload];
		increasePolicy._payload = arrayToWork.filter((item) => item.id !== id);
		const newObject = {
			_payload: increasePolicy._payload,
		};
		setIncreasePolicies(newObject);
	};

	const handleSubmit = async () => {
		let increasePolicy = increasePolicies;
		let arrayToWork = [...increasePolicy._payload];
		try {
			const values = await form.validateFields();
			arrayToWork.forEach((item) => {

				if(!item?.id){
					Object.keys(values).forEach(x => {
						let id = x.split(";")[1];
						let index = arrayToWork.findIndex(y => y.id == id);
						if(index == -1){
							item.id = id;
							return true;
						}
					});
				}
				
				item.lower_limit = values[`lower_limit;${item.id}`];
				item.upper_limit = values[`upper_limit;${item.id}`];
				item.increase_type = parseInt(values[`increase_type;${item.id}`]);
				item.amount = values[`amount;${item.id}`];
				item.id = item.id < 0 ? null : item.id;
			});

			dispatch(
				updateOrCreateIncreasePolicyById(arrayToWork, async (response) => {
					const res = await response;
					res.error
 
						? errorGlobal("¡UPS! Ocurió un error mientras se actualizaba.")
						: success("Políticas de incremento guardadas correctamente.");

					dispatch(fetchAllIncreasePolicy(id));
				})
			);
		} catch (err) {
			console.error(err);
		}
	};

	return (
		<>
			<Form form={form} onFinish={handleSubmit} name="increasePolicyForm">
				<Button
					type="primary"
					style={{
						margin: "2%",
						backgroundColor: "#5cb85c",
						borderColor: "#5cb85c",
					}}
					disabled={Array.isArray(increasePolicies) || JSON.parse(localStorage.stateDisable)}
					onClick={handleAddIncrementPolicy}
				>
					Agregar Politica de incremento
				</Button>
				<div style={{ marginLeft: "20px" }}>
					<div className="wrapperPolicy">
						<div className={"listItemIncrement"}>
							<ListTitleData data={dataTitlePolicyIncrement} />
						</div>

						{!Array.isArray(increasePolicies) &&
							increasePolicies._payload.map((item) => (
								<IncreasePolicyComponent
									key={item.id}
									handleDeleteIncrementPolicy={handleDeleteIncrementPolicy}
									item={item}
									form={form}
								/>
							))}
					</div>
				</div>
				<Form.Item>
					<Button
						type="primary"
						style={{
							margin: "2%",
							marginLeft: "60vw",
						}}
						disabled={Array.isArray(increasePolicies) || JSON.parse(localStorage.stateDisable)}
						htmlType={"submit"}
					>
						Guardar políticas de incremento
					</Button>
				</Form.Item>
			</Form>
		</>
	);
};

export default IncreasePolicyContainer;
