import * as constants from '../../constants';
import LocalStorage from '../../../views/config/LocalStorage'

let Storage = new LocalStorage();


export const fetchAllIncreasePolicy = (id) => ({
    type: constants.API,
    payload: {
        method: 'GET',
	    headers: { 'access-token':Storage.getToken() },
        url: `${constants.RUTA}/increasePolicy/${id}`,
        success: (response) => (setAllIncreasePolicy(response))
    }
});

export const updateOrCreateIncreasePolicyById = (data, onSuccess, onError) => ({
    type: constants.API,
    payload: {
        method: 'PUT',
        url: `${constants.RUTA}/increasePolicy`,
        data,
        success: (data) => (updateOrCreateIncreasePolicy(data)),  
        postProcessSuccess: onSuccess,
        postProcessError: onError           
    }
});

export const deleteIncreasePolicyById = ( id, onSuccess, onError) => ({
    type: constants.API,
    payload: {
        method: 'DELETE',
        url: `${constants.RUTA}/increasePolicy/${id}`,
        success: () => (removeIncreasePolicy(id)),
        postProcessSuccess: onSuccess,
        postProcessError: onError
    }
});

export const resetIncreasePolicyAction = () => ({
    type: constants.API,
    payload: {
		method: null,
		success: () => (resetIncreasePolicy())
	}
})

const resetIncreasePolicy = () =>({
	type:constants.RESET_INCREASE_POLICY,
	payload: []
});

const setAllIncreasePolicy = (data) => ({
    type: constants.SET_ALL_INCREASE_POLICY,
    payload: data
});

const updateOrCreateIncreasePolicy = ( id, data) => ({
    type: constants.UPDATE_OR_CREATE_INCREASE_POLICY ,
    payload: { id, data }
});

const removeIncreasePolicy = ( id) => ({
    type: constants.REMOVE_INCREASE_POLICY,
    payload:  id
});