import React, {createContext, Fragment} from "react";
import { Button, PageHeader } from "antd";
import TableProjects from "./components/TableProjects";
import { Link } from "react-router-dom";
import { validateModule, validatePermission} from "../../../handle/PermissionMethods/PermissionMethods";
import * as constants from "../../../../redux/constants";
import { Redirect } from "react-router-dom/cjs/react-router-dom.min";

export const ProjectContext = createContext();

const Projects = () => {
	
	if (!validateModule(constants.PROJECTS_SUBMODULE)) {
		return <Redirect to={constants.DEFAULT_ROUTE} />;
	}
	return (
		<Fragment>
			
			<PageHeader
				style={{ border: "1px solid rgb(235, 237, 240);" }}
				title={"Proyectos"}
				extra={[
					validatePermission(constants.CREATE_PROJECT_PERMISSION) ? (
						<Button type="primary">
						<Link to="/projects/new-project">Nuevo Proyecto</Link>
					</Button>
					) : null
					
				]}
			/>
			<TableProjects />
		</Fragment>
	);
};

export default Projects;
