import "../css/quotes.scss";
import ReactQuill from "react-quill";
import { useDispatch } from "react-redux";
import { SendOutlined } from "@ant-design/icons";
import React, { useState, useEffect } from "react";
import { Form, Input, Button, Row, Col, Tooltip, Modal } from "antd";
import { sendEmail } from "../../../../../redux/actions/Price/Quotation/QuotesAction";
import { errorGlobal, success } from "../../../../handle/Notification/Notification";

const layout = {
	labelCol: {
		span: 24,
	},
	wrapperCol: {
		span: 24,
	},
};

const style = {
	scrollContainer: {
		overflow: "auto",
		backgroundColor: "#f5f5f5",
		height: "180px",
		color: "rgba(0,0,0,.25)",
	},
};

const SendEmail = ({ data, update_table }) => {
	const dispatch = useDispatch();

	useEffect(() => {
		fillFields();
	}, [data]);

	let config = {
		theme: "snow",
		scrollingContainer: ".scroll-container",
		modules: {
			toolbar: false,
		},
		readOnly: true,
	};

	const fillFields = () => {
		form.setFieldsValue({
			email: data.email_quote_format,
		});
	};

	const [form] = Form.useForm();

	const [visible, setVisible] = useState(false);

	const onShow = () => setVisible(true);

	const onClose = () => setVisible(false);
	const onFinish = () => {
		let dt = [];
		dt.push(data.project_name);
		dt.push(data.email);
		dt.push(data.email_quote_format);
		dt.push(data.id);
		dispatch(
			sendEmail(dt, data.customer_id, (response) => {
				if (response.error) return errorGlobal("¡UPS! Ha ocurrido un error");
				onClose();
				update_table();
				success("Se ha enviado el email correctamente");
			})
		);
	};
	const textEnviarCotizacion = (
		<span style={{ color: "#6A6963" }}> Enviar Cotización </span>
	);
	const titleEnviar = <span>Enviar Email - Cotización #{data.code} </span>;

	return (
		<>
			<Tooltip placement="left" color="#ffffff" title={textEnviarCotizacion}>
				<Button
					style={{ background: "#5cb85c", borderColor: "#4cae4c" }}
					icon={<SendOutlined style={{ color: "#fff" }} />}
					onClick={onShow}
					size="small"
				/>
			</Tooltip>
			<Modal
				size="small"
				footer={false}
				visible={visible}
				backgroundColor="#eb5a44"
				title={titleEnviar}
				onCancel={onClose}
			>
				<Row>
					<Col offset={2} span={20}>
						<Form form={form} {...layout} name="sendEmail" onFinish={onFinish}>
							<Form.Item
								label="Nombre del cliente:"
								name="clientName"
								rules={[
									{
										required: false,
										message: "El campo es requerido",
									},
								]}
							>
								<Input disabled defaultValue={data.name} />
							</Form.Item>
							<Form.Item
								label="Nombre del proyecto:"
								name="projectName"
								rules={[
									{
										required: false,
										message: "El campo es requerido",
									},
								]}
							>
								<Input disabled defaultValue={data.project_name} />
							</Form.Item>

							<Form.Item
								label="Correo:"
								name="email"
								rules={[
									{
										required: false,
										message: "El campo es requerido",
									},
								]}
							>
								<ReactQuill
									className="reactQuill"
									{...config}
									style={style.scrollContainer}
								/>
							</Form.Item>
							<Form.Item>
								<Button
									style={{ backgroundColor: "#52c41a", color: "#fff" }}
									htmlType="submit"
								>
									Enviar email
								</Button>
							</Form.Item>
						</Form>
					</Col>
				</Row>
			</Modal>
		</>
	);
};

export default SendEmail;
