import * as constants from "../../../constants";

export const fetchExcelComissionReport = (data, postProcessSuccess ) => ({
    type: constants.API,
	payload: {
		method: 'POST',
		data: data,
		url: `${constants.RUTA}/report/comission-report`,
		success: (response) => (fethComissionReport(response)),
		postProcessSuccess: postProcessSuccess
	}
})

const fethComissionReport = (data) => ({
	type: constants.GET_COMISSIONS,
	payload: data
});