import React from 'react';
import {PageHeader} from "antd";
import {Link} from 'react-router-dom';
import TabsQuotes from "./Components/TabsQuotes";
import {ArrowRightOutlined} from '@ant-design/icons';
import * as constants from "../../../../redux/constants";
import {Redirect} from "react-router-dom/cjs/react-router-dom.min";
import { PropertyDataProvider } from "./Components/context/PropertyDataContext";
import {validatePermission} from "../../../handle/PermissionMethods/PermissionMethods";

const Quotes = () => {
	if (!validatePermission(constants.CREATE_QUOTATION_PERMISSION)) {
		return <Redirect to={constants.DEFAULT_ROUTE}/>;
	}
	return (
		<React.Fragment>
			<PageHeader
				title={"Nueva cotización"}
				extra={[
					<Link key="1" to="/sales/quotes"
								style={{
									backgroundColor: '#1890ff',
									padding: 8,
									borderRadius: 2,
									color: '#ffffff'
								}}
					>
						Listar Cotizaciones <ArrowRightOutlined style={{fontSize: 20}}/>
					</Link>
				]}
			/>
			<PropertyDataProvider>
				<TabsQuotes/>
			</PropertyDataProvider>
		</React.Fragment>
	)
}

export default Quotes;
