import * as constants from "../../../constants";

export const fetchAllSalesReport = (data, postProcessSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		data: data,
		url: `${constants.RUTA}/report/sales`,
		success: (response) => (getSalesReport(response)),
		postProcessSuccess: postProcessSuccess,
		postProcessError: onError
	}
});

const getSalesReport = (data) => ({
	type: constants.GET_SALES_REPORT,
	payload: data
});

