import {useCustomer} from "./Hooks/useCustomer";
import {SearchOutlined} from "@ant-design/icons";
import React, {useEffect, useState} from "react";
import {Link, useParams} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import ClientData from "./Components/ClientData/ClientData";
import MovementData from "./Components/MovementData/MovementData";
import {useEditQuotes} from "../../Price/Quotes/Hooks/useEditQuotes";
import ClientDocuments from "./Components/ClientData/ClientDocuments";
import CustomerInterest from "./Components/ClientData/CustomerInterest";
import {Button, Card, Col, Descriptions, Divider, Form, Input, Row, Select} from "antd";
import {errorGlobal, success, warning} from "../../../handle/Notification/Notification";
import {fetchAllCustomer, getCustomerById} from "../../../../redux/actions/Customer/CustomerAction";
import {updateCustomerInterestById} from "../../../../redux/actions/Customer/CustomerInterestAction";

const validateMessages1 = {
	required: "El campo es requerido",
}
const validateMessages = {
	// eslint-disable-next-line no-template-curly-in-string
	required: "${label} es requerido",
	types: {
		// eslint-disable-next-line no-template-curly-in-string
		email: "${label} no es un correo válido",
		// eslint-disable-next-line no-template-curly-in-string
		number: "${label} debe ser un número",
	},
}
const CustomerTracking = () => {
	const {id} = useParams()
	const [searchCustomerId, setSearchCustomerId] = useState(null)
	const [form] = Form.useForm()
	const [search,setSearch] = useState(false)
	const [query,setQuery] = useState([])
	const dispatch = useDispatch()
	const {customer,setCustomer,interest,setInterest} = useCustomer()
	const {formatDate} = useEditQuotes()
	const [customerAttachment, setCustomerAttachment] = useState([])
	const customers = useSelector((state)=> state?.customer)
	useEffect(() => {
		dispatch(fetchAllCustomer())
		if(id !== undefined){
			setSearchCustomerId(id)
			if (!search){
				getCustomer()
			}
		}
	}, [])
	useEffect(() => {
		setSearchCustomerId(id)
		getCustomer()
	},[id])
	const getCustomer = () => {
		if (searchCustomerId === null && id === undefined){
			warning("Debe seleccionar un usuario")
		}else {
			let ident = searchCustomerId !== null ? searchCustomerId : id
			dispatch(
				getCustomerById(ident,async (response)=>{
					const res = await response
					if (res.error){
						errorGlobal(res.message)
					}else {
						setSearch(true)
						setFiles(res)
						setQuery(res._payload)
						setCustomerAttachment(res._payload.customer?.customer_attachments)
						setCustomer({
							email: res._payload.customer.email,
							id: res._payload.customer.id,
							identification: res._payload.customer.identification,
							name: res._payload.customer.name,
							occupation: res._payload.customer.occupation,
							phoneNumber: res._payload.customer.phone_number,
							registerDate: formatDate(res._payload.customer.register_date)
						})
						setInterest({
							id: res._payload.customer.interest_level === null || res._payload.customer.interest_level === undefined? "" :res._payload.customer.interest_level,
							interests: res._payload.customer.interests
						})
					}
				})
			)
		}
	}
	
	const setFiles = (value) => {
		form.setFieldsValue({
			interestLevel: value._payload.customer.interest_level === null ? "" :value._payload.customer.interest_level,
			interests: value._payload.customer.interests
		})
	}
	const onFinish = async () => {
		const value = await form.validateFields()
		let dto = interesDTO(value)
		dispatch(updateCustomerInterestById(customer.id,dto,async (respond)=>{
			const res = await respond
			if (res.error){
				errorGlobal(res.message)
			}else {
				success(res.message)
				getCustomer()
			}
		}))
	}
	const interesDTO = (value) => {
		return {
			interest_level: value["interestLevel"],
			interests: value["interests"]
		}
	}
	return (
		<>
			<Card
				title={"Seguimiento de Clientes "}
				style={{marginBottom: "80px"}}
				extra={
					<Link to="/customer/client">
						{" "}
						<Button size="small" type="primary">Volver</Button>
						{" "}
					</Link>
				}
			>
				<Form
					layout="vertical"
					size="middle"
					validateMessages={validateMessages1}
					onFinish={()=>getCustomer()}
				>
					<Row gutter={16}>
						<Col xs={24} sm={24} md={24} lg={24} xl={24}>
							<Descriptions title={"Buscar Cliente"}/>
							<Input.Group compact >
								<Form.Item
									name="searchCustomer"
									label="Digite la Idetificación o Nombre Para Buscar"
									rules={[
										{
											required: true,
										},
									]}
								>
									<Select
										showSearch
										style={{width: "100%"}}
										optionFilterProp="children"
										initialvalues={searchCustomerId}
										onChange={setSearchCustomerId}
										filterOption={(input, option) =>
											option.children
												.toLowerCase()
												.indexOf(input.toLowerCase()) >= 0
										}
									>
										<Select.Option value="">Seleccione...</Select.Option>
										{
											customers?._payload?.map((item)=>{
												return(
													<Select.Option
														key={item.id + item.name + item.id}
														value={item.id}
													>
														{` ${item.name} - CC: ${item.identification ?? '-' }`}
													</Select.Option>
												)
											})
										}
									</Select>
								</Form.Item>
								<Form.Item
									name="SearchCustomer"
									label=" "
								>
									<Button
										onClick={()=>getCustomer()}
										icon={<SearchOutlined/>}
									/>
								</Form.Item>
							</Input.Group>
						</Col>
					</Row>
				</Form>
				{
					search 
						? (
							<>
								<Divider/>
								<ClientData customer={customer}/>
								<Divider/>
								<Row gutter={16}>
									<Col xs={24} sm={24} md={12} lg={12} xl={12}>
										<Form
											layout="vertical"
											size="middle"
											form={form}
											onFinish={()=>onFinish()}
											validateMessages={validateMessages}
										>
											<CustomerInterest interest={interest} />
										</Form>
									</Col>
									<ClientDocuments myOnClick={getCustomer} id={searchCustomerId} customerAttachments={customerAttachment} />
								</Row>
								<Divider/>
								<Descriptions title={"Datos de Movimiento del Cliente"}/>
								<MovementData query={query} myOnClick={getCustomer} />
							</>
						)
						: null
				}
			</Card>
		</>
	)
}

export default CustomerTracking