import "./tableLegalize.scss";
import { useDispatch } from "react-redux";
import React, { useEffect, useState } from "react";
import { Form, Button, Select, DatePicker, Descriptions } from "antd";
import {errorGlobal,success} from "../../../../../handle/Notification/Notification";
import {getAllQuotation,updateQuoteLegalizationSaleById} from "../../../../../../redux/actions/Price/Quotation/QuotesAction";

const InfoQuote = ({ info, onClose }) => {
	const [dataSource, setDataSource] = useState(null);
	useEffect(() => {
		setDataSource(info);
	}, [info]);

	const dispatch = useDispatch();

	const validateMessages = {
		// eslint-disable-next-line no-template-curly-in-string
		required: "${label} es requerido",
		// eslint-disable-next-line no-template-curly-in-string
		number: "El ${label} no puede ser menor a 0",
		types: {
			number: "No es un numero válido!",
		},
	};

	const formatterPeso = new Intl.NumberFormat("es-CO", {
		style: "currency",
		currency: "COP",
		minimumFractionDigits: 0,
	});

	const [form] = Form.useForm();

	const handleSumit = async () => {
		try {
			const values = await form.validateFields();
			const dto = {
				cutoff_date: values["cutoffDate"],
				sale_date: values["datePurchase"].format("YYYY-MM-DD"),
				quotation_id: info.id,
			};

			dispatch(
				updateQuoteLegalizationSaleById(dto, async (response) => {
					const res = await response;
					if (res.error) {
						errorGlobal("¡UPS! Ocurió un error mientras se actualizaba");
					} else {
						success("Se ha efectuado la venta correctamente");
					}

					dispatch(getAllQuotation());
				})
			);
			handleReset();
			onClose();
		} catch (errorInfo) {
			console.error("Failed:", errorInfo);
		}
	};

	const handleReset = () => {
		form.resetFields();
	};

	return (
		<>
			{dataSource && (
				<>
					<Form
						validateMessages={validateMessages}
						form={form}
						onFinish={handleSumit}
					>
						<Descriptions
							className="descriptionStyle"
							bordered
							style={{ width: "80%", margin: "auto" }}
							title={false}
							size="small"
							column={1}
						>
							<Descriptions.Item label={<strong>Projecto</strong>}>
								{dataSource.project_name}
							</Descriptions.Item>
							<Descriptions.Item label={<strong>Cliente</strong>}>
								{dataSource.name}
							</Descriptions.Item>
							<Descriptions.Item label={<strong>Cédula</strong>}>
								{dataSource.identification}
							</Descriptions.Item>
							<Descriptions.Item label={<strong>Correo</strong>}>
								{dataSource.email}
							</Descriptions.Item>
							<Descriptions.Item label={<strong>Teléfono</strong>}>
								{dataSource.phone_number}
							</Descriptions.Item>
							<Descriptions.Item label={<strong>Fecha de Compra</strong>}>
								<Form.Item
									label={null}
									name="datePurchase"
									rules={[{ required: true, message: "Campo requerido" }]}
								>
									<DatePicker
										placeholder={"Seleccione una fecha"}
										value={form.getFieldValue("datePurchase")}
										style={{ width: "100%" }}
									/>
								</Form.Item>
							</Descriptions.Item>
							<Descriptions.Item label={<strong>No. Unidad</strong>}>
								{dataSource.label}
							</Descriptions.Item>
							<Descriptions.Item label={<strong>Código Fiduciario</strong>}>
								{dataSource.fiduciary_code}
							</Descriptions.Item>
							<Descriptions.Item label={<strong>Área Unidad</strong>}>
								{dataSource.square_meters}
							</Descriptions.Item>
							<Descriptions.Item label={<strong>Parquedero</strong>}>
								{dataSource.parking}
							</Descriptions.Item>
							<Descriptions.Item label={<strong>Valor Total</strong>}>
								{formatterPeso.format(dataSource.value_total_unit)}
							</Descriptions.Item>

							<Descriptions.Item label={<strong>Fecha de Corte</strong>}>
								<Form.Item
									label={null}
									name="cutoffDate"
									rules={[{ required: true, message: "Campo requerido" }]}
								>
									<Select
										value={form.getFieldValue("cutoffDate")}
										defaultValue=""
										style={{ width: "100%", height: "35px" }}
									>
										<Select.Option value="">Seleccionar</Select.Option>
										<Select.Option value={0}>5 de cada meses</Select.Option>
										<Select.Option value={1}>15 de cada meses</Select.Option>
									</Select>
								</Form.Item>
							</Descriptions.Item>
						</Descriptions>
						<Button
							style={{
								background: "#1890ff",
								color: "#fff",
								margin: "15px 45%",
							}}
							htmlType="submit"
						>
							Legalizar venta
						</Button>
					</Form>
				</>
			)}
		</>
	);
};

export default InfoQuote;
