import React, { useEffect } from 'react';
import { Col, Row } from 'antd';
import { formatterMoney } from '../../../../handle/FormatterMoney/FormatterMoney';


const PaymentPlan = ({ paymentPlan }) => {
	let newArrayPayment = [];
	useEffect(() => {
		for (let i = 0; i < paymentPlan.length; i++) {
			if (i !== 0) {
				newArrayPayment.push(paymentPlan[i]);
			}

		}

	}, []);



	return (
		<Row>
			<Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 24 }}>
				<table className="table-report">
					<tr>
						<td colspan="8" class="title head_color th-report"><b>3. PLAN DE PAGOS</b></td>
					</tr>
					<tr class="title head_color th-report">
						<td colspan="2" className="th-report">N° de MES</td>
						<td colspan="2" className="th-report">MES</td>
						<td colspan="2" className="th-report">Valor Pagado</td>
						<td colspan="2" className="th-report">Valor Pactado</td>
					</tr>
					<tr className="th-report">
						<td colspan="2" class="cell_not_center th-report">C.I. NO FINANCIADA</td>
						<td colspan="2" className="th-report">-</td>
						<td colspan="2" class="cell_not_center_rigth th-report">{paymentPlan[0].paid_value}</td>
						<td colspan="2" class="cell_not_center_rigth th-report">{paymentPlan[0].agreed_value}</td>
					</tr>
					{

						paymentPlan.map((value,index) =>{
							 return(index===0)?<></>:<> <tr >
								<td colspan="2" class="cell_not_center th-report">{value.month_number}</td>
								<td colspan="2" class="th-report">{value.month}</td>
								<td colspan="2" class="cell_not_center_rigth th-report">{value.paid_value}</td>
								<td colspan="2" class="cell_not_center_rigth th-report">{value.agreed_value}</td>
							</tr></>
						})


					}

				</table>
			</Col>
		</Row>
	);
}

export default PaymentPlan;