import React, {useEffect} from "react";
import { useDispatch, useSelector } from "react-redux";
import { fetchAllPaymentRelationshipSetting } from "../../../../../../../redux/actions/Project/ConfiguracionPagosRecudosAction";
import Spinner from "../../../../../../handle/Spinner/Spinner";
import PaymentRelationshipSetup from "./PagosRelacion";
import { Card } from "antd";

const MainPaymentRelationshipSetup = ({id}) => {
	
	const dispatch = useDispatch();
	useEffect(() => {
		dispatch(fetchAllPaymentRelationshipSetting(id));
	}, []);

	const paymentRelationshipSetupDatabase = useSelector((state) => state?.paymentRelationshipSetting);

	if (Array.isArray(paymentRelationshipSetupDatabase))
		return (
			<Card
				title="Configuración para pagos y de relación"
				style={{ borderColor: "black" }}
			>
				<Spinner />
			</Card>
		);

	return (
		<PaymentRelationshipSetup
			paymentRelationshipSetupDatabase={paymentRelationshipSetupDatabase}
			id={id}
		/>
	);
};

export default MainPaymentRelationshipSetup;
