import React, { useEffect, useState } from "react";
import { fetchAllAssessor, fetchDataAssesorReport } from "../../../../redux/actions/Configuration/AssesorAction";
import { fetchAllAfiliatedCompany } from "../../../../redux/actions/Configuration/AfiliatedCompanyAction";
import { captureDate } from "../../../handle/HandleDatePicker/HandleDate";
import { useDispatch, useSelector } from "react-redux";
import { Card, DatePicker, Col, Row, Form, Select, Button } from "antd";
import PaginationTables from "./components/PaginationTables";
import { validateModule } from "../../../handle/PermissionMethods/PermissionMethods";
import * as constants from "../../../../redux/constants";
import { Redirect } from "react-router-dom/cjs/react-router-dom.min";
import Spinner from '../../../handle/Spinner/Spinner';
import { errorGlobal, warning } from "../../../handle/Notification/Notification";
import ExportExcelAdvisorsReport from "./components/ExportExcelAdvisorsManagementReport";

const AdvisorsManagementReport = () => {
	const [loading] = useState(false);
	const [idAssessor, setIdAssessor] = useState(null);
	const [idAfiliatedCompany, setIdAfiliatedCompany] = useState(null);
	const [endDate, setEndDate] = useState(null);
	const [startDate, setStartDate] = useState(null);
	const [assesorData, setAssesorData] = useState(null);
	const [consultind, setConsulting] = useState( null )
	const dispatch = useDispatch();

	useEffect(() => {
		dispatch(fetchAllAssessor(async (response) => {
			const res = await response
			if (res.error === true) {
				errorGlobal("Opss! ha ocurrido un error " + res.message)
			}
			if (!res) {
				warning("No hay Datos")
			}
		}))
		dispatch(fetchAllAfiliatedCompany(async (response) => {
			const res = await response
			if (res.error === true) {
				errorGlobal("Opss! ha ocurrido un error " + res.message)
			}
			if (!res) {
				warning("No hay Datos")
			}
		}))
	}, []
	)

	const selectAssessor = useSelector((state) => state?.assessor);
	const selectAfiliatedCompany = useSelector((state) => state?.afiliatedCompany);

	let data = {
		"assesor_id": idAssessor,
		"start_date": startDate,
		"final_date": endDate,
		"company_id": idAfiliatedCompany
	}
	const onFinish = () => {
		setConsulting(false);
		dispatch(fetchDataAssesorReport(data, async (response) => {
			setConsulting(true);
			const res = await response
			if (res.error === true) {
				errorGlobal("Opss! ha ocurrido un error " + res.message)
			} else {
				setAssesorData(res._payload);
			}
			if (!res) {
				warning("No hay Datos")
			}
		},async ()=>{	setConsulting(null);}))
	
	};

	const validateMessages = {
		required: "${label} es requerido",
		types: {
			email: "${label} no es un correo válido",
			number: "${label} debe ser un número",
		},
	};
	if (!validateModule(constants.ADVISORY_MANAGEMENT_REPORT_SUBMODULE)) {
		return <Redirect to={constants.DEFAULT_ROUTE} />;
	}
	return (
		<>
			<Card title="Informe Gestión Asesores">
				<Form
					layout="vertical"
					onFinish={onFinish}
					validateMessages={validateMessages}
				>{loading ? <Spinner /> : <>
					<Row gutter={16}>
						<Col xs={24} sm={24} md={12} lg={12} xl={12}>
							<Form.Item label="Asesor:" name="Asesor">
								<Select
									defaultValue={""}
									showSearch
									style={{ width: "100%" }}
									optionFilterProp="children"
									onChange={setIdAssessor}
									filterOption={(input, option) => option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
								>
									<Select.Option value="">Todos...</Select.Option>
									{!Array.isArray(selectAssessor) &&
										selectAssessor._payload.map((item) => (
											<Select.Option key={item.id} value={item.id}>{` ${item.name} `}</Select.Option>
										))}
								</Select>
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={12} lg={12} xl={12}>
							<Form.Item
								label="Empresa:"
								name="Empresa"
								rules={[
									{
										required: true,
									},
								]}
							>
								<Select
									defaultValue={""}
									showSearch
									style={{ width: "100%" }}
									optionFilterProp="children"
									onChange={setIdAfiliatedCompany}
									filterOption={(input, option) =>
										option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
									}
								>
									<Select.Option value="">Seleccione...</Select.Option>
									{!Array.isArray(selectAfiliatedCompany) &&
										selectAfiliatedCompany._payload.map((item) => (
											<Select.Option key={item.id} value={item.id}>{` ${item.name} `}</Select.Option>
										))}
								</Select>
							</Form.Item>
						</Col>
					</Row>
					<Row gutter={16}>
						<Col xs={24} sm={24} md={12} lg={12} xl={12}>
							<Form.Item
								label="Fecha Inicio:"
								name="Fecha Inicio:"
								rules={[
									{
										required: true,
									},
								]}
							>
								<DatePicker
									value={startDate}
									onChange={(value) => captureDate(value, setStartDate)}
									style={{ width: "100%" }}
								/>
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={12} lg={12} xl={12}>
							<Form.Item
								label="Fecha Fin:"
								name="Fecha Fin:"
								rules={[
									{
										required: true,
									},
								]}
							>
								<DatePicker
									value={endDate}
									onChange={(value) => captureDate(value, setEndDate)}
									style={{ width: "100%" }}
								/>
							</Form.Item>
						</Col>
					</Row>
					<Row>
						<Col xs={18} sm={20} md={21} lg={22} xl={22} />

						<Col xs={2} sm={2} md={2} lg={2} xl={2}>
							<Form.Item>
								<Button type="primary" htmlType="submit">
									Buscar
								</Button>
							</Form.Item>
						</Col>

					</Row>
					{consultind  ? <>
						<Row>
							<Col xs={18} sm={20} md={21} lg={22} xl={22} />
							<Col xs={2} sm={2} md={2} lg={2} xl={2}>
								<Form.Item>
									<ExportExcelAdvisorsReport data={data} />
								</Form.Item>
							</Col>
						</Row>
						<PaginationTables data={assesorData} />
					</> : assesorData==null?<></>:<Spinner/>}
				</>
					}
				</Form>

			</Card>

		</>
	);
};

export default AdvisorsManagementReport;
