import React, { useEffect, useState } from "react";
import { fetchAllAssessor } from "../../../../redux/actions/Configuration/AssesorAction";
import { useDispatch, useSelector } from "react-redux";
import { Card } from "antd";
import Spinner from "../../../handle/Spinner/Spinner";
import { errorGlobal } from "../../../handle/Notification/Notification";
import FormMonitoringActivities from "./FormMonitoringActivities";
import TableMonitoringActivities from "./TableMonitoringActivities";
import {convertAllMapDataState} from "../../../handle/HandleFilterTable/handleFilterTable";

const CompetitionReport = () => {
	const dispatch = useDispatch();
	const [dataSource, setDataSource] = useState([]);
	const [fetching, setFetching] = useState(true);
	const [selectedAssesor, setSelectedAssessor] = useState(null);

	useEffect(() => {
		dispatch(
			fetchAllAssessor(async (response) => {
				const res = await response;
				if (res.error) return errorGlobal("¡UPS, Ha ocurrido un error");
			})
		);
	}, []);
	const databaseAssessor = useSelector((state) => state?.assessor);
	return (
		<>
			{!Array.isArray(databaseAssessor) ? (
				<FormMonitoringActivities
					databaseAssessor={databaseAssessor}
					setDataSource={setDataSource}
					setFetching={setFetching}
					setSelectedAssessor={setSelectedAssessor}
				/>
			) : (
				<Card title="Seguimiento a actividades">
					<Spinner />
				</Card>
			)}
			{fetching ? (
				dataSource.length > 0 && (
					<>
						<TableMonitoringActivities
							dataSource={convertAllMapDataState(dataSource)}
							selectedAssesor={selectedAssesor}
						/>
					</>
				)
			) : (
				<Card>
					<Spinner />
				</Card>
			)}
		</>
	);
};
export default CompetitionReport;
