import moment from 'moment';
import {Table, Tooltip} from "antd";
import Card from '../../../Card/Card';
import {Button, Col, Row} from "antd";
import LastPayment from './LastPayment';
import React, {useEffect, useState} from 'react';
import {ArrowDownOutlined} from "@ant-design/icons";
import {useDispatch, useSelector} from 'react-redux';
import Spinner from '../../../../handle/Spinner/Spinner';
import * as constants from "../../../../../redux/constants";
import {InfoOutlined, SettingOutlined} from "@ant-design/icons";
import ModalAdjustmentCollection from './ModalAdjustTrustCollection';
import {RUTA_FORMAT_PAYMENT_FIDUCIARY} from '../../../../../redux/constants';
import {formatterMoney} from '../../../../handle/FormatterMoney/FormatterMoney';
import {errorGlobal, warning} from "../../../../handle/Notification/Notification";
import { DownloadDocument } from "../../../../handle/Multimedia/DownloadDocument";
import {validatePermission} from "../../../../handle/PermissionMethods/PermissionMethods";
import {getColumnSearchProps, noWhiteWindow} from '../../../../handle/HandleFilterTable/handleFilterTable';
import {getAllTrustCollections} from '../../../../../redux/actions/Collection/TrustCollectionAction';

const TableTrustCollection = () => {

	const dispatch = useDispatch();

	useEffect(
		() =>
			dispatch(
				getAllTrustCollections(async (response) => {
					const res = await response;
					if (!res) {
						warning("No hay Datos en la tabla");
					} else if (res.error === true) {
						errorGlobal("¡UPS! ha ocurrido un error" + res.message)
					}
					setStateLoading(false);
				})
			),
		[dispatch]
	);

	const trustCollection = useSelector((state) => state?.trustColletion._payload);

	const [stateLoading, setStateLoading] = useState(true);
	const [visibleAdjustment, setVisibleAdjustment] = useState(false);
	const [dataRecord, setDataRecord] = useState(null);

	const showAdjustmetModal = (record) => {
		setVisibleAdjustment(true);
		setDataRecord(record);
	}

	const handleCancel = () => {
		setVisibleAdjustment(false);
		setDataRecord(null);
	}

	const getActions = (record) => {
		const textAdjustment = <span style={{color: '#6A6963'}}> Ajustes </span>

		const text = <span style={{color: '#6A6963'}}>{record.concept}
			<hr/>
            Creado: {record.created_at} <br/>  Modificado: {record.updated_at} </span>

		return (
			<>
				{" "}
				<Tooltip placement="left" color='#ffffff' title={textAdjustment}>
					<Button
						onClick={() => showAdjustmetModal(record)}
						style={{background: '#52c41a', color: '#fff'}}
						size={'small'}
						icon={<SettingOutlined/>}
					/>
				</Tooltip>
				{"  "}

				<Tooltip placement="left" color='#ffffff' title={text}>
					<Button size="small" type="primary" icon={<InfoOutlined/>}/>
				</Tooltip>
			</>
		);

	};

	const columns = [
		{
			title: 'Cliente',
			width: 60,
			dataIndex: 'customer_name',
			key: 'customer_name',
			align: "center",
			render: (customer_name) => {
				let newName = customer_name.split("_");
				return newName[0] + " " + newName[1]
			}
		},
		{
			title: 'Proyecto',
			width: 50,
			dataIndex: 'project_name',
			key: 'project_name',
			align: "center",
			...getColumnSearchProps('project_name'),
			...noWhiteWindow
		},
		{
			title: 'Unidad',
			dataIndex: 'unit_label',
			key: 'unit_label',
			width: 10,
			align: "center",
			...getColumnSearchProps('unit_label'),
			...noWhiteWindow
		},
		{
			title: 'Cód. Fiduciario',
			dataIndex: 'code_fiduciary',
			key: 'code_fiduciary',
			width: 30,
			align: "center",
			...getColumnSearchProps('code_fiduciary'),
			...noWhiteWindow
		},
		{
			title: 'Fecha de consignación',
			dataIndex: 'consignment_date',
			key: 'consignment_date',
			width: 40,
			align: "center",
			render: (consignment_date) => {
				return moment(consignment_date).format('DD/MM/YYYY')
			}
		},
		{
			title: 'Valor consignación',
			dataIndex: 'consignment_amount',
			key: 'consignment_amount',
			width: 40,
			align: "center",
			render: (consignment_amount) => {
				return formatterMoney.format(consignment_amount);
			}
		},
		{
			title: 'Concepto',
			dataIndex: 'concept',
			key: 'concept',
			width: 40,
			align: "center",
			...getColumnSearchProps('concept'),
			...noWhiteWindow
		},
		{
			title: 'Acciones',
			key: 'acciones',
			fixed: 'right',
			align: 'center',
			width: 15,
			render: (_, record) => getActions(record)
		},
	];


	const downloadFormat = () => {
			DownloadDocument(RUTA_FORMAT_PAYMENT_FIDUCIARY);
	}

	return (
		<>

			<Card
				title={
					<>
						<Row gutter={16}>
							{validatePermission(constants.DOWNLOAD_FORMAT_TRUST_FUND_PERMISSION) ? (
								<Col>
									<Button onClick={downloadFormat} style={{background: '#389e0d', color: '#fff'}}
											icon={<ArrowDownOutlined/>}>Descargar formato</Button>
								</Col>
							) : null}
							{validatePermission(constants.SHOW_LAST_TRUST_FUND_PERMISSION) ? (
								<Col>
									<LastPayment/>
								</Col>
							) : null}
						</Row>
					</>
				}

				table={[<>{stateLoading ? <Spinner/> : <Table columns={columns} dataSource={trustCollection}/>} </>]}
			>

			</Card>
			{visibleAdjustment &&
			<ModalAdjustmentCollection handleCancel={handleCancel} onClose={setVisibleAdjustment} data={dataRecord}/>}
		</>
	);


}

export default TableTrustCollection;