import React from "react";
import {useDispatch} from "react-redux";
import {DeleteOutlined, DownloadOutlined, UploadOutlined} from "@ant-design/icons";
import {errorGlobal, success} from "../../../../../handle/Notification/Notification";
import {Button, Col, Descriptions, Popconfirm, Row, Table, Tooltip, Upload} from "antd";
import {deleteFileCustomer, setFileCustomer} from "../../../../../../redux/actions/Customer/CustomerAction";

const ClientDocuments = ({customerAttachments,id,myOnClick}) => {
	const dispatch = useDispatch()
	const textDelete = <span style={{color: "#6A6963"}}>Eliminar</span>
	let customerAttachment = []
	const columns = [
		{
			title: "#",
			width: 20,
			dataIndex: "key",
			key: "key",
		},
		{
			title: "Documento",
			width: 80,
			dataIndex: "document",
			key: "document",
		},
		{
			title: "Acciones",
			dataIndex: "actions",
			width: 30,
			key: "id_attachment",
			render: (_, record) => getActions("user", record),
		},
	]
	
	const setSource = () => {
		if (customerAttachment.length === 0){
			customerAttachments.forEach((elem)=>{
				customerAttachment.push({
					key: elem.id,
					document: elem.name,
					path: elem.path
				})
			})
			
		}
	}
	setSource()
	const uploadProps = {
		showUploadList: false,
		onSuccess(response) {
			if (response.error){
				errorGlobal(response.message)
			}else {
				myOnClick()
				success(response.message)
			}
		},
		onError(response) {
			errorGlobal(response)
		},
		customRequest({onSuccess,file}) {
			let formData = new FormData()
			formData.append("customer_file", file, file.name)
			dispatch(setFileCustomer(id,formData,onSuccess))
		}
	}
	
	const deleteFileCustomerOfRow = (record) => {
		dispatch(
			deleteFileCustomer(record.key,record,async (response)=>{
				const res = await response
				if (res.error){
					errorGlobal(res.message)
				}else {
					myOnClick()
					success(res.message)
				}
			})
		)
	}

	const viewPrint = (path) => {
		const tagA = document.createElement("a");
		tagA.href = path;
		tagA.target = "_blank";
		document.body.appendChild(tagA);
		tagA.click();
	}
	
	const getActions = (name, record) => {
		return(
			<>
				<Tooltip placement="left" color="#ffffff" title={textDelete}>
					<Popconfirm 
						title={"¿Deseo eliminar?"}
						okText="Si"
						cancelText="No"
						onConfirm={()=>deleteFileCustomerOfRow(record)}
					>
						<Button
							type="primary"
							size="small"
							danger
							icon={<DeleteOutlined/>}
						/>
					</Popconfirm>
				</Tooltip>
				{"  "}
				<Button
					type="primary"
					size="small"
					icon={<DownloadOutlined/>}
					onClick={()=>viewPrint(record.path)}
				/>
			</>
		)
	}
	
	return (
		<Col xs={24} sm={24} md={14} lg={12} xl={12}>
			<Row>
				<Descriptions title={"Documentos del cliente"} />
				<Table
					bordered
					columns={columns}
					dataSource={customerAttachment}
					style={{width: "100%"}}
					pagination={{pageSize: 10}}
					scroll={{y: 240}}
				/>
			</Row>
			<Row>
				<div style={{marginLeft: "78%"}}>
					<Upload {...uploadProps} >
						<Button type="primary" icon={<UploadOutlined/>}>
							Subir Archivos
						</Button>
					</Upload>
				</div>
			</Row>
		</Col>
	)
}

export default ClientDocuments