import moment from "moment";
import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import Spinner from "../../../../handle/Spinner/Spinner";
import {FORMATTER_INPUT_NUMBER} from "../../../../../redux/constants";
import { errorGlobal, success } from "../../../../handle/Notification/Notification";
import {Form, Input, Button, Row, Col, DatePicker, Select, InputNumber} from "antd";
import { fetchAllProjects } from "../../../../../redux/actions/Project/ProjectsAction";
import { fetchAllCoupons } from "../../../../../redux/actions/Configuration/CuoponsActions";
import { updateCouponById } from "../../../../../redux/actions/Configuration/CuoponsActions";

const UpdateCuopons = ({ data, onClose }) => {

	const [form] = Form.useForm();
	const [loading, setLoading] = useState(false);
	const dispatch = useDispatch();

	const onReset = () => form.resetFields();

	const handleSubmit = async () => {
		try {
			setLoading(true)
			const values = await form.validateFields();
			const dto = {
				...values,
				expiration_code: values["dateDiscount"].format("YYYY-MM-DD"),
				creator_user_id: data.creator,
				customer_id: data.customerid,
				project_id:
					values["project_id"] === "0" ? null : parseInt(values["project_id"]),
			};
			delete dto.dateDiscount;
			delete dto.customer;

			dispatch(
				updateCouponById(data.id, dto, async (response) => {
					let res = await response;
					if (res.error) {
						errorGlobal("¡UPS! Ocurrio un error mientras se actualizaba");
					} else {
						success("Cupón actualizado!");
						dispatch(fetchAllCoupons());
					}
					onReset();
					onClose(false);
				})
			);
		} catch (errorInfo) {
			console.error(errorInfo);
		}
	};

	useEffect(() => {
		fillFields();
	}, [])


	useEffect(() => {
		dispatch(fetchAllProjects());
	}, []);


	let elementProject = useSelector((state) => state?.project);


	const fillFields = () => {
		form.setFieldsValue({
			code: data.code,
			customer: data.customer,
			dateDiscount: moment(data.expirationCode),
			project_id: data.project ? data.project : "0",
			coupon_type: data.coupon_type,
			discount_type: data.discount_type,
			discount_percentage: parseFloat(data.coupo_valuePercentage).toFixed(2),
			discount_value: parseFloat(data.coupon_valueDiscount).toFixed(2)
		});
		
	};

	return (
		<>
			<Form
				name="FormCuopons"
				layout="vertical"
				form={form}
				onFinish={handleSubmit}
			>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={8} lg={8} xl={8}>
						<Form.Item
							label="Codigo"
							name="code"
							rules={[
								{
									required: false,
									message: "Campo requerido",
								},
							]}
						>
							<Input
								placeholder="Codigo"
								disabled
							/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={8} lg={8} xl={8}>
						<Form.Item
							label="Cliente"
							name="customer"
							rules={[
								{
									required: false,
									message: "Campo requerido",
								},
							]}
						>
							<Input
								placeholder="Cliente"
								value={data.customer}
								disabled
							/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={8} lg={8} xl={8}>
						<Form.Item
							label="Proyecto"
							name="project_id"
							rules={[
								{
									required: false,
									message: "Campo requerido",
								},
							]}
						>
							<Select
								style={{ width: "100%" }}
								value={form.getFieldValue("project_id")}
							>
								<Select.Option value="">Seleccionar...</Select.Option>
								<Select.Option value="0">TODOS</Select.Option>
								{elementProject.hasOwnProperty("_payload") &&
									elementProject._payload?.map((item) => (
										<Select.Option value={item.id}>{item.name}</Select.Option>
									))}
							</Select>
						</Form.Item>
					</Col>
				</Row>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={8} lg={8} xl={8}>
						<Form.Item
							label="Fecha de vencimiento"
							name="dateDiscount"
							rules={[
								{
									required: true,
									message: "Campo requerido",
								},
							]}
						>
							<DatePicker
								style={{ width: "100%" }}
								value={form.getFieldValue("dateDiscount")}
							/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={8} lg={8} xl={8}>
						<Form.Item
							label="Tipo de cupón"
							name="coupon_type"
							rules={[
								{
									required: true,
									message: "Campo requerido",
								},
							]}
						>
							<Select
								style={{ width: "100%" }}
								placeholder="Select..."
								value={form.getFieldValue("coupon_type")}
							>
								<Select.Option value="">Seleccionar...</Select.Option>
								<Select.Option value={1}>Descuento</Select.Option>
								<Select.Option value={2}>Pronto Pago</Select.Option>
								<Select.Option value={3}>Canje</Select.Option>
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={8} lg={8} xl={8}>
						<Form.Item
							label="Tipo de descuento"
							name="discount_type"
							rules={[
								{
									required: true,
									message: "Campo requerido",
								},
							]}
						>
							<Select
								style={{ width: "100%" }}
								placeholder="Select..."
								value={form.getFieldValue("discount_type")}
							>
								<Select.Option value="">Seleccionar...</Select.Option>
								<Select.Option value={1}>Valor Total</Select.Option>
								<Select.Option value={2}>Porcentaje</Select.Option>
							</Select>
						</Form.Item>
					</Col>
				</Row>
				<Row gutter={16}>
					<Col xs={10} sm={10} md={8} lg={8} xl={8}>
						<Form.Item
							noStyle
							shouldUpdate={(prevValues, currentValues) =>
								prevValues.discount_type !== currentValues.discount_type
							}
						>
							{({ getFieldValue }) =>
								getFieldValue("discount_type") === 2 ? (
									<Form.Item
										label="Descuento (%)"
										name="discount_percentage"
										rules={[
											{
												validator: (_, value) =>
													parseFloat(value) > 0 && value != null ? Promise.resolve() : Promise.reject(new Error('Campo requerido y debe ser mayor a 0')),
											}
										]}
									>
										<Input
											style={{ width: '100%' }}
											placeholder="Valor"
											value={parseFloat(form.getFieldValue("discount_percentage"))}
										/>
									</Form.Item>
								) : (
									<Form.Item
										label="Descuento ($)"
										name="discount_value"
										rules={[
											{
												validator: (_, value) =>
													parseFloat(value) > 0 && value != null ? Promise.resolve() : Promise.reject(new Error('Campo requerido y debe ser mayor a 0')),
											}
										]}
									>
										<InputNumber
											{...FORMATTER_INPUT_NUMBER}
											style={{ width: '100%' }}
											placeholder="Valor"
											value={parseFloat(form.getFieldValue("discount_value"))}
										/>
									</Form.Item>
								)
							}
						</Form.Item>
					</Col>
				</Row>

				<Row>
					<Col offset={21} xs={24} sm={24} md={4} lg={4} xl={4}>
						<Form.Item>
							{ !loading ? (<Button type="primary" htmlType="submit">
									Guardar
								</Button>) : <Spinner />
							}
						</Form.Item>
						
					</Col>
				</Row>
			</Form>
			
		</>
	);
};

export default UpdateCuopons;
