import {Checkbox, Form} from "antd";
import React, {useEffect, useState} from "react";
import UploadOrDownloadFile from "./UploadOrDownloadFile";

const AfterSalesAccomplished = ({record,setCheckes}) => {
	const [state,setState] = useState(false)
	let id = record?.id !== undefined ? record?.id : 0
	const [chek,setChek] = useState(false)
	let name = "finish"+id
	let nameUp = "complianceDocument"+id
	let link = record?.afterSalesAccomplished.link
	let nameFile = record?.afterSalesAccomplished.name
	console.log(record)
	useEffect(()=>{
		if (record?.state === 'TRAMITADA' || record?.state === "EN PROCESO" || record.state === 'DECLINADA'){
			setState(true)
		}else {
			setState(false)
		}
	},[record])
	const setValues = (e) => {
		setCheckes(record.id,e.target.checked)
		setChek(e.target.checked)
	}
	return <>
		<UploadOrDownloadFile
			label={"Documento Adjunto"}
			name={nameUp}
			state={state}
			id={id}
			link={link}
			nameFile={nameFile}
		/>
		<Form.Item
			label={"Postventa cumplida?"}
			name={name}
			valuePropName={chek}
		>
			<Checkbox 
				value={chek} 
				disabled={state} 
				onChange={(e) => setValues(e)} />
		</Form.Item>
	</>
}

export default AfterSalesAccomplished