import React, {useEffect, useState} from "react";
import {Button, Card, Divider} from "antd";
import ManagementCompany from "./CompaniesComponent/ManagementCompany";
import NormalCompanieComponent from "./CompaniesComponent/NormalCompanieComponent";
import {useDispatch, useSelector} from "react-redux";
import {
	fetchAllCommissonParameterAction,
	updateOrCreateCommissonParameterActionById
} from "../../../../../../../redux/actions/Project/CommissionParameters/CommissionParametersAction";
import {errorGlobal, errorPolicy, success} from "../../../../../../handle/Notification/Notification";

const StepsForm = (props) => {
	const { id } = props;
	const idProject = id;
	const dataCommissionParamaterSelector = useSelector((state) => state?.comissionParameter);
	
	const dispatch = useDispatch();


	useEffect(() => {
		const { _payload } = dataCommissionParamaterSelector;
		setDataComissionParameter(_payload);
	}, []);

	const [dataCommissionParameter, setDataComissionParameter] = useState([]);

	useEffect(() => {
		if (dataCommissionParameter.length > 0) {
			const arrayToWorkComissionParameter = [...dataCommissionParameter];
			let found = 0;
			let managementCompany = [];
			let normalCompany = [];

			arrayToWorkComissionParameter.forEach((item) => {
				found = 0;
				if (item.company_type === 1) {
					managementCompany.push(item);
				} else {
					const newNormalCompanies = {
						key: keyNormalCompany,
						company: [item],
					};

					if (normalCompany.length === 0) {
						setKeyNormalCompany(keyNormalCompany + 1);
						normalCompany.push(newNormalCompanies);
					} else {
						normalCompany.forEach((element) => {
							if (
								element.company[0].affiliated_company_id ===
								item.affiliated_company_id
							) {
								found = 1;
								element.company.push(item);
							}
						});

						if (found === 0) {
							setKeyNormalCompany(keyNormalCompany + 1);
							normalCompany.push(newNormalCompanies);
						}
						
					}
				}
			});

			setManagementCompany(managementCompany);
			setNomalCompanies(normalCompany);
		}
		setSelectAffiliatedCompanies(props.afiliatedCompany._payload);
	}, [dataCommissionParameter]);

	const [selectAffiliatedCompanies, setSelectAffiliatedCompanies] = useState(
		[]
	);

	const [managementCompany, setManagementCompany] = useState([]);
	const [normalCompanies, setNomalCompanies] = useState([]);
	const [count, setCount] = useState(0);
	const [keyNormalCompany, setKeyNormalCompany] = useState(1);

	useEffect(() => {
		let arrayWork = Array.isArray(props.afiliatedCompany?._payload) ?  props.afiliatedCompany?._payload : JSON.parse(JSON.stringify(props.afiliatedCompany?._payload ?? []));
		let companies = joinDataCompanies();

		if (companies.length > 0) {
			const arrayToWork = [...arrayWork];
			let companyId = [];
			let filterCompanies = [];
			
			companies.forEach((item) => {
				companyId.push(
					item.affiliated_company_id === "" ? null : item.affiliated_company_id
				);
			});
			
			filterCompanies = arrayToWork.filter((companie) => {
				let res = companyId.find((idCompanie) => {
					return idCompanie === companie.id;
				});
				return res === undefined;
			});
			
			setSelectAffiliatedCompanies(filterCompanies);
		}
	}, [managementCompany, normalCompanies]);

	const updateNormalCompanies = (keyCompany, dataSource) => {
		const arrayToWorkNormalCompany = [...normalCompanies];
		arrayToWorkNormalCompany.map((item) => {
			if (item.key === keyCompany) {
				item.company = dataSource;
			}
			return item
		});

		setNomalCompanies(arrayToWorkNormalCompany);
	};

	const addNewNormalCompany = () => {
		const newNormalCompany = {
			key: keyNormalCompany,
			company: [
				{
					affiliated_company_id: "",
					commission: 0,
					commission_parameters_policy_comply: [
						{
							id: 1000,
							percentage_total_value: null,
							policy_to_comply_id: "",
							keyPolicy: 1000,
						},
					],
					company_type: 2,
					id: count + 1,
					label: "",
					projects_id: idProject,
				},
			],
		};

		const addNormalCompanies = [...normalCompanies, newNormalCompany];
		setKeyNormalCompany(keyNormalCompany + 1);
		setNomalCompanies(addNormalCompanies);
		setCount(count + 1);
	};

	const deleteNormalCompany = (key) => {
		const companies = [...normalCompanies];

		const normalCompaniesFilter = companies.filter((item) => {
			return item.key !== key;
		});

		setNomalCompanies(normalCompaniesFilter);
	};

	const joinDataCompanies = () => {
		let cleanNormalCompanies = [];

		const arrayToWorkNormalCompany = [...normalCompanies];

		arrayToWorkNormalCompany.forEach((item) => {
			item.company.forEach((item) => {
				cleanNormalCompanies.push(item);
			});
		});

		return cleanNormalCompanies.concat(managementCompany);
	};

	const cleanDtoToSend = (array) => {
		let arrayWork = JSON.parse(JSON.stringify(array));
		arrayWork.map((item) => {
			if (item.hasOwnProperty("created_at")) {
				delete item.created_at;
				delete item.updated_at;
				delete item.deleted_at;
				delete item.key;
				item.projects_id = parseInt(item.projects_id);
				item.affiliated_company_id = parseInt(item.affiliated_company_id);
				item.commission = parseFloat(item.commission);
			} else {
				delete item.key;
				item.id = null;
				item.projects_id = parseInt(item.projects_id);
				item.affiliated_company_id = parseInt(item.affiliated_company_id);
				item.commission = parseFloat(item.commission);
			}
			return item
		});
		for (let index = 0; index < arrayWork.length; index++) {
			arrayWork[index].commission_parameters_policy_comply.map((item) => {
				if (item.hasOwnProperty("created_at")) {
					delete item.created_at;
					delete item.updated_at;
					delete item.deleted_at;
					delete item.keyPolicy;
				} else {
					delete item.keyPolicy;
					item.id = null;
				}
				if (item.policy_to_comply_id !== 1) {
					item.percentage_total_value = null;
				}
				return item
			});
		}

		return arrayWork;
	};

	const validateOfNull = (array) => {
		let found = 0;
		let arrayWork = JSON.parse(JSON.stringify(array));
		for (let index = 0; index < arrayWork.length; index++) {
			if (
				arrayWork[index].afiliated_companies_id === null ||
				arrayWork[index].commission === null ||
				arrayWork[index].label === ""
			) {
				found = 1;
			}
		}

		return found === 1;

		
	};

	const handleSave = () => {
		let companies = joinDataCompanies();
		const dataCommissionParamter = cleanDtoToSend(companies);
		if (validateOfNull(dataCommissionParamter))
			return errorPolicy("Hay campos vacios!");
		dispatch(
			updateOrCreateCommissonParameterActionById(idProject, dataCommissionParamter, (res) => {
				res.error
					? errorGlobal("¡UPS! ha ocurrido un error mientras se actualizaba")
					: success("Actualizado exitosamente");
					dispatch(fetchAllCommissonParameterAction(idProject));
				}
			)
		);
	};

	const createManagementCompany = () => {
		const newManagementCompany = {
			affiliated_company_id: "",
			commission: 0,
			commission_parameters_policy_comply: [
				{
					id: 1000,
					percentage_total_value: null,
					policy_to_comply_id: "",
					keyPolicy: 1000,
				},
			],
			company_type: 1,
			id: 1000,
			label: "",
			projects_id: idProject,
		};

		setManagementCompany([newManagementCompany]);
	};

	const deleteManagementCompany = () => {
		setManagementCompany([]);
	};

	return (
		<>
			<Card title="Parámetros y comisiones" style={{ borderColor: "black" }}>
				<div style={{ display: "inline" , marginTop:"10%"}}>
					<Button type="primary" onClick={addNewNormalCompany}>
						Agregar Empresa
					</Button>
					<Button
						disabled={managementCompany.length !== 0}
						type="primary"
						style={{
							marginLeft: "5%",
							backgroundColor: "#5cb85c",
							borderColor: "#5cb85c",
						}}
						onClick={createManagementCompany}
					>
						Agregar Administradora
					</Button>
				</div>
				<div style={{ marginTop: "5%" }}>
					<div>
						{managementCompany.length > 0 && (
							<ManagementCompany
								idProject={idProject}
								policyToComply={props.policyToComply}
								dataCommissionParamaterSelector={managementCompany}
								setDataManagementCompany={setManagementCompany}
								selectAffiliatedCompanies={selectAffiliatedCompanies}
								deleteManagementCompany={deleteManagementCompany}
							/>
						)}
					</div>
					<Divider />
					{normalCompanies.length > 0 &&
						normalCompanies.map((item) => (
							<NormalCompanieComponent
								setSelectAffiliatedCompanies={setSelectAffiliatedCompanies}
								idProject={idProject}
								normalCompanies={item}
								updateNormalCompanies={updateNormalCompanies}
								selectAffiliatedCompanies={selectAffiliatedCompanies}
								policyToComply={props.policyToComply}
								key={item.id}
								id={item.id}
								keyCompany={item.key}
								onDelete={deleteNormalCompany}
							/>
						))}
				</div>

				<Button
					onClick={() => {
						handleSave();
					}}
					type="primary"
					style={{ marginTop: "1%", marginLeft: "85%" }}
					disabled={JSON.parse(localStorage.stateDisable)}
				>
					Guardar
				</Button>
			</Card>
		</>
	);
};

export default StepsForm;
