import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import FormSellingParameter from "./FormSellingParameter";
import { Card } from "antd";
import Spinner from "../../../../../../handle/Spinner/Spinner";
import { getSaleParameterById } from "../../../../../../../redux/actions/Project/Sales_ParameterAction";

const MainSellingParameter = ({id}) => {

	const dispatch = useDispatch();
	useEffect(() => {
		dispatch(getSaleParameterById(id));	
	}, []);

	const salesParemeterDatabase = useSelector((state) => state?.sales_Parameter);

	if (Array.isArray(salesParemeterDatabase))
		return (
			<Card title="Parámetros de ventas" style={{ borderColor: "black" }}>
				<Spinner />
			</Card>
		);

	return (
		<FormSellingParameter
			salesParemeterDatabase={salesParemeterDatabase}
			id={id}
		/>
	);
};

export default MainSellingParameter;
