import React, { useState } from "react";
import { Button, Modal, Tooltip } from "antd";
import { CreditCardOutlined } from "@ant-design/icons";
import FormRemainingPaymentMethod from "./FormRemainingPaymentMethod";

const RemainingPaymentMethod = ({ salesMade }) => {
	const [isModalVisible, setIsModalVisible] = useState(false);

	const showModal = () => {
		setIsModalVisible(true);
	};

	const onCloseModal = () => {
		setIsModalVisible(false);
	};

	const textRemainingPaymentMethod = (
		<span style={{ color: "#6A6963" }}>Forma de pago restante</span>
	);

	const titleModal = (
		<header>Forma de Pago Restante - Venta No. {salesMade.unit_code} </header>
	);
	return (
		<>
			<Tooltip
				placement="left"
				color="#ffffff"
				title={textRemainingPaymentMethod}
			>
				<Button
					style={{ background: "#52c41a", color: "#fff" }}
					size={"small"}
					onClick={showModal}
					icon={<CreditCardOutlined />}
					disabled={salesMade.is_desisted}
				/>
			</Tooltip>
			{isModalVisible && (
				<Modal
					title={titleModal}
					centered
					visible={isModalVisible}
					footer={false}
					onCancel={() => setIsModalVisible(false)}
					width="160vh"
				>
					<FormRemainingPaymentMethod
						isModalVisible={isModalVisible}
						salesMade={salesMade}
						onCloseModal={onCloseModal}
					/>
				</Modal>
			)}
		</>
	);
};

export default RemainingPaymentMethod;
