import React from "react";
import {Col, Row, Table, Tag} from "antd";
import {getColumnSearchProps, noWhiteWindow} from "../../../../../../handle/HandleFilterTable/handleFilterTable";

const ListQuotes = ({quotations}) => {
	
	const State = (state) => {
		switch (state) {
			case "Disponible":
				return <Tag color={"green"}> {state} </Tag>
			case "Separada":
				return <Tag color={"lime"}> {state} </Tag>
			case "Comprada":
				return <Tag color={"red"}> {state} </Tag>
			default:
				return <Tag color={"white"}> {state} </Tag>
		}
	}
	
	const separateUnits = [
		{
			title: 'Código',
			width: 20,
			dataIndex: 'code',
			align: "center",
			key: 'code',
			sorter: (a, b) => a.code - b.code,

		},
		{
			title: 'Proyecto',
			width: 50,
			dataIndex: 'project',
			align: "center",
			key: 'project',
			...getColumnSearchProps('project'),
			...noWhiteWindow
		},
		{
			title: 'Unidad',
			dataIndex: 'unit',
			key: 'unit',
			align: "center",
			width: 30,
			sorter: (a, b) => a.unit - b.unit
		},
		{
			title: 'Cód. Fiduciario',
			dataIndex: 'trustCode',
			align: "center",
			key: 'trustCode',
			width: 30,
			...getColumnSearchProps('trustCode'),
			...noWhiteWindow
		},
		{
			title: 'No. Habitaciones',
			dataIndex: 'numberRooms',
			key: 'numberRooms',
			align: "center",
			width: 30,
			...getColumnSearchProps('numberRooms'),
			...noWhiteWindow
		},
		{
			title: 'Estado Unidad del Proyecto',
			dataIndex: 'projectUnitStatus',
			key: 'projectUnitStatus',
			align: "center",
			width: 40,
			render: (_, record) => State(record.projectUnitStatus)
		},
	]
	return (
		<Row>
			<Col span={24}>
				<Table
					size='small'
					columns={separateUnits}
					scroll={{ x: 1100, y: 300 }}
					dataSource={quotations}
					bordered ={true}
				/>
			</Col>
		</Row>
	)
}

export default ListQuotes