import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { getProjectById } from "../../../../../../../redux/actions/Project/ProjectsAction";
import { Card } from "antd";
import EditProject from "./EditProject";
import Spinner from "../../../../../../handle/Spinner/Spinner";

const MainEditProject = ({id}) => {
	
	const dispatch = useDispatch();
	const [project, setProject] = useState([]);

	useEffect(() => {
		dispatch(
			getProjectById(id, async (response) => {
				const res = await response;
				setProject(res);
			})
		);
	}, []);

	if (Array.isArray(project)) {
		return (
			<Card title="Información básica" style={{ borderColor: "black" }}>
				<Spinner />
			</Card>
		);
	} else {
		return <EditProject project={project} projectId={id}/>;
	}
};

export default MainEditProject;
