import React, { useEffect } from "react";
import { PageHeader, Button } from "antd";
import TableControlPower from "./component/TableControlPower";
import { useDispatch } from "react-redux";
import { fetchAllControlPowers } from "../../../../redux/actions/Project/ControlPowerAction";
import { fetchAllPricesRange } from "../../../../redux/actions/Configuration/PricesRangeAction";
import { fetchAllBuilder } from "../../../../redux/actions/Project/BuilderAction";
import { Link } from "react-router-dom";
import { validateModule, validatePermission } from "../../../handle/PermissionMethods/PermissionMethods";
import * as constants from "../../../../redux/constants";
import { Redirect } from "react-router-dom/cjs/react-router-dom.min";

const ControlPower = () => {
	const dispatch = useDispatch();
	useEffect(() => {
		dispatch(fetchAllControlPowers());
	}, []);
	useEffect(() => {
		dispatch(fetchAllPricesRange());
	}, []);
	useEffect(() => {
		dispatch(fetchAllBuilder());
	}, []);

	if (!validateModule(constants.POWER_CONTROL_SUBMODULE)) {
		return <Redirect to={constants.DEFAULT_ROUTE} />;
	}
	return (
		<>
			<PageHeader
				style={{ border: "1px solid rgb(235, 237, 240);" }}
				title={"Control de Competencia"}
				extra={[
					validatePermission(constants.CREATE_POWER_CONTROL_PERMISSION) ? (
						<Button type="primary">
							<Link to="/projects/new-control-competences">
								Nuevo Control de Competencia
							</Link>
						</Button>
					) : null

				]}
			/>
			<TableControlPower />
		</>
	);
};

export default ControlPower;
