import moment from "moment";
import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { UploadOutlined } from "@ant-design/icons";
import { ExcelRenderer } from "react-excel-renderer";
import Spinner from '../../../../handle/Spinner/Spinner';
import { PlusOutlined, RedoOutlined } from "@ant-design/icons";
import { errorGlobal,success } from "../../../../handle/Notification/Notification";
import { Form, Input, Button, Row, Col, Table, Modal, Upload, Divider } from "antd";
import { convertAllMapDataState } from "../../../../handle/HandleFilterTable/handleFilterTable";
import { createTrustCollection,getAllTrustCollections } from "../../../../../redux/actions/Collection/TrustCollectionAction";

const FormTrustCollection = () => {
	
	const [pressBottonState, setPressBottonState] = useState(false);

	const [visible, setVisible] = useState(false);
	const [fileUpload, setFileUpload] = useState(null);
	const [dataErrors, setDataErrors] = useState([]);
	const [dataProcessed, setDataProcessed] = useState([]);
	const [fileProcesed, serFileProcesed] = useState(null);
	const [fileCorrect, setFileCorrect] = useState(false);

	const [form] = Form.useForm();

	const dispatch = useDispatch();

	const showModal = () => {
		setVisible(true);
	};

    const columns = [
		{
			title: "Descripción",
			width: 400,
			dataIndex: "description",
			key: "description",
			align: "center",
		},
	];

	const onClose = () => {
		setVisible(false);
		form.resetFields();
		setDataErrors([]);
		setDataProcessed([]);
	};

	const uploadProps = {
		name: "file",
		beforeUpload: (upload) => {
			if (upload.type !== "application/vnd.ms-excel") {
				let dataErros = [];
				dataErros.push({
					description:
						"Tipo de archivo no permitido. Cargue un Excel de tipo XLS",
				});
				dataErrors.forEach((d) => {
					dataErros.push(d);
				});
				setDataErrors(dataErros);
				errorGlobal(`Formato inválido`);
			} else {
				let dataProcess = [];
				dataProcess.push({
					description:
						"El formato archivo es válido. Nombre del archivo: " + upload.name,
				});
				dataProcessed.forEach((d) => {
					dataProcess.push(d);
				});
				setDataProcessed(dataProcess);
				setFileCorrect(true);
				return false;
			}
		},
		
		onChange: (info) => {
			form.setFieldsValue({
				file_name: info.file.name,
				
			});
			setFileUpload(info.file);
			serFileProcesed(info.fileList.pop().originFileObj);
		},
		showUploadList: false,
	};
	
	let userStorage = JSON.parse(localStorage.getItem("USER_INFO"));

    const validateFile = async () => {
		setPressBottonState(true);
        if (fileCorrect) {
            ExcelRenderer(fileProcesed, (err, resp) => {
                if (err) {
                    console.error(err)
                } else {
                    let rows = resp.rows[1];
                    let validDate = moment(rows[2]).isValid();
                    if (rows.length > 0 && rows[0].length > 0 && toString(rows[1]).length > 0 && validDate === true &&
						rows[3].length > 0 && parseFloat(rows[4]) !== NaN)
					{
                        let dataProcess = [];
                        dataProcess.push({
                            description: "El archivo fue procesado con éxito. Nombre del archivo: " + fileUpload.name
                        });
                        dataProcessed.forEach(d => { dataProcess.push(d) });
                        setDataProcessed(dataProcess);
                        sendFile(fileProcesed);
                    } else {
                        let dataErros = [];
                        dataErros.push({
                            description: "No se pudo procesar el archivo."
                        });
                        dataErrors.forEach(d => { dataErros.push(d) });
                        setDataErrors(dataErros);
                    }
                }
            })
        }
    }
    const sendFile = async (file) => {
	
            try {
                const values = await form.validateFields();
                const dto = {
                    ...values,
                    user_id: userStorage.id,
                }
                let formData = new FormData();
                formData.append('fiduciary_collection_document', file, file.name);
                formData.append('user_id', dto.user_id);
                delete dto.file_name;

                dispatch(createTrustCollection(formData, (response) => {
                    if (!response.error) {
						success("Recaudo fiduacial creado");
                        dispatch(getAllTrustCollections());
                        onClose();
                    }else{
                        errorGlobal("¡UPS! Ocurrió un error mientras se registraba el recaudo");
                    }
                }));
				setPressBottonState(false);
            } catch (errorInfo) {
                errorGlobal("¡UPS! Ocurrió un error mientras se registraba el recaudo " + errorInfo);
				setPressBottonState(false);
            }
	
    }

	return (
		<>
			<Button type="primary" onClick={showModal} icon={<PlusOutlined />}>
				Nuevo
			</Button>
			<Modal
				visible={visible}
				footer={false}
				onCancel={onClose}
				width={900}
				title="Nuevo recaudo fiducia"
			>
				<Form layout="vertical" size="middle" form={form}>
					<Row gutter={16} align="middle" justify="center">
						<Col xs={24} sm={24} md={16} lg={16} xl={10}>
							<Form.Item
								name="file_name"
								label="Cargar archivo fiducia"
								rules={[{ required: true, message: "Campo requerido" }]}
							>
								<Input style={{width: "100%"}} disabled={true}/>
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={16} lg={16} xl={5}>
							<Form.Item  name="SelectFile" label=" ">
								<Upload {...uploadProps}>
									<Button  disabled={pressBottonState} type="primary" icon={<UploadOutlined />}>
										Elegir archivo
									</Button>
								</Upload>
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={16} lg={16} xl={5}>
							<Form.Item name="butttonProcesed" label=" ">
								<Button 
								    disabled={pressBottonState}
									type="primary"
									style={{ background: "#c21313", borderColor: "#c21313" }}
									onClick={validateFile}
									icon={<RedoOutlined />}
								>
									Procesar
								</Button>
							</Form.Item>
						</Col>
						{ pressBottonState? <Col xs={2} sm={2} md={2} lg={2} xl={2}><Spinner/></Col>:null}
					</Row>
					<Divider />

					<Row gutter={16}>
						<Col>
							<Form.Item label="Errores">
								<Table
									pagination={false}
									bordered
									columns={columns}
									dataSource={convertAllMapDataState(dataErrors)}
								/>
							</Form.Item>
						</Col>
						<Col>
							<Form.Item label="Procesados">
								<Table
									pagination={false}
									bordered
									columns={columns}
									dataSource={convertAllMapDataState(dataProcessed)}
								/>
							</Form.Item>
						</Col>
					</Row>
				</Form>
			</Modal>
		</>
	);
};

export default FormTrustCollection;
