import React from 'react';
import {Col, Row} from 'antd';
import {FORMATTER_PESO} from "../../../../../redux/constants";


const detailToDate = ({detailToDate}) => {
    
    
  return (
		<Row>
			<Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 24 }}>
			<table className="table-report">
                <tr>
                    <td colSpan="7" class="title head_color th-report"><b>2.2 DETALLE A LA FECHA</b></td>
                </tr>
                <tr>
                    <td colSpan="7" class="title head_color th-report"><b>Inmueble General</b></td>
                </tr>
                <tr class="title head_color th-report">
                    <td className="th-report">Fecha de Corte</td>
                    <td className="th-report">Pagado a la Fecha</td>
                    <td className="th-report">Valor Pactado a la
                        Fecha</td>
                    <td className="th-report">Saldo a Favor o
                        Pendiente</td>
                    <td className="th-report">Días de Mora</td>
                    <td className="th-report">* Descuento de
                        Intereses</td>
                    <td className="th-report">** Recargo de
                        Intereses</td>
                </tr>
                <tr className="th-report">
                    <td className="th-report ">{detailToDate.row_0.cutoff_date}</td>
                    <td class="cell_not_center_rigth th-report padding-number">{FORMATTER_PESO.format(detailToDate.row_0.value_paid_to_date)}</td>
                    <td class="cell_not_center_rigth th-report padding-number">{FORMATTER_PESO.format(detailToDate.row_0.agreed_value)}</td>
                    <td class="cell_not_center_rigth th-report padding-number">{FORMATTER_PESO.format(detailToDate.row_0.value_for_or_against)}</td>
                    <td class="cell_not_center_rigth with_for_info_detailToDate th-report padding-number">{detailToDate.row_0.days_in_arrears}</td>
                    <td class="cell_not_center_rigth with_for_info_detailToDate th-report padding-number">          </td>
                    <td class="cell_not_center_rigth with_for_info_detailToDate th-report padding-number">          </td>
                </tr>
                <tr class="title head_color th-report">
                    <td class="title  th-report">Fecha Limite Para
                        Reforma</td>
                    <td className="th-report">Pago de Reforma a
                        la Fecha</td>
                    <td className="th-report">Valor Reforma
                        Aprobada</td>
                    <td className="th-report">Saldo Pendiente</td>
                    <td colSpan="3">Estado de reforma</td>
                </tr>
                <tr >
                    <td className="th-report">{detailToDate.row_1.reform_deadline}</td>
                    <td class="cell_not_center_rigth th-report padding-number">{FORMATTER_PESO.format(detailToDate.row_1.reform_payment)}</td>
                    <td class="cell_not_center_rigth th-report padding-number">{FORMATTER_PESO.format(detailToDate.row_1.approved_reform_value)}</td>
                    <td class="cell_not_center_rigth th-report padding-number">{detailToDate.row_1.value_against}</td>
                    <td colSpan="3" className="th-report padding-number" >                   </td>
                </tr>
            </table>
			</Col>
		</Row>
  );
}

export default detailToDate;