import * as constants from "../../../constants";

export default function desistSaleReducer(state = [], action) {
	switch (action.type) {
		case constants.FETCH_BY_ID_SALE_WITHDRAWAL:
			return action.payload._payload.length > 0
				? action.payload._payload[0]
				: action.payload._payload;
		case constants.UPDATE_SALE_WITHDRAWAL:
			return action.payload._payload;
		case constants.CREATE_SALE_WITHDRAWAL:
			return action.payload._payload;
		case constants.RESET_SALE_WITHDRAWAL:
			return action.payload;
		default:
			return state;
	}
}
