import React from "react";
import {Descriptions} from "antd";
import {formatterMoney} from "../../../../../handle/FormatterMoney/FormatterMoney";

export const PaymentForTheProject = React.memo(({title,quote}) => {
	return (
		<>
			<Descriptions
				title={title}
				style={{ marginTop: 15 }}
				size="small"
				bordered
				column={{ xxl: 2, xl: 1, lg: 1, md: 1, sm: 1, xs: 1 }}
			/>
			<Descriptions
				style={{ marginTop: 15 }}
				size="small"
				bordered
				column={{ xxl: 2, xl: 1, lg: 1, md: 1, sm: 1, xs: 1 }}
			>
				<Descriptions.Item
					label={`C.I. No Financiada (${parseInt(quote?.initialFeeNotFundedPercentage)}%)`}
				>
					{formatterMoney.format(quote?.initialFeeNotFunded)}
				</Descriptions.Item>
				<Descriptions.Item
					label={`C.I. Financiada (${parseInt(quote?.initialFeeFinancedPercentage)}%)`}
				>
					{formatterMoney.format(quote?.initialFeeFinanced)}
				</Descriptions.Item>
			</Descriptions>
		</>
	)
});