import React, { useEffect, useState } from "react";
import { fetchAllCustomerWithYourUnits, getInfoAccountStatus } from "../../../../redux/actions/Customer/CustomerAction";
import { fetchAllProjects } from "../../../../redux/actions/Project/ProjectsAction";
import { connect, useDispatch } from "react-redux";
import { warning } from "../../../handle/Notification/Notification";
import { Card, Col, Row, Form, Select, Button, PageHeader } from "antd";
import PaginationTables from "./components/PaginationTables";
import { validateModule } from "../../../handle/PermissionMethods/PermissionMethods";
import * as constants from "../../../../redux/constants";
import { Redirect } from "react-router-dom/cjs/react-router-dom.min";
import ExportExcelPdf from "./components/AccountStatusReport";
import Spinner from "../../../handle/Spinner/Spinner";

const CompetitionReport = ({ }) => {
	const dispatch = useDispatch();
	const [customerWithYourUnits, setCustomerWithYourUnits] = useState([]);
	const [info_table, setInfoTable] = useState(null);
	const [currentCustomer, setcurrentCustomer] = useState(null);
	const [idSale, setIdSale] = useState(null);
	const [userId, setUserId] = useState(JSON.parse(localStorage.getItem('USER_INFO')));
	const [consultind, setConsulting] = useState( null )
	
	useEffect(() => {
		dispatch(fetchAllCustomerWithYourUnits(async (response) => {
			let res = await response;
			if (!res.error) {
				setCustomerWithYourUnits(res._payload);
			}
		}));
	}, []);
	const validateMessages = {
		required: "${label} es requerido",
		types: {
			email: "${label} no es un correo válido",
			number: "${label} debe ser un número",
		},
	};

	const onFinish = () => {
		setConsulting(false);
		setInfoTable(null);
		let data = {
			"id_customer": currentCustomer.customer_id,
			"id_sale": idSale,
			"id_user": userId.id,
		}
		dispatch(getInfoAccountStatus(data, async (response) => {
			setConsulting(true);
			let res = await response;
			if (!res.error) {
				if (res._payload === []) {
					return warning("El cliente actual no tiene ninguna informacion con la unidad seleccionada");
				}
				setInfoTable(res._payload);
			} setConsulting(false);
		}));
	};


	if (!validateModule(constants.ACCOUNT_STATUS_SUBMODULE)) {
		return <Redirect to={constants.DEFAULT_ROUTE} />;
	}

	const onChangeCustomer = (value) => {
		if (value !== "") {
			let customer = customerWithYourUnits.filter(element => element.customer_id === value);
			setcurrentCustomer(customer[0]);
		}
	}

	const onChangeUnit = (value) => {
		if (value !== "") {
			setIdSale(value);
		}
	}
	return (
		<Card title="Informe de Estado de Cuenta">
			<Form
				layout="vertical"
				onFinish={onFinish}
				validateMessages={validateMessages}
			>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item
							label="Cliente:"
							name="Cliente"
							rules={[
								{
									required: true,
								},
							]}
						>
							<Select
								defaultValue={""}
								showSearch
								style={{ width: "100%" }}
								optionFilterProp="children"
								onChange={(value) => { onChangeCustomer(value) }}
								filterOption={(input, option) =>
									option.children.toLowerCase().indexOf(input.toLowerCase()) >=
									0
								}
							>
								<Select.Option value="">Seleccione...</Select.Option>
								{								
									customerWithYourUnits.map((item) => (
										<Select.Option
											value={item.customer_id}
										>{` ${item.customer_name.replace('_', ' ')} - CC: ${item.customer_identification}`}</Select.Option>
									))}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item
							label="Proyecto:"
							name="searchProject"
							rules={[
								{
									required: true,
								},
							]}
						>
							<Select
								defaultValue={""}
								showSearch
								style={{ width: "100%" }}
								optionFilterProp="children"
								onChange={(value) => { onChangeUnit(value) }}
								disabled={currentCustomer == null}
								filterOption={(input, option) =>
									option.children.toLowerCase().indexOf(input.toLowerCase()) >=
									0
								}
							>
								<Select.Option value="">Seleccione...</Select.Option>
								{currentCustomer != null &&
									currentCustomer.sales?.map((item) => (
										<Select.Option value={item.sale_id}>{` ${item.project_name} - ${item.label_unit}`}</Select.Option>
									))}
							</Select>
						</Form.Item>
					</Col>
				</Row>
				<Row>
					<Col xs={18} sm={20} md={21} lg={22} xl={22} />
					<Col xs={2} sm={2} md={2} lg={2} xl={2}>
						<Form.Item>
							<Button type="primary" htmlType="submit">
								Buscar
							</Button>
						</Form.Item>
					</Col>
				</Row>
			</Form>
			{info_table!=null? <>
					<ExportExcelPdf id_customer={currentCustomer.customer_id} id_sale={idSale} id_user={userId.id} />	
				<PageHeader />
				<PaginationTables data={info_table} />
			</> : consultind==null?<></>:<Spinner/>
			}
		</Card>
	);
};

const mapDispatchToProps = (dispatch) => ({
	dispatchFetchAllProjectAction: () => dispatch(fetchAllProjects()),
});

export default connect(null, mapDispatchToProps)(CompetitionReport);
