import React, {useEffect, useState} from "react"
import {Col, Descriptions, Row} from "antd";
import {formatterMoney} from "../../../../../handle/FormatterMoney/FormatterMoney";
import {FormatterPeso} from "../Utilitis/Utilitis";

const DescriptionOfProjectValues = React.memo(({paymentMethodProposedByTheProject}) => {

	const [finalValue,setFinalValue] = useState(paymentMethodProposedByTheProject);
	
	const decideTheColor = () => {
		if (finalValue.financialDiscountOrIncrease === 0){
			return (<Descriptions.Item label="Descuento financiero"> {FormatterPeso.format(0)} </Descriptions.Item>)
		}else if (finalValue.financialDiscountOrIncrease < 0){
			return (<Descriptions.Item className="valor3" label="Descuento financiero"> {FormatterPeso.format(finalValue.financialDiscountOrIncrease)} </Descriptions.Item>)
		}else if (finalValue.financialDiscountOrIncrease > 0){
			return (<Descriptions.Item className={"valor2"} label="Incremento financiero"> {FormatterPeso.format(finalValue.financialDiscountOrIncrease)} </Descriptions.Item>)
		}
	}
	
	useEffect(() => {
		setFinalValue(paymentMethodProposedByTheProject);
	}, [paymentMethodProposedByTheProject]);

	return (
		<Row>
			<Col span={24}>
				<Descriptions
					style={{ marginTop: 15 }}
					size="small"
					bordered
					column={{ xxl: 1, xl: 1, lg: 1, md: 1, sm: 1, xs: 1 }}
				>
					<Descriptions.Item label="Valor de la unidad">
						{formatterMoney.format(finalValue.unitValue)}
					</Descriptions.Item>
					<Descriptions.Item label="Valor presente">
						{formatterMoney.format(finalValue.presentValue)}
					</Descriptions.Item>
				</Descriptions>
			</Col>
			<Col span={24}>
				<Descriptions
					style={{ marginTop: 15 }}
					size="small"
					bordered
					column={{ xxl: 1, xl: 1, lg: 1, md: 1, sm: 1, xs: 1 }}
				>
					{
						decideTheColor()
					}
					<Descriptions.Item label="Descuento comercial">
						{formatterMoney.format(finalValue.commercialDiscounts)}
					</Descriptions.Item>
					<Descriptions.Item label="Descuento cupones">
						{formatterMoney.format(finalValue.discountCoupons)}
					</Descriptions.Item>
					<Descriptions.Item label="Descuento acabados">
						{finalValue.finishesIncluded
								? formatterMoney.format(0)
								: formatterMoney.format(finalValue.discountFinishes)
						}
					</Descriptions.Item>
				</Descriptions>
			</Col>
		</Row>
	)
})

export default DescriptionOfProjectValues