import React, { useEffect } from "react";
import { Table, Checkbox } from "antd";
import { useDispatch } from "react-redux";
import { fetchAllExchangesAction } from "../../../../../../redux/actions/Price/SaleMade/SalesMadeAction";
import { errorGlobal } from "../../../../../handle/Notification/Notification";
import moment from "moment";
import Spinner from "../../../../../handle/Spinner/Spinner";
import {convertAllMapDataState} from "../../../../../handle/HandleFilterTable/handleFilterTable";

const TableManageExchanges = ({
	requestExchanges,
	setRequestExchanges,
	isModalVisible,
	data,
	addExchangeToApply,
}) => {
	const dispatch = useDispatch();

	useEffect(() => {
		if (isModalVisible) {
			let request = {
				sale_id: data.id,
				customer_id: data.customer.id,
			};
			dispatch(
				fetchAllExchangesAction(request, async (response) => {
					let res = await response;
					if (res.error) {
						errorGlobal("¡UPS, Ha ocurrido un problema.");
					} else {
						setRequestExchanges(res);
					}
				})
			);
		}
	}, [isModalVisible]);

	const columns = [
		{
			title: <strong>Codigo</strong>,
			width: 5,
			dataIndex: "code",
			key: "code",
			align: "center",
		},
		{
			title: <strong>Fecha de vencimiento</strong>,
			width: 6,
			dataIndex: "expiration_code",
			key: "expiration_code",
			align: "center",
			render: (_, record) => (
				<h6>{moment(record.expiration_code).format("YYYY-MM-DD")}</h6>
			),
		},
		{
			title: <strong>Tipo de descuento</strong>,
			width: 7,
			dataIndex: "discount_type",
			key: "discount_type",
			align: "center",
			render: (_, record) =>
				record.discount_type === 1 ? <h6>Valor total</h6> : <h6>Porcentaje</h6>,
		},
		{
			title: <strong>Descuento</strong>,
			width: 6,
			dataIndex: "discount_value",
			key: "discount_value",
			align: "center",
			render: (_, record)=> record.discount_type === 1 ? (
				formatterPeso.format(record.discount_value)
			) : (
				parseFloat(record.discount_percentage).toFixed(2) + " %"
			)
		},
		{
			title: <strong>Total a descontar</strong>,
			width: 6,
			dataIndex: "discount_value",
			key: "discount_value",
			align: "center",
			render: (_, record) =>
				record.discount_type === 1 ? (
					<h6 style={{ fontSize: "14px" }}>{formatterPeso.format(record.discount_value)}</h6>
				) : (
					<h6>
						{formatterPeso.format(parseFloat(data.value_total) * (parseFloat(record.discount_percentage) / 100))}
					</h6>
				),
		},
		{
			title: <strong>Acciones</strong>,
			width: 4,
			dataIndex: "actions",
			key: "actions",
			align: "center",
			render: (_, record) => getActions(record),
		},
	];
	const formatterPeso = new Intl.NumberFormat("es-CO", {
		style: "currency",
		currency: "COP",
		minimumFractionDigits: 0,
	})

	const getActions = (record) => (
		<Checkbox
			key={record.id}
			name={`check${record.id}`}
			onChange={() => addExchangeToApply(record)}
			defaultChecked={record.used}
			disabled={record.used}
		/>
	);

	return !Array.isArray(requestExchanges) ? (
		<Table
			size={"small"}
			scroll={{ x: 700, y: 400 }}
			bordered={true}
			columns={columns}
			dataSource={convertAllMapDataState(requestExchanges?._payload)}
			pagination={false}
		/>
	) : (
		<Spinner />
	);
};

export default TableManageExchanges;
