import * as constants from "../../constants"
const Ruta = '/report/potential-customers'
const PDF = '/PDF'
const EXCEL = '/EXCEL'

export const getAllPotentialClientsReport = ( data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: "POST",
		url: constants.RUTA + Ruta,
		data,
		success: (data) => allPotentialClientsReport(data),
		postProcessSuccess: onSuccess,
		postProcessError: onError
	},
});
export const GeneratePDF = (data, onSuccess, onError) =>({
	type: constants.API,
	payload: {
		method: "POST",
		url: constants.RUTA + Ruta + PDF,
		data,
		postProcessSuccess: onSuccess,
		postProcessError: onError
	},
})
export const GenerateEXCEL = (data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: "POST",
		url: constants.RUTA + Ruta + EXCEL,
		data,
		postProcessSuccess: onSuccess,
		postProcessError: onError
	},
})
const allPotentialClientsReport = (trustCollection) => ({
	type: constants.GET_POTENTIAL_CUSTOMERS,
	payload: trustCollection,
});