import { Form, Row, Button, Col, Card } from "antd";
import React  from "react";
import { useDispatch } from "react-redux";
import { getActivitiesByAssessor } from "../../../../redux/actions/Report/MonitoringActivities/MonitoringActivitiesActions";
import DatabaseSelect from "../../../handle/DatabaseSelect/DatabaseSelect";
import { errorGlobal } from "../../../handle/Notification/Notification";

let initalValues = {
	assessor: "",
};

const FormMonitoringActivities = ({
	databaseAssessor,
	setDataSource,
	setFetching,
	setSelectedAssessor,
}) => {
	
	const dispatch = useDispatch();
	const [form] = Form.useForm();

	const handleSubmit = async () => {
		const values = await form.validateFields();
		setFetching(false);
		dispatch(
			getActivitiesByAssessor(values.assessor, async (response) => {
				let data = await response._payload;
				if (data.error) {
					return errorGlobal("¡UPS! Ha ocurrido un problema.");
				} else {
					let dataWorked = data.map((item) => {
						return {
							period: item.tiempo,
							scheduled: item.programadas,
							performed: item.realizadas,
							pending: item.pendientes,
							expired: item.vencidas,
							visits: item.visitas,
						};
					});
					setFetching(true);
					setDataSource(dataWorked);
				}
			})
		);
	};

	return (
		<Form
			layout="vertical"
			onFinish={handleSubmit}
			form={form}
			initialValues={initalValues}
		>
			<Card
				title="Seguimiento a actividades"
				actions={[
					<Row>
					<Col xs={18} sm={20} md={21} lg={22} xl={22} />
					<Col xs={2} sm={2} md={2} lg={2} xl={2}>
						<Form.Item>
							<Button type="primary" htmlType="submit">
								Buscar
							</Button>
						</Form.Item>
					</Col>
				</Row>,
				]}
			>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<DatabaseSelect
							setSelectedAssessor={setSelectedAssessor}
							dataToMap={databaseAssessor._payload}
							SelectValue="id"
							label="name"
							isRequired={true}
							labelFormItem="Asesor"
							nameFormItem="assessor"
						/>
					</Col>
				</Row>
			</Card>
		</Form>
	);
};

export default FormMonitoringActivities;
