import React from "react";
import { Select } from "antd";

const ParkingSelect = ({disabledSimpleParking, disabledDoubleParking, record, updateParking}) => {

	return (
		<Select
			onChange={(value) => {
				updateParking(value, record);
			}}
			disabled={record?.state}
			value={record?.parking === "null" || record?.parking === "" ? 0  : record?.parking}
			name="parking"
		>
			<Select.Option  value={0}>Seleccion</Select.Option>
			<Select.Option   disabled={disabledSimpleParking} value={1}>
				Sencillo
			</Select.Option >
			<Select.Option disabled={disabledDoubleParking} value={2}>
				Doble Lineal
			</Select.Option>
		</Select>
	);
};

export default ParkingSelect;
