import React from "react";
import { useDispatch } from "react-redux";
import { Form, Input, Button } from "antd";
import { CreateByName } from "../../../../../redux/actions/Configuration/PricesRangeAction";
import { success, errorGlobal } from "../../../../handle/Notification/Notification";

const layout = {
	labelCol: {
		span: 24,
	},
	wrapperCol: {
		span: 21,
	},
};

const FormPresupuesto = (prods) => {
	const dispatch = useDispatch();
	const { onClose , setPriceRange } = prods;
    const [form] = Form.useForm();
    const onFinish = async() => {
        form.setFieldsValue({state:true});
        const values = await form.validateFields();
        let fData = new FormData();
        fData.append("name", values["nombre"]);
        fData.append("state", 1);
		dispatch(
			CreateByName(fData, (response) => {
				if (response.error) {
					errorGlobal("¡UPS! Ocurrió un error mientras se registraba");
				} else {
					success("Rango de precio registrado");
                    onClose();
                    setPriceRange(response);
				}
			})
		);
		
	};
	return (
		<Form {...layout} name="FormPresupuesto" form={form} onFinish={onFinish}>
			<Form.Item
				label="Nombre"
				name="nombre"
				rules={[
					{
						required: true,
						message: "El campo es requerido",
					},
				]}
			>
				<Input />
			</Form.Item>
			<Form.Item>
				<Button type="primary" htmlType="submit">
					Guardar
				</Button>
			</Form.Item>
		</Form>
	);
};

export default FormPresupuesto;
