import React from 'react';
import moment from 'moment';
import { useEffect } from 'react';
import { Descriptions } from 'antd';
import { dateFormat } from '../../../../handle/HandleDatePicker/HandleDate';

const SaleDetail = ({project, creator_user,unit,customer,date_reform_request}) => {

    useEffect(()=>{
    },[project, creator_user,unit,customer,date_reform_request])
    const finalFront = (
        <>
        
         <Descriptions title="Detalle de la venta">
            <Descriptions.Item label="Proyecto">{project.name}</Descriptions.Item>
            <Descriptions.Item label="Empresa comercializadora">{project?.businessName}</Descriptions.Item>
            <Descriptions.Item label="Usuario creador">{creator_user?.name}</Descriptions.Item>
            <Descriptions.Item label="Cliente">{customer.name}</Descriptions.Item>
            <Descriptions.Item label="Unidad">{unit.label}</Descriptions.Item>
            <Descriptions.Item label="Fecha de solicitud">{date_reform_request ? moment(date_reform_request, dateFormat).format(dateFormat) : ''}</Descriptions.Item>
        </Descriptions>
        </>
		
	);

    

    return (<>
      
        {finalFront}
    </>)
}
export default SaleDetail;