import * as constants from "../../../constants";

export default function assignResponsibleReducer(state = [], action) {
	switch (action.type) {
		case constants.SET_ALL_ASSIGN_RESPONSIBLE:
			return action.payload;
		case constants.UPDATE_OR_CREATE_ASSIGN_RESPOSIBLE:
			return [];
		case constants.RESET_ASSIGN_RESPONSIBLE:
			return action.payload;
		default:
			return state;
	}
}
