import React from "react";
import { Card } from "antd";
import "./availableProjects.scss";
import UnitsProject from "./UnitsProjectComponent";

const AvailableProjects = ({projects,customerId}) => {
	return (
		<>
			<Card
				title="Proyectos disponibles"
				style={{
					border: "2px solid #bce8f1",
				}}
			>
				<div className="wrapper">
					{projects.map((item) => (
						<UnitsProject key={item} project={item} customerId={customerId} />
					))}
				</div>
			</Card>
		</>
	);
};

export default AvailableProjects;
