import {Button, Tabs} from "antd";
import {useDispatch} from "react-redux";
import ListSales from "./Tables/ListSales";
import ListQuotes from "./Tables/ListQuotes";
import React, {useEffect, useState} from "react";
import {useCustomer} from "../../Hooks/useCustomer";
import TableComponent from "../../../../Table/Table";
import TabPane from "@ant-design/pro-card/es/components/TabPane";
import {ActivityViewPrint} from "../../../../../../redux/actions/Customer/AfterSalesCustomer";
import CustomerRegisterActivities from "../../../Componets/Activity/CustomerRegisterActivities";

const MovementData = ({query,myOnClick}) => {
	const {quotations,setQuotations} = useCustomer()
	const dispatch = useDispatch()
	const [customerActivitiesModel,setCustomerActivitiesModel] = useState([])
	
	const viewPrint = () => {
		dispatch(
			ActivityViewPrint(query.customer.id, async (response) => {
				const res = await response;
				const tagA = document.createElement("a");
				tagA.href = res._payload;
				tagA.target = "_blank";
				document.body.appendChild(tagA);
				tagA.click();
			})
		);
	}
	function mapActivitiesModel (ActivitiesData){
		return  {
			id : ActivitiesData.id,
			creatorUser : ActivitiesData.user.person.name,
			scheduleDate : ActivitiesData.scheduled,
			typeActivity : ActivitiesData?.activity_type?.name,
			managementType : ActivitiesData?.management_type?.name,
			activity : ActivitiesData.activity_description,
			activityPerformed : ActivitiesData.done,
			commentary : ActivitiesData.commentary
		}
	}
	useEffect(() => {
		let quotes = []
		let activities = []
		query?.activities?.forEach((ele)=>{
			activities.push(mapActivitiesModel(ele))
		})
		query?.customer?.quotation?.forEach((elem)=>{
			quotes.push({
				code: elem.code,
				project: elem.project,
				unit: elem.unit,
				trustCode: elem.trustCode,
				numberRooms: parseInt(elem.numberRooms),
				projectUnitStatus: elem.projectUnitStatus
			})
		})
		setCustomerActivitiesModel(activities)
		setQuotations(quotes)
	}, [query]);
	
	
	return (
		<>
			<Tabs>
				<TabPane
					tab={<span>Datos de Cotizaciones</span>}
					key={"1"}
				>
					<ListQuotes quotations={quotations} />
				</TabPane>
				<TabPane
					tab={<span>Datos de Compras</span>}
					key={"2"}
				>
					<ListSales query={query} />
				</TabPane>
				<TabPane
					tab={<span>Actividades</span>}
					key={"3"}
				>
					<div style={{ display: "inline-flex", marginLeft: '77%', marginBottom: '0.5%' }}>
						<Button onClick={()=>viewPrint()} style={{ marginRight: 10 }} type="primary">Imprimir</Button>
						<CustomerRegisterActivities myClick={myOnClick} customerData={query} />
					</div>
					<TableComponent myOnClick={myOnClick} data={customerActivitiesModel} x={1300} y={300} />
				</TabPane>
			</Tabs>
		</>
	)
}

export default MovementData