import {Modal, Form, Input, Button, Col, Row} from "antd";
import React, {useState} from "react";
import {useDispatch} from "react-redux";
import { saveSalesPLan } from "../../../../../redux/actions/Report/Report_sales_plan/reportSalesPlanAction";
import { errorGlobal, success } from "../../../../handle/Notification/Notification";

const ModalSavePlanReport = ({data}) => {

	const dispatch = useDispatch();

    let userStorage = JSON.parse(localStorage.getItem('USER_INFO'));
	const [form] = Form.useForm();
	const [descriptionPlan, setdescriptionPlan] = useState('');

	const [isModalVisible, setIsModalVisible] = useState(false);

	const showModal = () => {
		setIsModalVisible(true);
	};
	const onReset = () => {
		form.resetFields();
	};
	const handleCancel = () => {
		setIsModalVisible(false);
	};
	const onFinish = (value) => {
        let users_id = userStorage.id;
		const dataConsult = {
            "project_id": data?.project_id,
            "generate_to": data?.generate_to,
            "description":value.descriptinInput,
            "users_id":users_id
        };
        dispatch(
			saveSalesPLan(dataConsult, async (response) => {
				const res = await response;
				if (res.error) {
					if (res.message === "Error number of units exceeded") {
						errorGlobal("¡UPS! Número de unidades excedido.");
					} else {
						errorGlobal("¡UPS! Ha ocurrido un error");
					}
				} else {
					success("Ha sido agregado con éxito");
					handleCancel();
					onReset();
					
				}
			})
		);
	};
	


	const onFinishFailed = (errorInfo) => {
		console.error("Failed:", errorInfo);
	};

	return (
		<>	
			<Button onClick={showModal} type="primary">Guardar</Button>
			<Modal
				title="Planes de ventas"
				visible={isModalVisible}
				onCancel={handleCancel}
				width={700}
				footer={false}
			>
				<Form
					layout="vertical"
					name="basic"
					onFinish={onFinish}
					onFinishFailed={onFinishFailed}
					form={form}
				>
					<Row>
						<Col span={8}>
							<Form.Item
								label="Descripcion del Plan de Ventas"
								name="descriptinInput"
								rules={[
									{
										required: true,
										message: "Campo requerido",
									},
								]}
							>
                                <Input 
                                placeholder="Descripcion del Plan de Ventas" 
                                value={descriptionPlan} onChange={(value) => setdescriptionPlan(value)}
                                style={{width: "450px"}}
                                />
							</Form.Item>
						</Col>

					</Row>
					<Row>
						<Col span={6}/>
						<Col span={6}/>
						<Col span={6}/>
						<Col span={6}>
							<Form.Item label=" ">
								<Button
									type="primary"
									htmlType="submit"
								>
									Guardar
								</Button>
							</Form.Item>
						</Col>
					</Row>
				</Form>
			</Modal>
		</>
	);
};

export default ModalSavePlanReport;
