import {Tabs, Divider} from 'antd';
import React, {useEffect} from 'react';
import {useDispatch} from "react-redux";
import SeparateUnits from "./SeparateUnits";
import CustomerFollowUp from "./CustomerFollowUp";
import * as constants from "../../../../redux/constants";
import CollectionManagement from "./CollectionManagement";
import StatusOfLegalization from "./StatusOfLegalization";
import MonitoringOfGeneralActivities from "./MonitoringOfGeneralActivities";
import {validatePermission} from "../../../handle/PermissionMethods/PermissionMethods";
import {resetQuoteAction} from "../../../../redux/actions/Price/Quotation/QuotesAction";

const PaginationTables = () => {

	const dispatch = useDispatch();
	useEffect(() => {
		
		dispatch(resetQuoteAction());
		
	}, [dispatch]);
	
	const {TabPane} = Tabs;
	return (
		<Divider orientation="left">Tablas</Divider>,
			<Tabs defaultActiveKey="1">
				{validatePermission(constants.SEPERATED_UNIT_QUOTATION_PERMISSION) ? (
					<TabPane
						tab={
							<span>Unidades Separadas </span>
						}
						key="1"
					>
						<SeparateUnits/>
					</TabPane>
				) : null}
				{validatePermission(constants.SHOW_CUSTOMERS_TRACKING_PERMISSION) ? (
					<TabPane
						tab={
							<span>
            					Seguimiento Clientes
							</span>
						}
						key="2"
					>
						<CustomerFollowUp/>
					</TabPane>
				) : null}
				{validatePermission(
					constants.SHOW_ACTIVITY_TRACKING_REPORT_PERMISSION
				) ? (
					<TabPane
						tab={
							<span>
            					Seguimiento de Actividades Generales
         					 </span>
						}
						key="3"
					>
						<MonitoringOfGeneralActivities/>
					</TabPane>
				) : null}
				{validatePermission(constants.SHOW_COLLECTION_MANAGEMENT_PERMISSION) ? (
					<TabPane
						tab={
							<span>
            					Gestión de Recaudos
          					</span>
						}
						key="4"
					>
						<CollectionManagement/>
					</TabPane>
				) : null}
				{validatePermission(
					constants.SHOW_LEGALIZATION_AND_DEED_REPORT_PERMISSION
				) ? (
					<TabPane
						tab={
							<span>
								Estado de Legalización
          					</span>
						}
						key="5"
					>
						<StatusOfLegalization/>
					</TabPane>
				) : null}

			</Tabs>
	);
}

export default PaginationTables;