import React, {useState, useEffect} from 'react';
import {useDispatch} from 'react-redux'
import {Modal, Button, Upload, Row, Space, Image} from 'antd';
import { EyeOutlined} from '@ant-design/icons';
import {PlusOutlined} from '@ant-design/icons';
import {errorGlobal} from "../../../../../../handle/Notification/Notification";
import {deleteUnitTypeByIdAttachment, creatUnitTypeAttachment} from '../../../../../../../redux/actions/Project/UnitType';
import {RUTA_API_MULTIMEDIA} from '../../../../../../../redux/constants';

const ModalImagenes = (props) => {

	const dispatch = useDispatch()
	const [isModalVisible, setIsModalVisible] = useState(false);
	const [isModalPrevVisible, setIsModalPrevVisible] = useState(false);
	const [fileImage, setFileImage] = useState('');
	const req = props.record.images;
	const [images, setImages] = useState([]);
	useEffect(() => {
		if (req != null) {
			let tempImages = [];
			req.forEach((r, index) => {
				tempImages.push({
					uid: r.id,
					percent: 50,
					name: 'Multimedia ' + (index + 1),
					status: 'done',
					url: r.path,
				});
			})
			setImages(tempImages);
		}

	}, [])

	useEffect(() => {
		if (req != null) setData(req)
	}, [req]);
	const [data, setData] = useState()

	const showModal = () => {
		setIsModalVisible(true);
	};


	const visibleModals = () => {
		if (isModalVisible) {
			setIsModalPrevVisible(true);
			setIsModalVisible(false);

		} else {
			setIsModalVisible(true);
			setIsModalPrevVisible(false);
		}
	}

	const handleOk = () => {
		setIsModalVisible(false);
	};

	const handleCancel = () => {
		setIsModalVisible(false);
	};

	const handleOkPrev = () => {
		setIsModalPrevVisible(false);
		setIsModalVisible(true);
	};

	const handleCancelPrev = () => {
		setIsModalPrevVisible(false);
		setIsModalVisible(true);
	};

	const handleDelete = (record) => {
		const dataSource = [...data];
		setData(dataSource.filter((item) => item.id !== record.uid));
		dispatch(deleteUnitTypeByIdAttachment(record.uid, props.record.id))
	};

	

	const handlePreview = async file => {
		setFileImage((file.hasOwnProperty('url') ? file.url : file.thumbUrl))
		visibleModals();
	};

	const uploadImage = async (file) => {

		setImages(file.fileList)

	};
	const uploadProps = {
		beforeUpload: file => {
			if (file.type !== 'image/png' && file.type !== 'image/jpeg') {
				errorGlobal("Formato no válido");
				return file.type === ('image/png' || 'image/jpeg') ? true : Upload.LIST_IGNORE;

			}
		}, customRequest({onSuccess, onError, file}) {
			let formData = new FormData();
			formData.append('file', file, file.name);
			formData.append('type', 1);
			dispatch(creatUnitTypeAttachment(props.record.id, formData, onSuccess, onError));
		}
		,
		onChange: (info) => {
			console.info(info.fileList);
		}
	};

	const uploadButton = (
		<div>
			<PlusOutlined/>
			<div style={{marginTop: 8}}>Upload</div>
		</div>
	);

	return (
		<>
			<Button type="primary" onClick={showModal}>
				<EyeOutlined/>
			</Button>
			<Modal
				title="Imágenes"
				visible={isModalVisible}
				onOk={handleOk}
				onCancel={handleCancel}
				footer={false}
			>
				<Row>
					<Space>
						<Upload {...uploadProps}
								action={RUTA_API_MULTIMEDIA}
								listType="picture-card"
								fileList={images}
								accept="image/*"
								onPreview={handlePreview}
								onChange={uploadImage} 
								onRemove={handleDelete}
						>
							{(images.length < 100) ? '+ Upload' : uploadButton}
						</Upload>
					</Space>
				</Row>
				<br/> <br/>
				<br/>
				<br/>
			</Modal>
			<Modal
				visible={isModalPrevVisible}
				onOk={handleOkPrev}
				onCancel={handleCancelPrev}
				footer={null}
			>
				<Image
					width={470}
					src={fileImage}
					preview={false}
				/>
			</Modal>
		</>
	);
};

export default ModalImagenes