import React, {  useState } from "react";
import { useDispatch } from "react-redux";
import { Button, Modal, Tooltip } from "antd";
import { UsergroupAddOutlined } from "@ant-design/icons";
import AssignResponsibleContainer from "./AssignResponsibleContainer";
import { resetAssignResponsibleAction } from "../../../../../../redux/actions/Price/SaleMade/assignResponsibleAction";

const AssignResponsible = ({ data }) => {
	const [isModalVisible, setIsModalVisible] = useState(false);
	const dispatch = useDispatch();

	const showModal = () => {
		setIsModalVisible(true);
	};

	const handleCancel = () => {
		setIsModalVisible(false);
	};

	const resetStateAssignResponsible = () => {
		dispatch(resetAssignResponsibleAction());
	};

	const textAssignResponsible = (
		<span style={{ color: "#6A6963" }}>Asignar Responsables</span>
	);

	return (
		<>
			<Tooltip placement="left" color="#ffffff" title={textAssignResponsible}>
				<Button
					type="primary"
					style={{ background: "#40a9ff", color: "#fff" }}
					size={"small"}
					onClick={showModal}
					icon={<UsergroupAddOutlined />}
					disabled = {data.is_desisted}
				/>
			</Tooltip>
			{isModalVisible && (
				<Modal
					title={"Asignar responsables"}
					centered
					visible={isModalVisible}
					footer={false}
					onCancel={handleCancel}
					afterClose={resetStateAssignResponsible}
					width={"60vw"}
				>
					{isModalVisible && (
						<AssignResponsibleContainer
							isModalVisible={isModalVisible}
							data={data}
							handleCancel={handleCancel}
						/>
					)}
				</Modal>
			)}
		</>
	);
};

export default AssignResponsible;
