import React, { useEffect, useState } from "react";
import { fetchAllProjects } from "../../../../redux/actions/Project/ProjectsAction";
import { fetchExcelComissionReport } from "../../../../redux/actions/Report/ComissionReport/ComissionAction";
import { fetchAllAfiliatedCompany } from "../../../../redux/actions/Configuration/AfiliatedCompanyAction";
import { connect, useDispatch, useSelector } from "react-redux";
import { Card, Col, Row, Form, Select, Button } from "antd";
import { errorGlobal } from "../../../handle/Notification/Notification";
import { validateModule } from "../../../handle/PermissionMethods/PermissionMethods";
import * as constants from "../../../../redux/constants";
import { Redirect } from "react-router-dom/cjs/react-router-dom.min";

const CommissionComplianceReport = ({
	dispatchFetchAllAfiliatedCompanyAction,
	dispatchFetchAllProjectAction,
}) => {
	const dispatch = useDispatch();

	useEffect(()=>{
		dispatch(fetchAllAfiliatedCompany());
	dispatch(fetchAllProjects());
	},[]);

	const selectProject = useSelector((state) => state?.project);
	const selectAfiliatedCompany = useSelector((state) => state?.afiliatedCompany);

	const [ idProject, setIdProject] = useState(null);
	const [ idAfiliatedCompany, setIdAfiliatedCompany] = useState(null);
	const [consultind, setConsulting] = useState( false );
	
	useEffect(() => dispatchFetchAllProjectAction(), [dispatch]);
	useEffect(() => dispatchFetchAllAfiliatedCompanyAction(), [dispatch]);

	const onFinish = () => {
		setConsulting(true);
		let data = {
			"project_id": idProject,
			"company_id": idAfiliatedCompany
		}
		dispatch(fetchExcelComissionReport(data, async (response) => {
			let res = await response;
			if (res.code === 200) {
				const tagA = document.createElement("a");
				tagA.href = res._payload;
				tagA.target = "_blank";
				document.body.appendChild(tagA);
        tagA.click();
			} else {
				errorGlobal(res.message);
			}
			setConsulting(false);
		}));
		
	};

	const onChangeProject = (value) => {
		if (value !== "") {
			setIdProject(value);
		}
	}
	
	const onChangeCompany = (value) => {
		if (value !== "") {
			setIdAfiliatedCompany(value);
		}
	}

	const validateMessages = {
		// eslint-disable-next-line no-template-curly-in-string
		required: "${label} es requerido",
		types: {
			// eslint-disable-next-line no-template-curly-in-string
			email: "${label} no es un correo válido",
			// eslint-disable-next-line no-template-curly-in-string
			number: "${label} debe ser un número",
		},
	};
	
	if (!validateModule(constants.COMISSION_VERIFICATION_SUBMODULE)) {
		return <Redirect to={constants.DEFAULT_ROUTE} />;
	}
	
	return (
		<Card title="Informe Cumplimiento de Comisiones">
			<Form
				layout="vertical"
				onFinish={onFinish}
				validateMessages={validateMessages}
			>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item label="Proyecto:" name="Proyecto"
						rules={[
							{
								required: true
							}]}>
									<Select      defaultValue={""}
									showSearch
									style={{ width: '100%' }}
									optionFilterProp="children"
									onChange={(value) => { onChangeProject(value) }}
									filterOption={(input, option) =>
										option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
									}
								>
									<Select.Option value="">Seleccione...</Select.Option>
									{
										!Array.isArray(selectProject) && selectProject._payload?.map(item => (
											<Select.Option key={item.id} value={item.id}>{` ${item.name} `}</Select.Option>

										))

									}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item
							label="Empresa:"
							name="Empresa"
							rules={[
								{
									required: true,
								},
							]}
						>
							<Select
								defaultValue={""}
								showSearch
								style={{ width: "100%" }}
								optionFilterProp="children"
								onChange={(value) => { onChangeCompany(value) }}
								filterOption={(input, option) =>
									option.children.toLowerCase().indexOf(input.toLowerCase()) >=
									0
								}
							>
								<Select.Option value="">Seleccione...</Select.Option>
								{!Array.isArray(selectAfiliatedCompany) &&
									selectAfiliatedCompany._payload.map((item) => (
										<Select.Option key={item.id} value={item.id}>{` ${item.name} `}</Select.Option>
									))}
							</Select>
						</Form.Item>
					</Col>
				</Row>
				<Row>
					<Col xs={18} sm={20} md={21} lg={22} xl={22} />

					<Col xs={2} sm={2} md={2} lg={2} xl={2}>
						<Form.Item>
							<Button type="primary" htmlType="submit" loading={consultind}>
								Exportar
							</Button>
						</Form.Item>
					</Col>
				</Row>
			</Form>
		</Card>
	);
};

const mapDispatchToProps = (dispatch) => ({
	dispatchFetchAllProjectAction: () => dispatch(fetchAllProjects()),
	dispatchFetchAllAfiliatedCompanyAction: () =>
		dispatch(fetchAllAfiliatedCompany()),
});
export default connect(null, mapDispatchToProps)(CommissionComplianceReport);
