import * as constants from "../../../../../redux/constants";
export const fillDataTable = (data) =>{
    return  [
        {
            descripcion: "Alto",
            value: 0,
            valueAssesors:((data!=null) ? (data.customers_info.high_interest) : 0),
            valueDisponibles: 0
        },
        {
            descripcion: "Medio",
            value: 0,
            valueAssesors:((data!=null) ? (data.customers_info.medium_interest) : 0),
            valueDisponibles: 0
        },
        {
            descripcion: "Bajo",
            value: 0,
            valueAssesors:((data!=null) ? (data.customers_info.low_interest) : 0),
            valueDisponibles: 0
        },
        {
            descripcion: "Sin interés",
            value: 0,
            valueAssesors:((data!=null) ? (data.customers_info.null_interest) : 0),
            valueDisponibles: 0
        },
        {
            descripcion: "Actuales",
            value: 0,
            valueAssesors:0,
            valueDisponibles: 0
        },
        {
            descripcion: "Total",
            value: 0,
            valueAssesors:((data!=null) ? (data.customers_info.total_interest) : 0),
            valueDisponibles: 0
        }
    ]
}

export const fillDataResumenAssesor = (data) => {
   return (
       (data!=null) ? ((data.has_data==1) ? (
           [
            {
                nombreAsesor: data.tittle_info.assesor_name,
                unidadesCotizadas: data.unidades_cotizadas,
                clientesNuevos: data.clientes_nuevos,
                clientesVisitados: data.clientes_visitados,
                unidadesVendidas: data.unidades_vendidas,
                totalVentas: constants.FORMATTER_PESO.format(data.total_ventas),
                unidadesSeparadas: data.unidades_separadas,
                desistimientos: data.desistimientos,
                actividadesProgramadas: data.actividades_programadas,
                actividadesRealizadas: data.actividades_realizadas,
                actividadesVencidas: data.actividades_vencidas
            }
           ]
       ): 
       [])
       :[]
       
   )
}