import React, {useEffect, useState} from "react";
import {useDispatch} from "react-redux";
import {Alert, Button, Card, Col, Divider, Form, Input, InputNumber, Row, Select} from "antd";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import {creatPaymentRelationshipSetting} from "../../../../../../../redux/actions/Project/ConfiguracionPagosRecudosAction";
import {errorGlobal, success} from "../../../../../../handle/Notification/Notification";
import {FORMATTER_INPUT_NUMBER} from "../../../../../../../redux/constants";

const PagosRelacion = ({ paymentRelationshipSetupDatabase, id }) => {
	const dispatch = useDispatch();

	let config = {
		theme: "snow",
		readOnly: false,
	};

	let element = Array.isArray(paymentRelationshipSetupDatabase)
		? paymentRelationshipSetupDatabase[0]
		: paymentRelationshipSetupDatabase._payload[0];

	useEffect(() => {
		data != null ? fillFields() : console.warn("No han cargado")
		setBuilder_credit_payments(parseFloat(element.builder_credit_payments))
		setBuilder_credit_payments_fal(parseFloat(element.builder_credit_value)-parseFloat(element.builder_credit_payments))
	}, []);
	console.log(element)
	const [data] = useState(element);
	const [email_quote_format, setFormatoCorreoCotizacion] = useState(
		paymentRelationshipSetupDatabase._payload[0].email_quote_format || ""
	);
	const [form] = Form.useForm();
	const [builder_credit_payments,setBuilder_credit_payments] = useState(parseFloat(element.builder_credit_payments))
	const [builder_credit_payments_fal,setBuilder_credit_payments_fal] = useState(parseFloat(element.builder_credit_value)-parseFloat(element.builder_credit_payments))
	const [initial_fee_percentage, setCuotaInicial] = useState(
		element ? element.initial_fee_percentage : ""
	);
	const [bank_financing_percentage, setFinanciacionBanco] = useState(
		element ? element.bank_financing_percentage : 0
	);
	const [payment_period_id, setPeriodoPago] = useState(
		element ? element.payment_period_id : 1
	);
	const [separation_value, setValorSeparacion] = useState(
		element ? element.separation_value : ""
	);
	const [maximum_separation_days, setNumeroDiasMaximoSeparacion] = useState(
		element ? element.maximum_separation_days : ""
	);
	const [
		initial_fee_promise_signature_trust_percentage,
		setPorcentajeInicialFirmaPromesa,
	] = useState(
		element ? element.initial_fee_promise_signature_trust_percentage : ""
	);
	const [
		initial_fee_finance_in_construction_percentage,
		setPorcentajeInicialFinanciarConstruccion,
	] = useState(
		element ? element.initial_fee_finance_in_construction_percentage : ""
	);
	const [
		max_number_days_pay_initial_fee,
		setNumeroDiasMaximoPagarCuotaInicial,
	] = useState(element ? element.max_number_days_pay_initial_fee : "");
	const [
		maximum_number_months_finance_initial_fee,
		setNumeroMesesMaximoFinanciarCuotaInicial,
	] = useState(
		element ? element.maximum_number_months_finance_initial_fee : ""
	);
	const [valid_days_quote, setNumerousDiastolicIonization] = useState(
		element ? element.valid_days_quote : ""
	);
	const [
		maximum_days_without_advisory_client,
		setMaximoDiasSinRelacionClienteAsesor,
	] = useState(element ? element.maximum_days_without_advisory_client : "");
	const [builder_credit_value, setValorCredito] = useState(
		element ? element.builder_credit_value : ""
	);
	const [income_information, setInformacionRecaudo] = useState(
		element ? element.income_information : ""
	);
	const [income_data, setDatosRecaudo] = useState(
		element ? element.income_data : ""
	);
	const [account_status_data, setDatosEstadoCuenta] = useState(
		element ? element.account_status_data : ""
	);

	const dataPagosRelacion = {
		initial_fee_percentage,
		bank_financing_percentage,
		payment_period_id: 1,
		separation_value,
		maximum_separation_days,
		initial_fee_promise_signature_trust_percentage,
		initial_fee_finance_in_construction_percentage,
		valid_days_quote,
		maximum_days_without_advisory_client,
		builder_credit_value,
		max_number_days_pay_initial_fee,
		maximum_number_months_finance_initial_fee,
		email_quote_format,
		income_information,
		income_data,
		account_status_data,
	};

	const fillFields = () => {
		form.setFieldsValue({
			initial_fee_percentage: initial_fee_percentage,
			bank_financing_percentage: bank_financing_percentage,
			payment_period_id: String(payment_period_id),
			separation_value: separation_value,
			maximum_separation_days: maximum_separation_days,
			initial_fee_promise_signature_trust_percentage:
				initial_fee_promise_signature_trust_percentage,
			initial_fee_finance_in_construction_percentage:
				initial_fee_finance_in_construction_percentage,
			valid_days_quote: valid_days_quote,
			maximum_days_without_advisory_client:
				maximum_days_without_advisory_client,
			builder_credit_value: builder_credit_value,
			max_number_days_pay_initial_fee: max_number_days_pay_initial_fee,
			maximum_number_months_finance_initial_fee:
				maximum_number_months_finance_initial_fee,
			email_quote_format: email_quote_format,
			income_information: income_information,
			income_data: income_data,
			account_status_data: account_status_data,
		});
	};

	function calculiPercentages(numerous) {
		return 100 - numerous;
	}

	function llenarPorcentajeFinanciacionBanco(value) {
		form.setFieldsValue({
			bank_financing_percentage: value,
		});
		setFinanciacionBanco(value);
	}

	function redistribuirPorcentaje(numero) {
		let value = calculiPercentages(numero);
		llenarPorcentajeFinanciacionBanco(value);
	}

	const calculatePercentage = (value) => {
		setCuotaInicial(value);

		redistribuirPorcentaje(value);
		form.setFieldsValue({
			initial_fee_promise_signature_trust_percentage: value,
			initial_fee_finance_in_construction_percentage: 0,
		});
		setPorcentajeInicialFirmaPromesa(value);
		setPorcentajeInicialFinanciarConstruccion(0);
	};

	const calculatePercentageBankFinancing = (value) => {
		setFinanciacionBanco(value);
		let remainingPercentage = 100 - value;
		form.setFieldsValue({
			initial_fee_percentage: remainingPercentage,
		});
		setCuotaInicial(remainingPercentage);
		form.setFieldsValue({
			initial_fee_promise_signature_trust_percentage: remainingPercentage,
			initial_fee_finance_in_construction_percentage: 0,
		});
	};

	const updateInitialFeePromise = (value) => {
		let totalValue = initial_fee_percentage;
		let remainingPercentage = totalValue - value;
		if (value > totalValue) {
			setPorcentajeInicialFinanciarConstruccion(
				initial_fee_finance_in_construction_percentage
			);
			setPorcentajeInicialFirmaPromesa(
				initial_fee_promise_signature_trust_percentage
			);
			form.setFieldsValue({
				initial_fee_finance_in_construction_percentage:
					initial_fee_finance_in_construction_percentage,
				initial_fee_promise_signature_trust_percentage:
					initial_fee_promise_signature_trust_percentage,
			});
		} else {
			setPorcentajeInicialFinanciarConstruccion(remainingPercentage);
			setPorcentajeInicialFirmaPromesa(value);
			form.setFieldsValue({
				initial_fee_promise_signature_trust_percentage: value,
				initial_fee_finance_in_construction_percentage: remainingPercentage,
			});
		}
	};

	const updateInitialFeeFinance = (value) => {
		let totalValue = initial_fee_percentage;
		let remainingPercentage = totalValue - value;
		if (value > totalValue) {
			setPorcentajeInicialFinanciarConstruccion(
				initial_fee_finance_in_construction_percentage
			);
			setPorcentajeInicialFirmaPromesa(
				initial_fee_promise_signature_trust_percentage
			);
			form.setFieldsValue({
				initial_fee_finance_in_construction_percentage:
					initial_fee_finance_in_construction_percentage,
				initial_fee_promise_signature_trust_percentage:
					initial_fee_promise_signature_trust_percentage,
			});
		} else {
			setPorcentajeInicialFinanciarConstruccion(value);
			setPorcentajeInicialFirmaPromesa(remainingPercentage);
			form.setFieldsValue({
				initial_fee_promise_signature_trust_percentage: remainingPercentage,
				initial_fee_finance_in_construction_percentage: value,
			});
		}
	};

	const onFinish = () => {
		dispatch(
			creatPaymentRelationshipSetting(id, dataPagosRelacion, async (res) => {
				const response = await res;
				response.error
					? errorGlobal("¡UPS! ha ocurrido un error mientras se actualizaba")
					: success("Actualizado exitosamente");
			})
		);
	};

	return (
		<Card
			title="Configuración para pagos y de relación"
			style={{ borderColor: "black" }}
		>
			<Form
				name="PagosRelacion"
				layout="vertical"
				onFinish={onFinish}
				form={form}
				style={{ marginLeft: "27px", marginRight: "27px", marginTop: "27px" }}
			>
				<Row gutter={16}>
					<Col xs={5} sm={24} md={8} lg={8} xl={8}>
						<Form.Item label="% Cuota Inicial" name="initial_fee_percentage">
							<InputNumber
								min={0}
								max={100}
								formatter={(value) => `${value}%`}
								parser={(value) => value.replace("%", "")}
								value={initial_fee_percentage}
								onChange={(value) => calculatePercentage(value)}
								style={{ width: "100%" }}
								rules={[{required: true}]}
							/>
						</Form.Item>
					</Col>
					<Col xs={5} sm={24} md={8} lg={8} xl={8}>
						<Form.Item
							label="% Financiación Banco"
							name="bank_financing_percentage"
						>
							<InputNumber
								min={0}
								max={100}
								formatter={(value) => `${value}%`}
								parser={(value) => value.replace("%", "")}
								onChange={(value) => calculatePercentageBankFinancing(value)}
								style={{ width: "100%" }}
							/>
						</Form.Item>
					</Col>
					<Col xs={5} sm={24} md={8} lg={8} xl={8}>
						<Form.Item valuePropName={1} label="Período de Pago" name="payment_period_id">
							<Select
								onChange={(value) => setPeriodoPago(value)}
								defaultValue={1}
								value={1}
							>
								<Select.Option value={1}>Mensual</Select.Option>
							</Select>
						</Form.Item>
					</Col>
				</Row>
				<Row gutter={16}>
					<Divider orientation="left">Separación</Divider>
					<Col span={12}>
						<Form.Item label="Valor de Separación" name="separation_value">
							<InputNumber
								{...FORMATTER_INPUT_NUMBER}
								parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
								value={separation_value}
								onChange={(value) => setValorSeparacion(value)}
								style={{ width: "100%" }}
							/>
						</Form.Item>
					</Col>
					<Col span={12}>
						<Form.Item
							label="Número de Días Máximo de Separación"
							name="maximum_separation_days"
							valuePropName={0}
						>
							<InputNumber
								value={maximum_separation_days}
								onChange={(value) => setNumeroDiasMaximoSeparacion(value)}
								style={{ width: "100%" }}
							/>
						</Form.Item>
					</Col>
				</Row>
				<Divider orientation="left">Cuota Inicial Forma de Pago</Divider>
				<Row gutter={16}>
					<Col xs={5} sm={24} md={8} lg={8} xl={12}>
						<Form.Item
							label="% Cuota inicial - Firma promesa o encargo fiduciario"
							name="initial_fee_promise_signature_trust_percentage"
						>
							{() => {}}
							<InputNumber
								min={0}
								max={initial_fee_percentage}
								formatter={(value) => `${value}%`}
								parser={(value) => value.replace("%", "")}
								value={initial_fee_promise_signature_trust_percentage}
								onChange={(value) => updateInitialFeePromise(value)}
								style={{ width: "100%" }}
							/>
						</Form.Item>
						<Form.Item
							label="% Cuota inicial - A financiar durante la construcción"
							name="initial_fee_finance_in_construction_percentage"
						>
							<InputNumber
								min={0}
								max={initial_fee_percentage}
								formatter={(value) => `${value}%`}
								parser={(value) => value.replace("%", "")}
								value={initial_fee_finance_in_construction_percentage}
								onChange={(value) => updateInitialFeeFinance(value)}
								style={{ width: "100%" }}
							/>
						</Form.Item>
					</Col>
					<Col xs={5} sm={24} md={8} lg={8} xl={12}>
						<Form.Item
							label="Número de Días Máximo para Pagar la Cuota de Inicial"
							name="max_number_days_pay_initial_fee"
						>
							<InputNumber
								value={max_number_days_pay_initial_fee}
								onChange={(value) =>
									setNumeroDiasMaximoPagarCuotaInicial(value)
								}
								style={{ width: "100%" }}
							/>
						</Form.Item>
						<Form.Item
							label="Número de Meses Máximo para Financiar la Cuota de Inicial"
							name="maximum_number_months_finance_initial_fee"
						>
							<InputNumber
								value={maximum_number_months_finance_initial_fee}
								onChange={(value) =>
									setNumeroMesesMaximoFinanciarCuotaInicial(value)
								}
								style={{ width: "100%" }}
							/>
						</Form.Item>
					</Col>
				</Row>
				<Row gutter={16}>
					<Col span={12}>
						<Divider orientation="left">Cotización</Divider>
						<Form.Item
							label="Número de Días válidos de Cotizaciones"
							name="valid_days_quote"
						>
							<InputNumber
								value={valid_days_quote}
								onChange={(value) => setNumerousDiastolicIonization(value)}
								style={{ width: "100%" }}
							/>
						</Form.Item>
					</Col>
					<Col span={12}>
						<Divider orientation="left">Relación Cliente-Asesor</Divider>
						<Form.Item
							label="Máximo dias sin relación Cliente-Asesor"
							name="maximum_days_without_advisory_client"
						>
							<InputNumber
								value={maximum_days_without_advisory_client}
								onChange={(value) =>
									setMaximoDiasSinRelacionClienteAsesor(value)
								}
								style={{ width: "100%" }}
							/>
						</Form.Item>
					</Col>
				</Row>
				<Row gutter={16}>
					<Divider orientation="left">Crédito Constructor</Divider>
					<Col span={8}>
						<Form.Item label="Valor del Crédito" name="builder_credit_value">
							<InputNumber
								{...FORMATTER_INPUT_NUMBER}
								parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
								value={builder_credit_value}
								onChange={(value) => setValorCredito(value)}
								style={{ width: "100%" }}
							/>
						</Form.Item>
					</Col>
					<Col span={8}>
						<Form.Item label="Total Abonos" name="totalAbono">
							<InputNumber
								{...FORMATTER_INPUT_NUMBER}
								parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
								defaultValue={builder_credit_payments}
								disabled
								style={{ width: "100%" }}
							/>
						</Form.Item>
					</Col>
					<Col span={8}>
						<Form.Item label="Total Deuda" name="totalDeuda">
							<InputNumber
								{...FORMATTER_INPUT_NUMBER}
								parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
								defaultValue={builder_credit_payments_fal}
								disabled
								style={{ width: "100%" }}
							/>
						</Form.Item>
					</Col>
				</Row>
				<Row>
					<Col span={24}>
						<Divider orientation="left">Formato Correo Cotización</Divider>

						<ReactQuill
							{...config}
							value={email_quote_format}
							onChange={setFormatoCorreoCotizacion}
							style={{ minHeight: "300px" }}
						/>

						<br />
						<br />
						<Alert
							message="ETIQUETAS"
							description={
								<span>
									'Cliente.nombre' = Nombre Completo del Cliente <br />
									'Cotizacion.codigo'= Código de la Cotización <br />
									'Asesor.nombre' = Nombre del Asesor Responsable <br />
									'Asesor.email' = Email del Asesor Responsable
								</span>
							}
							type="info"
							showIcon
						/>
					</Col>
				</Row>
				<Row gutter={16}>
					<Col span={24}>
						<Divider orientation="left">Infomación Plan de Ventas</Divider>
						<Form.Item label="Información de recaudo" name="income_information">
							<Input.TextArea
								value={income_information}
								onChange={(e) => setInformacionRecaudo(e.target.value)}
								style={{ width: "100%" }}
								Rows={5}
							/>
						</Form.Item>
					</Col>
				</Row>
				<Row gutter={16}>
					<Col span={24}>
						<Form.Item label="Datos de recaudo" name="income_data">
							<Input.TextArea
								value={income_data}
								onChange={(e) => setDatosRecaudo(e.target.value)}
								style={{ width: "100%" }}
								Rows={5}
							/>
						</Form.Item>
					</Col>
				</Row>
				<Row gutter={16}>
					<Col span={24}>
						<Divider orientation="left">Infomación Estado de Cuenta</Divider>
						<Form.Item
							label="Datos de Estado de Cuenta"
							name="account_status_data"
						>
							<Input.TextArea
								value={account_status_data}
								onChange={(e) => setDatosEstadoCuenta(e.target.value)}
								style={{ width: "100%" }}
								Rows={5}
							/>
						</Form.Item>
					</Col>
				</Row>
				<Row>
					<Col span={2} offset={22}>
						<Form.Item>
							<Button disabled={JSON.parse(localStorage.stateDisable)} type="primary" htmlType="submit">
								Guardar
							</Button>
						</Form.Item>
					</Col>
				</Row>
			</Form>
		</Card>
	);
};

export default PagosRelacion;
