import Text from 'antd/lib/typography/Text';
import React, {useEffect, useState} from 'react';
import Card from "../../../components/Card/Card";
import {SearchOutlined} from '@ant-design/icons';
import {Button, Tooltip, Space, Table, Spin} from 'antd';
import ModalDetailReform from './Componets/ModalDetailReform';
import {shallowEqual, useDispatch, useSelector} from 'react-redux';
import {convertAllMapDataState} from "../../../handle/HandleFilterTable/handleFilterTable";
import {fetchAllSalesWhitReformRequest} from '../../../../redux/actions/Price/ReformRequest/SalesWhitReformRequestAction';
import {salesWhitReformRequestSel} from '../../../../redux/reducers/Price/ReformRequest/Selectors/ReformRequestSelector';

const Customers = () => {
	const [isModalVisible, setIsModalVisible] = useState(false);
	const [fetching, setFetching] = useState(true);
	const [recordSelected, setRecordSelected] = useState(null);
	const salesWithReform = useSelector(salesWhitReformRequestSel, shallowEqual);

	const dispatch = useDispatch()

	const updateListReformRequest = () => {
		dispatch(fetchAllSalesWhitReformRequest(() => {
			setFetching(false)
		}))
	}

	useEffect(() => {
		updateListReformRequest()
	}, []);


	const showModal = (record) => {
		setIsModalVisible(true);
		setRecordSelected(record);
	};

	
	const handleCancel = () => {

		setIsModalVisible(false);
		setRecordSelected(null);
	};


	const columns = [
		{
			title: "#",
			width: 5,
			dataIndex: 'id',
			key: 'id',
			render: (text, record) => (
				<>
					<Space>
						<label>{1 + (salesWithReform.indexOf(record))}</label>
					</Space>
				</>
			)


		},
		{
			title: 'Cliente',
			width: 50,
			dataIndex: 'customer',
			key: 'customer',
			textWrap: 'word-break',
			render: (text, record) => (
				<>
					<Space direction="vertical" size={1}>
						<label className="margin-0">{record.customer.name}</label>
						<label className="margin-0"><b>Tel: </b>{record.customer.phone_number}</label>
						<label><Text strong>Correo: </Text> <Text
							ellipsis={{rows: 1, expandable: true, tooltip: true}}>{record.customer.email} </Text></label>
					</Space>
				</>
			),
		},
		{
			title: 'Proyecto',
			width: 40,
			dataIndex: 'project',
			key: 'project',
			render: (text, record) => (
				<>
					<Text ellipsis={{rows: 1, expandable: true, tooltip: true}}>{record.project.name} </Text>
				</>
			),
		},
		{
			title: "Unidad",
			width: 15,
			dataIndex: "unit",
			key: "unit",
			render: (text, record) => (
				<>
					<Text ellipsis={{rows: 1, expandable: true, tooltip: true}}>{record.unit.label} </Text>
				</>
			),
		},
		{
			title: 'Valor total reformas',
			dataIndex: 'total_value_reform',
			width: 40,
			key: 'total_value_reform',
			render: (text, record) => (
				<>
					<Text ellipsis={{rows: 1, expandable: true, tooltip: true}}>{record?.total_value_reform} </Text>
				</>
			),
		},
		{
			title: "Asesor",
			dataIndex: "adviser",
			width: 40,
			key: "adviser",
			render: (text, record) => (
				<>
					<Text ellipsis={{rows: 1, expandable: true, tooltip: true}}>{record?.assesor?.name} </Text>
				</>
			),
		},
		{
			title: "Acciones",
			dataIndex: "actions",
			width: 25,
			fixed: "right",
			render: (text, record) => {
				return (
					<>
						<Tooltip title="Detalle de reforma">
							<Button type="default" icon={<SearchOutlined/>} size="small" onClick={() => showModal(record)}/>
						</Tooltip>
					</>
				);
			},
		},
	];


	const finalFront = (<>
		<Card
			title="Solicitudes de reformas"
			table={<Table columns={columns} rowKey="id" dataSource={convertAllMapDataState(salesWithReform)} scroll={{x: 1000}} size='small'
										bordered={true}/>}
		/>
		{isModalVisible &&
		<ModalDetailReform handleCancel={handleCancel} visibleModal={isModalVisible} onClose={setIsModalVisible}
											 idSale={recordSelected.id}/>}
	</>)


	return (
		<>
			{fetching && <Spin tip="Cargando...">{finalFront}</Spin>}
			{!fetching && finalFront}</>


	);
};

export default Customers;
