import * as constants from "../../../constants";

export const setAllLegalizeDocumentAction = (id, onSuccess) => ({
	type: constants.API,
	payload: {
		method: "GET",
		headers: constants.TOKEN,
		url: `${constants.RUTA}/sales/get-sale-legalization/${id}`,
		success: (response) => setAllLegalizeDocument(response),
		postProcessSuccess: onSuccess,
	},
});

export const uploadLegalizeDocumentAction = (id, data , onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: "POST",
		headers: constants.TOKEN,
		url: `${constants.RUTA}/sales/legalization-document/${id}`,
		data,
		success: (response) => uploadLegalizeDocument(response),
        postProcessSuccess: onSuccess,
		postProcessError: onError
	},
});

export const updatePercentageLegalizationDocumentAction = (id, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: "PUT",
		headers: constants.TOKEN,
		url: `${constants.RUTA}/salesmade/update-percentage-legalization-document/${id}`,
		success: (response) => updatePercentageLegalizationDocument(response),
        postProcessSuccess: onSuccess,
		postProcessError: onError
	},
});

export const resetLegalizeDocumentsAction = () => ({
    type: constants.API,
    payload: {
		method: null,
		success: () => (resetLegalizeDocuments())
	}
})

const  resetLegalizeDocuments = () => ({
	type: constants.RESET_LEGALIZE_DOCUMENTS,
	payload: []
});

const uploadLegalizeDocument = (data) => ({
	type: constants.UPLOAD_LEGALIZE_DOCUMENT,
	payload: data
});

const updatePercentageLegalizationDocument = (data) => ({
	type: constants.UPDATE_PERCENTAGE_LEGALIZED_DOCUMENTS,
	payload: data
});

const setAllLegalizeDocument = (data) => ({
	type: constants.SET_ALL_LEGALIZE_DOCUMENTS,
	payload: data
});
