import React, { useEffect, useState } from "react";
import {fetchAllReformRequest, fetchAllStatusReformRequest, fetchPathExcelForReformReport, fetchPathPdfForReformReport} from '../../../../redux/actions/Report/Report_request_reforms/reportRequestReformsAction';
import { fetchAllCustomer } from '../../../../redux/actions/Customer/CustomerAction';
import { fetchAllProjects } from '../../../../redux/actions/Project/ProjectsAction';
import {useDispatch, useSelector} from 'react-redux';
import {Card, Col, Row, Form, Select, Input, Button, Popover, Table} from "antd";
import { CloseOutlined, CheckOutlined } from '@ant-design/icons';
import Spinner from "../../../handle/Spinner/Spinner";
import {success, warning} from "../../../handle/Notification/Notification";
import {convertAllMapDataState} from "../../../handle/HandleFilterTable/handleFilterTable";
import { DownloadDocument } from "../../../handle/Multimedia/DownloadDocument";

const ReformReportComponent = () => {
	const [visiblePopover, setVisiblePopover] = useState( false )
	const [show, setShow] = useState( false )
	const [dataSelected, setDataSelected] = useState( false )
	const [consultind, setConsulting] = useState( false )
	const ListReforms = useSelector(state => state?.reportRequestReformsReducer);
	
	const ListStates = useSelector( state => state?.stateRequestReformsReducer);
	const ListCustomers = useSelector( state => state?.customer);
	const ListProjects = useSelector( state => state?.project);
	const dispatch = useDispatch();
	const [form] = Form.useForm();

	useEffect(() => {
		dispatch(fetchAllStatusReformRequest());
		dispatch(fetchAllCustomer());
		dispatch(fetchAllProjects());
	}, []);

	const onFinish = (values) => {
		let data = {
			"request_status":  (values.stateRequest && values.stateRequest !== " ") ? values.stateRequest  : "Todo",
			"customer":  (values.searchCustomer && values.searchCustomer !== " ") ? values.searchCustomer  : 0,
			"project": values.searchProject  || 0
		}
		setDataSelected(data);
		setConsulting(true);
		setShow(false);
		dispatch(fetchAllReformRequest(data, (response) => {
			if(!response.error){
				setShow(true);
				success("Datos recuperados");
			}else{
				warning("No hay datos");
			}
			setConsulting(false);
		}));
	};

	const exportExcel = () => {
		dispatch(fetchPathExcelForReformReport(dataSelected, async (response) => {
			const res = await response;
		if(res){

			DownloadDocument(res._payload);
		}else{
			warning("No hay Datos")
		}
		}));
	}

	const exportPdf = () => {
		dispatch(fetchPathPdfForReformReport(dataSelected, async (response) => {
			const res = await response;
			if(res){
				DownloadDocument(res._payload);
			}else{
				warning("No hay Datos")
			}
			
		}));	
	}
	


	const columns = [
		{
			title: "#",
			width: 10	,
			align: "center",
			dataIndex: "index",
			key: "index",
			render: (text, record) => {
				return record.index
			}
		},
		{
			title: "N° de Unidad",
			width: 25,
			align: "center",
			dataIndex: "unit_label",
			key: "unit_label",
			render: (text, record) => {
				return record.unit_label
			}

		},
		{
			title: "Proyecto",
			width: 30,
			align: "center",
			dataIndex: "project_name",
			key: "project_name",
			render: (text, record) => {
				return record.project.name
			}
		},
		{
			title: "Fecha de Solicitud del Cliente",
			width: 20,
			align: "center",
			dataIndex: "created_at",
			key: "created_at",
			render: (text, record) => {
				return record.created_at
			}
		},
		{
			title: "Asesor",
			width: 25,
			align: "center",
			dataIndex: "assessor_name",
			key: "assessor_name",
			render: (text, record) => {
				return record.assessor.name
			}
		},

		{
			title: "Nombre del Cliente",
			width: 25,
			align: "center",
			dataIndex: "customer_name",
			key: "customer_name",
			render: (text, record) => {
				return record.customer.name
			}
		},

		{
			title: "Kit de Acabados",
			width: 20,
			align: "center",
			dataIndex: "quotation_finishes_included",
			key: "quotation_finishes_included",
			render: (text, record) => {
				if (record.quotation.finishes_included !== "Si") {
					return  <CheckOutlined style={{ fontSize: '18px', color: '#0D6B04' }} />
				}
				else {
					return <CloseOutlined style={{ fontSize: '18px', color: '#f5222d' }} />
				}
			}
		},

		{
			title: "Solicitud del Cliente",
			width: 90,
			dataIndex: "body_description",
			key: "body_description",
			render: (text, record) => {
				return record.body_description
			}
		},

		{
			title: "# de días antes de Respuesta Técnica",
			width: 20,
			dataIndex: "days_before_answer",
			key: "days_before_answer",
			render: (text, record) => {
				return record.days_before_answer
			}
		},

		{
			title: "Respuesta Técnica",
			width: 25,
			dataIndex: "approved_technical",
			key: "approved_technical",
			render: (text, record) => {
				return record.approved_technical
			}
		},

		{
			title: "Fecha de Respuesta Técnica",
			width: 20,
			dataIndex: "date_answer",
			key: "date_answer",
			render: (text, record) => {
				return record.date_answer
			}
		},

		{
			title: "Presupuesto",
			width: 25,
			dataIndex: "budget",
			key: "budget",
			render: (text, record) => {
				return record.budget
			}
		},

		{
			title: "Fecha Max. Pago",
			width: 20,
			dataIndex: "project_estimated_delivery_date",
			key: "project_estimated_delivery_date",
			render: (text, record) => {
				return record.project.estimated_delivery_date
			}
		},

		{
			title: "Aprobación Cliente",
			width: 25,
			dataIndex: "approved_customer",
			key: "approved_customer",
			render: (text, record) => {
				return record.approved_customer
			}
		},
		{
			title: "Pago Realizado",
			width: 20,
			dataIndex: "",
			align: "center",
			key: "",
			render: (text, record) => {
				if (record.date_pay === "Si") {
					return <CheckOutlined style={{ fontSize: '18px', color: '#0D6B04' }} /> 
				}
				else {
					return <CloseOutlined style={{ fontSize: '18px', color: '#f5222d' }} />
				}
			}
		},
		{
			title: "Estado de Solicitud",
			width: 20,
			dataIndex: "status",
			key: "status",
			render: (text, record) => {
				return record.status
			}
		}
	];
	
	const tableDate = (<Table
		columns={columns}
		dataSource={convertAllMapDataState(ListReforms?.reforms)}
		scroll={{ x: 1900, y: 300 }}
		size='small'
		bordered={true}
		rowKey = "index"
	/>);
	const content = (
		<div>
			<a style={{ marginRight: 21 }} onClick={exportExcel}>
				Excel
			</a>
			<a onClick={exportPdf}>PDF</a>
		</div>
	);
	return (
		<Card title="Informe de Reformas">
			<Form layout="vertical" onFinish={onFinish} form={form}>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={8} lg={8} xl={8}>
						<Form.Item label="Proyecto:" name="searchProject">
							<Select
								defaultValue={""}
								showSearch
								style={{ width: "100%" }}
								optionFilterProp="children"
								filterOption={(input, option) =>
									option.children.toLowerCase().indexOf(input.toLowerCase()) >=
									0
								}
							>
								<Select.Option value="">Seleccione...</Select.Option>
								{
									!Array.isArray(ListProjects) && ListProjects._payload?.map(item => (
										<Select.Option key={item?.id+item?.name+item?.id} value={item.id}>{` ${item.name} `}</Select.Option>

									))

								}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={8} lg={8} xl={8}>
						<Form.Item label="Estado de Solicitud:" name="stateRequest">
							<Select  defaultValue="Seleccionar">
								<Select.Option value="">Seleccione...</Select.Option>
								{
									!Array.isArray(ListStates) && ListStates._payload?.map((item) => (
										<Select.Option key={item?.id+item?.name+item?.id} value={item.name_state}>{` ${item.name_state} `}</Select.Option>
									))

								}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={8} lg={8} xl={8}>
						<Input.Group>
							<Form.Item name="searchCustomer" label="Cliente:">
								<Select
									defaultValue={""}
									showSearch
									style={{ width: "100%" }}
									optionFilterProp="children"
									
									filterOption={(input, option) =>
										option.children
											.toLowerCase()
											.indexOf(input.toLowerCase()) >= 0
									}
								>
									<Select.Option value="">Seleccione...</Select.Option>
									{
										!Array.isArray(ListCustomers) && ListCustomers._payload?.map(item => (
											<Select.Option key={item?.id+item?.name+item?.id} value={item.id}>{` ${item.name} - CC: ${item.identification}`}</Select.Option>

										))

									}
								</Select>
							</Form.Item>
						</Input.Group>
					</Col>
				</Row>
				<Row>
					<Col xs={18} sm={20} md={21} lg={22} xl={22} />

					<Col xs={2} sm={2} md={2} lg={2} xl={2}>
						<Form.Item>
							<Button type="primary" htmlType="submit">
								Buscar
							</Button>
						</Form.Item>
					</Col>
				</Row>
			</Form>
			<Row justify="end" aria-disabled={ListReforms.pdf_path != null}>
				<Col>
					{
						show ? (
							<Popover
								content={content}
								trigger="click"
								visible={visiblePopover}
								onVisibleChange={(state) => {setVisiblePopover(state)}}
							>
							<Button
								 type="primary"
								 style={{ backgroundColor: "#5cb85c", borderColor: "#5cb85c" }} 
							>
								Exportar
							</Button>
						</Popover>) : null
					}
				</Col>
			</Row>
			<br/>
			{
				
				show ? tableDate : consultind ? <Spinner/> : null
			}
		</Card>
	);

}

export default ReformReportComponent;
