import React, {useContext} from "react";
import {Col, Form, PageHeader, Row} from "antd";
import CommercialDiscounts from "../../../../Quotes/Components/editQuote/CommercialDiscounts";
import DiscountsCoupons from "../../../../Quotes/Components/editQuote/DiscountsCoupons";
import {SalesMadeContext} from "../context/SalesMadeContext";

export const TablesForDiscounts = () => {
	
	const { discountsSet,coupon,propertyData,info,Project } = useContext(SalesMadeContext);
	
	return(
		<Form.Item>
			<Row gutter={2}>
				<Col span={12}>
					{
						propertyData.tradePolicies && (<>
							<PageHeader
								style={{ border: " solid rgb(235, 237, 240);" }}
								title={"Descuentos comerciales"}
							/>
							<CommercialDiscounts Project={Project} Discoun={discountsSet} />
						</>)
					}
				</Col>
				<Col span={12}>
					{
						propertyData.Coupon && (<>
							<PageHeader
								style={{ border: " solid rgb(235, 237, 240);" }}
								title={"Descuentos cupones"}
							/>
							<DiscountsCoupons
								Discoun={discountsSet}
								Precie={propertyData.unitValue}
								dataSource={coupon}
								ProjectId={info.ProjectId}
								CustomerId={info.CustomerId}
							/>
						</>)
					}
				</Col>
			</Row>
		</Form.Item>
	)
}