import React from "react";
import { useDispatch } from "react-redux";
import {deleteControlPowerById, fetchAllControlPowers} from "../../../../../redux/actions/Project/ControlPowerAction";
import {  Popconfirm, Tooltip, Button } from "antd";
import { QuestionCircleOutlined, DeleteOutlined } from "@ant-design/icons";
import {error, errorGlobal, success} from "../../../../handle/Notification/Notification";

const DeleteControlPower = (props) => {
	const dispatch = useDispatch();
	function cancel() {
		error("Operación cancelada");
	}

	const customerdelete = () => {
		dispatch(
			deleteControlPowerById(props.id, async (res) => {
				const response = await res;
				response.error
					? errorGlobal("¡UPS, Ha ocurrido un error")
					: success("Control de competencias eliminado");
				dispatch(fetchAllControlPowers());
			})
		);
	};

	const textDelete = <span style={{ color: "#6A6963" }}>Eliminar</span>;

	return (
		<>
			<Tooltip placement="left" color="#ffffff" title={textDelete}>
				<Popconfirm
					title={`¿Desea eliminar el control de competencia? `}
					onConfirm={customerdelete}
					onCancel={cancel}
					okText="Si"
					cancelText="No"
					icon={<QuestionCircleOutlined style={{ color: "red" }} />}
				>
					<Button
	icon={<DeleteOutlined/>}
	type="primary"
	size="small"
	danger
	/>
				</Popconfirm>
			</Tooltip>
		</>
	);
};

export default DeleteControlPower;
