import React from 'react';
import { Row, Card, Form, Table, Divider } from "antd";


import {fillDataTable, fillDataResumenAssesor} from "./FillDataTable";



const PaginationTables = ({data}) => {
  const propertyValueColumns = [
    {
      title: 'Nivel Interés',
      dataIndex: "descripcion",
      width: 80,
    },
    {
      title: 'Total',
      align: 'center',
      dataIndex: "value",
      width: 20,
    },
    {
      title: 'Disponibles',
      align: 'center',
      dataIndex: "valueDisponibles",
      width: 20,
    },

  ];
  const NivelInteres = [
    {
      title: 'Nivel Interés',
      width: 80,
      dataIndex: 'descripcion',
      key: 'Nivel Interés',
    },
    {
      title: 'Total',
      dataIndex: 'value',
      key: 'Total',
      width: 80,
    },
    {
      title: ((data!=null) ? data.tittle_info.assesor_name : ""),
      dataIndex: 'valueAssesors',
      key: "Asesor",
      width: 100,
    },
    {
      title: 'Disponibles',
      dataIndex: 'valueDisponibles',
      key: 'Disponibles',
      width: 80,
    }
  ];
  const ResumenAssesdor = [
    {
      title: 'Asesor',
      dataIndex: 'nombreAsesor',
      width: 120
    },
    {
      title: 'Unidades Cotizadas',
      width: 120,
      dataIndex: 'unidadesCotizadas',
      key: 'Unidades Cotizadas',
    },
    {
      title: 'Clientes Nuevos',
      dataIndex: 'clientesNuevos',
      key: 'Clientes Nuevos',
      width: 120,
    },
    {
      title: 'Clientes Visitados',
      dataIndex: 'clientesVisitados',
      key: 'Clientes Visitados',
      width: 120,
    },
    {
      title: 'Unidades Vendidas',
      dataIndex: 'unidadesVendidas',
      key: 'Unidades Vendidas',
      width: 120,
    },
    {
      title: 'Valor Total Ventas',
      dataIndex: 'totalVentas',
      key: 'Valor Total Ventas',
      width: 120,
    },
    {
      title: 'Unidades Separadas',
      dataIndex: 'unidadesSeparadas',
      key: 'Unidades Separadas',
      width: 120,
    },
    {
      title: 'Desistimientos',
      dataIndex: 'desistimientos',
      key: 'Desistimientos',
      width: 120,
    },
    {
      title: 'Actividades Programadas',
      dataIndex: 'actividadesProgramadas',
      key: 'Actividades Programadas',
      width: 120,
    },
    {
      title: 'Actividades Realizadas',
      dataIndex: 'actividadesRealizadas',
      key: 'Actividades Realizadas',
      width: 120,
    },
    {
      title: 'Actividades Vencidas',
      dataIndex: 'actividadesVencidas',
      key: 'Actividades Vencidas',
      width: 120,
    }
  ];

  const Participation = () => {
    return (
      <Table.Summary.Row>
        <Table.Summary.Cell colSpan={2}>Participación (%){""}</Table.Summary.Cell>
        <Table.Summary.Cell >0%{""}</Table.Summary.Cell>
        {(data!=null) ? ( (data.has_data===1) ?
          <Table.Summary.Cell >0%{""}</Table.Summary.Cell>
          :<></>
        ):(<></>)}
      </Table.Summary.Row>
    )
  }
  return (
    <>
      <Card>
        <Form>
          <Row>
            <Table
              size="small"
              bordered
              style={{ width: "200px", height: "330px" }}
              summary={Participation}
              columns={(data!= null) ? ((data.has_data===1) ? (NivelInteres) : (propertyValueColumns)) : (propertyValueColumns)}
              dataSource={(fillDataTable(data))}
              pagination={false}
            />
          </Row>
          <Divider style={{ visibility: "hidden" }} />
          <Row>
            <Table style={{ width: "100%"}} columns={ResumenAssesdor} scroll={{ x: 1500 }} dataSource={(fillDataResumenAssesor(data))} />
          </Row>
        </Form>
      </Card>
    </>
  );
}

export default PaginationTables;