import * as constants from "../../../constants";

export const fetchInfoReportSalesPlan = (data, postProcessSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		data: data,
		url: `${constants.RUTA}/sales_plan_report`,
		postProcessSuccess: postProcessSuccess,
		postProcessError: onError
	}
});
