import { Card } from "antd";
import React from "react";
import Spinner from "../../../../../handle/Spinner/Spinner";
import TableLegalizeDocuments from "./TableLegalizeDocuments";
import {convertAllMapDataState} from "../../../../../handle/HandleFilterTable/handleFilterTable";

const MainLegalizeDocuments = ({ dataSource, setDataSource , idSale }) => {
	return (
		<Card
			title="Listado de Documentos"
			actions={[
				<h6>
					Formatos Aceptados: .pdf, .doc, .xls, .docx, .xlsx, .jpg, .png, .jpeg,
					.bmp, .gif
				</h6>,
			]}
		>
			{(!Array.isArray(dataSource)) ? (
				<TableLegalizeDocuments idSale={idSale} data={convertAllMapDataState(dataSource?._payload)} setDataSource={setDataSource} />
			): <Spinner/>}
		</Card>
	);
};

export default MainLegalizeDocuments;
