import React, {useEffect, useState} from "react";
import {Button, Form} from "antd";
import ListTitleData from "../ListTitleDataComponent";
import {useParams} from "react-router-dom";
import DiscountPolicyComponent from "../PoliciesProject/DiscountPolicyComponent";
import {errorGlobal, success} from "../../../../../../../handle/Notification/Notification";
import {useDispatch} from "react-redux";
import {
	deleteDiscountPolicyById, fetchAllDiscountPolicy,
	updateOrCreateDiscountPolicyById
} from "../../../../../../../../redux/actions/Project/DiscountPolicyAction";
import moment from "moment";

const dataTitlePolicyDiscount = [
	"Fecha Inicio",
	"Fecha Fin",
	"Límite Inferior",
	"Límite Superior",
	"Tipo de Descuento",
	"Valor",
];

function validateResponse(res) {
	return (res.error) ?
		(res.message === "Start date is greater than the end date") ?
			"Fecha final menor o igual a la fecha inicial" :
 
			"¡UPS! Ocurió un error mientras se actualizaba." :
		"Políticas de descuento guardadas correctamente.";
}

const DiscountPolicyContainer = ({ discountPoliciesDatabase }) => {
	const { id } = useParams();
	const idParams = id;
	const dispatch = useDispatch();
	const [form] = Form.useForm();
	const [count, setCount] = useState(1);
	const [discountPolicies, setDiscountPolicies] = useState([]);

	useEffect(() => {
		setDiscountPolicies(discountPoliciesDatabase);
	}, [discountPoliciesDatabase]);

	const handleSubmit = async () => {
		let arrayToWork = [...discountPolicies._payload];
		try {
			const values = await form.validateFields();
			arrayToWork.forEach((item, index) => {
				if(!item?.id){
					Object.keys(values).forEach(x => {
						let id = x.split(";")[1];
						let index = arrayToWork.findIndex(y => y.id == id);
						if(index == -1){
							item.id = id;
							return true;
						}
					});
				}
				
				item.lower_limit = values[`lower_limit;${item.id}`];
				item.upper_limit = values[`upper_limit;${item.id}`];
				item.discount_type = parseInt(values[`discount_type;${item.id}`]);
				item.amount = values[`amount;${item.id}`];
				item.initial_date =
					values[`initial_date;${item.id}`].format("YYYY-MM-DD");
				item.final_date = values[`final_date;${item.id}`].format("YYYY-MM-DD");
				item.id = item.id < 0 ? null : item.id;
			});

			dispatch(
				updateOrCreateDiscountPolicyById(arrayToWork, async (response) => {
					const res = await response;
					const Mensaje = validateResponse(res);
						res.error
						? errorGlobal(Mensaje)
						: success(Mensaje);
						
					 dispatch(fetchAllDiscountPolicy(id));
				})
			);
		} catch (err) {
			console.error(err);
		}
	};

	const handleAddDiscountPolicy = () => {
		let discountPolicy = discountPolicies;
		let arrayToWork = [...discountPolicy._payload];
		const newItem = {
			id: count * -1,
			lower_limit: null,
			upper_limit: null,
			discount_type: "",
			amount: null,
			initial_date: moment(),
			final_date: moment(),
			project_id: parseInt(idParams),
		};
		discountPolicy._payload = [...arrayToWork, newItem];
		setDiscountPolicies(discountPolicy);
		setCount(count + 1);
	};

	const handleDeleteDiscountPolicy = (id) => {
		if (id && id >= 0) {
			dispatch(
				deleteDiscountPolicyById(id, async (response) => {
					const res = await response;
					if (res.error) {
						errorGlobal("¡UPS! Ocurió un error mientras se actualizaba.");
					} else {
						success("La política ha sido eliminada con éxito.");
					}
				})
			);
		}
		let discountPolicy = discountPolicies;
		let arrayToWork = [...discountPolicy._payload];
		discountPolicy._payload = arrayToWork.filter((item) => item.id !== id);
		const newObject = {
			_payload: discountPolicy._payload,
		};
		setDiscountPolicies(newObject);
	};

	return (
		<Form form={form} onFinish={handleSubmit} name="discountPolicyForm">
			<Button
				type="primary"
				style={{
					margin: "2%",
					backgroundColor: "#5cb85c",
					borderColor: "#5cb85c",
				}}
				disabled={Array.isArray(discountPolicies) || JSON.parse(localStorage.stateDisable)}
				onClick={handleAddDiscountPolicy}
			>
				Agregar Politica de descuento
			</Button>
			<div style={{ marginLeft: "20px" }}>
				<div className="wrapperPolicy">
					<div className={"listItemDiscount"}>
						<ListTitleData data={dataTitlePolicyDiscount} />
					</div>

					{!Array.isArray(discountPolicies) &&
						discountPolicies._payload.map((item) => (
							<DiscountPolicyComponent
								key={item.id}
								handleDeleteDiscountPolicy={handleDeleteDiscountPolicy}
								item={item}
								form={form}
							/>
						))}
				</div>
			</div>
			<Form.Item>
				<Button
					type="primary"
					style={{
						margin: "2%",
						marginLeft: "60vw",
					}}
					disabled={Array.isArray(discountPolicies) || JSON.parse(localStorage.stateDisable)}
					htmlType={"submit"}
				>
					Guardar políticas de descuento
				</Button>
			</Form.Item>
		</Form>
	);
};

export default DiscountPolicyContainer;
