import * as constants from "../../constants";

export default function discountPolicy(state = [], action) {
	switch (action.type) {
		case constants.SET_ALL_DISCOUNT_POLICY:
			return action.payload;
		case constants.UPDATE_OR_CREATE_DISCOUNT_POLICY:
			return state;
		case constants.REMOVE_DISCOUNT_POLICY:
			return state;
		case constants.RESET_DISCOUNT_POLICY:
			return action.payload;
		default:
			return state;
	}
}
