import * as constants from '../../../constants';
import LocalStorage from '../../../../views/config/LocalStorage'

let Storage = new LocalStorage();


export const fetchAllAssignmentAccountants = (id) => ({
    type: constants.API,
    payload: {
        method: 'GET',
	    headers: { 'access-token':Storage.getToken() },
        url: `${constants.RUTA}/projectAccountants/${id}`,
        success: (response) => (setAllAssignmentAccountants(response))
    }
});

export const updateOrCreateAssignmentAccountantsById = (data, onSuccess, onError) => ({
    type: constants.API,
    payload: {
        method: 'PUT',
        url: `${constants.RUTA}/projectAccountants/assignProject`,
        data,
        success: (data) => (updateOrCreateAssignmentAccountants(data)),  
        postProcessSuccess: onSuccess,
        postProcessError: onError           
    }
});

export const deleteAssignmentAccountantsById = ( id, onSuccess, onError) => ({
    type: constants.API,
    payload: {
        method: 'DELETE',
        url: `${constants.RUTA}/projectAccountants/${id}`,
        success: () => (removeAssignmentAccountants(id)),
        postProcessSuccess: onSuccess,
        postProcessError: onError
    }
});

export const resetAssignmentAccountantsAction = () => ({
    type: constants.API,
    payload: {
		method: null,
		success: () => (resetAssignmentAccountants())
	}
})

const resetAssignmentAccountants = () =>({
	type:constants.RESET_ASSIGNED_COUNTERS,
	payload: []
});

const setAllAssignmentAccountants = (data) => ({
    type: constants.SET_ALL_ASSIGNED_COUNTERS,
    payload: data
});

const updateOrCreateAssignmentAccountants = ( id, data) => ({
    type: constants.UPDATE_OR_CREATE_ASSIGNED_COUNTERS,
    payload: { id, data }
});

const removeAssignmentAccountants = ( id) => ({
    type: constants.REMOVE_ASSIGNED_COUNTERS,
    payload:  id
});