import React from 'react';
import {Col, Row} from 'antd';
import {FORMATTER_PESO} from "../../../../../redux/constants";


const GeneralAccountStatement = ({generalAccountStatementInfo}) => {
  return (
		<Row>
			<Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 24 }}>
			<table className="table-report">
                <tr>
                    <td colSpan="5" className="title head_color th-report"><b>2. ESTADO DE CUENTA</b></td>
                </tr>
                <tr>
                    <td colSpan="5" className="title head_color th-report"><b>2.1 ESTADO DE CUENTA GENERAL</b></td>
                </tr>
                <tr>
                    <td colSpan="2" className="title head_color th-report"><b>CONCEPTOS</b></td>
                    <td  className="title head_color th-report"><b>Valor Pagado</b></td>
                    <td  className="title head_color th-report"><b>Valor Pactado</b></td>
                    <td  className="title head_color th-report"><b>Saldo Pendiente</b></td>
                </tr>
                <tr >
                    <td rowSpan="2" className="cell_not_center th-report">Cuota Inicial</td>
                    <td className="cell_not_center th-report">C.I. No Financiada ({generalAccountStatementInfo.unfunded_initial_fee.percentage_unfunded_initial_fee}%)</td>
                    <td className="cell_not_center_rigth th-report padding-number">{FORMATTER_PESO.format(generalAccountStatementInfo.unfunded_initial_fee.value_paid)}</td>
                    <td className="cell_not_center_rigth th-report padding-number">{FORMATTER_PESO.format(generalAccountStatementInfo.unfunded_initial_fee.agreed_value)}</td>
                    <td className="cell_not_center_rigth th-report padding-number">{generalAccountStatementInfo.unfunded_initial_fee.pending_value}</td>
                </tr>
                <tr >
                    <td className="cell_not_center th-report">C.I. a Financiada ({generalAccountStatementInfo.value_to_finance.percentage_value_to_finance}%)</td>
                    <td className="cell_not_center_rigth th-report padding-number">{FORMATTER_PESO.format(generalAccountStatementInfo.value_to_finance.value_paid)}</td>
                    <td className="cell_not_center_rigth th-report padding-number">{FORMATTER_PESO.format(generalAccountStatementInfo.value_to_finance.agreed_value)}</td>
                    <td className="cell_not_center_rigth th-report padding-number">{generalAccountStatementInfo.value_to_finance.pending_value}</td>
                </tr>
                <tr className="row_bank_percentage  th-report">
                    <td className="cell_not_center th-report">Financiación Banco / Recursos
                        propios</td>
                    <td className="cell_not_center th-report padding-number">{generalAccountStatementInfo.bank_financing.percentage_bank_financing}%</td>
					<td className="cell_not_center_rigth th-report padding-number">{FORMATTER_PESO.format(generalAccountStatementInfo.bank_financing.value_paid)}</td>
					<td className="cell_not_center_rigth th-report padding-number">{FORMATTER_PESO.format(generalAccountStatementInfo.bank_financing.agreed_value)}</td>
					<td className="cell_not_center_rigth th-report padding-number">{generalAccountStatementInfo.bank_financing.pending_value}</td>

                </tr>
                <tr className="cell_not_center th-report">
                    <td colspan="2" className="cell_not_center th-report">Total Valor Inmueble sin Reformas</td>
					<td className="cell_not_center_rigth th-report padding-number">{FORMATTER_PESO.format(generalAccountStatementInfo.value_total_unit.value_paid)}</td>
					<td className="cell_not_center_rigth th-report padding-number">{FORMATTER_PESO.format(generalAccountStatementInfo.value_total_unit.agreed_value)}</td>
					<td className="cell_not_center_rigth th-report padding-number">{generalAccountStatementInfo.value_total_unit.pending_value}</td>

                </tr>
                <tr >
                    <td colSpan="2" className="cell_not_center th-report">Valor Reformas</td>
					<td className="cell_not_center_rigth th-report padding-number">{FORMATTER_PESO.format(generalAccountStatementInfo.value_of_reforms.value_paid)}</td>
					<td className="cell_not_center_rigth th-report padding-number">{FORMATTER_PESO.format(generalAccountStatementInfo.value_of_reforms.agreed_value)}</td>
					<td className="cell_not_center_rigth th-report padding-number">{generalAccountStatementInfo.value_of_reforms.pending_value}</td>
                </tr>
                <tr >
                    <td colSpan="2" className="cell_not_center th-report">Subtotal Valor Inmueble Incluido Reformas</td>
					<td className="cell_not_center_rigth th-report padding-number">{FORMATTER_PESO.format(generalAccountStatementInfo.sub_total_value_unit_with_reforms.value_paid)}</td>
					<td className="cell_not_center_rigth th-report padding-number">{FORMATTER_PESO.format(generalAccountStatementInfo.sub_total_value_unit_with_reforms.agreed_value)}</td>
					<td className="cell_not_center_rigth th-report padding-number">{generalAccountStatementInfo.sub_total_value_unit_with_reforms.pending_value}</td>
                </tr>
                <tr className="cell_not_center th-report">
                    <td colspan="2" className=" th-report">* Descuento de interes</td>
                    <td className="cell_not_center_rigth th-report">   </td>
                    <td className="cell_not_center_rigth th-report">    </td>
                    <td className="cell_not_center_rigth th-report">    </td>
                </tr>
                <tr className="cell_not_center th-report">
                    <td colspan="2" className=" th-report">** Recargo de interes</td>
                    <td className="cell_not_center_rigth th-report">   </td>
                    <td className="cell_not_center_rigth th-report">    </td>
                    <td className="cell_not_center_rigth th-report">    </td>
                </tr>
                <tr >
                    <td colspan="2" className="cell_not_center th-report">Total Valor Inmueble</td>
					<td className="cell_not_center_rigth th-report padding-number">{FORMATTER_PESO.format(generalAccountStatementInfo.sub_total_value_unit_with_reforms.value_paid)}</td>
					<td className="cell_not_center_rigth th-report padding-number">{FORMATTER_PESO.format(generalAccountStatementInfo.sub_total_value_unit_with_reforms.agreed_value)}</td>
					<td className="cell_not_center_rigth th-report padding-number">{generalAccountStatementInfo.sub_total_value_unit_with_reforms.pending_value}</td>
                </tr>


            </table>
			</Col>
		</Row>
  );
}

export default GeneralAccountStatement;