import React, { useEffect, useState } from "react";
import { Button, Modal, Tooltip } from "antd";
import { FileProtectOutlined } from "@ant-design/icons";
import MainLegalizeDocuments from "./MainLegalizeDocuments";
import { useDispatch } from "react-redux";
import {
	setAllLegalizeDocumentAction,
	updatePercentageLegalizationDocumentAction,
} from "../../../../../../redux/actions/Price/SaleMade/legalizeDocumentAction";
import {
	errorGlobal,
	success,
	warning,
} from "../../../../../handle/Notification/Notification";
import { fetchAllSaleMade } from "../../../../../../redux/actions/Price/SaleMade/SalesMadeAction";

const LegalizeDocuments = ({ data }) => {
	const dispatch = useDispatch();
	const [dataSource, setDataSource] = useState([]);
	const [isModalVisible, setIsModalVisible] = useState(false);

	

	useEffect(() => {
		if (isModalVisible) {
			dispatch(
				setAllLegalizeDocumentAction(data.id, async (response) => {
					let res = await response;
					if (res.error) {
 
						errorGlobal("¡UPS! ha ocurrido un error");
					} else {
						setDataSource(res);
					}
				})
			);
		}
	}, [isModalVisible]);

	const showModal = () => {
		setIsModalVisible(true);
	};

	const handleCancel = () => {
		setIsModalVisible(false);
	};

	const legalizeDocuments = () => {
		if (dataSource.hasOwnProperty("_payload")) {
			dispatch(
				updatePercentageLegalizationDocumentAction(
					data.id,
					async (response) => {
						let res = await response;
						if (res.error) {
 
							errorGlobal("¡UPS! ha ocurrido un error");
						} else {
							success("Documentos legalizados con éxito");
							dispatch(fetchAllSaleMade());
							handleCancel();
						}
					}
				)
			);
		} else {
			warning("No hay documentos que legalizar.");
		}
	};

	const textLegalizeDocuments = (
		<span style={{ color: "#6A6963" }}>Legalizar documentos</span>
	);

	return (
		<>
			{" "}
			<Tooltip placement="left" color="#ffffff" title={textLegalizeDocuments}>
				<Button
					style={{ background: "#52c41a", color: "#fff" }}
					size={"small"}
					onClick={()=>showModal()}
					icon={<FileProtectOutlined />}
					disabled = {data.is_desisted}
				/>
			</Tooltip>
			{isModalVisible && (
				<Modal
					title={"Legalizar Documentos"}
					visible={isModalVisible}
					footer={[
						<Button key="close" type={"primary"} onClick={()=>handleCancel()}>
							Cerrar
						</Button>,
						<Button
							type={"primary"}
							style={{
								background: "#389e0d",
								borderColor: "#389e0d",
								color: "#fff",
							}}
							key="legalizedDocuments"
							onClick={()=>legalizeDocuments()}
						>
							Legalizar Documentos
						</Button>,
					]}
					onCancel={() => setIsModalVisible(false)}
					width={1000}
				>
					{isModalVisible && (
						<MainLegalizeDocuments
							dataSource={dataSource}
							setDataSource={setDataSource}
							idSale={data.id}
						/>
					)}
				</Modal>
			)}
		</>
	);
};

export default LegalizeDocuments;
