import moment from "moment";
import {Card, Table} from "antd";
import {useDispatch} from "react-redux";
import React, {useEffect, useState} from "react";
import {ShowClient} from "./Components/ShowClient";
import {errorGlobal} from "../../../handle/Notification/Notification";
import {dateFormat} from "../../../handle/HandleDatePicker/HandleDate";
import {listAfterSale} from "../../../../redux/actions/Price/AfterSale/AfetrSaleActions";
import AfterSales from "../../Customers/CustomerTracking/Components/MovementData/ActionsSales/AfterSales/AfterSales";

const AfterSalesList = () => {
	const dispatch = useDispatch();
	const [data,setData] = useState([])
	
	useEffect(()=>{
		searchAfterSalesList()
	},[])
	
	const searchAfterSalesList = () => {
		dispatch(listAfterSale(async (response)=>{
			const res = await response
			setData(res._payload)
		},async (error)=>{
			const err = await error
			errorGlobal(err.message);
		}))
	}
	
	const differenceBetweenDates = (f1) => {
		let fecha1 = new Date(f1).getTime()
		let fecha2 = new Date().getTime()
		let diff = fecha2 - fecha1;
		let result = diff/(1000*60*60*24)
		return parseFloat(result).toFixed(2)
	}
	
	const columns = [
		{
			title: '#',
			width: 5,
			key: 'id',
			dataIndex: 'id',
			align: "center",
			render: (_, record) => (data.indexOf(record) + 1)
		}, {
			title: 'Cliente',
			width: 40,
			dataIndex: 'customer',
			key: 'customer',
			align: "center",
			render: (_, record) => <ShowClient record={record} />
		}, {
			title: 'Proyecto',
			width: 50,
			dataIndex: 'project_name',
			align: "center",
			key: 'project_name',
		}, {
			title: 'Unidad',
			width: 15,
			dataIndex: 'unit_label',
			align: "center",
			key: 'unit_label',
		}, {
			title: 'Asesor',
			dataIndex: 'assessor_name',
			width: 40,
			align: "center",
			key: 'assessor_name',
		}, {
			title: 'Descripcion',
			dataIndex: 'description',
			key: 'description',
			align: "center",
			width: 40,
		}, {
			title: 'Fecha de solicitud',
			dataIndex: 'date_request',
			key: 'date_request',
			align: "center",
			width: 40,
			render: (text, record) => (record?.date_request ? moment(record?.date_request).format(dateFormat): '')
		}, {
			title: 'Dias transcurrido desde la solicitud',
			dataIndex: 'days_passed',
			key: 'days_passed',
			align: "center",
			width: 40,
			render: (text, record) => differenceBetweenDates(record.date_request),
		}, {
			title: 'Estado',
			dataIndex: 'state',
			key: 'state',
			align: "center",
			width: 40,
		}, {
			title: 'Acciones',
			dataIndex: 'action',
			key: 'action',
			width: 25,
			align: "center",
			fixed: 'right',
			render: (_, record) => <AfterSales basicInformation={record} />
		}
	]
	return (
		<>
			<Card
				title="Solicitudes de postventas"
			>
				<Table
					columns={columns}
					dataSource={data}
				/>
			</Card>
		</>
	)
}

export default AfterSalesList