import {useHistory} from 'react-router-dom';
import FormSendEmail from "./FormSendEmail";
import FormSeparacion from "./FormSeparacion";
import {ProviderQuotes} from "./ProviderQuotes";
import React, {useEffect, useState} from "react";
import {Table, Button, Tooltip, Tag} from "antd";
import {useDispatch, useSelector} from "react-redux";
import * as constants from "../../../../../redux/constants";
import FormLegalizarVenta from "./legalizeSale/FormLegalizeSale";
import TootipInfo from "../../../TooltipInfoComponent/TootipInfo";
import {validatePermission} from "../../../../handle/PermissionMethods/PermissionMethods";
import {PrinterOutlined, CloseOutlined, CheckOutlined, EditOutlined} from "@ant-design/icons";
import {downloadQuotes, getAllQuotation} from "../../../../../redux/actions/Price/Quotation/QuotesAction";
import {convertAllMapDataState, getColumnSearchProps, noWhiteWindow} from "../../../../handle/HandleFilterTable/handleFilterTable";

const TableQuotes = () => {
	const dispatch = useDispatch();
	const fechaLocal = new Date().toISOString().slice(0, 10);
	const [setLoading] = useState(true);
	useEffect(
		() => {
			dispatch(
				getAllQuotation(async () => {
					setLoading(false);
				})
			)
		},[]
	);
	const history = useHistory();

	let quotations = useSelector((state) => state?.quotation._payload);

	useEffect(() => {
	}, [quotations])
	const columns = [
		{
			title: "#",
			dataIndex: "code",
			key: "code",
			width: 80,
			align: "center",
			fixed: "left",
			...getColumnSearchProps("code"),
			...noWhiteWindow
		},
		{
			title: "Fecha y hora",
			dataIndex: "created_at",
			key: "created_at",
			width: 120,
			align: "center",
			...getColumnSearchProps("created_at"),
			...noWhiteWindow
		},
		{
			title: "Válida hasta",
			dataIndex: "final_date",
			key: "validaHasta",
			align: "center",
			width: 90,
		},
		{
			title: "Cliente",
			dataIndex: "name",
			key: "cliente",
			width: 160,
			render: (_, record) => getRenderCustomerPersonaldata(record),
		},
		{
			title: "Asesor",
			dataIndex: "adviser_name",
			key: "adviser_name",
			align: "center",
			width: 100,
			...getColumnSearchProps("adviser_name"),
			...noWhiteWindow
		},
		{
			title: "Proyecto",
			dataIndex: "project_name",
			key: "project_name",
			align: "center",
			width: 120,
			...getColumnSearchProps("project_name"),
			...noWhiteWindow
		},
		{
			title: "Unidad",
			dataIndex: "label",
			key: "unidad",
			align: "center",
			width: 60,
		},
		{
			title: "Valor",
			dataIndex: "value_total_unit",
			key: "value_total_unit",
			align: "center",
			width: 100,
			render: (text) => constants.FORMATTER_PESO.format(text),
		},
		{
			title: "Enviada",
			dataIndex: "enviada",
			key: "enviada",
			align: "center",
			width: 60,
			render: (_, record) => getRenderStateEmail(record),
		},
		{
			title: "Estado unidad",
			dataIndex: "unit_state",
			align: "center",
			fixed: "right",
			key: "estadoUnidad",
			width: 90,
			render: (_, record) => {
				switch (record.unit_state) {
					case "Disponible":
						return <Tag color={"green"}> {record.unit_state} </Tag>;
					case "Separada":
						return <Tag color={"lime"}> {record.unit_state} </Tag>;
					case "Comprada":
						return <Tag color={"red"}> {record.unit_state} </Tag>;
					default:
						return <Tag color={"white"}> {record.unit_state} </Tag>;
				}
			},
		},
		{
			title: "Acciones",
			key: "actions",
			fixed: "right",
			width: 150,
			render: (_, record) => getActions(record),
		},
	];

	const getRenderCustomerPersonaldata = (record) => {
		return (
			<>
				<strong>Nombre:</strong> <span>{record.name}</span> <br/>
				<strong>Teléfono:</strong> <span>{record.phone_number}</span> <br/>
				<strong>Correo:</strong> <span>{record.email}</span>
			</>
		);
	};

	const getRenderStateEmail = (record) => {
		if (record.email_sent === 1)
			return <CheckOutlined style={{fontSize: "18px", color: "#0D6B04"}}/>;
		else
			return <CloseOutlined style={{fontSize: "18px", color: "#f5222d"}}/>;
	};

	const getActions = (record) => {
		const download = () => {
			dispatch(
				downloadQuotes(record.id, async (response) => {
					const res = await response;
					const tagA = document.createElement("a");
					tagA.href = `${res._payload.path}`;
					tagA.target = "_blank";
					document.body.appendChild(tagA);
					tagA.click();
				})
			);
		};
		const textVerImprimir = (
			<span style={{color: "#6A6963"}}> Ver/Imprimir </span>
		);

		const textEditar = <span style={{color: "#6A6963"}}> Editar </span>;

	//	let FechaCotizacion = new Date(record?.final_date).toISOString().slice(0, 10);
	
		return (
			<>
				<ProviderQuotes quotes={null}>
					{
						validatePermission(constants.SEND_EMAIL_QUOTATION_PERMISSION) ? (
							<FormSendEmail
								data={record}
								update_table={() => {
									dispatch(
										getAllQuotation(async (response) => {
											const res = await response;
											setLoading(false);
										})
									);
								}}
							/>
						) : null
					}


					{"  "}
					{
						validatePermission(constants.LEGALIZE_SALE_QUOTATION_PERMISSION) ? (
							<FormLegalizarVenta record={record}/>
						) : null
					}
					{"  "}
					{
						validatePermission(constants.UPDATE_QUOTATION_PERMISSION) ? (
							<Tooltip placement="left" color="#ffffff" title={textEditar}>
								<Button
									disabled={record.unit_state === "Comprada" || record?.final_date < fechaLocal}
									type="primary"
									style={{
										backgroundColor: "#ffa247",
										borderColor: "#ffa247",
										color: "#fff",
									}}
									size="small"
									icon={<EditOutlined/>}
									onClick={() => {
										history.push(`/sales/update-quote/${record.id}`)
									}}
								/>
							</Tooltip>
						) : null
					}

					{/* {record.unit_state !== "Comprada" && <UpdateQuotes record={record} />} */}
					{"  "}
					{
						validatePermission(constants.SEPERATED_UNIT_QUOTATION_PERMISSION) ? (
							<FormSeparacion props={record}/>
						) : null
					}

					{"  "}
					{
						validatePermission(constants.PRINT_QUOTATION_PERMISSION) ? (
							<Tooltip placement="left" color="#ffffff" title={textVerImprimir}>
								<Button
									size="small"
									onClick={()=>download(record)}
									style={{backgroundColor: "#4C4B99", marginTop: 3}}
									icon={<PrinterOutlined style={{color: "#fff"}}/>}
								/>
							</Tooltip>
						) : null
					}

					{"  "}
					<TootipInfo
						created_at={record.created_at}
						name={record.name}
						updated_at={record.updated_at}
					/>
				</ProviderQuotes>
			</>
		);
	};

	return (
		<Table
			columns={columns}
			loading={false}
			dataSource={convertAllMapDataState(quotations)}
			scroll={{x: 1500, y: 300}}
		/>
	);
};

export default TableQuotes;
