import React, {useState, useEffect} from "react";
import {Card, Button, Form, Row, Col, Select, Table, Input, Popconfirm, InputNumber} from "antd";
import { DeleteOutlined, PlusOutlined, MinusOutlined } from "@ant-design/icons";
import { errorPolicy } from "../../../../../../../handle/Notification/Notification";
import PolicyComply from "../PolicyComply";
import {convertAllMapDataState} from "../../../../../../../handle/HandleFilterTable/handleFilterTable";

const ManagementCompany = (props) => {
	

	const [dataSource, setDataSource] = useState(
		props.dataCommissionParamaterSelector
	);
	const [key, setKey] = useState(0);
	const [keyPolicy, setKeyPolicy] = useState(0);
	const [count, setCount] = useState(1000);
	const [currentCompanyAffiilated, setCurrentCompanyAffiilated] = useState("");
	const [selectAffiliatedCompanies , setSelectAffiliatedCompanies] = useState([]);

	useEffect(() => {
		setSelectAffiliatedCompanies(props.selectAffiliatedCompanies);
	},[props.selectAffiliatedCompanies]);

	useEffect(() => {
		for (let index = 0; index < dataSource.length; index++) {
			setCurrentCompanyAffiilated(dataSource[0].affiliated_company_id);
		}
	}, [dataSource]);

	useEffect(() => {
		if (dataSource.length === 0) {
			handleAdd();
		}
	}, [dataSource]);

	useEffect(() => {
		resetTable();
	}, [dataSource]);

	if (
		props.dataCommissionParamaterSelector === undefined ||
		props.dataCommissionParamaterSelector === null ||
		props.selectAffiliatedCompanies === null
	)
		return null;

	const columns = [
		{
			title: "#",
			dataIndex: "key",
			align: "center",
		},
		{
			title: "Etiqueta",
			dataIndex: "label",
			width: "20%",
			align: "center",
			render: (_, record) => (
				<Input
					name="label"
					value={record.label}
					style={{ width: "100%" }}
					onChange={(e) => {
						updateLabelManagementCompany(e.target.value, record);
					}}
				/>
			),
		},
		{
			title: "Comisión",
			dataIndex: "commission",
			align: "center",
			width: "10%",
			render: (_, record) => (
				<InputNumber
					min={0}
					max={100}
					formatter={(value) => `${value}%`}
					parser={(value) => value.replace("%", "")}
					name="commission"
					value={record.commission}
					style={{ width: "100%" }}
					onChange={(value) => {
						updateCommissionManagementCompany(value, record);
					}}
				/>
			),
		},
		{
			title: "Política a Cumplir",
			dataIndex: "policy",
			width: "40%",
			align: "center",
			render: (_, record) => (
				<>
					{record.commission_parameters_policy_comply.map((item) => (
						<PolicyComply
							dataPolicyToComply={props.policyToComply}
							dataSource={convertAllMapDataState(dataSource)}
							setDataSource={setDataSource}
							key={item.id}
							item={item}
						/>
					))}
				</>
			),
		},
		{
			title: "",
			dataIndex: "operation",
			width: "20%",
			align: "center",
			render: (_, record) =>
				dataSource.length >= 1 ? (
					<>
						<Button
	onClick={() => handleAddPolicy(record.key)}
	icon={<PlusOutlined/>}
	type="primary"
	size="small"
	/>
						{"  "}
						<Button
	onClick={() => handleDeletePolicy(record.key)}
	icon={<MinusOutlined/>}
	type="primary"
	size="small"
	style={{backgroundColor: "#ffa247", borderColor: "#ffa247"}}
	/>
						{"  "}
						<Popconfirm
							title="Confirmar acción?"
							onConfirm={() => handleDelete(record.key)}
						>
							<Button
	icon={<DeleteOutlined/>}
	type="primary"
	size="small"
	danger
	/>
						</Popconfirm>
					</>
				) : null,
		},
	];

	const resetTable = () => {
		for (let index = 0; index < dataSource.length; index++) {
			dataSource[index].key = index + 1;
			setKey(index + 1);
		}

		for (let index = 0; index < dataSource.length; index++) {
			for (
				let indexPolicy = 0;
				indexPolicy <
				dataSource[index].commission_parameters_policy_comply.length;
				indexPolicy++
			) {
				dataSource[index].commission_parameters_policy_comply[
					indexPolicy
				].keyPolicy = indexPolicy + 1;
				setKeyPolicy(indexPolicy + 1);
			}
		}
	};

	const handleAddPolicy = (key) => {
		const newPolicy = {
			id: count,
			percentage_total_value: null,
			policy_to_comply_id: "",
			keyPolicy: keyPolicy + 1,
		};
		const dataWorkSource = [...dataSource];

		for (let index = 0; index < dataWorkSource.length; index++) {
			if (dataWorkSource[index].key === key) {
				dataWorkSource[index].commission_parameters_policy_comply.push(
					newPolicy
				);
			}
		}
		setCount(count + 1);
		setDataSource(dataWorkSource);
		setKeyPolicy(keyPolicy + 1);
		props.setDataManagementCompany(dataWorkSource);
	};

	const handleDeletePolicy = (keyParameter) => {
		const dataWorkSource = [...dataSource];

		for (let index = 0; index < dataWorkSource.length; index++) {
			if (dataWorkSource[index].key === keyParameter) {
				if (
					dataWorkSource[index].commission_parameters_policy_comply.length > 1
				) {
					dataWorkSource[index].commission_parameters_policy_comply.pop();
				} else {
					errorPolicy("Debe existir almenos una condición por campo");
				}
			}
		}

		setDataSource(dataWorkSource);
		props.setDataManagementCompany(dataWorkSource);
	};

	const handleDelete = (key) => {
		const dataSources = [...dataSource];
		if (dataSources.length <= 1) {
			errorPolicy("Debe existir por lo menos una policitica por empresa");
			return;
		}

		setDataSource(dataSources.filter((item) => item.key !== key));
		props.setDataManagementCompany(
			dataSources.filter((item) => item.key !== key)
		);
	};

	const handleAdd = () => {
		const newData = {
			affiliated_company_id: currentCompanyAffiilated,
			commission: null,
			commission_parameters_policy_comply: [
				{
					id: count,
					percentage_total_value: null,
					policy_to_comply_id: "",
					keyPolicy: keyPolicy + 1,
				},
			],
			company_type: 1,
			key: key + 1,
			id: count + 1,
			label: "",
			projects_id: props.idProject,
		};
		const addDataSource = [...dataSource, newData];
		setDataSource(addDataSource);
		setKeyPolicy(keyPolicy + 1);
		setKey(key + 1);
		setCount(count + 1);
		props.setDataManagementCompany(addDataSource);
	};

	const deleteManagementCompany = () => {
		props.deleteManagementCompany();
	};

	const handleUpdateAffiliatedCompany = (value) => {
		const dataWorkSource = [...dataSource];
		dataWorkSource.map((item) => {
			item.affiliated_company_id = value;
			return item
		});
		setDataSource(dataWorkSource);
		props.setDataManagementCompany(dataWorkSource);
		setCurrentCompanyAffiilated(value);
	};

	const updateLabelManagementCompany = (value, record) => {
		const dataWorkSource = [...dataSource];
		dataWorkSource.map((item) => {
			if (item.key === record.key) {
				item.label = value;
			}
			return item
		});
		setDataSource(dataWorkSource);
		props.setDataManagementCompany(dataWorkSource);
	};

	const updateCommissionManagementCompany = (value, record) => {
		const dataWorkSource = [...dataSource];
		dataWorkSource.map((item) => {
			if (item.key === record.key) {
				item.commission = value;
			}
			return item
		});
		setDataSource(dataWorkSource);
		props.setDataManagementCompany(dataWorkSource);
	};

	return (
		<>
			<Card
				title="ADMINISTRADORA"
				style={{ borderColor: "black" }}
				extra={
					<Popconfirm
						title="Confirmar acción?"
						onConfirm={deleteManagementCompany}
					>
						<Button icon={<DeleteOutlined />} type="primary" danger>
							{" "}
							Borrar Empresa{" "}
						</Button>
					</Popconfirm>
				}
			>
				<Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
					<Col className="gutter-row" span={24}>
						<Form layout="vertical">
							<Row gutter={16}>
								<Form.Item
									label="Empresa Asociada"
									name="marketer"
									style={{ width: "40%" }}
									rules={[
										{
											required: true,
											message: "El campo es requerido",
										},
									]}
								>
									<Select
										showSearch
										optionFilterProp="children"
										filterOption={(input, option) =>
											option.children
												.toLowerCase()
												.indexOf(input.toLowerCase()) >= 0
										}
										defaultValue={String(dataSource[0].affiliated_company_id)}
										onChange={(value) => {
											handleUpdateAffiliatedCompany(value);
										}}
										placeholder="Seleccione..."
									>
										<Select.Option value="">Seleccione...</Select.Option>
										{Array.isArray(selectAffiliatedCompanies)
											? selectAffiliatedCompanies.map((item) => (
													<Select.Option key={item.id} value={String(item.id)}>
														{item.name}
													</Select.Option>
											  ))
											: null}
									</Select>
								</Form.Item>
							</Row>
							<Row gutter={16}>
								<Form.Item style={{ width: "90%" }}>
									<Table
										bordered
										dataSource={convertAllMapDataState(dataSource)}
										columns={columns}
										footer={() => (
											<Button onClick={handleAdd} type="primary">
												{" "}
												Agregar Politica
											</Button>
										)}
									/>
								</Form.Item>
							</Row>
						</Form>
					</Col>
				</Row>
			</Card>
		</>
	);
};

export default ManagementCompany;
