import React, { useState } from 'react';
import { Modal, Table, Button } from 'antd';
import { ExpandAltOutlined } from '@ant-design/icons';


const RedistribuirUnidades = (props) => {    

    const [isModalVisible, setIsModalVisible] = useState(false);

    const showModal = () => {
        setIsModalVisible(true);
    };
    const handleCancel = () => {
        setIsModalVisible(false);
    };
    const getActions = () => {
		return (<span style={{ color: '#6A6963' }}> Eliminar</span>)
	}

    const columns = [
        {
          title: '#',
          dataIndex: 'id',
          key:'id',
          editable: true,
          width: 80,
        },
        {
          title: 'Unidad',
          dataIndex: 'unidad',
          width: 100,
          editable: true,
          disabled: true
        },
        {
          title: 'Piso',
          dataIndex: 'piso',
          width: 100,
          editable: true,
          disabled: true
        },
        {
            title: 'Acciones',
            width: 100,
            dataIndex: 'action',
            fixed: 'right',
            render: (_, record) => getActions(record)
        },
      ];

    return (
    <>
      <Button disabled={props.disabled} size='small' type="primary" onClick={showModal} style={{marginBottom: '20px', marginLeft: '5px'}} icon={<ExpandAltOutlined />} />
      <Modal title={`Redistribuir - Tipo Unidad ${props.recordAea}`} visible={isModalVisible}  onCancel={handleCancel} width={600} footer={false} >
        <Table	
			columns={columns} 
			dataSource={null} 
			size='small' 
			bordered ={true}
		/>
      </Modal>
    </>
  );
};

export default RedistribuirUnidades