import React, {useState, useEffect} from "react";
import {useDispatch} from "react-redux";
import {UploadOutlined} from "@ant-design/icons";
import {Form, Input, InputNumber, DatePicker, Upload, Row, Col, Select, Divider, Checkbox, Button, Card} from "antd";
import moment from "moment";
import {success, errorGlobal} from "../../../../../../handle/Notification/Notification";
import {getOptionsSelect, getProjectById, updateProjectByIdPost} from "../../../../../../../redux/actions/Project/ProjectsAction";
import MapQuest from "../../../../../../handle/MapComponent/MapQuest";
import colombiaJSON from "../../../../../../handle/MapComponent/ColombiaJSON";
import ActionsMap from "../../../../../../handle/MapComponent/ActionsMap";
import NameActions from "../../../../../../handle/MapComponent/NameActions";
import {RUTA_API_MULTIMEDIA, VALID_DOCUMENT_UPLOAD} from "../../../../../../../redux/constants";
import {getChangeDateState} from "../../../../../../handle/Utilitis";

const EditProject = ({project, projectId}) => {
	const buildLogo = (logo) => {
		let newLogo;
		if (logo) {
			newLogo = [
				{
					uid: `-xxx${logo.id}`,
					id: logo.id,
					name: logo.name,
					status: "done",
					url: logo.path,
				},
			];
		} else {
			newLogo = [{}];
		}

		return newLogo;
	};

	const dispatch = useDispatch();
	const [form] = Form.useForm();
	const [projectAttachmentRemoved] = useState([]);
	const [projectTypes] = useState([]);
	const [projectStatuses] = useState([]);
	const [formSale, setFormSale] = useState(project._payload[0]["sale_type_id"] ?? 0);
	const [projectClassifications] = useState([]);
	const [loading, setLoading] = useState(false);
	const [typesSale, setTypesSale] = useState([]);
	const [municipalities, setMunicipalities] = useState([]);
	const [quantityDisabled, setQuantityDisabled] = useState(2);
	const [country, setCountry] = useState(0);
	const [logo, setLogo] = useState([
		{url: buildLogo(project._payload[0]["promoter_logo"])[0].url},
	]);
	const [active,setActive] = useState(true)

	const [lat] = useState("3.998749");
	const [lng] = useState("-75.2545871");
	const [stateDisable, setStateDisable] = useState(project._payload[0]["state"] == 0);
	localStorage.stateDisable = stateDisable;
	
	useEffect(()=>{
		handleDisableFiduciaryCode()
	},[formSale])

	let departments = [];
	let land = ["Colombia"];
	let location = project._payload[0]["location_id"];
	let currentDeparment = null;
	let currentCity = null;
	let countries = ["Colombia"];
	for (let i = 0; i < colombiaJSON.length; i++) {
		departments.push(colombiaJSON[i].departamento);
	}

	useEffect(() => {
		dispatch(
			getOptionsSelect((response) => {
				let typesSale = [];
				if (!response.error) {
					response._payload.project_classification.forEach(
						(value) => {
							projectClassifications.push({
								name: value.name,
								id: value.id,
							});
						}
					);
					response._payload.project_state.forEach((value) => {
						if(projectStatuses.findIndex(x => x.id == value.id) == -1){
							projectStatuses.push({
								name: value.name,
								id: value.id,
							});
						}
						
					});
					response._payload.project_types.forEach((value) => {
						if(projectTypes.findIndex(x => x.id == value.id) == -1){
							projectTypes.push({
								name: value.name,
								id: value.id,
							});
						}
						
					});
					response._payload.sales_types.forEach((value) => {
						if(typesSale.findIndex(x => x.id == value.id) == -1){
							typesSale.push({
								name: value.name,
								id: value.id,
							});
						}
						
					});

					setTypesSale(typesSale);
				}
			})
		);
	}, []);

	useEffect(() => {
		fillFields();
	}, []);

	useEffect(() => {
		if (project._payload[0]["location_id"] != null) {
			getCurrentCity();
			let query =
				currentCity?.name +
				" - " +
				currentDeparment?.departamento +
				" - " +
				countries["Colombia"];
			ActionsMap(query, null, NameActions.Maker_Location, 14);
		}
	}, []);

	useEffect(() => {
		if (quantityDisabled === "2") {
			backToNull("quantity");
		}
		if (formSale === 1) {
			backToNull("fiduciaryCode");
		}
	}, [quantityDisabled, formSale]);

	const backToNull = (value) => {
		form.setFieldsValue({
			[value]: 0,
		});
	};

	const handleDisableFiduciaryCode = () => {
		switch (formSale) {
			case 1:
				setActive(true)
				return true
			case 2:
				setActive(false)
				return false
			case 3:
				setActive(false)
				return false
			default:
				return false
		}
	};

	const onChangeCountry = (value) => {
		setCountry(value);
		if (value === "-") {
			form.setFieldsValue({department: "-"});
			form.setFieldsValue({city: "-"});
		}
		form.setFieldsValue({country: value});
		ActionsMap(land[value], null, NameActions.Find_Location, 4.5);
	};

	const onChangeDepartment = (value) => {
		if (value !== "-") {
			let departmentInJson = colombiaJSON.find(
				(element) => element.departamento === departments[value]
			);
			form.setFieldsValue({
				department: value,
			});
			ActionsMap(
				null,
				departmentInJson.latLng,
				NameActions.Find_Location_by_latlng,
				7
			);
			setMunicipalities(departmentInJson.ciudades);
		}
	};

	const onChangeMunicipality = (value) => {
		if (value !== "-") {
			let auxiliary = municipalities[value];
			let varMunicipalities =
				auxiliary.name +
				" - " +
				departments[form.getFieldValue("department")] +
				" - " +
				land[form.getFieldValue("country")];
			location = auxiliary["id"];
			form.setFieldsValue({
				city: value,
			});
			ActionsMap(varMunicipalities, null, NameActions.Maker_Location, 14);
		}
	};
	
	
	
	const captureEstimatedDateStartConstruction = (date) => {
		if (date !== null) {
			if (projectStatuses.length !== 0) {
				
				let estimatedDeliveryDate = form.getFieldsValue(["estimatedDeliveryDate"]).estimatedDeliveryDate;
				let estimatedDateStartConstruction = form.getFieldsValue(["estimatedDateStartConstruction"]).estimatedDateStartConstruction;
				
				form.setFieldsValue({
					stateProject: getChangeDateState(estimatedDeliveryDate, estimatedDateStartConstruction, projectStatuses),
				});
			}
		}
	};
	const captureEstimatedDeliveryDate = (date) => {
		if (date !== null) {
			if (projectStatuses.length !== 0) {


				let estimatedDeliveryDate = form.getFieldsValue(["estimatedDeliveryDate"]).estimatedDeliveryDate;
				let estimatedDateStartConstruction = form.getFieldsValue(["estimatedDateStartConstruction"]).estimatedDateStartConstruction;

				form.setFieldsValue({
					stateProject: getChangeDateState(estimatedDeliveryDate, estimatedDateStartConstruction, projectStatuses),
				});
			}
		}
	};

	const getProjectState = (state) => {
		switch (state) {
			case 3:
				return "Finalizado";
			case 2:
				return "En construcción";
			case 1:
				return "En planos";
			default:
				return "";
		}
	};

	const fillFields = () => {
		form.setFieldsValue({
			projectName: project._payload[0]["name"],
			saleForm: parseInt(project._payload[0]["sale_type_id"]),
			fiduciaryCode: project._payload[0]["fiduciary_code"] ?? 0,
			state: project._payload[0]["state"],
			stateProject: getProjectState(project._payload[0]["project_state_id"]),
			zone: project._payload[0]["area"]==="null"?null:project._payload[0]["area"],
			descriptionProject:
				project._payload[0]["description"] === "null"
					? null
					: project._payload[0]["description"],
			descriptionPoint:
				project._payload[0]["description_point"] === "null"
					? null
					: project._payload[0]["description_point"],
			projectType: project._payload[0]["project_type_id"],
			classification: project._payload[0]["classification_id"],
			totalUnits: project._payload[0]["total_units"],
			numberFloors: project._payload[0]["floors_number"] ?? 0,
			simpleParkingLots: project._payload[0]["simple_parking_number"] ?? 0,
			doubleLineaParkingLot: project._payload[0]["double_parking_number"] ?? 0,
			parkingLotVisitors:project._payload[0]["visitor_parking_number"] > 0 ? "1" : "2",
			quantity: project._payload[0]["visitor_parking_number"],
			startDateSale: moment(project._payload[0]["sale_start_date"]),
			estimatedDateStartConstruction: moment(project._payload[0]["start_build_date"]),
			estimatedDeliveryDate: moment(project._payload[0]["estimated_delivery_date"]),
			dateMaximumReforms: moment(project._payload[0]["maximum_reforms_date"]),
			uploadImages: buildImages(project._payload[0]["project_attachment"]),
			promoterImages: buildPrometerImages(project._payload[0]["project_attachment"]),
			uploadDocuments: buildDocuments(project._payload[0]["project_attachment"]),
			country: countries,
			department: getCurrentDepartament(),
			city: getCurrentCity(),
		});
	};

	const getCurrentDepartament = () => {
		if (project._payload[0]["location_id"] != null) {
			currentDeparment = colombiaJSON.find((element) =>
				element.ciudades.find(
					(item) => item.id === project._payload[0]["location_id"]
				)
			);
			setMunicipalities(currentDeparment?.ciudades);
			return currentDeparment?.id;
		}
	};

	const getCurrentCity = () => {
		if (project._payload[0]["location_id"] != null) {
			currentCity = currentDeparment?.ciudades.find(
				(item) => item.id === project._payload[0]["location_id"]
			);
			return currentDeparment?.ciudades.indexOf(currentCity);
		}
	};

	const buildImages = (array) => {
		let newImages = [];
		if (array) {
			array.forEach((item) => {
				if (
					!item.path.includes("promoters") &&
					!item.path.includes("documents")
				) {
					const newObject = {
						uid: `-xxx${item.id}`,
						id: item.id,
						name: item.name,
						status: "done",
						url: item.path,
					};
					newImages.push(newObject);
				}
			});
		} else {
			newImages.push({});
		}
		return newImages;
	};

	const buildPrometerImages = (array) => {
		let newPrometerImages = [];
		if (array) {
			array.forEach((item) => {
				if (item.path.includes("promoters")) {
					const newObject = {
						uid: `-xxx${item.id}`,
						id: item.id,
						name: item.name,
						status: "done",
						url: item.path,
					};
					newPrometerImages.push(newObject);
				}
			});
		} else {
			newPrometerImages.push({});
		}
		return newPrometerImages;
	};

	const buildDocuments = (array) => {
		let newDocuments = [];
		if (array) {
			array.forEach((item) => {
				if (item.path.includes("documents")) {
					const newObject = {
						uid: `-xxx${item.id}`,
						id: item.id,
						name: item.name,
						status: "done",
						url: item.path,
					};
					newDocuments.push(newObject);
				}
			});
		} else {
			newDocuments.push({});
		}

		return newDocuments;
	};

	const handleSubmit = async () => {
		let values = await form.validateFields();
		let fData = new FormData();
		fData.append("name", values["projectName"]);
		fData.append("sale_type", values["saleForm"]);
		fData.append("fiduciary_code", values["fiduciaryCode"]);
		fData.append("state", values.state);
		fData.append("project_state", 1);
		fData.append("location", location);
		fData.append("area", values["zone"]);
		fData.append("description", values["descriptionProject"]);
		fData.append("description_point", values["descriptionPoint"]);
		fData.append("project_type", values["projectType"]);
		fData.append("classification", values["classification"]);
		fData.append("total_units", values["totalUnits"]);
		fData.append("floors_number", values["numberFloors"]);
		fData.append("simple_parking_number", values.simpleParkingLots ?? 0);
		fData.append("double_parking_number", values.doubleLineaParkingLot ?? '');
		fData.append("visitor_parking_number", values.quantity ?? 0);
		fData.append(
			"sale_start_date",
			values["startDateSale"].format("YYYY-MM-DD")
		);
		fData.append(
			"start_build_date",
			values["estimatedDateStartConstruction"].format("YYYY-MM-DD")
		);
		fData.append(
			"estimated_delivery_date",
			values["estimatedDeliveryDate"].format("YYYY-MM-DD")
		);
		fData.append(
			"maximum_reforms_date",
			values["dateMaximumReforms"].format("YYYY-MM-DD")
		);

		for (let i = 0; i < values["uploadImages"].length; i++) {
			if (values["uploadImages"][i].hasOwnProperty("originFileObj")) {
				fData.append(
					`images[]`,
					values["uploadImages"][i].originFileObj,
					values["uploadImages"][i].name
				);
			}
		}
		for (let i = 0; i < values["promoterImages"].length; i++) {
			if (values["promoterImages"][i].hasOwnProperty("originFileObj")) {
				fData.append(
					`promoters[]`,
					values["promoterImages"][i].originFileObj,
					values["promoterImages"][i].name
				);
			}
		}
		for (let i = 0; i < values["uploadDocuments"].length; i++) {
			if (values["uploadDocuments"][i].hasOwnProperty("originFileObj")) {
				fData.append(
					`documents[]`,
					values["uploadDocuments"][i].originFileObj,
					values["uploadDocuments"][i].name
				);
			}
		}
		for (let i = 0; i < logo.length; i++) {
			if (logo[i].hasOwnProperty("originFileObj")) {
				fData.append(`logo`, logo[i].originFileObj, logo[i].name);
			}
		}
		if (projectAttachmentRemoved !== []) {
			projectAttachmentRemoved.forEach((value) => {
				fData.append(`attachmenDelete[]`, value);
			});
		}

		dispatch(
			updateProjectByIdPost(fData, projectId, (response) => {
				if (response.error)
					errorGlobal(
						"¡UPS! ha ocurrido un error mientras se actualizaba"
					);
				else {
					success("Actualizado exitosamente");
					dispatch(getProjectById(projectId));
				}
			})
		);
	};

	const onRemove = (file) => {
		setLogo([]);
		if (file.id !== undefined) {
			projectAttachmentRemoved.push(file.id);
		}
	};

	const onChange = ({fileList: newFileList}) => {
		setLogo(newFileList);
	};

	const normFile = (e) => {
		if (Array.isArray(e)) {
			return e;
		}

		return e && e.fileList;
	};

	return (
		<>
			<Card title="Información básica" style={{borderColor: "black"}}>
				<Form
					layout="vertical"
					onFinish={handleSubmit}
					initialValues={{state: true}}
					name="formBasicInformationProject"
					form={form}
					style={{
						marginLeft: "27px",
						marginRight: "27px",
						marginTop: "27px",
					}}
				>
					<Row gutter={16} style={{marginLeft: "27px"}}>
						<Col xs={5} sm={24} md={24} lg={24} xl={24}>
							<Divider orientation="left">
								Información General
							</Divider>
							<Row gutter={16}>
								<Col xs={5} sm={24} md={8} lg={8} xl={6}>
									<Row>
										<Col span={24} offset={1}>
											<Form.Item
												style={{
													paddingTop: "60px",
													paddingLeft: "50px",
												}}
												valuePropName="fileList"
												getValueFromEvent={normFile}
											>
												<Upload
													action={RUTA_API_MULTIMEDIA}
													listType="picture-card"
													accept="image/*"
													name={"logo"}
													fileList={logo}
													onChange={onChange}
													onRemove={onRemove}
												>
													{logo.length < 1
														? "+ Upload"
														: ""}
												</Upload>
											</Form.Item>
										</Col>
									</Row>
								</Col>
								<Col xs={5} sm={24} md={8} lg={8} xl={8}>
									<Form.Item
										label="Nombre del proyecto"
										name="projectName"
										rules={[
											{
												min: 3,
												message:
													"El nombre del proyecto debe tener al menos 3 caracteres.",
											},
											{
												required: true,
												message:
													"El nombre del proyecto es requerido.",
											},
										]}
									>
										<Input/>
									</Form.Item>
									<Form.Item
										label="Descripción"
										name={"descriptionProject"}
									>
										<Input.TextArea rows={5}/>
									</Form.Item>
								</Col>
								<Col xs={5} sm={24} md={8} lg={8} xl={8}>
									<Form.Item
										label="Forma de venta"
										name="saleForm"
										rules={[
											{
												required: true,
												message:
													"La forma de venta es requerida.",
											},
										]}
									>
										<Select
											onChange={setFormSale}
											defaultValue=""
										>
											<Select.Option value="">
												Seleccione...
											</Select.Option>
											{typesSale.map((value) => {
												return (
													<Select.Option
														key={value.id}
														value={value.id}
													>
														{value.name}
													</Select.Option>
												);
											})}
										</Select>
									</Form.Item>
									<Form.Item
										label="Código fiduciario"
										name="fiduciaryCode"
										rules={[
											{
												validator: async (_, value) => {
													if (value < 99 && formSale != 1) {
														return Promise.reject(
															new Error(
																"Código fiduciario debe tener minímo 3 dígitos."
															)
														);
													}
												},
											},
										]}
									>
										<InputNumber
											disabled={active}
											style={{width: "100%"}}
										/>
									</Form.Item>
									<Form.Item
										label="Estado del proyecto"
										name="stateProject"
									>
										<Input disabled/>
									</Form.Item>
								</Col>
							</Row>
							<Row gutter={16}>
								<Col xs={5} sm={24} md={12} lg={12} xl={12}>
									<Row gutter={16}>
										<Col
											xs={5}
											sm={24}
											md={12}
											lg={12}
											xl={12}
										>
											<Form.Item
												label="País"
												name="country"
											>
												<Select
													defaultValue="-"
													style={{width: "100%"}}
													onChange={onChangeCountry}
												>
													<Select.Option value="-">
														Seleccionar...
													</Select.Option>
													{land.map((item, index) => {
														return (
															<Select.Option
																key={index}
																value={index}
															>
																{item}
															</Select.Option>
														);
													})}
												</Select>
											</Form.Item>
										</Col>
										<Col
											xs={5}
											sm={24}
											md={12}
											lg={12}
											xl={12}
										>
											<Form.Item
												label="Departamento"
												name="department"
											>
												<Select
													defaultValue="-"
													onChange={
														onChangeDepartment
													}
													style={{width: "100%"}}
													disabled={country === "-"}
												>
													<Select.Option value="-">
														Seleccionar...
													</Select.Option>
													{departments.map(
														(item, index) => {
															return (
																<Select.Option
																	key={index}
																	value={
																		index
																	}
																>
																	{item}
																</Select.Option>
															);
														}
													)}
												</Select>
											</Form.Item>
										</Col>
									</Row>
									<Row gutter={16}>
										<Col
											xs={5}
											sm={24}
											md={12}
											lg={12}
											xl={12}
										>
											<Form.Item
												label="Ciudad"
												name="city"
												rules={[
													{
														required: true,
														message:
															"La ciudad es obligatoria.",
													},
												]
												}
											>
												<Select
													defaultValue="-"
													onChange={
														onChangeMunicipality
													}
													disabled={
														form.getFieldValue(
															"department"
														) === "" ||
														form.getFieldValue(
															"department"
														) === undefined ||
														form.getFieldValue(
															"department"
														) === "-"
													}
													style={{width: "100%"}}
												>
													<Select.Option value="-">
														Seleccionar...
													</Select.Option>
													{municipalities.map(
														(item, index) => {
															return (
																<Select.Option
																	key={index}
																	value={
																		index
																	}
																>
																	{item.name}
																</Select.Option>
															);
														}
													)}
												</Select>
											</Form.Item>
										</Col>
										<Col
											xs={5}
											sm={24}
											md={12}
											lg={12}
											xl={12}
										>
											<Form.Item name="zone" label="Zona">
												<Input/>
											</Form.Item>
										</Col>
									</Row>
									<Row gutter={16}>
										<Col
											xs={5}
											sm={24}
											md={24}
											lg={24}
											xl={24}
										>
											<Form.Item
												name="descriptionPoint"
												label="Descripción de punto"
											>
												<Input.TextArea rows={3}/>
											</Form.Item>
										</Col>
									</Row>
								</Col>
								<Col xs={5} sm={24} md={12} lg={12} xl={12}>
									<MapQuest
										height="43.5vh"
										width="76vh"
										center={[lat, lng]}
										tileLayer={"map"}
										zoom={4.7}
										apiKey={
											process.env.REACT_APP_API_KEY_MAP
										}
									/>
								</Col>
							</Row>
							<br/>
							<Row gutter={20}>
								<Col xs={5} sm={24} md={5} lg={5} xl={8}>
									<Form.Item
										name="projectType"
										label="Tipos de proyectos"
										rules={[
											{
												required: true,
												message:
													"El tipo de proyecto es requerido.",
											},
										]}
									>
										<Select defaultValue="">
											<Select.Option value="">
												Seleccionar...
											</Select.Option>
											{projectTypes.map((value) => {
												return (
													<Select.Option
														key={value.id}
														value={value.id}
													>
														{value.name}
													</Select.Option>
												);
											})}
										</Select>
									</Form.Item>
								</Col>
								<Col xs={5} sm={24} md={10} lg={10} xl={8}>
									<Form.Item
										label="Clasificación"
										name="classification"
										rules={[
											{
												required: true,
												message:
													"La clasificación es requerida.",
											},
										]}
									>
										<Select defaultValue="">
											<Select.Option value="">
												Seleccionar...
											</Select.Option>
											{projectClassifications.map(
												(value) => {
													return (
														<Select.Option
															key={value.id}
															value={value.id}
														>
															{value.name}
														</Select.Option>
													);
												}
											)}
										</Select>
									</Form.Item>
								</Col>
								<Col xs={5} sm={24} md={3} lg={3} xl={7}>
									<Form.Item
										label="Total de unidades"
										name="totalUnits"
										rules={[
											{
												required: true}
										]}
									>
										<InputNumber
											min={0}
											style={{width: "100% "}}
										/>
									</Form.Item>
								</Col>
							</Row>
							<Row gutter={16}>
								<Col xs={5} sm={24} md={3} lg={3} xl={8}>
									<Form.Item
										label="Número de pisos"
										name="numberFloors"
									>
										<InputNumber
											min={0}
											style={{width: "100%"}}
										/>
									</Form.Item>
								</Col>
								<Col xs={5} sm={24} md={4} lg={4} xl={8}>
									<Form.Item
										label="Parqueaderos sencillos"
										name="simpleParkingLots"
									>
										<InputNumber
											defaultValue={0}
											min={0}
											style={{width: "100%"}}
										/>
									</Form.Item>
								</Col>
								<Col xs={5} sm={24} md={5} lg={5} xl={7}>
									<Form.Item
										label="Parqueaderos doble lineal"
										name="doubleLineaParkingLot"	
									>
										<InputNumber
											defaultValue={0}
											min={0}
											style={{width: "100%"}}
										/>
									</Form.Item>
								</Col>
							</Row>
							<Row gutter={16}>
								<Col xs={5} sm={24} md={6} lg={6} xl={8}>
									<Form.Item
										label="Parqueadero de visitantes"
										name="parkingLotVisitors"
									>
										<Select
											defaultValue="2"
											onChange={setQuantityDisabled}
										>
											<Select.Option value="1">
												SI
											</Select.Option>
											<Select.Option value="2">
												NO
											</Select.Option>
										</Select>
									</Form.Item>
								</Col>
								<Col xs={5} sm={24} md={4} lg={4} xl={7}>
									<Form.Item
										label="Cantidad"
										name="quantity"
										rules={[
											{
												type: "number",
												min: 0,
												max: 999,
												message:
													"Cantidad máxima de parqueaderos excedida.",
											},
										]}
									>
										<InputNumber
											defaultValue={0}
											disabled={quantityDisabled === "2"}
											style={{width: "100%"}}
										/>
									</Form.Item>
								</Col>
								<Col xs={5} sm={24} md={2} lg={2} xl={4}>
									<Form.Item
										valuePropName="checked"
										name="state"
										label="Estado"
									>
										<Checkbox>Activo</Checkbox>
									</Form.Item>
								</Col>
							</Row>
							<Divider orientation="left">
								Línea de Tiempo del Proyecto
							</Divider>
							<Row gutter={16}>
								<Col xs={5} sm={24} md={5} lg={5} xl={11}>
									<Form.Item
										name="startDateSale"
										label="Fecha de inicio de la venta"
										rules={[
											{
												required: true,
												message: "Fecha requerida.",
											},
										]}
									>
										<DatePicker
											style={{width: "100%"}}
										/>
									</Form.Item>
								</Col>
								<Col xs={5} sm={24} md={7} lg={7} xl={11}>
									<Form.Item
										label="Fecha estimada de inicio de construcción"
										name="estimatedDateStartConstruction"
										rules={[
											{
												required: true,
												message: "Fecha Requerida",
											},
										]}
									>
										<DatePicker
											onChange={
												captureEstimatedDateStartConstruction
											}
											style={{width: "100%"}}
										/>
									</Form.Item>
								</Col>
							</Row>
							<Row gutter={16}>
								<Col xs={5} sm={24} md={6} lg={6} xl={11}>
									<Form.Item
										label="Fecha estimada de entrega"
										name="estimatedDeliveryDate"
										rules={[
											{
												required: true,
												message: "Fecha Requerida",
											},
										]}
									>
										<DatePicker
											onChange={
												captureEstimatedDeliveryDate
											}
											style={{width: "100%"}}
										/>
									</Form.Item>
								</Col>
								<Col xs={5} sm={24} md={6} lg={6} xl={11}>
									<Form.Item
										label="Fecha máxima para reformas"
										name="dateMaximumReforms"
										rules={[
											{
												required: true,
												message: "Fecha Requerida",
											},
										]}
									>
										<DatePicker style={{width: "100%"}}/>
									</Form.Item>
								</Col>
							</Row>
							<Row gutter={16}>
								<Col xs={5} sm={24} md={8} lg={8} xl={20}>
									<Divider orientation="left">
										Documentos
									</Divider>
									<Form.Item
										valuePropName="fileList"
										name="uploadDocuments"
										getValueFromEvent={normFile}
									>
										<Upload
											action={RUTA_API_MULTIMEDIA}
											accept={VALID_DOCUMENT_UPLOAD}
											multiple
											name="uploadDocuments"
											maxCount={100}
											onRemove={onRemove}
										>
											<Button icon={<UploadOutlined/>}>
												Subir documentos
											</Button>
										</Upload>
									</Form.Item>
								</Col>
							</Row>
							<Row gutter={16}>
								<Col xs={24} sm={24} md={8} lg={8} xl={12}>
									<Divider orientation="left">
										Imágenes
									</Divider>
									<Form.Item
										name="uploadImages"
										valuePropName="fileList"
										getValueFromEvent={normFile}
									>
										<Upload
											action={RUTA_API_MULTIMEDIA}
											listType="picture-card"
											accept="image/*"
											multiple
											name="uploadImages"
											maxCount={100}
											onRemove={onRemove}
										>
											+ Subir
										</Upload>
										{/* <Modal
											visible={previewVisible}
											title={previewTitle}
											footer={null}
											onCancel={handleCancel}
										>
											<img
												alt="example"
												style={{ width: "100%" }}
												src={previewImage}
											/>
										</Modal> */}
									</Form.Item>
								</Col>
								<Col>
									<Divider orientation="left">
										Imágenes de promotores
									</Divider>
									<Form.Item
										valuePropName="fileList"
										name="promoterImages"
										getValueFromEvent={normFile}
									>
										<Upload
											action={RUTA_API_MULTIMEDIA}
											listType="picture-card"
											accept="image/*"
											multiple
											name="promoterImages"
											maxCount={100}
											onRemove={onRemove}
										>
											+ subir
										</Upload>
										{/* <Modal
											visible={previewVisible}
											title={previewTitle}
											footer={null}
											onCancel={handleCancel}
										>
											<img
												alt="example"
												style={{ width: "100%" }}
												src={previewImage}
											/>
										</Modal> */}
									</Form.Item>
								</Col>
							</Row>
							<Divider/>
							<Form.Item
								wrapperCol={{
									span: 12,
									offset: 21,
								}}
							>
								<Button
									disabled={stateDisable}
									size="long"
									type="primary"
									htmlType="submit"
								>
									Guardar
								</Button>
							</Form.Item>
						</Col>
					</Row>
				</Form>
			</Card>
		</>
	);
};

export default EditProject;
