import React, { useEffect, useState } from "react";
import { FilterOutlined } from '@ant-design/icons';
import { connect, useDispatch, useSelector } from 'react-redux';
import { dateFormat} from "../../../handle/HandleDatePicker/HandleDate";
import { DownloadDocument } from "../../../handle/Multimedia/DownloadDocument";
import {fetchAllBuilder} from "../../../../redux/actions/Project/BuilderAction";
import { Card, Col, Row, Form, Select, Button, DatePicker, Table, Popover } from 'antd';
import {getAllCompetingProjects} from '../../../../redux/actions/Project/ProjectsAction';
import {getAllProjectStates} from "../../../../redux/actions/Project/ProjectStateAction";
import {fetchAllActivatedUsers} from '../../../../redux/actions/Configuration/usersAction';
import { fetchAllPricesRangeActivated } from "../../../../redux/actions/Configuration/PricesRangeAction";
import {getCompetingReportExcel, getCompetingReportFilter, getCompetingReportPDF} from "../../../../redux/actions/Project/ControlPowerAction";
import {errorGlobal} from "../../../handle/Notification/Notification";
import {validateMessages} from "../../../handle/ValidateMessages/validateMenssages";

const columns = [
	{
		title: "#",
		width: 5,
		dataIndex: "id",
		key: "name",

	},
	{
		title: "Nombre",
		width: 20,
		dataIndex: "name",
		key: "",

	},
	{
		title: "Dirección",
		width: 40,
		dataIndex: "address",
		key: "",
	},
	{
		title: "Constructora",
		width: 40,
		dataIndex: "constructionCompanyName",
		key: "",
	},
	{
		title: "Comercializadora",
		width: 50,
		dataIndex: "marketer_company",
		key: "",
	},

	{
		title: "Tipo de Proyecto",
		width: 20,
		dataIndex: "projectTypeName",
		key: "",
	},

	{
		title: "Habitaciones",
		width: 25,
		dataIndex: "bathrooms_number",
		key: "",

	},

	{
		title: "Baños",
		width: 20,
		dataIndex: "rooms_number",
		key: "",
	},

	{
		title: "Rango de Precios",
		width: 20,
		dataIndex: "projectPriceRangeName",
		key: "",
	},

	{
		title: "Estado",
		width: 20,
		dataIndex: "projectStateName",
		key: "",
	},

	{
		title: "Fecha de Lanzamiento",
		width: 20,
		dataIndex: "release_date",
		key: "",
	},

	{
		title: "Fecha de Construcción",
		width: 23,
		dataIndex: "building_date",
		key: "",
	},

	{
		title: "Fecha de Entrega",
		width: 20,
		dataIndex: "delivery_date",
		key: "",
	},

	{
		title: "Total Unidades",
		width: 20,
		dataIndex: "total_units",
		key: "",
	},
	{
		title: "Unidades Vendidas",
		width: 20,
		dataIndex: "sold_units",
		key: "",

	},
	{
		title: "Velocidad Ventas (mes)",
		width: 20,
		dataIndex: "saleSpeed",
		key: "",
	},
	{
		title: "% Unidades Vendidas",
		width: 20,
		dataIndex: "percentageSold",
		key: "",
	},
	{
		title: "Precio m2 Lanzamiento",
		width: 20,
		dataIndex: "release_meter_price",
		key: "",
	},
	{
		title: "Precio m2 Actual",
		width: 20,
		dataIndex: "current_meter_price",
		key: "",
	},
	{
		title: "Fuente Nombre/Teléfono",
		width: 40,
		dataIndex: "namePhone",
		key: "",
	},
	{
		title: "Especificaciones de Unidades",
		width: 50,
		dataIndex: "specsUnits",
		key: "",
	},
	{
		title: "Descripción de Unidades",
		width: 50,
		dataIndex: "descriptionsUnits",
		key: "",
	},
	{
		title: "Observaciones",
		width: 50,
		dataIndex: "observations",
		key: "",
	},
	{
		title: "Fecha Creación",
		width: 20,
		dataIndex: "dateInit",
		key: "",
	}
];

const CompetitionReport = () => {

	const [startDate, setStartDate] = useState("all")
	const [endDate, setEndDate] = useState("all")
	const [idProjectState, setIdProjectState] = useState("all")
	const [idCompeting, setIdCompeting] = useState("all");
	const [idBuilder, setIdBuilder] = useState("all");
	const [idUser, setIdUser] = useState("all");
	const [idPriceRange, setIdPriceRange] = useState("all");
	const dataCompetitionReport = { "startDate" :startDate, endDate, idProjectState, idCompeting, idBuilder, idUser, idPriceRange }

	const dispatch = useDispatch();

	useEffect(() => {
		dispatch(fetchAllActivatedUsers());
		dispatch(getAllCompetingProjects());
		dispatch(fetchAllBuilder());
		dispatch(fetchAllPricesRangeActivated());
		dispatch(getAllProjectStates());
	}, [dispatch]);


	const selectUser = useSelector(state => state?.users);
	const selectCompetition = useSelector(state => state?.controlPower);
	const selectBuilder = useSelector(state => state?.builder);
	const selectPrices = useSelector(state => state?.pricesRange);
	const selectProjectState = useSelector(state => state?.projecState);

	const [dataReport,setContent ] = useState([]);
	const [visibleButtonDownload, setVisibleButtonDownload] = useState(false);
	const [visibleTable, setVisibleTable] = useState(false);

	const exportExcel = () => {
		dispatch(
			getCompetingReportExcel( dataCompetitionReport, async (response) => {
				const res = await response;
				DownloadDocument(res._payload.path);
			}
		));
	}
	
	const exportPdf = () => {
		dispatch(
			getCompetingReportPDF(dataCompetitionReport, async (response) => {
				const res = await response;
				if (res._payload.path !== '') DownloadDocument(res._payload.path);
			}
		));
	}
	
	const filesType = (
		<div>
			<a style={{ marginRight: 21 }} onClick={exportExcel}>
				Excel
			</a>
			<a onClick={exportPdf}>PDF</a>
		</div>
	);
	
	const onChangeStartDate = async (response) => {

		if (response != null) {
			setStartDate(response.format(dateFormat));
		}
		else{
			setStartDate("all");
		}
	}

	const onChangeEndDate = async (response) => {
		if (response != null) {
			setEndDate(response.format(dateFormat));
		}
		else{
			setEndDate("all");
		}
	}

	const onFinish = async () => {

		const postProcess = async (response) => {
		  await setContent(response._payload);
			if (response.error) {
				errorGlobal(response.message);
				setVisibleButtonDownload(false);
				setVisibleTable(false);
			}else{
				setVisibleButtonDownload(true);
				setVisibleTable(true);
			}
		}
		dispatch(getCompetingReportFilter(dataCompetitionReport, postProcess));
	};
	
	return (
		<Card title="Informe de Competencias">
			<Form layout="vertical" onFinish={onFinish} validateMessages={validateMessages}>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item label="Nombre:" name="Nombre">
							<Select defaultValue="Todos" onChange={setIdCompeting}>
								<Select.Option value="all">Todos</Select.Option>
								{
									!Array.isArray(selectCompetition) && selectCompetition._payload?.map(item => (
										<Select.Option key={item?.id+item?.name+item?.id} value={item.id}>{` ${item.name} `}</Select.Option>
									))
								}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item label="Constructor:" name="Constructor">
							<Select defaultValue="Todos"
											onChange={setIdBuilder}>
								<Select.Option value="all">Todos</Select.Option>
								{
									!Array.isArray(selectBuilder) && selectBuilder._payload.map(item => (
										<Select.Option key={item?.id+item?.name+item?.id} value={item.id}>{` ${item.name} `}</Select.Option>
									))
								}
							</Select>
						</Form.Item>
					</Col>
				</Row>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item label="Dirección:" name="Dirección">
							<Select
								defaultValue="Todos"
								showSearch
								style={{ width: '100%' }}
								optionFilterProp="children"
								onChange={setIdCompeting}
								filterOption={(input, option) =>
									option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
								}
							>
								<Select.Option value="all">Todos</Select.Option>
								{
									!Array.isArray(selectCompetition) && selectCompetition._payload.map(item => (
										<Select.Option key={item?.id+item?.name+item?.id} value={item.id}>{` ${item.address} `}</Select.Option>

									))
								}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item label="Rango de precios:" name="Rango de precios">
							<Select defaultValue="Todos"
											onChange={setIdPriceRange}>
								<Select.Option value="all">Todos</Select.Option>
								{
									!Array.isArray(selectPrices) && selectPrices._payload?.map(item => (
										<Select.Option key={item?.id+item?.name+item?.id} value={item.id}>{` ${item.name} `}</Select.Option>

									))
								}
							</Select>
						</Form.Item>
					</Col>
				</Row>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item 
							label="Fecha de Inicio:" name="Fecha de Inicio"
						>
							<DatePicker
								value={startDate}
								onChange={(value) => onChangeStartDate(value)}
								style={{ width: "100%" }}
							/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item 
							label="Fecha Fin" name="Fecha Fin:"
						>
							<DatePicker
								value={endDate}
								onChange={(value) => onChangeEndDate(value)}
								style={{ width: "100%" }} 
							/>
						</Form.Item>
					</Col>
				</Row>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item label="Estado del Proyecto:" name="Estado del Proyecto">
							<Select
								defaultValue="Todos"
								showSearch
								style={{ width: '100%' }}
								optionFilterProp="children"
								onChange={setIdProjectState}
								filterOption={(input, option) =>
									option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
								}
							>
								<Select.Option value="all">Todos</Select.Option>
								{
									!Array.isArray(selectProjectState) && selectProjectState._payload.map(item => (
										<Select.Option key={item?.id+item?.name+item?.id} value={item.id}>{` ${item.name} `}</Select.Option>

									))

								}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item label="Usuario:" name="Usuario">
							<Select
								defaultValue="Todos"
								showSearch
								style={{ width: '100%' }}
								optionFilterProp="children"
								onChange={setIdUser}
								filterOption={(input, option) =>
									option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
								}
							>
								<Select.Option value="">Seleccione...</Select.Option>
								{
									!Array.isArray(selectUser) && selectUser._payload.map(item => (
										<Select.Option key={item.id} value={item.id}>{` ${item.name} `}</Select.Option>
									))

								}
							</Select>
						</Form.Item>
					</Col>
				</Row>
				<Row>
				<Col xs={12} sm={12} md={12} lg={12} xl={12} />
					<Col xs={4} sm={4} md={4} lg={4} xl={4}>
						<Form.Item>
							<Button icon={<FilterOutlined />} type="primary" htmlType="submit">
								Filtrar
							</Button>
						</Form.Item>
					</Col>
				</Row>
				<Row>
				<Col xs={20} sm={20} md={20} lg={20} xl={22} />
					<Col xs={2} sm={2} md={2} lg={2} xl={2}>
						{visibleButtonDownload ? (<>
								<Popover
								content={filesType}
								trigger="click"
							>
									<Button  style={{ backgroundColor: "#5cb85c", borderColor: "#5cb85c",color:'white' }} >
										Exportar
									</Button>
							</Popover>		
								
						</>
						
							):
							// eslint-disable-next-line jsx-a11y/heading-has-content
							(<h1/>)
						}

					</Col>
				</Row>
			</Form>
			<br/>
			{visibleTable ?
				(
					<Table
						columns={columns}
						dataSource={dataReport}
						scroll={{ x: 3200, y: 300 }}
						size='small'
						bordered={true}
					/>) :
				// eslint-disable-next-line jsx-a11y/heading-has-content
				(<h1/>)
			}
		</Card>
	);
}
export default connect(null)(CompetitionReport);