import React, {useContext} from "react";
import {Col, Form, Row} from "antd";
import DescriptionOfProjectValues from "../../../../Quotes/Components/editQuote/DescriptionOfProjectValues";
import {SalesMadeContext} from "../context/SalesMadeContext";

export const TotalInformationToPay = () => {
	
	const { paymentMethodProposedByTheProject, paymentMethodProposedByTheClient} = useContext(SalesMadeContext);
	
	return(
		<Form.Item>
			<Row gutter={2}>
				<Col span={11}>
					<DescriptionOfProjectValues
						paymentMethodProposedByTheProject={paymentMethodProposedByTheProject}
					/>
				</Col>
				<Col span={1}/>
				<Col span={11}>
					<DescriptionOfProjectValues
						paymentMethodProposedByTheProject={paymentMethodProposedByTheClient}
					/>
				</Col>
			</Row>
		</Form.Item>
	)
}