import * as constants from "../../../constants";

export const getActivitiesByAssessor = (id, postProcessSuccess) => ({
	type: constants.API,
	payload: {
		method: "GET",
		url: `${constants.RUTA}/activity/get_activities_by_assessor/${id}`,
		postProcessSuccess: postProcessSuccess,
	},
});

