import React, {useEffect} from "react"
import {Card, Col, Descriptions, Row} from "antd";
import {FormatterPeso} from "../Utilitis/Utilitis";
import TablePagoCliente from "../propertyData/TablePagoCliente";
import TableMetodosPagos from "../propertyData/TableMetodosPagos";

const FormOfPaymentsTable = ({modified_values,calculatePor,financedFee,propertyData,valorQuotes,formOfPaymentProposedByTheProject,
	                             finalValueProposedByTheClient, finalValuesProposedByTheProject,paymentMethodProposedByTheClient,
	                             calculatePresentValueByTheClient}) => {
	const decideTheColor = () => {
		if (finalValueProposedByTheClient.financialDiscountOrIncrease === 0){
			return (<Descriptions.Item label="Descuento financiero"> {FormatterPeso.format(0)} </Descriptions.Item>)
		}else if (finalValueProposedByTheClient.financialDiscountOrIncrease < 0){
			return (<Descriptions.Item className={"valor2"} label="Incremento financiero"> {FormatterPeso.format((-1)*finalValueProposedByTheClient.financialDiscountOrIncrease)} </Descriptions.Item>)
		}else if (finalValueProposedByTheClient.financialDiscountOrIncrease > 0){
			return (<Descriptions.Item className="valor3" label="Descuento financiero"> {finalValueProposedByTheClient.financialDiscountOrIncrease.toFixed(2)==0.00?FormatterPeso.format(0):FormatterPeso.format((-1)*finalValueProposedByTheClient.financialDiscountOrIncrease)} </Descriptions.Item>)
		}
	}
	
	return <>
		<Row gutter={2}>
			<Col span={12}>
				<Descriptions
					title="Forma de pago propuesta por el proyecto"
					column={{ xxl: 2, xl: 1, lg: 1, md: 1, sm: 1, xs: 1 }}
				/>
				<Card style={{ margin: 10 }}>
					<TableMetodosPagos amountOfFees={formOfPaymentProposedByTheProject} />
				</Card>
			</Col>
			<Col span={12}>
				<Descriptions
					title="Foma de pago propuesta por el cliente"
					column={{ xxl: 1, xl: 1, lg: 1, md: 1, sm: 1, xs: 1 }}
				/>
				<Card style={{ margin: 10 }}>
					<TablePagoCliente
						dataTable={paymentMethodProposedByTheClient}
						modified_values={modified_values}
						calculatePor={calculatePor}
						valueShare={financedFee}
						valueUnit={propertyData.valorTotal}
						valueMonth={valorQuotes}
						calculatePresentValueByTheClient={calculatePresentValueByTheClient}
						trueUnitValue={finalValueProposedByTheClient.valueOfTheUnitProposed}
					/>
				</Card>
			</Col>
		</Row>
		<Row gutter={2}>
			<Col span={12}>
				<Descriptions
					style={{ marginTop: 15, width: '80%' }}
					size="small"
					bordered
					column={{ xxl: 1, xl: 1, lg: 1, md: 1, sm: 1, xs: 1 }}
				>
					<Descriptions.Item label="Valor de la unidad">
						{FormatterPeso.format(finalValuesProposedByTheProject.valueOfTheUnitProposedByTheProject)}
					</Descriptions.Item>
					<Descriptions.Item label="Valor presente">
						{FormatterPeso.format(finalValuesProposedByTheProject.presentValueGivenByTheProject)}
					</Descriptions.Item>
				</Descriptions>
			</Col>
			<Col span={12}>
				<Descriptions
					style={{ marginTop: 15, width: '80%' }}
					size="small"
					bordered
					column={{ xxl: 1, xl: 1, lg: 1, md: 1, sm: 1, xs: 1 }}
				>
					<Descriptions.Item label="Valor de la unidad">
						{FormatterPeso.format(finalValueProposedByTheClient.valueOfTheUnitProposed)}
					</Descriptions.Item>
					<Descriptions.Item label="Valor presente">
						{FormatterPeso.format(finalValueProposedByTheClient.presentValueGiven)}
					</Descriptions.Item>
					{ decideTheColor() }
					<Descriptions.Item label="Descuento acabados">
						{!propertyData.check
							? FormatterPeso.format(propertyData.finishKitValue)
							: FormatterPeso.format(0)}
					</Descriptions.Item>
					{propertyData.valorCupon !== 0 ? (
						<Descriptions.Item label="Descuento cupones">
							{FormatterPeso.format(propertyData.valorCupon)}
						</Descriptions.Item>
					) : null}
				</Descriptions>
			</Col>
		</Row>
	</>
}

export default FormOfPaymentsTable