import _ from "lodash";
import * as constants from "../../../constants";


const clearFlags = {
    isFetchingAllPosSale:           false,
    errorAllPosSale:                false,
    isFetchingAllPosSaleBySaleId:   false,
    errorAllPosSaleBySaleId:        false,
    isAddingPosSale:                false,
    errorAddPosSale:                false,
    isUpdatingPosSale:              false,
    errorUpdatingPosSale:           false,
    isDeletingPosSale:              false,
    errorDeletingPosSale:           false,
}

const initialState = {
    project: {
        id: undefined,
        name: undefined,
    },
    customer: {
        name: undefined,
        id: undefined
    },
    unit: {
        label: undefined,
        id: undefined
    },
    creator_user: {
        id: undefined,
        name: undefined
    },
    sale: {
        id: undefined,
    },
    body_description: null,
    days_passed: null,
    date_pos_sale: null,
    pos_sale_requests: [],
    all_pos_sale_requests: [],

    ...clearFlags


}


export default function posSaleReducer(state = initialState, action) {
    switch (action.type) {
        case constants.START_FETCHING_ALL_POS_SALE:
            return { ...state, ...clearFlags, isFetchingAllPosSale: true, };
        case constants.GET_ALL_POS_SALE:
            return { ...state,  all_pos_sale_requests: action.payload._payload, isFetchingAllPosSale: false, };
        case constants.ERROR_FETCHING_ALL_POS_SALE:
            return { ...state, isFetchingAllPosSale:  false, errorAllPosSale: true,};
        case constants.START_FETCHING_ALL_POS_SALE_BY_SALE_ID:
            return { ...state, ...clearFlags, isFetchingAllPosSaleBySaleId: true, };
        case constants.GET_ALL_POS_SALE_BY_SALE_ID:
            return { ...action.payload._payload, isFetchingAllPosSaleBySaleId: false, all_pos_sale_requests: state.all_pos_sale_requests,...clearFlags};
        case constants.ERROR_FETCHING_ALL_POS_SALE_BY_SALE_ID:
            return { ...state, isFetchingAllPosSaleBySaleId:  false, errorAllPosSaleBySaleId: true,};
        case constants.START_ADD_POS_SALE:
            return { ...state, ...clearFlags, isAddingPosSale: true, }
        case constants.ADD_POS_SALE:
            return { ...state, isAddingPosSale:   false,} 
        case constants.ERROR_ADD_POS_SALE:
            return { ...state, isAddingPosSale: false, errorAddPosSale: true}
        case constants.START_REMOVE_POS_SALE:
            return { ...state, ...clearFlags, isDeletingPosSale:  true, };
        case constants.REMOVE_POS_SALE:
            return { ...state, isDeletingPosSale: false};
        case constants.ERROR_REMOVE_POS_SALE:
            return { ...state, isDeletingPosSale: false, errorDeletingPosSale:   true};
        case constants.START_UPDATE_POS_SALE:
            return { ...state, ...clearFlags, isUpdatingPosSale:  true, }
        case constants.UPDATE_POS_SALE:
            return { ...state, isUpdatingPosSale:  false}
        case constants.ERROR_UPDATE_POS_SALE:
            return { ...state, isUpdatingPosSale: false ,errorUpdatingPosSale: true}
        case constants.CLEAR_ALL_STATE_POS_SALE:
            return _.cloneDeep(initialState)
        case constants.CLEAR_STATE_POS_SALE_BY_SALE:
            // let initialCopy = _.cloneDeep(initialState);
            return {...initialState,...clearFlags, all_pos_sale_requests: state.all_pos_sale_requests}
        default:
            return state;
    }
}