import { Modal } from "antd";
import InfoUnit from "./InfoUnit";
import {ParkingValue} from "../Utilitis/Utilitis";
import React, { useEffect, useState } from "react";
import ListTitleData from "../../../../Projects/Projects/components/UpdatedProject/incrementoDescuento/ListTitleDataComponent";

const labels = [
	"m2",
	"Etiqueta",
	"Disponibles",
	"No. Habitaciones",
	"No. Baños",
	"No. Niveles",
	"Piso",
	"Parqueadero",
	"Valor",
	" ",
];

const UnitDetails = ({ visible, handleOk, handleCancel, data }) => {
	const [dataSource, setDataSource] = useState([]);

	useEffect(() => {
		if (!Array.isArray(data)) {
			adapter();
		}
	}, [data]);
	
	const adapter = () => {
		let units = data.unit;
		let parkingValue;
		let check = false;
		let paymentPetting = data.payment_relationship_settings;
		let saleParameters = data.sale_parameters;
		// eslint-disable-next-line array-callback-return
		units.map((value) => {
			parkingValue = ParkingValue(value.parking,data.sale_parameters);
			if (parseInt(data.finish_kit_value) !== 0) {
				check = true;
			}
			Object.assign(value, {
				squareMeter: data.area,
				nameProject: data.name,
				fechaDeConstruccion: data.fechaDeConstruccion,
				projectId: data.id,
				customerId:data.customerId,
				project_state: data.project_state,
				bathroomNumber: data.bathroom_number,
				numberRooms: data.number_rooms,
				numberLevels: data.number_levels,
				estimatedDeliveryDate: data.estimated_delivery_date,
				finishKitValue: data.finish_kit_value,
				valueOtherAdditional: data.value_other_additional,
				initialFeePercentage: paymentPetting?.initial_fee_percentage,
				initialFeeFinanceInConstructionPercentage: paymentPetting?.initial_fee_finance_in_construction_percentage,
				initialFeePromiseSignatureTrustPercentage: paymentPetting?.initial_fee_promise_signature_trust_percentage,
				bankFinancingPercentage: paymentPetting?.bank_financing_percentage,
				maximumNumberMonthsFinanceInitialFee: paymentPetting?.maximum_number_months_finance_initial_fee,
				separationValue: paymentPetting?.separation_value,
				parkingValue: parkingValue,
				interestOnPlansPercentage: saleParameters?.interest_on_plans_percentage,
				interestInConstructionPercentage: saleParameters?.interest_in_construction_percentage,
				check: check,
				bonusView:value.bonus_view,
				squareMeterValue: value.quote_meter_value,
				parking: value.parking,
				Coupons: data.Cupones,
				discountPolicy: data.discount_policy,
			});
		});

		setDataSource(units);
	};

	if (!dataSource) return null;

	return (
		<>
			<Modal
				key={dataSource}
				okText={"Cerrar"}
				visible={visible}
				onOk={handleOk}
				onCancel={handleCancel}
				title={data.name}
				width={"75%"}
			>
				<div className="wrapper">
					<ListTitleData data={labels} />
					{dataSource.map((item, index) => (
						<InfoUnit key={index} info={item} />
					))}
				</div>
			</Modal>
		</>
	);
};

export default UnitDetails;
