import React, {useEffect, useState} from "react";
import { fetchAllAssessor } from "../../../../redux/actions/Configuration/AssesorAction";
import { fetchAllAfiliatedCompany } from "../../../../redux/actions/Configuration/AfiliatedCompanyAction";
import { useDispatch } from "react-redux";
import { validateModule } from "../../../handle/PermissionMethods/PermissionMethods";
import * as constants from "../../../../redux/constants";
import { Redirect } from "react-router-dom/cjs/react-router-dom.min";
import {Card} from "antd";
import FormPotentialClientsDatabase from "./FormPotentialClientsDatabase";
import {errorGlobal, success} from "../../../handle/Notification/Notification";
import {exportExcelPotentialClientsDatabase} from "../../../../redux/actions/Report/ReportPotentialClientsDatabase/reportPotentialClientDatabaseAction";
import { DownloadDocument } from "../../../handle/Multimedia/DownloadDocument";
const PotentialClientsDatabase = () => {
	const [consultind, setConsulting] = useState( false )
	const exportExcel = async (values) => {
		setConsulting(true);
		dispatch(
			exportExcelPotentialClientsDatabase(values, async (response) => {
			
				const res = await response;
			
				if (res.error) {
					errorGlobal("¡UPS!, Ha ocurrido un error");
				} else {
					if (res._payload !== '') DownloadDocument(res._payload);
				}
			})
		);setConsulting(false);
	};

	const dispatch = useDispatch();

	useEffect(() => {
		dispatch(fetchAllAssessor())
		dispatch(fetchAllAfiliatedCompany())
	}, [dispatch]);
	
	if (!validateModule(constants.POTENTIAL_CLIENTS_DATABASE_SUBMODULE)) {
		return <Redirect to={constants.DEFAULT_ROUTE} />;
	}
	return (
		<Card title="Base de Datos Clientes Potenciales">
			<FormPotentialClientsDatabase exportExcel={exportExcel} consultindExport={consultind}/>
		</Card>
	);
};

export default PotentialClientsDatabase;
