import moment from "moment";
import React, { useState } from "react";
import { getRequest } from "./HandleDeed";
import { useDispatch } from "react-redux";
import { FileTextOutlined, UploadOutlined } from "@ant-design/icons";
import {errorGlobal,success} from "../../../../../handle/Notification/Notification";
import {updateDeed,getDeedById} from "../../../../../../redux/actions/Price/SaleMade/deedAction";
import {Button,Checkbox,Col,DatePicker,Form,Input,InputNumber,Modal,Row,Tooltip,Upload} from "antd";
import {FORMATTER_INPUT_NUMBER,RUTA_API,VALID_DOCUMENT_UPLOAD} from "../../../../../../redux/constants";

const RegisterDeed = ({ salesMade }) => {
	const dispatch = useDispatch();
	var deed = {};
	let fullNameCustomer = getFullName(salesMade.customer["person"]);
	const [isModalVisible, setIsModalVisible] = useState(false);

	const showModal = () => {
		setIsModalVisible(true);
	};
	const [document, setDocument] = useState({});
	let [fileList, setFileList] = useState([]);
	
	const [form] = Form.useForm();

	function setFileDeed() {
		setFileList([
			{
				uid: deed["document"]["id"],
				name: deed["document"]["name"],
				status: "done",
				url: RUTA_API + deed["document"]["path"],
			},
		]);
	}

	const searchedDeed = async () => {
		dispatch(
			getDeedById(salesMade["deed_id"], async (response) => {
				let request = await response;
				deed = request["_payload"];
				setFileDeed();
				showModal();
				fillFields();
			})
		);
	};

	const onFinish = async () => {
		let fData;
		if (Number.isInteger(fileList[0]["uid"])) {
			fData = await getRequest(form, fileList[0]["uid"], salesMade["id"]);
		} else {
			fData = await getRequest(form, document, salesMade["id"]);
		}

		dispatch(
			updateDeed(salesMade["deed_id"], fData, async (response) => {
				const res = await response;
				if (!res.error) {
					success("Escrituración actualizada");
					handleCancel();
 
				} else errorGlobal("¡UPS! Ha ocurrido un error");
			})
		);
	};

	const propsUpload = {
		beforeUpload: (file) => {
			return false;
		},
		onChange({ file, fileList }) {
			if (file.status !== "uploading") {
				setDocument(fileList[0] ? fileList[0].originFileObj : {});
				setFileList(fileList);
			}
		},
	};

	const onReset = () => {
		form.resetFields();
	};

	const handleCancel = () => {
		setIsModalVisible(false);
		onReset();
	};

	function getFullName(person) {
		let names = person.name.split("_");
		return names[0] + " " + names[1];
	}

	const fillFields = () => {
		form.setFieldsValue({
			date_outlay: getDateOfString(deed["date_outlay"]),
			date_deed: getDateOfString(deed["date_deed"]),
			date_peace_and_safety: getDateOfString(deed["date_peace_and_safety"]),
			date_delivery_property: getDateOfString(deed["date_delivery_property"]),
			date_legalization: getDateOfString(deed["date_legalization"]),
			date_log: getDateOfString(deed["date_log"]),
			date_preorder: getDateOfString(deed["date_preorder"]),
			builder_credit_payment: deed["builder_credit_payment"],
			deed_value: deed["deed_value"],
			owner: deed["owner"] || "",
			comments: deed["comments"] || "",
			is_deed_sale: deed["is_deed_sale"] === 1,
			document: [
				{
					uid: deed["document"]["id"],
					name: deed["document"]["name"],
					status: "done",
					url: RUTA_API + deed["document"]["path"],
				},
			],
		});
	};

	const getDateOfString = (date) => {
		return date != null ? moment(date) : "";
	};

	
	const textDeed = <span style={{ color: "#6A6963" }}>Escrituración</span>;
	const titleModal = (
		<header>Escrituración - Venta No. {salesMade.unit_code} </header>
	);

	return (
		<>
			<Tooltip placement="left" color="#ffffff" title={textDeed}>
				<Button
					style={{ background: "#52c41a", color: "#fff" }}
					size={"small"}
					onClick={() => searchedDeed()}
					icon={<FileTextOutlined />}
					disabled={salesMade.is_desisted}
				/>
			</Tooltip>
			{isModalVisible && (
				<Modal
					title={titleModal}
					centered
					visible={isModalVisible}
					onCancel={() => setIsModalVisible(false)}
					width={900}
					footer={null}
				>
					<Form layout="vertical" size="middle" form={form} onFinish={onFinish}>
						<Row gutter={15}>
							<Col xs={24} sm={24} md={12} lg={12} xl={12}>
								<Form.Item name="customer" label="Cliente">
									<Input
	disabled={true}
	defaultValue={fullNameCustomer}
	style={{width: "100%"}}
	/>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={12} lg={12} xl={12}>
								<Form.Item name="project" label="Proyecto">
									<Input
	disabled={true}
	defaultValue={salesMade.project_name}
	style={{width: "100%"}}
	/>
								</Form.Item>
							</Col>
						</Row>
						<Row gutter={15}>
							<Col xs={24} sm={24} md={12} lg={12} xl={12}>
								<Form.Item name="unit" label="Unidad">
									<Input
	disabled={true}
	defaultValue={salesMade.unit_name}
	style={{width: "100%"}}
	/>
								</Form.Item>
							</Col>
						</Row>
						<Row gutter={15}>
							<Col xs={24} sm={24} md={12} lg={12} xl={12}>
								<Form.Item
									name="document"
									label="Documento"
									rules={[
										{
											required: true,
										},
									]}
								>
									<div style={{ display: "inline-block" }}>
										<Upload
											{...propsUpload}
											accept={VALID_DOCUMENT_UPLOAD}
											maxCount={1}
											fileList={fileList}
										>
											<Button type="primary" icon={<UploadOutlined />}>
												Elegir Archivo
											</Button>
										</Upload>
									</div>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={12} lg={12} xl={12}>
								<Form.Item name="date_deed" label="Fecha de escrituración">
									<DatePicker style={{ width: "100%" }} />
								</Form.Item>
							</Col>
						</Row>
						<Row gutter={15}>
							<Col xs={24} sm={24} md={12} lg={12} xl={12}>
								<Form.Item name="date_outlay" label="Fecha de desembolso">
									<DatePicker style={{ width: "100%" }} />
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={12} lg={12} xl={12}>
								<Form.Item name="deed_value" label="Valor escriturado">
									<InputNumber
										{...FORMATTER_INPUT_NUMBER}
										style={{ width: "100%" }}
									/>
								</Form.Item>
							</Col>
						</Row>
						<Row gutter={15}>
							<Col xs={24} sm={24} md={12} lg={12} xl={12}>
								<Form.Item
									name="date_peace_and_safety"
									label="Fecha de Paz y Salvo"
								>
									<DatePicker style={{ width: "100%" }} />
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={12} lg={12} xl={12}>
								<Form.Item name="date_preorder" label="Fecha de preentrega">
									<DatePicker style={{ width: "100%" }} />
								</Form.Item>
							</Col>
						</Row>
						<Row gutter={15}>
							<Col xs={24} sm={24} md={12} lg={12} xl={12}>
								<Form.Item
									name="date_delivery_property"
									label="Fecha de entrega del inmueble"
								>
									<DatePicker style={{ width: "100%" }} />
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={12} lg={12} xl={12}>
								<Form.Item name="date_log" label="Fecha de registro">
									<DatePicker style={{ width: "100%" }} />
								</Form.Item>
							</Col>
						</Row>
						<Row gutter={15}>
							<Col xs={24} sm={24} md={12} lg={12} xl={12}>
								<Form.Item
									name="date_legalization"
									label="Fecha de legalización"
								>
									<DatePicker style={{ width: "100%" }} />
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={12} lg={12} xl={12}>
								<Form.Item
									name="builder_credit_payment"
									label="Abono crédito constructor"
								>
									<InputNumber
										{...FORMATTER_INPUT_NUMBER}
										style={{ width: "100%" }}
									/>
								</Form.Item>
							</Col>
						</Row>
						<Row gutter={15}>
							<Col xs={24} sm={24} md={12} lg={12} xl={12}>
								<Form.Item name="owner" label="Propietario">
									<Input style={{width: "100%"}}/>
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={12} lg={12} xl={12}>
								<Form.Item name="comments" label="Observacion">
									<Input.TextArea />
								</Form.Item>
							</Col>
						</Row>
						<Row gutter={15}>
							<Col xs={24} sm={24} md={4} lg={4} xl={4}>
								<Form.Item
									valuePropName="checked"
									name="is_deed_sale"
									label="Venta escriturada"
								>
									<Checkbox> </Checkbox>
								</Form.Item>
							</Col>
						</Row>
						<Row xs={24} sm={24} md={12} lg={12} xl={12}>
							<Form.Item>
								<Button
									type="primary"
									htmlType="submit"
									style={{ position: "absolute", left: 650, top: 20 }}
								>
									Actualizar escrituración
								</Button>
							</Form.Item>
						</Row>
					</Form>
				</Modal>
			)}
		</>
	);
};

export default RegisterDeed;
