import * as constants from "../../../constants";

export default function quotationReducer(state = [], action) {
	switch (action.type) {
		case constants.GET_ALL_QUOTES:
			return action.payload;
		case constants.GET_QUOTES_SEPARATED:
			return action.payload;
		case constants.UPDATE_QUOTE_SEPARATION:
			return state;
		case constants.GET_QUOTATION_BY_ID:
			return action.payload._payload;
		case constants.UPDATE_QUOTE_LEGALIZATION_SALE:
			return state;
		case constants.RESET_QUOTE:
			return action.payload;
		default:
			return state;
	}
}
