import React from "react";
import { Select, Form, Col, InputNumber, Row, Divider } from "antd";

const ProjectDescription = ({itemStyle}) => {
	return (
		<>
			{" "}
			<Divider orientation="left">Descripción del proyecto</Divider>
			<Row gutter={16}>
				<Col xs={24} sm={24} md={8} lg={8} xl={8}>
					<Form.Item name="numberTowers" label="Número de torres">
						<InputNumber
							style={itemStyle}
							placeholder="Número de torres"
						/>
					</Form.Item>
					<Form.Item
						label="Planta eléctrica"
						name="powerPlant"
						showSearch
						optionFilterProp="children"
						filterOption={(input, option) =>
							option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
						}
						style={itemStyle}
					>
						<Select defaultValue="2">
							<Select.Option value="">Seleccione...</Select.Option>
							<Select.Option value="1">SI</Select.Option>
							<Select.Option value="2">NO</Select.Option>
						</Select>
					</Form.Item>
					<Form.Item
						label="Cuarto útil"
						name="usefulRoom"
						showSearch
						optionFilterProp="children"
						filterOption={(input, option) =>
							option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
						}
						style={itemStyle}
					>
						<Select defaultValue="2">
							<Select.Option value="">Seleccione...</Select.Option>
							<Select.Option value="1">SI</Select.Option>
							<Select.Option value="2">NO</Select.Option>
						</Select>
					</Form.Item>
				</Col>
				<Col xs={24} sm={24} md={8} lg={8} xl={8}>
					<Form.Item name="numberFloors" label="Número de pisos">
						<InputNumber value={"0"} defaultValue={"0"} style={itemStyle} placeholder="Número de pisos" />
					</Form.Item>
					<Form.Item
						label="Shut basura"
						name="trashShut"
						showSearch
						optionFilterProp="children"
						filterOption={(input, option) =>
							option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
						}
						style={itemStyle}
					>
						<Select defaultValue="2">
							<Select.Option value="">Seleccione...</Select.Option>
							<Select.Option value="1">SI</Select.Option>
							<Select.Option value="2">NO</Select.Option>
						</Select>
					</Form.Item>
					<Form.Item
						label="Piscina"
						name="pool"
						showSearch
						optionFilterProp="children"
						filterOption={(input, option) =>
							option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
						}
						style={itemStyle}
					>
						<Select defaultValue="2">
							<Select.Option value="">Seleccione...</Select.Option>
							<Select.Option value="1">SI</Select.Option>
							<Select.Option value="2">NO</Select.Option>
						</Select>
					</Form.Item>
				</Col>
				<Col xs={24} sm={24} md={8} lg={8} xl={8}>
					<Form.Item
						label="Área de gimnasio"
						name="gymArea"
						showSearch
						optionFilterProp="children"
						filterOption={(input, option) =>
							option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
						}
						style={itemStyle}
					>
						<Select defaultValue="2">
							<Select.Option value="">Seleccione...</Select.Option>
							<Select.Option value="1">SI</Select.Option>
							<Select.Option value="2">NO</Select.Option>
						</Select>
					</Form.Item>
					<Form.Item
						label="Parque infantil"
						name="playground"
						showSearch
						optionFilterProp="children"
						filterOption={(input, option) =>
							option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
						}
						style={itemStyle}
					>
						<Select defaultValue="2">
							<Select.Option value="">Seleccione...</Select.Option>
							<Select.Option value="1">SI</Select.Option>
							<Select.Option value="2">NO</Select.Option>
						</Select>
					</Form.Item>
					<Form.Item
						label="Salón social"
						name="socialRoom"
						style={itemStyle}
					>
						<Select
							showSearch
							optionFilterProp="children"
							filterOption={(input, option) =>
								option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
							}
							defaultValue="2"
						>
							<Select.Option value="">Seleccione...</Select.Option>
							<Select.Option value="1">SI</Select.Option>
							<Select.Option value="2">NO</Select.Option>
						</Select>
					</Form.Item>
				</Col>
			</Row>
		</>
	);
};

export default ProjectDescription;
