import React, {useState} from "react";
import {useDispatch} from "react-redux";
import {ToolOutlined} from "@ant-design/icons";
import {Button, Form, Modal, Tooltip} from "antd";
import BasicInformation from "./Components/BasicInformation";
import InteractiveTable from "./Components/InteractiveTable";
import {postSale} from "../../../../../../../../redux/actions/Customer/AfterSalesCustomer";

const AfterSales = ({basicInformation}) => {
	const [visible,setVisible] = useState(false)
	const dispatch = useDispatch()
	const [form] = Form.useForm()
	const textAfterSales = <span style={{ color: '#6A6963' }}> Posventas </span>
	const [data,setData] = useState([])
	
	const showModal = () => {setVisible(true);searchForInformation()}
	const reset = () => {form.resetFields()}
	const closeModal = () => {setVisible(false);reset()}
	
	const searchForInformation = () => {
		dispatch(postSale(basicInformation.id,async (success)=>{
			const response = await success
			setInfoBase(response._payload.infoBase)
			setData(response._payload.afterSale)
		}))
	}
	
	const setInfoBase = (data) => {
		form.setFieldsValue({
			project: data.project_name,
			tradingCompany: data.trading_company,
			creatorUser: data.creator_user,
			customer: data.customer_name,
			unit: data.unit_name,
			applicationDate: data.application_date
		})
	}
	
	return (
		<>
			<Tooltip placement="left" color='#ffffff' title={textAfterSales}>
				<Button
					size="small"
					onClick={()=>showModal()}
					style={{ backgroundColor: '#002140', marginTop: 3 }}
					icon={<ToolOutlined style={{ color: '#fff' }} />}
				/>
			</Tooltip>
			<Modal
				title={"Solicitud de Postventa"}
				visible={visible}
				onCancel={()=>closeModal()}
				cancelText={"Cerrar"}
				okText={"Guardar"}
				width={"90%"}
			>
				<Form
					form={form}
					layout="vertical"
					autoComplete="off"
				>
					<BasicInformation/>
					<InteractiveTable 
						sale_id={basicInformation.id}
						query={data}
						searchForInformation={searchForInformation}
					/>
				</Form>
			</Modal>
		</>
	)
}

export default AfterSales