import React, {useState} from "react";
import { useDispatch } from "react-redux";
import {  Col, Row, Button,  Popover } from "antd";

import { DownloadDocument } from "../../../../handle/Multimedia/DownloadDocument";
import {
	fetchExcelAccountState, fetchPdfAccountState,
	sendEmailAccountState
} from "../../../../../redux/actions/Report/ReportAccountStatus/reportAccountStatusAction";
import {warning} from "../../../../handle/Notification/Notification";


 const ExportExcelPdf = ({ id_customer, id_sale, id_user }) => {
const [loading,setLoading]=useState(false)
	const dispatch = useDispatch();
	const exportExcel = () => {
		let data = {
			"id_customer": id_customer,
			"id_sale": id_sale,
			"id_user": id_user,
		}
		dispatch(fetchExcelAccountState(data, async (response) => {
			const res = await response;
			await DownloadDocument(res._payload);
		}));
	}
	const exportPdf = () => {
		let data = {
			"id_customer": id_customer,
			"id_sale": id_sale,
			"id_user": id_user,
		}
		dispatch(fetchPdfAccountState(data, async (response) => {
			const res = await response;
			await DownloadDocument(res._payload);
		}));	
	}
	
	 const SendEmail = () => {
		 let data = {
			 "id_customer": id_customer,
			 "id_sale": id_sale,
			 "id_user": id_user,
		 }
		 dispatch(sendEmailAccountState(data,async (response) => {
			 setLoading(true)
			 const res = await response;
			 if(res.code=== 200){setLoading(false)}else{warning("ups!!, fallo el envio.")}
		 }));
	 }
	const content = (
		<div>
			<a style={{ marginRight: 21 }} onClick={exportExcel}>
				Excel
			</a>
			<a onClick={exportPdf}>PDF</a>
		</div>
	);
return (
		<Row>
			<Col xs={20} sm={20} md={20} lg={20} xl={20} />
			<Col xs={2} sm={2} md={2} lg={2} xl={2}>
					<Button
						loading={loading}
						style={{ backgroundColor: "#5cb85c", borderColor: "#5cb85c", color:"white" }}
						type="secundary"
						onClick={SendEmail}
					>
						Enviar
					</Button>
			</Col>
			<Col xs={2} sm={2} md={2} lg={2} xl={2}>
				<Popover content={content} trigger="click">
					<Button
						style={{ backgroundColor: "#5cb85c", borderColor: "#5cb85c" }}
						type="primary"
					>
						Exportar
					</Button>
				</Popover>
			</Col>
		</Row>
);
}

export  default  ExportExcelPdf;




