import React from "react"
import {useSelector} from "react-redux";
import {Col, DatePicker, Form, Row, Select} from "antd";

const ApplicationForm = () => {
	const selectAssessor = useSelector((state) => state?.assessor);
	const selectAfiliatedCompany = useSelector((state) => state?.afiliatedCompany);
	const selectProject = useSelector((state) => state?.project);
	
	return <>
		<Row gutter={16}>
			<Col xs={24} sm={24} md={12} lg={12} xl={12}>
				<Form.Item label="Proyecto:" name="Proyecto">
					<Select
						defaultValue={"All"}
						showSearch
						style={{ width: "100%" }}
						optionFilterProp="children"
						filterOption={(input, option) =>
							option.children.toLowerCase().indexOf(input.toLowerCase()) >=
							0
						}
					>
						<Select.Option value="All" active>Todos</Select.Option>
						{
							!Array.isArray(selectProject) && selectProject._payload?.map(item => (
								<Select.Option key={item.id} value={item.id}>{` ${item.name} `}</Select.Option>
							))}
					</Select>
				</Form.Item>
			</Col>
			<Col xs={24} sm={24} md={12} lg={12} xl={12}>
				<Form.Item label="Empresa:" name="Empresa">
					<Select
						defaultValue={"All"}
						showSearch
						style={{ width: "100%" }}
						optionFilterProp="children"
						filterOption={(input, option) =>
							option.children.toLowerCase().indexOf(input.toLowerCase()) >=
							0
						}
					>
						<Select.Option value="All">Todos</Select.Option>
						{!Array.isArray(selectAfiliatedCompany) &&
							selectAfiliatedCompany._payload.map((item) => (
								<Select.Option key={item.id}  value={item.id}>{` ${item.name} `}</Select.Option>
							))}
					</Select>
				</Form.Item>
			</Col>
			<Col xs={24} sm={24} md={12} lg={12} xl={12}>
				<Form.Item label="Asesor:" name="Asesor">
					<Select
						defaultValue={"All"}
						showSearch
						style={{ width: "100%" }}
						optionFilterProp="children"
						filterOption={(input, option) =>
							option.children.toLowerCase().indexOf(input.toLowerCase()) >=
							0
						}
					>
						<Select.Option value="All">Todos</Select.Option>
						{!Array.isArray(selectAssessor) &&
							selectAssessor._payload.map((item) => (
								<Select.Option key={item.id} value={item.id}>{` ${item.name} `}</Select.Option>
							))}
					</Select>
				</Form.Item>
			</Col>
		</Row>
		<Row gutter={16}>
			<Col xs={24} sm={24} md={12} lg={12} xl={12}>
				<Form.Item
					label="Fecha Inicio:"
					name="FechaInicio"
					rules={[
						{
							required: true,
						},
					]}
				>
					<DatePicker
						style={{ width: "100%" }}
					/>
				</Form.Item>
			</Col>
			<Col xs={24} sm={24} md={12} lg={12} xl={12}>
				<Form.Item
					label="Fecha Fin:"
					name="FechaFin"
					rules={[
						{
							required: true,
						},
					]}
				>
					<DatePicker
						style={{ width: "100%" }}
					/>
				</Form.Item>
			</Col>
		</Row>
	</>
}

export default ApplicationForm