import * as constants from "../../constants";

export default function sales_ParameterReducer(state = [], action) {
	switch (action.type) {
		case constants.UPDATE_OR_CREATE_SALES_PARAMETERS:
			return state._payload.map((item) => {
				if (item._id === action.payload.saleParameteId)
					return { ...item, ...action.payload.data };
				else return item;
			});
		case constants.GET_SALE_PARAMETER_BY_ID:
			return action.payload;
		case constants.RESET_SALE_PARAMETER:
			return action.payload;
		default:
			return state;
	}
}
