import React, { useState, useEffect } from "react";
import {Form, Button, Row, Col, Divider, Card, PageHeader} from "antd";
import ProjectData from "../NewCompetitionControl/ProjectData";
import ProjectDescription from "../NewCompetitionControl/ProjectDescription";
import SpecificationsArea from "../NewCompetitionControl/SpecificationsArea";
import { Redirect, useParams } from "react-router-dom";
import {fetchAllControlPowers, getControlPowerById, updateControlPowerById} from "../../../../../../redux/actions/Project/ControlPowerAction";
import {errorGlobal, success} from "../../../../../handle/Notification/Notification";
import {useDispatch, useSelector} from "react-redux";
import moment from "moment";
import { fetchAllBuilder } from "../../../../../../redux/actions/Project/BuilderAction";
import { fetchAllPricesRange } from "../../../../../../redux/actions/Configuration/PricesRangeAction";
import {getOptionsSelect} from "../../../../../../redux/actions/Project/ProjectsAction";
import {getChangeDateState} from "../../../../../handle/Utilitis";
import {getAllProjectStates} from "../../../../../../redux/actions/Project/ProjectStateAction";

const UpdateControlPower = () => {
	const [form] = Form.useForm();
	const [redirect, setRedirect] = useState(false);
	const [controlCompetencesDatabase, setControlCompetencesDatabase] = useState([]);
	const [projectTypes] = useState([]);
	const [controlCompetence, setControlCompetence] = useState({});
	const { id } = useParams();
	const dateFormat = "YYYY/MM/DD";

	const dispatch = useDispatch();

	useEffect(() => {
		dispatch(
			getControlPowerById(id, async (response) => {
				const res = await response;
				setControlCompetencesDatabase(res);
			})
		);
		dispatch(fetchAllBuilder());
		dispatch(fetchAllPricesRange());
	}, []);

	useEffect(() => {
		dispatch(getOptionsSelect((response) => {
			response._payload.project_types.forEach((value) => {
				projectTypes.push({
					name: value.name,
					id: value.id,
				});
			});
		}));
	}, []);

	

	useEffect(() => {
		dispatch(getAllProjectStates());
	}, [dispatch])

	const projectStates =  useSelector((state) => state?.projecState?._payload);
	

	useEffect(() => {
		if (!Array.isArray(controlCompetencesDatabase) && projectStates) {
			fillFields();
		}
	}, [controlCompetencesDatabase, projectStates]);
	
	const calculatePercentageUnitsSold = () => {
		let vari = controlCompetencesDatabase._payload
		return  (100 * vari?.sold_units)/vari?.total_units
	}

	
	
	const fillFields = () => {
		let projectState = getChangeDateState(controlCompetencesDatabase?._payload?.delivery_date ?? new Date(), controlCompetencesDatabase?._payload?.building_date ?? null, projectStates);
	
		form.setFieldsValue({
			name: controlCompetencesDatabase._payload.name,
			marketer: controlCompetencesDatabase._payload.marketer_company,
			projectType: parseInt(controlCompetencesDatabase._payload.project_type_id) || "",
			
			releaseDate: controlCompetencesDatabase._payload.release_date
				? moment(controlCompetencesDatabase._payload.release_date)
				: undefined,
			deliveryDate: controlCompetencesDatabase._payload.delivery_date
				? moment(controlCompetencesDatabase._payload.delivery_date)
				: undefined,
			totalUnits: controlCompetencesDatabase._payload.total_units,
			launchPriceM2: parseInt(
				controlCompetencesDatabase._payload.release_meter_price
			),
			sourceName: controlCompetencesDatabase._payload.information_source_name,
			observations: controlCompetencesDatabase._payload.observations,
			builder: parseInt(controlCompetencesDatabase._payload.construction_companies_id),
			address: controlCompetencesDatabase._payload.address,
			priceRange: parseInt(controlCompetencesDatabase._payload.price_range_id) || "",
			buildingDate: controlCompetencesDatabase._payload.building_date
				? moment(controlCompetencesDatabase._payload.building_date)
				: undefined,
			projectStatus: projectState,
			unitsSoldToDate: controlCompetencesDatabase._payload.sold_units,
			currentPriceOfM2: parseInt(
				controlCompetencesDatabase._payload.current_meter_price
			),
			sourcePhone: controlCompetencesDatabase._payload.information_source_phone,
			state: controlCompetencesDatabase._payload.state,
			numberTowers: controlCompetencesDatabase._payload.towers_number,
			powerPlant: controlCompetencesDatabase._payload.has_power_plant
				? String(controlCompetencesDatabase._payload.has_power_plant)
				: "2",
			usefulRoom: controlCompetencesDatabase._payload.has_useful_room
				? String(controlCompetencesDatabase._payload.has_useful_room)
				: "2",
			numberFloors: controlCompetencesDatabase._payload.floors_number,
			trashShut: controlCompetencesDatabase._payload.has_shut_garbage
				? String(controlCompetencesDatabase._payload.has_shut_garbage)
				: "2",
			pool: controlCompetencesDatabase._payload.has_pool
				? String(controlCompetencesDatabase._payload.has_pool)
				: "2",
			gymArea: controlCompetencesDatabase._payload.has_gym_area
				? String(controlCompetencesDatabase._payload.has_gym_area)
				: "2",
			playground: controlCompetencesDatabase._payload.has_playground
				? String(controlCompetencesDatabase._payload.has_playground)
				: "2",
			socialRoom: controlCompetencesDatabase._payload.has_social_room
				? String(controlCompetencesDatabase._payload.has_social_room)
				: "2",
			numberBedrooms: controlCompetencesDatabase._payload.rooms_number,
			numberBalconies: controlCompetencesDatabase._payload.balconies_number,
			integralKitchen: controlCompetencesDatabase._payload.has_integral_kitchen
				? String(controlCompetencesDatabase._payload.has_integral_kitchen)
				: "2",
			numberBathrooms: controlCompetencesDatabase._payload.bathrooms_number,
			serviceBedroom: controlCompetencesDatabase._payload.has_service_bedroom
				? String(controlCompetencesDatabase._payload.has_service_bedroom)
				: "2",
			study: controlCompetencesDatabase._payload.has_study
				? String(controlCompetencesDatabase._payload.has_study)
				: "2",
		});

		//form.setFieldsValue(controlCompetence);
	};

	const onReset = () => {
		form.resetFields();
	};
	const itemStyle = {
		width: "100%",
	};

	const handleSubmit = async () => {
		const user = JSON.parse(localStorage.getItem("USER_INFO"));
		const values = await form.validateFields();
		const dto = {
			creator_user_id: user.id,
			name: values["name"],
			address: values["address"] || null,
			release_date: values?.releaseDate?.format(dateFormat) ??  null,
			building_date:values?.buildingDate?.format(dateFormat) ?? null,
			delivery_date: values.deliveryDate?.format(dateFormat) ?? null,
			total_units: values["totalUnits"] || null,
			sold_units: values["unitsSoldToDate"] || null,
			current_meter_price: values["currentPriceOfM2"] || null,
			release_meter_price: values["launchPriceM2"] || null || null,
			information_source_name: values["sourceName"] || null,
			information_source_phone: values["sourcePhone"] || null,
			observations: values["observations"] || null,
			towers_number: values["numberTowers"] || null,
			floors_number: values["numberFloors"] || null,
			has_power_plant: values["powerPlant"] || null,
			has_shut_garbage: values["trashShut"] || null,
			has_useful_room: values["usefulRoom"] || null,
			has_pool: values["pool"] || null,
			has_gym_area: values["gymArea"] || null,
			has_social_room: values["socialRoom"] || null,
			has_playground: values["playground"] || null,
			rooms_number: values["numberBedrooms"] || null,
			bathrooms_number: values["numberBathrooms"] || null,
			balconies_number: values["numberBalconies"] || null,
			has_service_bedroom: values["serviceBedroom"] || null,
			has_integral_kitchen: values["integralKitchen"] || null,
			has_study: values["study"] || null,
			state: values["state"] || null,
			construction_companies_id: values["builder"] || null,
			project_state_id: projectStates.find(x => x?.name?.toLowerCase().trim() == values["projectStatus"]?.toLowerCase().trim() )?.id || null,
			project_type_id: values["projectType"] || null,
			marketer_company: values["marketer"] || null,
			price_range_id: values["priceRange"] || null,
		};
		
		dispatch(
			updateControlPowerById(id, dto, async (response) => {
				const res = await response;
				if (res.error) {
					errorGlobal("¡UPS! Ha ocurrido un error.");
				} else {
					success("Control de competencia actualizado con éxito.");
					dispatch(fetchAllControlPowers());
					onReset();
					setRedirect(true);
				}
			})
		);
	};

	return (
		<>
			<PageHeader
				className="site-page-header"
				onBack={() => window.history.back()}
				title="Editar control de competencia"
				style={{ marginLeft: "-25px" }}
			>
				<Card style={{ borderColor: "black" }}>
					<Row gutter={{ xs: 8, sm: 16, md: 24, lg: 24 }}>
						<Col className="gutter-row" span={24}>
							{redirect ? (
								<Redirect to="/projects/control-powers" />
							) : (
								<Form
									form={form}
									layout="vertical"
									onFinish={handleSubmit}
									style={{ marginLeft: "27px", marginRight: "27px" }}
									initialValues={{
										state: true,
									}}
								>
									{
										<>
											<ProjectData controlCompetencesDatabase={controlCompetencesDatabase} itemStyle={itemStyle} calculatePercentageUnitsSold={calculatePercentageUnitsSold} projectStates={projectStates} />
											<ProjectDescription itemStyle={itemStyle} />
											<SpecificationsArea itemStyle={itemStyle} />
											<Divider/>
											<Form.Item style={{ marginTop: 20 }}>
												<Button
													type="primary"
													htmlType="submit"
													style={{ position: "absolute", right: 1, top: 20 }}
												>
													Enviar
												</Button>
											</Form.Item>
										</>
									}
								</Form>
							)}
						</Col>
					</Row>
				</Card>
			</PageHeader>
		</>
	);
};

export default UpdateControlPower;
