import React from "react";
import { Table, Checkbox } from "antd";
import {convertAllMapDataState} from "../../../../../handle/HandleFilterTable/handleFilterTable";

const discountType = {
	1: "Valor m2",
	2: "Valor total",
};

const formatterPeso = new Intl.NumberFormat("es-CO", {
	style: "currency",
	currency: "COP",
	minimumFractionDigits: 2,
});

const CommercialDiscounts = ({ Project, Discoun }) => {
	let Discount = [];
	const columns = [
		{
			title: "Política",
			width: 120,
			dataIndex: "dates",
		},
		{
			title: "Tipo descuento",
			width: 20,
			dataIndex: "discountType",
		},
		{
			title: "Descuento",
			width: 20,
			dataIndex: "discount",
		},
		{
			title: "Acciones",
			width: 10,
			render: (_, record) => split(record),
		},
	];
	const split = (record) => {
		return (
			<Checkbox
				style={{ paddingLeft: "50%" }}
				onChange={(event) => onChangeKit(record, event)}
			/>
		);
	};
	const onChangeKit = (value, event) => {
		let ubi;
		let valor = 0;
		if (event.target.checked) {
			Project.discountPolicy?.forEach((element) => {
				if (value.key === element.id) {
					Discount.push(value.key,);
					valor = element.amount;
				}
			});
			Project.discount_policy?.forEach((element) => {
				if (value.key === element.id) {
					Discount.push(value.key,);
					valor = element.amount;
				}
			});
		} else {
			Project.discountPolicy?.forEach((element) => {
				if (value.key === element.id) {
					valor = element.amount;
				}
			});
			Project.discount_policy?.forEach((element) => {
				if (value.key === element.id) {
					valor = element.amount;
				}
			});
			Discount.forEach((element, index) => {
				if (element.id === value.key) {
					ubi = index;
					Discoun(element.amount, Discount);
				}
			});
			Discount.splice(ubi, 1);
		}
		Discoun(valor, value.key, event.target.checked, value.discountType);
	};
	let dataF = [];
	const data = () => {
		if (Project !== undefined) {
			Project.discountPolicy?.forEach((element) => {
				dataF = dataF.concat({
					key: element.id,
					dates: "Desde la "+parseInt(element.lower_limit)+
						"º hasta la "+parseInt(element.upper_limit)+
						"º unidad vendida",
					discountType: discountType[element.discount_type],
					discount: formatterPeso.format(parseInt(element.amount)),
				});
			});
			Project.discount_policy?.forEach((element) => {
				dataF = dataF.concat({
					key: element.id,
					dates: "Desde la "+parseInt(element.lower_limit)+
						"º hasta la "+parseInt(element.upper_limit)+
						"º unidad vendida",
					discountType: discountType[element.discount_type],
					discount: formatterPeso.format(parseInt(element.amount)),
				});
			});
		}
	};
	data();

	return (
		<>
			<Table
				style={{ width: "90%" }}
				pagination={false}
				columns={columns}
				dataSource={convertAllMapDataState(dataF)}
				bordered
			/>
		</>
	);
};
export default CommercialDiscounts;
