import React from "react";
import {  fetchExcelAssesorReport } from "../../../../../redux/actions/Configuration/AssesorAction";
import { useDispatch } from "react-redux";
import {  Col, Row, Button, Popover } from "antd";
import { errorGlobal} from "../../../../handle/Notification/Notification";

const ExportExcelAdvisorsReport = ({ data }) => {
    const dispatch = useDispatch();
    const exportExcel = () => {
        dispatch(fetchExcelAssesorReport(data, async (response) => {
            let res = await response;
            if (res.code === 200) {
                const tagA = document.createElement("a");
                tagA.href = res._payload;
                tagA.target = "_blank";
                tagA.click();
            }
            else {
                errorGlobal(res.message);
            }
        }));
    }

    const content = (
        <div>
            <a onClick={exportExcel}>
                Excel
            </a>
        </div>
    );
    return (

        <Row>
            <Col xs={2} sm={2} md={2} lg={2} xl={2}>
                <Popover content={content} trigger="click">
                    <Button
                        style={{ backgroundColor: "#5cb85c", borderColor: "#5cb85c" }}
                        type="primary"
                    >
                        Exportar
                    </Button>
                </Popover>
            </Col>
        </Row>


    );
}

export default ExportExcelAdvisorsReport;
