import * as constants from "../../../constants";

export const quotesProjectFilter = (data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: "POST",
		url: `${constants.RUTA}/quotation/projects_filter`,
		data,
		headers: constants.TOKEN,
		success: (project) => projectFilter(project),
		postProcessSuccess: onSuccess,
		postProcessError: onError,
	},
});

export const quotesSaveData = (data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: "POST",
		url: `${constants.RUTA}/quotation`,
		data,
		headers: constants.TOKEN,
		postProcessSuccess: onSuccess,
		postProcessError: onError,
	},
});

export const sendEmail = (data,customer_id, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: "POST",
		url: `${constants.RUTA}/quotation/send_mail/${customer_id}`,
		data,
		headers: constants.TOKEN,
		postProcessSuccess: onSuccess,
		postProcessError: onError,
	},
});

export const updateQuoteSeparationById = (id, data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: "PUT",
		url: `${constants.RUTA}/unit/apart-unit/${id}`,
		data,
		success: (id, data) => updateQuoteSeparation(id, data),
		postProcessSuccess: onSuccess,
		postProcessError: onError,
	},
});

export const updateQuoteLegalizationSaleById = (data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: "POST",
		url: `${constants.RUTA}/sales`,
		data,
		success: (data) => updateQuoteLegalizationSale(data),
		postProcessSuccess: onSuccess,
		postProcessError: onError,
	},
});
export const updateQuoteById = (quoteId, data, onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: "PUT",
		url: `${constants.RUTA}/quotation/${quoteId}`,
		data,
		postProcessSuccess: onSuccess,
		postProcessError: onError,
	},
});
