export const getAllQuotation = () => ({
	type: constants.API,
	payload: {
		method: "GET",
		url: `${constants.RUTA}/quotation`,
		headers: constants.TOKEN,
		success: (reseponse) => AllQuotes(reseponse),
	},
});

export const getQuotationSeparated = () => ({
	type: constants.API,
	payload: {
		method: "GET",
		url: `${constants.RUTA}/dashboard/get_quotations_separated`,
		headers: constants.TOKEN,
		success: (reseponse) => AllQuotes(reseponse),
	},
});

export const updateQuotationSeparated = (data,onSuccess,onError) => ({
	type: constants.API,
	payload: {
		method: "POST",
		url: `${constants.RUTA}/dashboard/update_quotations_separated`,data,
		success: (reseponse) => AllQuotes(reseponse),
		postProcessSuccess: onSuccess,
		postProcessError: onError,
	},
});

export const getQuoteById = (quoteId, onSuccess,onError) => ({
	type: constants.API,
	payload: {
		method: "GET",
		url: `${constants.RUTA}/quotation/${quoteId}`,
		success: (project) => getQuoteByIdAction(project),
		postProcessSuccess: onSuccess,
		postProcessError: onError,
	},
});

export const downloadQuotes = (quoteId, onSuccess) => ({
	type: constants.API,
	payload: {
		method: "GET",
		url: `${constants.RUTA}/quotation/download-quotation/${quoteId}`,
		postProcessSuccess: onSuccess,
	},
});

export const printView = (salesId, onSuccess) => ({
	type: constants.API,
	payload: {
		method: "GET",
		url: `${constants.RUTA}/salesmade/download-sale/${salesId}`,
		postProcessSuccess: onSuccess,
	},
});

export const resetQuoteAction = () => ({
	type: constants.API,
	payload: {
		method: null,
		success: () => (resetQuote())
	}
})

const updateQuoteSeparation = (id, data) => ({
	type: constants.UPDATE_QUOTE_SEPARATION,
	payload: { id, data },
});

const updateQuoteLegalizationSale = (data) => ({
	type: constants.UPDATE_QUOTE_LEGALIZATION_SALE,
	payload: { data },
});

const projectFilter = (project) => ({
	type: constants.LIST_QUOTES,
	payload: project,
});

const getQuoteByIdAction = (project) => ({
	type: constants.GET_QUOTATION_BY_ID,
	payload: project,
});

const AllQuotes = (quotation) => ({
	type: constants.GET_ALL_QUOTES,
	payload: quotation,
});

const resetQuote = () =>({
	type:constants.RESET_QUOTE,
	payload: []
});
