import React from 'react';
import { PageHeader } from "antd"
import * as constants from "../../../../redux/constants";
import FormTrustCollection from "./Components/FormTrustCollection";
import { Redirect } from "react-router-dom/cjs/react-router-dom.min";
import TableTrustCollection from './Components/TableTrustCollection';
import { validateModule, validatePermission } from "../../../handle/PermissionMethods/PermissionMethods";

const TrustFunds = () => {
	if (!validateModule(constants.TRUST_FUND_SUBMODULE)) {
		return <Redirect to={constants.DEFAULT_ROUTE} />;
	}

	return (
		<React.Fragment>
			<PageHeader
				style={{ border: "1px solid rgb(235, 237, 240);" }}
				title={"Recaudos fiducias"}
				extra={[
					validatePermission(constants.CREATE_TRUST_FUND_PERMISSION) ? (
						<FormTrustCollection/>
					) : null
					
				]}
			/>
			<TableTrustCollection />
		</React.Fragment>

	);
}

export default TrustFunds;