import * as constants from "../../../constants";

export default function saleMadeReducer(state = [], action) {
	switch (action.type) {
		case constants.SET_ALL_SALE_MADE:
			return action.payload;
		case constants.CHANGE_CUSTOMER:
			return state;
		case constants.SET_ALL_EXCHANGES:
			return state;
		case constants.APPLY_EXCHANGE:
			return state;
		default:
			return state;
	}
}
