import * as constants from "../../../constants";

export const setAllRemainingPaymentMethodAction = (id, onSuccess) => ({
	type: constants.API,
	payload: {
		method: "GET",
		headers: constants.TOKEN,
		url: `${constants.RUTA}/payments/by-id-sale/${id}`,
		postProcessSuccess: onSuccess,
	},
});

export const createRemainingPaymentMethodAction = (
	data,
	onSuccess,
	onError
) => ({
	type: constants.API,
	payload: {
		method: "POST",
		headers: constants.TOKEN,
		url: `${constants.RUTA}/payments`,
		data,
		success: (response) => createRemainingPaymentMethod(response),
		postProcessSuccess: onSuccess,
		postProcessError: onError,
	},
});
