import * as constants from "../../../constants";

export default function assignedSiteDirectos(state = [], action) {
	switch (action.type) {
		case constants.SET_ALL_ASSIGNED_SITE_DIRECTORS:
			return action.payload;
		case constants.UPDATE_OR_CREATE_ASSIGNED_SITE_DIRECTORS:
			return state._payload.map((item) => {
				if (item._id === action.payload.assginedSiteDirectorId)
					return { ...item, ...action.payload.data };
				else return item;
			});
		case constants.REMOVE_ASSIGNED_SITE_DIRECTORS:
			return state._payload.filter((item) => item._id !== action.payload);
		case constants.RESET_ASSIGNED_SITE_DIRECTORS:
			return action.payload;
		default:
			return state;
	}
}
