import React, {createContext, useContext, useEffect, useRef, useState} from "react";
import {Button, Card, Col, Form, Input, Popconfirm, Row, Table} from "antd";
import {MinusOutlined, PlusOutlined} from "@ant-design/icons";
import "./index.scss";
import {useDispatch} from "react-redux";
import {error, errorGlobal, errorPolicy, success} from "../../../../../../../handle/Notification/Notification";
import {
	deleteSaleLegalizationById,
	fetchAllSaleLegalization,
	updateOrCreateSaleLegalizationById
} from "../../../../../../../../redux/actions/Project/SaleLegalizationAction";
import splitStringWhitUnderScore from "../../../../../../../handle/FormatterWords/formatterWords";
import {convertAllMapDataState} from "../../../../../../../handle/HandleFilterTable/handleFilterTable";

const EditableContext = createContext(null);

const EditableRow = ({ index, ...props }) => {
	const [form] = Form.useForm();
	return (
		<Form form={form} component={false}>
			<EditableContext.Provider value={form}>
				<tr {...props} />
			</EditableContext.Provider>
		</Form>
	);
};

const EditableCell = ({
	title,
	editable,
	children,
	dataIndex,
	record,
	handleSave,
	...restProps
}) => {
	const [editing, setEditing] = useState(false);
	const inputRef = useRef(null);
	const form = useContext(EditableContext);
	useEffect(() => {
		if (editing) {
			inputRef.current.focus();
		}
	}, [editing]);

	const toggleEdit = () => {
		setEditing(!editing);
		form.setFieldsValue({
			[dataIndex]: splitStringWhitUnderScore(record[dataIndex]) ,
		});
	};

	const save = async () => {
		try {
			const values = await form.validateFields();
			toggleEdit();
			handleSave({ ...record, ...values });
		} catch (errInfo) {
			console.error("Save failed:", errInfo);
		}
	};

	let childNode = children;
	if (editable) {
		childNode = editing ? (
			<Form.Item
				name={dataIndex}
				rules={[
					{
						required: true,
						message: `${title} es requerido.`,
					},
				]}
			>
				<Input
					style={{ width: "100%"}}
					ref={inputRef}
					onPressEnter={save}
					onBlur={save}
				/>
			</Form.Item>
		) : (
			<div
				className="editable-cell-value-wrap"
				style={{
					paddingRight: 24,
				}}
				onClick={toggleEdit}
			>
				{children}
			</div>
		);
	}

	return <td {...restProps}>{childNode}</td>;
};

const FormCardDocument = ({saleLegalizationDatabase, id}) => {
	const idProject = id;

	const dispatch = useDispatch();

	useEffect(() => {
		setTimeout(() => {
			setDataSource(saleLegalizationDatabase._payload);
		}, 1000);
	}, []);

	useEffect(() => {
		if (saleLegalizationDatabase._payload.length > 0) {
			setCount(
				saleLegalizationDatabase._payload[saleLegalizationDatabase._payload.length - 1].id
			);
		} else {
			setCount(0);
		}
	}, []);

	const [dataSource, setDataSource] = useState([]);
	const [count, setCount] = useState(1);
	const [key, setKey] = useState(0);
	const columns = [
		{
			title: "#",
			dataIndex: "key",
			width: 10,
		},
		{
			title: "Etiqueta",
			dataIndex: "name",
			editable: true,
			width: "30%",
			render: (value) => {
				return {
					  children: splitStringWhitUnderScore(value),
				  };
			}
		},
		{
			title: "Nombre del archivo",
			dataIndex: "label",
			editable: true,
			width: "50%",
			render: (value) => {
				return {
					  children: splitStringWhitUnderScore(value),
				  };
			}
		},
		{
			title: "",
			dataIndex: "operation",
			with: 10,
			render: (_, record) => (
				<>
					<Popconfirm
						title="¿Confirmar acción?"
						onConfirm={() => handleDelete(record.id)}
					>
						<Button
							icon={<MinusOutlined />}
							type="primary"
							size="small"
							danger
						/>
					</Popconfirm>
				</>
			),
		},
	];

	useEffect(() => {
		resetTable();
	}, [dataSource]);

	const handleDelete = (id) => {
		const arrayWork = JSON.parse(JSON.stringify(dataSource));
		for (let index = 0; index < arrayWork.length; index++) {
			if (arrayWork[index].id === id) {
				if (arrayWork[index].hasOwnProperty("created_at")) {
					dispatch(
						deleteSaleLegalizationById(id, async (response) => {
							const res = await response;
							const dataSources = [...dataSource];
							const arrayfilter = dataSources.filter((item) => item.id !== id);
							setDataSource(arrayfilter);
							dispatch(fetchAllSaleLegalization(idProject));
							res.error
								? error(res.message)
								: success("Ha sido eliminado con éxito");
						})
					);
				} else {
					const dataSources = [...dataSource];
					const arrayfilter = dataSources.filter((item) => item.id !== id);
					setDataSource(arrayfilter);
				}
			}
		}
	};

	const resetTable = () => {
		for (let index = 0; index < dataSource.length; index++) {
			dataSource[index].key = index + 1;
			setKey(index + 1);
		}
	};

	const handleAdd = () => {
		const newData = {
			key: key + 1,
			id: count + 1,
			name: "",
			label: "",
			project_id: idProject,
		};
		const addDataSource = [...dataSource, newData];
		setDataSource(addDataSource);
		setCount(count + 1);
		setKey(key + 1);
	};

	const handleSave = (row) => {
		const newData = [...dataSource];
		const index = newData.findIndex((item) => row.key === item.key);
		const item = newData[index];
		newData.splice(index, 1, { ...item, ...row });
		setDataSource(newData);
	};

	const components = {
		body: {
			row: EditableRow,
			cell: EditableCell,
		},
	};

	const columnsE = columns.map((col) => {
		if (!col.editable) {
			return col;
		}

		return {
			...col,
			onCell: (record) => ({
				record,
				editable: col.editable,
				dataIndex: col.dataIndex,
				title: col.title,
				handleSave: handleSave,
			}),
		};
	});

	const cleanDtoSaleLegalization = (array) => {
		let arrayWork = JSON.parse(JSON.stringify(array));
		arrayWork.map((item) => {
			if (item.hasOwnProperty("created_at")) {
				delete item.created_at;
				delete item.updated_at;
				delete item.deleted_at;
				delete item.key;
			} else {
				delete item.key;
				item.id = null;
			}
			return item
		});
		return arrayWork;
	};

	const validateOfNull = (value) =>
		value === "" || value === null;

	const validateEmptyFields = (array) => {
		let found = 0;
		for (let index = 0; index < array.length; index++) {
			if (
				validateOfNull(array[index].name) ||
				validateOfNull(array[index].label)
			) {
				found = 1;
			}
		}

		if (found === 1) {
			errorPolicy("Campos vacios");
			return true;
		}

		return false;
	};

	const handleClick = () => {
		const objectToSend = cleanDtoSaleLegalization(dataSource);
		if (validateEmptyFields(objectToSend)) return;
		dispatch(
			updateOrCreateSaleLegalizationById(objectToSend, async (response) => {
				const res = await response;
 
				res.error ? errorGlobal("¡UPS! ha ocurrido un error mientras se actualizaba") : success("Ha sido agregado con éxito");
				dispatch(fetchAllSaleLegalization(idProject));
			})
		);
	};

	return (
		<>
			<Card title="Listado de documentos" style={{ borderColor: "black" }}>
				<Row gutter={{ xs: 8, sm: 16, md: 24, lg: 24 }}>
					<Col className="gutter-row" span={20}>
						<Form layout="vertical" style={{marginLeft:'25px', marginRight:'25px'}}>
							<Row gutter={16}>
								<Form.Item style={{ width: "100%" }}>
									<Button
										onClick={handleAdd}
										type="primary"
										style={{ marginBottom: 20 }}
									>
										<PlusOutlined />
										Documentos
									</Button>
									{
										<Table
											components={components}
											rowClassName={() => "editable-row"}
											bordered
											dataSource={convertAllMapDataState(dataSource)}
											columns={columnsE}
										/>
									}
								</Form.Item>
							</Row>
							<Form.Item>
								<Button
									disabled={JSON.parse(localStorage.stateDisable)}
									onClick={handleClick}
									style={{ marginLeft: "80%" }}
									type="primary"
								>
									Guardar
								</Button>
							</Form.Item>
						</Form>
					</Col>
				</Row>
			</Card>
		</>
	);
};

export default FormCardDocument;
