import "./availableProjects.scss";
import UnitDetails from "./UnitDetails";
import { Card, Table, Button, Tag } from "antd";
import React, { useEffect, useState } from "react";
import ImageUnitComponent from "./ImageUnitComponent";
import { EyeOutlined, ArrowUpOutlined } from "@ant-design/icons";
import {convertAllMapDataState} from "../../../../../handle/HandleFilterTable/handleFilterTable";

const UnitsProject = ({project,customerId}) => {
	const [dataSource, setDataSource] = useState(project);
	const [data, setData] = useState([]);
	const [visible, setVisible] = useState(false);
	const [visibleImage, setVisibleImage] = useState(false);
	const [handleUnitInfo, setHandleUnitInfo] = useState([]);

	const handleVisibility = (record) => {
		setVisible(true);
		setHandleUnitInfo(record);
	};
	const handleOk = () => {
		setVisible(false);
	};
	const handleCancel = () => {
		setVisible(false);
	};
	const handleVisibilityImage = (record) => {
		setHandleUnitInfo(record);
		setVisibleImage(true);
	};
	const handleOkImage = () => {
		setVisibleImage(false);
	};
	const handleCancelImage = () => {
		setVisibleImage(false);
	};

	useEffect(() => {
		setData(project);
		addatribu();
	}, [project]);
	
	const ProjectState = (state) => {
		switch (state) {
			case 1:
				return "Sobre Planos"
			case 2:
				return "En Construcción"
			case 3:
				return "Finalizado"
		}
	}

	const addatribu = () => {
		let vardata = project.unit_type;
		vardata.map((value) => {
			Object.assign(value, {
				estimated_delivery_date: project.estimated_delivery_date,
				name: project.name,
				fechaDeConstruccion: project.start_build_date,
				customerId:customerId,
				sale_parameters: project.sale_parameter,
				total_units_available: value.unit.length,
				total_units_separated: (value.units_number - value.unit.length),
				payment_relationship_settings: project.payment_relationship_setting,
				discount_policy: project.discount_policy,
				project_state: ProjectState(parseInt(project.project_state_id)),
				Cupones: project.Cupones,
			});
			return value
		});

		setDataSource(vardata);
	};

	const formatterPeso = new Intl.NumberFormat("es-CO", {
		minimumFractionDigits: 2,
	});

	const columns = [
		{
			title: "Tipo unidad / área",
			dataIndex: "id",
			key: "unitType",
			align: "center",
			render: (_, record) => (
				<>
					<Button
						onClick={() => {
							handleVisibility(record);
						}}
						type="primary"
						disabled={record.total_units_available === 0}
					>
						{formatterPeso.format(record.area)}
					</Button>
				</>
			),
		},
		{
			title: "Disponibles / Separadas",
			dataIndex: "separateAvailable",
			key: "separateAvailable",
			align: "center",
			render: (_, record) => (
				<h6>{`${record.total_units_available} / ${record.total_units_separated}`}</h6>
			),
		},
		{
			title: "Nivel de ingresos",
			dataIndex: "incomeLevel",
			key: "incomelevel",
			align: "center",
			render: (_, record) => (
				<h6> {formatterPeso.format(record.income_level)} </h6>
			),
		},
		{
			title: "Adjuntos",
			dataIndex: "Attachments",
			align: "center",
			key: "Attachments",
			render: (_, record) => (
				<>
					<Button
						type="primary"
						onClick={() => {
							handleVisibilityImage(record);
						}}
					>
						{" "}
						<EyeOutlined />{" "}
					</Button>
				</>
			),
		},
	];

	if (Array.isArray(data)) return null;

	return (
		<>
			<Card
				headStyle={{
					color: "#31708f",
					backgroundColor: "#d9edf7",
					borderColor: "#bce8f1",
				}}
				title={data.name}
				style={{ minWidth: 600, marginRight: 20 }}
			>
				<Table
					style={{ width: "100%" }}
					pagination={false}
					dataSource={convertAllMapDataState(dataSource)}
					columns={columns}
					bordered
					title={() => <h6 style={{ textAlign: "left" }}>Estado</h6>}
					footer={() => (
						<div style={{ height: 40 }}>
							<Tag style={{ float: "left" }} color="black">
								<ArrowUpOutlined /> Haga clic aquí y seleccione la unidad
								deseada
							</Tag>
						</div>
					)}
				/>
				<ImageUnitComponent
					data={handleUnitInfo}
					visible={visibleImage}
					handleOk={handleOkImage}
					handleCancel={handleCancelImage}
				/>
				<UnitDetails
					visible={visible}
					handleOk={handleOk}
					handleCancel={handleCancel}
					data={handleUnitInfo}
				/>
			</Card>
		</>
	);
};

export default UnitsProject;
