import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Card } from "antd";
import { getUnitByIdProject } from "../../../../../../../redux/actions/Project/UnitsAction";
import Spinner from "../../../../../../handle/Spinner/Spinner";
import UnitsFloor from "./UnitsFloor";
import {warning} from "../../../../../../handle/Notification/Notification";

const MainUnitsFloor = ({id}) => {

	const dispatch = useDispatch();

	useEffect(() => {
		warning("Para un mejor funcionamiento, [Configuraciones para Pagos y de Relación, Parámetros de Ventas]  deben estar gestionados")
		dispatch(getUnitByIdProject(id));
	}, []);

	const unitsFloorDataBase = useSelector((state) => state?.unit);

	if (Array.isArray(unitsFloorDataBase))
		return (
			<Card title="Especificación de unidades por piso" style={{ borderColor: "black" }}>
				<Spinner />
			</Card>
		);

	return (
		<UnitsFloor unitsFloorDataBase={unitsFloorDataBase} id={id} />
	);
};

export default MainUnitsFloor;
