import React, {useEffect} from "react";
import {useDispatch, useSelector} from "react-redux";
import {useParams} from "react-router-dom";
import {Tabs, Card, PageHeader} from "antd";
import TabsFormParametroComision from "./parametrosComiciones/TabsFormParametroComision";
import SaleLegalization from "./legalizacionVenta/SaleLegalization";
import {resetProjectAction} from "../../../../../../redux/actions/Project/ProjectsAction";
import "../../css/Index.scss";
import Spinner from "../../../../../handle/Spinner/Spinner";
import {resetPaymentRelationshipSettingAction} from "../../../../../../redux/actions/Project/ConfiguracionPagosRecudosAction";
import MainPolicyIncreaseDiscountComponent from "./incrementoDescuento/MainPolicyIncreaseDiscountComponent";
import MainAssignmentTechniciansAccountants from "./asignacionTecnicosContadores/MainAssignmentTechniciansAccountants";
import {fetchAllUsers} from "../../../../../../redux/actions/Configuration/usersAction";
import {
	fetchAllPolicyToComplyAction,
	resetPolicyToComplyAction
} from "../../../../../../redux/actions/Project/CommissionParameters/PolicyToComplyAction";
import {fetchAllAfiliatedCompany} from "../../../../../../redux/actions/Configuration/AfiliatedCompanyAction";
import {
	fetchAllCommissonParameterAction,
	resetCommissonParameterAction
} from "../../../../../../redux/actions/Project/CommissionParameters/CommissionParametersAction";
import {resetUnitsAction} from "../../../../../../redux/actions/Project/UnitsAction";
import MainSellingParameter from "./parametrosVentas/MainSellingParameter";
import MainUnitType from "./tipoUnidades/MainUnitType";
import MainEditProject from "./informacionBasica/MainEditProject";
import {resetSaleParameterAction} from "../../../../../../redux/actions/Project/Sales_ParameterAction";
import {resetAssignmentAccountantsAction} from "../../../../../../redux/actions/Project/AssignmentTechniciansAccountantsAction/AssigmentCountersAction";
import {resetAssignmentSiteDirectorsAction} from "../../../../../../redux/actions/Project/AssignmentTechniciansAccountantsAction/AssignmentSiteDirectosAction";
import {resetAssignmentTechniciansAction} from "../../../../../../redux/actions/Project/AssignmentTechniciansAccountantsAction/AssignmentTechniciansAction";
import {resetDiscountPolicyAction} from "../../../../../../redux/actions/Project/DiscountPolicyAction";
import {resetIncreasePolicyAction} from "../../../../../../redux/actions/Project/IncreasePolicyAction";
import {resetUnitTypeByAction} from "../../../../../../redux/actions/Project/UnitType";
import {resetSaleLegalizationAction} from "../../../../../../redux/actions/Project/SaleLegalizationAction";
import MainPaymentRelationshipSetup from "./configuracionPagosRelaciones/MainPaymentRelationshipSetup";
import MainUnitsFloor from "./UnitsPerFloor/MainUnitsFloor";

const {TabPane} = Tabs;

const TabsEdit = (props) => {

	const {id} = useParams();
	const dispatch = useDispatch();

	useEffect(() => {
		dispatch(fetchAllUsers());
		dispatch(fetchAllPolicyToComplyAction());
		dispatch(fetchAllAfiliatedCompany());
		dispatch(fetchAllCommissonParameterAction(id));

		return () => {
			dispatch(resetSaleParameterAction());
			dispatch(resetAssignmentAccountantsAction());
			dispatch(resetAssignmentSiteDirectorsAction());
			dispatch(resetAssignmentTechniciansAction());
			dispatch(resetCommissonParameterAction());
			dispatch(resetPolicyToComplyAction());
			dispatch(resetPaymentRelationshipSettingAction());
			dispatch(resetDiscountPolicyAction());
			dispatch(resetIncreasePolicyAction());
			dispatch(resetProjectAction());
			dispatch(resetSaleLegalizationAction());
			dispatch(resetUnitsAction());
			dispatch(resetUnitTypeByAction());
		};
	}, []);

	const policyToComply = useSelector((state) => state?.policyToComply);
	const afiliatedCompany = useSelector((state) => state?.afiliatedCompany);
	const dataCommissionParamaterSelector = useSelector((state) => state?.comissionParameter);

	function handleClick() {
		props.history.replace("/projects");
	}

	return (
		<>
			<div className="site-page-header-ghost-wrapper">


				<PageHeader
					className="site-page-header"
					onBack={() => handleClick()}
					title="Editar Proyecto"
					style={{marginLeft: "-25px"}}
				/>

				<Tabs animated type="card" tabPosition="top">
					<TabPane tab={<p>Informaión Básica</p>} key="1">
						<Card>
							<MainEditProject id={id}/>
						</Card>
					</TabPane>
					<TabPane
						tab={
							<>
								<p>
									<span>Configuraciones para </span> <br/>
									<span>Pagos y de Relación </span>
								</p>
							</>
						}
						key="2"
					>
						<Card>
							<MainPaymentRelationshipSetup id={id}/>
						</Card>
					</TabPane>
					<TabPane
						tab={
							<>
								<p>
									<span>Parámetros de Ventas</span>
								</p>
							</>
						}
						key="3"
					>
						<Card>
							<MainSellingParameter id={id}/>
						</Card>
					</TabPane>
					<TabPane
						tab={
							<>
								<p>
									<span>Tipo de Unidades</span>
								</p>
							</>
						}
						key="4"
					>
						<MainUnitType id={id}/>
					</TabPane>
					<TabPane
						tab={
							<>
								<p>
									<span>Especificación de Unidades</span> <br/>
									<span>por Piso</span>
								</p>
							</>
						}
						key="5"
					>
						<MainUnitsFloor id={id}/>
					</TabPane>
					<TabPane
						tab={
							<>
								<p>
									<span>Políticas de Incremento</span> <br/>
									<span>y Descuento </span>
								</p>
							</>
						}
						key="6"
					>
						<Card>
							<MainPolicyIncreaseDiscountComponent id={id}/>
						</Card>
					</TabPane>
					<TabPane
						tab={
							<>
								<p>
									<span>Documentos Legalización </span> <br/>
									<span>de Ventas</span>
								</p>
							</>
						}
						key="7"
					>
						<SaleLegalization id={id}/>
					</TabPane>
					<TabPane
						tab={
							<>
								<p>
									<span>Asignación de Técnicos </span> <br/>
									<span>y Contadores </span>
								</p>
							</>
						}
						key="8"
					>
						<Card>
							<MainAssignmentTechniciansAccountants id={id}/>
						</Card>
					</TabPane>
					<TabPane
						tab={
							<>
								<p>
									<span>Parámetros Comisiones</span>
								</p>
							</>
						}
						key="9"
					>
						{!Array.isArray(policyToComply) &&
						!Array.isArray(dataCommissionParamaterSelector) ? (
							<TabsFormParametroComision
								id={id}
								dataCommissionParamaterSelector={
									dataCommissionParamaterSelector
								}
								policyToComply={policyToComply}
								afiliatedCompany={afiliatedCompany}

							/>
						) : (
							<Card
								title="Parámetros y comisiones"
								style={{borderColor: "black"}}
							>
								<Spinner/>
							</Card>
						)}
					</TabPane>
				</Tabs>
			</div>
		</>
	);
};

export default TabsEdit;
