import React, {useContext} from "react";
import {Col, Form, Row} from "antd";
import {PaymentForTheProject} from "../../../../Quotes/Components/editQuote/PaymentForTheProject";
import TableMetodosPagos from "../../../../Quotes/Components/propertyData/TableMetodosPagos";
import TableSalePagoClientes from "./TableSalePagoClientes";
import {SalesMadeContext} from "../context/SalesMadeContext";

export const WayToPay = () => {

	const titleProject = "Forma de pago propuesta por el proyecto";
	const titleCustomer = "Forma de Pago propuesta por el cliente";
	
	const { paymentMethodProposedByTheProject,paymentMethodProposedByTheClient } = useContext(SalesMadeContext);
	
	return(
		<>
			<Form.Item>
				<Row gutter={2}>
					<Col span={11}>
						<PaymentForTheProject title={titleProject} quote={paymentMethodProposedByTheProject}/>
					</Col>
					<Col span={1}/>
					<Col span={11}>
						<PaymentForTheProject quote={paymentMethodProposedByTheClient} title={titleCustomer} />
					</Col>
				</Row>
			</Form.Item>
			<Form.Item>
				<Row gutter={2}>
					<Col span={11}>
						<TableMetodosPagos amountOfFees={paymentMethodProposedByTheProject.formOfPayments} />
					</Col>
					<Col span={1}/>
					<Col span={11}>
						<TableSalePagoClientes/>
					</Col>
				</Row>
			</Form.Item>
		</>
	)
}