import React, { useState, useEffect } from "react";
import {
	Form,
	Select,
	Row,
	Col,
	InputNumber,
	Upload,
	Divider,
	DatePicker,
	Checkbox,
	Input,
	Button,
} from "antd";

import {
	UploadOutlined,
	DeleteOutlined,
	ArrowDownOutlined,
} from "@ant-design/icons";
import Spinner from "../../../../../handle/Spinner/Spinner";
import { useDispatch } from "react-redux";
import {
	createRemainingPaymentMethodAction,
	setAllRemainingPaymentMethodAction,
} from "../../../../../../redux/actions/Price/SaleMade/remainingPaymentMethodAction";
import {
	errorGlobal,
	success,
} from "../../../../../handle/Notification/Notification";
import moment from "moment";
import {
	FORMATTER_INPUT_NUMBER,
	VALID_DOCUMENT_UPLOAD,
} from "../../../../../../redux/constants";
import LocalStorage from "../../../../../config/LocalStorage";

const { Option } = Select;

const FormRemainingPaymentMethod = ({
	salesMade,
	isModalVisible,
	onCloseModal,
}) => {
	const [form] = Form.useForm();
	const dispatch = useDispatch();
	const [paymentType, setPaymentType] = useState(false);
	const [aprovationLetter, setAprovationLetter] = useState(false);
	const [remainingPaymentMethod, setRemainingPaymentMethod] = useState([]);
	const [documentRemainingPaymentMethod, setDocumentRemainingPaymentMethod] =
		useState({});

	const isObjEmpty = (obj) => {
		return Object.keys(obj).length === 0;
	};

	const label = `${aprovationLetter ? "Recibida" : "No recibida"}`;

	useEffect(() => {
		if (isModalVisible) {
			dispatch(
				setAllRemainingPaymentMethodAction(salesMade.id, async (response) => {
					const res = await response;
					if (res.error) {
						errorGlobal("¡UPS, Ha ocurrido un error.");
					} else {
						setRemainingPaymentMethod(res);
					}
				})
			);
		}
	}, [isModalVisible]);

	useEffect(async () => {
		await form.validateFields([
			"nameDocument",
			"bankName",
			"expirationDate",
			"consignmentValue",
		]);
	}, [paymentType]);

	useEffect(() => {
		if (!Array.isArray(remainingPaymentMethod)) {
			form.setFieldsValue({
				customer: salesMade.customer.person.name,
				project: salesMade.project_name,
				unit: salesMade.unit_name,
			});
			if (remainingPaymentMethod._payload) {
				form.setFieldsValue({
					nameDocument: remainingPaymentMethod._payload?.multimedia
						? remainingPaymentMethod._payload.multimedia.name
						: null,
					bankName: remainingPaymentMethod._payload.bank_name || null,
					expirationDate:
						moment(remainingPaymentMethod._payload.consignment_date) || null,
					consignmentValue:
						remainingPaymentMethod._payload.consignment_amount || null,
					paymentType:
						String(remainingPaymentMethod._payload.consignment_type) || "2",
					creditApprovalDocument: remainingPaymentMethod._payload?.multimedia
						? [remainingPaymentMethod._payload.multimedia]
						: null,
					approvementLetter:
						remainingPaymentMethod._payload.state === 1 ? true : false,
				});

				setAprovationLetter(
					remainingPaymentMethod._payload.state === 1 ? true : false
				);
				setDocumentRemainingPaymentMethod(
					remainingPaymentMethod._payload?.multimedia
						? remainingPaymentMethod._payload?.multimedia
						: {}
				);
			}
		}
	}, [remainingPaymentMethod]);

	const onChangePaymentType = (value) => {
		if (value === "1") {
			setPaymentType(true);
		} else {
			setPaymentType(false);
		}
	};

	const propsUpload = {
		showUploadList: false,
		beforeUpload: (file) => {
			return false;
		},
		maxCount: 1,
		onChange(info) {
			if (info.file.status !== "uploading") {
				form.setFieldsValue({
					creditApprovalDocument: info.file,
					nameDocument: info.file.name,
				});
				setDocumentRemainingPaymentMethod(info.file);
				info.fileList[0].name = "archivo cargado";
			}
		},
	};

	const handleSubmit = async () => {
		const localStorage = new LocalStorage();
		let values = await form.validateFields();
		values.document = document;

		let fDto = new FormData();
		if (remainingPaymentMethod._payload){
			fDto.append("id", remainingPaymentMethod._payload.id);
		}
		fDto.append("consignment_type", values["paymentType"]);
		fDto.append(
			"consignment_date",
			values["expirationDate"].format("YYYY-MM-DD")
		);
		fDto.append("consignment_amount", values["consignmentValue"]);
		fDto.append("state", values["approvementLetter"] ? 1 : 0);
		fDto.append("bank_name", values["bankName"] || null);
		fDto.append("sale_id", salesMade.id);
		fDto.append("user_id", localStorage.getIdFromCurrentUser());
		fDto.append("payment_document", documentRemainingPaymentMethod);
		dispatch(
			createRemainingPaymentMethodAction(fDto, async (response) => {
				let res = await response;
				if (res.error) {
					errorGlobal("¡UPS, Ha ocurrido un error.");
				} else {
					success("Ha sido actualizado con éxito");
					onCloseModal();
				}
			})
		);
	};

	const onRemove = () => {
		form.setFieldsValue({
			creditApprovalDocument: null,
			nameDocument: null,
		});
		setDocumentRemainingPaymentMethod({});
	};

	const onChangeAprovationLetterCheck = (e) => {
		e.target.checked ? setAprovationLetter(true) : setAprovationLetter(false);
	};

	const download = () => {
		const tagA = document.createElement("a");
		tagA.href = documentRemainingPaymentMethod.path;
		tagA.target = "_blank";
		document.body.appendChild(tagA);
		tagA.click();
	};

	return (
		<>
			{" "}
			{!Array.isArray(remainingPaymentMethod) ? (
				<Form
					layout="vertical"
					onFinish={handleSubmit}
					size="middle"
					form={form}
					initialValues={{
						approvementLetter: false,
					}}
				>
					<Row gutter={16}>
						<Col xs={24} sm={24} md={12} lg={12} xl={12}>
							<Form.Item name="customer" label="Cliente">
								<Input disabled={true} style={{width: "100%"}}/>
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={12} lg={12} xl={12}>
							<Form.Item name="project" label="Proyecto">
								<Input disabled={true} style={{width: "100%"}}/>
							</Form.Item>
						</Col>

						<Col xs={24} sm={24} md={12} lg={12} xl={12}>
							<Form.Item name="unit" label="Unidad">
								<Input disabled={true} style={{width: "100%"}}/>
							</Form.Item>
						</Col>
					</Row>
					<Row gutter={16}>
						<Col xs={24} sm={24} md={12} lg={12} xl={12}>
							<div style={{ display: "flex", justifyContent: "space-evenly" }}>
								<Form.Item
									name="nameDocument"
									label="Documento aprobación de crédito"
									rules={[
										{
											required: paymentType,
											message: "El campo es requerido.",
										},
									]}
								>
									<Input style={{ width: "250px" }} disabled={true} />
								</Form.Item>

								{documentRemainingPaymentMethod &&
								documentRemainingPaymentMethod.hasOwnProperty("path") ? (
									<Form.Item label="." name="downloadCreditApprovalDocument">
										<Button
											style={{ background: "#389e0d", color: "#fff" }}
											type="primary"
											icon={<ArrowDownOutlined />}
											onClick={download}
										>
											Descargar
										</Button>
									</Form.Item>
								) : null}
								{isObjEmpty(documentRemainingPaymentMethod) ? (
									<Form.Item label="." name="creditApprovalDocument">
										<Upload
											name="creditApprovalDocument"
											accept={VALID_DOCUMENT_UPLOAD}
											{...propsUpload}
										>
											<Button type="primary" icon={<UploadOutlined />}>
												Elegir archivo
											</Button>
										</Upload>
									</Form.Item>
								) : null}

								<Form.Item
									noStyle
									shouldUpdate={(prevValues, currentValues) =>
										prevValues.creditApprovalDocument !==
										currentValues.creditApprovalDocument
									}
								>
									{({ getFieldValue }) =>
										getFieldValue("creditApprovalDocument") ? (
											<Form.Item name="deleteDocument" label=".">
												<Button
													onClick={onRemove}
													type="primary"
													icon={<DeleteOutlined />}
													danger
												>
													Borrar
												</Button>
											</Form.Item>
										) : null
									}
								</Form.Item>
							</div>
						</Col>
						<Col xs={24} sm={24} md={12} lg={12} xl={12}>
							<Form.Item
								name="paymentType"
								label="Tipo de pago"
								rules={[{ required: true, message: "El campo es requerido." }]}
							>
								<Select onChange={onChangePaymentType} defaultValue="">
									<Select.Option key={"1"} value="">Seleccionar...</Select.Option>
									<Select.Option key={"2"} value="1">Banco</Select.Option>
									<Select.Option key={"2"} value="2">Contado</Select.Option>
								</Select>
							</Form.Item>
						</Col>
					</Row>
					<Row gutter={16}>
						<Form.Item
							noStyle
							shouldUpdate={(prevValues, currentValues) =>
								prevValues.paymentType !== currentValues.paymentType
							}
						>
							{({ getFieldValue }) =>
								getFieldValue("paymentType") === "1" ? (
									<Col xs={24} sm={24} md={12} lg={12} xl={12}>
										<Form.Item
											rules={[
												{
													required: paymentType,
													message: "El campo es requerido.",
												},
											]}
											label="Banco"
											name="bankName"
										>
											<Input style={{ width: "100%" }} />
										</Form.Item>
									</Col>
								) : null
							}
						</Form.Item>

						<Col xs={24} sm={24} md={12} lg={12} xl={12}>
							<Form.Item
								rules={[
									{ required: paymentType, message: "El campo es requerido." },
								]}
								name="expirationDate"
								label="Fecha de vencimiento"
							>
								<DatePicker
									placeholder="Fecha de vencimiento"
									style={{ width: "100%" }}
								/>
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={12} lg={12} xl={12}>
							<Form.Item
								rules={[
									{ required: paymentType, message: "El campo es requerido." },
								]}
								name="consignmentValue"
								label="Valor de consignacion"
							>
								<InputNumber
									{...FORMATTER_INPUT_NUMBER}
	style={{width: "100%"}}
	/>
							</Form.Item>
						</Col>
						<Form.Item
							noStyle
							shouldUpdate={(prevValues, currentValues) =>
								prevValues.paymentType !== currentValues.paymentType
							}
						>
							{({ getFieldValue }) =>
								getFieldValue("paymentType") === "1" ? (
									<Col xs={8} sm={8} md={8} lg={8} xl={8}>
										<Form.Item
											valuePropName="checked"
											name="approvementLetter"
											label="Carta aprobación"
										>
											<Checkbox onChange={onChangeAprovationLetterCheck}>
												{label}
											</Checkbox>
										</Form.Item>
									</Col>
								) : null
							}
						</Form.Item>
					</Row>

					<Row>
						<Divider />
						<Col xs={8} sm={8} md={8} lg={8} xl={21} />
						<Col>
							<Button type="primary" htmlType="submit">
								Guardar
							</Button>
						</Col>
					</Row>
				</Form>
			) : (
				<Spinner />
			)}
		</>
	);
};

export default FormRemainingPaymentMethod;
