import React, { useEffect,useState } from "react";
import {fetchAllProjects} from '../../../../redux/actions/Project/ProjectsAction';
import {useDispatch, useSelector } from 'react-redux';
import { Card, Col, Row, Form, Select, Button } from 'antd';
import {success, warning} from "../../../handle/Notification/Notification";
import {fetchGeneralCommercialManagement} from "../../../../redux/actions/Report/GeneralCommercialManagement/GeneralCommercialManagementAction";
import Spinner from "../../../handle/Spinner/Spinner";

const  GeneralCommercialManagementReport = () => {

	const dispatch = useDispatch();
	
	useEffect(() => {
		dispatch(fetchAllProjects());
	}, []);
	
	const selectProject = useSelector(state => state?.project);

	const [idProject, setIdProject] = useState(null)
	const [consultind, setConsulting] = useState( false )
	
	const onFinish = () => {
		const message = "Debe seleccionar un proyecto";
		let data = (idProject) ? idProject : message;
		if (data === message) {
			warning(message);
		}else{
			setConsulting(true);
			dispatch(fetchGeneralCommercialManagement(data, (response) => {
				if(!response.error){
					success("Datos recuperados");
					const a = document.createElement("a");
					a.href = response._payload;
					document.body.appendChild(a);
					a.click();
					setConsulting(false);
				}else{
					warning("No hay datos");
					setConsulting(false);
				}
			}));
		}
	};
	
	return(
		<Card title="Informe General de Gestión Comercial">
			<Form layout="vertical" onFinish={onFinish}>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={12} lg={12} xl={12}>
						<Form.Item label="Proyecto:" name="Proyecto">
							<Select  defaultValue={""}
									showSearch
									style={{ width: '100%' }}
									optionFilterProp="children"
									onChange={setIdProject} 
									filterOption={(input, option) =>
										option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
									}
								>
									<Select.Option value="">Seleccione...</Select.Option>
									{
										!Array.isArray(selectProject) && selectProject._payload?.map(item => (
											<Select.Option  key={item.id} value={item.id}>{` ${item.name} `}</Select.Option>

										))
									}
							</Select>
						</Form.Item>
					</Col>
				</Row>
				<Row>
					<Col xs={18} sm={20} md={21} lg={22} xl={22} />

					<Col xs={2} sm={2} md={2} lg={2} xl={2}>
						<Form.Item>
							<Button type="primary" htmlType="submit">
								Exportar
							</Button>
						</Form.Item>
					</Col>
				</Row>
			</Form>
			{
				 consultind ? <Spinner/> : null
			}
		</Card>
	);
};


export default  GeneralCommercialManagementReport;
