import { Card } from "antd";
import React,{useContext} from "react";
import "../availableProjects/availableProjects.scss";
import SelectedItemComponent from "./SelectedItemComponent";
import SelectedUnitsContext from "../context/SelectedUnitsContext";

const SelectedUnitComponent = () => {

	const { selectedUnits } = useContext(SelectedUnitsContext);

	return (
		<Card style={{ padding: 10,border: "2px solid #bce8f1", }} title="Unidades Seleccionadas">
			<section className="wrapper ">
			{
				selectedUnits.map( (item) => (
					<SelectedItemComponent key={item.id} dataSource={item} />
				))
			}
			</section>
		</Card>
	);
};

export default SelectedUnitComponent;
