import React from 'react';
import {PageHeader} from "antd"
import * as constants from "../../../../redux/constants";
import { Redirect } from "react-router-dom/cjs/react-router-dom.min";
import FormDirectCollection from './Components/FormDirectCollection';
import TableDirectCollection from './Components/TableDirectCollection';
import { validateModule, validatePermission } from "../../../handle/PermissionMethods/PermissionMethods";


const DirectCollections = () => {
	if (!validateModule(constants.DIRECT_COLLECTION_SUBMODULE)) {
		return <Redirect to={constants.DEFAULT_ROUTE} />;
	}

	return (
		<React.Fragment>
			<PageHeader
				style={{border: "1px solid rgb(235, 237, 240);"}}
				title={"Recaudos directos"}
				extra={[
					validatePermission(constants.CREATE_DIRECT_COLLECTION_PERMISSION) ? (
						<FormDirectCollection />
					) : null
                    
				]}
			/>
			<TableDirectCollection/>
		</React.Fragment>

	);
}

export default DirectCollections;