import * as constants from './../../constants';
import LocalStorage from '../../../views/config/LocalStorage'

let Storage = new LocalStorage();

export const fetchAllProjects = (onSuccess, onError) => ({
	type: constants.API,
	payload: {
		method: 'GET',
		headers: {'access-token': Storage.getToken()},
		url: `${constants.RUTA}/project`,
		success: (response) => (setAllProjects(response)),
		postProcessSuccess: onSuccess,
		postProcessError: onError
	}
	
});


export const getProjectById = (id, onSuccess) => ({
	type: constants.API,
	payload: {
		method: 'GET',
		url: `${constants.RUTA}/project/${id}`,
		postProcessSuccess: onSuccess,
		success: (response) => (getByIdProject(response))
	}
});


export const getOptionsSelect = ( onSuccess) => ({
	type: constants.API,
	payload: {
		method: 'GET',
		url: `${constants.RUTA}/project/fill/combo`,
		postProcessSuccess: onSuccess,
		success: (response) => (getOptions(response))
	}
});

export const getZoneSelect = (onSuccess) => ({
	type: constants.API,
	payload: {
		method: 'GET',
		url: `${constants.RUTA}/project/get_area`,
		postProcessSuccess: onSuccess,
		success: (response) => (getOptions(response))
	}
});

export const createProject = (data,onSuccess) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		url: `${constants.RUTA}/project`,
		data,
		postProcessSuccess: onSuccess,
		success: (response) => (getByIdProject(response))
	}
});

export const updateProjectByIdPost = (data,id,onSuccess) => ({
	type: constants.API,
	payload: {
		method: 'POST',
		url: `${constants.RUTA}/project/${id}`,data,
		postProcessSuccess: onSuccess,
		success: (response) => (getByIdProject(response))
	}
});

export const updateStateProjectById = (id,onSuccess) => ({
	type: constants.API,
	payload: {
		method: 'PUT',
		url: `${constants.RUTA}/project/state/${id}`,
		postProcessSuccess: onSuccess,
		success: (response) => (setAllProjects(response)),
	}
});

export const getAllCompetingProjects = (onSuccess) => ({
	type: constants.API,
	payload: {
		method: 'GET',
		url: `${constants.RUTA}/competingProjectSearchs/searchCompeting`,
		success: (response) => (getCompetings(response)),
		postProcessSuccess: onSuccess
	}
});


