export const resetProjectAction = () => ({
    type: constants.API,
    payload: {
		method: null,
		success: () => (resetProject())
	}
})

const resetProject = () =>({
	type:constants.RESET_PROJECT,
	payload: []
});

export const getCompetings = (competings) =>({
	type:constants.GET_COMPETING_PROJECTS,
	payload: competings
})
