const getOptions = (data) => ({
	type : constants.GET_OPTION_SELECT,
	payload: data
})

const setAllProjects = (data) => ({
	type: constants.SET_ALL_PROJECTS,
	payload: data
});

const getByIdProject = (data) => ({
	type: constants.GET_BY_PROJECTS,
	payload: data,
});
