import React from "react";

export const ShowClient = ({record}) => {
	return (
		<>
			<strong>Nombre:</strong> <span>{record.customer.name}</span> <br/>
			<strong>Teléfono:</strong> <span>{record.customer.telephone}</span> <br/>
			<strong>Correo:</strong> <span>{record.customer.email}</span>
		</>
	)
}