import React, {useEffect, useState} from 'react'
import { Table } from 'antd';
import { convertAllMapDataState } from "../../../../../handle/HandleFilterTable/handleFilterTable";

const columns = [
	{
		title: 'Número de Cuota',
		dataIndex: 'month',
		width:80,
		render: (_, record) => split(record),
		align: 'center'
	},
	{
		title: 'Valor',
		className: 'column-money',
		dataIndex: 'value',
		width: 80,
		render: (_, record) => values(record),
		align: 'center'
	},
	{
		title: 'Porcentaje',
		dataIndex: 'porcentaje',
		width: 80,
		render: (_, record) => percent(record),
		align: 'center'
	}
];
const formatterPeso = new Intl.NumberFormat("es-CO", {
	style: "currency",
	currency: "COP",
	minimumFractionDigits: 2,
});

const values = (record) => {
	return (
		<>
			{ formatterPeso.format(record.value) }
		</>
	);
};

const split = (record) => {
	return (
		<>
			{
				record.month === "C.I. No Financiada" 
					?(record.month) 
					:record.month === "Entrega" 
						?(record.month)
						:(
							<div style={{ width: "100%" , display: "flex", textAlign:"center" }}>
								<div style={{ width: "30%" , borderRight: "1px solid #f0f0f0" }}>
									{record.key}
								</div>
								<div style={{ width: "70%" }}>
									{record.month}
								</div>
							</div>
						)
			}
		</>
	);
};

const percent = (record) => {
	return (
		<>
			{ parseFloat(record.porcentaje).toFixed(2)+' %' }
		</>
	);
};

const TableMetodosPagos = React.memo(({amountOfFees}) => {
	
	const [datos,setDatos] = useState(amountOfFees);
	
	useEffect(()=>{
		setDatos(amountOfFees);
	},[amountOfFees]);
	return(
		<Table 
			bordered 
			title={() => ''} 
			columns={columns} 
			pagination={false} 
			size="small" 
			dataSource={convertAllMapDataState(datos)}
		/>
	);
});

export default TableMetodosPagos;