import React from 'react';
import { Table, Card, Button } from 'antd';
import Modal from '../../Modal/Modal';
import { EyeOutlined, InfoOutlined } from '@ant-design/icons';
import './css/style.scss';
import { validateModule } from "../../../handle/PermissionMethods/PermissionMethods";
import * as constants from "../../../../redux/constants";
import { Redirect } from "react-router-dom/cjs/react-router-dom.min";
import {convertAllMapDataState} from "../../../handle/HandleFilterTable/handleFilterTable";

const getActions = (name) => {

	switch (name) {
		case "proceedings":
			return (
				<>
					<Modal
						title="Acta Resumen - 20/06/2018"
						size="small"
						icon={<EyeOutlined />}
						width={800}
						form={
							<table className="table borderless">
								<thead>
									<tr>
										<th>Actualizaciones</th>
									</tr>
								</thead>
								<tbody>
									<tr>
										<td>GPI Constructores</td>
										<td>
											<tr>
												<td>10% de Valor Total.</td>
											</tr>
											<tr>
												<td>Escrituración.</td>
											</tr>
											<tr>
												<td>Multa por Desistimiento</td>
											</tr>
										</td>
										<td>
											<tr>
												<td>$43.919.509,62</td>
											</tr>
											<tr>
												<td>$0,00</td>
											</tr>
											<tr>
												<td>$2.000.000,00</td>
											</tr>
										</td>
									</tr>
									<tr>
										<td colSpan="2">TOTAL COMISIONES:</td>
										<td>
											<p style={{ marginLeft: 11 }}>$45.919.509,62</p>
										</td>
									</tr>
								</tbody>
							</table>
						}
					/>
					{"  "}
					<Button
						icon={<InfoOutlined />}
						size="small"
						style={{ backgroundColor: "#40a9ff", color: "#ffffff" }}
					/>
				</>
			);
		default:
			return <h1>No project match</h1>;
	}
};

const data = [
	{
		id: "1",
		Proyecto: "Vivento",
		TotalComisiones: "$53.791.605,62",
		FechaCreación: "20/06/2018",
	},
];

const columns = [
	{
		title: "#",
		width: 120,
		dataIndex: "id",
		key: "id",
	},
	{
		title: "Proyecto",
		width: 120,
		dataIndex: "Proyecto",
		key: "Proyecto",
	},
	{
		title: "Total Comisiones",
		width: 120,
		dataIndex: "TotalComisiones",
		key: "TotalComisiones",
	},
	{
		title: "Fecha Creación",
		width: 120,
		dataIndex: "FechaCreación",
		key: "FechaCreación",
	},
	{
		title: "Acciones",
		key: "acciones",
		fixed: "right",
		width: 50,
		render: () => getActions("proceedings"),
	},
];

const AdvertisingEffectivenessReport = () => {
	if (!validateModule(constants.PROCEEDINGS_SUBMODULE)) {
		return <Redirect to={constants.DEFAULT_ROUTE} />;
	}
	return (
		<Card title="Actas">
			<Table columns={columns} dataSource={convertAllMapDataState(data)} scroll={{ x: 600, y: 800 }} />
		</Card>
	);
};

export default AdvertisingEffectivenessReport;
