import {Card, Divider} from "antd";
import React, {useContext, useEffect, useState} from "react";
import PropertyDataContext from "../context/PropertyDataContext";
import PropertyDetails from "../QuoteDescription/PropertyDetails";
import FormOfPaymentsTable from "../QuoteDescription/FormOfPaymentsTable";
import {useQuotes} from "../../Hooks/useQuotes";

const DescriptionCotizacion = () => {
	const { propertyData, handlePropertyData } = useContext(PropertyDataContext);
	const {project,initialFeeState,formOfPaymentProposedByTheProject,finalValuesProposedByTheProject,dataDates,
		paymentMethodProposedByTheClient,finalValueProposedByTheClient,calculatePresentValueByTheClient} = useQuotes();
	const [response, setResponse] = useState({});
	useEffect(() => {
		initiateResponse()
	}, []);
	useEffect(() => {
		transferData();
	}, [response]);
	
	useEffect(() => {
		initiateResponse()
	}, [propertyData,initialFeeState,finalValueProposedByTheClient]);

	const initiateResponse = () => {
		let velorMes = initialFeeState.initialFeeFinanced / dataDates.mesTotal;
		let modi = {
			amount_dues: dataDates.mesTotal,
			unit_id: propertyData.id,
			value: parseFloat(parseFloat(velorMes).toFixed(2)),
			discounts: propertyData.discounts,
			cupones: propertyData.cupones,
			modified_values: (response.modified_values) ? response.modified_values: [],
			finishes_included: propertyData.check ? 1 : 0,
			value_m2: propertyData.ValorM2,
			value_total_unit: propertyData.valorTotal,
			value_discounts: propertyData.valorDescuento,
			value_coupons: propertyData.valorCupon,
			value_initial_unit: propertyData.ValorInicial,
			initial_fee_value: initialFeeState.initialFee,
			total_unfunded_initial_fee_value: initialFeeState.initialFeeNotFunded,
			total_funded_initial_fee_value: initialFeeState.initialFeeFinanced,
			total_funded_fee_value: formOfPaymentProposedByTheProject[formOfPaymentProposedByTheProject.length - 1].value,
			value_presented: parseInt(finalValueProposedByTheClient.presentValueGiven)
		};
		setResponse(modi);
	};
	const calculatePor = (valor, valorTotal) => {
		let percentage = (parseInt(valor) * 100) / parseInt(valorTotal);
		if (valorTotal === 0) return 0;
		if (valor === 0 || valorTotal === 0) return 0;
		return percentage;
	};
	
	const transferData = () => {
		Object.assign(propertyData, {
			response: response,
		});
		handlePropertyData(propertyData);
	};

	const modified_values = (record) => {
		const arrayToWork = response;
		arrayToWork.modified_values = record;
		setResponse(arrayToWork);
	};
	let valorQuotes = initialFeeState.initialFeeFinanced / dataDates.mesTotal;
	transferData();
	
	return (
		<Card
			style={{
				border: "2px solid #bce8f1",
			}}
		>
			<PropertyDetails
				project={project}
				initialFeeState={initialFeeState}
			/>
			<FormOfPaymentsTable
				propertyData={propertyData}
				financedFee={initialFeeState.initialFeeFinanced}
				calculatePor={calculatePor}
				modified_values={modified_values}
				valorQuotes={valorQuotes}
				formOfPaymentProposedByTheProject={formOfPaymentProposedByTheProject}
				finalValuesProposedByTheProject={finalValuesProposedByTheProject}
				paymentMethodProposedByTheClient={paymentMethodProposedByTheClient}
				calculatePresentValueByTheClient={calculatePresentValueByTheClient}
				finalValueProposedByTheClient={finalValueProposedByTheClient}
			/>
			<Divider/>
		</Card>
	);
};

export default DescriptionCotizacion;
