import React, {useEffect, useState} from "react"
import {Button, Card, Checkbox, Col, Row, Table, Tag} from "antd";
import {ArrowRightOutlined} from '@ant-design/icons';
import {formatterMoney} from "../../../../../handle/FormatterMoney/FormatterMoney";
import Text from "antd/es/typography/Text";

const PropertyValue = ({propertyData,changeStateTableIncluded,onChangeKit,changeStateTableCoupon,recalculatePaymentPlan}) => {
	const [propertyValue, setPropertyValue] = useState();
	
	useEffect(()=>{
		setPropertyData();
	},[propertyData]);
	
	const tagStyle = {
		borderRadius: "10px",
		width: "90%",
		fontSize: "12px",
		margin: "-5px 0px 0px 9px",
	}
	
	const setPropertyData = () => {
		let propertyValue = [
			{
				descripcion: "Valor m2",
				value: formatterMoney.format(propertyData.valueM),
			}
		]

		if (propertyData.discountFinishes !== "0"){
			propertyValue = propertyValue.concat({
				descripcion: "Incluye Acabados",
				value: (
					<Row>
					<Col xs={11} sm={11} md={11} lg={11} xl={11}/>
					<Checkbox
					
						// checked={finishesIncluded}
						checked={(propertyData.includesFinishes)}
						onChange={(event) => onChangeKit(event)}
					/>
					</Row>
				),
			})
		}
		propertyValue = propertyValue.concat(
			{
				descripcion: (
					<Tag style={tagStyle} color="black">
						<span>
							Haga click aquí para <br /> ver las Políticas <br /> Comerciales{" "}
							<ArrowRightOutlined />
						</span>
					</Tag>
				),
				value: (
					<Button 
						onClick={changeStateTableIncluded} 
						type="primary">
						P. Comerciales
					</Button>
				),
			},
			{
				descripcion: (
					<Tag style={tagStyle} color="black">
						<span>
							Haga click aquí para <br /> aplicar Cupón <ArrowRightOutlined />
						</span>
					</Tag>
				),
				value: (
					<Button 
						onClick={changeStateTableCoupon} 
						type="primary">
						Cupones
					</Button>
				),
			}
		)
		if (!propertyData.includesFinishes){
			propertyValue = propertyValue.concat(
				{
					descripcion: "Valor Inicial",
					value: formatterMoney.format(propertyData.initialValue),
				}
			)
		}
		if ( !propertyData.includesFinishes) {
			propertyValue = propertyValue.concat({
				descripcion: "Descuento Acabados",
				value: formatterMoney.format(propertyData.discountFinishes),
			});
		}
		if (propertyData.discountCoupons !== 0) {
			propertyValue = propertyValue.concat({
				descripcion: "Descuento Cupones",
				value: formatterMoney.format(propertyData.discountCoupons),
			});
		}
		if (propertyData.discountCouponsPrevious !== 0) {
			propertyValue = propertyValue.concat({
				descripcion: (<Text type="danger">Descuento Cupones Anterior</Text>),
				value: (<Text type="danger">{formatterMoney.format(propertyData.discountCouponsPrevious)}</Text>),
			});
		}
		if (propertyData.commercialDiscounts !== 0) {
			propertyValue = propertyValue.concat({
				descripcion: "Descuento Comercial",
				value: formatterMoney.format(propertyData.commercialDiscounts),
			});
		}
		if (propertyData.previousCommercialDiscount !== 0) {
			propertyValue = propertyValue.concat({
				descripcion: (<Text type="danger">Descuento Comercial Anterior</Text>),
				value: (<Text type="danger">{formatterMoney.format(propertyData.previousCommercialDiscount)}</Text>),
			});
		}
		propertyValue = propertyValue.concat({
			descripcion: "Valor de la Unidad",
			value: formatterMoney.format(propertyData.unitValue),
		});
		setPropertyValue(propertyValue);
	}
	
	const propertyValueColumns = [
		{
			dataIndex: "descripcion",
			width: 80,
			height: 40,
			padding: "10px",
		},
		{
			dataIndex: "value",
			width: 100,
			height: 40,
			padding: "10px",
		
		},
	]
	
	return (
		<Card
			headStyle={{
				color: "#31708f",
				backgroundColor: "#d9edf7",
				borderColor: "#bce8f1",
			}}
			title="Valor del Inmueble"
		>
			<Table
				style={{ width: "100%" }}
				pagination={false}
				bordered
				columns={propertyValueColumns}
				dataSource={propertyValue}
				footer={() => (
					<Row>
								<Col xs={7} sm={7} md={7} lg={7} xl={7}/>
				<Button 
					onClick={()=>{recalculatePaymentPlan()}} 
					type="primary">Recalcular Plan de Pago</Button>
				
					</Row>
					)}
			/>
		</Card>
	)
}

export default PropertyValue