import React, { useState, useEffect } from "react";
import { Button, Table, Tooltip } from "antd";
import { EditOutlined } from "@ant-design/icons";
import {  useSelector } from "react-redux";
import DeleteControlPower from "./DeleteControlPower";
import Spinner from "../../../../handle/Spinner/Spinner";
import { Link } from "react-router-dom";
import {convertAllMapDataState, getColumnSearchProps, noWhiteWindow} from "../../../../handle/HandleFilterTable/handleFilterTable";
import TootipInfo from "../../../TooltipInfoComponent/TootipInfo";
import {  validatePermission } from "../../../../handle/PermissionMethods/PermissionMethods";
import * as constants from "../../../../../redux/constants";

const TableControlPower = () => {
	const [loading, setLoading] = useState(false);
	const [dataSource, setDataSource] = useState([]);
	const dataControlPower = useSelector((state) => state?.controlPower);

	useEffect(() => {
		dataControlPower.length === 0 ? setLoading(true) : setLoading(false);
	}, [dataControlPower]);

	useEffect(() => {
		if (dataControlPower.hasOwnProperty("_payload")) {
			setDataSource(dataControlPower._payload);
		}
	}, [dataControlPower]);

	const textEditar = <span style={{ color: "#6A6963" }}> Editar </span>;
	const getActions = (record) => {
		return (
			<>
				{validatePermission(constants.UPDATE_POWER_CONTROL_PERMISSION) ? (
					<Link to={`/projects/edit-control-competences/${record.id}`}>
						<Tooltip placement="left" color="#ffffff" title={textEditar}>
							<Button
								type="primary"
								size="small"
								style={{ backgroundColor: "#ffa247", border: "#ffa247" }}
								icon={<EditOutlined />}
							/>
						</Tooltip>
					</Link>
				) : null}

				{"  "}
				{validatePermission(constants.DELETE_PROJECT_PERMISSION) ? (
					<DeleteControlPower id={record.id} />
				) : null}
				{"  "}
				<TootipInfo created_at={record.created_at} name={record.name} updated_at={record.updated_at}/>
			</>
		);
	};
	
	const columns = [
		{
			title: "Nombre",
			width: 40,
			dataIndex: "name",
			key: "name",
			...getColumnSearchProps("name"),
			...noWhiteWindow
			
		},
		{
			title: "Constructora",
			width: 50,
			dataIndex: "construction_companies_name",
			key: "construction_companies_name",
			...getColumnSearchProps("construction_companies_name"),
			...noWhiteWindow
		},
		{
			title: "Tipo de Proyecto",
			dataIndex: "project_types_name",
			key: "project_types_name",
			width: 50,
		},
		{
			title: "Comercializadora",
			dataIndex: "marketer_companies_name",
			key: "marketer_companies_name",
			width: 40,
			...getColumnSearchProps("marketer_companies_name"),
			...noWhiteWindow
		},
		{
			title: "Estado",
			dataIndex: "project_states_name",
			key: "project_states_name",
			width: 40,
		},
		{
			title: "Acciones",
			key: "acciones",
			fixed: "right",
			width: 25,
			render: (_, record) => getActions(record),
		},
	];

	return (
		<Table
			bordered={true}
			loading={loading && <Spinner />}
			columns={columns}
			dataSource={convertAllMapDataState(dataSource)}
			scroll={{ x: 1000 }}
		/>
	);
};

export default TableControlPower;
