import Highlighter from "react-highlight-words";
import {CouponProvider} from "./CouponProvider";
import React, {useState, useEffect} from "react";
import {useDispatch, useSelector} from "react-redux";
import ModalUpdateCoupons from "./ModalUpdateCoupons";
import * as constants from "../../../../../redux/constants";
import {formatDate} from "../../Quotes/Components/Utilitis/Utilitis";
import {QuestionCircleOutlined, EditOutlined} from "@ant-design/icons";
import { Table, Popconfirm, Button, Tooltip, Input, Space, Card } from "antd";
import {fetchAllCoupons} from "../../../../../redux/actions/Configuration/CuoponsActions";
import { validatePermission} from "../../../../handle/PermissionMethods/PermissionMethods";
import { error, errorGlobal, success } from "../../../../handle/Notification/Notification";
import {convertAllMapDataState} from "../../../../handle/HandleFilterTable/handleFilterTable";
import { downloadCoupon, sendCoupon, deleteCoupon } from "../../../../../redux/actions/Configuration/CuoponsActions";
import { CheckOutlined, PrinterOutlined, SendOutlined, InfoOutlined, DeleteOutlined, CloseOutlined, SearchOutlined } from "@ant-design/icons";

const TableCoupons = () => {
	const dispatch = useDispatch();
	const [coupons, setCoupons] = useState([]);
	const [visibleUpdateCoupons, setVisibleUpdateCoupons] = useState(false);
	const couponsSelector = useSelector((state) => state?.coupon);
	const [dataRecord, setDataRecord] = useState(null);

	const showUpdateModal = (record) => {
		setVisibleUpdateCoupons(true);
		setDataRecord(record);
	}

	const handleCancel = () => {
		setVisibleUpdateCoupons(false);
		setDataRecord(null);
	}

	useEffect(() => dispatch(fetchAllCoupons()), []);

	let couponsPayload = [];
	useEffect(() => {
		let couponsTable = [];
		if (couponsSelector.hasOwnProperty("_payload")) {
			couponsSelector._payload.forEach((p, index) => {

				couponsTable.push({
					num: index + 1,
					code: p.code,
					customer: p.customer.person.name.replace("_", " "),
					expiration_code: formatDate(p.expiration_code),
					expirationCode: p.expiration_code,
					coupon_type: p.coupon_type,
					discount_type: p.discount_type,
					valueDiscount:
						p.discount_type === 1
							? formatterPeso.format(p.discount_value)
							: formatterPercent.format(p.discount_percentage) + "%",
					used: p.used,
					id: p.id,
					project: p.project_id,
					customerid: p.customer_id,
					creator: p.creator_user_id,
					coupon_valueDiscount: p.discount_value,
					coupo_valuePercentage: parseFloat(p.discount_percentage).toFixed(2),
					created: p.created_at,
					updated: p.updated_at,
				});
			});
			setCoupons(couponsTable);
			couponsPayload = couponsTable;
		}
	}, [couponsSelector]);

	const [state, setState] = useState({
		searchText: "",
		searchedColumn: "",
	});

	const formatterPeso = new Intl.NumberFormat("es-CO", {
		style: "currency",
		currency: "COP",
		minimumFractionDigits: 0,
	});

	const formatterPercent = new Intl.NumberFormat("es-CO", {
		minimumFractionDigits: 0,
	});

	const getColumnSearchProps = (dataIndex) => ({
		filterDropdown: ({
							 setSelectedKeys,
							 selectedKeys,
							 confirm,
							 clearFilters,
						 }) => (
			<div style={{padding: 8}}>
				<Input
					placeholder={`Buscar`}
					value={selectedKeys[0]}
					onChange={(e) =>
						setSelectedKeys(e.target.value ? [e.target.value] : [])
					}
					onPressEnter={() => handleSearch(selectedKeys, confirm, dataIndex)}
					style={{width: 188, marginBottom: 8, display: "block"}}
				/>
				<Space>
					<Button
						type="primary"
						onClick={() => handleSearch(selectedKeys, confirm, dataIndex)}
						icon={<SearchOutlined/>}
						size="small"
					>
						Buscar
					</Button>
					<Button
						onClick={() => handleReset(clearFilters)}
						size="small"
						style={{width: 90}}
					>
						Resetear
					</Button>
				</Space>
			</div>
		),
		filterIcon: (filtered) => (
			<SearchOutlined style={{color: filtered ? "#1890ff" : undefined}}/>
		),
		onFilter: (value, record) =>
			record[dataIndex]
				? record[dataIndex]
					.toString()
					.toLowerCase()
					.includes(value.toLowerCase())
				: "",
		render: (text) =>
			state.searchedColumn === dataIndex ? (
				<Highlighter
					highlightStyle={{backgroundColor: "#ffc069", padding: 0}}
					searchWords={[state.searchText]}
					autoEscape
					textToHighlight={text ? text.toString() : ""}
					constructor={""} hasOwnProperty={""} isPrototypeOf={""} propertyIsEnumerable={""}
					toLocaleString={""} toString={""} valueOf={""}
				/>
			) : (
				text
			),
	});

	const handleSearch = (selectedKeys, confirm, dataIndex) => {
		confirm();
		setState({
			searchText: selectedKeys[0],
			searchedColumn: dataIndex,
		});
	};

	const handleReset = (clearFilters) => {
		clearFilters();
		setState({searchText: ""});
	};

	const columns = [
		{
			title: " #",
			dataIndex: "num",
			key: "num",
			width: 100,
			align: "center",
			fixed: "left",
			...getColumnSearchProps("num"),
		},
		{
			title: "Código",
			dataIndex: "code",
			key: "code",
			align: "center",
			width: 120,
			...getColumnSearchProps("codigo"),
		},
		{
			title: "Cliente",
			dataIndex: "customer",
			key: "customer",
			align: "center",
			width: 120,
			...getColumnSearchProps("cliente"),
		},
		{
			title: "Fecha de vencimiento",
			dataIndex: "expiration_code",
			key: "expiration_code",
			align: "center",
			width: 160,
			...getColumnSearchProps("fechaVencimiento"),
		},
		{
			title: "Tipo",
			dataIndex: "coupon_type",
			key: "coupon_type",
			align: "center",
			width: 90,
			...getColumnSearchProps("tipo"),
			render: (_, record) => {
				switch (record.coupon_type) {
					case 1:
						return <h6> Descuento </h6>;
					case 2:
						return <h6> Pronto Pago </h6>;
					default:
						return <h6> Canje </h6>;
				}
			},
		},
		{
			title: "Tipo de descuento",
			dataIndex: "discount_type",
			key: "discount_type",
			align: "center",
			width: 100,
			...getColumnSearchProps("tipoDescuento"),
			render: (_, record) => {
				switch (record.discount_type) {
					case 1:
						return <h6> Valor Total </h6>;
					default:
						return <h6> Porcentaje </h6>;
				}
			},
		},
		{
			title: "Valor de descuento",
			dataIndex: "valueDiscount",
			key: "valueDiscount",
			align: "center",
			width: 80,
			...getColumnSearchProps("valorDescuento"),
		},
		{
			title: "Usado",
			dataIndex: "used",
			align: "center",
			key: "used",
			width: 120,
			render: (_, record) => getStateUsed(record),
		},
		{
			title: "Acciones",
			key: "acciones",
			align: "center",
			fixed: "right",
			width: 120,
			render: (_, record) => getActions(record),
		},
	];


	const getStateUsed = (record) => {
		if (record.used)
			return <CheckOutlined style={{fontSize: "18px", color: "#0D6B04"}}/>;
		else
			return <CloseOutlined style={{fontSize: "18px", color: "#f5222d"}}/>;
	};
	const getActions = (record) => {
		const formatDate = (date) => {
			const formatDate = Date.parse(date);
			let dt = new Date(formatDate);
			dt = dt.getDate() + "/" + (dt.getMonth() + 1) + "/" + dt.getFullYear();
			return dt;
		}

		const cancel = () =>{
			error('Operación cancelada');
		}


		let createdAt = formatDate(record.created);
		let updatedAt = formatDate(record.updated);

		const text = <span style={{color: '#6A6963'}}> Creado: {createdAt} <br/>  Modificado: {updatedAt} </span>
		const textDelete = <span style={{color: '#6A6963'}}> Eliminar </span>
		const textPrinter = <span style={{color: '#6A6963'}}> Imprimir </span>
		const textEditar = <span style={{color: '#6A6963'}}> Editar	</span>
		const textEnviar = <span style={{color: '#6A6963'}}> Enviar	</span>

		const searchCoupon = (record) => {
			if (coupons !== []) {
				for (let index = 0; index < coupons.length; index++) {
					if (coupons[index].id === record.id) {
						return coupons[index];
					}
				}
			}
		}

		const send = () => {
			dispatch(sendCoupon(record.id));
		}

		const deleteCouponTbale = () => {
			dispatch(deleteCoupon(record.id, (response) => {
				if (!response.error) {
					success("Eliminado correctamente")
					dispatch(fetchAllCoupons())
				} else {
					errorGlobal('No se pudo eliminar este cupón');
				}
			}))
		}


		const download = () => {
			dispatch(downloadCoupon(record.id, async (response) => {
				const res = await response;
				const tagA = document.createElement("a");
				tagA.href = res._payload.path;
				tagA.target = "_blank";
				tagA.click();
			}))


		}


		return (
			<>
				<CouponProvider coupon={searchCoupon(record)}>
					{validatePermission(constants.UPDATE_COUPON_PERMISSION) ? (
						<Tooltip placement="left" color="#ffffff" title={textEditar}>
							<Button
								disabled={record.used}
								onClick={() => showUpdateModal(record)}
								type="primary"
								style={{backgroundColor: "#ffa247", borderColor: "#ffa247"}}
								size="small"
								icon={<EditOutlined/>}
							/>
						</Tooltip>
					) : null}

					{"  "}
					{validatePermission(constants.PRINT_COUPON_PERMISSION) ? (
						<Tooltip placement="left" color='#ffffff' title={textPrinter}>
							<Button disabled={record.used} size="small" onClick={download} style={{backgroundColor: '#4C4B99', marginTop: 3}}
									icon={<PrinterOutlined style={{color: '#fff'}}/>}/>
						</Tooltip>
					) : null}

					{"  "}
					{validatePermission(constants.UPDATE_COUPON_PERMISSION) ? (
						<Tooltip placement="left" color='#ffffff' title={textDelete}>
							<Popconfirm
								disabled={record.used}
								title={`¿Desea eliminar el cupón?`}
								onConfirm={deleteCouponTbale}
								onCancel={cancel}
								okText="Si"
								cancelText="No"
								icon={<QuestionCircleOutlined style={{color: 'red'}}/>}
							>
								<Button disabled={record.used} icon={<DeleteOutlined/>} type='primary' size='small' danger/>
							</Popconfirm>
						</Tooltip>
					) : null}

					{"  "}
					{validatePermission(constants.SEND_COUPON_PERMISSION) ? (
						<Tooltip placement="left" color='#ffffff' title={textEnviar}>
							<Button disabled={record.used} size="small" onClick={send} style={{backgroundColor: '#5cb85c '}}
									icon={<SendOutlined style={{color: '#fff'}}/>}/>
						</Tooltip>
					) : null}
					{"  "}

					<Tooltip placement="left" color='#ffffff' title={text}>
						<Button size="small" style={{backgroundColor: '#1890FF', marginTop: 3}}
								icon={<InfoOutlined style={{color: '#ffffff'}}/>}/>
					</Tooltip>
				</CouponProvider>

			</>
		);
	};

	return (
		<>
			<Card>
				<Table columns={columns} dataSource={convertAllMapDataState(coupons)} scroll={{x: 1500, y: 300}}/>
			</Card>
			{visibleUpdateCoupons &&
			<ModalUpdateCoupons handleCancel={handleCancel} onClose={setVisibleUpdateCoupons} data={dataRecord}/>}
		</>
	);
}


export default TableCoupons
