import React, {useEffect, useState} from "react";
import {Select, InputNumber} from "antd";

const PolicyComply = ({item, setDataSource, dataSource, dataPolicyToComply}) => {
	const [showInputTotalValue, setShowInputTotalValue] = useState("hidden");
	const [policyToComply, setPolicyToComply] = useState(item);

	const handleUpdate = (value) => {
		policyToComply.policy_to_comply_id = parseInt(value);
		setPolicyToComply(policyToComply);
		if (policyToComply.policy_to_comply_id == 1) {
			setShowInputTotalValue("visible");
		} else {
			setShowInputTotalValue("hidden");
		}

		updateDataSourcePolicies(policyToComply.keyPolicy, policyToComply);
	};

	const handleUpdateInput = (value) => {
		policyToComply.percentage_total_value = parseFloat(value);
		setPolicyToComply(policyToComply);
		updateDataSourcePolicies(policyToComply.keyPolicy, policyToComply);
	};

	const updateDataSourcePolicies = (key, policyToComply) => {
		const dataWorkSource = [...dataSource];

		for (let index = 0; index < dataWorkSource.length; index++) {
			dataWorkSource[index].commission_parameters_policy_comply.map(
				(item) => {
					if (item.keyPolicy === key) {
						item = policyToComply;
					}
					return item
				}
			);
		}

		setDataSource(dataWorkSource);
	};

	useEffect(() => {
		if (policyToComply.policy_to_comply_id == 1) {
			setShowInputTotalValue("visible");
		} else {
			setShowInputTotalValue("hidden");
		}
	}, []);
	if (item.id === null || !item.hasOwnProperty("keyPolicy")) return null;


	return (
		<div
			style={{
				display: "inline-flex",
				width: "100%",
				marginTop: "1%"
			}}
		>
			<div>
				<InputNumber
					min={0}
					max={100}
					formatter={(value) => `${value}%`}
					parser={(value) => value.replace("%", "")}
					onChange={(value) => {
						handleUpdateInput(value);
					}}
					defaultValue={item.percentage_total_value}
					style={{width: "100%", visibility: showInputTotalValue}}
				/>
			</div>
			<div style={{width: "100%", maxWidth: '60%', minWidth: '60%'}}>
				<Select
					defaultValue={String(policyToComply.policy_to_comply_id)}
					onChange={(value) => {
						handleUpdate(value);
					}}
					style={{width: "90%", paddingLeft: "1%"}}
				>
					<Select.Option value="">Seleccione</Select.Option>
					{dataPolicyToComply._payload.map((item) => (
						<Select.Option key={item.id} value={String(item.id)}> {item.name}</Select.Option>
					))}
				</Select>
			</div>
		</div>
	);
};

export default PolicyComply;
