import React from "react"
import {shallowEqual, useSelector} from "react-redux";
import {Col, DatePicker, Form, Row, Select} from "antd";

const BasicInformation = () => {
	window.addEventListener("keypress", function(event){
		if (event.keyCode == 13){
			event.preventDefault();
		}
	}, false);
	const selectChannelElement = useSelector((state) => state?.channel, shallowEqual)
	return (
		<Row>
			<Col span={8} >
				<Form.Item
					name="fechaQuote"
					label="Fecha cotización"
					rules={[{ required: true, message: "Campo requerido" }]}
					style={{ width: "50%" }}
				>
					<DatePicker
						style={{ width: "80%" }}
					/>
				</Form.Item>
			</Col>
			<Col span={8} >
				<Form.Item
					name="comoEntero"
					label="¿Como se enteró?"
					rules={[{ required: false, message: "Campo requerido" }]}
					style={{ width: "50%" }}
				>
					<Select
						showSearch
						style={{ width: "100%" }}
					>
						<Select.Option value={0}>Seleccione...</Select.Option>
						{selectChannelElement.hasOwnProperty("_payload") &&
						selectChannelElement._payload.map((item) => (
							<Select.Option key={item.id} value={item.id}>{` ${item.name}`}</Select.Option>
						))}
					</Select>
				</Form.Item>
			</Col>
			<Col span={8}>
				<Form.Item
					name="nivelInteres"
					label="Nivel de interés"
					rules={[{ required: false, message: "Campo requerido" }]}
					style={{ width: "50%" }}
				>
					<Select
						placeholder="Seleccione un Nivel de Interés"
						showSearch
						style={{ width: "100%" }}
						optionFilterProp="children"
					>
						<Select.Option value={0}>Seleccione</Select.Option>
						<Select.Option value={1}>Bajo</Select.Option>
						<Select.Option value={2}>Medio</Select.Option>
						<Select.Option value={3}>Alto</Select.Option>
					</Select>
				</Form.Item>
			</Col>
		</Row>
	)
}

export default BasicInformation