import React, { useState,useEffect } from "react";
import { Card, Button, Form, Row, Col, Select, Table, Popconfirm } from "antd";
import { DeleteOutlined, PlusOutlined } from "@ant-design/icons";
import { useDispatch } from "react-redux";
import { deleteAssignmentSiteDirectorsById, fetchAllAssignmentSiteDirectors } from "../../../../../../../../redux/actions/Project/AssignmentTechniciansAccountantsAction/AssignmentSiteDirectosAction";
import {convertAllMapDataState} from "../../../../../../../handle/HandleFilterTable/handleFilterTable";


const AssignedSiteDirectorsComponent = (props) => {

	useEffect(() => {
		for (
			let index = 0;
			index < props.dataAssignedSiteDirectors._payload.length;
			index++
		) {
			props.dataAssignedSiteDirectors._payload[index].key = index + 1;
			setKey(index + 1);
		}
	}, []);

	useEffect(() => {
		setTimeout(() => {
			setDataSource(props.dataAssignedSiteDirectors._payload);
			setSelectUsers(props.dataSelectUsers);
		}, 1000);
	}, []);

	useEffect(() => {
		if (props.dataAssignedSiteDirectors._payload.length > 0) {
			setCount(
				props.dataAssignedSiteDirectors._payload[
					props.dataAssignedSiteDirectors._payload.length - 1
				].id
			);
		}
		else{
			setCount(0);
		}
	}, []);

	const dispatch = useDispatch();
	const [key, setKey] = useState(0);
	const [dataSource, setDataSource] = useState([]);
	const [count, setCount] = useState(0);
	const [selectUser, setSelectUsers] = useState([]);


	const columns = [
		{
			title: "#",
			dataIndex: "key",
			width: 20,
		},
		{
			title: "Director de Obra",
			dataIndex: "construction_director_id",
			render: (_,record) => (
				<>
					<div style={{ width: "100%" }}>
						<Form.Item
							rules={[
								{
									required: true,
									message: "El campo es requerido",
								},
							]}
						>
							<Select 
							onChange={(value) => {
									handleUpdate(value, record);
								}}
							style={{ width: "80%" }} 
							defaultValue={record.construction_director_id}>
								<Select.Option value=""> Seleccione </Select.Option>
								{selectUser && selectUser.map((item) => (
									<Select.Option key={item.id} value={item.id}>
										{" "}
										{item.name}{" "}
									</Select.Option>
								))}
							</Select>
						</Form.Item>
					</div>
				</>
			),
		},
		{
			title: "",
			dataIndex: "operation",
			width: 20,
			render: (_, record) =>
				dataSource.length >= 1 ? (
					<>
						<Popconfirm
							title="Confirmar acción?"
							onConfirm={() => handleDelete(record.id)}
						>
							<Button
	icon={<DeleteOutlined/>}
	type="primary"
	size="small"
	danger
	/>
						</Popconfirm>
					</>
				) : null,
		},
	];

	const handleDelete = (id) => {
		const arrayWork = JSON.parse(JSON.stringify(dataSource));
		for (let index = 0; index < arrayWork.length; index++) {
			if(arrayWork[index].id === id){
				if (arrayWork[index].hasOwnProperty("created_at")) {
					dispatch(
						deleteAssignmentSiteDirectorsById(id, async () => {
							const dataSources = [...dataSource];
							const arraylist = dataSources.filter((item) => item.id !== id);
							setDataSource(arraylist);
							props.setListAssignedSiteDirectors(arraylist);
							
						})
					);
					dispatch(fetchAllAssignmentSiteDirectors(id));
				} else {
					const dataSources = [...dataSource];
					const arrayfilter = dataSources.filter((item) => item.id !== id);
					setDataSource(arrayfilter);
					props.setListAssignedSiteDirectors(arrayfilter);
				}
			}
		}
	};

	const handleUpdate = (value, newData) => {
		const dataSources = [...dataSource];
		dataSources.map((item) => {
			if (item.id === newData.id) {
				item.construction_director_id = value;
			}
			return item
		});
		setDataSource(dataSources);
		props.setListAssignedSiteDirectors(dataSources);
	};

	const handleAdd = () => {
		const newData = {
			key: key + 1,
			id: count + 1,
			project_id: parseInt(props.projectId),
			construction_director_id: "",
		};
		const addDataSource = [...dataSource, newData];
		setDataSource(addDataSource);
		setCount(count + 1);
		setKey(key + 1);
		props.setListAssignedSiteDirectors(dataSource);
	};

	return (
		<>
			<Card title="Directores de Obra Asignados" style={{ borderColor: "black",  marginLeft:'25px', marginRight:'25px' }}>
				<Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
					<Col className="gutter-row" span={24}>
						<Row gutter={16}>
							<div style={{ width: "100%",  marginLeft:'25px', marginRight:'25px' }}>
								<Table
                                    pagination={false}
									bordered
									dataSource={convertAllMapDataState(dataSource)}
									columns={columns}
									footer={() => (
										<Button
											style={{ marginLeft: "75%" }}
											onClick={handleAdd}
											type="primary"
                                            icon = {<PlusOutlined/>}
										>
											{" "}

											Directores de Obra
										</Button>
									)}
								/>
							</div>
						</Row>
					</Col>
				</Row>
			</Card>
		</>
	);
};

export default AssignedSiteDirectorsComponent;